import _ from 'lodash';
import React, { useCallback, useState, useMemo } from 'react';
import { Header, Content, Sidenav, Dropdown, Nav, Icon } from 'rsuite';
import { DropdownProps } from 'rsuite/lib/Dropdown';
import { NavLink, Link, match as Match, matchPath, useParams, useLocation } from 'react-router-dom';

import Frame from '@hypers/react-frame';
import BasicRoute from '@/components/Routes/BasicRoute';
import { MenuItem } from '@/constants/config';
import Footer from '@/components/Layouts/Footer';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import UserProfile from '@/components/Layouts/UserProfile';
import { findTree } from '@/utils/tree';
import { getEnvFromSession } from '@/utils/env';
import { getUserProfile } from '@/utils/location';

export interface NormalLayoutProps {
  menuConfig: MenuItem[];
  children: React.ReactChildren | React.ReactChild;
  defaultExpand?: boolean;
  hideSidebar?: boolean;
  renderProjectNav?: any;
}

// 读取配置项，生成Route组件，包含每个页面模块
export const generateComponent = (menu: MenuItem[], pathToStringUrlParam: BaseObject<any>) => {
  return menu.reduce((accu, curr) => {
    if (curr.children) {
      accu = accu.concat(generateComponent(curr.children, pathToStringUrlParam));
    } else if (curr.component) {
      accu = accu.concat([
        <BasicRoute
          key={curr.key}
          titleKey={curr.key}
          path={curr.link.toStringURL(pathToStringUrlParam)} // 复用路由的link
          exact
          component={curr.component as any}
        />
      ]);
    }

    return accu;
  }, []);
};

const isActive = (link: string, path: string) => link === path || path.indexOf(`${link}/`) === 0;

const parseLink = (link: string | APIDictObject, urlParams: Match['params']) => {
  if (_.isObject(link)) {
    return link.toStringURL(urlParams);
  }
  return link;
};

const NormalLayout = React.memo(
  ({
    children,
    menuConfig,
    defaultExpand,
    hideSidebar = false,
    renderProjectNav
  }: NormalLayoutProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { pathname } = useLocation();
    let urlParams = useParams();
    const urlParamsMemo = useMemo(() => {
      return JSON.parse(JSON.stringify(urlParams));
    }, [JSON.stringify(urlParams)]);
    const {
      project: productName = formatMessage('i18n_projectTitle'),
      logo: logoUrl
    } = getEnvFromSession();
    const [expand, setExpand] = useState(defaultExpand !== false);
    const renderFrameTitle = useCallback(() => <Link to="/">{productName}</Link>, [productName]);

    const activeKey = React.useMemo(() => {
      const activeItems = findTree(menuConfig, (i: MenuItem) =>
        isActive(i.link && i.link.toStringURL(urlParamsMemo), pathname)
      );
      return _.get(activeItems, 'key');
    }, [pathname, menuConfig, urlParamsMemo]);

    const defaultOpenKeys = useMemo(() => {
      // 递归去找有children的
      let result = [] as any;
      const getKeys = (n: Array<any>) => {
        _.map(n, nn => {
          if (nn.children) {
            result.push(nn.key);
            getKeys(nn.children);
          }
        });
      };

      getKeys(menuConfig);
      return result;
    }, [menuConfig]);

    const handleToggle = useCallback(() => {
      setExpand(prev => !prev);
    }, []);

    /**
     * 一级用<Nav.Item />
     * 二级用<Dropdown /><Dropdown.Item />
     * 三级用<Dropdown.Menu></Dropdown.Item />
     */
    const renderNavs = useCallback(
      (navs: MenuItem[], level = 1) => {
        const isMultiLevel = level > 1;
        return navs.map(({ children: navChildren, ...item }: MenuItem) => {
          if (!navChildren && level === 1) {
            // 第一级无children 用<Nav.Item />
            return (
              <Nav.Item
                key={item.key}
                eventKey={item.key}
                icon={item.icon}
                componentClass={NavLink}
                to={parseLink(item.link, urlParamsMemo)}
                className={isMultiLevel ? 'render-nav-multi-levle' : ''} // 只有expand模式，才padding-left: 65px;
              >
                {formatMessage(item.key)}
              </Nav.Item>
            );
          }

          // 递归的渲染有children的
          const menuChildren = _.map(navChildren, child => {
            const link = parseLink(child.link, urlParamsMemo);
            const active = Boolean(
              matchPath(link, {
                path: pathname,
                exact: true,
                strict: false
              })
            );

            // hidden 优先级比较高
            if (child.hidden) {
              return null;
            }

            // 有children递归的渲染
            if (child.children) {
              return renderNavs([child], level + 1);
            }

            // 只要是没有children的都是Dropdown.Item，除了第一级的<Nav.Item />
            return (
              <Dropdown.Item
                key={child.key}
                eventKey={child.key}
                componentClass={NavLink}
                to={link}
                className={isMultiLevel ? 'render-nav-multi-levle' : ''} // 只有expand模式，才padding-left: 65px;
                active={active}
              >
                {formatMessage(child.key)}
              </Dropdown.Item>
            );
          });

          const dropDownProps = {
            key: item.key,
            eventKey: item.key,
            placement: 'rightStart' as DropdownProps['placement'],
            trigger: 'hover' as DropdownProps['trigger'],
            title: formatMessage(item.key),
            icon: item.icon
          };

          // 多级的菜单要用Memu而不能再套用 DropDown了
          // https://rsuitejs.com/components/dropdown#%E5%A4%9A%E7%BA%A7%E8%8F%9C%E5%8D%95
          if (level >= 2 && menuChildren.length) {
            return <Dropdown.Menu {...dropDownProps}>{menuChildren}</Dropdown.Menu>;
          }

          return <Dropdown {...dropDownProps}>{menuChildren}</Dropdown>;
        });
      },
      [pathname, formatMessage, urlParamsMemo]
    );

    const navContent = useMemo(() => {
      return renderNavs(menuConfig);
    }, [renderNavs, menuConfig]);

    const helpInEnv = getEnvFromSession('help');

    // 渲染侧边栏底部 主要是帮助文档
    const renderBottomNav = useCallback(() => {
      if (_.isEmpty(helpInEnv) || !helpInEnv) {
        return null;
      }
      if (helpInEnv.length > 1) {
        return (
          <Nav>
            <Dropdown
              placement={expand ? 'topStart' : 'rightEnd'}
              trigger="click"
              renderTitle={() => {
                return (
                  <Icon
                    style={{
                      width: 56,
                      height: 56,
                      lineHeight: '56px',
                      textAlign: 'center'
                    }}
                    icon="help-o"
                  />
                );
              }}
            >
              {helpInEnv.map((i: any) => {
                return (
                  <Dropdown.Item
                    key={i.url}
                    componentClass="a"
                    href={i.url}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {i.title}
                  </Dropdown.Item>
                );
              })}
            </Dropdown>
          </Nav>
        );
      }
      return (
        <Nav>
          <Nav.Item
            className="help-btn"
            componentClass="a"
            href={_.get(helpInEnv[0], 'url')}
            target="_blank"
            rel="noopener noreferrer"
          >
            <Icon icon="help-o" />
          </Nav.Item>
        </Nav>
      );
    }, [expand, helpInEnv]);

    const userProfileResp = getUserProfile();
    const apps = _.get(userProfileResp, 'hacProfile.apps');
    const code = _.get(userProfileResp, 'isHaa') ? 'haa' : 'hfa';
    // const apps = [
    //   {
    //     id: 3,
    //     name: 'Hyper Advertising Analytics',
    //     shortName: 'Advertising',
    //     code: 'hfa',
    //     localizedName: '广告分析',
    //     authorized: true,
    //     theme: '#FF9800',
    //     url: 'https://adstracker.hypers.com.cn',
    //     iconUrl: 'https://account.hypers.com.cn/resources/img/apps/100x100/saas/HFA.png',
    //     logoNormalUrl: 'https://account.hypers.com.cn/resources/img/apps/100x100/saas/HFA.png',
    //     logoSmallUrl: 'https://account.hypers.com.cn/resources/img/apps/100x100/saas/HFA.png'
    //   }
    // ];
    const logo = logoUrl ? <img src={logoUrl} /> : null;
    return (
      <Frame>
        <Frame.Nav
          logo={logo}
          renderTitle={renderFrameTitle}
          expand={expand}
          onExpand={handleToggle}
          apps={apps}
          activeCode={code}
          onRenderBottomNav={renderBottomNav}
        >
          {renderProjectNav && renderProjectNav({ expand })}
          {/**
           * openKeys 通过 context传给 Dropdown
           * Dropdown通过props传递给menu
           *
           * 所以openKeys 中，只要包含 任意的Dropdown Item/DropDown Menu的key，
           * 就可以被展开
           *
           * 这里拿到expandKeys拿到的的是 多级的key的一个path
           */}
          <Sidenav
            className="page-side-bar"
            expanded={expand}
            defaultOpenKeys={defaultOpenKeys}
            activeKey={activeKey}
            appearance="subtle"
          >
            <Sidenav.Body>
              <Nav>{navContent}</Nav>
            </Sidenav.Body>
          </Sidenav>
        </Frame.Nav>
        <Frame.Content>
          <Header>
            <UserProfile />
          </Header>
          <Content className="main-content">
            {children}
            {hideSidebar || <Footer />}
          </Content>
        </Frame.Content>
      </Frame>
    );
  }
);

NormalLayout.displayName = 'NormalLayout';

export default NormalLayout;
