export const isNotEmpty = _.negate(_.isEmpty);

/**
 * 加载 js 文件
 * @param url
 */
export function loadJsFile(url: string): Promise<any> {
  return new Promise(resolve => {
    const script = document.createElement('script');
    const head = document.getElementsByTagName('head')[0];
    script.type = 'text/javascript';
    // 注意服务器返回的JS的编码需与这里的设置一致
    script.charset = 'utf-8';
    script.src = url;
    script.onload = () => {
      resolve();
    };
    head.insertBefore(script, head.firstChild);
  });
}

/**
 * 加载 css 文件
 * @param url
 */
export function loadCssFile(url: string): Promise<any> {
  return new Promise(resolve => {
    const container = document.getElementsByTagName('head')[0];
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.type = 'text/css';
    link.href = url;
    link.onload = () => {
      resolve();
    };
    container.appendChild(link);
  });
}

/**
 * 获取 dom 实例，主要用于的得到 getRef() / useRef() 返回值的实例
 * @param ref
 */
export function findDomInstance<T>(ref: MutableRefObject<T>): T {
  return ref && ref.current;
}

/**
 * 如果是 null undefined {} [] "" 则返回 true  其他都是 false
 * @param value
 * @returns {boolean}
 */
export const isNilEmpty = (value: any): boolean => {
  if (_.isString(value)) {
    return value.length === 0;
  }
  if (_.isObject(value)) {
    return _.isEmpty(Object.keys(value));
  }
  if (_.isArray(value)) {
    return _.isEmpty(value.length);
  }
  return _.isNil(value);
};

export const toDate = (date: string | number | Date | Dayjs) => dayjs(date).toDate();

export function setPageError(errorCode: string) {
  store.dispatch({ type: ACTION_TYPES.UPDATE_PAGE_ERROR_CODE, errorCode });
}
