import * as React from 'react';
import _ from 'lodash';
import { Icon } from 'rsuite';

interface StatusIconProps {
  status?: string;
}

const StatusIcon = ({ status = 'disabled' }: StatusIconProps): React.ReactElement => (
  <Icon icon="circle" className={`icon-status icon-${_.toLower(status)}`} />
);

export default React.memo(StatusIcon);
