import React, { memo, useState, useEffect, useCallback } from 'react';
import { Panel } from 'rsuite';
import _ from 'lodash';
import { TIME } from '@/constants/config';
import { onFetchSummary, onFetchInfo, onFetchStatus } from '../actions';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { prefixClassName } from '../constants';
import { CampaignStatusIcon } from '@/components/Icon';
import { numberFormat } from '@/utils/format';
import { EmptyBlock, LoadingBlock } from '@/components/EmptyOrLoading';
import Promise from 'promise';
import { statusData } from '@/views/common/management/campaign/constants';

interface Props {
  dateParams?: TIME;
  id: string | number;
}

interface CampaignSummary {
  impressions?: number | string;
  uimpressions?: number | string;
  clicks?: number | string;
  uclicks?: number | string;
  clickRate?: number | string;
  name?: string;
  campaignId?: number;
  status?: string;
  dqastatus?: string;
  startDate?: Date | string | null;
  start_date?: Date | string | null;
  endDate?: Date | string | null;
  end_Date?: Date | string | null;
  target?: string;
  targetDemo?: string;
  givtimp?: string | null;
  givtclk?: string | null;
  dqaStorag?: string | null;
}

export default memo<Props>(function ({ dateParams, id }) {
  const [data, setData] = useState<CampaignSummary>({});
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    let p1 = new Promise(resolve => {
      onFetchSummary(
        {
          ..._.pick(dateParams, ['start', 'end']),
          id
        },
        res => {
          resolve(_.get(res, 'data.data'));
        }
      );
    });
    let p2 = new Promise(resolve => {
      onFetchInfo(
        {
          campaignId: id
        },
        res => {
          resolve(_.get(res, 'data'));
        }
      );
    });
    let p3 = new Promise(resolve => {
      onFetchStatus(
        {
          ..._.pick(dateParams, ['start', 'end']),
          id
        },
        res => {
          resolve(_.get(res, 'data'));
        }
      );
    });
    Promise.all([p1, p2, p3])
      .then(res => {
        setLoading(false);
        const newData = {};
        res.forEach(item => _.merge(newData, item));
        setData(newData);
      })
      .catch(message => {
        console.log(message);
      });
  }, [dateParams.start, dateParams.end, dateParams, id]);

  const { formatMessage } = useFmtMsgFori18n();

  // 显示DQA状态
  const showDqastatus = useCallback(() => {
    const { dqastatus } = data;
    if (dqastatus === 'Released') {
      return formatMessage('i18n_release');
    }
    return formatMessage('i18n_unrelease');
  }, [data, formatMessage]);

  // 渲染汇总指标
  const renderNum = useCallback(() => {
    return (
      <>
        <Panel className={prefixClassName('report')}>
          <div>
            <div>{formatMessage('i18n_impressionsNum')}</div>
            <div>{numberFormat(data.impressions)}</div>
          </div>
          <div>
            <div>{formatMessage('i18n_clicksNum')}</div>
            <div>{numberFormat(data.clicks)}</div>
          </div>
          <div>
            <div>{formatMessage('i18n_suspiciousImpNum')}</div>
            <div>{numberFormat(data.givtimp)}</div>
          </div>
          <div style={{ width: 200 }}>
            <div>{formatMessage('i18n_suspiciousClkNum')}</div>
            <div>{numberFormat(data.givtclk)}</div>
          </div>
        </Panel>
      </>
    );
  }, [data.clicks, data.givtclk, data.givtimp, data.impressions, formatMessage]);

  const renderHtml = useCallback(() => {
    if (loading) {
      return <LoadingBlock />;
    }
    if (_.isEmpty(data) || _.isNil(data)) {
      return <EmptyBlock />;
    }
    let statusValue = statusData.filter(item => item.value === data.status)[0].label;
    return (
      <>
        <div className={prefixClassName('title')}>
          {formatMessage('i18n_campaign')}: {data.name}
        </div>
        <div className={prefixClassName('info')}>
          <span className={`campaign-${_.lowerCase(data.status)}`}>
            {formatMessage('i18n_status')}: {CampaignStatusIcon(data.status)}
            &nbsp;
            {data.status && _.capitalize(statusValue)}
          </span>
          <span>
            {formatMessage('i18n_segFeature')} :{' '}
            {data.targetDemo && formatMessage(`i18n_${_.lowerCase(data.targetDemo)}`)}
            &nbsp;
            {data.target}
          </span>
          <span>
            {formatMessage('i18n_planDateRange')}: {data.start_date} ~ {data.end_date}
          </span>
          <span>
            {formatMessage('i18n_releaseState')}: {showDqastatus()}
          </span>
        </div>
      </>
    );
  }, [loading, data, formatMessage, showDqastatus]);
  return (
    <>
      <Panel style={{ height: 122 }}>{renderHtml()}</Panel>
      {renderNum()}
    </>
  );
});
