import React, { memo, useState, useEffect, useCallback } from 'react';
import { Panel } from 'rsuite';
import _ from 'lodash';
import { TIME } from '@/constants/config';
import { onFetchSummary } from './actions';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { prefixClassName } from './constants';
import { CampaignStatusIcon } from '@/components/Icon';
import { numberFormat, toShortNum } from '@/utils/format';
import { EmptyBlock, LoadingBlock } from '@/components/empty-loading-block';
import Promise from 'promise';
import { statusData } from '@/views/users/campaignmanagement/constants';
import { BasicReportProps } from '@/constants/config';
import { FilterParam } from './constants';

interface CampaignSummary {
  impressions?: number | string;
  uimpressions?: number | string;
  clicks?: number | string;
  uclicks?: number | string;
  clickRate?: number | string;
  CTR?: number | string;
  ROI?: number | string;
  CPO?: number | string;
  CPA?: number | string;
  ATV?: number | string;
}
export default memo<BasicReportProps<FilterParam>>(({ dateParams, postParams }) => {
  const [data, setData] = useState<CampaignSummary>({});
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    onFetchSummary(
      {
        ..._.pick(dateParams, ['start', 'end']),
        ...postParams
      },
      res => {
        setLoading(false);
        setData(_.get(res, 'data.data', {}));
      }
    );
  }, [dateParams, postParams]);

  const { formatMessage } = useFmtMsgFori18n();

  return (
    <>
      <Panel className={prefixClassName('report')}>
        <div>
          <div>{formatMessage('i18n_impressionsNum')}</div>
          <div>{toShortNum(data.impressions)}</div>
        </div>
        <div>
          <div>{formatMessage('i18n_uniqueImpressionsNum')}</div>
          <div>{toShortNum(data.uimpressions)}</div>
        </div>
        <div>
          <div>{formatMessage('i18n_clicksNum')}</div>
          <div>{toShortNum(data.clicks)}</div>
        </div>
        <div>
          <div>{formatMessage('i18n_uniqueClicksNum')}</div>
          <div>{toShortNum(data.uclicks)}</div>
        </div>
        <div>
          <div>CTR</div>
          <div>{numberFormat(data.CTR)}</div>
        </div>
        <div>
          <div>ROI</div>
          <div>{numberFormat(data.ROI)}</div>
        </div>
        <div>
          <div>CPO</div>
          <div>{toShortNum(data.CPO)}</div>
        </div>
        <div>
          <div>CPA</div>
          <div>{toShortNum(data.CPA)}</div>
        </div>
        <div>
          <div>ATV</div>
          <div>{toShortNum(data.ATV)}</div>
        </div>
      </Panel>
    </>
  );
});
