import React, { memo, useState, useEffect } from 'react';
import _ from 'lodash';

import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import SvgIcon from '@/components/Icon/SvgIcon';
import GenderPie from './GenderPie';
import { GenderProps } from '../type';

const mockData: GenderProps = {
  male: '54.93%',
  female: '45.07%'
};

interface Props extends BasicReportProps {
  campaignType: 'campaign' | 'crossCampaign';
}

export default memo<Props>(function({ dateParams, filterParams, id, campaignType }) {
  const { loading, onFetch } = useFetch<CommonResponse>(FETCH_METHOD.GET, APIs.API_TAB_REPORT, {
    cacheParams: true,
    params: {
      id,
      type: 'gender',
      hierarchy: campaignType,
      report: 'audiences',
      end: dateParams.end,
      ...filterParams
    }
  });
  const [data, setData] = useState<GenderProps>(mockData);

  useEffect(() => {
    dateParams.end &&
      onFetch().then(rsp => {
        !_.isEmpty(_.get(rsp, 'data.records.0', {})) && setData(_.get(rsp, 'data.records.0', {}));
      });
  }, [id, dateParams.end]);

  const { formatMessage } = useFmtMsgFori18n();

  return (
    <div className="panel-figure">
      <div className="panel-figure-header">{formatMessage('i18n_audienceFigure')}</div>
      <div className="panel-figure-main">
        <div className="panel-figure-main-svg">
          <div className="panel-figure-main-svg-male">
            <SvgIcon icon="man" width={26} height={80} style={{ color: '#34C3FF' }} />
          </div>
          <div className="panel-figure-main-value">{data.male}</div>
        </div>
        <div className="panel-figure-main-pie">
          <GenderPie loading={loading} data={data} />
        </div>
        <div className="panel-figure-main-svg">
          <div className="panel-figure-main-svg-female">
            <SvgIcon icon="woman" width={26} height={80} style={{ color: '#EA3797' }} />
          </div>
          <div className="panel-figure-main-value">{data.female}</div>
        </div>
        <div
          style={{
            color: '#272C36',
            fontSize: 16,
            position: 'absolute',
            bottom: '-26px',
            left: '50%',
            marginLeft: -66
          }}
        >
          {formatMessage('i18n_audienceGenderDistribute')}
        </div>
      </div>
    </div>
  );
});
