import React from 'react';
import { Nav } from 'rsuite';
import { NavProps } from 'rsuite/lib/Nav';

interface NavEntity {
  eventKey: string;
  label: string;
}

export interface CustomNavProps extends NavProps {
  data: Array<NavEntity>;
}

export default (props: CustomNavProps) => {
  const { data, ...rest } = props;
  return (
    <Nav {...rest}>
      {data.map(d => (
        <Nav.Item key={d.eventKey} eventKey={d.eventKey}>
          {d.label}
        </Nav.Item>
      ))}
    </Nav>
  );
};
