import _ from 'lodash';
import dayjs from 'dayjs';
import { YYYYMMDD } from '@/utils/date';
import { defaultFormData, TAB_KEY } from './constants';

export const mapToRequest = (formData: typeof defaultFormData): Record<string, any> => {
  return {
    ...formData,
    dateRange: undefined,
    start: YYYYMMDD(_.get(formData, 'dateRange.0', '')),
    end: YYYYMMDD(_.get(formData, 'dateRange.1', ''))
  };
};

export const mapFromRespRow = (rsp: Record<string, any>): Record<string, any> => {
  return {
    dateRange: [dayjs(rsp.startDate).toDate(), dayjs(rsp.endDate).toDate()],
    division: rsp.division,
    fields: rsp.fields,
    hierarchy: _.toLower(rsp.hierarchy),
    ids: rsp.ids,
    name: rsp.name,
    limit: rsp.limit
  };
};

export const mapFromRespDeviceExport = (rsp: Record<string, any>): Record<string, any> => {
  const frequencyFromBackend: any[] = _.isArray(rsp.frequency) ? rsp.frequency : [];
  const frequencyHasValue = frequencyFromBackend.length === 2;

  return {
    dateRange: [dayjs(rsp.startDate).toDate(), dayjs(rsp.endDate).toDate()],
    devicePrefix: rsp.devicePrefix,
    division: rsp.division,
    frequency: frequencyHasValue ? frequencyFromBackend : [1, 20],
    frequencyChecked: frequencyHasValue,
    hierarchy: _.toLower(rsp.hierarchy),
    ids: rsp.ids,
    name: rsp.name,
    isMD5: Number(rsp.isMD5) === 1,
    limit: rsp.limit
  };
};

export const mapFromRespCustomReport = (rsp: Record<string, any>): Record<string, any> => {
  return {
    dateRange: [dayjs(rsp.startDate).toDate(), dayjs(rsp.endDate).toDate()],
    dimension: rsp.dimension,
    filters: _.map(rsp.filters, f => ({
      type: f.type,
      operation: 'equal',
      value: f.value
    })),
    hierarchy: _.toLower(rsp.hierarchy),
    ids: rsp.ids,
    metrics: rsp.metrics,
    name: rsp.name,
    secondary: rsp.secondary
  };
};

export const mapFromRespOverlap = (rsp: Record<string, any>): Record<string, any> => {
  return {
    name: rsp.name,
    exportType: rsp.exportType,
    data: _.map(rsp.data, d => ({
      dimension: d.dimension,
      // eslint-disable-next-line @typescript-eslint/camelcase
      id_set: _.join(d.idSet, ','),
      // 后端始终会传 8位的字符 YYYYMMDD，
      dateRange: [dayjs(d.start).toDate(), dayjs(d.end).toDate()]
    }))
  };
};

// 重合度计算data这一项对应许多组，后端校验信息需要对应到具体位置，所以errors类型不太一样
// 需要将其映射为{dateRange-0: 'message', ids-1: 'message'}这种形式
interface DataErrorType {
  index: number; // 哪个组
  name: 'date' | 'dimension' | 'ids' | 'id_set' | 'idSet'; // 哪一项， 时间范围、导出层级还是层级id
  value: string | string[]; // 具体错误信息
}

export const mapErrorsWithExportType = (
  exportType: TAB_KEY,
  rspErrors: Record<string, (DataErrorType[] | string)[]>
): Record<string, string> => {
  if (exportType !== TAB_KEY.CALC_OVERLAP) {
    return _.mapValues(rspErrors, v => _.join(v, '; '));
  }

  const newErrors: Record<string, string> = {};
  !_.isEmpty(rspErrors.name) && (newErrors.name = _.join(rspErrors.name, '; '));
  !_.isEmpty(rspErrors.exportType) && (newErrors.exportType = _.join(rspErrors.exportType, '; '));

  // 单独处理data的error：
  // 若某一项为string，则append到data这一项中
  // 若某一项为DataErrorType[]，则映射到相应的dateRange-idx，或ids-idx中
  const dataErrors: Record<string, string[]> = {};
  _.forEach(rspErrors.data, itemError => {
    if (_.isString(itemError)) {
      _.isArray(dataErrors.data)
        ? dataErrors.data.push(itemError)
        : (dataErrors.data = [itemError]);
    } else {
      _.forEach(itemError, i => {
        let compName = '';
        switch (i.name) {
          case 'date':
            compName = 'dateRange';
            break;
          case 'dimension':
            compName = 'dimension';
            break;
          case 'ids':
          case 'id_set':
          case 'idSet':
            compName = 'ids';
            break;
          default:
            compName = i.name;
        }
        const fieldName = `${compName}-${i.index}`;
        const fieldMsg = _.isArray(i.value) ? _.join(i.value, '; ') : i.value;
        _.isArray(dataErrors[fieldName])
          ? dataErrors[fieldName].push(fieldMsg)
          : (dataErrors[fieldName] = [fieldMsg]);
      });
    }
  });

  _.assign(newErrors, _.mapValues(dataErrors, v => _.join(v, '; ')));

  return newErrors;
};
