import React, { useCallback } from 'react';
import { Checkbox, Button } from 'rsuite';
import _ from 'lodash';

import { useFmtMsgFori18n } from '@/utils/formatMessage';

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

export interface UseCheckConfirmFooterProps {
  checkAndConfirmFooter?: boolean; // 启用footer，支持选中和确认
  data?: Array<any>;
  value?: any;
  onChange?: (val: string[], e?: React.SyntheticEvent<HTMLElement>) => void;
  onFooterSubmitClicked?: () => void;
  footerSubmitLoading?: boolean;
  footerDisabled?: boolean;
}

interface OutProps {
  data?: Array<any>;
  value?: any;
}

const useCheckConfirmFooterForPicker: PropMapper<UseCheckConfirmFooterProps, OutProps> = ({
  data,
  value,
  checkAndConfirmFooter = false,
  onChange,
  footerSubmitLoading = false,
  onFooterSubmitClicked,
  footerDisabled = false,
  ...props
}) => {
  const { formatMessage } = useFmtMsgFori18n();
  const renderExtraFooter = useCallback(() => {
    const selectedNum = _.get(value, 'length', 0);
    const indeterminate = selectedNum < data.length && selectedNum > 0;
    const checked = data.length !== 0 && selectedNum === data.length;

    const handleChange = () => {
      const targetValue = checked ? [] : data.map(s => s.id);
      onChange && onChange(targetValue);
    };

    return (
      <div className="check-confirm-picker-footer">
        <Checkbox inline indeterminate={indeterminate} checked={checked} onChange={handleChange}>
          {formatMessage('i18n_selectAll')}
        </Checkbox>
        <Button
          appearance="primary"
          size="sm"
          disabled={footerDisabled}
          loading={footerSubmitLoading}
          onClick={onFooterSubmitClicked}
        >
          {formatMessage('i18n_confirm')}
        </Button>
      </div>
    );
  }, [value, data, onChange, footerSubmitLoading, onFooterSubmitClicked]);

  return {
    data,
    value,
    onChange,
    ...(checkAndConfirmFooter && { renderExtraFooter }),
    ...props
  };
};

export default useCheckConfirmFooterForPicker;
