export default memo<BasicReportProps>(function ({ id, filterParams }) {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState<DataProps>();
  const [delay, setDelay] = useState(500);

  // 定时器
  useInterval(() => {
    setDelay(1000 * 60);
    setLoading(true);
    onFetchRealTime(
      {
        id,
        secondary: 'daily',
        tag: 'impression',
        [filterParams.selectObj.key]: filterParams.selectObj.value
      },
      res => {
        setLoading(false);
        setData(_.get(res, 'data.data'));
      }
    );
  }, delay);

  // 触发请求
  useEffect(() => {
    setDelay(500);
  }, [id, filterParams]);

  const { formatMessage } = useFmtMsgFori18n();
  const renderHtml = useCallback(() => {
    if (loading) {
      return <LoadingBlock />;
    }
    if (_.isEmpty(data)) {
      return <EmptyBlock />;
    }
    return (
      <>
        <div className="panel-title">{formatMessage('i18n_totayTotalImp')}</div>
        <div className={prefixClassName('card')}>
          <div className={prefixClassName('detail')}>
            <div className={prefixClassName('total')}>
              <div>
                <div>{formatMessage('i18n_accumulatedImpressions')}</div>
                <div>{numberFormat(data.total_pv)}</div>
              </div>
            </div>
            <div className={prefixClassName('total')}>
              <div>
                <div>{formatMessage('i18n_accumulatedUniqueImpressions')}</div>
                <div>{numberFormat(data.total_uv)}</div>
              </div>
            </div>
          </div>
          <div className={prefixClassName('chart')}>
            <Line
              data={data}
              legend={[
                formatMessage('i18n_accumulatedImpressions'),
                formatMessage('i18n_accumulatedUniqueImpressions')
              ]}
            />
          </div>
        </div>
      </>
    );
  }, [loading, data, formatMessage]);

  return (
    <>
      <Panel style={{ height: 289 }}>{renderHtml()}</Panel>
    </>
  );
});
