import React, { useContext, useCallback, useState } from 'react';
import { ButtonToolbar, Button, Divider, Alert } from 'rsuite';
import _ from 'lodash';

import { useMainPage } from '@/components/hooks/useMainPage';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

import {
  defaultAvailableTableParam,
  availableTableParamFormatter,
  getAccBatchResult,
  checkLevelIsCampaign
} from '../constants';
import AvailableTable from './AvailableTable';
import Uploader from './Uploader';
import BatchResultTable from './BatchResultTable';
import { CreateFormCtx } from '../context';

/**
  * summary
  * - [x] 可选表格的内容是Campaign还是Placement，取决于第一步最后一项交叉项目的选择
  * - [x] 已选表格的内容来自于可选表格和批量上传
  * - [x] 上传结果包含正确项和错误项，按现有方式渲染
  * - [x] 错误项不可删除，需在已选表格最前列出；正确项可在已选表格中删除
  * - [ ] 错误项不会随表单最终提交
  * - [x] 可选表格相当于全集，所以注意可选表格checkbox的改变
  * - [x]当前未上传任何文件时，可以通过可选表格勾选一些初始值，上传的时候需要通过selectedId将这些值传给后端
  * - [x] 上传后，仍然可以通过界面上的勾选删除操作来修改已选的值
  * - [x] 需要记录上传前的初始值，因为在上传完成后，对uploader的任何改变(清空或者替换文件)，都是在初始值基础上结合上传结果的，上传后界面上的增删操作会被丢弃
  * - [x] 初始值仅在当前没有上传文件时更新

  举个例子：
  * 初始界面勾选1，已选为[1]，初始值为[1]
  * 上传文件2，已选为[1, 2]
  * 界面勾选3，已选为[1, 2, 3]
  * 替换文件4，已选为[1, 4]，3被丢弃
  * 界面勾选5，已选为[1, 4, 5]
  * 清空文件，已选为[1]，5被丢弃
  * 界面勾选6，已选为[1, 6]，初始值也相应更新为[1, 6]
 */
export default () => {
  const { formatMessage } = useFmtMsgFori18n();
  const { setStep, drawerFormResult, handleCancel } = useContext(CreateFormCtx);
  const {
    formData: { status, batchResult, crossLevel },
    handleFormSubmit,
    drawer
  } = drawerFormResult;
  const { customParamResult } = useMainPage({
    customConfig: [
      {
        // 只有上面的表格是跟其他一样封装请求的，下面的表格数据是batch update返回的
        isTable: true,
        defaultParam: defaultAvailableTableParam,
        paramFormatter: availableTableParamFormatter
      }
    ]
  });

  const [availableTable] = customParamResult;
  const {
    param: availableTableParam,
    fmtParam: availableTableFmtParam,
    handleChangePagination: availabletableChangePagination,
    handleSearchWord: availableTableSearchWord,
    handleChangeCustomParam: availableTableChangeCustomParam
  } = availableTable;

  const handlePrevStep = useCallback(() => {
    setStep(0);
  }, [setStep]);

  const handleSubmit = useCallback(() => {
    const acc = getAccBatchResult(batchResult);
    if (!_.get(acc, 'length', 0)) {
      Alert.error(
        checkLevelIsCampaign(crossLevel) ? 'i18n_selCampaignWarning' : 'i18n_selAdgroupWarning'
      );
    } else if (_.every(acc, i => i.status !== 'ACTIVE')) {
      Alert.error(formatMessage('i18n_noActiveCrossWarning')); // 所交叉对象状态应为ACTIVE或COMPLETE,且至少有一个为ACTIVE
    } else {
      handleFormSubmit();
    }
  }, [batchResult, handleFormSubmit, formatMessage, crossLevel]);

  return (
    <div className="step-2">
      <AvailableTable
        filterParams={availableTableParam}
        setTableParams={availabletableChangePagination}
        tableParams={availableTableFmtParam}
        handleSearchWord={availableTableSearchWord}
        handleChangeCustomParam={availableTableChangeCustomParam}
      />
      <Divider />
      <Uploader />
      <Divider />
      <BatchResultTable />
      <ButtonToolbar>
        <Button onClick={handlePrevStep}>{formatMessage('i18n_prevStep')}</Button>
        <Button
          appearance="primary"
          onClick={handleSubmit}
          loading={drawer.submitting}
          // 编辑的时候会从后端拿到status，且不能更改，创建的时候不存在status
          disabled={_.includes(['CANCELLED', 'COMPLETE'], status)}
        >
          {formatMessage('i18n_save')}
        </Button>
        <Button onClick={handleCancel}>{formatMessage('i18n_cancel')}</Button>
      </ButtonToolbar>
    </div>
  );
};
