import React, { useMemo, useEffect } from 'react';
import _ from 'lodash';

import CheckPicker, { CustomCheckPickerProps } from '@/components/Picker/CheckPicker';
import { getUserProfile } from '@/utils/location';
import * as APIs from '@/constants/APIs';

interface BEPickerProps<T = number> {
  chosenBE: ReadonlyArray<T>;
  beGetParam?: object;
  beChangeHandler?: (beArray: ReadonlyArray<T>) => void;
  isReport?: boolean; //BEPicker是否用于report页面，true=>pickerData从接口获取可以看到所属和共享be,false&&外部用户=>只能看到自己所属be
}

type BEProps = BEPickerProps & CustomCheckPickerProps & { onChange?: never };

const pickerDefaultParam = {
  valueKey: 'id',
  labelKey: 'name',
  supportAll: true,
  searchKeys: ['id', 'name'],
  style: { maxWidth: '300px' },
  menuStyle: { maxWidth: '400px', minWidth: '224px' },
  prepend: 'BE'
};

const defaultGetParam = {
  orderColumn: 'name',
  orderType: 'asc'
};

export default ({ chosenBE, beGetParam, beChangeHandler, isReport = true, ...rest }: BEProps) => {
  const { internal = false, beId = 123, beName = 'TEST' } = getUserProfile();
  const bePickerData = useMemo(() => [{ id: beId, name: beName }], [beId, beName]);
  const beGetParamMemo = useMemo(
    () => _.merge({}, beGetParam, isReport ? { filterPri: 1 } : {}, defaultGetParam),
    [beGetParam]
  );
  useEffect(() => {
    // 如果是外部的，并且beId存在的话并且isReport=false，那么就触发一次onchange,默认勾选
    if (!internal && beId && !isReport) {
      beChangeHandler && beChangeHandler([beId]);
    }
    // FIXME: 有一个循环，如果这里加了beChangeHandler 作为deps 之后，filterParam的改变会更新changeHandler，这时候就无限循环了。。
    // }, [beId, internal, beChangeHandler]);
    // FIXME：等所有用到的地方都统一成useMainPage的时候，就可以补回来了
  }, [beId, isReport, internal]);

  // 如果是外部用户并且不在报告页面中，直接从env中的be加载，只有一个
  if (!internal && !isReport) {
    return (
      <CheckPicker
        value={chosenBE}
        onChange={beChangeHandler}
        data={bePickerData}
        searchable={false} // 一个就不给搜索了
        cleanable={false} // 也不给你删
        countable={false}
        {...pickerDefaultParam}
        {...rest}
      />
    );
  }

  // 通过接口获取be列表
  return (
    <CheckPicker
      value={chosenBE}
      onChange={beChangeHandler}
      url={APIs.API_BE}
      params={beGetParamMemo}
      {...pickerDefaultParam}
      {...rest}
    />
  );
};
