import React from 'react';

import { DateCtxProps, defaultDateProps } from '@/components/dateSource';

import { CampaignType } from './types.d';

export interface CampaignCtxProps {
  type: CampaignType;
  campaignDetail: BaseObject<any>;
  campaignDetailLoading: boolean;
  campaignValidUrls: Array<string>;
  campaignValidUrlsLoading: boolean;
}

export const CampaignCtx = React.createContext<CampaignCtxProps>({
  type: CampaignType.CAMPAIGN,
  campaignDetail: {},
  campaignDetailLoading: true,
  campaignValidUrls: [],
  campaignValidUrlsLoading: true
});

export const DateCtx = React.createContext<DateCtxProps>(defaultDateProps);
