import React, { memo, useMemo, useContext, useEffect } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';
import { useMainPage } from '@/components/hooks/useMainPage';
import { basicClassName, defaultFilter, filterParamKeys, FilterParam } from './constants';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import Summary from './Summary';
import Chart from '@/components/Chart/ReportCustomChart';
import Table from './Table';
import Toolbar from './Toolbar';
import { DateCtx } from '@/views/campaigns/common/context';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

const customParamFormatter = (filterParam: FilterParam) => {
  const originModel = _.get(filterParam, filterParamKeys.ATTRMODEL);
  const originPara = _.get(filterParam, filterParamKeys.PARA);
  return {
    ...filterParam,
    [filterParamKeys.ATTRMODEL]: !_.isEmpty(originModel) ? originModel.join(',') : '',
    [filterParamKeys.PARA]: !_.isEmpty(originPara) ? originPara.join(',') : ''
  };
};

export default memo(() => {
  const { commonParam, fmtCommonParam, handleChangeCommonParam, customParamResult } = useMainPage({
    defaultCommonParam: defaultFilter,
    customConfig: [
      {
        isTable: true,
        paramFormatter: customParamFormatter
      }
    ]
  });
  const { formatMessage } = useFmtMsgFori18n();
  const [tableResult] = customParamResult; // 根据传入的idx对应从结果中取出特定的custom
  const {
    param,
    fmtParam,
    handleChangePagination,
    handleChangeCustomParam,
    handleSearchWord
  } = tableResult;

  const {
    dateRangeVal,
    dateRangeValTimeFormat,
    setDateRangeValByDateArray,
    validDateLoaded,
    maxDateFromRespBeforePreset,
    reportDateFn,
    minDateFromResp
  } = useContext(DateCtx);

  const campaignId = useCampaignId();

  const postParams = useMemo(
    () => ({
      id: campaignId,
      hierarchy: 'campaign',
      report: 'cpo'
    }),
    [campaignId]
  );

  useEffect(() => {
    reportDateFn({
      params: {
        secondary: 'daily',
        report: 'cpo'
      }
    });
  }, [reportDateFn]);

  if (!validDateLoaded) {
    return null;
  }
  return (
    <>
      <div className={basicClassName}>
        <Panel className="page-toolbar">
          <Toolbar
            dateVal={dateRangeVal}
            maxDate={maxDateFromRespBeforePreset}
            minDate={minDateFromResp}
            filterParams={commonParam}
            handleChangeDate={setDateRangeValByDateArray}
            handleChangeCommonParam={handleChangeCommonParam}
          />
        </Panel>
        <Summary dateParams={dateRangeValTimeFormat} postParams={postParams} />
        <Chart
          dateParams={dateRangeValTimeFormat}
          filterParams={fmtCommonParam}
          postParams={postParams}
          collapsible
          yAxis={[{ type: 'number', name: formatMessage('i18n_impressions') }]}
          title={formatMessage('i18n_trendView')}
          pure
        />
        <Panel className="panel-table">
          <Table
            dateParams={dateRangeValTimeFormat}
            filterParams={param}
            tableParams={fmtParam}
            postParams={postParams}
            setTableParams={handleChangePagination}
            handleChangeCustomParam={handleChangeCustomParam}
            handleSearchWord={handleSearchWord}
          />
        </Panel>
      </div>
    </>
  );
});
