import React, { memo } from 'react';
import { Panel } from 'rsuite';

import CustomRadioGroup from '@/components/Form/RadioGroup';

interface Props {
  title: string;
  filters?: Array<object>;
  active?: string;
  onChange: Function;
  children?: React.ReactNode;
}

export default memo<Props>(function({ title, filters, active, onChange, children }) {
  return (
    <>
      <Panel>
        <div className="overview-card">
          {title}
          <CustomRadioGroup
            inline
            appearance="picker"
            data={filters}
            value={active}
            onChange={onChange as any}
            useDefaultStyle
          />
        </div>
        {children}
      </Panel>
    </>
  );
});
