import * as React from 'react';
import _ from 'lodash';

import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import getRandomNum from '@/utils/random';

/**
 * 用于需要请求获取下拉菜单数据的Picker，
 * 可设置请求参数和请求时机
 */

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

export interface UseFetchProps {
  url?: Url; // 请求api，若未设置则不触发请求
  deps?: ReadonlyArray<any>; // 依赖项，当依赖项发生变化且不为空时，触发请求；若未设置，则只在组件加载时触发请求
  params?: Record<string, any>; // 请求参数
  dataPath?: string | string[]; // 用于指明response里数据的路径
  data?: any[]; // 若设置data，则直接赋值，不触发请求
  onFetchSuccess?: (data: any[]) => void; // 请求成功后的回调
  dataRespFormatter?: (data: any[]) => any[];
  loading?: boolean;
}

export interface OutProps {
  loading: boolean;
  data: any[];
}

const { useState, useEffect } = React;

const useFetchForPicker: PropMapper<UseFetchProps, OutProps> = ({
  url,
  deps,
  params,
  dataPath = 'data.records',
  data,
  onFetchSuccess,
  dataRespFormatter,
  loading = false,
  ...props
}) => {
  const [randomMutex] = useState(getRandomNum());
  const { onFetch: onFetchPickerData, loading: pickerLoading } = useFetch<CommonResponse>(
    FETCH_METHOD.GET,
    url,
    {
      cacheParams: true,
      mutex: randomMutex
    }
  );

  const [pickerData, setPickerData] = useState<any[]>([]);

  // 如果定义了deps，则依据deps的变化获取pickerData
  useEffect(() => {
    if (!_.isArray(data) && !_.isNil(url) && !_.isNil(deps) && !_.some(deps, _.isNil)) {
      onFetchPickerData({ params }).then(rsp => {
        const dataResp = _.get(rsp, dataPath, []);
        const newPickerData = _.isFunction(dataRespFormatter)
          ? dataRespFormatter(dataResp)
          : dataResp;
        setPickerData(newPickerData);
        onFetchSuccess && onFetchSuccess(newPickerData);
      });
    }
  }, deps);

  // 如果未定义deps，则只在打开form时获取pickerData
  useEffect(() => {
    if (!_.isArray(data) && !_.isNil(url) && _.isNil(deps)) {
      onFetchPickerData({ params }).then(rsp => {
        const dataResp = _.get(rsp, dataPath, []);
        const newPickerData = _.isFunction(dataRespFormatter)
          ? dataRespFormatter(dataResp)
          : dataResp;
        setPickerData(newPickerData);
        onFetchSuccess && onFetchSuccess(newPickerData);
      });
    }
  }, []);

  return {
    loading: pickerLoading || loading,
    data: _.isArray(data) ? data : pickerData,
    ...props
  };
};

export default useFetchForPicker;
