import React, { memo, useCallback } from 'react';
import _ from 'lodash';

import * as APIs from '@/constants/APIs';
import DatePicker from '@/components/Picker/DatePicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { dateIsAfter, dateIsBefore, getToday, subDate } from '@/utils/date/index';

import { filterParamKeys, utilsFn } from './constants';
import { BasicReportProps } from '@/constants/config';

const campaignReqParam = {
  orderColumn: 'name',
  orderType: 'asc'
};
const campaignRespFormatter = (data: any[]) => {
  return data.map(({ id, name, primary }) => ({
    id,
    name,
    primary: _.get(primary, 'beName', '')
  }));
};

export default memo(({ filterParams, handleChangeCommonParam }: BasicReportProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const campaignIds = utilsFn.getCampainIdList(filterParams);
  const dateVal = utilsFn.getDate(filterParams);

  const chosenCampaignChangeHandler = useCallback(
    val => {
      handleChangeCommonParam({
        [filterParamKeys.CAMPAIGN]: val
      });
    },
    [handleChangeCommonParam]
  );

  const dateChangeHandler = useCallback(
    (val: Date) => {
      handleChangeCommonParam({
        [filterParamKeys.DATE]: val
      });
    },
    [handleChangeCommonParam]
  );

  const disabledDate = useCallback((date: Date) => {
    const today = getToday();
    return dateIsAfter(date, today) || dateIsBefore(date, subDate(today, 6, 'month'));
  }, []);

  return (
    <>
      <DatePicker
        value={dateVal}
        cleanable={false}
        onChange={dateChangeHandler}
        disabledDate={disabledDate}
        appearance="subtle"
      />
      <CheckPicker
        url={APIs.API_CAMPAIGN}
        value={campaignIds}
        onChange={chosenCampaignChangeHandler}
        params={campaignReqParam}
        appearance="subtle"
        groupBy="primary"
        dataRespFormatter={campaignRespFormatter}
        prepend={formatMessage('i18n_campaign')}
        style={{ maxWidth: '300px' }}
        menuStyle={{ maxWidth: '400px' }}
      />
    </>
  );
});
