import { combineReducers } from 'redux-immutablejs';
import _ from 'lodash';

const requireFunctionModel = require.context('../../views', true, /\/reducer\.ts$/);
const reducerPaths = requireFunctionModel.keys();
const reducers = reducerPaths.reduce((obj: BaseObject<any>, path: string) => {
  const isRootReducer = path === './reducer.ts';
  const keyPath = isRootReducer
    ? 'root'
    : path
        .replace(/^\.\/(.*)\/reducer\.ts$/, '$1')
        .split('/')
        .join('_');
  const reducer = requireFunctionModel(path).default;
  obj[_.snakeCase(keyPath)] = reducer;
  return obj;
}, {});

export default combineReducers(reducers);
