import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import * as APIs from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell, DateCell, TextCell, HeaderSummary } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import DropDown from '@/components/Picker/DropDown';
import { BasicReportProps } from '@/constants/config';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import download from '@/utils/download';
import {
  attributeModelData,
  attributesData,
  dropDownData,
  FilterParam,
  filterParamKeys
} from './constants';
import { getUserProfile } from '@/utils/location';

const mockData: CustomTableDataProps<CellDataProps> = {
  data: [
    {
      secondary: '2020/03/09',
      value: 'aqy',
      placementId: 1,
      impressions: 40,
      clicks: 20,
      uimpressions: '--',
      uclicks: '--',
      clickRate: 50.0,
      placementName: 'test'
    }
  ],
  page: {
    current: 0,
    pagesize: 10,
    total: 6,
    size: 100
  },
  total: {
    impressions: 140,
    uimpressions: 70,
    clicks: 70,
    uclicks: '--'
  }
};
interface CellDataProps {
  secondary: string;
  value: string;
  placementId: number;
  impressions: string | number;
  clicks: string | number;
  uimpressions: string | number;
  uclicks: string | number;
  clickRate: number;
  placementName: string;
}

type TableProps = Partial<BasicReportProps<FilterParam>>;
export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    handleSearchWord,
    handleChangeCustomParam,
    setTableParams
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();

    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: APIs.API_TAB_REPORT,
      postParams
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    //导出表格
    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport(
          {
            ...dateParams,
            ...tableParams,
            ...postParams
          },
          APIs.API_XLSX
        );
    }, [onExport, tableParams, dateParams]);

    const attributeModelDataIntl = usePickerDataFromObj(attributeModelData);
    const paraDataIntl = usePickerDataFromObj(attributesData);
    const dropDownDataIntl = usePickerDataFromObj(dropDownData, { labelKey: 'label' });
    const { beId } = getUserProfile();

    //自定义属性最多选2项
    const handleChangePara = useCallback(value => {
      const newValue = _.size(value) > 2 ? _.drop(value) : value;
      handleChangeCustomParam({ [filterParamKeys.PARA]: newValue });
    }, []);
    //导出下拉
    const handleDropDownSelect = useCallback(
      key => {
        if (key === 'table') {
          handleExportClicked();
        } else {
          download(APIs.API_CPO_EXPORT, {
            ...dateParams,
            ...tableParams,
            campaignId: _.get(postParams, 'id')
          });
        }
      },
      [tableParams, dateParams]
    );
    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SecondaryPicker
          key="secondary"
          onChange={(value: string) => handleChangeCustomParam({ [filterParamKeys.SEC]: value })}
          value={filterParams[filterParamKeys.SEC]}
          data={['total', 'daily']}
          cleanable={false}
        />,
        <CheckPicker
          key="attrModel"
          prepend={formatMessage('i18n_attribution')}
          placeholder={formatMessage('i18n_select', { name: '' })}
          data={attributeModelDataIntl}
          value={filterParams[filterParamKeys.ATTRMODEL]}
          style={{ maxWidth: '250px' }}
          onChange={(value: any) => handleChangeCustomParam({ [filterParamKeys.ATTRMODEL]: value })}
        />,
        <SelectPicker
          key={filterParamKeys.ACTION}
          prepend={formatMessage('i18n_conversionActivities')}
          placeholder={formatMessage('i18n_select', { name: '' })}
          url={APIs.API_CONVERSION}
          params={{
            cpoCampaignId: _.get(postParams, 'id')
          }}
          searchable
          cleanable
          supportAll
          value={filterParams[filterParamKeys.ACTION]}
          style={{ maxWidth: '250px' }}
          onChange={(value: any) => handleChangeCustomParam({ [filterParamKeys.ACTION]: value })}
        />,
        <CheckPicker
          key={filterParamKeys.PARA}
          prepend={formatMessage('i18n_customAttributes')}
          placeholder={formatMessage('i18n_select', { name: '' })}
          data={paraDataIntl}
          value={filterParams[filterParamKeys.PARA]}
          style={{ maxWidth: '250px' }}
          onChange={handleChangePara}
          searchable={false}
        />,
        <DropDown
          key="export"
          data={dropDownDataIntl as any}
          className="pull-right"
          title={formatMessage('i18n_exportData')}
          appearance="default"
          onSelect={handleDropDownSelect}
        />
      ];
    };
    //渲染自定义属性
    const renderParaCol = (): React.ReactElement[] => {
      let list: React.ReactElement[] = [];
      paraDataIntl.forEach((i: any) => {
        if (_.includes(filterParams[filterParamKeys.PARA], i.id) && i.id !== 'no') {
          list.push(
            <Column key={i.id} width={200} resizable sortable>
              <HeaderCell>{i.name}</HeaderCell>
              <Cell dataKey={`${i.id}`} />
            </Column>
          );
        }
      });
      return list;
    };
    // 渲染模型
    const renderModelCol = (): React.ReactElement[] => {
      const data = ['ROI', 'CPO', 'CPA', 'ATV'];
      let list: React.ReactElement[] = [];
      attributeModelDataIntl.forEach((i: any) => {
        if (_.includes(filterParams[filterParamKeys.ATTRMODEL], i.id)) {
          data.forEach(j => {
            list.push(
              <Column key={`${i.id}#${j}`} width={200} resizable sortable align="right">
                <HeaderCell>
                  <HeaderSummary title={i.name} summary={j} />
                </HeaderCell>
                <Cell dataKey={`${i.id}#${j}`} />
              </Column>
            );
          });
        }
      });
      return list;
    };
    // 渲染表列
    const renderCol = (): any => {
      const list = [];
      list.push(
        <Column width={150} sortable resizable fixed key="name">
          <HeaderCell>{formatMessage('i18n_cpoGroupName')}</HeaderCell>
          <TextCell dataKey="groupName" maxLine={1} />
        </Column>,
        renderParaCol(),
        filterParams.secondary === 'daily' && (
          <Column
            width={filterParams.secondary === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams.secondary === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage('i18n_daily')}
            </HeaderCell>
            <DateCell dataKey="dateslot" />
          </Column>
        ),
        <Column width={150} align="right" sortable key="impressions">
          <HeaderCell>{formatMessage('i18n_impressionsNum')}</HeaderCell>
          <NumberCell dataKey="impressions" />
        </Column>,
        <Column width={150} align="right" sortable key="uimpressions">
          <HeaderCell>{formatMessage('i18n_uniqueImpressionsNum')}</HeaderCell>
          <NumberCell dataKey="uimpressions" />
        </Column>,
        <Column width={150} align="right" sortable key="clicks">
          <HeaderCell>{formatMessage('i18n_clicksNum')}</HeaderCell>
          <NumberCell dataKey="clicks" />
        </Column>,
        <Column width={200} align="right" sortable key="uclicks">
          <HeaderCell>{formatMessage('i18n_uniqueClicksNum')}</HeaderCell>
          <NumberCell dataKey="uclicks" />
        </Column>,
        renderModelCol()
      );
      return list;
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange} headerHeight={60}>
        {renderCol()}
      </Table>
    );
  }
);
