import dayjs from 'dayjs';
import { CONF_DATE } from '@/constants/config';

export const YYYYMMDD = (d = new Date() as dayjs.ConfigType): string => {
  return dayjs(d).format(CONF_DATE);
};

export const YYYYMMDD_ADD_DAY = (d = new Date() as dayjs.ConfigType, unit = 0) => {
  return dayjs(dayjs(d).add(unit, 'day')).format(CONF_DATE);
};

export const toDate = (d: any) => (d === null || d === undefined ? '--' : YYYYMMDD(d));
