import React, { useCallback } from 'react';
import _ from 'lodash';
import { Input } from 'rsuite';
import PickerGroup from '../PickerGroup';
import CheckPicker from '../CheckPicker';
import { API_PLACEMENT } from '@/constants/APIs';
import { UseFetchProps } from '@/components/Picker/PickerHooks/useFetchForPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface Props {
  values?: Record<string, any>;
  errors?: Record<string, string>;
  groupName?: string;
  placementName?: string;
  disabled?: boolean;
  cleanable?: boolean;
  fetchOptions?: UseFetchProps;
  onChange?: Function;
  reqParams?: object;
}

export default React.memo(
  ({
    values,
    errors,
    groupName,
    placementName,
    disabled = false,
    onChange,
    fetchOptions,
    reqParams
  }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();

    const handleFetchSuccess = useCallback(
      data => {
        _.isFunction(_.get(fetchOptions, 'onFetchSuccess')) && fetchOptions.onFetchSuccess(data);
      },
      [fetchOptions]
    );
    return (
      <PickerGroup
        values={values}
        errors={errors}
        onChange={onChange} // onChange会通过PickerGroup分配给以下两个picker，但是be的onChange有额外行为
      >
        <Input style={{ width: 200 }} maxLength={30} name={groupName} placeholder="Group" />
        <CheckPicker
          name={placementName}
          prepend={formatMessage('i18n_placement')}
          url={API_PLACEMENT}
          disabled={disabled}
          onFetchSuccess={handleFetchSuccess}
          params={reqParams}
        />
      </PickerGroup>
    );
  }
);
