import React, { memo, useReducer, useMemo, useCallback, useState } from 'react';
import _ from 'lodash';
import { Panel, Breadcrumb, Button, FormGroup, ButtonToolbar } from 'rsuite';
import { useHistory } from 'react-router';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { API_KEYWORD_BATCHES, API_ADGROUP_CREATIVE_UPLOAD } from '@/constants/APIs';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import RadioGroup from '@/components/Form/RadioGroup';
import { KeywordModel } from '@/constants/form/models';
import { FileType } from 'rsuite/lib/Uploader';
import Toolbar from './Toolbar';
import { UploadReducerAction, UploadReducerType, UploadReducerState } from '../constants';
import { SingleFileUploader } from '@/components/Form/SingleFileUploader';
import download from '@/utils/download';
import { UploadCtx } from './context';
import { defaultFormData, keywordData, urlData } from './constants';

const reducer = (state: UploadReducerState, action: UploadReducerAction) => {
  switch (action.type) {
    case UploadReducerType.SET_UPLOADING_STATUS: {
      const { isUploading } = action;
      return {
        ...state,
        isUploading,
        data: []
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_START: {
      return {
        ...state,
        loading: true,
        isUploading: false
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_ACC: {
      const { data } = action;
      return {
        ...state,
        data,
        loading: false,
        isUploading: false
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_EXP: {
      return {
        ...state,
        loading: false,
        isUploading: false
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_START: {
      return {
        ...state,
        reqDelay: 3000
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_END: {
      return {
        ...state,
        reqDelay: null, // null表示不再执行了
        loading: false,
        isUploading: false
      };
    }

    default:
      return state;
  }
};
const fileKeyInForm = 'fileName';

export default memo(() => {
  const [show, setShow] = useState<boolean>(false);
  const { formatMessage } = useFmtMsgFori18n();
  const [info, setInfo] = useState<string>(null); // 错误信息
  const history = useHistory();

  const [state, dispatch] = useReducer(reducer, {
    isUploading: false,
    data: [],
    loading: false,
    reqDelay: null
  });
  const uploadCtxMemoVal = useMemo(
    () => ({
      state,
      dispatch
    }),
    [state, dispatch]
  );

  const {
    formData,
    formErrors,
    formRef,
    handleFormChange,
    setFormErrors,
    checkForm,
    setDrawer,
    drawer
  } = useDrawerForm({
    defaultFormData,
    api: API_KEYWORD_BATCHES, // useless
    model: KeywordModel as any
  });

  const shouldUpload = useCallback(
    (file: FileType) => {
      const ext = _.last(file.name.split('.'));
      const result = _.lowerCase(ext) === 'xlsx';
      if (!result) {
        setFormErrors(prevErrors => ({
          ...prevErrors,
          [fileKeyInForm]: formatMessage('i18n_uploadFileTypeError')
        }));
      }
      return Boolean(result);
    },
    [formatMessage]
  );

  const onSuccess = useCallback(
    res => {
      handleFormChange((prev: any) => ({
        ...prev,
        fileName: _.get(res, 'file_path')
      }));
      dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_START });
    },

    [dispatch, handleFormChange]
  );

  const onProgress = useCallback(() => {
    dispatch({ type: UploadReducerType.SET_UPLOADING_STATUS, isUploading: true });
  }, [dispatch]);

  const onRemove = useCallback(() => {
    dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_END });
    handleFormChange((prev: any) => ({
      ...prev,
      fileName: ''
    }));
    setInfo(null);
    setShow(false);
  }, [dispatch, setInfo, handleFormChange]);

  const onError = useCallback(
    res => {
      const err = _.get(res, 'response.fields.error.0.message', '');
      if (err) {
        setFormErrors(prevErrors => ({
          ...prevErrors,
          [fileKeyInForm]: err
        }));
      }

      dispatch({ type: UploadReducerType.SET_UPLOADING_STATUS, isUploading: false });
    },
    [dispatch]
  );

  const resetFormError = useCallback(() => {
    setFormErrors(prevErrors => ({
      ...prevErrors,
      [fileKeyInForm]: ''
    }));
  }, []);

  const UploaderComp = useCallback(
    () => (
      <SingleFileUploader
        accept=".xlsx"
        autoUpload
        action={API_ADGROUP_CREATIVE_UPLOAD}
        shouldUpload={shouldUpload}
        onSuccess={onSuccess}
        onError={onError}
        onReupload={resetFormError}
        onProgress={onProgress}
        onRemove={onRemove}
        onChange={resetFormError}
      />
    ),
    [shouldUpload, onSuccess, onError, resetFormError, onProgress, onRemove]
  );
  const urlDataIntl = usePickerDataFromObj(urlData);

  const { onFetch: onSubmit } = useFetch<CommonResponse>(FETCH_METHOD.POST, API_KEYWORD_BATCHES, {
    disabledNotification: true
  });
  const { onFetch } = useFetch<CommonResponse>(FETCH_METHOD.GET, API_KEYWORD_BATCHES, {
    disabledNotification: true
  });
  const handleSubmit = useCallback(() => {
    if (!formRef.current.check()) {
      return;
    }
    //设置提交状态
    setDrawer((prevState: any) => ({
      ...prevState,
      submitting: true
    }));
    setShow(false);
    onSubmit({ data: formData })
      .then(res => {
        const { errors } = res;
        const serverErrors = _.mapValues(errors, val => String(val));
        return _.isEmpty(serverErrors) ? Promise.resolve(res) : Promise.reject(serverErrors);
      })
      .then(res => {
        setShow(true);
        const taskId = _.get(res, 'data.taskId', '');
        if (!taskId) {
          // 取消提交加载状态
          setDrawer((prevState: any) => ({
            ...prevState,
            submitting: false
          }));
          return;
        }
        let timer = setInterval(() => {
          onFetch({ params: { taskId } })
            .then(rsp => {
              // 批量错误显示为错误文件的下载链接
              if (_.get(rsp, 'data.info')) {
                return Promise.reject(_.get(rsp, 'data.info'));
              }
              const {
                data: { errors }
              } = rsp;

              return _.isEmpty(errors) || !errors
                ? Promise.resolve(rsp.data)
                : Promise.reject(errors);
            })
            .then(rsp => {
              if (!_.isEmpty(rsp) && rsp) {
                // 取消提交加载状态
                setDrawer((prevState: any) => ({
                  ...prevState,
                  submitting: false
                }));
                clearInterval(timer);
                download(API_KEYWORD_BATCHES, {
                  taskId
                });
              }
            })
            .catch((err: any) => {
              if (_.isString(err)) {
                if (err.indexOf('/') > -1) {
                  err = err.split('/');
                  err = err[err.length - 1];
                  err = `/api/v1/batchImportError?fileId=${err}`;
                }
                setInfo(err);
              } else {
                setFormErrors(err);
              }
              // 取消提交加载状态
              setDrawer((prevState: any) => ({
                ...prevState,
                submitting: false
              }));
              clearInterval(timer);
            });
        }, 3000);
      })
      .catch((err: any) => {
        setFormErrors(err);
        // 取消提交加载状态
        setDrawer((prevState: any) => ({
          ...prevState,
          submitting: false
        }));
      });
  }, [checkForm, formData, formatMessage, setFormErrors, setDrawer]);

  return (
    <>
      <Breadcrumb className="campaign-navigator">
        <Breadcrumb.Item
          onClick={() => {
            history.push('/placement-management');
          }}
        >
          {formatMessage('i18n_menuAdunitManage')}
        </Breadcrumb.Item>
        <Breadcrumb.Item
          onClick={() => {
            history.push('/placement-management/keyword');
          }}
        >
          {formatMessage('i18n_keywordCode')}
        </Breadcrumb.Item>
      </Breadcrumb>
      <UploadCtx.Provider value={uploadCtxMemoVal}>
        <Panel className="upload-panel-no-overflow">
          <Toolbar />
        </Panel>
        <Panel>
          <Form
            formData={formData}
            formErrors={formErrors}
            model={KeywordModel}
            formRef={formRef}
            onChange={handleFormChange as any}
            setFormErrors={setFormErrors}
            checkForm={checkForm}
          >
            {/* 关键词编码 */}
            <CustomField
              name="keywordEncodeType"
              label={formatMessage('i18n_keywordEncode')}
              accepter={RadioGroup}
              data={keywordData}
              useDefaultStyle={true}
            />
            {/* 跳转地址编码 */}
            <CustomField
              name="clkEncodeType"
              label={formatMessage('i18n_urlAddressCode')}
              accepter={RadioGroup}
              data={urlDataIntl}
              useDefaultStyle={true}
            />
            <CustomField name="fileName" accepter={UploaderComp} />

            {info && (
              <a
                style={{ margin: '24px auto', color: '#f44336', display: 'block' }}
                href={info}
                target="blank"
              >
                {formatMessage('i18n_viewDetailsError')}
              </a>
            )}
            <FormGroup>
              <ButtonToolbar>
                <Button
                  appearance="primary"
                  loading={drawer.submitting}
                  onClick={handleSubmit as () => void}
                >
                  {formatMessage('i18n_execute')}
                </Button>
                {show && (
                  <span style={{ color: '#f44336' }}>{formatMessage('i18n_keywordTips')}</span>
                )}
              </ButtonToolbar>
            </FormGroup>
          </Form>
        </Panel>
      </UploadCtx.Provider>
    </>
  );
});
