import React from 'react';
import _ from 'lodash';

import CheckPicker from '@/components/Picker/CheckPicker';

import { FilterCommonProps } from './common';

export default ({
  filterAsyncKeys,
  data,
  loading,
  value,
  disabled,
  onChange,
  labelKey,
  valueKey,
  mapLabel
}: FilterCommonProps) => {
  const role = _.has(data, '0.role');

  return (
    <CheckPicker
      data={data}
      loading={loading}
      value={value}
      onChange={onChange}
      cleanable
      style={{ width: 250, maxHeight: 250 }}
      appearance="subtle"
      groupBy={role ? 'role' : null}
      disabled={disabled}
      labelKey={labelKey}
      valueKey={valueKey}
      mapLabel={mapLabel}
      {..._.get(filterAsyncKeys, 'length', 0) > 0 && { searchKeys: [] }}
    />
  );
};
