import React, { memo, useState, useCallback, useEffect } from 'react';
import { Panel } from 'rsuite';
import { RouteComponentProps, useLocation } from 'react-router-dom';
import _ from 'lodash';

import { useMainPage } from '@/components/hooks/useMainPage';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import { API_ADGROUP } from '@/constants/APIs';
import { AdgroupModel } from '@/constants/form/models';

import Table from './Table';
import Toolbar from './Toolbar';
import DrawerForm from './DrawerForm';
import { mapFromResponse, mapToRequest } from './dataMapper';
import {
  filterParamKeys,
  FilterParam,
  defaultFormData,
  defaultCampaignIdKeyFromState,
  defaultCommonParam,
  defaultCustomParam
} from './constants';

const filterParamFormatter = (filterParam: FilterParam) => {
  const originCampaign = filterParam[filterParamKeys.CAMPAIGN];
  return {
    ...filterParam,
    [filterParamKeys.CAMPAIGN]: originCampaign.toString()
  };
};

type RootCompProps = RouteComponentProps<{}>;

export default memo(({ location }: RootCompProps) => {
  const { state } = useLocation();
  const defaultCmpId = _.get(state, defaultCampaignIdKeyFromState) || '';
  const { commonParam, handleChangeCommonParam, customParamResult } = useMainPage({
    defaultCommonParam: {
      ...defaultCommonParam,
      ...(defaultCmpId !== '' && { [filterParamKeys.CAMPAIGN]: [Number(defaultCmpId)] })
    },
    commonParamFormatter: filterParamFormatter,
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultCustomParam
      }
    ]
  });

  const [tableResult] = customParamResult;
  const {
    param,
    fmtParam,
    handleSearchWord,
    handleChangePagination,
    handleChangeCustomParam
  } = tableResult;

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  const {
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    handleFormChange,
    handleFormCreate,
    handleFormEdit,
    handleFormClose,
    handleFormSubmit,
    setFormErrors,
    checkForm
  } = useDrawerForm({
    api: API_ADGROUP,
    defaultFormData,
    model: AdgroupModel,
    mapFromResponse,
    mapToRequest,
    submitSuccessCallback: refetchTableData
  });

  const [allCampaigns, setAllCampaigns] = useState([]);
  //渲染完成之后清空history中的state
  useEffect(() => {
    history.replaceState({}, '');
  }, []);
  return (
    <>
      <Panel className="page-toolbar">
        <Toolbar
          filterParams={commonParam}
          handleChangeCommonParam={handleChangeCommonParam}
          allCampaigns={allCampaigns}
          setAllCampaigns={setAllCampaigns}
        />
      </Panel>
      <Panel className="panel-table">
        <Table
          location={location}
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          refetchTableData={refetchTableData}
          handleSearchWord={handleSearchWord}
          handleChangeCustomParam={handleChangeCustomParam}
          handleFormCreate={handleFormCreate}
          handleFormEdit={handleFormEdit}
        />
        <DrawerForm
          drawer={drawer}
          formData={formData}
          originalFormData={originalFormData}
          formErrors={formErrors}
          formRef={formRef}
          onChange={handleFormChange}
          onSubmit={handleFormSubmit}
          onClose={handleFormClose}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
          allCampaigns={allCampaigns}
        />
      </Panel>
    </>
  );
});
