import React, { memo, useMemo, useCallback } from 'react';
import { Button } from 'rsuite';

import * as APIs from '@/constants/APIs';
import Table, { Column, HeaderCell } from '@/components/Table';
import { DateTimeCell, FloatActionCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { FULL_DATE_HOURS_MINUTES } from '@/constants/dateFormat';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps } from '@/constants/config';
import { useModal } from '@/components/hooks/useModal';

import ExportStatusCell from '../part/ExportStatusCell';
import FileCell from '../part/FileCellForHaa';
import Modal from './Modal';

type TableProps = BasicReportProps & {
  handleFormCreate: Function;
  handleFormEdit: Function;
  refetchTableData: Function;
};

export default memo(
  ({
    tableParams,
    setTableParams,
    handleFormCreate,
    handleFormEdit,
    refetchTableData
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onChange } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_TASK_EXPORT
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
    const modal = useModal(false, {}, APIs.API_TASK_EXPORT_PUT, refetchTableData);
    const onUpdate = modal.onFetch;
    const renderToolbar = useCallback(() => {
      return (
        <>
          <Button
            appearance="primary"
            onClick={() => {
              handleFormCreate();
            }}
          >
            {formatMessage('i18n_newTask')}
          </Button>
        </>
      );
    }, [formatMessage, handleFormCreate]);

    const renderActions = useCallback(
      (rowData: Record<string, any>) => {
        return (
          <>
            <FloatActionCell.Actions.Edit
              onClick={() => {
                handleFormEdit({
                  id: rowData.id
                });
              }}
            />
            <FloatActionCell.Actions.Delete onClick={() => modal.showModal({ id: rowData.id })} />
          </>
        );
      },
      [handleFormEdit, modal]
    );

    return (
      <>
        <Modal {...modal} />
        <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange} wordWrap>
          <Column flexGrow={1} minWidth={120} sortable>
            <HeaderCell>{formatMessage('i18n_taskId')}</HeaderCell>
            <FloatActionCell dataKey="id" />
          </Column>
          <Column flexGrow={2} minWidth={200} sortable>
            <HeaderCell>{formatMessage('i18n_taskName')}</HeaderCell>
            <FloatActionCell dataKey="name" renderActions={renderActions} />
          </Column>
          <Column flexGrow={1} minWidth={200} sortable>
            <HeaderCell>{formatMessage('i18n_createTime')}</HeaderCell>
            <DateTimeCell dataKey="createTime" format={FULL_DATE_HOURS_MINUTES} />
          </Column>
          <Column flexGrow={1} minWidth={200} sortable>
            <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
            <ExportStatusCell
              dataKey="status"
              refetchTableData={refetchTableData}
              onFetch={onUpdate}
            />
          </Column>
          <Column flexGrow={2} minWidth={300}>
            <HeaderCell>{formatMessage('i18n_filePath')}</HeaderCell>
            <FileCell dataKey="files" />
          </Column>
        </Table>
      </>
    );
  }
);
