import React, { memo } from 'react';
import _ from 'lodash';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';
import { DataProps } from '../type';

interface Props {
  legend: string[];
  data: DataProps;
}

export default memo<Props>(function({ legend, data }) {
  const cols = [];
  for (let i = 0; i < 24.5; i += 0.5) {
    cols.push(i);
  }

  const pv = data.time_slot_pv;
  const uv = data.time_slot_uv;
  let timeSlotUv = [],
    timeSlotPv = [];
  pv.forEach((item: any) => {
    timeSlotPv.push(item.value);
  });
  uv.forEach((item: any) => {
    timeSlotUv.push(item.value);
  });
  timeSlotPv.unshift(0);
  timeSlotUv.unshift(0);

  const newData = [
    {
      name: legend[0],
      data: timeSlotPv
    },
    {
      name: legend[1],
      data: timeSlotUv
    }
  ];

  const config = {
    type: CHART_KEY.LINE,
    data: {
      cols,
      data: newData
    },
    xAxis: {
      type: 'category',
      axisLabel: {
        interval: 1
      }
    },
    hasTools: false
  };

  return <Chart config={config} height={217} pure />;
});
