import _ from 'lodash';

import RESPONSE_STATUS from '@/constants/responseStatus';

/**
 * 转换为 FormData
 * @param action
 * @return {{status: *, data: {}}}
 */
export function toDefaultData(action: any, dataKey = 'data') {
  const { status, response } = action;
  return {
    status,
    data: status === RESPONSE_STATUS.SUCCESS ? _.get(response, dataKey) || {} : {},
    errors: response.errors || null
  };
}

/**
 *
 * @param key
 * @return {function(*, *=): *}
 */
export const storeAsDefault = (key = 'formData', dataKey = 'data') => (state: any, action: any) =>
  state.merge({
    [key]: toDefaultData(action, dataKey)
  });
