import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';
import { API_CONVERSION_REPORT } from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell, DateCell, TextCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps, secondaryData } from '@/constants/config';
import SelectPicker from '@/components/Picker/SelectPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';

import { FilterParam, segData, abnomalData, filterParamKeys } from '../constants';

const mockData: CustomTableDataProps<any> = {
  data: [
    {
      id: 1,
      segmentationId: 0,
      suspiciousConv: 10,
      ususpiciousConv: 1,
      segmentation: '总计',
      conversionGroupName: 'test-内部',
      conversionName: 'test-内部活动',
      uimpressions: '--',
      uclicks: '--'
    }
  ],
  page: {
    current: 0,
    pagesize: 10,
    total: 6,
    size: 100
  },
  total: {
    impressions: 140,
    uimpressions: 70,
    clicks: 70,
    uclicks: '--'
  }
};

export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    setTableParams,
    handleSearchWord,
    handleChangeCommonParam
  }: BasicReportProps<FilterParam>) => {
    const { formatMessage } = useFmtMsgFori18n();

    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: API_CONVERSION_REPORT,
      postParams: {
        ...postParams,
        var: 'json'
      }
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport(
          {
            ...postParams,
            var: 'xlsx'
          },
          API_CONVERSION_REPORT
        );
    }, [onExport, postParams]);

    const segDataIntl = usePickerDataFromObj(segData);
    const abnomalDataIntl = usePickerDataFromObj(abnomalData);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SecondaryPicker
          key="secondary"
          data={[null, 'daily']}
          value={filterParams[filterParamKeys.SEC]}
          onChange={(value: any) => handleChangeCommonParam({ [filterParamKeys.SEC]: value })}
          cleanable={false}
        />,
        <SelectPicker
          key="segmentation"
          prepend={formatMessage('i18n_segmentation')}
          data={segDataIntl}
          value={filterParams[filterParamKeys.SEG]}
          onChange={(value: string) => handleChangeCommonParam({ [filterParamKeys.SEG]: value })}
        />,
        <SelectPicker
          key="errorType"
          prepend={formatMessage('i18n_errorType')}
          data={abnomalDataIntl}
          value={filterParams[filterParamKeys.ABNOMAL]}
          onChange={(value: string) =>
            handleChangeCommonParam({ [filterParamKeys.ABNOMAL]: value })
          }
        />,
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };
    // 渲染表列
    const renderCol = (): React.ReactElement[] => {
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams.secondary)[0],
        'label'
      );

      return [
        <Column width={100} sortable resizable fixed key="id">
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>,
        <Column width={150} sortable resizable fixed key="name">
          <HeaderCell>
            {formatMessage('i18n_activity')}
            {formatMessage('i18n_name')}
          </HeaderCell>
          <TextCell dataKey="conversionName" maxLine={1} />
        </Column>,
        <Column width={150} sortable resizable fixed key="conversionGroupName">
          <HeaderCell>{formatMessage('i18n_belongActivityGroup')}</HeaderCell>
          <TextCell dataKey="conversionGroupName" maxLine={1} />
        </Column>,
        <Column width={150} sortable resizable fixed key="be">
          <HeaderCell>{formatMessage('i18n_belongBE')}</HeaderCell>
          <TextCell dataKey="be" maxLine={1} />
        </Column>,
        filterParams[filterParamKeys.ABNOMAL] === 'abnormal' && (
          <Column width={150} sortable resizable fixed key="errorType">
            <HeaderCell>{formatMessage('i18n_errorType')}</HeaderCell>
            <TextCell dataKey="errorType" maxLine={1} />
          </Column>
        ),
        filterParams[filterParamKeys.SEG] && (
          <Column width={150} sortable resizable key="segmentation">
            <HeaderCell>{formatMessage('i18n_platform')}</HeaderCell>
            <Cell dataKey="segmentation" />
          </Column>
        ),
        filterParams[filterParamKeys.SEC] && (
          <Column
            width={filterParams[filterParamKeys.SEC] === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams[filterParamKeys.SEC] === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        filterParams[filterParamKeys.SEC] === 'hourly' && (
          <Column width={100} key="hourly" sortable resizable>
            <HeaderCell>{formatMessage('i18n_hour')}</HeaderCell>
            <Cell dataKey="timeslot" />
          </Column>
        ),
        <Column width={200} resizable align="right" sortable key="suspiciousConv">
          <HeaderCell>{formatMessage('i18n_suspiciousConversions')}</HeaderCell>
          <NumberCell dataKey="suspiciousConv" />
        </Column>,
        <Column width={250} resizable align="right" sortable key="ususpiciousConv">
          <HeaderCell>{formatMessage('i18n_suspiciousUniqueConversions')}</HeaderCell>
          <NumberCell dataKey="ususpiciousConv" />
        </Column>
      ];
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
        {renderCol()}
      </Table>
    );
  }
);
