import React, { memo } from 'react';
import _ from 'lodash';
import 'echarts/lib/component/title';
import PieChart from '@rsuite/charts/lib/charts/PieChart';
import { getSerie } from './index';
import { ChartProps } from './type';

export default memo(
  ({ config, option = {}, loading, height, ...props }: ChartProps & BaseObject<any>) => {
    const { type, data } = config;
    const newSeries = _.get(option, 'series');

    return (
      <PieChart loading={loading} data={data} option={option} height={height} {...props}>
        {_.map(newSeries, serie => getSerie(serie, type))}
      </PieChart>
    );
  },
  (prevProps, nextProps) => _.isEqual(prevProps, nextProps)
);
