import React, { useCallback } from 'react';
import _ from 'lodash';
import { StatusIcon } from '@/components/Icon';
import Cell, { BaseCellProps } from './FloatActionCell';

interface StatusCellProps extends Partial<BaseCellProps<any>> {
  dataKey?: string;
  statusMapperFn?: (statusFromBackend: string | number) => string;
}

const StatusCell = ({
  rowData,
  dataKey = 'status',
  statusMapperFn,
  ...props
}: StatusCellProps): React.ReactElement => {
  const renderContent = useCallback(() => {
    const statusFromBackend = _.get(rowData, dataKey, '');
    const targetStatus = (statusMapperFn && statusMapperFn(statusFromBackend)) || statusFromBackend;

    return <StatusIcon status={targetStatus} />;
  }, [rowData, dataKey, statusMapperFn]);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderContent={renderContent}
      {...props}
      className="status-cell"
    />
  );
};

export default React.memo(StatusCell);
