import React, { memo, useContext, useMemo, useCallback, useState, useEffect } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';
import { API_TAB_REPORT } from '@/constants/APIs';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import { useMainPage } from '@/components/hooks/useMainPage';
import { DateCtx } from '@/views/campaigns/common/context';
import {
  reportKeys,
  defaultFilter,
  commonParamFormatter,
  FilterParam,
  filterParamKeys
} from '@/views/common/conversionReport/constants';
import Toolbar from './Toolbar';
import SecondToolbar from './SecondToolbar';
import { cellData } from './constants';
import Chart from '@/components/Chart/ReportCustomChart';
import Table from './Table';

const customParamFormatter = (filterParam: FilterParam) => {
  const originIndicators = _.get(filterParam, filterParamKeys.INDICATORS);
  return {
    ...filterParam,
    [filterParamKeys.INDICATORS]: !_.isEmpty(originIndicators) ? originIndicators.join(',') : ''
  };
};

export default memo(() => {
  const {
    commonParam,
    fmtCommonParam,
    handleChangeCommonParam,
    handleDebouncedChangeCommonParam,
    customParamResult
  } = useMainPage({
    defaultCommonParam: {
      ...defaultFilter,
      [filterParamKeys.MDIMENSION]: 'site' // 主要维度
    },
    commonParamFormatter,
    customConfig: [
      {
        isTable: true,
        defaultParam: {
          [filterParamKeys.INDICATORS]: _.filter(cellData, 'defaultSelected').map(
            (item: any) => item.id
          )
        },
        paramFormatter: customParamFormatter
      }
    ]
  });
  const campaignId = useCampaignId();
  const [tableResult] = customParamResult;
  const { param, fmtParam, handleChangePagination, handleChangeCustomParam } = tableResult;
  const {
    dateRangeVal,
    dateRangeValTimeFormat,
    setDateRangeValByDateArray,
    validDateLoaded,
    maxDateFromRespBeforePreset,
    reportDateFn,
    minDateFromResp
  } = useContext(DateCtx);

  const [projects, setProjects] = useState<Array<any>>([]);
  const handleChangeProjects = useCallback(
    (value: string, selected: any) => {
      if (_.isEmpty(selected)) {
        setProjects([]);
      } else {
        projects.push(value);
        _.uniq(projects);
        setProjects(projects);
      }
    },
    [projects]
  );

  const postParams = useMemo(
    () => ({
      id: campaignId,
      hierarchy: 'campaign',
      report: reportKeys.FLOW
    }),
    [campaignId]
  );
  const handleSearchWord = useCallback(
    (word: string) => {
      handleDebouncedChangeCommonParam({
        sSearch: word
      });
    },
    [handleDebouncedChangeCommonParam]
  );
  useEffect(() => {
    reportDateFn({
      params: {
        secondary: 'daily',
        report: reportKeys.FLOW
      }
    });
  }, []);
  if (!validDateLoaded) {
    return null;
  }
  return (
    <>
      <Panel className="page-toolbar">
        <Toolbar
          dateVal={dateRangeVal}
          maxDate={maxDateFromRespBeforePreset}
          minDate={minDateFromResp}
          filterParams={commonParam}
          handleChangeDate={setDateRangeValByDateArray}
          handleChangeCommonParam={handleChangeCommonParam}
          handleChangeProjects={handleChangeProjects}
        />
      </Panel>
      <Panel className="panel-table">
        <SecondToolbar
          filterParams={commonParam}
          handleChangeCommonParam={handleChangeCommonParam}
          handleSearchWord={handleSearchWord}
        />

        <Chart
          dateParams={dateRangeValTimeFormat}
          filterParams={fmtCommonParam}
          postParams={postParams}
          collapsible
        />
        <Table
          dateParams={dateRangeValTimeFormat}
          filterParams={param}
          tableParams={fmtParam}
          postParams={postParams}
          setTableParams={handleChangePagination}
          handleChangeCustomParam={handleChangeCustomParam}
          projects={projects}
        />
      </Panel>
    </>
  );
});
