import React, { memo } from 'react';
import _ from 'lodash';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';
import { toPercent } from '@/utils/format';
import { GenderProps } from '../type';

interface Props {
  data: GenderProps;
  loading: boolean;
}
export default memo<Props>(function({ data, loading }) {
  const { formatMessage } = useFmtMsgFori18n();

  let female = parseFloat(data.female),
    male = parseFloat(data.male),
    unkonwn = (100 - (female + male)).toFixed(2);
  const serieData = [
    {
      center: ['50%', '50%'],
      type: 'pie',
      radius: ['90%', '100%'],
      label: {
        show: false
      },
      hoverAnimation: false,
      name: '',
      data: [
        { value: female, name: formatMessage('i18n_female') },
        { value: unkonwn, name: formatMessage('i18n_unknown') },
        { value: male, name: formatMessage('i18n_male') }
      ]
    }
  ];
  let option = {
    color: ['#EA3797', '#F7F7FA', '#34C3FF'],
    tooltip: {
      trigger: 'item',
      formatter: (params: any) => {
        if (params.name && params.value) {
          return `${params.name}: ${toPercent(params.value / 100)}`;
        }
        return null;
      }
    },
    grid: {
      top: 0,
      left: 0,
      right: 0,
      bottom: 0
    },
    series: serieData
  };

  const config = {
    type: CHART_KEY.PIE
  };

  return <Chart config={config} option={option} height={100} loading={loading} pure />;
});
