import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { useHistory } from 'react-router';
import { Button, Alert } from 'rsuite';
import { API_REPORT } from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import {
  NumberCell,
  DateCell,
  TextCell,
  FloatActionCell,
  HeaderSummary
} from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps, secondaryData } from '@/constants/config';
import CheckPicker from '@/components/Picker/CheckPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { cellData } from './constants';
import { filterParamKeys, FilterParam } from '@/views/common/conversionReport/constants';

const mockData: CustomTableDataProps<any> = {
  data: [
    {
      id: 1,
      name: 'test',
      secondary: '2020/03/09',
      value: 'aqy',
      placementId: 1,
      impressions: 40,
      clicks: 20,
      uimpressions: '--',
      uclicks: '--',
      clickRate: 50.0,
      placementName: 'test'
    }
  ],
  page: {
    current: 0,
    pagesize: 10,
    total: 6,
    size: 100
  },
  total: {
    impressions: 140,
    uimpressions: 70,
    clicks: 70,
    uclicks: '--'
  }
};
type Props = BasicReportProps<FilterParam> & {
  projects?: Array<any>;
};
export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    setTableParams,
    handleChangeCustomParam,
    projects
  }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();
    const history = useHistory();
    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: API_REPORT,
      postParams: {
        ...postParams,
        var: 'json'
      }
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
    const cellDataIntl = usePickerDataFromObj(cellData);
    const isProject = useMemo(
      () =>
        filterParams[filterParamKeys.PROJECT] && !_.isEmpty(filterParams[filterParamKeys.PROJECT]),
      [filterParams[filterParamKeys.PROJECT]]
    );

    // 除了基本指标
    const disabledItemValues = isProject
      ? []
      : cellDataIntl.filter(i => !!!i.defaultSelected).map(i => i.id);
    //显示警告信息
    const handleShowWarning = useCallback(() => {
      if (!isProject) {
        Alert.warning(formatMessage('i18n_seleOneProject'), 5000);
      }
    }, [isProject]);
    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport(
          {
            ...postParams,
            var: 'xlsx'
          },
          API_REPORT
        );
    }, [onExport, postParams]);
    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <CheckPicker
          key="indicators"
          prepend={formatMessage('i18n_metrics')}
          placeholder={formatMessage('i18n_select', { name: '' })}
          data={cellDataIntl}
          style={{ maxWidth: '224px' }}
          value={filterParams[filterParamKeys.INDICATORS]}
          onChange={(value: any) =>
            handleChangeCustomParam({ [filterParamKeys.INDICATORS]: value })
          }
          disabledItemValues={disabledItemValues}
          onOpen={handleShowWarning}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };
    // 基本指标
    const baseIndicators = ['impressions', 'uimpressions', 'clicks', 'uclicks'];
    // 渲染已选指标列
    const renderSelectedCol = (): React.ReactElement[] => {
      const ShowCell = (i: any, j: any) => {
        if (i.type && i.type === 'string') {
          return <TextCell dataKey={`${j.id}#${i.id}`} />;
        }
        return <NumberCell dataKey={`${j.id}#${i.id}`} />;
      };
      let list: React.ReactElement[] = [];
      cellDataIntl.forEach((i: any) => {
        // 不包含基本指标选中
        if (
          _.includes(filterParams[filterParamKeys.INDICATORS], i.id) &&
          !_.includes(baseIndicators, i.id)
        ) {
          filterParams[filterParamKeys.PROJECT].forEach((j: any) => {
            const obj = projects.filter((o: any) => o.id === j)[0];
            if (!obj) {
              return;
            }
            list.push(
              <Column width={200} resizable sortable align="right" key={i.id + obj.id}>
                <HeaderCell>
                  <HeaderSummary title={obj.label + '（' + obj.id + '）'} summary={i.name} />
                </HeaderCell>
                {ShowCell(i, obj)}
              </Column>
            );
          });
        }
      });
      return list;
    };
    // 渲染表列
    const renderCol = (): React.ReactElement[] => {
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams[filterParamKeys.SEC])[0],
        'label'
      );
      let list = [
        filterParams.mDimension !== 'keyword' && (
          <Column width={100} sortable fixed key="id">
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>
        ),
        filterParams.mDimension !== 'keyword' && (
          <Column width={200} resizable sortable fixed key="name">
            <HeaderCell>{formatMessage(`i18n_${filterParams.mDimension}`)}</HeaderCell>
            <FloatActionCell dataKey="name" />
          </Column>
        ),
        filterParams.mDimension === 'keyword' && (
          <Column width={200} resizable sortable fixed key="keyword">
            <HeaderCell>{formatMessage('i18n_keyword')}</HeaderCell>
            <TextCell dataKey="value" maxLine={1} />
          </Column>
        ),
        (filterParams.mDimension === 'placement' || filterParams.mDimension === 'keyword') && (
          <Column width={200} resizable sortable fixed key="site">
            <HeaderCell>{formatMessage('i18n_site')}</HeaderCell>
            <TextCell dataKey="site" maxLine={1} />
          </Column>
        ),
        filterParams.mDimension === 'keyword' && (
          <Column width={200} resizable sortable fixed key="placement">
            <HeaderCell>{formatMessage('i18n_placement')}</HeaderCell>
            <TextCell dataKey="placement" maxLine={1} />
          </Column>
        ),
        filterParams[filterParamKeys.SEC] && (
          <Column
            width={filterParams[filterParamKeys.SEC] === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams[filterParamKeys.SEC] === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        _.includes(filterParams[filterParamKeys.INDICATORS], 'impressions') && (
          <Column width={150} align="right" sortable key="impressions">
            <HeaderCell>{formatMessage('i18n_impressionsNum')}</HeaderCell>
            <NumberCell dataKey="impressions" />
          </Column>
        ),
        _.includes(filterParams[filterParamKeys.INDICATORS], 'uimpressions') && (
          <Column width={200} align="right" sortable key="uimpressions">
            <HeaderCell>{formatMessage('i18n_uniqueImpressionsNum')}</HeaderCell>
            <NumberCell dataKey="uimpressions" />
          </Column>
        ),
        _.includes(filterParams[filterParamKeys.INDICATORS], 'clicks') && (
          <Column width={150} align="right" sortable key="clicks">
            <HeaderCell>{formatMessage('i18n_clicksNum')}</HeaderCell>
            <NumberCell dataKey="clicks" />
          </Column>
        ),
        _.includes(filterParams[filterParamKeys.INDICATORS], 'uclicks') && (
          <Column width={200} align="right" sortable key="uclicks">
            <HeaderCell>{formatMessage('i18n_uniqueClicksNum')}</HeaderCell>
            <NumberCell dataKey="uclicks" />
          </Column>
        )
      ];
      return _.concat(list, renderSelectedCol());
    };

    return (
      <Table
        {...tableDataMemo}
        renderToolbar={renderToolbar}
        onChange={onChange}
        headerHeight={filterParams[filterParamKeys.PROJECT] ? 60 : 40}
      >
        {renderCol()}
      </Table>
    );
  }
);
