import * as React from 'react';
import { FormattedMessage as RIFormattedMessage } from 'react-intl';

import IntlProvider from '@/components/IntlProvider';
import { extractI18NKey } from '@/utils/formatMessage';
import { getCurrentLocale } from '@/utils/locale';

/**
 * 只有在调用 Alert.success/error 等相关方法时 使用该组件处理国际化问题
 */
export default React.memo((props: RIFormattedMessage.Props) => {
  const [currentLocale] = React.useState(getCurrentLocale());
  const { id, ...rest } = props;
  const i18nKey = extractI18NKey(id) || id;
  return (
    <IntlProvider language={currentLocale}>
      {() => <RIFormattedMessage id={i18nKey} {...rest} />}
    </IntlProvider>
  );
});
