import React from 'react';
import ReactDOM from 'react-dom';
import Cookie from 'js-cookie';
import _ from 'lodash';
import { removeClass } from '@/utils/domLib';
import App from './App';
import ready from './ready';

ready().then(([props]) => {
  // 对于成功和可处理异常的清空，设置cookie，移除loading，渲染内部页面
  !Cookie.get('django_language') && Cookie.set('django_language', _.toLower(navigator.language));
  removeClass(document.getElementById('body'), 'body-loading');
  ReactDOM.render(<App {...props as object} />, document.getElementById('app'));
});
