import React, { memo } from 'react';
import { Panel } from 'rsuite';

interface Props {
  title: string;
  className?: string;
  children?: React.ReactNode;
}

export default memo<Props>(function({ title, className, children }) {
  return (
    <>
      <Panel className={className}>
        <div className="card-title">{title}</div>
        {children}
      </Panel>
    </>
  );
});
