import _ from 'lodash';
import { useLocation, useHistory } from 'react-router-dom';

import { validateAuth } from './validate';

/**
 * 默认和userInfo 里面的 validUrl判断，如果传入了validUrl参数之后跟validUrl交集
 */
export const useAuthPath = (redirectEnabled = true, validUrls?: Array<string>) => {
  const { replace } = useHistory();
  const { pathname } = useLocation();
  const validResult = validateAuth(pathname, validUrls); // 如果是dev模式，不会拦截

  if (_.isNumber(validResult)) {
    redirectEnabled && replace(`/error/${validResult}`);
    return false;
  }

  return true;
};
