import React, { RefObject, useCallback } from 'react';
import { Form } from 'rsuite';
import { FormInstance, FormProps } from 'rsuite/lib/Form';
import {
  FormDataContext,
  FormDataContextProps,
  FormErrorContext,
  FormErrorContextProps
} from './FormContext';

type Props = Omit<FormProps, 'formValue' | 'formError'> &
  FormDataContextProps &
  FormErrorContextProps & {
    formRef?: RefObject<FormInstance>;
    children?: React.ReactElement | React.ReactElement[];
    ref?: React.RefObject<FormInstance>;
  };

export default React.memo(
  ({
    formData,
    formErrors,
    onChange,
    setFormErrors,
    checkForm,
    model,
    formRef,
    ...props
  }: Props) => {
    const handleCheck = useCallback(
      errors => {
        setFormErrors(errors);
      },
      [setFormErrors]
    );

    // 此处没有给Form传递onChange，
    // 而是在CustomField和MultiField中，使用context中的onChange来更新数据，防止混淆

    return (
      <FormDataContext.Provider
        value={{
          formData,
          onChange
        }}
      >
        <FormErrorContext.Provider
          value={{
            formErrors,
            setFormErrors,
            checkForm,
            model
          }}
        >
          <Form
            formValue={formData}
            formError={formErrors}
            ref={formRef}
            model={model}
            onCheck={handleCheck}
            checkTrigger="change"
            {...props}
          />
        </FormErrorContext.Provider>
      </FormDataContext.Provider>
    );
  }
);
