import { useEffect } from 'react';
import _ from 'lodash';

import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { API_SITE, API_CROSS_SITE, API_REGION, API_PLATFORM } from '@/constants/APIs';

import { ASYNC_FN_KEY } from './enum';
import { PickerAsyncDataCtxProps } from '../../context';

const extractRecordsFromResp = (resp: any) => {
  return _.get(resp, 'data.records', []);
};

const extractDataFromResp = (resp: any) => {
  return _.get(resp, 'data', []);
};

const checkExist = (key: ASYNC_FN_KEY, asyncKeyList: Array<ASYNC_FN_KEY>) =>
  asyncKeyList.indexOf(key) !== -1;

// 把每一个async打平，然后自动请求在filterData里面的asyncKey，如果遇到依赖请求的话，就把函数暴露出去，用的时候在调用吧。
// 这里不再做聚合数据了，因为聚合数据的话国际化这里不太好处理
export const useAsyncData = (
  asyncKeyList: Array<ASYNC_FN_KEY>,
  campaignId: Array<string> | string = ''
): PickerAsyncDataCtxProps => {
  const formatCampaignId = _.isArray(campaignId) ? campaignId.join(',') : campaignId;
  const stringifiedAsyncKeyList = JSON.stringify(asyncKeyList);

  const { onFetch: onFetchSites, loading: sitesLoading, response: siteResp } = useFetch<
    // 不处理请求取消
    CommonResponse
  >(FETCH_METHOD.GET, API_SITE, {
    params: {
      campaignId: formatCampaignId,
      status: 1
    },
    cancelPrevious: false
  });

  const {
    onFetch: onFetchCrossSite,
    loading: crossSiteLoading,
    response: crossSiteResp
  } = useFetch<CommonResponse>(FETCH_METHOD.GET, API_CROSS_SITE, {
    // 不处理请求取消
    params: {
      sub: 1,
      campaignId: formatCampaignId
    },
    cancelPrevious: false
  });

  const {
    onFetch: onFetchPlacement,
    loading: placementLoading,
    response: placementResp
  } = useFetch<CommonResponse>(FETCH_METHOD.GET, API_SITE, {
    // 不处理请求取消
    params: {
      sub: 1,
      status: 1,
      campaignId: formatCampaignId
    },
    cancelPrevious: false
  });

  const { onFetch: onFetchRegion, loading: regionLoading, response: regionResp } = useFetch<
    // 不处理请求取消
    CommonResponse
  >(FETCH_METHOD.GET, API_REGION, { params: {}, cancelPrevious: false });

  const { onFetch: onFetchPlatform, loading: platformLoading, response: platformResp } = useFetch<
    // 不处理请求取消
    CommonResponse
  >(FETCH_METHOD.GET, API_PLATFORM, { params: {}, cancelPrevious: false });

  useEffect(() => {
    const a = JSON.parse(stringifiedAsyncKeyList);
    if (checkExist(ASYNC_FN_KEY.CROSS_SITE, a)) {
      onFetchCrossSite();
    }

    if (checkExist(ASYNC_FN_KEY.SITE, a)) {
      onFetchSites();
    }

    if (checkExist(ASYNC_FN_KEY.PLACEMENT, a)) {
      onFetchPlacement();
    }

    if (checkExist(ASYNC_FN_KEY.PLATFORM, a)) {
      onFetchPlatform();
    }

    if (checkExist(ASYNC_FN_KEY.REGION, a)) {
      onFetchRegion();
    }
  }, [stringifiedAsyncKeyList]); // 只请求一次，除非asyncKey变了

  return {
    [ASYNC_FN_KEY.SITE]: {
      loading: sitesLoading,
      data: extractRecordsFromResp(siteResp),
      fetchFn: onFetchSites
    },
    [ASYNC_FN_KEY.CROSS_SITE]: {
      loading: crossSiteLoading,
      data: extractRecordsFromResp(crossSiteResp),
      fetchFn: onFetchCrossSite
    },
    [ASYNC_FN_KEY.PLACEMENT]: {
      loading: placementLoading,
      data: extractRecordsFromResp(placementResp),
      fetchFn: onFetchPlacement
    },
    [ASYNC_FN_KEY.PLATFORM]: {
      loading: platformLoading,
      data: extractRecordsFromResp(platformResp),
      fetchFn: onFetchPlatform
    },
    [ASYNC_FN_KEY.REGION]: {
      loading: regionLoading,
      data: extractRecordsFromResp(regionResp),
      fetchFn: onFetchRegion
    }
  };
};
