import _ from 'lodash';

export const mapFromResponse = (rspData: Record<string, any>) => {
  return {
    ...rspData,
    status: rspData.status === 1
  };
};

export const mapToRequest = (formData: Record<string, any>, isEdit: boolean) => {
  const { status: oldStatus } = formData;
  const status = oldStatus ? 1 : 0;

  return _.mapValues(
    {
      ..._.omit(formData, 'BE'),
      status
    },
    val => (_.isArrayLike(val) && _.isEmpty(val) ? undefined : val)
  );
};
