import * as React from 'react';
import classnames from 'classnames';
import { CheckPicker } from 'rsuite';
import { CheckPickerProps } from 'rsuite/lib/CheckPicker';
import useFetchForPicker, {
  UseFetchProps
} from '@/components/Picker/PickerHooks/useFetchForPicker';
import useSearchForPicker, {
  UseSearchProps
} from '@/components/Picker/PickerHooks/useSearchForPicker';
import useAllForPicker, { UseAllProps } from '@/components/Picker/PickerHooks/useAllForPicker';
import useLoadingForPicker, {
  UseLoadingProps
} from '@/components/Picker/PickerHooks/useLoadingForPicker';
import usePrependForPicker, {
  UsePrependProps
} from '@/components/Picker/PickerHooks/usePrependForPicker';
import useCheckConfirmFooterForPicker, {
  UseCheckConfirmFooterProps
} from '@/components/Picker/PickerHooks/useCheckConfirmFooterForPicker';
import useFilterValueForPicker, {
  UseFilterValueProps
} from '@/components/Picker/PickerHooks/useFilterValueForPicker';
import useContainerForPicker, {
  UseContainerProps
} from '@/components/Picker/PickerHooks/useContainerForPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

import composer from './PickerHooks/composer';

// FIXME: 这里mapLabel 好像没有效果

export type CustomCheckPickerProps = Partial<CheckPickerProps> &
  UseFetchProps &
  UseSearchProps &
  UseFilterValueProps &
  UseAllProps &
  UseLoadingProps &
  UsePrependProps &
  UseCheckConfirmFooterProps &
  UseContainerProps;

// 对外暴露一个参数，支持转发ref
const CustomCheckPicker = React.memo(
  React.forwardRef((props: CustomCheckPickerProps, ref?: React.RefObject<any>) => {
    const { formatMessage } = useFmtMsgFori18n();
    return (
      <CheckPicker
        {...composer<CustomCheckPickerProps, CheckPickerProps>(
          useFetchForPicker,
          useSearchForPicker,
          useFilterValueForPicker,
          useAllForPicker,
          useLoadingForPicker,
          usePrependForPicker,
          useCheckConfirmFooterForPicker,
          useContainerForPicker
        )({
          labelKey: 'name',
          valueKey: 'id',
          placement: 'autoVerticalStart',
          searchKeys: ['id', 'name'],
          isMulti: true,
          placeholder: formatMessage('i18n_select', { name: '' }),
          ...props,
          className: classnames(props.className, 'cus-picker')
        })}
        ref={ref}
      />
    );
  })
);

CustomCheckPicker.displayName = 'CheckPicker';
export default CustomCheckPicker;
