import React, { useCallback } from 'react';
import _ from 'lodash';
import Helmet from 'react-helmet';
import { useHistory } from 'react-router';

import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { assembleTitle } from '@/utils';
import { getFirstPage, getUserProfile, isIframe } from '@/utils/location';
import { getEnvFromSession } from '@/utils/env';

interface Props {
  errorCode?: string;
  showNav?: boolean;
  intl?: any;
}

const ERROR_INFO = {
  '403': {
    picName: 'default.svg'
  },
  '404': {
    picName: '404.svg'
  },
  '500': {
    picName: 'default.svg'
  }
};

const ErrorPage = React.memo(function ErrorPage({ errorCode, showNav }: Props) {
  const { formatMessage } = useFmtMsgFori18n();
  const { replace } = useHistory();
  const { picName } = _.get(ERROR_INFO, errorCode, {
    picName: 'default.svg'
  });

  function getErrorMessage() {
    let localeKey = `i18n_error${errorCode}`;
    if (Object.keys(ERROR_INFO).indexOf(errorCode.toString()) === -1) {
      localeKey = 'i18n_errorUnknown';
    }

    return formatMessage(localeKey);
  }

  const message = getErrorMessage();

  const handleGoHome = useCallback(() => {
    if (!_.isNumber(getUserProfile().id)) {
      // 未成功登录，跳转HAC系统
      window.location.replace(getEnvFromSession('hacUrl') || 'https://account.hypers.com.cn/');
    } else if (isIframe()) {
      // 在hac授权过程中，跳转hac页
      replace('/hac');
    } else {
      // 正常情况，跳转菜单第一项
      replace(getFirstPage());
    }
  }, [replace]);

  return (
    <div className="error-wrapper">
      <Helmet>
        <title>{assembleTitle(message)}</title>
      </Helmet>
      <div className="error-content">
        <img alt={`${errorCode}`} src={`/resources/images/error/${picName}`} />
        <h2>
          {message}({errorCode})
        </h2>
        {showNav && (
          <div className="nav-help" onClick={handleGoHome}>
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <a>{formatMessage('i18n_backToHomePage')}</a>
          </div>
        )}
      </div>
    </div>
  );
});

export default ErrorPage;
