import { createReducer } from 'redux-immutablejs';
import { fromJS } from 'immutable';
import getCreateSelectors from '@/utils/getCreateSelectors';
import ACTION_TYPES from '@/constants/actionTypes';
import { toDefaultData, storeAsDefault } from '@/utils/actionToStore';

const initialState = fromJS({
  environmentInfo: {},
  userInfo: null,
  errorCode: null,
  menuData: {},
  projectsData: {}
});

export default createReducer(initialState, {
  [ACTION_TYPES.UPDATE_PAGE_ERROR_CODE]: (state, action) =>
    state.merge({
      errorCode: action.errorCode
    }),
  [ACTION_TYPES.UPDATE_ENVIRONMENT_INFO]: (state, action) => {
    return state.merge({
      environmentInfo: toDefaultData(action).data
    });
  },
  [ACTION_TYPES.UPDATE_USER_INFO]: (state, action) => {
    return state.merge({
      userInfo: toDefaultData(action).data
    });
  },
  [ACTION_TYPES.UPDATE_MENU]: storeAsDefault('menuData'),
  [ACTION_TYPES.UPDATE_PROJECTS]: storeAsDefault('projectsData')
});

export const createSelectors = getCreateSelectors(['store', 'root'], initialState.toJS());
