import React, { memo } from 'react';
import _ from 'lodash';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';

interface Props {
  value: string;
  height: number;
}
export default memo<Props>(function({ value, height }) {
  const { formatMessage } = useFmtMsgFori18n();

  const serieData = [
    {
      center: ['50%', '50%'],
      type: 'pie',
      radius: ['90%', '100%'],
      label: {
        show: false,
        fontSize: 20,
        formatter: (params: any) => {
          if (params.name === formatMessage('i18n_hit')) {
            return `${params.value}%`;
          }
          return '';
        }
      },
      hoverAnimation: false,
      name: '',
      data: [
        { value: (100 - parseFloat(value)).toFixed(2), name: formatMessage('i18n_miss') },
        { value: parseFloat(value), name: formatMessage('i18n_hit') }
      ]
    }
  ];
  let option = {
    color: ['#F7F7FA', '#34C3FF'],
    title: {
      text: `${parseFloat(value).toFixed(2)}%`,
      x: 'center',
      y: 'center',
      textStyle: {
        fontWeight: 'normal',
        fontSize: 18
      }
    },
    tooltip: {
      trigger: 'item',
      formatter: (params: any) => {
        if (params.name && params.value) {
          return `${params.name}: ${params.value}`;
        }
        return null;
      }
    },
    grid: {
      top: 0,
      left: 0,
      right: 0,
      bottom: 0
    },
    series: serieData
  };

  const config = {
    type: CHART_KEY.PIE
  };

  return <Chart config={config} option={option} height={height} pure />;
});
