import React, { useMemo } from 'react';
import _ from 'lodash';
import { Icon, Popover, Whisper } from 'rsuite';
import { GoingIcon } from '@/components/Icon/index';
import { Cell } from '@/components/Table';

const SuccessIcon = () => <Icon icon="ok-circle" style={{ color: '#34c3ff' }} />;

const WarningIcon = () => <Icon icon="exclamation-circle2" style={{ color: '#F44336' }} />;

export default React.memo(({ rowData, dataKey, ...props }: any) => {
  const StatusIcon = useMemo(() => {
    const status = _.get(rowData, dataKey);
    let icon = null;
    switch (status) {
      case 'NEW':
        icon = <SuccessIcon />;
        break;
      case 'UPDATED':
      case 'UPDATE':
        icon = <GoingIcon />;
        break;
      case 'ERROR':
      case 'No UPDATE':
        icon = <WarningIcon />;
        break;
      default:
    }
    return icon;
  }, [rowData, dataKey]);

  const content = useMemo(
    () => (
      <div>
        {StatusIcon}{' '}
        {_.join(_.map(_.split(_.get(rowData, dataKey), ' '), s => _.capitalize(s)), ' ')}
      </div>
    ),
    [StatusIcon, rowData, dataKey]
  );

  const speaker = useMemo(() => {
    const info = _.get(rowData, 'error_info') || _.get(rowData, 'update_info');

    return info ? (
      <Popover className="placement-upload-popover">
        {info.map(({ error, update }: any, i: number) => (
          <div key={i}>{error || update}</div>
        ))}
      </Popover>
    ) : null;
  }, [rowData.error_info, rowData.update_info]);

  return (
    <Cell
      {...props}
      className={`placement-upload-status-${_.kebabCase(_.lowerCase(_.get(rowData, dataKey)))}`}
    >
      {speaker ? (
        <Whisper placement="autoVertical" trigger="hover" speaker={speaker}>
          {content}
        </Whisper>
      ) : (
        content
      )}
    </Cell>
  );
});
