import React, { memo, useState, useEffect } from 'react';
import _ from 'lodash';
import { onFetchRealTime } from '../actions';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useInterval from '@/components/hooks/useInterval';
import Chart from '@/components/Chart';
import { CHART_KEY, GEO } from '@/components/Chart/config';
import { FILTER } from '@/constants/config';
import { toThousands } from '@/utils/format';
import { getCurrentLocale } from '@/utils/locale';
/** ** chart geo  *** */
import { cityGeoEn, cityGeoZh } from '@hypers/geo-transform';
import { cityCoordsMap } from '@/constants/geo';

const mockData: ChartData[] = [
  {
    city: 'geo.china.shanghai',
    click: 475,
    imp: 20294,
    sum: 20769
  },
  {
    city: 'geo.china.guangzhou',
    click: 443,
    imp: 16951,
    sum: 17394
  }
];

interface ChartData {
  city: string;
  click: number | null;
  imp: number | null;
  sum: number | null;
}

interface Props {
  id: number | string;
  tag?: string;
  filterParams?: FILTER;
}

function fmtRegionName(values: string) {
  if (!values) {
    return null;
  }
  if (values.indexOf('省') > -1) {
    values = _.replace(values, '省', '');
  }
  if (values.indexOf('市') > -1) {
    values = _.replace(values, '市', '');
  }
  return values;
}

export default memo<Props>(function ({ id, tag, filterParams }) {
  const [data, setData] = useState<ChartData[]>();
  const [loading, setLoading] = useState(true);
  const [delay, setDelay] = useState(500);
  useInterval(() => {
    setDelay(1000 * 60);
    setLoading(true);
    onFetchRealTime(
      {
        id,
        tag,
        [filterParams.selectObj.key]: filterParams.selectObj.value
      },
      res => {
        setLoading(false);
        setData(_.get(res, 'data.records'));
      }
    );
  }, delay);

  // 触发请求
  useEffect(() => {
    setDelay(500);
    setData(null);
  }, [id, filterParams]);
  const locale = getCurrentLocale() || 'zh-CN';

  const { formatMessage } = useFmtMsgFori18n();
  const city = locale === 'zh-CN' ? cityGeoZh : cityGeoEn;
  const config = {
    type: CHART_KEY.ORIGIN
  };
  const chartData =
    data && !_.isEmpty(data)
      ? data.map((item: ChartData) => ({
        ...cityCoordsMap[fmtRegionName(cityGeoZh[item.name])],
        name: city[item.name],
        num: item.value,
        value: item.value
      }))
      : [];
  const max = Math.max(...chartData.map(item => item.value)) || 0;
  const min = Math.min(...chartData.map(item => item.value)) || 0;
  const maxSize4Pin = 15,
    minSize4Pin = 10;

  const option = {
    color: ['rgba(250,216,96,0.3)'],
    legend: {
      show: false
    },
    tooltip: {
      trigger: 'item',
      formatter(params) {
        var res = params.name;
        if (_.get(params, 'data.imp') && _.get(params, 'data.click')) {
          res += `<br>${formatMessage('i18n_total')}：${toThousands(params.value[2])}`;
          res += `<br>${formatMessage('i18n_impressions')}：${toThousands(
            _.get(params, 'data.imp')
          )}`;
          res += `<br>${formatMessage('i18n_clicks')}：${toThousands(_.get(params, 'data.click'))}`;
        } else {
          res += `<br>${toThousands(params.value[2])}`;
        }
        return res;
      }
    },
    geo: GEO,
    series: [
      {
        name: formatMessage('i18n_countrytop10'),
        type: 'effectScatter',
        coordinateSystem: 'geo',
        data: chartData.map(item => {
          return {
            ...item,
            value: [item.longitude, item.latitude, item.value]
          };
        }),
        symbolSize(val) {
          let a =
            max !== min ? (maxSize4Pin - minSize4Pin) / (max - min) : maxSize4Pin - minSize4Pin;
          let b = minSize4Pin - a * min;
          b = maxSize4Pin - a * max;
          return a * val[2] + b;
        },
        showEffectOn: 'render',
        color: '#4E85B7',
        hoverAnimation: true,
        label: {
          normal: {
            show: false,
            textStyle: {
              color: '#fff',
              fontSize: 9
            }
          }
        }
      }
    ]
  };
  return (
    <>
      <Chart config={config} loading={loading} option={option} pure />
    </>
  );
});
