import React, { useCallback, useEffect } from 'react';
import { Panel } from 'rsuite';
import _ from 'lodash';

import useDrawerForm from '@/components/hooks/useDrawerForm';
import { useMainPage } from '@/components/hooks/useMainPage';
import { API_CAMPAIGN, API_CPO } from '@/constants/APIs';
import { CampaignModel, CpoModel } from '@/constants/form/models';
import { useLocation } from 'react-router-dom';
import Table from './Table';
import DrawerForm from './DrawerForm';
import DrawerCpo from './DrawerCpo';
import { defaultFormData, defaultCpoFormData, defaultParam } from './constants';
import { mapFromResponse, mapToRequest, cpoFromResponse, cpoToRequest } from './dataMappers';

/*
 本页面中以下项暂时隐藏，包括model文件中的相关注释请勿删除：
 * 监测类型-独立曝光、独立点击、受众测量
 * 受众累计报
 * UV频次
 * UA频次
 * IVT供应商
 * IVT类型
 * MTA 供应商
 */

const paramFormatter = (f: any) => ({
  ...f,
  beId: (_.get(f, 'beId', []) || []).join(','),
  advertiser: _.get(f, 'advertiser.length', 0)
    ? JSON.stringify(_.get(f, 'advertiser', []) || [])
    : ''
});

const CampaignManagement = React.memo(() => {
  const { state } = useLocation();
  const defaultBe = _.get(state, 'beId') ? [_.toNumber(_.get(state, 'beId'))] : [];

  const { customParamResult } = useMainPage({
    customConfig: [
      {
        isTable: true,
        defaultParam: {
          ...defaultParam,
          beId: defaultBe
        },
        paramFormatter
      }
    ]
  });

  const [tableResult] = customParamResult;
  const {
    handleSearchWord,
    handleChangePagination,
    handleChangeCustomParam,
    param,
    fmtParam
  } = tableResult;

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  const {
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    handleFormChange,
    handleFormCreate,
    handleFormEdit,
    handleFormClose,
    handleFormSubmit,
    setFormErrors,
    checkForm,
    setDrawer
  } = useDrawerForm({
    defaultFormData,
    api: API_CAMPAIGN,
    submitSuccessCallback: refetchTableData,
    model: CampaignModel,
    mapFromResponse,
    mapToRequest
  });

  const {
    drawer: cpoDrawer,
    formData: cpoFormData,
    formErrors: cpoFormErrors,
    formRef: cpoFormRef,
    handleFormChange: cpoHandleFormChange,
    handleFormEdit: cpoHandleFormEdit,
    handleFormClose: cpoHandleFormClose,
    handleFormSubmit: cpoHandleFormSubmit,
    setFormErrors: cpoSetFormErrors,
    checkForm: cpoCheckForm,
    setDrawer: cpoSetDrawer
  } = useDrawerForm({
    defaultFormData: defaultCpoFormData,
    api: API_CPO,
    submitSuccessCallback: refetchTableData,
    model: CpoModel,
    mapFromResponse: cpoFromResponse,
    mapToRequest: cpoToRequest
  });
  //渲染完成之后清空history中的state
  useEffect(() => {
    history.replaceState({}, '');
  }, []);
  return (
    <>
      <Panel className="panel-table page-config-advertiser">
        <Table
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          handleSearchWord={handleSearchWord}
          handleChangeCustomParam={handleChangeCustomParam}
          onCreate={handleFormCreate}
          onEdit={handleFormEdit}
          onCpoEdit={cpoHandleFormEdit}
          refetchTableData={refetchTableData}
        />
        <DrawerForm
          drawer={drawer}
          formData={formData}
          originalFormData={originalFormData}
          formErrors={formErrors}
          formRef={formRef}
          onChange={handleFormChange}
          onSubmit={handleFormSubmit}
          onClose={handleFormClose}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
          setDrawer={setDrawer}
        />
        <DrawerCpo
          drawer={cpoDrawer}
          formData={cpoFormData}
          formErrors={cpoFormErrors}
          formRef={cpoFormRef}
          onChange={cpoHandleFormChange}
          onSubmit={cpoHandleFormSubmit}
          onClose={cpoHandleFormClose}
          setFormErrors={cpoSetFormErrors}
          checkForm={cpoCheckForm}
          setDrawer={cpoSetDrawer}
        />
      </Panel>
    </>
  );
});

CampaignManagement.displayName = 'CampaignManagement';
export default CampaignManagement;
