import React, { useMemo } from 'react';
import _ from 'lodash';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import Toggle from '@/components/Form/Toggle';
import SelectPicker from '@/components/Picker/SelectPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { API_BE } from '@/constants/APIs';
import { TeamModel } from '@/constants/form/models';

const beParams = {
  orderColumn: 'name',
  orderType: 'asc',
  status: 1
};

export default React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();

    const localBEData = useMemo(
      () => [
        {
          id: formData.beId,
          name: formData.BE
        }
      ],
      [formData.beId, formData.BE]
    );

    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editTeam' : 'i18n_newTeam')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        className="team-drawer"
        onSubmit={onSubmit}
        onHide={onClose}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={TeamModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} required />

          {/* 所属BE */}
          <CustomField
            name="beId"
            label={formatMessage('i18n_belongBE')}
            accepter={SelectPicker}
            data={drawer.isEdit && localBEData} // 编辑时，为了防止所属BE已被停用而无法显示出来，就不再从后端获取
            url={API_BE}
            params={beParams}
            menuStyle={{ maxWidth: '400px' }}
            required
            searchable
            disabled={drawer.isEdit}
          />

          {/* 状态 */}
          <CustomField
            name="status"
            label={formatMessage('i18n_status')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_enabled')}
            unCheckedChildren={formatMessage('i18n_disabled')}
          />

          {/* 备注 */}
          <CustomField name="note" label={formatMessage('i18n_remark')} />

          {/* 注释 */}
          {drawer.isEdit && (
            <CustomField
              name="annotation"
              label={formatMessage('i18n_annotation')}
              rows={2}
              componentClass="textarea"
            />
          )}
        </Form>
      </Drawer>
    );
  }
);
