import React, { memo, useCallback, useMemo } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';

import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import * as APIs from '@/constants/APIs';
import BEPicker from '@/components/Picker/logic/BEPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { TextCell, MultiLineCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps } from '@/constants/config';

import { filterParamKeys, utilsFn } from './constants';
import EditCheckPicker from './part/EditCheckPicker';

const maxLine = 3;
const mapTeams = (origin: any) =>
  origin.map((i: any) => _.get(_.toPairs(i), '0.1', '')).filter((i: any) => !_.isEmpty(i));

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    handleSearchWord,
    handleChangeCustomParam,
    refetchTableData
  }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_TEAM_USER,
      postParams: {
        var: ''
      }
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const renderActions = useCallback(
      rowData => {
        return (
          <>
            <EditCheckPicker rowData={rowData} refetchTableData={refetchTableData} />
          </>
        );
      },
      [refetchTableData]
    );

    const handleBEChange = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.BE]: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport({
          var: '.xlsx'
        });
    }, [onExport]);

    const renderToolbar = () => {
      return (
        <>
          <BEPicker
            chosenBE={utilsFn.getChosenBE(filterParams)}
            beChangeHandler={handleBEChange}
            isReport={false}
          />
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
          <Button key="exportTable" className="pull-right" onClick={handleExportClicked}>
            {formatMessage('i18n_exportTable')}
          </Button>
          ,
        </>
      );
    };

    return (
      <Table {...tableDataMemo} onChange={onChange} renderToolbar={renderToolbar} wordWrap>
        {/* ID */}
        <Column width={100} fixed sortable>
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>

        {/* 用户名 */}
        <Column fixed flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_username')}</HeaderCell>
          <TextCell dataKey="username" maxLine={maxLine} />
        </Column>

        {/* 邮箱 */}
        <Column fixed flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_email')}</HeaderCell>
          <TextCell dataKey="email" maxLine={maxLine} />
        </Column>

        {/* 所属Team */}
        <Column flexGrow={1} minWidth={250}>
          <HeaderCell>{formatMessage('i18n_belongTeam')}</HeaderCell>
          <MultiLineCell
            dataKey="teams"
            maxLine={maxLine}
            formatter={data => {
              const teamNames = mapTeams(data);
              return _.isEmpty(teamNames) ? [formatMessage('i18n_notAssigned')] : teamNames;
            }}
            renderActions={renderActions}
          />
        </Column>

        {/* 所属BE */}
        <Column minWidth={250} sortable flexGrow={1}>
          <HeaderCell>{formatMessage('i18n_belongBE')}</HeaderCell>
          <TextCell dataKey="beName" maxLine={maxLine} placementForContent="autoVerticalStart" />
        </Column>
      </Table>
    );
  }
);
