import React, { useCallback, useMemo, useEffect } from 'react';
import _ from 'lodash';
import { Tag, Icon } from 'rsuite';
import Drawer from '@/components/Drawer';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Table from './DrawerTable';
import { useMainPage } from '@/components/hooks/useMainPage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { API_CAMPAIGN } from '@/constants/APIs';
import { defaultDrawerTableFilter } from '../constants';

type ItemType = { id: number; name: string };

interface Props {
  show: boolean;
  checkedItems: ItemType[];
  setCheckedItems: Function;
  onSubmit: Function;
  onClose: Function;
}

const filterParamFormatter = (filterParam: any) => ({
  ..._.omit(filterParam, 'date'),
  beId: filterParam.beId.toString(),
  advertiser: _.get(filterParam, 'advertiser.length', 0)
    ? JSON.stringify(_.get(filterParam, 'advertiser', []))
    : '',
  startDate: filterParam.date[0],
  endDate: filterParam.date[1]
});

export default React.memo(({ show, checkedItems, setCheckedItems, onSubmit, onClose }: Props) => {
  const { formatMessage } = useFmtMsgFori18n();

  // 获取table数据
  const { customParamResult } = useMainPage({
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultDrawerTableFilter,
        paramFormatter: filterParamFormatter
      }
    ]
  });
  const [tableResult] = customParamResult;
  const {
    param,
    fmtParam,
    handleChangeCustomParam,
    handleSearchWord,
    handleChangePagination
  } = tableResult;

  // 避免在打开一级drawer时就开始请求二级drawer数据
  const requestChecker = useCallback(() => show, [show]);

  const { tableData, onChange: onChangeTable } = useTable({
    tableParams: fmtParam,
    setTableParams: handleChangePagination,
    api: API_CAMPAIGN,
    requestChecker
  });
  const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

  // 当打开二级drawer时，参数重置，并触发请求表格数据
  useEffect(() => {
    show &&
      handleChangeCustomParam({
        ...defaultDrawerTableFilter,
        sSearch: ''
      });
  }, [show]);

  // check相关
  const handleCheck = useCallback(
    (checkedValues, changedValue, checked) => {
      setCheckedItems((prevState: ItemType[]) => {
        const changedItems = tableDataMemo.data.filter(i =>
          _.includes(changedValue, _.get(i, 'id'))
        );
        return checked
          ? _.unionBy(prevState, changedItems, 'id')
          : _.differenceBy(prevState, changedItems, 'id');
      });
    },
    [setCheckedItems, tableDataMemo.data]
  );

  const handleRemove = useCallback(
    id => () => {
      setCheckedItems((prevState: ItemType[]) => _.filter(prevState, i => i.id !== id));
    },
    [setCheckedItems]
  );

  return (
    <Drawer
      show={show}
      title={formatMessage('i18n_campaignFilter')}
      className="drawer-table campaign-filter-drawer"
      size="lg"
      onSubmit={onSubmit as (e: React.MouseEvent) => void}
      onHide={onClose as (e: React.MouseEvent) => void}
    >
      <div>
        {formatMessage('i18n_selectedCampaign')}: {checkedItems.length}
      </div>
      <div className="tag-wrap filter-tag">
        {checkedItems.map(i => (
          <Tag key={i.id}>
            <span title={i.name}>{i.name}</span>
            <Icon icon="close" className="clickable" onClick={handleRemove(i.id)} />
          </Tag>
        ))}
      </div>
      <Table
        tableDataMemo={tableDataMemo}
        checkedItems={checkedItems}
        handleCheck={handleCheck}
        filterParams={param}
        onChange={onChangeTable}
        handleSearchWord={handleSearchWord}
        handleChangeCustomParam={handleChangeCustomParam}
      />
    </Drawer>
  );
});
