import React, { useCallback } from 'react';

import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';

export default ({ dataKey, ...props }: BaseCellProps<any>) => {
  const renderContent = useCallback(rowData => {
    return (
      <>
        <div style={{ marginBottom: '5px' }}>
          <div className='show-label'>set-cookie：</div>
          <div className='show-content'>{rowData?.['set-cookie']}</div>
        </div>
        <div style={{ marginBottom: '5px' }}>
          <div className='show-label'>cookie：</div>
          <div className='show-content'>{rowData?.cookie}</div>
        </div>
        <div style={{ marginBottom: '5px' }}>
          <div className='show-label'>user_agent</div>
          <div className='show-content'>{rowData?.user_agent}</div>
        </div>
      </>
    );
  }, []);

  return (
    <Cell
      dataKey={dataKey}
      renderContent={renderContent}
      renderTitle={renderContent}
      {...props}
    />
  );
};