import React from 'react';
import _ from 'lodash';
import { Table } from 'rsuite';
import { CampaignStatusIcon } from '@/components/Icon/index';

const { Cell } = Table;

export default ({ rowData, dataKey, ...props }: any) => {
  const status = _.get(rowData, dataKey);
  return (
    <Cell {...props} className={`campaign-status-cell campaign-${_.lowerCase(status)}`}>
      {CampaignStatusIcon(status)} {_.capitalize(status)}
    </Cell>
  );
};
