import _ from 'lodash';
import Cookie from 'js-cookie';

const localeCookieKey = 'django_language';

export const getCurrentLocale = () => {
  const localeInCookie = Cookie.get(localeCookieKey) || '';
  return _.get(localeInCookie.split('-'), '0', '') === 'zh' ? 'zh-CN' : 'en-US';
};

export const setCurrentLocale = (locale: string) => {
  Cookie.set(localeCookieKey, _.toLower(locale));
};
