import React, { useState, useCallback, useMemo, useRef } from 'react';
import _ from 'lodash';
import { Alert } from 'rsuite';

import { API_TEAM, API_TEAM_USER } from '@/constants/APIs';
import { EditCheckPicker } from '@/components/Table/Cells/FloatActionCell/Actions';
import getRandomNum from '@/utils/random';
import fetch, { FETCH_METHOD } from '@/utils/request/fetch';
import RESPONSE_STATUS from '@/constants/responseStatus';

const REQ_TEAM_URL = API_TEAM;
export default ({ rowData, refetchTableData }: any) => {
  const pickerRef = useRef(null);
  const [checkedValues, setCheckedValues] = useState([]);
  const [reqDepsNum, setReqDepsNum] = useState(null); // 必须是null，这样才 _.isNil，一开始不会被执行
  const [submitLoading, setSubmitLoading] = useState(false);
  const [overrideData, setOverrideData] = useState(null);
  const [pickerLoading, setPickerLoading] = useState(true);

  const reqParam = useMemo(() => {
    const { beId } = rowData;
    return {
      beId,
      status: 1,
      orderColumn: 'name',
      orderType: 'asc',
      var: ''
    };
  }, [rowData]);

  const defaultTeams = useMemo(() => {
    const { teams } = rowData;
    return _.map(teams, t => {
      const [id, name] = _.get(_.toPairs(t), '0');
      return { id: +id, name };
    });
  }, [rowData]);

  const defaultTeamsIds = useMemo(() => defaultTeams.map(d => d.id), [defaultTeams]);

  const handleOpen = useCallback(() => {
    setOverrideData(null);
    setPickerLoading(true);
    setCheckedValues(defaultTeamsIds);
    setReqDepsNum(getRandomNum());
  }, [defaultTeamsIds]);

  const formatResData = useCallback(
    (data: Array<any>) => {
      return _.unionBy(data, defaultTeams, 'id');
    },
    [defaultTeams]
  );

  const handleChange = useCallback((value: any) => {
    setCheckedValues(value);
  }, []);

  const handleClose = useCallback(() => {
    setOverrideData([]);
    setPickerLoading(true);
  }, []);

  const onFetchSuccess = useCallback(() => {
    setPickerLoading(false);
  }, []);

  const handleSubmit = useCallback(() => {
    const { id } = rowData;
    setSubmitLoading(true);
    fetch(API_TEAM_USER, {
      method: FETCH_METHOD.PUT,
      params: {
        var: `/${id}`
      },
      data: {
        teams: checkedValues.toString()
      }
    })
      .then(resp => {
        const { status } = resp;
        if (status === RESPONSE_STATUS.FIELD_INVALID) {
          const errObj = _.get(resp, 'errors', {});
          _.forOwn(errObj, (v, k) => {
            Alert.error(`${k}: ${v}`);
          });
          return;
        }

        refetchTableData && refetchTableData();
        const triggerHideFn = _.get(pickerRef, 'current.triggerRef.current.hide', null);
        _.isFunction(triggerHideFn) && triggerHideFn();
      })
      .finally(() => {
        setSubmitLoading(false);
      });
  }, [checkedValues, rowData, refetchTableData]);

  return (
    <EditCheckPicker
      onOpen={handleOpen}
      onClose={handleClose}
      value={checkedValues}
      url={REQ_TEAM_URL}
      deps={[reqDepsNum]}
      params={reqParam}
      onChange={handleChange}
      menuClassName="team-user-menu"
      checkAndConfirmFooter
      dataRespFormatter={formatResData}
      footerSubmitLoading={submitLoading}
      onFooterSubmitClicked={handleSubmit}
      ref={pickerRef}
      data={overrideData}
      loading={pickerLoading} // 覆盖loading的状态,这个组件负责管理
      onFetchSuccess={onFetchSuccess}
      footerDisabled={_.isEqual(_.sortBy(defaultTeamsIds), _.sortBy(checkedValues))}
      // TODO: props 没了
    />
  );
};
