import React, { memo, useCallback, useMemo, useState, useEffect } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';

import * as APIs from '@/constants/APIs';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SelectPicker from '@/components/Picker/SelectPicker';
import BEPicker from '@/components/Picker/logic/BEPicker';
import AdvBrandPicker from '@/components/Picker/logic/AdvBrandPicker';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import {
  FloatActionCell,
  CampaignStatusCell,
  DateCell,
  TextCell,
  MultiLineCell
} from '@/components/Table/Cells';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { getShortNameById, BasicReportProps } from '@/constants/config';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { getUserProfile } from '@/utils/location';
import BETeamPicker from './BETeamPicker';

import { campaignTypeData, statusData, filterParamKeys, defaultFilter } from './constants';

const maxLine = 3;

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    handleSearchWord,
    handleChangeCustomParam,
    refetchTableData
  }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const campaignTypeDataIntl = usePickerDataFromObj(campaignTypeData);
    const [type, setType] = useState(1);
    const isNormalCampaign = type === campaignTypeDataIntl[0].id;
    const isCrossCampaign = type !== campaignTypeDataIntl[0].id;
    const tableReqApi = isCrossCampaign ? APIs.API_CROSS_CAMPAIGN_VAR : APIs.API_CAMPAIGN_VAR;

    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: tableReqApi,
      postParams: {
        [filterParamKeys.TEAM]: 1, // 在切换crossCampaign/campaign之后的下一次渲染，这个postParam和tableParam会同时改变，触发重新请求
        ...(isCrossCampaign ? { var: 'json' } : { var: '' })
      }
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const renderActions = useCallback(
      rowData => {
        return (
          <>
            <BETeamPicker
              isCross={isCrossCampaign}
              rowData={rowData}
              refetchTableData={refetchTableData}
            />
          </>
        );
      },
      [isCrossCampaign, refetchTableData]
    );

    const handleTypeChange = useCallback(
      (typeVal: number) => {
        setType(typeVal);
        if (type !== typeVal) {
          handleChangeCustomParam({ ...defaultFilter });
        }
      },
      [handleChangeCustomParam, type]
    );

    const handleStatusChange = useCallback(
      (statusVal: string) => {
        handleChangeCustomParam({
          [filterParamKeys.STATUS]: statusVal
        });
      },
      [handleChangeCustomParam]
    );

    const handleBEChange = useCallback(
      (beVal: Array<any>) => {
        handleChangeCustomParam({
          [filterParamKeys.BE]: beVal
        });
      },
      [handleChangeCustomParam]
    );

    const handleAdvBrandChange = useCallback(
      (advBrandVal: Array<any>) => {
        handleChangeCustomParam({
          [filterParamKeys.ADVERTISER]: advBrandVal
        });
      },
      [handleChangeCustomParam]
    );

    const handleExport = useCallback(() => {
      onExport &&
        onExport({
          ...(isCrossCampaign ? { var: 'xlsx' } : { var: '.xlsx' }),
          [filterParamKeys.TEAM]: 1
        });
    }, [onExport, isCrossCampaign]);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SelectPicker
          key="type"
          prepend={formatMessage('i18n_type')}
          data={campaignTypeDataIntl}
          value={type}
          onChange={handleTypeChange}
        />,
        isNormalCampaign && (
          <BEPicker
            key="be"
            chosenBE={_.get(filterParams, filterParamKeys.BE, [])}
            beChangeHandler={handleBEChange}
          />
        ),
        isNormalCampaign && (
          <AdvBrandPicker
            key="advBrand"
            className="cus-picker"
            chosenAdvBrand={_.get(filterParams, filterParamKeys.ADVERTISER, [])}
            onChange={handleAdvBrandChange}
          />
        ),
        <SelectPicker
          key="status"
          prepend={formatMessage('i18n_status')}
          data={statusData}
          value={_.get(filterParams, filterParamKeys.STATUS, defaultFilter.status)}
          onChange={handleStatusChange}
        />,
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button key="exportTable" className="pull-right" onClick={handleExport}>
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };

    return (
      <Table
        {...tableDataMemo}
        renderToolbar={renderToolbar}
        onChange={onChange}
        className="ad-auth-table"
        wordWrap
      >
        {/* ID */}
        <Column width={100} fixed sortable>
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>
        {/* 推广计划名称 */}
        <Column width={200} resizable fixed sortable>
          <HeaderCell>
            {formatMessage(isNormalCampaign ? 'i18n_campaignName' : 'i18n_crossCamName')}
          </HeaderCell>
          <TextCell dataKey="name" maxLine={maxLine} />
        </Column>
        {/* Team授权 */}
        <Column width={200} resizable>
          <HeaderCell>{formatMessage('i18n_teamAuth')}</HeaderCell>
          <MultiLineCell
            dataKey="teams"
            renderActions={renderActions}
            maxLine={maxLine}
            formatter={data => {
              const formattedData = _.toPairs(data).reduce((acc, [beId, teams]: any) => {
                const chosenBEName = _.get(teams, '0.beName', '');
                const isAllTeam = teams.length === 1 && !_.isNumber(_.get(teams, '0.teamId'));

                const displayTeams = isAllTeam
                  ? `${chosenBEName} / All Members`
                  : teams.map((t: any) => `${chosenBEName} / ${_.get(t, 'teamName', '')}`);
                return acc.concat(displayTeams);
              }, []);
              return _.isEmpty(formattedData) ? [formatMessage('i18n_notAssigned')] : formattedData;
            }}
          />
        </Column>
        {/* 主要拥有者 */}
        {isNormalCampaign && (
          <Column width={200} resizable sortable>
            <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
            <TextCell
              dataKey="primary"
              formatter={data =>
                `${getShortNameById(_.get(data, 'role', ''))}: ${_.get(data, 'beName', '')}`
              }
              maxLine={maxLine}
            />
          </Column>
        )}
        {/* 次要拥有者 */}
        {getUserProfile().internal && (
          <Column width={200} resizable>
            <HeaderCell>{formatMessage('i18n_secondaryOwner')}</HeaderCell>
            <MultiLineCell
              dataKey={isNormalCampaign ? 'secondaries' : 'secondary'}
              maxLine={maxLine}
              formatter={data =>
                _.isArray(data) &&
                data.reduce(
                  (acc, i) =>
                    _.isEmpty(i)
                      ? acc
                      : acc.concat(
                        `${getShortNameById(_.get(i, 'role', ''))}: ${_.get(i, 'beName', '')}`
                      ),
                  []
                )
              }
            />
          </Column>
        )}
        {/* 广告主 */}
        {isNormalCampaign && (
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_advertiser')}</HeaderCell>
            <TextCell dataKey="advertiser" maxLine={maxLine} />
          </Column>
        )}
        {/* 品牌 */}
        {isNormalCampaign && (
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_brand')}</HeaderCell>
            <TextCell dataKey="brand" maxLine={maxLine} />
          </Column>
        )}
        {/* 状态 */}
        <Column width={150} sortable>
          <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
          <CampaignStatusCell dataKey="status" />
        </Column>
        {/* 开始日期 */}
        <Column width={180} sortable>
          <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
          <DateCell dataKey="startDate" />
        </Column>
        {/* 结束日期 */}
        <Column width={180} sortable>
          <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
          <DateCell dataKey="endDate" />
        </Column>
        {/* 数据收集开始日期 */}
        {isNormalCampaign && (
          <Column width={180} sortable>
            <HeaderCell>{formatMessage('i18n_dataCollectionStartDate')}</HeaderCell>
            <DateCell dataKey="collectStartDate" />
          </Column>
        )}
        {/* 目标人群 */}
        <Column width={150} sortable>
          <HeaderCell>{formatMessage('i18n_targetPerson')}</HeaderCell>
          <FloatActionCell dataKey="target" />
        </Column>
        {/* 创建者 */}
        {isNormalCampaign && (
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_creator')}</HeaderCell>
            <TextCell dataKey="creator" maxLine={maxLine} />
          </Column>
        )}
        {/* 频次 */}
        {!isNormalCampaign && (
          <Column width={100}>
            <HeaderCell>{formatMessage('i18n_frequency')}</HeaderCell>
            <FloatActionCell dataKey="enable_freq" />
          </Column>
        )}
      </Table>
    );
  }
);
