export enum filterParamKeys {
  BE = 'beId',
  STATUS = 'status'
}

export interface FilterParam {
  beId: Array<number>;
  status: string | number;
}

export const defaultFilter: FilterParam = {
  beId: [],
  status: ''
};

export const defaultDrawerTableFilter = {
  beId: [] as number[],
  advertiser: [] as any[],
  date: getLastNDaysRangeBeforeCertainDate(new Date(), 7),
  report: 1,
  status: '3,4'
};

export const utilsFn = {
  getChosenBE(filterParam: FilterParam) {
    return _.get(filterParam, filterParamKeys.BE, defaultFilter.beId);
  },
  getStatus(filterParam: FilterParam) {
    return _.get(filterParam, filterParamKeys.STATUS, defaultFilter.status);
  }
};
