import React, { useCallback } from 'react';
import _ from 'lodash';
import { InputGroup, InputNumber, Checkbox } from 'rsuite';

interface CustomInputGroupProps {
  values?: Record<string, any>;
  checkKey?: string;
  inputKey?: string;
  onChange?: Function;
  threshold?: number; // 多少开始显示 +
  min?: number;
  max?: number;
  errors?: any;
  disabled?: boolean;
}

export default ({
  values,
  checkKey,
  inputKey,
  onChange,
  threshold,
  min,
  max,
  errors,
  disabled = false
}: CustomInputGroupProps) => {
  const showPlusPostfixIfOverThrehold = useCallback(
    (num: number) => {
      return _.toNumber(num) > threshold;
    },
    [threshold]
  );

  const checkVal = _.get(values, checkKey, true);
  const inputVal = _.get(values, inputKey, []).map(Number);
  const inputErr = _.get(errors, checkKey, '');

  const handleChangeVStart = useCallback(
    val => {
      onChange(inputKey)([_.toNumber(val), inputVal[1] || 20]);
    },
    [onChange, inputVal, inputKey]
  );

  const handleChangeVEnd = useCallback(
    val => {
      onChange(inputKey)([inputVal[0], _.toNumber(val)]);
    },
    [onChange, inputVal, inputKey]
  );

  const handleChecked = useCallback(
    (val, checked) => {
      onChange(checkKey)(checked);
    },
    [onChange, checkKey]
  );

  return (
    <>
      <div className="custom-inputgrp-container">
        <Checkbox
          style={{ marginLeft: 0 }}
          checked={checkVal}
          inline
          onChange={handleChecked}
          disabled={disabled}
        />
        <InputGroup className="custom-inputgrp" style={{ width: 218 }} disabled={disabled}>
          <InputNumber
            min={min}
            max={inputVal[1]}
            value={inputVal[0]}
            onChange={handleChangeVStart}
            postfix={showPlusPostfixIfOverThrehold(inputVal[0]) ? '+' : ''}
          />
          <InputGroup.Addon>~</InputGroup.Addon>
          <InputNumber
            min={min}
            max={max}
            value={inputVal[1]}
            onChange={handleChangeVEnd}
            postfix={showPlusPostfixIfOverThrehold(inputVal[1]) ? '+' : ''}
          />
        </InputGroup>
      </div>
      <div style={{ color: 'red', marginTop: 6 }}>{inputErr}</div>
    </>
  );
};
