import React, { useCallback, useContext } from 'react';
import { Checkbox, TagPicker } from 'rsuite';
import { CheckPickerProps } from 'rsuite/lib/CheckPicker';

import { DrawerContainerContext } from '@/components/Drawer';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

export default React.memo(
  ({
    data,
    value = [],
    onChange,
    labelKey,
    valueKey,
    supportAll = true,
    ...rest
  }: CheckPickerProps & {
    supportAll?: boolean;
  }) => {
    const { formatMessage } = useFmtMsgFori18n();
    const getContainer = useContext(DrawerContainerContext);

    const handleCheckAll = useCallback(
      (val, checked: boolean, e: React.SyntheticEvent<HTMLElement, Event>) => {
        onChange(checked ? data.map(d => d[valueKey]) : [], e);
      },
      [onChange, data, valueKey]
    );

    const handleClean = useCallback(
      (e: React.SyntheticEvent<HTMLElement, Event>) => {
        onChange([], e);
      },
      [onChange]
    );

    const renderFooter = useCallback(() => {
      return (
        <div className="checkpicker-with-removable-extra-footer">
          <Checkbox
            inline
            indeterminate={value.length > 0 && value.length < data.length}
            checked={data.length === value.length}
            onChange={handleCheckAll}
          >
            {formatMessage('i18n_selectAll')}
          </Checkbox>
        </div>
      );
    }, [data, value, handleCheckAll, formatMessage]);

    return (
      <TagPicker
        data={data}
        value={value}
        onChange={onChange}
        onClean={handleClean}
        cleanable={false}
        labelKey={labelKey}
        valueKey={valueKey}
        placement="autoVerticalStart"
        container={getContainer}
        {...(supportAll ? { renderExtraFooter: renderFooter } : null)}
        {...rest}
      />
    );
  }
);
