import React from 'react';
import _ from 'lodash';
import SelectPicker, { CustomSelectPickerProps } from '@/components/Picker/SelectPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { secondaryData } from '@/constants/config';

export default React.memo<Omit<CustomSelectPickerProps, 'data'>>(function({ data = [], ...props }) {
  const { formatMessage } = useFmtMsgFori18n();
  const list = usePickerDataFromObj(secondaryData, { labelKey: 'label' });
  let newData = _.filter(list, i => data.indexOf(i.value) > -1);
  // newData.unshift({
  //   key: '1',
  //   value: null,
  //   label: formatMessage('i18n_none')
  // });
  const pickerDataProps = {
    data: newData
  };
  return (
    <SelectPicker
      prepend={formatMessage('i18n_timerDimension')}
      labelKey="label"
      valueKey="value"
      {...props}
      {...pickerDataProps}
    />
  );
});
