import React, { useCallback, useContext } from 'react';
import _ from 'lodash';
import { Input, Icon } from 'rsuite';
import { Schema } from 'schema-typed';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import CustomFieldWrapper from '@/components/Form/CustomFieldWrapper';
import { ErrorMessageWithAbsContainer } from '@/components/Form/ErrorMessage';
import { FormErrorContext } from '@/components/Form/FormContext';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

const GroupItem = ({
  idx,
  maxDate,
  hierarchyData,
  value = {},
  onChange,
  onAdd,
  onRemove,
  disabled,
  localModel
}: any) => {
  const { formatMessage } = useFmtMsgFori18n();
  // 这个重合度计算 比较特殊，这里统一使用 字段跟其他不一样
  const { dateRange, dimension, id_set: idSet = '' } = value;
  const { formErrors, setFormErrors } = useContext(FormErrorContext);

  const handleLevelChange = useCallback(
    val => {
      onChange(
        {
          ...value,
          dimension: val
        },
        idx
      );
    },
    [onChange, value, idx]
  );

  const handleDateChange = useCallback(
    val => {
      localModel.checkForFieldAsync(`dateRange-${idx}`, val).then(checkResult => {
        setFormErrors(prevErrors => ({
          ...prevErrors,
          [`dateRange-${idx}`]: checkResult.errorMessage
        }));
      });

      onChange(
        {
          ...value,
          dateRange: val
        },
        idx
      );
    },
    [onChange, value, idx, localModel]
  );

  const handleIdsChange = useCallback(
    val => {
      onChange(
        {
          ...value,
          // eslint-disable-next-line @typescript-eslint/camelcase
          id_set: val
        },
        idx
      );
    },
    [onChange, value, idx]
  );

  const handleIdsBlur = useCallback(() => {
    localModel.checkForFieldAsync(`ids-${idx}`, idSet).then(checkResult => {
      setFormErrors(prevErrors => ({
        ...prevErrors,
        [`ids-${idx}`]: checkResult.errorMessage
      }));
    });
  }, [localModel, idSet]);

  const handleRemove = useCallback(() => {
    onRemove(idx);
  }, [idx, onRemove]);

  return (
    <>
      {/** 时间范围 */}
      <CustomFieldWrapper label={formatMessage('i18n_dateRange')} cleanable={false} required>
        <>
          <DateRangePicker
            placement="autoVerticalStart"
            value={dateRange}
            maxDate={maxDate}
            onChange={handleDateChange}
            disabled={disabled}
          />
          <ErrorMessageWithAbsContainer error={_.get(formErrors, `dateRange-${idx}`)} />
        </>
      </CustomFieldWrapper>

      {/** 导出层级 */}
      <CustomFieldWrapper label={formatMessage('i18n_exportLevel')} accepter={SelectPicker}>
        <>
          <SelectPicker
            data={hierarchyData}
            className="rs-form-control-wrapper"
            value={dimension}
            onChange={handleLevelChange}
            disabled={disabled}
          />
          <ErrorMessageWithAbsContainer error={_.get(formErrors, `dimension-${idx}`)} />
        </>
      </CustomFieldWrapper>

      {/** 层级ID */}
      <CustomFieldWrapper
        accepter={SelectPicker}
        label={formatMessage('i18n_levelId')}
        message={formatMessage('i18n_idListTips')}
        required
      >
        <>
          <div className="level-container">
            <Input
              value={idSet}
              onChange={handleIdsChange}
              onBlur={handleIdsBlur}
              disabled={disabled}
            />
            <div className="level-icon" onClick={disabled ? undefined : onAdd}>
              <Icon icon="plus" />
            </div>
            {idx !== 0 && (
              <div className="level-icon" onClick={disabled ? undefined : handleRemove}>
                <Icon icon="trash" />
              </div>
            )}
          </div>
          <ErrorMessageWithAbsContainer error={_.get(formErrors, `ids-${idx}`)} />
        </>
      </CustomFieldWrapper>
    </>
  );
};

interface Props {
  value?: Array<any>;
  onChange?: Function;
  maxDate: Date;
  hierarchyData: Array<any>;
  defaultItemData: BaseObject<any>;
  errorMessage?: any;
  customError?: Array<any>;
  disabled?: boolean;
  localModel: Schema;
}

export default ({
  value = [],
  onChange,
  maxDate,
  hierarchyData,
  defaultItemData,
  disabled,
  localModel
}: Props) => {
  const { formErrors, setFormErrors } = useContext(FormErrorContext);

  const handlePartialChange = useCallback(
    (val, idx) => {
      onChange(
        _.map(value, (v, i) => {
          if (i === idx) {
            return val;
          }

          return v;
        })
      );
    },
    [onChange, value]
  );

  const handleAdd = useCallback(() => {
    onChange(value.concat([{ ...defaultItemData }]));
  }, [onChange, value, defaultItemData]);

  const handleRemove = useCallback(
    idx => {
      const newValue = _.filter(value, (v, i) => idx !== i);

      // 删除时，需要校验每一项
      Promise.all(
        newValue.map((val, i) =>
          Promise.resolve().then(() =>
            Promise.all([
              localModel
                .checkForFieldAsync(`dateRange-${i}`, _.get(val, 'dateRange'))
                .then(checkResult =>
                  checkResult.hasError ? { [`dateRange-${i}`]: checkResult.errorMessage } : {}
                ),
              localModel
                .checkForFieldAsync(`ids-${i}`, _.get(val, 'id_set'))
                .then(checkResult =>
                  checkResult.hasError ? { [`ids-${i}`]: checkResult.errorMessage } : {}
                )
            ]).then(([dateErr, idsErr]) => ({
              ...dateErr,
              ...idsErr
            }))
          )
        )
      ).then(newErrors => {
        setFormErrors(prevErrors => ({
          ..._.pick(prevErrors, 'name', 'exportType'),
          ..._.reduce(
            newErrors,
            (acc, i) => ({
              ...acc,
              ...i
            }),
            {}
          )
        }));
        onChange(newValue);
      });
    },
    [onChange, value, localModel]
  );

  return (
    <div className="calc-data-group">
      {_.map(value, (v, idx) => (
        <GroupItem
          key={idx}
          maxDate={maxDate}
          hierarchyData={hierarchyData}
          value={v}
          idx={idx}
          onChange={handlePartialChange}
          onAdd={handleAdd}
          onRemove={handleRemove}
          disabled={disabled}
          localModel={localModel}
        />
      ))}
      <ErrorMessageWithAbsContainer error={_.get(formErrors, 'data')} />
    </div>
  );
};
