import React, { memo } from 'react';
import _ from 'lodash';
import { FormGroup, ControlLabel } from 'rsuite';
interface CustomLabelCommonProps {
  name?: string;
  value: any;
  label: string;
}

export default memo(({ value, label }: CustomLabelCommonProps) => {
  return (
    <FormGroup>
      <ControlLabel>{label}</ControlLabel>
      <div className="rs-form-control-wrapper">
        <span className="rs-control-label" style={{ textAlign: 'left' }}>
          {_.isArray(value) && !_.isEmpty(value)
            ? value.map(i => {
                return (
                  <span key={i} style={{ marginRight: '20px' }}>
                    {i}
                  </span>
                );
              })
            : value}
        </span>
      </div>
    </FormGroup>
  );
});
