import { GET } from '@/utils/request/fetch';
import * as APIs from '@/constants/APIs';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';

/**
 * 实时
 */
export function onFetchRealTime(params: object, cb: (res: object) => void) {
  return GET(APIs.API_REALTIME, {
    params: {
      ...params,
      hierarchy: 'campaign'
    }
  }).then(cb);
}

/**
 * 实时
 */
interface Ret {
  loading: boolean;
  onFetch: Function;
  response: any;
}

export default (params: any): Ret => {
  const { loading, onFetch, response } = useFetch<CommonResponse>(
    FETCH_METHOD.GET,
    APIs.API_REALTIME,
    {
      cacheParams: true,
      params: {
        hierarchy: 'campaign',
        ...params
      }
    }
  );

  return {
    loading,
    onFetch,
    response
  };
};
