/**
 * sessionStatus的封装，主要是处理传递高级过滤这样子选项很多的GET请求
 */

import { API_STATUS } from '@/constants/APIs';
import { GET, POST } from './request/fetch';

export const updateStatus = (data: BaseObject<any>) => {
  return POST(API_STATUS, { data });
};

export const fetchStatus = (params: BaseObject<any>) => {
  return GET(API_STATUS, { params });
};
