import React, { useMemo, useEffect, useCallback } from 'react';
import { Schema } from 'schema-typed';
import RadioGroup from '@/components/Form/RadioGroup';
import CustomField from '@/components/Form/CustomField';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import InputGroup from '@/components/Form/InputNumGrp';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import MultiField from '@/components/Form/MultiField';
import CheckPickerWithRemovableValue from '@/components/Picker/CheckPickerWithRemovableValue';
import Toggle from '@/components/Form/Toggle';
import CalcDataGroup from './CalcDataGroup';
import ConditionalFilter from './ConditionalFilter';
import {
  TAB_KEY,
  getDivisionData,
  getLimitData,
  fieldsData,
  pkTypeData,
  deviceTypeData,
  dimensionData,
  secondaryDimData,
  metricsData,
  defaultFormCalcData,
  HierarchyIdKey,
  conversionDimension
} from './constants';

interface DrawerCustomFieldProps {
  type: TAB_KEY;
  hierarchyData: Array<any>;
  maxDate: Date;
  hierarchy: string;
  disabled?: boolean;
  localModel: Schema;
}

export const DrawerCustomField = ({
  type,
  hierarchyData,
  maxDate,
  hierarchy,
  disabled = false,
  localModel
}: DrawerCustomFieldProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const divisionDataIntl = usePickerDataFromObj(getDivisionData(type, hierarchy as any));
  const limitDataIntl = usePickerDataFromObj(getLimitData(hierarchy as any));
  const fmtFieldsData = useMemo(
    () =>
      fieldsData.map(f => ({
        id: f,
        name: f
      })),
    []
  );
  const fmtDeviceTypeData = useMemo(
    () =>
      deviceTypeData.map(d => ({
        name: d,
        id: d
      })),
    []
  );
  const pkTypeDataIntl = usePickerDataFromObj(pkTypeData);
  const secondaryDimDataIntl = usePickerDataFromObj(secondaryDimData);
  const metricsDataIntl = usePickerDataFromObj(
    hierarchy === HierarchyIdKey.CONVERSION
      ? metricsData.filter(i => i.id === 'conversion')
      : metricsData.filter(i => i.id !== 'conversion')
  );
  const dimensionDataIntl = usePickerDataFromObj(
    hierarchy === HierarchyIdKey.CONVERSION
      ? dimensionData.filter(i => conversionDimension.indexOf(i.id) > -1)
      : dimensionData
  );
  switch (type) {
    case TAB_KEY.RAW_EXPORT: {
      return (
        <>
          {/** 分包导出 */}
          <CustomField
            name="division"
            label={formatMessage('i18n_subPackageExport')}
            accepter={SelectPicker}
            data={divisionDataIntl}
            disabled={disabled}
          />
          {/** 导出范围 */}
          <CustomField
            name="limit"
            label={formatMessage('i18n_exportLimits')}
            accepter={SelectPicker}
            data={limitDataIntl}
            required
            disabled={disabled}
          />
          {/** 导出字段 */}
          <CustomField
            name="fields"
            label={formatMessage('i18n_exportField')}
            accepter={CheckPickerWithRemovableValue}
            data={fmtFieldsData}
            labelKey="name"
            valueKey="id"
            cleanable
            supportAll
            required
            disabled={disabled}
          />
        </>
      );
    }

    case TAB_KEY.DEVICE_ID: {
      return (
        <>
          {/** 分包导出 */}
          <CustomField
            name="division"
            label={formatMessage('i18n_subPackageExport')}
            accepter={SelectPicker}
            data={divisionDataIntl}
            disabled={disabled}
          />
          {/** 导出范围 */}
          <CustomField
            name="limit"
            label={formatMessage('i18n_exportLimits')}
            accepter={SelectPicker}
            data={limitDataIntl}
            required
            disabled={disabled}
          />
          {/** 频次 */}
          <MultiField
            names={['frequencyChecked', 'frequency']}
            label={formatMessage('i18n_frequency')}
            accepter={InputGroup}
            inputKey="frequency"
            checkKey="frequencyChecked"
            threshold={19}
            min={1}
            max={20}
            disabled={disabled}
          />
          {/** 设备类型 */}
          <CustomField
            name="devicePrefix"
            label={formatMessage('i18n_deviceType')}
            accepter={SelectPicker}
            data={fmtDeviceTypeData}
            disabled={disabled}
          />
          <CustomField
            name="isMD5"
            label={formatMessage('i18n_whetherMd5')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_yes')}
            unCheckedChildren={formatMessage('i18n_no')}
            size="lg"
            disabled={disabled}
          />
        </>
      );
    }

    case TAB_KEY.CUSTOM_REPORT: {
      return (
        <>
          {/** 指标 */}
          <CustomField
            name="metrics"
            accepter={CheckPickerWithRemovableValue}
            label={formatMessage('i18n_metrics')}
            data={metricsDataIntl}
            labelKey="name"
            valueKey="id"
            required
            cleanable
            supportAll
            disabled={disabled}
            checkTrigger="none"
          />
          {/** 维度 */}
          <CustomField
            name="dimension"
            label={formatMessage('i18n_dimension')}
            accepter={CheckPicker}
            data={dimensionDataIntl}
            required
            disabled={disabled}
          />
          {/** 次级维度 */}
          <CustomField
            name="secondary"
            accepter={SelectPicker}
            label={formatMessage('i18n_secondary')}
            data={secondaryDimDataIntl}
            disabled={disabled}
          />
          {/** 过滤条件 */}
          <CustomField
            name="filters"
            className="export-haa-filter"
            label={formatMessage('i18n_filter')}
            accepter={ConditionalFilter}
            disabled={disabled}
            hierarchy={hierarchy}
          />
        </>
      );
    }

    case TAB_KEY.CALC_OVERLAP: {
      return (
        <>
          {/** 计算类型 */}
          <CustomField
            name="exportType" // form里面的字段就要exportType
            label={formatMessage('i18n_pkType')}
            accepter={RadioGroup}
            inline
            data={pkTypeDataIntl}
            disabled={disabled}
          />
          <CustomField
            name="data"
            accepter={CalcDataGroup}
            maxDate={maxDate}
            localModel={localModel}
            hierarchyData={hierarchyData}
            defaultItemData={defaultFormCalcData}
            disabled={disabled}
          />
        </>
      );
    }

    default:
      return null;
  }
};
