export default memo<BasicReportProps>(function ({ id, filterParams }) {
  const [impData, setImpData] = useState<Array<ObjectProps>>([]);
  const [clkData, setClkData] = useState<Array<ObjectProps>>([]);
  const [delay, setDelay] = useState(500);
  useInterval(() => {
    setDelay(1000 * 60);
    onFetchRealTime(
      {
        id,
        secondary: 'hourly',
        tag: 'impression',
        [filterParams.selectObj.key]: filterParams.selectObj.value
      },
      res => {
        setImpData(_.get(res, 'data.data.time_slot_pv'));
      }
    );
    onFetchRealTime(
      {
        id,
        secondary: 'hourly',
        tag: 'click',
        [filterParams.selectObj.key]: filterParams.selectObj.value
      },
      res => {
        setClkData(_.get(res, 'data.data.time_slot_pv'));
      }
    );
  }, delay);

  // 触发请求
  useEffect(() => {
    setDelay(500);
  }, [id, filterParams]);
  const { formatMessage } = useFmtMsgFori18n();
  return (
    <>
      <Panel>
        <div className="panel-title" style={{ marginBottom: 18 }}>
          {formatMessage('i18n_impAndClkLastThirtyMins')}
        </div>
        <Row className="show-grid">
          <Col xs={12}>
            <Bar data={impData} legend={[formatMessage('i18n_impressionsNum')]} />
          </Col>
          <Col xs={12}>
            <Bar data={clkData} legend={[formatMessage('i18n_clicksNum')]} />
          </Col>
        </Row>
      </Panel>
    </>
  );
});
