import _ from 'lodash';
import { FormattedNumber } from 'react-intl';
import React, { useCallback } from 'react';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from './FloatActionCell';

export interface NumberCellProps<T> extends BaseCellProps<T> {
  style?: string;
}

// 接口数值有可能是字符串数值，需要转化成number并且排除NaN
const NumberCell = ({ rowData, dataKey, style = 'decimal', ...props }: NumberCellProps<any>) => {
  const data = _.toNumber(_.get(rowData, dataKey));
  const renderContent = useCallback(() => {
    return _.isNumber(data) && !_.isNaN(data) ? (
      <FormattedNumber value={data} style={style} />
    ) : (
      <>{DEFAULT_TABLE_CELL_CONTENT}</>
    );
  }, [data, style]);

  return <Cell dataKey={dataKey} rowData={rowData} renderContent={renderContent} {...props} />;
};

export default React.memo(NumberCell);
