import _ from 'lodash';
import { advName, agName, pubName, defaultName, nameFlag } from './constants';

export const mapFromResponse = (rspData: Record<string, any>) => {
  return {
    ..._.omit(rspData, ['products', 'role']),
    status: rspData.status === 1,
    [advName]: {
      subList: _.get(rspData, 'role.advertiser.subList', []),
      chosen: _.isObject(_.get(rspData, 'role.advertiser'))
    },
    [agName]: {
      subList: _.get(rspData, 'role.agency.subList', []),
      chosen: _.isObject(_.get(rspData, 'role.agency'))
    },
    [pubName]: {
      subList: _.get(rspData, 'role.publisher.subList', []),
      chosen: _.isObject(_.get(rspData, 'role.publisher'))
    },
    [defaultName]: _.find(_.values(_.get(rspData, 'role')), i => i?.defaultRole)?.beRoleFlag,
    collaborators: _.map(rspData.collaborators, ({ beId, beRoleFlag }) => ({
      beId,
      roleId: beRoleFlag
    }))
  };
};

export const mapToRequest = (formData: Record<string, any>) => {
  const { internal } = formData;

  // 状态
  const { status: oldStatus } = formData;
  const status = oldStatus ? 1 : 2;

  // BE角色
  const {
    [advName]: advRole,
    [agName]: agRole,
    [pubName]: pubRole,
    [defaultName]: defaultRole
  } = formData;

  // 所选角色的picker值，分别传，但内部不用传
  const advBrandList =
    internal || !advRole?.chosen ? undefined : advRole?.subList;
  const ageBrandList =
    internal || !agRole?.chosen ? undefined : agRole?.subList;
  const siteList =
    internal || !pubRole?.chosen
      ? undefined
      : _.get(pubRole, 'subList', []).toString() || undefined;

  // 创建外部BE，需要传BERole字段，字符串类型，表示选择了哪些角色
  const BERoleArr = [];
  !_.isEmpty(advBrandList) && BERoleArr.push(nameFlag[advName]);
  !_.isEmpty(ageBrandList) && BERoleArr.push(nameFlag[agName]);
  !_.isEmpty(siteList) && BERoleArr.push(nameFlag[pubName]);
  const BERole = internal ? undefined : BERoleArr.toString();

  // 锁定
  const { isLockBeRole: oldIsLockBeRole, isLockAdS: oldIsLockAdS } = formData;
  const isLockBeRole = !internal && oldIsLockBeRole;
  const isLockAdS = !internal && oldIsLockAdS;

  // 产品
  // const { products: oldProducts } = formData;
  // const products = internal ? undefined : oldProducts.toString();

  // 合作方
  const { collaborators: oldCollaborators } = formData;
  const collaborators = internal
    ? undefined
    : _.map(oldCollaborators, ({ beId, roleId }) => ({ beId, beRoleFlag: roleId }));

  return {
    ..._.mapValues(_.omit(formData, ['role', advName, agName, pubName]), val =>
      val === '' ? undefined : val
    ),
    status,
    defaultRole,
    ageBrandList,
    advBrandList,
    siteList,
    BERole,
    isLockBeRole,
    isLockAdS,
    // products,
    collaborators
  };
};

export const mapErrors = (errors: Record<string, any> = {}) => {
  const newErrors = _.mapValues(_.omit(errors, ['advBrandList', 'ageBrandList', 'siteList']), val =>
    val.join('; ')
  );

  !_.isEmpty(errors.advBrandList) && (newErrors[advName] = errors.advBrandList.join('; '));
  !_.isEmpty(errors.ageBrandList) && (newErrors[agName] = errors.ageBrandList.join('; '));
  !_.isEmpty(errors.siteList) && (newErrors[pubName] = errors.siteList.join('; '));

  return newErrors;
};
