import React, { useMemo, useCallback } from 'react';
import _ from 'lodash';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { YYYYMMDD } from '@/utils/date';
import Table, { Column, HeaderCell } from '@/components/Table';
import { FloatActionCell, CampaignStatusCell, DateCell, MultiLineCell } from '@/components/Table/Cells';
import BEPicker from '@/components/Picker/logic/BEPicker';
import AdvBrandPicker from '@/components/Picker/logic/AdvBrandPicker';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { getShortNameById } from '@/constants/config';
import { utilsFn } from '@/views/common/tool/log/constants';

export default React.memo(({
  tableDataMemo,
  checkedItems,
  filterParams,
  onChange,
  handleCheck,
  handleSearchWord,
  handleChangeCustomParam
}: any) => {
  const { formatMessage } = useFmtMsgFori18n();

  const checkedIds = useMemo(() => _.map(checkedItems, 'id'), [checkedItems]);

  const handleChangeBE = useCallback(val => {
    handleChangeCustomParam({ beId: val });
  }, [handleChangeCustomParam]);

  const handleChangeAdvBrand = useCallback(val => {
    handleChangeCustomParam({ advertiser: val });
  }, [handleChangeCustomParam]);

  const handleChangeDate = useCallback(([start, end]: Date[]) => {
    handleChangeCustomParam({ date: [YYYYMMDD(start), YYYYMMDD(end)] });
  }, [handleChangeCustomParam]);

  const renderToolbar = () => (
    <>
      <BEPicker
        chosenBE={filterParams.beId}
        beChangeHandler={handleChangeBE}
      />
      <AdvBrandPicker
        supportAll
        chosenAdvBrand={filterParams.advertiser}
        onChange={handleChangeAdvBrand}
      />
      <DateRangePicker
        value={utilsFn.getDate(filterParams)}
        onChange={handleChangeDate}
        cleanable={false}
        placement="auto"
      />
      <SearchInput
        placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
        onChange={handleSearchWord}
      />
    </>
  );

  const offsetTopHeight = useMemo(() => {
    const tagBox = document.getElementsByClassName('tag-wrap')[0];
    return tagBox?.clientHeight || 0;
  }, [checkedItems]);

  return (
    <Table
      {...tableDataMemo}
      onChange={onChange}
      renderToolbar={renderToolbar}
      offsetTopHeight={offsetTopHeight}
      checkDataKey='id'
      onCheck={handleCheck}
      checkValues={checkedIds}
    >
      {/* ID */}
      <Column width={80} fixed sortable>
        <HeaderCell>ID</HeaderCell>
        <FloatActionCell dataKey='id' />
      </Column>

      {/* 推广计划名称 */}
      <Column flexGrow={1} minWidth={200} fixed sortable>
        <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
        <FloatActionCell dataKey='name' />
      </Column>

      {/* 主要拥有者 */}
      <Column flexGrow={1} minWidth={200}>
        <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
        <MultiLineCell
          dataKey='primary'
          formatter={data => _.isEmpty(data) ? [''] : [`${getShortNameById(_.get(data, 'role'))}: ${_.get(data, 'beName', '')}`]}
          maxLine={1}
        />
      </Column>

      {/* 广告主 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_advertiser')}</HeaderCell>
        <FloatActionCell dataKey='advertiser' />
      </Column>

      {/* 品牌 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_brand')}</HeaderCell>
        <FloatActionCell dataKey='brand' />
      </Column>

      {/* 状态 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
        <CampaignStatusCell dataKey='status' />
      </Column>

      {/* 开始日期 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
        <DateCell dataKey='startDate' />
      </Column>

      {/* 结束日期 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
        <DateCell dataKey='endDate' />
      </Column>

      {/* 数据收集开始日期 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_dataCollectionStartDate')}</HeaderCell>
        <DateCell dataKey='collectStartDate' />
      </Column>

      {/* 目标人群 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_targetPerson')}</HeaderCell>
        <FloatActionCell dataKey='target' />
      </Column>

      {/* 创建者 */}
      <Column flexGrow={1} minWidth={200} sortable>
        <HeaderCell>{formatMessage('i18n_creator')}</HeaderCell>
        <FloatActionCell dataKey='creator' />
      </Column>
    </Table>
  );
});