import chinaCitiesJson from 'echarts/map/json/china-cities.json';
import ngeohash from 'ngeohash';
import coordtransform from 'coordtransform';
import chinaJson from 'echarts/map/json/china.json';

export const provinces = chinaJson.features.map(feature => feature.properties.name);

interface Coords {
  longitude: number;
  latitude: number;
}

export function geohash2bdCoords(geohash: string): Coords {
  const wgs84Coords = ngeohash.decode(geohash);
  const gcj02Coords = coordtransform.wgs84togcj02(wgs84Coords.longitude, wgs84Coords.latitude);
  const bd09Coords = coordtransform.gcj02tobd09(gcj02Coords[0], gcj02Coords[1]);

  return {
    longitude: bd09Coords[0],
    latitude: bd09Coords[1]
  };
}

interface GeoCoordsObject {
  name: string;
  coords: Coords;
}

interface GeoCoordsMap {
  [name: string]: {
    latitude: number;
    longitude: number;
  };
}

const cityCoords: GeoCoordsObject[] = chinaCitiesJson.features.map((city: any) => ({
  // eslint-disable-next-line prettier/prettier
  name: city.properties?.name,
  coords: {
    longitude: city.properties?.cp?.[0],
    latitude: city.properties?.cp?.[1]
  }
}));

export const cityCoordsMap: GeoCoordsMap = cityCoords.reduce((map, city) => ({
  ...map,
  [city.name]: city.coords
}), {});

