import React, { memo, useCallback } from 'react';
import { Panel } from 'rsuite';
import _ from 'lodash';

import Navbar from '@/components/Navbar';
import { API_TASK_EXPORT } from '@/constants/APIs';
import { useMainPage } from '@/components/hooks/useMainPage';
import { TABLE } from '@/constants/config';
import { usePickerDataFromObj } from '@/utils/formatMessage';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import {
  ExportHaaCalcModel,
  ExportHaaRawExportModel,
  ExportHaaDeviceExportModel,
  ExportHaaCustomExportModel
} from '@/constants/form/models';

import { navData, defaultTableParam, TAB_KEY, getDefaultFormData } from './constants';
import {
  mapToRequest,
  mapFromRespCustomReport,
  mapFromRespDeviceExport,
  mapFromRespOverlap,
  mapFromRespRow,
  mapErrorsWithExportType
} from './dataMapper';
import Table from './Table';
import DrawerForm from './DrawerForm';

const tableOptions: TABLE = {
  orderColumn: 'id'
};

const getFormModel = (exportType: TAB_KEY) => {
  switch (exportType) {
    case TAB_KEY.RAW_EXPORT:
      return ExportHaaRawExportModel;
    case TAB_KEY.DEVICE_ID:
      return ExportHaaDeviceExportModel;
    case TAB_KEY.CUSTOM_REPORT:
      return ExportHaaCustomExportModel;
    case TAB_KEY.CALC_OVERLAP:
    default:
      return ExportHaaCalcModel;
  }
};
/**
 * http://adstracker.hypers.com.cn/
 * https://confluence.hypers.com/pages/viewpage.action?pageId=42337323
 * https://confluence.hypers.com/pages/viewpage.action?pageId=23605608
 * https://confluence.hypers.com/pages/viewpage.action?pageId=28935668
 * https://confluence.hypers.com/pages/viewpage.action?pageId=38241366
 * https://haa.hypers.com.cn/api/docs/
 */
export default memo(() => {
  const navDataIntl = usePickerDataFromObj(navData, { labelKey: 'label' }) as any;
  const { customParamResult } = useMainPage({
    tableOptions,
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultTableParam
      }
    ]
  });

  const [tableResult] = customParamResult;
  const { handleChangePagination, fmtParam, param, handleChangeCustomParam } = tableResult;

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  const { exportType } = param;
  const model = getFormModel(exportType);
  const defaultFormData = getDefaultFormData(exportType);

  const mapToReq = useCallback(
    (f: any) => {
      return {
        exportType, // 放在这里允许被覆盖，因为 重合度计算的exportType
        ...mapToRequest(f),
        ...(exportType === TAB_KEY.DEVICE_ID &&
          (f.frequencyChecked
            ? {
              frequency: f.frequency,
              frequencyChecked: undefined
            }
            : {
              frequency: undefined,
              frequencyChecked: undefined
            })),
        ...(exportType === TAB_KEY.DEVICE_ID && {
          isMD5: f.isMD5 ? true : false
        }),
        ...(exportType === TAB_KEY.CUSTOM_REPORT &&
          f.filters && {
          filters: _.filter(f.filters as Array<any>, fff => fff.type && fff.value).map(ffff => ({
            ...ffff,
            id: undefined
          }))
        }),
        ...(exportType === TAB_KEY.CUSTOM_REPORT &&
          f.secondary === '' && {
          secondary: undefined
        }),
        ...(exportType === TAB_KEY.CALC_OVERLAP && {
          start: undefined,
          end: undefined,
          data: _.map(f.data, d => mapToRequest(d))
        }),
        ...(f.division === '' && {
          // 如果选择了无，就不传给后端
          division: undefined
        })
      };
    },
    [exportType]
  );

  const mapFromResp = useCallback(
    (resp: any) => {
      switch (exportType) {
        case TAB_KEY.RAW_EXPORT:
          return mapFromRespRow(resp);
        case TAB_KEY.DEVICE_ID:
          return mapFromRespDeviceExport(resp);
        case TAB_KEY.CUSTOM_REPORT:
          return mapFromRespCustomReport(resp);
        case TAB_KEY.CALC_OVERLAP:
          return mapFromRespOverlap(resp);
        default:
          return resp;
      }
    },
    [exportType]
  );

  const mapErrors = useCallback(rspErrors => mapErrorsWithExportType(exportType, rspErrors), [
    exportType
  ]);

  const {
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    handleFormChange,
    handleFormCreate,
    handleFormEdit,
    handleFormClose,
    handleFormSubmit,
    setFormErrors,
    checkForm,
    setDrawer
  } = useDrawerForm({
    defaultFormData,
    api: API_TASK_EXPORT,
    submitSuccessCallback: refetchTableData,
    mapFromResponse: mapFromResp,
    mapToRequest: mapToReq,
    mapErrors,
    model
  });

  const navSelectHandler = useCallback(
    val => {
      handleChangeCustomParam({
        exportType: val
      });
    },
    [handleChangeCustomParam]
  );

  return (
    <>
      <Panel className="panel-table">
        <Navbar
          appearance="subtle"
          activeKey={param.exportType}
          data={navDataIntl}
          onSelect={navSelectHandler}
        />
        <Table
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          handleFormCreate={handleFormCreate}
          handleFormEdit={handleFormEdit}
          refetchTableData={refetchTableData}
        />
        <DrawerForm
          drawer={drawer}
          formData={formData}
          originalFormData={originalFormData}
          formErrors={formErrors}
          formRef={formRef}
          onChange={handleFormChange}
          onSubmit={handleFormSubmit}
          onClose={handleFormClose}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
          model={model}
          param={param}
          setDrawer={setDrawer}
        />
      </Panel>
    </>
  );
});
