import React, { useState, useCallback, useEffect } from 'react';
import _ from 'lodash';
import { InputGroup, Input } from 'rsuite';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import MultiField from '@/components/Form/MultiField';
import { AdgroupModel } from '@/constants/form/models';
import SelectPicker from '@/components/Picker/SelectPicker';
import RadioGroup from '@/components/Form/RadioGroup';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { API_MEDIA } from '@/constants/APIs';
import template from '@/utils/template';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import CheckboxGroup from '@/components/Form/CheckboxGroup';
import { TODAY } from '@/constants/config';
import { dateIsBefore, dateIsAfter } from '@/utils/date';
import DateRangePicker from './part/DateRangePicker';
import ImgUploader from './part/ImgUploader';
import RegionPicker from './part/RegionPicker';
import {
  platformTypeData,
  defaultFormData,
  deviceData,
  buyMethodData,
  frequencyData,
  asyncTrackerData,
  webMonitorConnectData
} from './constants';

const URLInput = ({ type, className, ...props }: any) => (
  <InputGroup className="placement-form-url">
    <InputGroup.Addon>{type}</InputGroup.Addon>
    <Input {...props} />
  </InputGroup>
);

export default React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm,
    allCampaigns
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();
    const asyncTrackerDataIntl = usePickerDataFromObj(asyncTrackerData);
    const webMonitorConnectDataIntl = usePickerDataFromObj(webMonitorConnectData);
    const platformTypeDataIntl = usePickerDataFromObj(platformTypeData, {
      skipNoi18nPrefix: true
    });
    const deviceDataIntl = usePickerDataFromObj(deviceData, {
      skipNoi18nPrefix: true
    });
    const buyMethodDataIntl = usePickerDataFromObj(buyMethodData, {
      skipNoi18nPrefix: true
    });

    const { onFetch: onFetchSiteDetail, loading: siteDetailLoading } = useFetch<CommonResponse>(
      FETCH_METHOD.GET,
      template`${API_MEDIA}/${'id'}`,
      {
        cacheParams: true
      }
    );

    const onFetchAdServerSuccess = useCallback(() => {
      if (_.isNumber(formData.siteId)) {
        // 新建时改变站点，广告服务器自动填写为该站点的缺省广告服务器 编辑不请求
        !drawer.isEdit &&
          onFetchSiteDetail({ params: { id: formData.siteId } }).then(rsp => {
            const defaultAdServerId = _.get(rsp, 'data.defaultAdS');
            !drawer.isEdit &&
              _.isNumber(defaultAdServerId) &&
              onChange(defaultAdServerId, 'adserverId');
          });
      }
    }, [formData.siteId]);

    // 此处upload错误会提前由服务端校验，因此需要提出单独处理，否则会因执行check而被覆盖掉
    const [uploadError, setUploadError] = useState('');

    const [uploading, setUploading] = useState(false);

    useEffect(() => {
      !_.isEmpty(uploadError) && (formErrors.thumbnail = uploadError);
    }, [formErrors, uploadError]);

    const handleSubmit = useCallback(() => {
      _.isEmpty(uploadError) && onSubmit();
    }, [uploadError, onSubmit]);

    const handleChangeWebMonitorConnect = useCallback(val => {
      // 如果选择了设备ID打通，就自动勾选cookie打通
      if (val.includes(1) && !val.includes(0)) {
        onChange([0, 1], 'webMonitorConnect');
      }
    }, []);
    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editAdunit' : 'i18n_newAdunit')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        className="placement-drawer"
        onSubmit={handleSubmit}
        onHide={onClose}
        disabled={uploading} // 上传过程中禁止提交
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={AdgroupModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 所属推广计划 */}
          <CustomField
            name="campaignId"
            label={formatMessage('i18n_belongCampaign')}
            required
            accepter={SelectPicker}
            data={_.filter(
              allCampaigns as any[],
              i => !_.includes(['CANCELLED', 'COMPLETE'], i.status) || drawer.isEdit
            ).map(i => ({ ...i, primary: _.get(i, 'primary.beName') }))}
            groupBy="primary"
            disabled={drawer.isEdit}
            searchable
            searchKeys={['id', 'name', 'primary']}
            menuStyle={{ maxWidth: '350px' }}
          />

          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} required />

          {/* 投放起止日期 */}
          <MultiField
            names={['startDate', 'endDate']} // ToDo: 使用通用DateRangePicker，并改变数据结构
            label={formatMessage('i18n_startAndEndDate')}
            required
            accepter={DateRangePicker}
            checkTrigger="none"
            disabledDate={date => dateIsBefore(date, TODAY)}
            startOptions={{
              minDate: TODAY,
              maxDate: _.get(formData, 'endDate'),
              disabled: _.includes([3, 4, 0, 5], formData.selfStatus)
            }}
            endOptions={{
              minDate: _.get(formData, 'startDate') || TODAY,
              disabled: _.includes([4, 0], formData.selfStatus)
            }}
          />

          {/* 所属站点 */}
          <CustomField
            name="siteId"
            label={formatMessage('i18n_belongSite')}
            required
            accepter={SelectPicker}
            disabled={!_.isNumber(formData.campaignId)}
            url={API_MEDIA}
            params={{
              campaignId: formData.campaignId,
              box: 1,
              status: 1,
              orderColumn: 'asc',
              orderType: 'name'
            }}
            deps={[formData.campaignId]}
            shouldFilter
            searchable
          />

          {/* 广告服务器  */}
          <CustomField
            name="adserverId"
            label={formatMessage('i18n_adServer')}
            required
            accepter={SelectPicker}
            disabled={drawer.isEdit || !_.isNumber(formData.siteId)}
            url="/api/v1/adserver"
            params={{
              siteId: formData.siteId,
              campaignId: formData.campaignId,
              box: 1,
              orderColumn: 'name',
              orderType: 'asc'
            }}
            deps={[formData.siteId]}
            shouldFilter
            onFetchSuccess={onFetchAdServerSuccess}
            loading={siteDetailLoading}
            searchable
          />

          {/* 平台类型 */}
          <CustomField
            name="platformType"
            label={formatMessage('i18n_pltType')}
            accepter={CheckboxGroup}
            data={platformTypeDataIntl}
            itemWidth="30%"
            required
          />

          {/* 预估广告计数 */}
          <CustomField name="estImp" label={formatMessage('i18n_expectedImpressions')} />

          {/* 预估点击 */}
          <CustomField name="estClick" label={formatMessage('i18n_expectedClicks')} />

          {/* 点击跟踪跳转URL-电脑 */}
          <CustomField
            name="clkUrlDSK"
            label={formatMessage('i18n_redirectUrlOfAdClick')}
            accepter={URLInput}
            type={formatMessage('i18n_pc')}
            checkTrigger="blur"
          />

          {/* 点击跟踪跳转URL-移动 */}
          <CustomField
            name="clkUrlMbl"
            accepter={URLInput}
            type={formatMessage('i18n_mobile')}
            checkTrigger="blur"
          />

          {/* 点击跟踪跳转URL-OTT */}
          <CustomField name="clkUrlOtt" accepter={URLInput} type="OTT" checkTrigger="blur" />

          {/* 创意类型 */}
          <CustomField
            name="creativeType"
            label={formatMessage('i18n_menuAdType')}
            disabled={drawer.isEdit && !_.isEmpty(originalFormData.creativeType)}
          />

          {/* 素材名 */}
          <CustomField
            name="creative"
            label={formatMessage('i18n_material')}
            disabled={drawer.isEdit && !_.isEmpty(originalFormData.creative)}
          />

          {/* 设备 */}
          <CustomField
            name="targetDevice"
            label={formatMessage('i18n_device')}
            accepter={SelectPicker}
            data={deviceDataIntl}
            disabled={
              drawer.isEdit && originalFormData.targetDevice !== defaultFormData.targetDevice
            }
          />

          {/* 广告位置 */}
          <CustomField
            name="position"
            label={formatMessage('i18n_adPosition')}
            disabled={drawer.isEdit && !_.isEmpty(originalFormData.position)}
          />

          {/* 素材大小 */}
          <CustomField
            name="program"
            label={formatMessage('i18n_materialSize')}
            disabled={drawer.isEdit && !_.isEmpty(originalFormData.program)}
          />

          {/* 买法 */}
          <CustomField
            name="buyMethod"
            label={formatMessage('i18n_buyMethod')}
            accepter={SelectPicker}
            data={buyMethodDataIntl}
            disabled={drawer.isEdit && originalFormData.buyMethod !== defaultFormData.buyMethod}
          />

          {/* 频次控制 */}
          <CustomField
            name="frequencyCap"
            label={formatMessage('i18n_frequencyCap')}
            accepter={SelectPicker}
            data={frequencyData}
            groupBy="tag"
            disabled={
              drawer.isEdit && originalFormData.frequencyCap !== defaultFormData.frequencyCap
            }
            searchKeys={['name', 'tag']}
            searchable
          />

          {/* 定投地域 */}
          <CustomField
            name="targetGeo"
            label={formatMessage('i18n_targetedGeo')}
            accepter={RegionPicker}
            disabled={
              drawer.isEdit &&
              _.isNumber(originalFormData.targetGeo) &&
              originalFormData.targetGeo !== defaultFormData.targetGeo
            }
          />

          {/* 点击监测类型 */}
          <CustomField
            name="asyncTracker"
            label={formatMessage('i18n_clkMonitorType')}
            accepter={RadioGroup}
            data={asyncTrackerDataIntl}
            itemWidth="45%"
            className="clk-monitor-group"
          />

          {/* 网站监测打通 */}
          <CustomField
            name="webMonitorConnect"
            label={formatMessage('i18n_webMonitorConnect')}
            accepter={CheckboxGroup}
            data={webMonitorConnectDataIntl}
            itemWidth="45%"
            onChange={handleChangeWebMonitorConnect}
          />

          {/* 展示跟踪跳转URL */}
          <CustomField name="impUrl" label={formatMessage('i18n_impUrl')} />

          {/* 创意素材上传 */}
          <CustomField
            name="thumbnail"
            label={formatMessage('i18n_creativeFile')}
            accepter={ImgUploader}
            isEdit={drawer.isEdit}
            url={formData.thumbnailUrl}
            setUploadError={setUploadError}
            setUploading={setUploading}
          />

          {/* 落地页渠道 */}
          <CustomField name="landingChannel" label={formatMessage('i18n_landingChannel')} />

          {/* 自定义属性 */}
          {drawer.isEdit && (
            <CustomField
              name="attributeGroup"
              label={formatMessage('i18n_conversionPara')}
              componentClass="textarea"
              disabled
            />
          )}

          {/* 备注 */}
          <CustomField
            name="remark"
            label={formatMessage('i18n_remark')}
            disabled={drawer.isEdit && !_.isEmpty(originalFormData.remark)}
          />

          {/* 注释 */}
          {drawer.isEdit && (
            <CustomField
              name="note"
              label={formatMessage('i18n_annotation')}
              rows={2}
              componentClass="textarea"
            />
          )}
        </Form>
      </Drawer>
    );
  }
);
