import React, { memo, useCallback, useMemo, useState } from 'react';
import _ from 'lodash';
import { useHistory } from 'react-router';
import { Button } from 'rsuite';
import { API_REPORT } from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import {
  NumberCell,
  DateCell,
  FloatActionCell,
  TextCell,
  HeaderSummary
} from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps, secondaryData } from '@/constants/config';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';

import { cellData, attributeModelData } from './constants';
import { segData, FilterParam, filterParamKeys } from '@/views/common/conversionReport/constants';

const mockData: CustomTableDataProps<any> = {
  data: [
    {
      id: 1,
      secondary: '2020/03/05',
      segmentationId: 1,
      timeslot: '1:00',
      clicks: 2220,
      uclicks: '--',
      impressions: 3330,
      uimpressions: '--',
      'default#attribution': 220,
      'default#revenue': 330,
      segmentation: '移动',
      name: 'test',
      'last_interactivey#attribution': 0,
      'last_interactivey#revenue': 0
    }
  ],
  page: {
    current: 0,
    pagesize: 10,
    total: 6,
    size: 100
  },
  total: {
    impressions: 140,
    uimpressions: 70,
    clicks: 70,
    uclicks: '--'
  }
};

type TableProps = Partial<BasicReportProps<FilterParam>>;

export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    setTableParams,
    handleChangeCustomParam
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const history = useHistory();
    const tablePostParamsMemo = useMemo(
      () => ({
        ...postParams,
        var: 'json'
      }),
      [postParams]
    );
    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: API_REPORT,
      postParams: tablePostParamsMemo
    });
    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const [cell, setCell] = useState<Array<string>>(cellData.map(i => i.id));

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport(
          {
            var: 'xlsx',
            ...postParams
          },
          API_REPORT
        );
    }, [onExport, postParams]);

    // 指标切换
    const handleChangeCell = useCallback((val: Array<string>) => {
      setCell(val);
    }, []);

    const cellDataIntl = usePickerDataFromObj(cellData);
    const segDataIntl = usePickerDataFromObj(segData);
    const attributeModelDataIntl = usePickerDataFromObj(attributeModelData);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SelectPicker
          key="segmentation"
          prepend={formatMessage('i18n_segmentation')}
          data={segDataIntl}
          value={filterParams[filterParamKeys.SEG]}
          onChange={(value: string) => handleChangeCustomParam({ [filterParamKeys.SEG]: value })}
        />,
        <CheckPicker
          key="indicators"
          prepend={formatMessage('i18n_metrics')}
          placeholder={formatMessage('i18n_select', { name: '' })}
          data={cellDataIntl}
          value={cell}
          style={{ maxWidth: '224px' }}
          onChange={handleChangeCell}
        />,
        <CheckPicker
          key="attrModel"
          prepend={formatMessage('i18n_attribution')}
          placeholder={formatMessage('i18n_select', { name: '' })}
          data={attributeModelDataIntl}
          value={filterParams[filterParamKeys.ATTRMODEL]}
          style={{ maxWidth: '250px' }}
          onChange={(value: any) => handleChangeCustomParam({ [filterParamKeys.ATTRMODEL]: value })}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };
    // 渲染已选指标列
    const renderSelectedCol = (): React.ReactElement[] => {
      return cellDataIntl.map((i: any) => {
        if (_.includes(cell, i.id) && (i.id !== 'attribution' && i.id !== 'revenue')) {
          return (
            <Column width={200} align="right" resizable sortable key={i.id}>
              <HeaderCell>{i.name}</HeaderCell>
              <NumberCell dataKey={i.id} />
            </Column>
          );
        }
        return null;
      });
    };
    // 渲染模型
    const renderModelCol = (): React.ReactElement[] => {
      let attrName = _.get(_.filter(cellDataIntl, { id: 'attribution' })[0], 'name');
      let revName = _.get(_.filter(cellDataIntl, { id: 'revenue' })[0], 'name');
      let list: React.ReactElement[] = [];
      attributeModelDataIntl.forEach((i: any) => {
        if (_.includes(filterParams[filterParamKeys.ATTRMODEL], i.id)) {
          list.push(
            _.includes(cell, 'attribution') && (
              <Column width={150} resizable align="right" sortable key={i.id}>
                <HeaderCell>
                  <HeaderSummary title={i.name} summary={attrName} />
                </HeaderCell>
                <NumberCell dataKey={`${i.id}#attribution`} />
              </Column>
            ),
            _.includes(cell, 'revenue') && (
              <Column width={150} resizable align="right" sortable key={`${i.id}revenue`}>
                <HeaderCell>
                  <HeaderSummary title={i.name} summary={revName} />
                </HeaderCell>
                <NumberCell dataKey={`${i.id}#revenue`} />
              </Column>
            )
          );
        }
      });
      return list;
    };

    const renderActions = (rowData: Record<string, any>) => {
      return (
        <>
          <FloatActionCell.Actions.Report
            onClick={() => {
              history.push({
                pathname: `/campaigns/${rowData.id}/attribution`
              });
            }}
          />
        </>
      );
    };
    // 渲染表列
    const renderCol = (): React.ReactElement[] => {
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams.secondary)[0],
        'label'
      );
      const list = [
        <Column width={100} sortable fixed key="id">
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>,
        <Column width={200} sortable resizable fixed key="name">
          <HeaderCell>
            {filterParams[filterParamKeys.MDIMENSION] === 'site'
              ? formatMessage('i18n_site')
              : formatMessage('i18n_placement')}
          </HeaderCell>
          <TextCell dataKey="name" maxLine={1} />
        </Column>,
        filterParams[filterParamKeys.MDIMENSION] === 'placement' && (
          <Column width={200} sortable resizable fixed key="site">
            <HeaderCell>{formatMessage('i18n_site')}</HeaderCell>
            <TextCell dataKey="site" maxLine={1} />
          </Column>
        ),
        filterParams.secondary && (
          <Column
            width={filterParams.secondary === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams.secondary === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        filterParams[filterParamKeys.SEG] && (
          <Column width={150} sortable resizable key="segmentation">
            <HeaderCell>{formatMessage('i18n_platform')}</HeaderCell>
            <Cell dataKey="segmentation" />
          </Column>
        )
      ];
      return _.concat(list, renderSelectedCol(), renderModelCol());
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange} headerHeight={60}>
        {renderCol()}
      </Table>
    );
  }
);
