import React, { memo, useContext } from 'react';
import { Breadcrumb, Content } from 'rsuite';
import _ from 'lodash';
import { useHistory } from 'react-router';

import { API_REPORT_DATE } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import { useModuleDate, DateKeys } from '@/components/dateSource';
import { defaultBrandKey, defaultAdvertiserKey } from '@/views/common/report/campaign/constants';

import { CampaignCtx, DateCtx } from './context';
import { CampaignType } from './types.d';

/**
 * 放在这里用一个context管理 dateSession 和各自的逻辑
 * 这里可以封装date的get 和 set，同时封装一个请求函数和是否请求成功
 *  传递给子组件
 *
 * 1. 第一次加载从localStorage取保存的date数据，单独给个campaignDate区分，作为useState 的初始值
 * 2. context 传递 这个 [value, setValue]
 * 3. 如果是campaign这里可以封装一个函数，保留loading的状态，函数结束之后，max、start、end 和这个state 里面进行操作。
 */

export default memo(props => {
  const history = useHistory();
  const { campaignDetail: campaign, type } = useContext(CampaignCtx);
  const campaignId = useCampaignId();
  const advertiserName = _.get(campaign, 'advertiserName');
  const campaignName = _.get(campaign, 'name');
  const advertiserId = _.get(campaign, 'advertiserId');
  const brandName = _.get(campaign, 'brandName');
  const brandId = _.get(campaign, 'brandId');
  const userId = _.get(campaign, 'userId');
  const { formatMessage } = useFmtMsgFori18n();
  const dateSessionStorageKey =
    type === CampaignType.CAMPAIGN ? DateKeys.CAMPAIGN : DateKeys.CROSS_CAMPAIGN;
  const dateCtxVal = useModuleDate(dateSessionStorageKey, API_REPORT_DATE, { id: campaignId });

  return (
    <>
      <Breadcrumb className="campaign-navigator">
        <Breadcrumb.Item
          onClick={() => {
            history.push({
              pathname: '/campaign-overview',
              state: {
                [defaultAdvertiserKey]: advertiserId
              }
            });
          }}
        >
          {formatMessage('i18n_advertiser')}：{advertiserName}
        </Breadcrumb.Item>
        <Breadcrumb.Item
          onClick={() => {
            history.push({
              pathname: '/campaign-overview',
              state: {
                [defaultAdvertiserKey]: advertiserId,
                [defaultBrandKey]: brandId
              }
            });
          }}
        >
          {formatMessage('i18n_brand')}：{brandName}
        </Breadcrumb.Item>
        <Breadcrumb.Item
          onClick={() => {
            history.push(`/campaigns/${campaignId}`);
          }}
        >
          {formatMessage('i18n_campaign')}：{campaignName}
        </Breadcrumb.Item>
      </Breadcrumb>
      <Content>
        <DateCtx.Provider value={dateCtxVal}>{props.children}</DateCtx.Provider>
      </Content>
    </>
  );
});
