export const defaultAvailableTableParam = {
  beId: [] as Array<any>
};

export type AvailableTableParamType = typeof defaultAvailableTableParam;

export const availableTableParamFormatter = (f: AvailableTableParamType) => {
  return {
    ...f,
    beId: f.beId.toString()
  };
};

