export const defaultFormData = {
  name: '',
  desc: '',
  primaryId: 1,
  primaryRole: 1,
  targetPeople: genderData[0].id,
  targetStartAge: 2,
  targetEndAge: 0,
  measure: false,
  tencentTotalReport: tencentData[1].id,
  calendar: [] as any,
  enableFreq: false,
  algorithmAdjust: false,
  crossLevel: levelData[0].id,
  batchResult: [] as any
};

export const checkLevelIsCampaign = (crossLevelVal: number) => {
  return crossLevelVal === levelData[0].id;
};

export const mapFromResponse = (resp: any) => {
  const {
    targetDemographic,
    targetStartAge,
    targetEndAge,
    tencentTotalReport,
    crossLevel,
    calendar,
    crossLevelItems = [],
    enableFreq,
    algorithmAdjust,
    ...data
  } = resp;

  return {
    ..._.mapKeys(data, (val, key) => _.camelCase(key)),
    targetPeople: _.get(_.find(genderData, { key: targetDemographic }), 'id') || genderData[0].id,
    targetStartAge: Number(_.replace(targetStartAge, 'A', '')),
    targetEndAge: Number(_.replace(targetEndAge, 'A', '')),
    tencentTotalReport:
      ({
        null: 0,
        daily: 1,
        weekly: 2
      } as any)[_.lowerCase(tencentTotalReport)] || 0,
    calendar: calendar ? [1] : [],
    crossLevel: _.get(_.find(levelData, { key: crossLevel }), 'id') || levelData[0].id,
    enableFreq: Boolean(enableFreq),
    algorithmAdjust: Boolean(algorithmAdjust),
    batchResult: _.map(crossLevelItems, i => ({
      ...i,
      primary: {
        beName: _.get(i, 'primary.be', ''),
        role: _.get(i, 'primary.beRole', '')
      }
    }))
  };
};

export const mapToRequest = (param: any) => {
  let tencentTotalReport;
  let enableFreq = 0;
  let algorithmAdjust = 0;
  let calendar = 0;

  if (param.measure) {
    tencentTotalReport = Number(param.tencentTotalReport);
    enableFreq = Number(param.enableFreq);
    algorithmAdjust = Number(param.algorithmAdjust);
    calendar = _.get(param, 'calendar.0') === 1 ? 1 : 0;
  }

  return {
    ...param,
    primaryRole: undefined,
    primaryId: undefined,
    crossLevelIds: _.map(_.get(param, 'batchResult', []), i => i.id).join(','),
    batchResult: undefined,

    tencentTotalReport,
    enableFreq,
    algorithmAdjust,
    calendar
  };
};

export const getAccBatchResult = (batchResult: Array<any>) =>
  _.filter(batchResult, b => _.isEmpty(b.errorInfo)) || [];
