import React, { memo, useMemo, useContext, useState, useCallback, useRef } from 'react';
import _ from 'lodash';
import { Button, Alert } from 'rsuite';

import * as APIs from '@/constants/APIs';
import Table, { Column, HeaderCell, tableUtils } from '@/components/Table';
import { FloatActionCell, DateCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import fetch, { FETCH_METHOD } from '@/utils/request/fetch';
import { DEFAULT_PAGE_SIZE, BasicReportProps } from '@/constants/config';
import getRandomNum from '@/utils/random';
import useInterval from '@/components/hooks/useInterval';

import { UploadFilterParam, UploadReducerType, adTypeData } from '../constants';
import StatusCell from './StatusCell';
import { UploadCtx } from './context';

const isErrorStatus = (status: string) => status === 'ERROR' || status === 'No UPDATE';
const CHECKBOX_KEY = 'checkKey'; // 使用campaign_id和placement_name拼接，提交时需略去

export default memo(({ filterParams }: BasicReportProps<{}, UploadFilterParam>) => {
  const { formatMessage } = useFmtMsgFori18n();
  const [randomMutex] = useState(getRandomNum());
  const { state, dispatch } = useContext(UploadCtx);
  const { isUploading, data, loading, reqDelay } = state;

  const [isSubmitting, setIsSubmitting] = useState(false); // 正在提交数据
  const [submittedDataKey, setSubmittedDataKey] = useState<Array<any>>([]); // 已经提交过的数据，在前端把他们隐藏了
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(DEFAULT_PAGE_SIZE);
  const tableRef = useRef(null);

  const fetchTableData = useCallback(() => {
    const { taskId } = filterParams;
    if (taskId) {
      dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_START });
      fetch(APIs.API_ADUNIT_BATCHES, {
        params: {
          taskId: `${taskId}`
        },
        mutex: randomMutex
      })
        .then((res: any) => {
          const dataResp = _.get(res, 'data.records', []).map((i: Record<string, any>) => ({
            ...i,
            [CHECKBOX_KEY]: `${i.campaign_id}*${i.placement_name}`
          }));
          // 只要有taskId就一定会有数据，只要返回不是空，就继续轮循
          const hasData = _.get(dataResp, 'length', 0);
          if (hasData) {
            dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_ACC, data: dataResp });
            dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_END });
            setSubmittedDataKey([]);
          }
        })
        .catch(() => {
          dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_EXP });
        });
    }
  }, [filterParams, randomMutex, dispatch]);

  useInterval(
    () => {
      fetchTableData();
    },
    reqDelay,
    true
  );

  const tableDataMemo = useMemo(() => {
    const disableCheckedValues = [] as any; // 保存禁用的id
    // 去除已经提交过的，过滤的数据
    const originData = data
      .filter(s => submittedDataKey.indexOf(s[CHECKBOX_KEY]) === -1)
      .map(item => {
        if (isErrorStatus(item.status)) {
          disableCheckedValues.push(item[CHECKBOX_KEY]);
        }

        if (!_.isString(item.creative_type)) {
          const type = _.find(adTypeData, ({ id }) => id === Number(item.creative_type));
          return {
            ...item,
            // eslint-disable-next-line @typescript-eslint/camelcase
            creative_type: type ? type.name : ''
          };
        }

        return item;
      });

    return {
      data: originData.slice((page - 1) * pageSize, pageSize * page),
      loading,
      ...(isUploading ? { loading: true } : null),
      disableCheckedValues,
      page: {
        current: (page - 1) * pageSize,
        size: pageSize,
        total: originData.length
      }
    };
  }, [data, loading, isUploading, submittedDataKey, page, pageSize]);

  const handleSubmit = useCallback(() => {
    const { data: dataResp } = tableDataMemo;
    const checkedValues = tableUtils(tableRef).getCheckValues();
    const submitDataList = dataResp.filter(d => checkedValues.indexOf(d[CHECKBOX_KEY]) !== -1);
    if (!submitDataList.length) {
      Alert.warning(formatMessage('i18n_select', { name: '' }));
      return;
    }

    setIsSubmitting(true);
    fetch(APIs.API_ADUNIT_BATCHES, {
      method: FETCH_METHOD.POST,
      data: {
        dataList: submitDataList.map((i: Record<string, any>) => _.omit(i, CHECKBOX_KEY))
      }
    })
      .then(() => {
        Alert.success(formatMessage('i18n_operationSucceeds'));
        setSubmittedDataKey(prevKeys => [...prevKeys, ..._.map(submitDataList, CHECKBOX_KEY)]);
        setIsSubmitting(false);
        setPage(1);
      })
      .catch(() => {
        setIsSubmitting(false);
      });
  }, [tableDataMemo, formatMessage]);

  const handleChangeTablePage = useCallback((p: number) => {
    setPage(p);
  }, []);

  const handleChangeTableLength = useCallback((pageSizeObj: any) => {
    const { pageSize: pSize } = pageSizeObj;
    setPageSize(pSize);
    setPage(1);
  }, []);

  // 定义table toolbar
  const renderToolbar = useCallback((): React.ReactElement[] => {
    return [
      <Button
        key="create"
        appearance="primary"
        disabled={!data.length}
        onClick={handleSubmit}
        loading={isSubmitting}
      >
        {formatMessage('i18n_submit')}
      </Button>,
      <div className="upload-num" key="total">
        {formatMessage('i18n_total')}：{_.get(data, 'length') || 0}
      </div>,
      <div className="upload-num" key="error">
        {formatMessage('i18n_errorTips')}：
        {_.get(_.filter(data, i => i.status === 'ERROR'), 'length') || 0}
      </div>
    ];
  }, [data, isSubmitting, handleSubmit, formatMessage]);

  return (
    <>
      <Table
        {...tableDataMemo}
        // 名字也是必须要唯一的，不然后端会报错，所以应该也可以用这个去作为key，理想的情况后端返回一个randomId，不然的化
        checkDataKey={CHECKBOX_KEY}
        renderToolbar={renderToolbar}
        innerRef={tableRef}
        onChangeLength={handleChangeTableLength}
        onChangePage={handleChangeTablePage}
      >
        <Column flexGrow={1} minWidth={200} fixed>
          <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
          <StatusCell dataKey="status" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_placementId')}</HeaderCell>
          <FloatActionCell dataKey="placement_id" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_adserverId')}</HeaderCell>
          <FloatActionCell dataKey="adserver_placement_id" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_placementName')}</HeaderCell>
          <FloatActionCell dataKey="placement_name" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
          <FloatActionCell dataKey="campaign_name" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_menuAdType')}</HeaderCell>
          <FloatActionCell dataKey="creative_type" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_adserverName')}</HeaderCell>
          <FloatActionCell dataKey="adserver_name" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_siteName')}</HeaderCell>
          <FloatActionCell dataKey="site_name" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_site') + formatMessage('i18n_domain')}</HeaderCell>
          <FloatActionCell dataKey="site_url" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_material')}</HeaderCell>
          <FloatActionCell dataKey="creative" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_device')}</HeaderCell>
          <FloatActionCell dataKey="target_device" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_adPosition')}</HeaderCell>
          <FloatActionCell dataKey="position" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_materialSize')}</HeaderCell>
          <FloatActionCell dataKey="program" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_buyMethod')}</HeaderCell>
          <FloatActionCell dataKey="buy_method" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_frequencyCap')}</HeaderCell>
          <FloatActionCell dataKey="frequency_cap" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_targetedGeo')}</HeaderCell>
          <FloatActionCell dataKey="target_geo" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_remark')}</HeaderCell>
          <FloatActionCell dataKey="remark" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
          <DateCell dataKey="placement_start_date" />
        </Column>

        <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
          <DateCell dataKey="placement_end_date" />
        </Column>
      </Table>
    </>
  );
});
