import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';
import { RouteComponentProps } from 'react-router-dom';

import * as APIs from '@/constants/APIs';
import SearchInput from '@/components/Table/Tools/SearchInput';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { TextCell, FloatActionCell, MultiLineCell, NameCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { getShortNameByName, BasicReportProps, __HAA__ } from '@/constants/config';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { useModal } from '@/components/hooks/useModal';

import Modal from './Modal';

const maxLine = 4;
const mapStatusFn = (statusFromBackend: number) => {
  switch (statusFromBackend) {
    case 1:
      return 'active';
    case 2:
    default:
      return 'paused';
  }
};

type TableProps = Partial<BasicReportProps & RouteComponentProps<{}>> & {
  handleFormCreate: Function;
  handleFormEdit: Function;
};

export default memo(
  ({
    tableParams,
    setTableParams,
    refetchTableData,
    handleSearchWord,
    handleFormCreate,
    handleFormEdit
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onExport, onChange } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_BE_MANAGEMENT,
      postParams: {
        var: ''
      }
    });

    const modal = useModal(false, {}, APIs.API_BE_PUT, refetchTableData);

    const tableDataMemo = useMemo(
      () =>
        _.mapValues(extractTableData(tableData), (val, key) =>
          key === 'data'
            ? _.map(
              val as Record<string, any>,
              ({ Tel, address, postalCode, website, ...rowData }) => ({
                ...rowData,
                organization: {
                  address,
                  postalCode,
                  Tel,
                  website
                }
              })
            )
            : val
        ),
      [tableData]
    );

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport({
          var: '.xlsx'
        });
    }, [onExport]);

    const renderToolbar = () => {
      return (
        <>
          <Button appearance="primary" onClick={handleFormCreate as () => void}>
            {formatMessage('i18n_create')}
          </Button>
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
          <Button key="exportTable" className="pull-right" onClick={handleExportClicked}>
            {formatMessage('i18n_exportTable')}
          </Button>
          ,
        </>
      );
    };

    const renderActions = useCallback(
      rowData => {
        const { status } = rowData;

        const disableClickHandler = () => {
          modal.showModal({
            ...rowData,
            status: 2
          });
        };

        const enableClickHandler = () => {
          modal.showModal({
            ...rowData,
            status: 1
          });
        };

        return [
          <FloatActionCell.Actions.Edit
            key="edit"
            onClick={() => {
              handleFormEdit({
                id: rowData.id
              });
            }}
          />,
          status === 1 ? (
            <FloatActionCell.Actions.Disable key="disable" onClick={disableClickHandler} />
          ) : (
              <FloatActionCell.Actions.Enable key="enable" onClick={enableClickHandler} />
            )
        ];
      },
      [modal, handleFormEdit]
    );

    return (
      <>
        <Modal {...modal} />
        <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange} wordWrap>
          {/* ID */}
          <Column width={100} fixed sortable>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>

          {/* 名称 */}
          <Column fixed width={200} sortable resizable>
            <HeaderCell>{formatMessage('i18n_name')}</HeaderCell>
            <NameCell
              dataKey="name"
              showStatus={true}
              statusMapperFn={mapStatusFn}
              renderActions={renderActions}
              maxLine={maxLine}
            />
          </Column>

          {/* 联系人姓名 */}
          <Column width={150} sortable resizable>
            <HeaderCell>{formatMessage('i18n_contactName')}</HeaderCell>
            <FloatActionCell dataKey="contact" />
          </Column>

          {/* 联系人邮件 */}
          <Column width={200} sortable resizable>
            <HeaderCell>{formatMessage('i18n_contactEmail')}</HeaderCell>
            <TextCell dataKey="email" maxLine={maxLine} />
          </Column>

          {/* 是否内部 */}
          <Column width={120}>
            <HeaderCell>{formatMessage('i18n_isInternal')}</HeaderCell>
            <FloatActionCell dataKey="internal" />
          </Column>

          {/* BE角色 */}
          <Column width={180}>
            <HeaderCell>{formatMessage('i18n_beRole')}</HeaderCell>
            <MultiLineCell
              dataKey="role"
              maxLine={maxLine}
              formatter={data =>
                _.isString(data) && data.split(',').map(i => getShortNameByName(i) || undefined)
              }
            />
          </Column>

          {/* Advertiser角色 */}
          <Column width={180} resizable>
            <HeaderCell>{`Advertiser ${formatMessage('i18n_role')}`}</HeaderCell>
            <MultiLineCell
              dataKey="advertiserRole"
              maxLine={maxLine}
              formatter={(data, rowData) => {
                if (_.get(rowData, 'internal') === 'Y') {
                  return [formatMessage('i18n_all')];
                }
                const list = _.get(data, 'brand', []);
                return _.isEmpty(list) ? ['--'] : list;
              }}
            />
          </Column>

          {/* Agency角色 */}
          <Column width={180} resizable>
            <HeaderCell>{`Agency ${formatMessage('i18n_role')}`}</HeaderCell>
            <MultiLineCell
              dataKey="agencyRole"
              maxLine={maxLine}
              formatter={(data, rowData) => {
                if (_.get(rowData, 'internal') === 'Y') {
                  return [formatMessage('i18n_all')];
                }
                const list = _.get(data, 'brand', []);
                return _.isEmpty(list) ? ['--'] : list;
              }}
            />
          </Column>

          {/* Publisher角色 */}
          <Column width={180} resizable>
            <HeaderCell>{`Publisher ${formatMessage('i18n_role')}`}</HeaderCell>
            <MultiLineCell
              dataKey="publisherRole"
              maxLine={maxLine}
              formatter={(data, rowData) => {
                if (_.get(rowData, 'internal') === 'Y') {
                  return [formatMessage('i18n_all')];
                }
                const list = _.get(data, 'media', []);
                return _.isEmpty(list) ? ['--'] : list;
              }}
            />
          </Column>

          {/* 锁定缺省BE */}
          <Column width={200} resizable>
            <HeaderCell>{formatMessage('i18n_lockDefaultBERole')}</HeaderCell>
            <FloatActionCell dataKey="isLockBeRole" />
          </Column>

          {/* 锁定缺省广告主 */}
          <Column width={200} resizable>
            <HeaderCell>{formatMessage('i18n_lockDefaultAdServer')}</HeaderCell>
            <FloatActionCell dataKey="isLockAdS" />
          </Column>

          {/* 产品列表 */}
          {!__HAA__ && (
            <Column width={200} resizable>
              <HeaderCell>{formatMessage('i18n_productList')}</HeaderCell>
              <MultiLineCell
                dataKey="products"
                maxLine={maxLine}
                formatter={data => _.isString(data) && data.split(',').map(i => i || undefined)}
              />
            </Column>
          )}

          {/* 合作方 */}
          <Column width={200} resizable>
            <HeaderCell>{formatMessage('i18n_collaborator')}</HeaderCell>
            <MultiLineCell
              dataKey="collaborators"
              formatter={data =>
                _.isArray(data) && data.map(i => `${getShortNameByName(i.beRole)}: ${i.beName}`)
              }
              maxLine={maxLine}
            />
          </Column>

          {/* 组织信息 */}
          <Column width={200} resizable>
            <HeaderCell>{formatMessage('i18n_organizationInfo')}</HeaderCell>
            <MultiLineCell
              dataKey="organization"
              formatter={data =>
                _.isObject(data) &&
                _.toPairs(data).map(i => `${formatMessage(`i18n_${i[0]}`)}: ${i[1] || '--'}`)
              }
              maxLine={maxLine}
            />
          </Column>

          {/* 备注 */}
          <Column width={200} sortable resizable>
            <HeaderCell>{formatMessage('i18n_remark')}</HeaderCell>
            <TextCell dataKey="note" maxLine={maxLine} />
          </Column>
        </Table>
      </>
    );
  }
);
