import React from 'react';
import _ from 'lodash';
import { InputGroup } from 'rsuite';
import DatePicker from '@/components/Picker/DatePicker';
import { ErrorMessageWithAbsContainer } from '@/components/Form/ErrorMessage';

// ToDo: 校验错误，即使正确也有错误显示，model无法读取endDate内容，可能没有清除错误信息的缘故

const startName = 'startDate';
const endName = 'endDate';

export default React.memo(
  ({ values, errors, disabled, startOptions, endOptions, onChange, ...props }: any) => {
    return (
      <InputGroup className="cus-date-picker-group">
        <DatePicker
          value={values[startName]}
          onChange={onChange(startName)}
          {...props}
          {...startOptions}
        />
        <ErrorMessageWithAbsContainer error={errors[startName]} />
        <InputGroup.Addon>~</InputGroup.Addon>
        <DatePicker
          value={values[endName]}
          onChange={onChange(endName)}
          {...props}
          {...endOptions}
        />
        <ErrorMessageWithAbsContainer error={errors[endName]} />
      </InputGroup>
    );
  }
);
