import React, { memo, useState, useEffect, useCallback } from 'react';
import _ from 'lodash';
import { Popover, Whisper } from 'rsuite';
import { FILTER } from '@/constants/config';
import { onFetchRealTime } from '../actions';
import useInterval from '@/components/hooks/useInterval';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { prefixClassName } from '../constants';
import { numberFormat } from '@/utils/format';
import { EmptyBlock, LoadingBlock } from '@/components/EmptyOrLoading';

interface Props {
  id: number | string;
  tag?: string;
  filterParams?: FILTER;
  mDimension?: string;
  labelKey?: string;
  valueKey?: string;
}

const mockData: Array<any> = [
  {
    clicks: 20,
    id: 40,
    impressions: 20,
    name: 'HHBTEST',
    sum: 40
  }
];

interface CampaignSummary {
  id: number;
  clicks?: number | string;
  uclicks?: number | string;
  impressions?: number | string;
  uimpressions?: number | string;
  campaignId?: number;
  secondary?: string;
  clickRate?: number | string;
  value: number | string;
  name: string;
  [key: string]: any;
}

export default memo<Props>(function ({
  id,
  tag,
  filterParams,
  labelKey = 'name',
  valueKey = 'value'
}) {
  const [data, setData] = useState<Array<CampaignSummary>>();
  const [loading, setLoading] = useState(true);
  const [delay, setDelay] = useState(500);
  useInterval(() => {
    setDelay(1000 * 60);
    setLoading(true);
    onFetchRealTime(
      {
        id,
        tag,
        [filterParams.selectObj.key]: filterParams.selectObj.value
      },
      res => {
        setLoading(false);
        setData(_.get(res, 'data.records', mockData));
      }
    );
  }, delay);

  // 触发请求
  useEffect(() => {
    setDelay(500);
  }, [id, filterParams]);

  const { formatMessage } = useFmtMsgFori18n();

  // 根据valuekey 排序
  const showData = _.orderBy(data, valueKey, 'desc');
  const max = _.get(showData, [0, valueKey]);

  const renderHtml = useCallback(() => {
    if (loading) {
      return <LoadingBlock />;
    }
    if (_.isEmpty(data) || _.isNil(data)) {
      return <EmptyBlock />;
    }
    return showData.map(i => {
      const name = i[labelKey];
      const value = i[valueKey];
      const percent = _.isString(value) && value.indexOf('%') > 0 ? value : value / max;
      const speaker = (
        <Popover>
          <p>{name} </p>
          <p>
            {formatMessage('i18n_total')}:{numberFormat(value)}
          </p>
          <p>
            {formatMessage('i18n_impressions')}:{numberFormat(i.impressions)}
          </p>
          <p>
            {formatMessage('i18n_clicks')}:{numberFormat(i.clicks)}
          </p>
        </Popover>
      );
      return (
        <Whisper key={name + value} placement="top" trigger="hover" speaker={speaker}>
          <div key={name + value}>
            <div className="barchart-label">
              <span className="barchart-name">{name}</span>
              <span className="barchart-num">{numberFormat(value)}</span>
            </div>
            <div
              className="barchart-bar"
              style={{
                width: numberFormat(percent, {
                  style: 'percent'
                })
              }}
            />
          </div>
        </Whisper>
      );
    });
  }, [loading, data, showData, labelKey, valueKey, max]);

  return (
    <>
      <div className={prefixClassName('barchart')} style={{ height: 300 }}>
        {renderHtml()}
      </div>
    </>
  );
});
