import _ from 'lodash';

export const mapFromResponse = (rspData: any) => {
  return {
    ..._.pick(rspData, [
      'id',
      'name',
      'isOffline',
      'conversionGroupId',
      'conversionGroup',
      'lookback',
      'beId'
    ]),
    isOffline: rspData.isOffline ? 1 : 0, // 类型 线上线下
    advertisers: rspData.advertisers || [],
    campaigns: rspData.campaigns
      ? rspData.campaigns.split(',').map((i: string) => _.toNumber(i))
      : null,
    status: rspData.status === 1
  };
};

export const mapToRequest = (formData: any) => {
  return {
    ..._.pick(formData, ['id', 'conversionGroupId', 'name', 'lookback', 'isOffline']),
    status: formData.status ? 1 : 0, // 状态,
    advertisers: formData.advertisers, // 广告主|品牌 eg [{id:1,children:[1,3]}]
    campaigns: _.isEmpty(formData.campaigns) ? '' : formData.campaigns.join(',') // 推广计划 eg [1,3]
  };
};
