import * as React from 'react';
import _ from 'lodash';
import { Container, Header, Content } from 'rsuite';
// import ProductLogo from '@/components/Layouts/ProductLogo';
import UserProfile from '@/components/Layouts/UserProfile';
import { getEnvFromSession } from '@/utils/env';
import { getUserProfile } from '@/utils/location';
import Footer from '@/components/Layouts/Footer';
import PageApp from 'rsuite-grid-sidebar';
/**
 * 没有侧边栏的布局
 */
interface NoSidebarLayoutProps {
  children: React.ReactNode;
}

const NoSidebarLayout = React.memo(function ({ children }: NoSidebarLayoutProps) {
  const { logo: logoUrl } = getEnvFromSession();
  const userProfileResp = getUserProfile();
  const apps = _.get(userProfileResp, 'hacProfile.apps');
  const code = _.get(userProfileResp, 'isHaa') ? 'haa' : 'hfa';
  const logo = logoUrl ? <img src={logoUrl} /> : null;
  return (
    <>
      <Container className="main-container">
        <PageApp logo={logo} apps={apps} activeCode={code} dynamicIcon />
        <Header>
          {/* <ProductLogo /> */}
          <UserProfile />
        </Header>
        <Content className="main-content">
          {children}
          <Footer />
        </Content>
      </Container>
    </>
  );
});

NoSidebarLayout.displayName = 'NoSidebarLayout';

export default NoSidebarLayout;
