import React, { useCallback } from 'react';
import { Panel, Tag, Divider } from 'rsuite';
import _ from 'lodash';

import { useFmtMsgFori18n, FormatMessageFnProps } from '@/utils/formatMessage';

import { FilterInnerProps, FilterNodeConfig, defaultChildrenKey } from '../constants';
import { PickerAsyncDataCtxProps } from '../context';
import { removeEmptyVal } from '../utils';

/**
 * conver to
 * [
 *    {
 *      key: 'site',
 *      children: [
 *        {
 *          label: 'xxx',
 *          removeKey: '0' | '0*1'
 *        }
 *      ]
 *    }
 * ]
 */
const formatFilterVal = (
  filterVal: FilterInnerProps['filterVal'],
  filterData: Array<FilterNodeConfig>,
  asyncData: PickerAsyncDataCtxProps,
  fmtFn: FormatMessageFnProps
) => {
  return filterVal
    .filter(v => v.key)
    .map(p => {
      const currFilterItem = (_.find(filterData, { key: p.key }) || {}) as FilterNodeConfig;

      const innerValue = _.isFunction(currFilterItem.valueOuterToInner)
        ? currFilterItem.valueOuterToInner(p.value as any[])
        : p.value;

      const children = _.isFunction(currFilterItem && currFilterItem.tagFormatter)
        ? currFilterItem.tagFormatter(currFilterItem, innerValue, asyncData, fmtFn)
        : [{ label: `${currFilterItem.label}：${p.value}` }];
      return {
        key: p.key,
        children
      };
    });
};

export default ({ filterVal, filterData, asyncData, onSubmit }: FilterInnerProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const handleClear = useCallback(() => {
    onSubmit && onSubmit([]);
  }, [onSubmit]);

  const handleClose = useCallback(
    (filterKey: string, valueOriginNum: number, removeKey: string) => {
      const currFilterData = _.find(filterData, { key: filterKey });
      const currentFilterValIdx = _.findIndex(filterVal, { key: filterKey });
      const { defaultValue, childrenKey = defaultChildrenKey } = currFilterData;
      let filterValCopy = _.cloneDeep(filterVal); // 先拷贝一份

      if (_.isArray(defaultValue) && valueOriginNum >= 2) {
        // 如果value是数组并且不止一个item，那么删除对应子数组的value
        let targetArray = filterValCopy[currentFilterValIdx].value as Array<any>; // 定位到最外层的value
        const splitedRemoveKey = removeKey.split('*');
        for (let i = 0; i < splitedRemoveKey.length - 1; i++) {
          // 将targetArray指向最后第二层的value
          targetArray = targetArray[splitedRemoveKey[i] as any][childrenKey];
        }

        if (_.get(targetArray, 'length', 0) >= 2) {
          // 最后一层子项有大于2的数量，删其中的一个idx
          targetArray.splice(Number(_.last(splitedRemoveKey)), 1);
        } else {
          // 最后一层只有一个，删整个项
          let originArray = filterValCopy[currentFilterValIdx].value as Array<any>;
          for (let i = 0; i < splitedRemoveKey.length - 2; i++) {
            // 追溯到上一级
            originArray = originArray[splitedRemoveKey[i] as any][childrenKey];
          }

          originArray.splice(Number(_.takeRight(splitedRemoveKey, 2)[0]), 1); // 删倒数第二个idx
        }
      } else {
        // 其他的情况删除整个项
        filterValCopy = filterVal.filter(fff => fff.key !== filterKey);
      }

      onSubmit(removeEmptyVal(filterValCopy)); // 只会在drawer 开关的时候同步，这里手动同步下
    },
    [filterData, filterVal, onSubmit]
  );

  if (!filterVal.filter(v => v.key).length) {
    return <></>;
  }

  return (
    <Panel
      className="filter-tag"
      header={
        <div className="filter-header">
          {formatMessage('i18n_filters')}
          {/*eslint-disable */}
          <a className="filter-header-clear" onClick={handleClear}>
            {formatMessage('i18n_clearAll')}
          </a>
          {/* eslint-enable */}
        </div>
      }
      style={{ margin: '18px auto' }}
    >
      {formatFilterVal(filterVal, filterData, asyncData, formatMessage).map((f, idx) => (
        <React.Fragment key={f.key}>
          {idx > 0 && <Divider key={`divider-${f.key}`} />}
          {_.map(f.children, fc => (
            <React.Fragment key={`${fc.label}-${f.key}`}>
              {fc.label && (
                <Tag closable onClose={() => handleClose(f.key, f.children.length, fc.removeKey)}>
                  {/*eslint-disable */}
                  <a title={fc.label}>{fc.label}</a>
                  {/* eslint-enable */}
                </Tag>
              )}
            </React.Fragment>
          ))}
        </React.Fragment>
      ))}
    </Panel>
  );
};
