const SvgIcon = ({
  icon,
  title,
  onClick,
  disabled,
  style = {},
  width,
  height,
  color = false,
  ...props
}: any) => {
  return (
    <i
      className="svg-icon"
      style={{
        width,
        height,
        ...style
      }}
      onClick={onClick}
    >
      <svg
        title={title}
        className={disabled ? 'disabled' : undefined}
        width={width}
        height={height}
        {...props}
      >
        <use xlinkHref={`#${color ? 'iconColor' : 'icon'}-${icon}`} />
      </svg>
      <span />
    </i>
  );
};

export default SvgIcon;
