import React, { useState, useCallback } from 'react';

import PresetContainer, { PresetContainerProps } from './Controlled';
import { removeIdForObj } from '../../utils';

/**
 * 这个组件
 *  1. 封装保存了高级过滤组件内部的value作为内部的state
 *  2. 并且把传过来的onSubmit封装了下，每一次都传出组件的内部的value
 * 内部的value的结构见 mock/index
 * */
export default (props: Omit<PresetContainerProps, 'value'>) => {
  const { onSubmit, submitLoading, ...rest } = props;
  const [filterVal, setFilter] = useState([]);
  const handleSubmit = useCallback(
    val => {
      setFilter(val);

      onSubmit && onSubmit(removeIdForObj(val));
    },
    [onSubmit]
  );

  return (
    <PresetContainer
      value={filterVal}
      submitLoading={submitLoading}
      onSubmit={handleSubmit}
      {...rest}
    />
  );
};
