import _ from 'lodash';

export const mapFromResponse = (rspData: any) => {
  return {
    ..._.pick(rspData, ['id', 'name', 'status', 'beId', 'be'])
  };
};

export const mapToRequest = (formData: any) => {
  return {
    ...formData,
    status: formData.status ? 1 : 0 // 状态
  };
};

export const mapErrors = errors => {
  const roleErrors: string[] = [];
  const items = ['BERole', 'defaultRole', 'ageBrandList', 'advBrandList', 'siteList'];
  const newErrors = { ..._.omit(errors, items) };

  items.forEach(i => errors[i] && roleErrors.push(errors[i]));
  roleErrors.length > 0 && (newErrors.role = roleErrors.join(', '));

  return newErrors;
};
