import React, { useContext } from 'react';
import _ from 'lodash';

import SelectPicker from '@/components/Picker/SelectPicker';
import { usePickerDataFromObj } from '@/utils/formatMessage';

import { FILTER_OPERATION, operatorData } from '../../../constants';
import { FilterExtCtx } from '../../../context';

export const getDefaultOpValue = (operatorKey: FILTER_OPERATION) => {
  return _.get(operatorData, `${operatorKey}.0.id`, '');
};

export const getOpValue = (operatorKey: FILTER_OPERATION) => {
  return operatorData[operatorKey] || [];
};

interface OperatorProps {
  operatorKey: FILTER_OPERATION;
  operatorValue: string;
  onChange: (operatorValue: string) => void;
}

export default ({ operatorKey, operatorValue, onChange }: OperatorProps) => {
  const { disabled } = useContext(FilterExtCtx);
  const pickerData = getOpValue(operatorKey);
  const pickerDataIntl = usePickerDataFromObj(pickerData);
  return (
    <SelectPicker
      data={pickerDataIntl}
      value={operatorValue}
      onSelect={onChange}
      appearance="subtle"
      disabled={disabled}
    />
  );
};
