import React, { memo, useState, useEffect } from 'react';
import { Loader } from 'rsuite';
import _ from 'lodash';

import { TIME } from '@/constants/config';
import { toThousands } from '@/utils/format';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';

import { onFetchSummary } from './actions';
import { summaryData } from './constants';

interface Props {
  dateParams?: TIME;
  id: number | string;
}

export default memo<Props>(function({ dateParams, id }) {
  const [data, setData] = useState([]);
  const summaryDataIntl = usePickerDataFromObj(summaryData, { labelKey: 'label' });
  useEffect(() => {
    onFetchSummary(
      {
        ..._.pick(dateParams, ['start', 'end']),
        id
      },
      res => {
        setData(_.get(res, 'data.data', {}));
      }
    );
  }, [dateParams.start, dateParams.end, dateParams, id]);

  const { formatMessage } = useFmtMsgFori18n();

  return (
    <div className="summary-wrap" style={{ height: 90 }}>
      <div className="summary-div-ul">
        {_.isNil(data) ? (
          <Loader center content={formatMessage('i18n_loading')} />
        ) : (
          _.toPairs(data).map(([key, value]) => {
            const { label } = summaryDataIntl.find(i => i.key === key) || {};
            return (
              <div className="summary-div-li" key={key}>
                <ul>
                  <li className="summary-span-title">{label}</li>
                  <li className="summary-span-value">{toThousands(value)}</li>
                </ul>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
});
