import React, { memo, useState, useCallback, useEffect, useMemo } from 'react';
import _ from 'lodash';

import { BasicReportProps } from '@/constants/config';
import * as APIs from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useChart from '@/components/hooks/useChart';
import Chart from '@/components/Chart';
import { useStore } from '@/utils/redux';
import { CHART_KEY } from '@/components/Chart/config';
import { numberFormat } from '@/utils/format';
import CustomRadioGroup from '@/components/Form/RadioGroup';
import { prefixClassName } from './constants';
import { getCurrentLocale } from '@/utils/locale';
import zhCountry from '@/components/Chart/locales/zh_CN/country';
import enCountry from '@/components/Chart/locales/en_US/country';
import zhProvince from '@/components/Chart/locales/zh_CN/china-province';
import enProvince from '@/components/Chart/locales/en_US/china-province';

interface ChartDataDetail {
  max: number | string;
  name: string;
  data: {
    name?: string;
    value?: number;
  }[];
}
const mockData: ChartDataDetail[] = [
  {
    max: 232,
    name: 'impressions',
    data: [
      { name: 'geo.china.unknown', value: 20 },
      { name: 'geo.china.hubei', value: 232 },
      { name: 'geo.china.guangdong', value: 8 }
    ]
  },
  {
    max: 232,
    name: 'uimpressions',
    data: [
      { name: 'geo.china.unknown', value: 20 },
      { name: 'geo.china.hubei', value: 232 },
      { name: 'geo.china.guangdong', value: 8 }
    ]
  },
  {
    max: 232,
    name: 'clicks',
    data: [
      { name: 'geo.china.unknown', value: 20 },
      { name: 'geo.china.hubei', value: 131 },
      { name: 'geo.china.guangdong', value: 28 }
    ]
  },
  {
    max: 232,
    name: 'uclicks',
    data: [
      { name: 'geo.china.unknown', value: 20 },
      { name: 'geo.china.hubei', value: 232 },
      { name: 'geo.china.guangdong', value: 8 }
    ]
  }
];

export default memo<BasicReportProps>(function ({ dateParams, filterParams, postParams }) {
  const [chartData, setChartData] = useState(null);
  const { data, loading } = useChart({
    dateParams,
    filterParams,
    api: APIs.API_CHART,
    postParams
  });
  const [active, setActive] = useState('impressions');

  const handleActiveChange = useCallback((value: string) => {
    setActive(value);
  }, []);

  const { formatMessage } = useFmtMsgFori18n();
  const locale = getCurrentLocale() || 'zh-CN';

  //传参改变清空chartData
  useEffect(() => {
    setChartData(null);
  }, [dateParams, filterParams, postParams]);

  let geoData: any;
  if (filterParams.mDimension === 'country') {
    geoData = locale === 'zh-CN' ? zhCountry : enCountry;
  } else {
    geoData = locale === 'zh-CN' ? zhProvince : enProvince;
  }
  useEffect(() => {
    if (!data) {
      return;
    }
    const newData = _.get(data, 'data').filter((i: ChartDataDetail) => i.name === active)[0];
    // newData.data &&
    //   newData.data.map((item: any) => {
    //     if (_.startsWith(item.name, 'geo')) {
    //       item.name = geoData[item.name];
    //     }
    //     return item;
    //   });
    setChartData(newData);
  }, [data, active]);

  const env = useStore(['store', 'root', 'environmentInfo']);
  const { areaVersion } = _.get(env, 'data');
  let text =
    filterParams.secondary === 'total'
      ? `${areaVersion}\n\n *${formatMessage('i18n_chartRemark')}`
      : areaVersion;
  let title = {
    text,
    show: true,
    top: 'auto',
    left: 'auto',
    right: 18,
    bottom: 0,
    textStyle: {
      color: '#B5B5B5',
      fontSize: 12,
      align: 'right'
    }
  };

  const config = useMemo(() => {
    return {
      type: CHART_KEY.MAP,
      data: chartData,
      map: filterParams.mDimension === 'country' ? 'world' : 'china'
    };
  }, [chartData]);
  const option = {
    title,
    tooltip: {
      trigger: 'item',
      formatter: (params: any) => {
        if (params.name && params.value) {
          return `${params.name}: ${numberFormat(params.value)}`;
        }
        return null;
      }
    }
  };
  const legend = [
    {
      id: 'impressions',
      name: formatMessage('i18n_impressions')
    },
    {
      id: 'clicks',
      name: formatMessage('i18n_clicks')
    }
  ];
  return (
    <>
      <div className={prefixClassName('map')}>
        <CustomRadioGroup
          inline
          appearance="picker"
          data={legend}
          value={active}
          onChange={handleActiveChange as any}
          useDefaultStyle
        />
        {chartData && <Chart config={config} loading={loading} option={option} title="" />}
      </div>
    </>
  );
});
