import React, { memo, useCallback, useMemo } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';
import { Link, useLocation, useHistory } from 'react-router-dom';

import * as APIs from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import SelectPicker from '@/components/Picker/SelectPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { FloatActionCell, CampaignStatusCell, DateCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { useModal } from '@/components/hooks/useModal';
import { BasicReportProps } from '@/constants/config';

import { statusData, filterParamKeys, defaultFilter } from './constants';
import Modal from './Modal';

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    refetchTableData,
    handleSearchWord,
    handleChangeCustomParam
  }: BasicReportProps) => {
    const history = useHistory();
    const { pathname } = useLocation();
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_CROSS_CAMPAIGN
    });

    const statusModal = useModal(false, {}, APIs.API_CROSS_CAMPAIGN_ID, refetchTableData);

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
    const handleStatusChanged = useCallback(
      (status: string) => {
        handleChangeCustomParam({
          [filterParamKeys.STATUS]: status
        });
      },
      [handleChangeCustomParam]
    );

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport(
          {
            var: 'xlsx',
            ...tableParams
          },
          APIs.API_CROSS_CAMPAIGN_VAR
        );
    }, [onExport, tableParams]);

    const renderActions = useCallback(
      rowData => {
        const editAble = !_.includes(['CANCELLED', 'COMPLETE'], rowData.status) || true;

        const handleEdit = () => {
          const { id } = rowData;
          if (_.isFinite(id)) {
            // don't know why cannot use nested method
            history.push(`${pathname}/${id}/edit`);
          }
        };

        const cancelClickCallback = () => {
          statusModal.showModal({
            ...rowData,
            status: 0
          });
        };

        const completeClickCallback = () => {
          statusModal.showModal({
            ...rowData,
            status: 4
          });
        };

        return [
          <FloatActionCell.Actions.Edit key="edit" onClick={handleEdit} />,
          <FloatActionCell.Actions.Complete
            key="complete"
            onClick={completeClickCallback}
            disabled={!editAble}
          />,
          <FloatActionCell.Actions.Cancel
            key="cancel"
            onClick={cancelClickCallback}
            disabled={!editAble}
          />
        ];
      },
      [statusModal, history, pathname]
    );

    const renderToolbar = (): React.ReactElement[] => {
      return [
        <Button key="create" appearance="primary" componentClass={Link} to={`${pathname}/create`}>
          {formatMessage('i18n_create')}
        </Button>,
        <SelectPicker
          key="secondary"
          prepend={formatMessage('i18n_status')}
          data={statusData}
          value={_.get(filterParams, 'status', defaultFilter.status)}
          onChange={handleStatusChanged}
        />,
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button key="exportTable" className="pull-right" onClick={handleExportClicked}>
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };

    return (
      <>
        <Modal {...statusModal} />
        <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
          <Column width={100} fixed sortable>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>
          <Column width={250} resizable fixed sortable>
            <HeaderCell>{formatMessage('i18n_crossCamName')}</HeaderCell>
            <FloatActionCell dataKey="name" renderActions={renderActions} />
          </Column>
          {/* <Column width={200}> // 暂时不用此列
          <HeaderCell>{formatMessage('i18n_secondaryOwner')}</HeaderCell>
          <MultiLineCell
            dataKey='secondary'
            maxLine={maxLine}
            formatter={data => _.isArray(data) && data.reduce((acc, i) =>
              _.isEmpty(i) ? acc : acc.concat(`${this.getShortRoleName(_.get(i, 'role', ''))}: ${_.get(i, 'beName', '')}`), [])}
          />
        </Column> */}
          <Column width={100} sortable>
            <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
            <CampaignStatusCell dataKey="status" />
          </Column>
          <Column width={150} sortable>
            <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
            <DateCell dataKey="startDate" />
          </Column>
          <Column width={150} sortable>
            <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
            <DateCell dataKey="endDate" />
          </Column>
          <Column width={150} sortable>
            <HeaderCell>{formatMessage('i18n_targetPerson')}</HeaderCell>
            <FloatActionCell dataKey="target" />
          </Column>
          <Column width={100} sortable>
            <HeaderCell>{formatMessage('i18n_frequency')}</HeaderCell>
            <FloatActionCell dataKey="enable_freq" />
          </Column>
        </Table>
      </>
    );
  }
);
