import { useState, useRef, useCallback } from 'react';
import _ from 'lodash';

/**
 * Modal中经常需要用户填写数据
 */
export const useModalForm = (defaultFormVal = {}, defaultFormErr = {}) => {
  const [formVal, setVal] = useState(defaultFormVal);
  const [formErr, setErr] = useState(defaultFormErr);
  const formRef = useRef(null);

  const checkFormErr = useCallback(
    (err: any) => {
      !_.isEqual(err, formErr) && setErr(err);
    },
    [formErr]
  );

  const setFormVal = useCallback(val => {
    setVal(val);
  }, []);

  const resetForm = useCallback(() => {
    setFormVal(defaultFormVal);
    checkFormErr(defaultFormErr);
  }, [setFormVal, checkFormErr, defaultFormVal, defaultFormErr]);

  return {
    formRef,
    setFormVal,
    checkFormErr,
    formVal,
    formErr,
    resetForm
  };
};
