import React, { useEffect, useCallback, useState, useMemo } from 'react';
import _ from 'lodash';

import { API_TASK_EXPORT_FILTER } from '@/constants/APIs';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import {
  AdvCustomFilter,
  FilterNodeConfig,
  FILTER_OPERATION,
  FILTER_TYPE
} from '@/components/AdvancedFilter';
import { HierarchyIdKey, conversionDimension } from './constants';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface Props {
  value?: Array<any>;
  onChange?: Function;
  disabled?: boolean;
  hierarchy?: string;
}

const valueKey = 'key';
const labelKey = 'value';

// Array<{type: string; value: Array<any>;}> 这样请求
export default ({ value = [], onChange, disabled, hierarchy }: Props) => {
  const { formatMessage } = useFmtMsgFori18n();
  const {
    onFetch: onFetchExpFilters,
    loading: expFilterLoading,
    response: expFilterResp
  } = useFetch<CommonResponse>(FETCH_METHOD.GET, API_TASK_EXPORT_FILTER, { cancelPrevious: true }); // 不处理请求取消
  const [filterData, setFilterData] = useState<FilterNodeConfig[]>([]);
  const innerVal = useMemo(() => {
    return _.map(value, v => ({
      id: v.id,
      key: v.type,
      operation: 'equal',
      value: v.value
    }));
  }, [value]);

  useEffect(() => {
    onFetchExpFilters().then(resp => {
      const data = _.get(resp, 'data', {});
      const newFilter = Object.keys(data).map(dKey => {
        const label = dKey === 'plt' ? 'platform' : dKey;
        return {
          key: dKey === 'platformType' ? 'plt' : dKey,
          label: formatMessage(`i18n_${dKey === 'site' ? 'media' : label}`),
          operation: FILTER_OPERATION.EQUAL,
          type: FILTER_TYPE.CHECK,
          asyncFnKey: [dKey],
          defaultValue: [],
          valueKey,
          labelKey
        };
      });

      setFilterData(newFilter);
    });
  }, []);

  const handleSubmit = useCallback(
    val => {
      onChange(
        _.map(val, v => ({
          id: v.id,
          type: v.key,
          value: v.value
        }))
      );
    },
    [onChange]
  );

  const asyncData = useMemo(() => {
    const data = _.get(expFilterResp, 'data', {}) || {};
    return Object.keys(data).reduce((accu, curr) => {
      const fmtDataList =
        _.map(data[curr], d => ({
          [labelKey]: d[labelKey] || '',
          [valueKey]: d[valueKey] || ''
        })) || [];
      accu = {
        ...accu,
        [curr]: {
          loading: false,
          data: fmtDataList.filter(d => d[valueKey] || d[labelKey]),
          fetchFn: null
        }
      };

      return accu;
    }, {});
  }, [expFilterResp]);

  return (
    <AdvCustomFilter
      inForm
      filterData={
        hierarchy === HierarchyIdKey.CONVERSION
          ? filterData.filter(i => conversionDimension.indexOf(i.key) > -1)
          : filterData
      }
      asyncData={asyncData}
      value={innerVal}
      onSubmit={handleSubmit}
      keyColumnLoading={expFilterLoading}
      limit={Infinity}
      disabled={disabled}
    />
  );
};
