import _ from 'lodash';
import memoize from 'memoize-one';

const getToJsSelector = (() => {
  const ALL_SELECT = {};
  return (path: string[]) => {
    let selector = _.get(ALL_SELECT, path);
    if (!selector) {
      selector = memoize((value: any) => (value && value.toJS ? value.toJS() : value));
      _.set(ALL_SELECT, path, selector);
    }
    return selector;
  };
})();

/**
 *
 * @param basePath
 * @param initialState
 */
export default (basePath: string[], initialState: Record<string, any>) => {
  return (state: any) => {
    const selectValue = (valueKey: string) => {
      const path = [...basePath, valueKey];
      const toJS = getToJsSelector(path);
      return () => toJS(state.getIn(path));
    };
    return _.mapValues(initialState, (value: any, key: string) => selectValue(key));
  };
};
