import React, { memo } from 'react';
import _ from 'lodash';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import { API_CONVERSIONGROUP } from '@/constants/APIs';
import CustomField from '@/components/Form/CustomField';
import MultiField from '@/components/Form/MultiField';
import Toggle from '@/components/Form/Toggle';
import RadioGroup from '@/components/Form/RadioGroup';
import { useUserId } from '@/components/hooks/useCertainParams';
import { ConversionModel } from '@/constants/form/models';
import SelectPicker from '@/components/Picker/SelectPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { isOfflineData } from './constants';
import AdvBrandCamMultiPicker from '@/components/Picker/logic/AdvBrandCamMultiPicker';

export default memo(
  ({
    drawer,
    formData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const userId = useUserId();
    const { formatMessage } = useFmtMsgFori18n();
    const title =
      formatMessage(`i18n_${drawer.isEdit ? 'edit' : 'create'}`) + formatMessage('i18n_activity');
    // 回溯期
    const lookbackData = [
      { id: 7, name: `1${formatMessage('i18n_week')}` },
      { id: 7 * 2, name: `2${formatMessage('i18n_week')}` },
      { id: 7 * 3, name: `3${formatMessage('i18n_week')}` },
      { id: 7 * 4, name: `4${formatMessage('i18n_week')}` },
      { id: 30, name: `30${formatMessage('i18n_days')}` },
      { id: 30 * 2, name: `${30 * 2}${formatMessage('i18n_days')}` },
      { id: 30 * 3, name: `${30 * 3}${formatMessage('i18n_days')}` }
    ];
    const isOfflineDataIntl = usePickerDataFromObj(isOfflineData);

    return (
      <Drawer
        show={drawer.show}
        title={title}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        onSubmit={onSubmit}
        onHide={onClose}
        className="conversion-drawer"
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={ConversionModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 所属活动组 */}
          <CustomField
            name="conversionGroupId"
            label={formatMessage('i18n_belongActivityGroup')}
            placeholder={formatMessage('i18n_selActivityGroup')}
            accepter={SelectPicker}
            labelKey="name"
            valueKey="id"
            url={API_CONVERSIONGROUP}
            params={{ beId: drawer.isEdit ? formData.beId : null }}
            deps={drawer.isEdit ? [formData.beId] : undefined}
            groupBy="beName"
            required
            cleanable
            searchable
          />
          <CustomField
            name="isOffline"
            label={formatMessage('i18n_type')}
            accepter={RadioGroup}
            data={isOfflineDataIntl}
            itemWidth="45%"
            className="offline-group"
          />
          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} required />
          {/* 状态 */}
          <CustomField
            name="status"
            label={formatMessage('i18n_status')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_enabled')}
            unCheckedChildren={formatMessage('i18n_disabled')}
          />
          {/* 归因范围 */}
          <MultiField
            names={['advertisers', 'campaigns']}
            label={formatMessage('i18n_attributeFan')}
            accepter={AdvBrandCamMultiPicker}
            cleanable
          />
          {/* 回溯期 */}
          <CustomField
            name="lookback"
            label={formatMessage('i18n_lookBackWindow')}
            accepter={SelectPicker}
            data={lookbackData}
            required
          />
        </Form>
      </Drawer>
    );
  }
);
