import React, { memo, useCallback, useContext, useEffect, useMemo } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';

import DateRangePicker from '@/components/Picker/DateRangePicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { useMainPage } from '@/components/hooks/useMainPage';
import {
  defaultCustom,
  siteAndAd,
  impAndClk,
  pltAndDev,
  basicClassName,
  prefixClassName
} from './constants';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import Chart from '@/components/Chart/ReportCustomChart';

// part
import Summary from './part/Summary';
import Card from './part/Card';
import HorizontalBar from './part/HorizontalBar';
import Map from './part/Map';
import Pie from './part/Pie';
import { DateCtx } from '../context';

const defaultCommonParam = {
  customCols: defaultCustom.map(i => i.id),
  impressions: siteAndAd[0].id,
  clicks: siteAndAd[0].id,
  ctr: siteAndAd[0].id,
  area: impAndClk[0].id,
  platform: pltAndDev[0].id
};

export default memo(() => {
  const campaignId = useCampaignId();
  const { formatMessage } = useFmtMsgFori18n();
  const checkPickerDataIntl = usePickerDataFromObj(defaultCustom);
  const siteAndAdIntl = usePickerDataFromObj(siteAndAd);
  const impAndClkIntl = usePickerDataFromObj(impAndClk);
  const pltAndDevIntl = usePickerDataFromObj(pltAndDev);

  const { commonParam, handleChangeCommonParam } = useMainPage({
    defaultCommonParam
  });

  const {
    dateRangeVal,
    dateRangeValTimeFormat,
    setDateRangeValByDateArray,
    maxDateFromRespBeforePreset,
    validDateLoaded,
    reportDateFn,
    minDateFromResp
  } = useContext(DateCtx);

  const handleCustomChange = useCallback(
    (value: Array<any>) => {
      handleChangeCommonParam({
        customCols: value
      });
    },
    [handleChangeCommonParam]
  );

  const handleImpChange = useCallback(
    (value: string) => {
      handleChangeCommonParam({
        impressions: value
      });
    },
    [handleChangeCommonParam]
  );

  const handleClkChange = useCallback(
    (value: string) => {
      handleChangeCommonParam({
        clicks: value
      });
    },
    [handleChangeCommonParam]
  );

  const handleCtrChange = useCallback(
    (value: string) => {
      handleChangeCommonParam({
        ctr: value
      });
    },
    [handleChangeCommonParam]
  );

  const handleAreaChange = useCallback(
    (value: string) => {
      handleChangeCommonParam({
        area: value
      });
    },
    [handleChangeCommonParam]
  );

  const handlePlatChange = useCallback(
    (value: string) => {
      handleChangeCommonParam({
        platform: value
      });
    },
    [handleChangeCommonParam]
  );

  useEffect(() => {
    reportDateFn({
      params: {
        report: 'campaign'
      }
    });
  }, [reportDateFn]);
  const postParams = useMemo(
    () => ({
      id: campaignId,
      hierarchy: 'campaign',
      report: 'campaign'
    }),
    [campaignId]
  );
  if (!validDateLoaded) {
    // 时间没确定不渲染东西，这样处理最方便。
    return null;
  }

  return (
    <>
      <div className={basicClassName}>
        <Panel className="page-toolbar">
          <DateRangePicker
            value={dateRangeVal}
            maxDate={maxDateFromRespBeforePreset}
            minDate={minDateFromResp}
            cleanable={false}
            appearance="subtle"
            onChange={setDateRangeValByDateArray}
          />
          <CheckPicker
            data={checkPickerDataIntl}
            value={commonParam.customCols}
            appearance="subtle"
            prepend={formatMessage('i18n_customization')}
            supportAll
            onChange={handleCustomChange}
          />
        </Panel>
        {_.includes(commonParam.customCols, 'summary') && (
          <Summary dateParams={dateRangeValTimeFormat} id={campaignId} />
        )}
        {_.includes(commonParam.customCols, 'trend') && (
          <Chart dateParams={dateRangeValTimeFormat} postParams={postParams} pure customType={1} />
        )}
        <div className={prefixClassName('card')}>
          {_.includes(commonParam.customCols, 'impressions') && (
            <Card
              title={formatMessage('i18n_impTop')}
              filters={siteAndAdIntl}
              active={commonParam.impressions}
              onChange={handleImpChange}
            >
              <HorizontalBar
                dateParams={dateRangeValTimeFormat}
                id={campaignId}
                tag="view"
                mDimension={commonParam.impressions}
                valueKey="impressions"
              />
            </Card>
          )}
          {_.includes(commonParam.customCols, 'clicks') && (
            <Card
              title={formatMessage('i18n_clkTop')}
              filters={siteAndAdIntl}
              active={commonParam.clicks}
              onChange={handleClkChange}
            >
              <HorizontalBar
                dateParams={dateRangeValTimeFormat}
                id={campaignId}
                tag="view"
                mDimension={commonParam.clicks}
                valueKey="clicks"
              />
            </Card>
          )}
          {_.includes(commonParam.customCols, 'ctr') && (
            <Card
              title={formatMessage('i18n_ctrTop')}
              filters={siteAndAdIntl}
              active={commonParam.ctr}
              onChange={handleCtrChange}
            >
              <HorizontalBar
                dateParams={dateRangeValTimeFormat}
                id={campaignId}
                tag="ctr"
                mDimension={commonParam.ctr}
              />
            </Card>
          )}

          {_.includes(commonParam.customCols, 'area') && (
            <Card
              title={formatMessage('i18n_area')}
              filters={impAndClkIntl}
              active={commonParam.area}
              onChange={handleAreaChange}
            >
              <Map
                dateParams={dateRangeValTimeFormat}
                id={campaignId}
                tag="area"
                mDimension={commonParam.area}
              />
            </Card>
          )}

          {_.includes(commonParam.customCols, 'platform') && (
            <Card
              title={formatMessage('i18n_platformSegmentation')}
              filters={pltAndDevIntl}
              active={commonParam.platform}
              onChange={handlePlatChange}
            >
              <Pie
                dateParams={dateRangeValTimeFormat}
                id={campaignId}
                tag={commonParam.platform}
                mDimension={commonParam.platform}
              />
            </Card>
          )}
        </div>
        <div className="remark">*{formatMessage('i18n_noGivtRemark')}</div>
      </div>
    </>
  );
});
