import * as React from 'react';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import Helmet from 'react-helmet';
import { assembleTitle } from '@/utils';

interface TitleProps extends InjectedIntlProps {
  titleKey: string | null;
}

export default injectIntl(function({ titleKey, intl: { formatMessage, messages } }: TitleProps) {
  titleKey = (titleKey || '').replace('i18n_', ''); // 这里直接用了HOC的形式，如果用use的format遇到id为空会报错
  const titleContent = assembleTitle(
    messages[titleKey] ? formatMessage({ id: titleKey }) : titleKey
  );
  return (
    <Helmet>
      <title>{titleContent}</title>
    </Helmet>
  );
});
