import React, { memo } from 'react';
import _ from 'lodash';
import 'echarts/lib/component/legendScroll';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/toolbox';
import 'echarts/lib/component/title';

import LineChart from '@rsuite/charts/lib/charts/LineChart';
import XAxis from '@rsuite/charts/lib/components/XAxis';
import { ChartProps } from './type';
import { getSerie, formatYAxis } from './index';

export default memo(
  ({ config, option = {}, loading, height, ...props }: ChartProps & BaseObject<any>) => {
    const { type, data: chartData, yAxis, xAxis } = config;
    const cols = _.get(chartData, 'cols', []);
    const items =
      _.get(chartData, 'records') || _.get(chartData, 'data') || _.get(chartData, 'items') || [];
    const data: object[] = [];
    cols.forEach((item: any, index: number) => {
      let one = [item];
      for (let i = 0; i < items.length; i += 1) {
        one.push(items[i].data[index]);
      }
      data.push(one);
    });

    return (
      <LineChart loading={loading} data={data} option={option} height={height} {...props}>
        {formatYAxis(yAxis)}
        <XAxis data={cols} boundaryGap position="bottom" {...xAxis} />
        {_.map(items, serie => getSerie(serie, type))}
      </LineChart>
    );
  },
  (prevProps, nextProps) => _.isEqual(prevProps, nextProps)
);
