import React, { SyntheticEvent, useContext, useCallback } from 'react';
import _ from 'lodash';
import { FormControl } from 'rsuite';

import { FormDataContext, FormErrorContext } from '@/components/Form/FormContext';
import CustomFieldNoControl, { CustomFieldCommonProps } from './CustomFieldWrapper';

export { DescriptionIcon } from './CustomFieldWrapper';

interface CustomFieldProps<T> extends CustomFieldCommonProps<T> {
  name: string; // 表单项名，用于存储表单数据
  accepter?: React.ComponentType<T>;
  checkTrigger?: 'change' | 'blur' | 'none';
  onChange?: (val?: any, e?: SyntheticEvent) => void;
}

type Props<T> = CustomFieldProps<T> & T;

type CustomFieldWithMemo = <T>(props: Props<T>) => JSX.Element;
interface NamedCustomFieldWithMemo extends CustomFieldWithMemo {
  displayName?: string;
}

function Field<T>({ name, accepter, checkTrigger, onChange, ...props }: Props<T>) {
  const { onChange: onFormChange } = useContext(FormDataContext);
  const { formErrors } = useContext(FormErrorContext);
  const { label, className, required, help, message, ...rest } = props;

  const handleChange = useCallback(
    (val: any, e: SyntheticEvent) => {
      _.isFunction(onFormChange) && onFormChange(val, name);
      _.isFunction(onChange) && onChange(val, e);
    },
    [name, onChange, onFormChange]
  );

  return (
    <CustomFieldNoControl
      label={label}
      className={className}
      required={required}
      help={help}
      message={message}
      {...props}
    >
      <FormControl
        checkTrigger={checkTrigger || (accepter ? 'change' : 'blur')} // 普通Input组件设为blur，其他为change
        {...(rest as any) as T}
        name={name}
        accepter={accepter}
        onChange={handleChange}
        errorMessage={_.get(formErrors, name)}
      />
    </CustomFieldNoControl>
  );
}

const CustomField: NamedCustomFieldWithMemo = React.memo(Field);
CustomField.displayName = 'CustomField';
export default CustomField;
