import React, { useState, useEffect, useCallback } from 'react';
import _ from 'lodash';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import Toggle from '@/components/Form/Toggle';
import SelectPicker from '@/components/Picker/SelectPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { API_ADSERVER } from '@/constants/APIs';
import { SiteModel } from '@/constants/form/models';
import { typeData, categoryData } from './constants';

const adServerParams = {
  orderColumn: 'name',
  orderType: 'asc'
};

export default React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();

    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editSite' : 'i18n_newSite')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        className="site-drawer"
        onSubmit={onSubmit}
        onHide={onClose}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={SiteModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} required />

          {/* 域名 */}
          <CustomField name="domain" label={formatMessage('i18n_domain')} />

          {/* 缺省广告服务器 */}
          <CustomField
            name="defaultAdS"
            label={formatMessage('i18n_defaultAdServer')}
            accepter={SelectPicker}
            // menuStyle={{maxWidth: '400px'}}
            url={API_ADSERVER}
            params={adServerParams}
            required
            searchable
          />

          {/* 广告网络 */}
          <CustomField
            name="adnetwork"
            label={formatMessage('i18n_adNetWork')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_open')}
            unCheckedChildren={formatMessage('i18n_close')}
          />

          {/* 网站分类 */}
          <CustomField
            name="categoryID"
            label={formatMessage('i18n_siteType')}
            accepter={SelectPicker}
            data={categoryData}
          />

          {/* 类型 */}
          <CustomField
            name="type"
            label={formatMessage('i18n_type')}
            accepter={SelectPicker}
            block
            data={typeData}
          />

          {/* 备注 */}
          <CustomField
            name="notes"
            label={formatMessage('i18n_remark')}
            componentClass="textarea"
            rows={2}
          />
        </Form>
      </Drawer>
    );
  }
);
