import React, { useState, useCallback, useEffect } from 'react';
import _ from 'lodash';
import CustomField from '@/components/Form/CustomField';
import PickerGroup from '@/components/Picker/PickerGroup';
import Toggle from '@/components/Form/Toggle';
import SelectPicker from '@/components/Picker/SelectPicker';
import { API_ADVERTISER } from '@/constants/APIs';
import { UseFetchProps } from '@/components/Picker/PickerHooks/useFetchForPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface Props {
  names?: Array<string>;
  values?: Record<string, any>;
  errors?: Record<string, string>;
  disabled?: boolean;
  fetchOptions?: UseFetchProps;
  onChange?: Function;
}

export default React.memo(
  ({ names, values, errors, disabled = false, fetchOptions = {}, onChange }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();
    return (
      <div className="cpo-audit">
        <CustomField
          name="isAudit"
          accepter={Toggle}
          checkedChildren={formatMessage('i18n_yes')}
          unCheckedChildren={formatMessage('i18n_no')}
        />
        <CustomField name={'auditEmail'} disabled={values['isAudit'] !== true} />
      </div>
    );
  }
);
