import React, { useCallback, useMemo } from 'react';
import { Switch, Redirect, Route, BrowserRouter, RouteComponentProps } from 'react-router-dom';
import _ from 'lodash';
import BasicRoute from '@/components/Routes/BasicRoute';
import NoSidebarLayout from '@/components/Layouts/NoSidebarLayout';
import GridSidebarLayout from '@/components/Layouts/GridSidebarLayout';
import ErrorPage404 from '@/components/ErrorPage/404';
import Common from '@/views/common';
import Campaigns from '@/views/campaigns/normal';
import CrossCampaigns from '@/views/campaigns/cross';
import HACFrame from '@/views/hac';
import Summary from '@/views/single/summary/route';
import CpoAudit from '@/views/single/cpo/cpoAudit';

import { useAuthPath } from '@/utils/auth';
import { isIframe, getUserProfile, getFirstPage } from '@/utils/location';
import { __PUBLIC__ } from '@/constants/config';

import ErrorPage from './error/ErrorPage';

/**
 * 基本的自测点：(运行 dev:fast可以避免userInfo请求，始终使用 localStorage里面的)
 * 1. 内部用户直接访问 / | /summary | /summary/ 都会出现汇总页面
 * 2. 外部用户直接访问
 *    / 跳转到 有权限的，用户定义的路由列表的首页
 *    /summary 跳转403
 *    /summary/ 跳转403
 * 3. 访问无权限的页面(所有页面的定义中有，但是validUrl没有) 403
 * 4. 访问有权限的页面(所有页面的定义中没有的路径) 404
 * 5. userInfo 用于除了campaign页面其他路由的鉴权
 * 6. campaign不受userInfo返回值的影响，使用campaignInfo接口返回的validUrls 鉴权
 */

interface Props {
  errorUrl?: string; // 一旦指定，任何路由都会被重定向至此，当用户对整个产品无权限时使用
  children?: React.ReactNode;
}

const UserInfoValidateHOC = React.memo((props: any) => {
  const result = useAuthPath();
  if (!result) {
    return null;
  }

  return <>{props.children}</>;
});

const getDefaultPageRoute = (defaultUrl: string) => {
  const renderRoute = (props: RouteComponentProps) => <Redirect to={defaultUrl} {...props} />;

  return <BasicRoute key="default" path="/" exact render={renderRoute} />;
};

// 路由匹配顺序 campaign>crossCampaign>common
const InsidePageRoutes = [
  <BasicRoute key="campaigns" path="/campaigns/:campaignId" component={Campaigns} />,
  <BasicRoute key="cross" path="/cross-campaigns/:campaignId" component={CrossCampaigns} />,
  <BasicRoute key="common" component={UserInfoValidateHOC}>
    <Common />
  </BasicRoute>
];

const SinglePageRoutes = [
  <BasicRoute key="summary" path="/summary" component={GridSidebarLayout}>
    <Summary />
  </BasicRoute>,
  <BasicRoute key="cpo" path="/cpo-audit" component={NoSidebarLayout}>
    <Route path="/cpo-audit/:campaignId" component={CpoAudit} />
  </BasicRoute>
];

const HACPageRoute = <BasicRoute key="hac" path="/hac" component={HACFrame} />;

const ErrorPageRoutes = (
  <BasicRoute key="error" path="/error" component={NoSidebarLayout}>
    <Switch>
      <Route path="/error/:code" component={ErrorPage} />
      <Route component={ErrorPage404} />
    </Switch>
  </BasicRoute>
);

const Routes = React.memo(({ errorUrl }: Props) => {
  const ErrorRedirect = useCallback(
    () => (_.isEmpty(errorUrl) ? null : <Redirect to={errorUrl} />),
    [errorUrl]
  );
  const routes = useMemo(() => {
    if (!_.isEmpty(errorUrl)) {
      // 有errorUrl时，只提供错误路由，默认路由指向404页面
      return [getDefaultPageRoute('/error/404'), ErrorPageRoutes];
    } else if (isIframe()) {
      // 在iframe中时，说明处于HAC授权过程中，只提供HAC路由和错误路由，默认路由指向hac页面
      return [getDefaultPageRoute('/hac'), HACPageRoute, ErrorPageRoutes];
    } else if (getUserProfile().internal) {
      // 正常情况下，向内部用户提供所有路由，默认路由指向汇总页
      return [
        getDefaultPageRoute('/summary'),
        HACPageRoute,
        SinglePageRoutes,
        ErrorPageRoutes,
        ...InsidePageRoutes
      ];
    } else {
      // 正常情况下，向外部用户提供所有路由，默认路由指向菜单第一项
      return [
        getDefaultPageRoute(getFirstPage()),
        HACPageRoute,
        SinglePageRoutes,
        ErrorPageRoutes,
        ...InsidePageRoutes
      ];
    }
  }, []);

  return (
    <BrowserRouter basename={__PUBLIC__}>
      <BasicRoute component={ErrorRedirect} />
      <Switch>{routes}</Switch>
    </BrowserRouter>
  );
});

export default Routes;
