import React, { memo, useState, useEffect } from 'react';
import { Loader, Panel, Whisper, Tooltip, Icon } from 'rsuite';
import _ from 'lodash';
import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Pie from './Pie';

const mockData = {
  name: '男18+',
  targetRate: '40.00%',
  DSKRate: '0.00%',
  OTTRate: '0.00%',
  MBLRate: '0.00%'
};

interface Props extends BasicReportProps {
  campaignType: 'campaign' | 'crossCampaign';
}

interface DataProps {
  name: string;
  targetRate: string;
  DSKRate: string;
  OTTRate: string;
  MBLRate: string;
}

const tip = (title: string) => (
  <Whisper placement="top" trigger="hover" speaker={<Tooltip>{title}</Tooltip>}>
    <Icon icon="help-o" />
  </Whisper>
);

export default memo<Props>(function({ dateParams, filterParams, id, campaignType }) {
  const { loading, onFetch } = useFetch<CommonResponse>(FETCH_METHOD.GET, APIs.API_TAB_REPORT, {
    cacheParams: true,
    params: {
      id,
      type: 'hit',
      hierarchy: campaignType,
      report: 'audiences',
      end: dateParams.end,
      ...filterParams
    }
  });
  const [data, setData] = useState<DataProps>(mockData);

  useEffect(() => {
    onFetch().then(rsp => {
      !_.isEmpty(_.get(rsp, 'data.records.0', {})) && setData(_.get(rsp, 'data.records.0', {}));
    });
  }, [id]);

  const { formatMessage } = useFmtMsgFori18n();

  return (
    <>
      <Panel className="panel-summary">
        {loading ? (
          <Loader center content={formatMessage('i18n_loading')} />
        ) : (
          <>
            <div className="panel-summary-left">
              <div className="panel-summary-li">
                <div className="panel-summary-desc">
                  {formatMessage('i18n_target')}{' '}
                  {tip(formatMessage('i18n_audiencesHitTargetRemark'))}
                </div>
                <div className="panel-summary-name">{data.name}</div>
              </div>
              <div className="panel-summary-li pie">
                <Pie height={100} value={_.get(data, 'targetRate')} />
              </div>
              <span className="panel-summary-left-tips">
                {formatMessage('i18n_releaseTime')}：{dateParams.end}
              </span>
            </div>
            <div className="panel-summary-right">
              <div className="panel-summary-li">
                <div className="panel-summary-desc">
                  {formatMessage('i18n_pc')} {tip(formatMessage('i18n_audiencesHitRemark'))}
                </div>
                <div className="panel-summary-value">{data.DSKRate}</div>
              </div>
              <div className="panel-summary-li">
                <div className="panel-summary-desc">
                  {formatMessage('i18n_mobile')} {tip(formatMessage('i18n_audiencesHitRemark'))}
                </div>
                <div className="panel-summary-value">{data.MBLRate}</div>
              </div>
              <div className="panel-summary-li">
                <div className="panel-summary-desc">
                  {formatMessage('i18n_ott')} {tip(formatMessage('i18n_audiencesHitRemark'))}
                </div>
                <div className="panel-summary-value">{data.OTTRate}</div>
              </div>
            </div>
          </>
        )}
      </Panel>
    </>
  );
});
