import React from 'react';
import { FormGroup, ControlLabel, HelpBlock } from 'rsuite';

import HelpInfo from '@/components/HelpInfo';

import { RequiredTxt } from './RequiredTxt';

export { DescriptionIcon } from '@/components/HelpInfo';

export interface CustomFieldCommonProps<T> {
  label?: React.ReactNode; // 表单项标签名，用于显示
  help?: React.ReactNode; // 帮助提示信息
  message?: React.ReactNode; // 表单项下方辅助信息
  required?: boolean; // 有无必填提示
  className?: string;
  children?: JSX.Element;
}

type Props<T> = CustomFieldCommonProps<T> & T;

type CustomFieldWithMemo = <T>(props: Props<T>) => JSX.Element;
interface NamedCustomFieldWithMemo extends CustomFieldWithMemo {
  displayName?: string;
}

function Field<T>({ message, label, help, required, className, children }: Props<T>) {
  return (
    <FormGroup className={className}>
      {label && (
        <ControlLabel>
          {label}
          {required && <RequiredTxt />}
          {help && <HelpInfo message={help} />}
        </ControlLabel>
      )}
      {children}
      {message && <HelpBlock>{message}</HelpBlock>}
    </FormGroup>
  );
}

const CustomFieldWrapper: NamedCustomFieldWithMemo = React.memo(Field);
CustomFieldWrapper.displayName = 'CustomFieldWrapper';
export default CustomFieldWrapper;
