import React from 'react';
import _ from 'lodash';

import SelectPicker, { CustomSelectPickerProps } from '@/components/Picker/SelectPicker';
import { API_ATTRIBUTE } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface ReqParamProps {
  beId?: Array<number>;
  campaignId?: number | string;
  sub?: number;
}
interface Props {
  chosenValue: number | string;
  reqParam?: ReqParamProps;
}

const pickerDefaultParam = {
  style: { maxWidth: '300px' },
  menuStyle: { maxWidth: '400px' },
  height: 250,
  cleanable: true,
  searchable: false
};

export default ({ chosenValue, reqParam, ...rest }: Props & CustomSelectPickerProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const beId = _.get(reqParam, 'beId') ? reqParam.beId.toString() : null;
  return (
    <SelectPicker
      value={chosenValue}
      url={API_ATTRIBUTE}
      placeholder={formatMessage('i18n_select', { name: '' })}
      params={{
        ...reqParam,
        hideDefaultAttr: 1,
        isDefault: 1,
        beId
      }}
      prepend={formatMessage('i18n_customAttributes')}
      {...pickerDefaultParam}
      {...rest}
    />
  );
};
