import React, { memo } from 'react';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import AttributePicker from '@/components/Picker/logic/AttributePicker';
import { BasicReportProps } from '@/constants/config';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { filterParamKeys, FilterParam, primaryData } from './constants';
import { segData } from '../constants';

type Props = Partial<BasicReportProps<FilterParam>> & {
  handleChangeDate: (newDateRange: Array<Date>) => void;
  dateVal: Array<string>;
  maxDate: string;
  minDate: string;
};
export default memo(
  ({
    dateVal,
    maxDate,
    minDate,
    filterParams,
    handleChangeDate,
    handleChangeCommonParam
  }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();
    return (
      <>
        <DateRangePicker
          value={dateVal}
          maxDate={maxDate}
          minDate={minDate}
          cleanable={false}
          appearance="subtle"
          onChange={handleChangeDate}
        />
        <SelectPicker
          prepend={formatMessage('i18n_primary')}
          data={usePickerDataFromObj(primaryData)}
          value={filterParams[filterParamKeys.MDIMENSION]}
          onChange={(value: string) =>
            handleChangeCommonParam({ [filterParamKeys.MDIMENSION]: value })
          }
          appearance="subtle"
        />
        <AttributePicker
          chosenValue={filterParams[filterParamKeys.PARA]}
          onChange={(value: string) => handleChangeCommonParam({ [filterParamKeys.PARA]: value })}
          appearance="subtle"
        />
        <SecondaryPicker
          onChange={(value: string) => handleChangeCommonParam({ [filterParamKeys.SEC]: value })}
          value={filterParams.secondary}
          data={[null, 'daily', 'weekly']}
          cleanable={false}
          appearance="subtle"
        />
        <SelectPicker
          prepend={formatMessage('i18n_segmentation')}
          data={usePickerDataFromObj(segData)}
          value={filterParams.segmentation}
          onChange={(value: string) => handleChangeCommonParam({ [filterParamKeys.SEG]: value })}
          appearance="subtle"
        />
      </>
    );
  }
);
