import React from 'react';

import { useModuleDate, DateKeys } from '@/components/dateSource';

import { UserModuleDateCtx } from './context';

export default React.memo((props: any) => {
  const dateCtxVal = useModuleDate(DateKeys.USER);
  return (
    <UserModuleDateCtx.Provider value={dateCtxVal}>{props.children}</UserModuleDateCtx.Provider>
  );
});
