import React, { memo, useContext } from 'react';
import { DatePicker } from 'rsuite';
import { DatePickerProps } from 'rsuite/lib/DatePicker';

import { setDisabledDate } from '@/utils/date';
import { DrawerContainerContext } from '@/components/Drawer';

type CustomDatePickerProps = {
  maxDate?: any;
  minDate?: any;
} & DatePickerProps;

export default memo(({ minDate, maxDate, ...rest }: CustomDatePickerProps) => {
  const getContainer = useContext(DrawerContainerContext);

  let newProps = rest;
  if (minDate || maxDate) {
    newProps = {
      ...rest,
      disabledDate: (date: Date) => setDisabledDate(maxDate, minDate, date)
    };
  }

  return <DatePicker container={getContainer} {...newProps} />;
});
