import React, { useCallback, useContext } from 'react';
import _ from 'lodash';
import { Input, InputGroup } from 'rsuite';
import RadioGroup from '@/components/Form/RadioGroup';
import { FormErrorContext } from '@/components/Form/FormContext';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { PC, MOBILE, OTT, AdsConfigItem, getAdsConfigOptions } from './constants';

interface AdsConfigObj {
  [PC]: string;
  [MOBILE]: string;
  [OTT]: string;
}

interface Props extends AdsConfigItem {
  value?: string | AdsConfigObj;
  onChange?: Function;
}

// 校验时机：
// 未提交前，任意改变都不进行校验；
// 提交后，会整体进行校验
// 之后仅对上一步有错的项进行校验：
// * radio切换为无/默认时，清除错误
// * input blur时重新校验当前项

export default React.memo(
  ({
    name,
    value,
    onChange,
    emptyLabel,
    noPlatform,
    defaultVal = '',
    defaultPC,
    defaultMobile,
    defaultOTT
  }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();
    const adsConfigOptions = usePickerDataFromObj(getAdsConfigOptions(emptyLabel), {
      skipNoi18nPrefix: true
    });

    const { formErrors, checkForm, setFormErrors } = useContext(FormErrorContext);

    const handleChangeRadio = useCallback(
      val => {
        if (val) {
          onChange(
            noPlatform
              ? defaultVal
              : {
                  [PC]: defaultPC || defaultVal,
                  [MOBILE]: defaultMobile || defaultVal,
                  [OTT]: defaultOTT || defaultVal
                }
          );
        } else {
          onChange(undefined);
          setFormErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined
          }));
        }
      },
      [defaultMobile, defaultOTT, defaultPC, defaultVal, name, noPlatform, onChange, setFormErrors]
    );

    const handleChangeInput = useCallback(
      (key?: string) => (val: string) => {
        onChange(
          noPlatform
            ? val
            : {
                ...(value as object),
                [key]: val
              }
        );
      },
      [noPlatform, onChange, value]
    );

    const handleBlurInput = useCallback(() => {
      if (_.isEmpty(formErrors[name])) {
        return;
      }
      checkForm(name).then(err => {
        setFormErrors(prevErrors => ({
          ...prevErrors,
          [name]: err
        }));
      });
    }, [checkForm, formErrors, name, setFormErrors]);

    return (
      <div className="radio-input">
        <RadioGroup
          value={_.isNil(value) ? 0 : 1}
          data={adsConfigOptions}
          onChange={handleChangeRadio}
          itemWidth="45%"
        />
        {!_.isNil(value) && noPlatform && (
          <Input
            value={value as string}
            onBlur={handleBlurInput}
            onChange={handleChangeInput()}
            className="radio-input-input"
          />
        )}
        {!_.isNil(value) && !noPlatform && (
          <div className="radio-input-group">
            <InputGroup>
              <InputGroup.Addon>{formatMessage('i18n_pc')}</InputGroup.Addon>
              <Input
                value={(value as AdsConfigObj)[PC]}
                onBlur={handleBlurInput}
                onChange={handleChangeInput(PC)}
              />
            </InputGroup>
            <InputGroup>
              <InputGroup.Addon>{formatMessage('i18n_mobile')}</InputGroup.Addon>
              <Input
                value={(value as AdsConfigObj)[MOBILE]}
                onBlur={handleBlurInput}
                onChange={handleChangeInput(MOBILE)}
              />
            </InputGroup>
            <InputGroup>
              <InputGroup.Addon>OTT</InputGroup.Addon>
              <Input
                value={(value as AdsConfigObj)[OTT]}
                onBlur={handleBlurInput}
                onChange={handleChangeInput(OTT)}
              />
            </InputGroup>
          </div>
        )}
      </div>
    );
  }
);
