import React from 'react';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

/**
 * picker未选择值时，默认表示全部
 */

export interface UseAllProps {
  supportAll?: boolean; // false表示禁用此功能，默认禁用
  allLabel?: React.ReactNode; // 当表示全部时自定义显示的内容
  placeholder?: React.ReactNode;
}

interface OutProps {
  placeholder: React.ReactNode;
}

const useAllForPicker: PropMapper<UseAllProps, OutProps> = ({
  supportAll = false,
  allLabel,
  placeholder,
  ...props
}) => {
  const { formatMessage } = useFmtMsgFori18n();
  return {
    placeholder: supportAll ? (
      <span className="picker-all-label">{allLabel || formatMessage('i18n_all')}</span>
    ) : (
      placeholder
    ),
    ...props
  };
};

export default useAllForPicker;
