import React, { useMemo } from 'react';

import { useFmtMsgFori18n } from '@/utils/formatMessage';

import { allFilter } from './config';
import { FILTER_KEY } from './enum';
import { useAsyncData } from './useAsyncData';
import FilterMain, { AdvFilterProps } from '../../Main';

export interface PresetContainerProps extends Omit<AdvFilterProps, 'filterData' | 'asyncData'> {
  filters: Array<FILTER_KEY>;
  campaignId: Array<string> | string;
}

export default ({ filters, campaignId, ...rest }: PresetContainerProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  // 过滤调调用方提供key-list但是，其中不在组件维护的常量配置列表里的无效的key。
  const filterData = useMemo(
    () => allFilter(formatMessage).filter(f => filters.indexOf(f.key as FILTER_KEY) !== -1),
    [formatMessage, filters]
  );
  // 列出所有的异步请求的列表，统一的调用一遍。
  const asyncKeyList = useMemo(
    () => filterData.reduce((accu, curr) => accu.concat(curr.asyncFnKey), []),
    [filterData]
  );
  const asyncData = useAsyncData(asyncKeyList, campaignId);

  return <FilterMain asyncData={asyncData} filterData={filterData} {...rest} />;
};
