import React, { ReactElement } from 'react';
import _ from 'lodash';
import { Whisper, Tooltip, Icon, IconButton, Dropdown } from 'rsuite';
import { WhisperProps } from 'rsuite/lib/Whisper/index';
import { IconProps } from 'rsuite/lib/Icon';

import { ACTION_TYPE } from './index';

interface ActionProps {
  icon: IconProps['icon'];
  children: ReactElement | string;
  placement?: WhisperProps['placement'];
  WrapperPickerComponent?: any;
}

interface ActionType {
  type?: ACTION_TYPE.ACTION | ACTION_TYPE.DROPDOWN_ITEM;
}

// togglecomponent 不方便直接加属性
const IconButtonLink = React.forwardRef((props: any, ref: React.RefObject<any>) => {
  return <IconButton appearance="link" {...props} circle ref={ref} />;
});

const renderAction = (
  { icon, children, placement = 'top', WrapperPickerComponent, ...props }: ActionProps,
  ref?: React.RefObject<any>
) => (
  <Whisper placement="top" speaker={<Tooltip>{children}</Tooltip>}>
    {WrapperPickerComponent ? (
      <WrapperPickerComponent
        {...props}
        className="wrapper-component"
        toggleComponentClass={IconButtonLink}
        icon={<Icon icon={icon} />}
        appearance="subtle"
        placement="auto"
        ref={ref}
      />
    ) : (
      <IconButton {...props} appearance="link" icon={<Icon icon={icon} />} circle />
    )}
  </Whisper>
);

const renderDropDownItem = ({ icon, children, ...props }: ActionProps) => (
  <Dropdown.Item {...props}>
    <Icon icon={icon} /> {children}
  </Dropdown.Item>
);

export default React.forwardRef(
  (
    { type = ACTION_TYPE.ACTION, ...props }: ActionType & ActionProps,
    ref?: React.RefObject<any>
  ) => {
    const render =
      _.get(
        {
          [ACTION_TYPE.ACTION]: renderAction,
          [ACTION_TYPE.DROPDOWN_ITEM]: renderDropDownItem
        },
        type
      ) || renderAction;

    return render(props, ref);
  }
);
