// @flow
import React, { useMemo, useContext, useEffect } from 'react';
import { ReactReduxContext } from 'react-redux';
import _ from 'lodash';
import { ActionCreator, ActionCreatorsMapObject } from 'redux';
import $$ from 'immutable';
import { useForceUpdate, usePrevious } from '@/components/hooks';

let weekCache = new WeakMap();

/**
 * 解决 immutableJS toJS 操作后结果的引用发生变化，引起不必要的重新 render
 */
export const toJS = immuData => {
  let data = weekCache.get(immuData);
  if (data) {
    return data;
  }
  if ($$.isImmutable(immuData)) {
    data = immuData.toJS();
    weekCache.set(immuData, data);
  } else {
    data = immuData;
  }

  return data;
};

/**
 * 将 Redux 转换为自定义 Hooks 的形式
 */
export function useStore(path: any[] | string = ['store']) {
  let paths = path;
  if (_.isString(path)) {
    paths = ['store', path === 'src/views' ? 'root' : _.snakeCase(path.replace('src/views/', ''))];
  }

  const { store } = useContext(ReactReduxContext);
  const forceUpdate = useForceUpdate();
  const state = store.getState().getIn(paths);
  const prevState = usePrevious(state);
  useEffect(() => {
    const unsubscription = store.subscribe(() => {
      if (store.getState().getIn(paths) !== prevState) {
        forceUpdate();
      }
    });
    return () => unsubscription();
  }, [store, prevState, paths, forceUpdate]);
  return toJS(state);
}

/**
 * 将 Redux 转换为自定义 Hooks 的形式
 */
export function useActions(actionCreators: ActionCreatorsMapObject) {
  const { store } = useContext(ReactReduxContext);
  const actions = useMemo(
    () =>
      _.mapValues(actionCreators, (creator: ActionCreator<any>) => (...rest: any[]) =>
        creator(...rest)(store.dispatch, store.getState)
      ),
    [actionCreators, store.dispatch, store.getState]
  );
  return actions;
}

/**
 * 将 Redux 转换为自定义 Hooks 的形式
 */
export function useAction(actionCreator: ActionCreator<any>) {
  const { store } = useContext(ReactReduxContext);
  const action = useMemo(
    () => (...rest: any[]) => actionCreator(...rest)(store.dispatch, store.getState),
    [actionCreator, store.dispatch, store.getState]
  );
  return action;
}
