import React, { memo, useCallback, useMemo } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';
import { useHistory } from 'react-router';

import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Table, { Column, Cell, HeaderCell, Toolbar } from '@/components/Table';
import { DateTimeCell, NumberCell, FloatActionCell } from '@/components/Table/Cells';
import { defaultBrandKey, defaultAdvertiserKey } from '@/views/common/report/campaign/constants';
import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { FULL_DATE } from '@/constants/dateFormat';

export default memo(({ dateParams, tableParams, postParams, setTableParams }: BasicReportProps) => {
  const history = useHistory();
  const { tableData, onChange, onExport } = useTable({
    dateParams,
    tableParams,
    setTableParams,
    api: APIs.API_BRAND_OVERVIEW,
    postParams: {
      ...postParams,
      var: 'json'
    }
  });
  const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

  const { formatMessage } = useFmtMsgFori18n();

  const renderActions = (rowData: Record<string, any>) => {
    return (
      <>
        <FloatActionCell.Actions.Report
          onClick={() => {
            history.push({
              pathname: '/campaign-overview',
              state: {
                [defaultAdvertiserKey]: rowData.advertiserId,
                [defaultBrandKey]: rowData.id
              }
            });
          }}
        />
      </>
    );
  };

  const handleExport = useCallback(() => {
    onExport({
      ...postParams,
      var: 'xlsx'
    });
  }, [onExport, postParams]);

  const renderCols = (): React.ReactElement[] => {
    const secondary = _.get(tableParams, 'secondary');
    let cols = [
      <Column width={100} sortable resizable fixed key="id">
        <HeaderCell>ID</HeaderCell>
        <Cell dataKey="id" />
      </Column>,
      <Column width={200} sortable resizable fixed key="name">
        <HeaderCell>BE</HeaderCell>
        <FloatActionCell dataKey="BE" />
      </Column>,
      <Column width={200} sortable resizable fixed key="name">
        <HeaderCell>{formatMessage('i18n_brand')}</HeaderCell>
        <FloatActionCell dataKey="brand" renderActions={renderActions} />
      </Column>,
      <Column width={200} sortable resizable fixed key="advertiser">
        <HeaderCell>{formatMessage('i18n_advertiser')}</HeaderCell>
        <FloatActionCell dataKey="advertiser" />
      </Column>,
      secondary && (
        <Column width={150} align="right" sortable key="secondary">
          <HeaderCell>{formatMessage(`i18n_${secondary}`)}</HeaderCell>
          <DateTimeCell format={FULL_DATE} dataKey="secondary" />
        </Column>
      ),
      secondary === 'hourly' && (
        <Column width={100} key="hourly" sortable resizable>
          <HeaderCell>{formatMessage('i18n_hour')}</HeaderCell>
          <Cell dataKey="timeslot" />
        </Column>
      ),
      <Column flexGrow={1} minWidth={150} align="right" sortable key="impressions">
        <HeaderCell>{formatMessage('i18n_impressions')}</HeaderCell>
        <NumberCell dataKey="impressions" />
      </Column>,
      <Column flexGrow={1} minWidth={150} align="right" sortable key="uimpressions">
        <HeaderCell>{formatMessage('i18n_uimpressions')}</HeaderCell>
        <NumberCell dataKey="uimpressions" />
      </Column>,
      <Column flexGrow={1} minWidth={150} align="right" sortable key="clicks">
        <HeaderCell>{formatMessage('i18n_clicks')}</HeaderCell>
        <NumberCell dataKey="clicks" />
      </Column>,
      <Column flexGrow={1} minWidth={150} align="right" sortable key="uclicks">
        <HeaderCell>{formatMessage('i18n_uclicks')}</HeaderCell>
        <NumberCell dataKey="uclicks" />
      </Column>
    ];
    return cols;
  };
  return (
    <>
      <Toolbar>
        <Button className="pull-right" appearance="default" onClick={handleExport}>
          {formatMessage('i18n_exportTable')}
        </Button>
      </Toolbar>
      <Table {...tableDataMemo} onChange={onChange}>
        {renderCols()}
      </Table>
    </>
  );
});
