import _ from 'lodash';
import { TODAY } from '@/constants/config';
import { YYYYMMDD } from '@/utils/date/format';
import { allowedMonitorTypeGroup, ivtTypeData, monitorVal } from './constants';

export const mapFromResponse = (rspData: Record<string, any>) => {
  const {
    measure,
    // tencent,
    targetStartAge,
    targetEndAge,
    monitorType,
    // ivtVendor,
    // ivtType,
    // mtaVendor,
    // mtaCategory,
    primary,
    secondaries = [],
    notes,
    ...data
  } = rspData;
  return {
    ...data,
    primaryBE: _.get(primary, 'beId', ''),
    primaryRole: _.get(primary, 'role', ''),
    secondaries: secondaries.reduce(
      (acc, { beId, role: roleId }) =>
        _.isNumber(beId) && _.isNumber(roleId) ? acc.concat({ beId, roleId }) : acc,
      []
    ),
    monitorType: allowedMonitorTypeGroup[monitorType] || [],
    //   .concat(
    //   measure ? monitorVal.measure : []
    // ),
    // tencent: _.isNumber(tencent) ? (tencent < 2 ? [tencent] : [0, 1]) : [],
    // ivtVendor: _.isNull(ivtVendor) ? '' : ivtVendor,
    // ivtType: _.isEmpty(ivtType)
    //   ? ''
    //   : _.find(ivtTypeData, i => _.includes(i.name, _.toUpper(ivtType))).id,
    ageStart: targetStartAge,
    ageEnd: targetEndAge === '+' ? 0 : targetEndAge,
    // vendorId: _.isNull(mtaVendor) ? '' : mtaVendor,
    // categoryId: _.isNull(mtaCategory) ? '' : mtaCategory,
    notes: _.isNull(notes) ? '' : notes
  };
};

export const mapToRequest = (data: Record<string, any>, isEdit: boolean) => {
  // 主要拥有者
  const { primaryBE, primaryRole } = data;
  const primary = [{ beId: primaryBE, role: primaryRole }];

  // 次要拥有者
  const { secondaries: oldSecondaries } = data;
  const secondaries = _.isEmpty(oldSecondaries)
    ? isEdit
      ? []
      : undefined
    : oldSecondaries.map(({ beId, roleId: role }) => ({ beId, role }));

  // 监测类型
  const { monitorType: oldMonitorType = [] } = data;
  const checkedImp = _.includes(oldMonitorType, monitorVal.imp);
  const checkedClk = _.includes(oldMonitorType, monitorVal.clk);

  // 受众测量
  // const measure = checkedImp && _.includes(oldMonitorType, monitorVal.measure) ? 1 : 0;
  // _.remove(oldMonitorType, i => i === monitorVal.measure);

  // 勾选"广告计数"才能选择"独立广告计数"，勾选"点击"才能选择"独立点击"
  checkedImp || _.remove(oldMonitorType, i => i === monitorVal.uimp);
  checkedClk || _.remove(oldMonitorType, i => i === monitorVal.uclk);
  const monitorType = _.findIndex(
    allowedMonitorTypeGroup,
    i => i.sort().toString() === oldMonitorType.sort().toString()
  );

  // 受众累计报
  // const { tencent: oldTencent = [] } = data;
  // const tencent = (measure === 0) ? undefined : oldTencent.length === 2 ? 2 : oldTencent[0];

  // UV频次
  // const { frequency: oldFrequency } = data;
  // const frequency = oldFrequency ? 1 : 0;

  // UA频次
  // const { enableFreq: oldEnableFreq } = data;
  // const enableFreq = measure === 1 && oldEnableFreq ? 1 : 0;

  // 站外流量监测
  const { extTraffic: oldExtTraffic } = data;
  const extTraffic = oldExtTraffic ? 1 : 0;

  // IVT供应商
  // const { ivtVendor: oldIvtVendor } = data;
  // const ivtVendor = _.isEmpty(oldIvtVendor) ? undefined : oldIvtVendor;

  // 品牌标识
  // const { ivtVendorBrand: oldIvtVendorBrand } = data;
  // const ivtVendorBrand = ivtVendor ? oldIvtVendorBrand : undefined;

  // IVT类型
  // const { ivtType: oldIvtType } = data;
  // const ivtType = ivtVendor ? oldIvtType : undefined;

  // 可见度监测
  // const { viewability: oldViewability } = data;
  // const viewability = ivtType === 2 && !_.isEmpty(oldViewability) ? 1 : 0;

  // 在HAA前端中隐藏的项，后端数据中仍会存在但不接受这些项，所以提交时需要把这些项过滤掉
  const result = _.mapValues(
    {
      ..._.omit(
        data,
        's2sPlatformId',
        'vendorId',
        'userId',
        'industryId',
        'primaryId',
        'primaryRole',
        'secondaries',
        'tencent',
        'frequency',
        'enableFreq',
        'ivtVendor',
        'ivtVendorBrand',
        'ivtType',
        'viewability',
        'mtaVendor',
        'mtaCategory'
      ),
      primary,
      monitorType,
      // measure,
      // tencent,
      // frequency,
      // enableFreq,
      extTraffic
      // ivtVendor,
      // ivtVendorBrand,
      // ivtType,
      // viewability
    },
    val => (_.isArrayLike(val) && _.isEmpty(val) ? undefined : val)
  );
  return {
    ...result,
    secondaries
  };
};

export const cpoFromResponse = (rspData: any) => {
  const {
    costGroup,
    orderStartDate = TODAY,
    orderEndDate = TODAY,
    launchStartDate = TODAY,
    launchEndDate = TODAY,
    ...data
  } = rspData;
  const newGroup: any = [];
  !_.isEmpty(costGroup) &&
    Object.keys(costGroup).forEach(i => {
      newGroup.push({
        group: i,
        placement: costGroup[i]
      });
    });
  return {
    ...data,
    isAudit: data.isAudit || data.isAudit === 1 ? true : false,
    conversions:
      data.conversions && _.isString(data.conversions)
        ? data.conversions.split(',').map(i => _.toNumber(i))
        : [],
    costGroup: newGroup,
    dateLaunch: [launchStartDate, launchEndDate],
    dateOrder: [orderStartDate, orderEndDate]
  };
};

export const cpoToRequest = (formData: any) => {
  const newGroup: any = {};
  formData.costGroup.map((i: any) => {
    newGroup[i.group] = i.placement;
  });
  let audit = formData.isAudit
    ? {
      auditEmail: formData.auditEmail
    }
    : {};
  return {
    ..._.omit(formData, [
      'costGroup',
      'dateLaunch',
      'dateOrder',
      'id',
      'secondaries',
      'isAudit',
      'auditEmail',
      'conversions'
    ]),
    ...audit,
    isAudit: formData.isAudit ? 1 : 0,
    campaignId: formData.campaignId || formData.id,
    conversions:
      formData.conversions && _.isArray(formData.conversions) ? formData.conversions.join(',') : '',
    costGroup: newGroup,
    cost: _.toNumber(formData.cost),
    orderStartDate: YYYYMMDD(formData.dateOrder[0]),
    orderEndDate: YYYYMMDD(formData.dateOrder[1]),
    launchStartDate: YYYYMMDD(formData.dateLaunch[0]),
    launchEndDate: YYYYMMDD(formData.dateLaunch[1])
  };
};
