import React, { useState, useEffect, useCallback } from 'react';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { AdserverModel } from '@/constants/form/models';
import RadioInput from './RadioInput';
import useAdsConfigDataIntl from './useAdsConfigDataIntl';

export default React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();
    const adsConfigDataIntl = useAdsConfigDataIntl();

    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editAdServer' : 'i18n_newAdServer')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        className="adserver-drawer"
        size="lg"
        onSubmit={onSubmit}
        onHide={onClose}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={AdserverModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} required />

          {adsConfigDataIntl.map(({ name, label, ...fieldProps }) => (
            <CustomField
              name={name}
              label={label}
              accepter={RadioInput}
              key={name}
              checkTrigger="none"
              className="radio-input-wrapper"
              {...fieldProps}
            />
          ))}

          {/* 备注 */}
          <CustomField
            name="notes"
            label={formatMessage('i18n_remark')}
            componentClass="textarea"
            rows={2}
          />
        </Form>
      </Drawer>
    );
  }
);
