import React, { memo, useCallback } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';

import { useMainPage } from '@/components/hooks/useMainPage';
import { YYYYMMDD } from '@/utils/date';

import Table from './Table';
import Toolbar from './Toolbar';
import {
  CommonParam,
  CustomParam,
  defaultCommonParam,
  defaultCustomParam,
  filterParamKeys,
  typeData
} from './constants';

const commonParamFormatter = (filterParam: CommonParam) => {
  const originDate = filterParam[filterParamKeys.DATE];
  const originBE = filterParam[filterParamKeys.BEID];
  const originClassify = filterParam[filterParamKeys.CLASSIFY];

  return {
    ...filterParam,
    [filterParamKeys.DATE]: YYYYMMDD(originDate),
    [filterParamKeys.BEID]: originClassify === typeData[0].id ? _.join(originBE, ',') : ''
  };
};

const customParamFormatter = (filterParam: CustomParam) => {
  const originStatus = filterParam[filterParamKeys.STATUS];
  return {
    ...filterParam,
    [filterParamKeys.STATUS]: _.join(originStatus, ',')
  };
};

export default memo(() => {
  const { commonParam, handleChangeCommonParam, customParamResult } = useMainPage({
    defaultCommonParam,
    commonParamFormatter,
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultCustomParam,
        paramFormatter: customParamFormatter
      }
    ]
  });

  const [tableResult] = customParamResult;
  const {
    param,
    fmtParam,
    handleChangePagination,
    handleChangeCustomParam,
    handleSearchWord
  } = tableResult;

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  return (
    <>
      <Panel className="page-toolbar">
        <Toolbar filterParams={commonParam} handleChangeCommonParam={handleChangeCommonParam} />
      </Panel>
      <Panel className="panel-table">
        <Table
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          handleSearchWord={handleSearchWord}
          handleChangeCustomParam={handleChangeCustomParam}
          refetchTableData={refetchTableData}
        />
      </Panel>
    </>
  );
});
