import React, { memo, useCallback } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';
import { useMainPage } from '@/components/hooks/useMainPage';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { API_CAMPAIGN, API_CUSTOM_REPORT } from '@/constants/APIs';
import { CustomReportModel } from '@/constants/form/models';
import { CommonResponse } from '@/utils/request/parsingResponse';
import DrawerForm from './DrawerForm';
import Table from './Table';
import { mapFromResponse, mapToRequest } from './dataMapper';
import { defaultFilter, FilterParam, filterParamKeys, defaultFormData } from './constants';

const filterParamFormatter = (filterParam: FilterParam) => {
  return {
    ...filterParam,
    [filterParamKeys.BE]: filterParam[filterParamKeys.BE].toString()
  };
};

export default memo(() => {
  const { customParamResult } = useMainPage({
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultFilter,
        paramFormatter: filterParamFormatter
      }
    ]
  });

  const [customTableResult] = customParamResult;
  const {
    param,
    fmtParam,
    handleChangePagination,
    handleSearchWord,
    handleChangeCustomParam
  } = customTableResult;

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  const {
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    handleFormChange,
    handleFormCreate,
    handleFormClose,
    handleFormSubmit,
    setFormErrors,
    checkForm,
    setDrawer
  } = useDrawerForm({
    api: API_CUSTOM_REPORT,
    defaultFormData,
    model: CustomReportModel,
    submitSuccessCallback: refetchTableData,
    mapToRequest
  });

  const { onFetch: onFetchCampaign } = useFetch<CommonResponse>(FETCH_METHOD.GET, API_CAMPAIGN, {
    params: {
      report: 1,
      pageSize: 1000
    }
  });

  // 这里直接从table data中获取表单数据，无需再获取，
  // 但其中不包含需要所选推广计划的名称，需要另外获取
  const handleFormEdit = useCallback(
    (rowData: any) => {
      setDrawer({
        show: true,
        loading: true,
        submitting: false,
        isEdit: true
      });
      onFetchCampaign({ params: { customId: rowData.id } }).then(rsp => {
        const campaigns = _.get(rsp, 'data.records', []);
        handleFormChange(mapFromResponse(rowData, campaigns));
        setDrawer({
          show: true,
          loading: false,
          submitting: false,
          isEdit: true
        });
      });
    },
    [handleFormChange, setDrawer]
  );

  return (
    <>
      <Panel className="panel-table">
        <Table
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          handleSearchWord={handleSearchWord}
          handleChangeCustomParam={handleChangeCustomParam}
          handleFormCreate={handleFormCreate}
          handleFormEdit={handleFormEdit}
          refetchTableData={refetchTableData}
        />
        <DrawerForm
          drawer={drawer}
          formData={formData}
          originalFormData={originalFormData}
          formErrors={formErrors}
          formRef={formRef}
          onChange={handleFormChange}
          onSubmit={handleFormSubmit}
          onClose={handleFormClose}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        />
      </Panel>
    </>
  );
});
