import React, { useCallback, useState, useRef } from 'react';
import { Uploader } from 'rsuite';
import { FileType, UploaderProps } from 'rsuite/lib/Uploader';

/**
 * 非受控的组件 多次选择文件上传会在filelist保存多个文件，但是这里不需要始终有一个就行了
 * 这里需要取消之前的文件的上传，所以得 调用了rsuite内部的方法 handleRemoveFile的时候就可以取消了
 */
export const SingleFileUploader = (props: UploaderProps) => {
  const { onChange, ...rest } = props;
  const uploaderRef = useRef(null);
  const [fileList, setFileList] = useState([]);
  const handleChange = useCallback(
    (newFileList: Array<FileType>) => {
      for (let i = 0; i < newFileList.length - 1; i++) {
        uploaderRef.current.handleRemoveFile &&
          uploaderRef.current.handleRemoveFile(newFileList[i].fileKey);
      }

      const item = newFileList.slice().pop();
      setFileList(item ? [item] : []);
      onChange && onChange(newFileList);
    },
    [onChange]
  );

  const renderFileInfo = useCallback((file: FileType) => {
    return (
      <span title={file.name} className="rs-uploader-file-item-title">
        {file.name}
      </span>
    );
  }, []);

  return (
    <Uploader
      {...rest}
      ref={uploaderRef}
      fileList={fileList}
      onChange={handleChange}
      renderFileInfo={renderFileInfo}
    />
  );
};
