import React, { memo, useState, useCallback } from 'react';
import _ from 'lodash';
import CheckPicker from '@/components/Picker/CheckPicker';
import BEPicker from '@/components/Picker/logic/BEPicker';
import AdvBrandPicker from '@/components/Picker/logic/AdvBrandPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import * as APIs from '@/constants/APIs';
import { getUserProfile } from '@/utils/location';
import { BasicReportProps } from '@/constants/config';

import { filterParamKeys } from './constants';

interface Props extends Partial<BasicReportProps> {
  allCampaigns: Record<string, any>[];
  setAllCampaigns: Function;
}

const defaultFetchParam = {
  orderColumn: 'name',
  orderType: 'asc'
};

export default memo(
  ({ filterParams, handleChangeCommonParam, allCampaigns, setAllCampaigns }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { internal = true, beId } = getUserProfile();
    const [chosenBE, setChosenBE] = useState([]);
    const [chosenAdvBrand, setChosenAdvBrand] = useState([]);

    const beChangeHandler = useCallback((val: Array<any>) => {
      setChosenBE(val);
    }, []);

    const advBrandChangeHandler = useCallback((val: any) => {
      setChosenAdvBrand(val);
    }, []);

    const chosenCampaignChangeHandler = useCallback(
      (val: Array<any>) => {
        handleChangeCommonParam({
          [filterParamKeys.CAMPAIGN]: val
        });
      },
      [handleChangeCommonParam]
    );

    const campaignSuccessHandler = useCallback(
      (data: Array<any>) => {
        const originChosenCampaigns = filterParams[filterParamKeys.CAMPAIGN];
        const dataIds = data.map(d => d.id);
        const newChosenCampaigns = originChosenCampaigns.filter((i: any) => dataIds.includes(i));
        if (JSON.stringify(originChosenCampaigns) !== JSON.stringify(newChosenCampaigns)) {
          // 只有不同才重新设置，触发重新请求
          handleChangeCommonParam({
            [filterParamKeys.CAMPAIGN]: newChosenCampaigns
          });
        }
        _.isEmpty(allCampaigns) && setAllCampaigns(data);
      },
      [allCampaigns, filterParams, handleChangeCommonParam, setAllCampaigns]
    );
    return (
      <>
        <BEPicker
          chosenBE={chosenBE}
          beChangeHandler={beChangeHandler}
          appearance="subtle"
          isReport={false}
        />
        <AdvBrandPicker
          chosenAdvBrand={chosenAdvBrand}
          onChange={advBrandChangeHandler}
          appearance="subtle"
        />
        {/* FIXME:站外用户 获取到be值再渲染推广计划列表 临时解决方案之后优化picker组件的Fetch逻辑改进*/}
        {!internal && !_.isEmpty(chosenBE) && (
          <CheckPicker
            url={APIs.API_CAMPAIGN}
            deps={[chosenBE, chosenAdvBrand]}
            value={filterParams[filterParamKeys.CAMPAIGN]}
            onChange={chosenCampaignChangeHandler}
            params={{
              ...defaultFetchParam,
              advertiser: chosenAdvBrand.length ? JSON.stringify(chosenAdvBrand) : '',
              ...(internal ? { beId: chosenBE.toString() } : { beId }) // 内部用户默认可以看到选择的全部，外部用户只能看到自己的
            }}
            onFetchSuccess={campaignSuccessHandler}
            appearance="subtle"
            prepend={formatMessage('i18n_campaign')}
            style={{ maxWidth: '300px' }}
            menuStyle={{ maxWidth: '400px' }}
            placeholder={formatMessage('i18n_select', { name: '' })}
          />
        )}
        {internal && (
          <CheckPicker
            url={APIs.API_CAMPAIGN}
            deps={[chosenBE, chosenAdvBrand]}
            value={filterParams[filterParamKeys.CAMPAIGN]}
            onChange={chosenCampaignChangeHandler}
            params={{
              ...defaultFetchParam,
              advertiser: chosenAdvBrand.length ? JSON.stringify(chosenAdvBrand) : '',
              ...(internal ? { beId: chosenBE.toString() } : { beId }) // 内部用户默认可以看到选择的全部，外部用户只能看到自己的
            }}
            onFetchSuccess={campaignSuccessHandler}
            appearance="subtle"
            prepend={formatMessage('i18n_campaign')}
            style={{ maxWidth: '300px' }}
            menuStyle={{ maxWidth: '400px' }}
            placeholder={formatMessage('i18n_select', { name: '' })}
          />
        )}
      </>
    );
  }
);
