import React, { memo, useCallback, useMemo, useState } from 'react';
import _ from 'lodash';
import { Button, Checkbox } from 'rsuite';

import * as APIs from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { BasicReportProps } from '@/constants/config';
import { AdvFilterTrigger, AdvFilterUncontrolled } from '@/components/AdvancedFilter';
import { updateStatus } from '@/utils/session';

import { demoFilters, filters, primaryData, frequencyData, TableParam } from './constants';
import { segData } from '../constants';

const footerStyles = {
  padding: '10px 2px',
  borderTop: '1px solid #e5e5e5'
};
const mockData: CustomTableDataProps<CellDataProps> = {
  data: [
    {
      name: '55岁以上男性',
      basePeople: '24194051',
      peopleRate: '3.31%',
      uimpressions: 78640,
      impressions: 212350,
      impressionsRate: '1.75%',
      hitRate: '0.33%',
      meanFrequency: '2.70',
      GRP: '0.88'
    }
  ],
  page: { current: 0, total: 21, size: 100, pagesize: 100 },
  total: {
    name: 0,
    basePeople: 0,
    peopleRate: 0,
    uimpressions: 0,
    impressions: 0,
    impressionsRate: 0,
    hitRate: 0,
    meanFrequency: 0,
    GRP: 0
  }
};

interface CellDataProps {
  name: string;
  basePeople: string | number;
  peopleRate: string;
  uimpressions: number;
  impressions: number;
  impressionsRate: string;
  hitRate: string;
  meanFrequency: string;
  GRP: string;
}

type TableProps = BasicReportProps<any, TableParam> & {
  campaignId: string;
  campaignType: 'campaign' | 'crossCampaign';
};

export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    setTableParams,
    handleChangeCustomParam,
    refetchTableData,
    campaignId,
    campaignType
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: APIs.API_TAB_REPORT,
      postParams
    });

    const tableDataMemo = useMemo(() => extractTableData(mockData), [tableData]);

    const [filterShow, setFilterShow] = useState(false);
    const [submitLoading, setSubmitLoading] = useState(false);
    const handleCloseFilter = useCallback(() => {
      setFilterShow(false);
    }, []);

    const handleShowFilter = useCallback(() => {
      setFilterShow(true);
    }, []);

    const handleSubmitFilter = useCallback(
      val => {
        setSubmitLoading(true);
        updateStatus({
          advanceFilter: val,
          objId: campaignId,
          hierarchy: campaignType,
          report: 'audiences'
        }).finally(() => {
          setFilterShow(false);
          refetchTableData();
          setSubmitLoading(false);
        });
      },
      [refetchTableData, campaignId]
    );

    // 导出
    const handleExportClicked = useCallback(() => {
      onExport && onExport(postParams, APIs.API_XLSX);
    }, [onExport, postParams]);

    // 国际化下拉data
    const primaryDataIntl = usePickerDataFromObj(primaryData);
    const segDataIntl = usePickerDataFromObj(segData);

    const handleChangeFre = useCallback(
      (value: any) => {
        handleChangeCustomParam({
          frequency: value
        });
      },
      [handleChangeCustomParam]
    );

    const handleFreAllChange = useCallback(
      (val, checked) => {
        handleChangeCustomParam({
          frequency: !checked ? [] : frequencyData.map(f => f.id)
        });
      },
      [handleChangeCustomParam]
    );

    const handleChangeMDimension = useCallback(
      val => {
        handleChangeCustomParam({
          mDimension: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleChangeSecondary = useCallback(
      val => {
        handleChangeCustomParam({
          secondary: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleChangeSegmentation = useCallback(
      val => {
        handleChangeCustomParam({
          segmentation: val
        });
      },
      [handleChangeCustomParam]
    );

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SelectPicker
          key="mDimension"
          prepend={formatMessage('i18n_primary')}
          data={primaryDataIntl}
          value={filterParams.mDimension}
          onChange={handleChangeMDimension}
        />,
        <SecondaryPicker
          key="secondary"
          onChange={handleChangeSecondary}
          value={filterParams.secondary}
          data={['weekly', 'total']}
          cleanable={false}
        />,
        <SelectPicker
          key="segmentation"
          prepend={formatMessage('i18n_segmentation')}
          data={segDataIntl}
          value={filterParams.segmentation}
          onChange={handleChangeSegmentation}
        />,
        <CheckPicker
          key="frequency"
          prepend={formatMessage('i18n_frequency')}
          data={frequencyData}
          searchable={false}
          value={filterParams.frequency}
          height={150}
          menuStyle={{
            minWidth: '100px'
          }}
          onChange={handleChangeFre}
          renderExtraFooter={() => {
            const valLen = _.get(filterParams, 'frequency.length', 0);
            return (
              <div style={footerStyles}>
                <Checkbox
                  inline
                  indeterminate={valLen > 0 && valLen !== frequencyData.length}
                  checked={valLen === frequencyData.length}
                  onChange={handleFreAllChange}
                >
                  {formatMessage('i18n_selectAll')}
                </Checkbox>
              </div>
            );
          }}
        />,
        <AdvFilterTrigger key="adv-filter" onClick={handleShowFilter} />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };

    // 渲染频次
    const renderFrequency = (): React.ReactElement[] => {
      let cols: React.ReactElement[] = [];
      _.forEach(_.get(filterParams, 'frequency', []) || [], (item: any) => {
        let ukey = item === 10 ? 'u10+' : `u${item}`;
        let ikey = item === 10 ? 'i10+' : `i${item}`;
        const title = item === 10 ? '10+' : item;
        cols.push(
          <Column width={150} align="right" resizable sortable key={ikey}>
            <HeaderCell>{formatMessage('i18n_adFreq', { title })}</HeaderCell>
            <NumberCell dataKey={ikey} />
          </Column>
        );
        cols.push(
          <Column width={120} align="right" resizable sortable key={ukey}>
            <HeaderCell>{formatMessage('i18n_audienceFreq', { title })}</HeaderCell>
            <NumberCell dataKey={ukey} />
          </Column>
        );
      });
      return cols;
    };

    // 渲染表列
    const renderCol = (): React.ReactElement[] => {
      // 判断高级过滤是否有站点
      const placement = _.filter(filterParams.advanceFilter, { key: 'platform' })[0];
      const showPlacement = !_.isEmpty(_.get(placement, 'value'));
      // 是否显示人口特征
      const demo = _.filter(filterParams.advanceFilter, { key: 'demo' })[0];
      const showDemo = !!(filterParams.mDimension === 'city' && !_.isEmpty(_.get(demo, 'value')));

      const cols = [
        showPlacement && (
          <>
            <Column width={120} resizable key="site">
              <HeaderCell>{`${formatMessage('i18n_placement')}ID`}</HeaderCell>
              <Cell dataKey="placementId" />
            </Column>
            ,
            <Column width={120} resizable key="placementName">
              <HeaderCell>{formatMessage('i18n_placement')}</HeaderCell>
              <Cell dataKey="placementName" />
            </Column>
          </>
        ),
        <Column width={150} resizable key="name">
          <HeaderCell>
            {filterParams.mDimension === 'demo'
              ? formatMessage('i18n_feature')
              : formatMessage('i18n_area3')}
          </HeaderCell>
          <Cell dataKey="name" />
        </Column>,
        showDemo && (
          <Column width={120} resizable sortable key="demo">
            <HeaderCell>{formatMessage('i18n_feature')}</HeaderCell>
            <Cell dataKey="demo" />
          </Column>
        ),
        <Column width={120} align="right" resizable sortable key="basePeople">
          <HeaderCell>{formatMessage('i18n_basePeople')}</HeaderCell>
          <NumberCell dataKey="basePeople" />
        </Column>,
        <Column width={150} align="right" resizable sortable key="peopleRate">
          <HeaderCell>{formatMessage('i18n_peopleRate')} </HeaderCell>
          <Cell dataKey="peopleRate" />
        </Column>,
        filterParams.segmentation === 'platform' && (
          <Column width={100} resizable sortable key="segmentationId">
            <HeaderCell>{formatMessage('i18n_platform')}</HeaderCell>
            <Cell dataKey="segmentationId" />
          </Column>
        ),
        <Column width={120} align="right" resizable sortable key="uimpressions">
          <HeaderCell>{formatMessage('i18n_audience')}</HeaderCell>
          <NumberCell dataKey="uimpressions" />
        </Column>,
        <Column width={120} align="right" resizable sortable key="impressions">
          <HeaderCell>{formatMessage('i18n_impressions')}</HeaderCell>
          <NumberCell dataKey="impressions" />
        </Column>,
        <Column width={120} align="right" resizable sortable key="impressionsRate">
          <HeaderCell>{`${formatMessage('i18n_impressions')}%`}</HeaderCell>
          <Cell dataKey="impressionsRate" />
        </Column>,
        <Column width={150} align="right" resizable sortable key="hitRate">
          <HeaderCell>{formatMessage('i18n_hitRate')}</HeaderCell>
          <Cell dataKey="hitRate" />
        </Column>,
        <Column width={120} align="right" resizable sortable key="meanFrequency">
          <HeaderCell>{formatMessage('i18n_meanFrequency')}</HeaderCell>
          <Cell dataKey="meanFrequency" />
        </Column>,
        <Column width={100} align="right" resizable sortable key="GRP">
          <HeaderCell>GRP</HeaderCell>
          <Cell dataKey="GRP" />
        </Column>
      ];
      return _.concat(cols, renderFrequency());
    };

    return (
      <>
        <AdvFilterUncontrolled
          filters={filterParams.mDimension === 'demo' ? filters : demoFilters}
          onSubmit={handleSubmitFilter}
          onClose={handleCloseFilter}
          campaignId={campaignId}
          drawerShow={filterShow}
          submitLoading={submitLoading}
        />
        <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
          {renderCol()}
        </Table>
      </>
    );
  }
);
