import React, { memo, useMemo, useCallback, useContext, useState } from 'react';
import { Button, Panel } from 'rsuite';
import _ from 'lodash';

import * as APIs from '@/constants/APIs';

import Table, { Column, HeaderCell } from '@/components/Table';
import { DateTimeCell, FloatActionCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps } from '@/constants/config';
import useInterval from '@/components/hooks/useInterval';
import fetch, { FETCH_METHOD } from '@/utils/request/fetch';

import SampleOtherCell from './part/SampleOtherCell';
import { filterParamKeys, sampleTypeData, defaultFilter, SamplingReducerType } from './constants';
import { SampleCtx } from './context';
import Toolbar from './Toolbar';

const POLLING_INTERVAL = 1000 * 30;
export const extractDimensionId = (dimensionId: string) => _.last(_.split(dimensionId, '+'));

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    handleChangeCustomParam,
    refetchTableData
  }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { state, dispatch } = useContext(SampleCtx);
    const [submitting, setSubmitting] = useState(false);
    const requestChecker = useCallback(() => {
      return state.polling;
    }, [state.polling]);

    const { tableData, onChange } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_SAMPLE,
      requestChecker
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    useInterval(
      () => {
        refetchTableData();
      },
      state.polling ? POLLING_INTERVAL : null,
      true
    );

    const handleSampleTypeChange = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.DIMENSION]: val
        });
      },
      [handleChangeCustomParam]
    );

    const startSampling = useCallback(() => {
      setSubmitting(true);
      const { dimension, dimensionId } = filterParams;
      fetch(APIs.API_SAMPLE, {
        method: FETCH_METHOD.POST,
        data: {
          dimension,
          dimensionId: extractDimensionId(dimensionId)
        }
      })
        .then(() => {
          dispatch({ type: SamplingReducerType.SET_POLLING_STATUS, pollingStatus: true });
        })
        .finally(() => {
          setSubmitting(false);
        });
    }, [filterParams, dispatch]);

    const stopPolling = useCallback(() => {
      dispatch({ type: SamplingReducerType.SET_POLLING_STATUS, pollingStatus: false });
    }, [dispatch]);

    const renderToolbar = () => {
      return (
        <>
          <Button
            onClick={state.polling ? stopPolling : startSampling}
            loading={submitting}
            appearance="primary"
            disabled={!state.sampleDimensionId}
          >
            {formatMessage(state.polling ? 'i18n_endSample' : 'i18n_startSample')}
          </Button>
          <span className="sample-info">
            {state.polling
              ? formatMessage('i18n_sampleTiping', { 0: _.get(tableDataMemo, 'data.length', 0) })
              : formatMessage('i18n_sampleTip')}
          </span>
        </>
      );
    };

    return (
      <>
        <Panel className="page-toolbar">
          <Toolbar
            filterParams={filterParams}
            handleChangeCustomParam={handleChangeCustomParam}
            polling={state.polling}
          />
        </Panel>
        <Panel className="panel-table">
          <Table
            {...tableDataMemo}
            renderToolbar={renderToolbar}
            onChange={onChange}
            withPagination={false}
            wordWrap
            wrapperClassNames="sample-table"
          >
            <Column flexGrow={1} minWidth={200} sortable>
              <HeaderCell>{formatMessage('i18n_timeStamp')}</HeaderCell>
              <DateTimeCell dataKey="timestamp" />
            </Column>

            <Column flexGrow={1} minWidth={200} sortable>
              <HeaderCell>query_string</HeaderCell>
              <FloatActionCell dataKey="query_string" />
            </Column>

            <Column flexGrow={1} minWidth={200} sortable>
              <HeaderCell>referer</HeaderCell>
              <FloatActionCell dataKey="referer" />
            </Column>

            <Column flexGrow={1} minWidth={200} sortable>
              <HeaderCell>ip</HeaderCell>
              <FloatActionCell dataKey="ip" />
            </Column>

            <Column flexGrow={2} minWidth={200} sortable>
              <HeaderCell>{formatMessage('i18n_other')}</HeaderCell>
              <SampleOtherCell dataKey="cookie" />
            </Column>
          </Table>
        </Panel>
      </>
    );
  }
);
