import _ from 'lodash';

const ENV_SESSION_STORAGE_KEY = 'ENV';
export function setEnvToSession(env: object) {
  sessionStorage.setItem(ENV_SESSION_STORAGE_KEY, encodeURIComponent(JSON.stringify(env)));
}

export function getEnvFromSession(key?: string) {
  const env = JSON.parse(decodeURIComponent(sessionStorage.getItem(ENV_SESSION_STORAGE_KEY))) || {};
  if (_.isEmpty(key)) {
    return env;
  } else {
    return env[key];
  }
}
