import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';
import { API_CONVERSION_REPORT } from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell, DateCell, TextCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps, secondaryData } from '@/constants/config';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SelectPicker from '@/components/Picker/SelectPicker';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';

import { FilterParam, segData, hierarchy, reportKeys, filterParamKeys } from '../constants';
import { primaryData } from './constants';
import { PathCell } from './part/PathCell';
import { PathHeaderCell } from './part/PathHeaderCell';

const mockData: any = {
  data: [
    {
      attributionTotal: 3,
      revenueTotal: 1.0,
      trackpath: [
        { type: 'i', track: 'sd', count: '1', item: 'wbctest2' },
        { type: 'c', track: 'sd', count: '3', item: 'test22' },
        { type: 'i', track: 'sd', count: '1', item: 'test' },
        { type: 'c', track: '', count: '1', item: '' },
        { type: 'i', track: '', count: '2', item: '' },
        { type: 'c', track: '', count: '1', item: '' }
      ]
    },
    {
      attributionTotal: 3,
      revenueTotal: 1.0,
      trackpath: [
        { type: 'c', track: 'sd', count: '1', item: 'T-cmpB-状态2-控制文件测试请勿修改' },
        {
          type: 'c',
          track: 'sd',
          count: '1',
          item: 'T-cmpD-状态4(数据处理结束日期未到)-控制文件测试请勿修改'
        },
        { type: 'c', track: 'sd', count: '1', item: '”,T-cmpA-状态1-控制文件测试请勿修改”,' },
        { type: 'i', track: 'sd', count: '1', item: 'T-cmpE-状态0-控制文件测试请勿修改' },
        { type: 'c', track: 'sd', count: '2', item: 'wbctest1' },
        { type: 'i', track: 'sd', count: '1', item: 'wbctest2' },
        { type: 'i', track: 'sd', count: '1', item: 'T-cmpE-状态0-控制文件测试请勿修改' },
        { type: 'c', track: 'sd', count: '2', item: 'wbctest1' },
        { type: 'i', track: 'sd', count: '1', item: 'wbctest2' }
      ]
    }
  ],
  page: {
    current: 0,
    pagesize: 10,
    total: 6,
    size: 100
  },
  total: {
    impressions: 140,
    uimpressions: 70,
    clicks: 70,
    uclicks: '--'
  }
};
interface CellDataProps {
  secondary: string;
  value: string;
  placementId: number;
  impressions: string | number;
  clicks: string | number;
  uimpressions: string | number;
  uclicks: string | number;
  clickRate: number;
  placementName: string;
}

export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    setTableParams,
    handleSearchWord,
    handleChangeCommonParam
  }: BasicReportProps<FilterParam>) => {
    const { formatMessage } = useFmtMsgFori18n();

    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: API_CONVERSION_REPORT,
      postParams: {
        ...postParams,
        var: 'json'
      }
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport(
          {
            ...postParams,
            var: 'xlsx'
          },
          API_CONVERSION_REPORT
        );
    }, [onExport, postParams]);

    const primaryDataIntl = usePickerDataFromObj(primaryData);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SelectPicker
          key={filterParamKeys.MDIMENSION}
          prepend={formatMessage('i18n_primary')}
          data={primaryDataIntl}
          value={filterParams.mDimension}
          onChange={(value: string) =>
            handleChangeCommonParam({ [filterParamKeys.MDIMENSION]: value })
          }
        />,
        <SecondaryPicker
          key={filterParamKeys.SEC}
          data={[null, 'daily', 'weekly']}
          value={filterParams[filterParamKeys.SEC]}
          onChange={(value: string) => handleChangeCommonParam({ [filterParamKeys.SEC]: value })}
          cleanable={false}
        />,
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };
    // 渲染表列
    const renderCol = (): React.ReactElement[] => {
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams[filterParamKeys.SEC])[0],
        'label'
      );
      let pathNamePrefix = '';
      switch (filterParams.mDimension) {
        case 'site':
          pathNamePrefix = formatMessage('i18n_trackPath');
          break;
        case 'campaign':
          pathNamePrefix = formatMessage('i18n_campaign');
          break;
        case 'placement':
          pathNamePrefix = formatMessage('i18n_menuAdunitManage');
          break;
        default:
          pathNamePrefix = '';
          break;
      }

      return [
        <Column width={700} fixed resizable key="path">
          <HeaderCell>
            {`${pathNamePrefix}${formatMessage('i18n_path')}`}
            <PathHeaderCell />
          </HeaderCell>
          <PathCell dataKey="trackpath" />
        </Column>,
        <Column width={150} sortable resizable fixed key="be">
          <HeaderCell>{formatMessage('i18n_belongBE')}</HeaderCell>
          <TextCell dataKey="BE" maxLine={1} />
        </Column>,
        filterParams[filterParamKeys.SEC] && (
          <Column
            width={filterParams[filterParamKeys.SEC] === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams[filterParamKeys.SEC] === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        filterParams[filterParamKeys.SEC] === 'hourly' && (
          <Column width={100} key="hourly" sortable resizable>
            <HeaderCell>{formatMessage('i18n_hour')}</HeaderCell>
            <Cell dataKey="timeslot" />
          </Column>
        ),
        <Column width={150} align="right" sortable key="conversions">
          <HeaderCell>{formatMessage('i18n_conversions')}</HeaderCell>
          <NumberCell dataKey="attributionTotal" />
        </Column>,
        <Column width={150} align="right" sortable key="conversionRevenue">
          <HeaderCell>{formatMessage('i18n_conversionRevenue')}</HeaderCell>
          <NumberCell dataKey="revenueTotal" />
        </Column>
      ];
    };

    return (
      <Table
        {...tableDataMemo}
        renderToolbar={renderToolbar}
        onChange={onChange}
        wrapperClassNames="coversion-table"
      >
        {renderCol()}
      </Table>
    );
  }
);
