import React, { memo } from 'react';
import RsEcharts from '@rsuite/charts/lib/ECharts';
import 'echarts/map/js/china';
import 'echarts/map/js/world';
import _ from 'lodash';
import { ChartProps } from './type';

export default memo(
  ({ locale, option = {}, loading, height }: ChartProps & BaseObject<any>) => {
    return <RsEcharts option={option} loading={loading} height={height} locale={locale} />;
  },
  (prevProps, nextProps) => _.isEqual(prevProps, nextProps)
);
