import React, { memo, useCallback, useRef, useMemo } from 'react';
import { Button, Divider, Alert } from 'rsuite';
import _ from 'lodash';

import * as APIs from '@/constants/APIs';
import { QUICK_DEV, BasicReportProps } from '@/constants/config';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import Table, { Column, HeaderCell, tableUtils } from '@/components/Table';
import { FloatActionCell, DateCell, NumberCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import CapitalizeCell from '@/components/Table/Cells/CapitalizeCell';
import { useModal } from '@/components/hooks/useModal';
import { FETCH_METHOD } from '@/utils/request/fetch';

import { filterParamKeys, statusData, secondaryData, utilsFn } from './constants';
import DQACell from './part/DQACell';
import dqaData from './mock/dqaData';
import Modal from './Modal';

/**
 * HAA暂时隐藏腾讯相关指标
 * 广告计数(排除GIVT)
 * 腾讯广告计数
 * 腾讯可识别广告计数
 * MCH广告计数
 * MCH UA
 */

const handleFetchAcc = (resp: any) => {
  const status = _.get(resp, 'status', 0);
  const message = _.get(resp, 'message', '');
  if (status === 7) {
    Alert.warning(message);
  }
};

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    handleSearchWord,
    handleChangeCustomParam,
    refetchTableData
  }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const secondaryDataIntl = usePickerDataFromObj(secondaryData);
    const isCross = utilsFn.getIsCross(filterParams);
    const isDaily = utilsFn.getIsDaily(filterParams);
    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_DQA,
      postParams: {
        var: 'json'
      },
      onFetchSuccess: handleFetchAcc
    });

    const tableDataMemo = useMemo(
      () => ({
        ...extractTableData(tableData),
        ...(QUICK_DEV && {
          // mock数据
          data: dqaData.records,
          page: {
            current: dqaData.page.current,
            total: dqaData.page.total,
            size: dqaData.page.size
          },
          loading: false,
          sortColumn: '',
          sortType: 'desc' as any
        })
      }),
      [tableData]
    );
    const updateModal = useModal(false, {}, APIs.API_DQA_PUT, refetchTableData, FETCH_METHOD.POST);

    const tableRef = useRef(null);
    const handleStatusChanged = useCallback(
      (status: Array<number>) => {
        handleChangeCustomParam({
          [filterParamKeys.STATUS]: status
        });
      },
      [handleChangeCustomParam]
    );

    const timeDimensionChanged = useCallback(
      (timeVal: string) => {
        handleChangeCustomParam({
          [filterParamKeys.TIME_DIMENSION]: timeVal
        });
      },
      [handleChangeCustomParam]
    );

    const handleExport = useCallback(() => {
      onExport &&
        onExport({
          var: 'xlsx'
        });
    }, [onExport]);

    const holdHandler = useCallback(() => {
      const checkedValues = tableUtils(tableRef).getCheckValues();
      if (!checkedValues.length) {
        Alert.warning(formatMessage('i18n_selCampaignWarning'));
      } else {
        updateModal.showModal({
          date: utilsFn.getDate(filterParams),
          classify: utilsFn.getClassifyValue(filterParams),
          ids: checkedValues.toString(),
          secondary: utilsFn.getTimeDimensionValue(filterParams),
          status: 2
        });
      }
    }, [formatMessage, updateModal, filterParams]);

    const releaseHandler = useCallback(() => {
      const checkedValues = tableUtils(tableRef).getCheckValues();
      if (!checkedValues.length) {
        Alert.warning(formatMessage('i18n_selCampaignWarning'));
      } else {
        updateModal.showModal({
          date: utilsFn.getDate(filterParams),
          classify: utilsFn.getClassifyValue(filterParams),
          ids: checkedValues.toString(),
          secondary: utilsFn.getTimeDimensionValue(filterParams),
          status: 0
        });
      }
    }, [formatMessage, updateModal, filterParams]);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <React.Fragment key="btn">
          <Button onClick={holdHandler}>Hold</Button>
          <Button onClick={releaseHandler}>Release</Button>
        </React.Fragment>,
        <Divider vertical key="divider" />,
        <CheckPicker
          key="status"
          prepend={formatMessage('i18n_status')}
          data={statusData}
          value={utilsFn.getStatus(filterParams)}
          onChange={handleStatusChanged}
          cleanable={false}
          supportAll
        />,
        <SelectPicker
          key="timerDimension"
          prepend={formatMessage('i18n_timerDimension')}
          data={secondaryDataIntl}
          value={utilsFn.getTimeDimensionValue(filterParams)}
          onChange={timeDimensionChanged}
        />,
        <Button key="exportTable" className="pull-right" onClick={handleExport}>
          {formatMessage('i18n_exportTable')}
        </Button>,
        <SearchInput
          key="search"
          wrapperClassName="pull-right"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />
      ];
    };

    return (
      <>
        <Modal {...updateModal} />
        <Table
          {...tableDataMemo}
          checkDataKey="id"
          innerRef={tableRef}
          renderToolbar={renderToolbar}
          onChange={onChange}
        >
          <Column width={150} sortable fixed>
            <HeaderCell>
              {formatMessage(isCross ? 'i18n_crossCampaignId' : 'i18n_campaignId')}
            </HeaderCell>
            <FloatActionCell dataKey="id" />
          </Column>

          <Column width={200} resizable sortable fixed>
            <HeaderCell>
              {formatMessage(isCross ? 'i18n_crossCamName' : 'i18n_campaignName')}
            </HeaderCell>
            <FloatActionCell dataKey="name" />
          </Column>

          {!isCross && (
            <Column width={120}>
              <HeaderCell>{formatMessage('i18n_isExternal')}</HeaderCell>
              <FloatActionCell dataKey="isExternal" />
            </Column>
          )}

          <Column width={100} sortable>
            <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
            <CapitalizeCell dataKey="status" />
          </Column>

          {!isCross && (
            <Column width={200} sortable>
              <HeaderCell>BE</HeaderCell>
              <FloatActionCell dataKey="be" />
            </Column>
          )}

          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
            <DateCell dataKey="startDate" />
          </Column>

          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
            <DateCell dataKey="endDate" />
          </Column>

          <Column width={300} sortable resizable>
            <HeaderCell>{isDaily ? 'Yesterday' : 'Last Week'} Failed Hold Checks</HeaderCell>
            <DQACell dataKey="yesterdayFailed" />
          </Column>

          <Column width={300} sortable resizable>
            <HeaderCell>{isDaily ? 'Today' : 'This Week'} Failed Hold Checks</HeaderCell>
            <DQACell dataKey="todayFailed" clickable />
          </Column>

          <Column width={300} sortable resizable>
            <HeaderCell>Remaining Failed Hold Checks</HeaderCell>
            <DQACell dataKey="remainingFailed" clickable />
          </Column>

          {/* <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_impNoGivt')}</HeaderCell>
          <NumberCell dataKey='imp' />
        </Column>

        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_tencentImp')}</HeaderCell>
          <NumberCell dataKey='tencentImp' />
        </Column>

        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_tencentIdyImpRate')}</HeaderCell>
          <FloatActionCell dataKey='tencentIdyImpRate' />
        </Column>

        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_tencentDemoImp')}</HeaderCell>
          <NumberCell dataKey='tencentDemoImp' />
        </Column>

        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_tencentDemoUA')}</HeaderCell>
          <NumberCell dataKey='tencentDemoUA' />
        </Column> */}
        </Table>
      </>
    );
  }
);
