import { useEffect, useCallback, useState } from 'react';
import _ from 'lodash';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import * as APIs from '@/constants/APIs';
import { CommonResponse } from '@/utils/request/parsingResponse';
import useUniKey from '@/components/hooks/useUniKey';
import { TIME } from '@/constants/config';

interface Props {
  id: number | string;
  report: string; // 获取时间报表类型
}
const mockData: TIME = {
  start: '2018-11-28',
  end: '2020-01-14',
  maxDate: '2020-01-14'
};
interface Ret {
  reportDate: TIME;
  loading: boolean;
  handRefresh: Function;
  setReportDate: React.Dispatch<React.SetStateAction<TIME>>;
}

export default function useFetchReportDate({ id, report }: Props): Ret {
  const { loading, onFetch } = useFetch<CommonResponse>(FETCH_METHOD.GET, APIs.API_REPORT_DATE, {
    cacheParams: true,
    params: {
      id,
      report
    }
  });
  const [reportDate, setReportDate] = useState<TIME>({});
  const [updateKey, newUpdateKey] = useUniKey();

  useEffect(() => {
    onFetch().then(rsp => {
      if (!_.isEmpty(rsp)) {
        setReportDate({
          start: _.get(rsp, 'data.start_date'),
          end: _.get(rsp, 'data.data_date'),
          maxDate: _.get(rsp, 'data.end_date')
        });
      } else {
        setReportDate(mockData);
      }
    });
  }, [report, id, updateKey]);

  // 刷新
  const handRefresh = useCallback(() => {
    newUpdateKey();
  }, [newUpdateKey]);
  // // 设置时间
  // const handSetDate = useCallback((date: TIME) => {
  //   setReportDate(date);
  // }, []);
  return {
    loading,
    reportDate,
    handRefresh,
    setReportDate
  };
}
