import _ from 'lodash';
import React, { useCallback } from 'react';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from './FloatActionCell';

export interface TextCellProps<T> extends BaseCellProps<T> {
  maxLine?: number;
  formatter?: (data?: any, rowData?: any) => any;
}

const TextCell = ({
  rowData,
  dataKey,
  maxLine = 3,
  renderActions,
  formatter,
  ...props
}: TextCellProps<any>) => {
  const text = _.get(rowData, dataKey, DEFAULT_TABLE_CELL_CONTENT);
  const child = (
    <>
      {(_.isFunction(formatter) && formatter(text, rowData)) ||
        (_.isString(text) && text) ||
        DEFAULT_TABLE_CELL_CONTENT}
    </>
  );

  const renderContent = useCallback(() => {
    return (
      <div
        style={{
          overflow: 'hidden',
          display: '-webkit-box',
          WebkitBoxOrient: 'vertical',
          WebkitLineClamp: maxLine,
          whiteSpace: 'initial'
        }}
      >
        {child}
      </div>
    );
  }, [child, maxLine]);
  const renderTitle = useCallback(() => {
    return <>{child}</>;
  }, [child]);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderContent={renderContent}
      renderTitle={renderTitle}
      renderActions={renderActions}
      {...props}
    />
  );
};

export default React.memo(TextCell);
