import React, { memo, useState, useCallback } from 'react';
import _ from 'lodash';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import BEPicker from '@/components/Picker/logic/BEPicker';

import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { API_CONVERSIONGROUP } from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import { defaultFilter, filterParamKeys, FilterParam } from './constants';

interface Props extends Partial<BasicReportProps<FilterParam>> {
  userId: number | string;
}

export default memo(({ filterParams, handleChangeCommonParam, userId }: Props) => {
  const { formatMessage } = useFmtMsgFori18n();
  // BE筛选
  const handleBEChange = useCallback(
    (beVal: Array<any>) => {
      handleChangeCommonParam({ [filterParamKeys.BE]: beVal, [filterParamKeys.GROUP]: [] });
    },
    [handleChangeCommonParam]
  );

  // 活动组筛选
  const handleGroupChange = useCallback(
    (val: any) => {
      handleChangeCommonParam({ [filterParamKeys.GROUP]: val });
    },
    [handleChangeCommonParam]
  );

  return (
    <>
      <BEPicker
        key="be"
        chosenBE={filterParams[filterParamKeys.BE]}
        beChangeHandler={handleBEChange}
        appearance="subtle"
      />
      <CheckPicker
        key="group"
        cleanable
        searchable
        supportAll
        prepend={formatMessage('i18n_conversiongroup')}
        groupBy="beName"
        url={API_CONVERSIONGROUP}
        params={{
          userId,
          beId: filterParams[filterParamKeys.BE]
            ? filterParams[filterParamKeys.BE].toString()
            : null
        }}
        deps={[filterParams[filterParamKeys.BE]]}
        value={filterParams[filterParamKeys.GROUP]}
        onChange={handleGroupChange}
        appearance="subtle"
        style={{ maxWidth: '300px' }}
        menuStyle={{ maxWidth: '400px', minWidth: '224px' }}
      />
    </>
  );
});
