import _ from 'lodash';
import { YYYYMMDD } from '@/utils/date';
import { webMonitorConnectData } from './constants';

export const mapFromResponse = (rspData: any) => {
  return {
    ..._.pick(rspData, [
      'id',
      'campaignId',
      'name',
      'status',
      'selfStatus',
      'canEdit',
      'targetDevice',
      'buyMethod',
      'frequencyCap',
      'targetGeo',
      'note',
      'asyncTracker',
      'thumbnail',
      'thumbnailUrl'
    ]),
    startDate: new Date(rspData.startDate),
    endDate: new Date(rspData.endDate),
    clkUrlDSK: _.get(rspData, 'unique_field.clickUrls.DSK') || '',
    clkUrlMbl: _.get(rspData, 'unique_field.clickUrls.MBL') || '',
    clkUrlOtt: _.get(rspData, 'unique_field.clickUrls.OTT') || '',
    estImp: _.get(rspData, 'unique_field.est_exposure') || '',
    estClick: _.get(rspData, 'unique_field.est_click') || '',
    adserverId: _.get(rspData, 'adserverId') || '',
    siteId: _.get(rspData, 'siteId') || '',
    creative: _.get(rspData, 'creative') || '',
    creativeType: _.get(rspData, 'creativeType') || '',
    landingChannel: _.get(rspData, 'landingChannel') || '',
    position: _.get(rspData, 'position') || '',
    program: _.get(rspData, 'program') || '',
    remark: _.get(rspData, 'remark') || '',
    platformType: _.get(rspData, 'platformType')
      .split(',')
      .filter(i => i !== ''),
    webMonitorConnect: webMonitorConnectData.reduce(
      (acc, i) => (_.get(rspData, ['unique_field', i.key]) === 0 ? [...acc, i.id] : acc),
      []
    ),
    impUrl: _.get(rspData, 'unique_field.impUrl') || '',
    attributeGroup: _.toPairs(_.get(rspData, 'attributeGroup', {}))
      .map(([key, val]) => `${key}=${val}`)
      .join('&')
  };
};

export const mapToRequest = (formData: Record<string, any>) => {
  const clkUrls = ['clkUrlDSK', 'clkUrlMbl', 'clkUrlOtt'];
  return _.mapValues(
    {
      ..._.omit(formData, ['webMonitorConnect', 'attributeGroup', 'thumbnailUrl', ...clkUrls]),
      platformType: _.get(formData, 'platformType', []).join(','),
      startDate: YYYYMMDD(formData.startDate),
      endDate: YYYYMMDD(formData.endDate),
      trackType: 'common',
      ...webMonitorConnectData.reduce(
        (acc, i) => ({
          ...acc,
          [i.key]: _.get(formData, 'webMonitorConnect', []).includes(i.id) ? 0 : 1
        }),
        {}
      ),
      ...clkUrls.reduce(
        (acc, i) => ({
          ...acc,
          [i]: _.isEmpty(_.get(formData, i)) ? undefined : _.get(formData, i)
        }),
        {}
      )
    },
    (val, key) =>
      (key !== 'thumbnail' && _.isString(val) && _.isEmpty(val)) || _.isNull(val) ? undefined : val
  );
};
