import _ from 'lodash';
import { useState } from 'react';
// eslint-disable-next-line import/named
import memoize from 'memoize-one';
import fetch, { FETCH_METHOD, isCancel, AxiosRequestCommonConfig } from '@/utils/request/fetch';
import checkDataStatus from '@/utils/request/checkDataStatus';
import RESPONSE_STATUS from '@/constants/responseStatus';
import { CommonResponse } from '@/utils/request/parsingResponse';
import getRandomNum from '@/utils/random';

export { FETCH_METHOD };

export interface UseFetchResult<T> {
  loading: boolean;
  response: T;
  config: AxiosRequestCommonConfig;
  onFetch: (options?: UseFetchOptions & AxiosRequestCommonConfig) => Promise<T>;
}

interface UseFetchOptions {
  disabledNotification?: boolean;
  cacheParams?: boolean;
  defaultLoading?: boolean;
  cancelPrevious?: boolean;
}

/**
 *
 * @param method  方法
 * @param url 地址
 * @param disabledNotification 是否禁用成功时的消息弹窗
 * @param cacheParams 缓存参数
 * @param defaultConfig 默认配置
 */
export default function useFetch<T>(
  method = FETCH_METHOD.GET,
  url: string | APIDictObject,
  {
    disabledNotification = false,
    cacheParams = false,
    defaultLoading = false,
    cancelPrevious = true,
    ...defaultConfig
  }: UseFetchOptions & AxiosRequestCommonConfig
): UseFetchResult<T> {
  const [randomMutex] = useState(getRandomNum());
  const [loading, setLoading] = useState<boolean>(defaultLoading);
  const [response, setResponse] = useState<T>(null);
  const [config, setConfig] = useState<AxiosRequestCommonConfig>(defaultConfig);

  return {
    loading,
    response,
    config,
    onFetch(newConfig?: AxiosRequestCommonConfig) {
      const reqParam = _.merge(
        {
          method,
          params: cacheParams ? config.params : {}
        },
        defaultConfig, // defaultConfig 放在第二个，不然在cacheParam: true的时候，defaultConfig 的改变无法更新config
        newConfig,
        cancelPrevious && { mutex: randomMutex }
      );

      setLoading(true);
      setConfig(reqParam);
      return fetch(url, reqParam)
        .then((rsp: any) => {
          checkDataStatus(rsp, {
            method,
            disabledNotification
          });
          setResponse(rsp);
          setLoading(false);
          return rsp;
        })
        .catch((error: any) => {
          // cancel 的请求不需要报错
          if (isCancel(error)) {
            return null;
          }

          setLoading(false);
          return Promise.reject(error);
        });
    }
  };
}

export const requestSuccess = (response?: CommonResponse): boolean =>
  response && response.status === RESPONSE_STATUS.SUCCESS;

export const requestLoading = (response?: CommonResponse): boolean =>
  response && response.status === RESPONSE_STATUS.REQUEST;

export const transformToChartData = memoize(function transformToChart(response: any) {
  const { status, data } = response || {
    status: '',
    data: {}
  };
  let result: any = {
    status
  };
  if (status === RESPONSE_STATUS.SUCCESS) {
    result.data = data;
  }
  if (status === RESPONSE_STATUS.INVALID) {
    result.data = {};
  }
  return result;
}, _.isEqual);

export const transformToTableData = memoize(function transformToTable(response: any) {
  const status = _.get(response, 'status', '');
  const page = _.get(response, 'data.page', {});
  const data = _.get(response, 'data', {});
  let result: any = {
    status,
    page
  };
  if (status === RESPONSE_STATUS.SUCCESS) {
    result.data = data.records;
  }
  return result;
}, _.isEqual);

export const transformToItemData = memoize(function transformToItem(response: any) {
  const { status, data } = response || {
    status: '',
    data: {}
  };
  return {
    status,
    data: data.records || []
  };
});

export const extractErrMsgFromResp = (resp: any): boolean | BaseObject<any> => {
  const { status, errors } = resp;
  if (status === RESPONSE_STATUS.FIELD_INVALID) {
    return errors;
  }

  return false;
};
