import React, { useMemo, useCallback, useContext } from 'react';
import _ from 'lodash';

import { BasicReportProps, getShortNameById } from '@/constants/config';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { API_CAMPAIGN, API_ADGROUP } from '@/constants/APIs';
import SearchInput from '@/components/Table/Tools/SearchInput';
import BEPicker from '@/components/Picker/logic/BEPicker';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import {
  FloatActionCell,
  MultiLineCell,
  DateCell,
  CampaignStatusCell
} from '@/components/Table/Cells';

import { AvailableTableParamType, checkLevelIsCampaign } from '../constants';
import { CreateFormCtx } from '../context';

export default ({
  filterParams,
  tableParams,
  setTableParams,
  handleSearchWord,
  handleChangeCustomParam
}: BasicReportProps<{}, AvailableTableParamType>) => {
  const { formatMessage } = useFmtMsgFori18n();
  const { drawerFormResult, selectedAvailable, setSelectedAvailable } = useContext(CreateFormCtx);
  const {
    formData: { crossLevel, measure, calendar, batchResult },
    handleFormChange
  } = drawerFormResult;
  const { tableData, onChange } = useTable({
    tableParams,
    setTableParams,
    api: checkLevelIsCampaign(crossLevel) ? API_CAMPAIGN : API_ADGROUP,
    postParams: {
      status: '3,4', // 只筛选active 的
      measure: measure ? 1 : undefined,
      calendar: _.get(calendar, 0) === 1 ? 1 : undefined
    }
  });

  const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

  const handleChangeBE = useCallback(
    val => {
      setSelectedAvailable([]);
      handleChangeCustomParam({
        beId: val
      });
    },
    [handleChangeCustomParam, setSelectedAvailable]
  );

  const handleSearch = useCallback(
    word => {
      setSelectedAvailable([]);
      handleSearchWord(word);
    },
    [handleSearchWord, setSelectedAvailable]
  );

  const handleItemChecked = useCallback(
    (values: any[], changedValue, rowData: any) => {
      const currId = _.slice(changedValue).pop() as any;
      const { data } = tableDataMemo;
      setSelectedAvailable(values);
      if (rowData === true) {
        // 新选中的值，同步选到下面
        const newItem = [_.find(data, { id: currId }) || {}];
        handleFormChange(
          _.map(batchResult, b => b.id).indexOf(currId) === -1
            ? batchResult.concat(newItem)
            : batchResult,
          'batchResult'
        );
      } else {
        // 新删掉的值，在下面的表格同步的清空
        handleFormChange(_.filter(batchResult, ppp => ppp.id === currId), 'batchResult');
      }
    },
    [handleFormChange, batchResult, setSelectedAvailable, tableDataMemo]
  );

  const renderToolbar = () => {
    return (
      <>
        <SearchInput
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearch}
        />
        <BEPicker
          beGetParam={{ status: 1 }}
          chosenBE={filterParams.beId}
          beChangeHandler={handleChangeBE}
          isReport={false}
        />
      </>
    );
  };

  const renderCampaignColumns = (): React.ReactElement[] => {
    return [
      <Column flexGrow={1} minWidth={200} fixed sortable key="campaign-name">
        <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
        <FloatActionCell dataKey="name" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="main-owner">
        <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
        <MultiLineCell
          dataKey="primary"
          formatter={data =>
            _.isEmpty(data)
              ? ['']
              : [`${getShortNameById(_.get(data, 'role', ''))}: ${_.get(data, 'beName', '')}`]
          }
          maxLine={1}
        />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="campaign-advertiser">
        <HeaderCell>{formatMessage('i18n_advertiser')}</HeaderCell>
        <FloatActionCell dataKey="advertiser" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="campaign-brand">
        <HeaderCell>{formatMessage('i18n_brand')}</HeaderCell>
        <FloatActionCell dataKey="brand" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-primary">
        <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
        <MultiLineCell
          dataKey="primary"
          formatter={data =>
            _.isEmpty(data)
              ? ['']
              : [`${getShortNameById(_.get(data, 'role', ''))}: ${_.get(data, 'beName', '')}`]
          }
        />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="campaign-status">
        <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
        <CampaignStatusCell dataKey="status" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="campaign-startDate">
        <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
        <DateCell dataKey="startDate" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="campaign-endDate">
        <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
        <DateCell dataKey="endDate" />
      </Column>,

      <Column flexGrow={1} minWidth={180} sortable key="campaign-target">
        <HeaderCell>{formatMessage('i18n_targetPerson')}</HeaderCell>
        <FloatActionCell dataKey="target" />
      </Column>
    ];
  };

  const renderAdunitColumns = (): React.ReactElement[] => {
    return [
      <Column flexGrow={1} minWidth={200} fixed sortable key="name">
        <HeaderCell>{formatMessage('i18n_placementName')}</HeaderCell>
        <FloatActionCell dataKey="name" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="campaign_name">
        <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
        <FloatActionCell dataKey="campaignName" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-primary">
        <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
        <MultiLineCell
          dataKey="primary"
          formatter={data =>
            _.isEmpty(data)
              ? ['']
              : [`${getShortNameById(_.get(data, 'role', ''))}: ${_.get(data, 'beName', '')}`]
          }
        />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="adserver_id">
        <HeaderCell>{formatMessage('i18n_adserverAdunitId')}</HeaderCell>
        <FloatActionCell dataKey="adserverPid" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="site_id">
        <HeaderCell>{formatMessage('i18n_siteId')}</HeaderCell>
        <FloatActionCell dataKey="siteId" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="site_name">
        <HeaderCell>{formatMessage('i18n_siteName')}</HeaderCell>
        <FloatActionCell dataKey="siteName" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="status">
        <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
        <CampaignStatusCell dataKey="status" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="start_date">
        <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
        <DateCell dataKey="startDate" />
      </Column>,

      <Column flexGrow={1} minWidth={200} sortable key="end_date">
        <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
        <DateCell dataKey="endDate" />
      </Column>
    ];
  };

  return (
    <>
      <Table
        {...tableDataMemo}
        renderToolbar={renderToolbar}
        onChange={onChange}
        checkDataKey="id"
        onCheck={handleItemChecked}
        checkValues={selectedAvailable}
        height={400}
      >
        {
          (
            <Column width={100} fixed sortable>
              <HeaderCell>ID</HeaderCell>
              <Cell dataKey="id" />
            </Column>
          ) as any // so weird
        }
        {checkLevelIsCampaign(crossLevel) ? renderCampaignColumns() : renderAdunitColumns()}
      </Table>
    </>
  );
};
