import React, { useMemo } from 'react';
import _ from 'lodash';
import ErrorMessage from '@/components/Form/ErrorMessage';

/**
 * 为多个Picker提供容器，可以在同行显示，也可分行
 */

interface Props {
  children: React.ReactElement[];
  inline?: boolean;
  values?: Record<string, any>;
  errors?: Record<string, any>;
  onChange?: Function;
}

const Component = React.memo(({
  children,
  inline,
  values,
  errors,
  onChange,
}: Props) => {

  const childrenPickers = useMemo(() => {
    return _.map(children, (Child, idx) => {
      const name = _.get(Child, 'props.name'); // 通过name指明哪些是picker，需要分配value和error
      const isPicker = _.isString(name) && !_.isEmpty(name);
      const onChangeProp = _.get(Child, 'props.onChange');
      const newChild = isPicker ? React.cloneElement(Child, {
        value: _.get(values, name),
        onChange: _.isFunction(onChangeProp) ? onChangeProp : onChange?.(name) // 若picker自身传了onChange，则取此值，否则取整个PickerGroup的onChange
      }) : Child;

      return (
        <div
          key={idx}
          className='cus-grouped-picker'
        >
          {newChild}
          {isPicker &&
          <ErrorMessage
            error={_.get(errors, name)}
          />}
        </div>
      );
    });
  }, [children, values, onChange, errors]);

  return (
    <div className='cus-grouped-picker-box'>
      {childrenPickers}
    </div>
  );
});

Component.displayName = 'PickerGroup';
export default Component;