import React from 'react';
import _ from 'lodash';
import { toThousands } from '@/utils/format';

interface Props {
  title: string;
  summary: string | number;
  total?: string | number;
}

export default ({ title, summary, total }: Props) => (
  <div style={{ display: 'inline-block' }}>
    {title}
    <div className="total-title">{_.isNumber(summary) ? toThousands(summary) : summary}</div>
    {total && _.isNumber(total) && <div className="total-summary">{toThousands(total)}</div>}
  </div>
);
