import React, { useCallback } from 'react';
import _ from 'lodash';
import { dateIsValid, toDate } from '@/utils/date/index';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from './FloatActionCell';

export default ({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const date = _.get(rowData, dataKey);
  // 该列为次级维度=>不是日期格式则直接显示后端返回内容,不是--
  const text = dataKey === 'secondary' ? date : DEFAULT_TABLE_CELL_CONTENT;
  const renderContent = useCallback(() => {
    return <span>{dateIsValid(new Date(date)) ? toDate(date) : text}</span>;
  }, [date]);

  return <Cell dataKey={dataKey} rowData={rowData} renderContent={renderContent} {...props} />;
};
