import React, { memo, useState, useEffect } from 'react';
import { Divider } from 'rsuite';
import _ from 'lodash';
import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useInterval from '@/components/hooks/useInterval';
import useChart from '@/components/hooks/useChart';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';

const mockData: ChartDataProps = {
  cols: ['2020/03/02', '2020/03/03', '2020/03/04', '2020/03/05', '2020/03/07', '2020/03/09'],
  data: [
    { data: [10.0, 10.0, 15.0, 10.0, 10.0, 10.0], name: 'impressions' },
    { data: ['--', '--', '--', '--', '--', '--'], name: 'uimpressions' },
    { data: [10.0, 10.0, 15.0, 10.0, 10.0, 10.0], name: 'clicks' },
    { data: ['--', '--', '--', '--', '--', '--'], name: 'uclicks' }
  ]
};

