// 默认数据 广告计数 独立广告计数 点击 独立点击
const defLegendData: Array<LegendDataProps> = [
  {
    id: 'impressions',
    name: 'i18n_impressions',
    yAxisIndex: 0,
    type: CHART_KEY.LINE
  },
  {
    id: 'uimpressions',
    name: 'i18n_uimpressions',
    yAxisIndex: 0,
    type: CHART_KEY.LINE
  },
  {
    id: 'clicks',
    name: 'i18n_clicks',
    yAxisIndex: 1,
    type: CHART_KEY.BAR
  },
  {
    id: 'uclicks',
    name: 'i18n_uclicks',
    yAxisIndex: 1,
    type: CHART_KEY.BAR
  }
];

interface ChartDataProps {
  cols: Array<string>;
  data: Array<DataProps>;
}
interface DataProps {
  data?: Array<string | number>;
  name?: string;
  index?: number;
  yAxisIndex?: number;
  type?: string;
}

interface LegendDataProps {
  id: string;
  name: string;
  index?: number; // 显示顺序 可选，有则按照index排序
  yAxisIndex?: number; // 对应y坐标序号
  type?: CHART_KEY; // 数值chart显示类型
}

interface Props {
  api?: Url;
  postParams?: object;
  customType?: number; // 表格类型  与legendData和yAxis互斥
  legendData?: Array<LegendDataProps>; // 图例chart显示data
  yAxis?: Array<any>; // 纵坐标类型名称
  pure?: boolean; // 是否包含分隔线
  collapsible?: boolean; // 是否包含展开收缩功能
  interval?: boolean; // 是否定时请求
  timer?: number; // 时间间隔
  title?: string; // chart标题
  random?: string; // 用于刷新charts，一开始为空，这样不会重复请求,
  hideTools?: boolean; // 是否隐藏工具栏，默认显示
}

// 报表通用chart
// customType 1 =>legendData: impressions clicks  yAxis: impressions clicks
// customType 2 =>legendData: impressions uimpressions  clicks uclicks  yAxis: impressions clicks
// customType undefined=>legendData yAxis

export default memo(
  ({
    dateParams,
    filterParams,
    api = APIs.API_CHART,
    postParams = {},
    legendData = [],
    customType,
    pure = false,
    collapsible = false,
    yAxis,
    interval = false,
    timer = 1000 * 60,
    title,
    random,
    hideTools = false
  }: Props & BasicReportProps) => {
    const { data, loading, handRefresh } = useChart({
      dateParams,
      filterParams,
      api,
      postParams: {
        ...postParams,
        var: 'chart'
      }
    });
    const [delay, setDelay] = useState(interval ? timer : null);
    const [chartData, setChartData] = useState<ChartDataProps>(null);
    const [originYAxis, setOriginYAxis] = useState(yAxis);
    const { formatMessage } = useFmtMsgFori18n();
    // 默认坐标
    const defYAxis = [
      { type: 'number', name: formatMessage('i18n_impressions') },
      { type: 'number', name: formatMessage('i18n_clicks') }
    ];
    //传参改变清空chartData
    useEffect(() => {
      setChartData(null);
    }, [postParams]);
    /* eslint-disable */
    useEffect(() => {
      if (_.isNil(data)) {
        return;
      }
      let originLegend = legendData;
      switch (customType) {
        case 1:
          setOriginYAxis(defYAxis);
          originLegend = defLegendData.filter(
            (i: LegendDataProps) => i.id === 'impressions' || i.id === 'clicks'
          );
          break;
        case 2:
          setOriginYAxis(defYAxis);
          originLegend = defLegendData;
          break;
        default:
          break;
      }
      let records: Array<DataProps> =
        _.get(data, 'records') || _.get(data, 'data') || _.get(data, 'items') || [];

      const newData: Array<any> = [];
      if (_.isEmpty(originLegend)) {
        records.forEach((i: any, index: number) => {
          newData.push({
            ...i,
            name: formatMessage(i.name),
            type: i.type || (index < 2 ? CHART_KEY.LINE : CHART_KEY.BAR)
          });
        });
      } else {
        originLegend.forEach((i: any) => {
          let sdata = records.filter((j: DataProps) => j.name === i.id);
          if (_.isEmpty(sdata)) {
            return;
          }
          newData.push({
            ...i,
            name: formatMessage(i.name),
            data: sdata[0].data,
            type: i.type || CHART_KEY.LINE
          });
        });
      }
      setChartData({
        ...data,
        data: newData
      });
    }, [data]);
    /* eslint-disable */

    // 定时器
    useInterval(() => {
      setDelay(timer);
      handRefresh();
    }, delay);

    // 一开始random为空，不请求，如果random非空在请求
    useEffect(() => {
      if (random) {
        handRefresh();
      }
    }, [handRefresh, random]);
    const config = {
      type: CHART_KEY.MIX,
      data: chartData,
      yAxis: originYAxis,
      hasTools: !hideTools
    };
    return (
      <>
        <Chart config={config} loading={loading} collapsible={collapsible} title={title} />
        {!pure && (
          <div className="chart-div">
            <Divider />
          </div>
        )}
      </>
    );
  }
);
