import React, { memo, useCallback, useContext } from 'react';
import _ from 'lodash';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import { API_HWA } from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import { filterParamKeys, FilterParam } from '@/views/common/conversionReport/constants';
import { CampaignCtx } from '@/views/campaigns/common/context';
import { primaryData } from './constants';

type Props = Partial<BasicReportProps<FilterParam>> & {
  handleChangeDate: (newDateRange: Array<Date>) => void;
  handleChangeProjects?: Function;
  dateVal: Array<string>;
  maxDate: string;
  minDate: string;
};
export default memo(
  ({
    dateVal,
    maxDate,
    minDate,
    filterParams,
    handleChangeDate,
    handleChangeCommonParam,
    handleChangeProjects
  }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();
    const handleSelectProject = useCallback(
      (value, item) => {
        if (_.includes(value, item.id) || _.isEmpty(value)) {
          handleChangeProjects(item, value);
        }
      },
      [handleChangeProjects]
    );
    const { campaignDetail } = useContext(CampaignCtx);
    const beId = _.get(campaignDetail, 'primary.beId');
    return (
      <>
        <DateRangePicker
          value={dateVal}
          maxDate={maxDate}
          minDate={minDate}
          cleanable={false}
          appearance="subtle"
          onChange={handleChangeDate}
        />
        <SelectPicker
          prepend={formatMessage('i18n_primary')}
          data={usePickerDataFromObj(primaryData)}
          value={filterParams.mDimension}
          onChange={(value: string) =>
            handleChangeCommonParam({ [filterParamKeys.MDIMENSION]: value })
          }
          appearance="subtle"
        />
        <CheckPicker
          onChange={(value: Array<string>) =>
            handleChangeCommonParam({ [filterParamKeys.PROJECT]: value })
          }
          onClean={() => handleChangeProjects()}
          onSelect={handleSelectProject}
          appearance="subtle"
          prepend={formatMessage('i18n_selectProjects')}
          supportAll
          url={API_HWA}
          dataPath={['data']}
          // params={{
          //   beId
          // }}
          renderMenuItem={(label, item) => {
            const name = _.get(item, 'label') + '（' + _.get(item, 'value') + '）';
            return <span title={name}>{name}</span>;
          }}
        />
      </>
    );
  }
);
