import _ from 'lodash';
import * as React from 'react';
import { Route, RouteComponentProps, RouteProps } from 'react-router-dom';

import Title from '@/components/Title';
import { localeKeys } from '@/locales/index';

import { RouteInfoCtx } from './RouteContext';

interface BasicRouteProps {
  // 设置页面 title
  titleKey?: localeKeys;
  component?: React.ReactNode;
  children?: React.ReactChild | React.ReactNode | React.ElementType;
}

const BasicRoute = React.memo((props: RouteProps & BasicRouteProps) => {
  const { titleKey, render, component: Component, children, ...rest } = props;
  const routeInfoMemo = React.useMemo(
    () => ({
      title: titleKey
    }),
    [titleKey]
  );

  return (
    <Route
      {...rest}
      render={(routeProps: RouteComponentProps) => (
        <>
          <Title titleKey={titleKey} />
          {_.isFunction(render) ? (
            render(routeProps)
          ) : (
            <RouteInfoCtx.Provider value={routeInfoMemo}>
              <Component {...routeProps}>
                {_.isFunction(children) ? children(routeProps) : children}
              </Component>
            </RouteInfoCtx.Provider>
          )}
        </>
      )}
    />
  );
});

export default BasicRoute;
