import _ from 'lodash';

import { SessionData } from '@/constants/config';

// 获取session
export function getStorage() {
  const session = localStorage.getItem('session');
  let result = {};
  try {
    result = JSON.parse(decodeURIComponent(session));
  } catch {
    result = {};
  }

  return result;
}

// 设置session
export function setStorage(data: BaseObject<any>) {
  const oldSession = getStorage();
  const newData = _.extend(oldSession, data);
  localStorage.setItem('session', encodeURIComponent(JSON.stringify(newData)));
}

// 初始化session
export function initSessionStorage(id: string) {
  const originStorage = getStorage();
  if (String(_.get(originStorage, 'id')) !== String(id)) {
    setStorage({ id });
  }
}

export const updateStorageByKey = (key: keyof SessionData, value: any) => {
  const result = getStorage();
  if (!_.isEmpty(result)) {
    (result as any)[key] = value;
  }

  setStorage(result);
};

export const removeStorage = () => localStorage.removeItem('session');
