import React from 'react';
import _ from 'lodash';
import classNames from 'classnames';
import { Button, Table } from 'rsuite';
import { TableCellProps } from 'rsuite/lib/Table/TableCell';

const { Cell } = Table;

interface DQAProps {
  clickable?: boolean;
  onCheck?: any;
}

export default ({
  rowData,
  dataKey,
  clickable = true,
  onCheck,
  ...props
}: TableCellProps & DQAProps) => {
  const ids = _.split(_.get(rowData, dataKey), ',').filter(i => i !== '');
  const btns = ids.map(i =>
    clickable ? (
      <Button appearance="link" onClick={() => onCheck(rowData, i)} key={i}>
        {i}
      </Button>
    ) : (
      i
    )
  );

  return (
    <Cell {...props} className={classNames('dqa-checks-cell', { clickable })}>
      {_.reduce(
        btns,
        (arr, item, index) => {
          if (index !== btns.length - 1) {
            return [...arr, item, '; '];
          }
          return [...arr, item];
        },
        []
      )}
    </Cell>
  );
};
