/* eslint-disable */

import _ from 'lodash';
import isArray from 'lodash/isArray';
import cloneDeep from 'lodash/cloneDeep';

// ESLint 不建议使用 一元运算符 for-in for-of

/**
 * 将树转为一维数组
 * @param {*} tree
 * @param {String | Array | Function} selector
 * @example flattenNodes(tree, 'id')
 * @example flattenNodes(tree, ['id', 'name', 'value'])
 * @example flattenNodes(tree, node => node.id)
 */
export function flattenNodes(tree, selector, childrenKey = 'children') {
  let arr = [];
  let getNewNode = node => node;
  if (selector) {
    if (_.isFunction(selector)) {
      getNewNode = selector;
    } else if (_.isArray(selector)) {
      getNewNode = node => {
        let newNode = {};
        selector.forEach(key => {
          newNode[key] = node[key];
        });
        return newNode;
      };
    } else if (_.isString(selector)) {
      getNewNode = node => node[selector];
    }
  }
  function loop(nodes) {
    if (!Array.isArray(nodes) || nodes.length === 0) {
      return;
    }
    nodes.forEach(node => {
      arr.push(getNewNode(node));
      loop(node[childrenKey]);
    });
  }
  loop(tree);
  return arr;
}

/**
 * 过滤树
 * @param {Array} tree
 * @param {Function} comparator
 */
export function filterTree(tree, comparator) {
  let ret = [];
  function loop(data, newTree) {
    data.forEach(node => {
      let item = _.omit(node, 'children');
      if (comparator(node)) {
        newTree.push(item);
        if (node.children) {
          item.children = [];
          loop(node.children, item.children);
        }
      }
    });
  }
  loop(tree, ret);
  return ret;
}

/**
 * 生成新树
 * @param {Array} tree
 * @param {Function} comparator
 */
export function mapTree(tree, fun, childrenKey = 'children') {
  let ret = [];
  let layer = -1;
  function loop(data, newTree) {
    layer += 1;
    data.forEach(node => {
      let item = fun(node, layer);
      newTree.push(item);
      if (node[childrenKey]) {
        item[childrenKey] = [];
        loop(node[childrenKey], item[childrenKey]);
      }
    });
    layer -= 1;
  }
  loop(tree, ret);
  return ret;
}

export default function filterNodesOfTree(data, check, childrenKey = 'children') {
  const treeData = cloneDeep(data);
  const findNodes = (nodes = []) =>
    nodes.filter(item => {
      if (isArray(item[childrenKey])) {
        const nextChildren = findNodes(item.children);
        if (nextChildren.length) {
          item[childrenKey] = nextChildren;
          return true;
        }
      }
      return check(item);
    });
  return findNodes(treeData);
}

/**
 * 在树中查找一个节点的路径
 * @param {Array} tree
 * @param {Function} comparator
 */
export function findPaths(tree: any, comparator: Function, childrenKey = 'children'): any {
  let stack = [] as any;
  let paths = null as any;
  function loop(data: any) {
    for (const node of data) {
      stack.push(node);
      if (comparator(node)) {
        paths = [...stack];
        return;
      }
      if (node[childrenKey]) {
        loop(node[childrenKey]);
      }
      stack.pop();
    }
  }
  loop(tree);
  return paths;
}

/**
 * 在树中查找一个节点
 * @param {Array} tree
 * @param {Function} comparator
 */
export function findTree(tree: any, comparator: Function): any {
  let ret = null;
  let isEnd = false;
  function loop(data: any) {
    if (isEnd) {
      return;
    }
    for (const node of data) {
      if (isEnd) {
        return;
      }
      if (comparator(node)) {
        ret = node;
        isEnd = true;
        return;
      }
      if (node.children) {
        loop(node.children);
      }
    }
  }
  loop(tree);
  return ret;
}
