import { createStore, applyMiddleware, compose } from 'redux';
import thunkMiddleware from 'redux-thunk';
import { combineReducers } from 'redux-immutablejs';
import { createLogger } from 'redux-logger';
// @ts-ignore
import { Iterable } from 'immutable';
import { __DEBUG__ } from '@/constants/config';
import reducers from './reducers';
import ACTION_TYPES from '@/constants/actionTypes';

const middlewares = [thunkMiddleware];

// eslint-disable-next-line no-restricted-globals
if (__DEBUG__ && location.href.includes('logger=log')) {
  // @ts-ignore
  const stateTransformer = state => (Iterable.isIterable(state) ? state.toJS() : state);
  const logger = createLogger({ stateTransformer });
  // @ts-ignore
  middlewares.push(logger);
}

/**
 * Enable Redux devtools browser extension
 * @see https://github.com/zalmoxisus/redux-devtools-extension#12-advanced-store-setup
 */
// @ts-ignore
// eslint-disable-next-line no-underscore-dangle
const composeEnhancers = window.REDUX_DEVTOOLS_EXTENSION_COMPOSE || compose;

function configureStore() {
  const store = createStore(
    combineReducers({
      store: reducers
    }),
    composeEnhancers(applyMiddleware(...middlewares))
  );

  if (module.hot) {
    // Enable Webpack hot module replacement for reducers
    module.hot.accept('./reducers', () => {
      store.replaceReducer({
        // @ts-ignore
        // eslint-disable-next-line global-require
        store: require('./reducers').default
      });
    });
  }
  return store;
}

export const store = configureStore();

export function action(type: ACTION_TYPES, payload: Record<string, any> = {}) {
  const { status, ...response } = payload;
  return { type, status, response };
}
