import _ from 'lodash';
import React from 'react';
import { Table } from 'rsuite';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';

const { Cell } = Table;

export interface MappingCellProps {
  rowData?: BaseObject<any>;
  dataKey: string;
  map: BaseObject<string | React.ReactElement | null>;
}

const styles = {
  link: { width: 'initial', maxWidth: '100%' }
};

export const MappingCell = ({ rowData, dataKey, map, ...props }: MappingCellProps) => (
  <Cell {...props}>
    {_.get(map, rowData[dataKey]) || rowData[dataKey] || DEFAULT_TABLE_CELL_CONTENT}
  </Cell>
);

export default React.memo(MappingCell);
