/* eslint-disable no-useless-escape */
/* eslint-disable max-len */

/**
 * @description 正则集合
 */
/**
 * cpo 组名（特殊字符只允许输入“_”）
 * @type {RegExp}
 */
export const GROUP_STRING = /^[0-9A-Za-z\u3400-\u4DB5\u4E00-\u9FA5\u9FA6-\u9FBB\uF900-\uFA2D\uFA30-\uFA6A\uFA70-\uFAD9\uFF00-\uFFEF\u2E80-\u2EFF\u3000-\u303F\u31C0-\u31EF\_]+$/;

/**
 * 允许的字符 中文 英文 数字 特殊符号
 * 中文字符： http://ju.outofmemory.cn/entry/53571
 * 特殊符号："`~!@#$%^&()-_=+[{]}',.·￣！＠＃￥％……＆×（）－——＝＋【｛】｝、｜；：「」『』‘’”“，《。》、？～＄＾＊＿"
 */
export const ALLOW_STRING = /^[0-9A-Za-z\u3400-\u4DB5\u4E00-\u9FA5\u9FA6-\u9FBB\uF900-\uFA2D\uFA30-\uFA6A\uFA70-\uFAD9\uFF00-\uFFEF\u2E80-\u2EFF\u3000-\u303F\u31C0-\u31EF\`\ \~\!\@\$\#\%\^\&\(\)\-\_\=\+\[\{\]\}\'\,\.\·\…\“\‘\’\”\×\—\/\\]+$/;

/**
 * 标签名（比白名单少了 “-”）
 * @type {RegExp}
 */
export const TAG_NAME_STRING = /^[0-9A-Za-z\u3400-\u4DB5\u4E00-\u9FA5\u9FA6-\u9FBB\uF900-\uFA2D\uFA30-\uFA6A\uFA70-\uFAD9\uFF00-\uFFEF\u2E80-\u2EFF\u3000-\u303F\u31C0-\u31EF\_\+\[\]\(\)\【\】\）]+$/;

/**
 * 国内电话号码
 * @type {RegExp}
 */
export const PHONE_CN = /^((\d{3}-\d{7,8}((-\d{1,4})?))|(\d{4}-\d{7,8}((-\d{1,4})?))|(\d{11,12}))$/;

/**
 * 金额
 * @type {RegExp}
 */
export const CURRENCY_REG = /^\d*(\.\d{1,2})?$/;

/**
 * Cookie 比率
 * @type {RegExp}
 */
export const COOKIE_RATE_REG = /^\d(\.\d{1,4})?$/;

/**
 * 自然数
 * @type {RegExp}
 */
export const NATURAL_NUMBER = /^\d+$/;

/**
 * 邮箱
 * @type {RegExp}
 */
// eslint-disable-next-line no-control-regex
export const EMAIL = /^((([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*)|((\x22)((((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(([\x01-\x08\x0b\x0c\x0e-\x1f\x7f]|\x21|[\x23-\x5b]|[\x5d-\x7e]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(\\([\x01-\x09\x0b\x0c\x0d-\x7f]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))))*(((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(\x22)))@((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)+(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))$/i;

/**
 * 域名
 * @type {RegExp}
 * [参考]{@link https://regexr.com/3au3g}
 */
export const DOMAIN = /^(?:[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?\.)+[a-zA-Z0-9][a-zA-Z0-9-]{0,61}[a-zA-Z0-9]$/;

/**
 * 域名(含Ip)
 * @type {RegExp}
 * [参考]{@link http://stackoverflow.com/questions/26093545/how-to-validate-domain-name-using-regex#answer-38578855}
 */
export const FULL_DOMAIN = /^((?:[a-zA-Z0-9])(?:[a-zA-Z0-9-\.]){0,61}(?:\.[a-zA-Z]{2,})+|\[(?:(?:(?:[a-fA-F0-9]){1,4})(?::(?:[a-fA-F0-9]){1,4}){7}|::1|::)\]|(?:(?:[0-9]{1,3})(?:\.[0-9]{1,3}){3}))(?:\:[0-9]{1,5})?$/;

/**
 * 完整的 IP 校验
 * @type {RegExp}
 * [参考]{@link https://stackoverflow.com/questions/34579640/unable-to-match-ip-regex-in-javascript#answer-34579651}
 */
export const FULL_IP = /^((\d\d?|1\d\d|2([0-4]\d|5[0-5]))\.){3}(\d\d?|1\d\d|2([0-4]\d|5[0-5]))$/;

/**
 * 模糊匹配 IP
 * eg: xxx.xxx.xxx.* xxx.xxx.*.* xxx.*.*.*
 * @type {RegExp}
 */
export const FUZZY_IP = /^((((\d\d?|1\d\d|2([0-4]\d|5[0-5]))\.){3}\*)|(((\d\d?|1\d\d|2([0-4]\d|5[0-5]))\.){2}(\*\.)\*)|(((\d\d?|1\d\d|2([0-4]\d|5[0-5]))\.){1}((\*\.){2})\*))$/;

/**
 * Ip 段
 * @type {RegExp}
 */
export const FULL_IP_RANGE = /^((\d\d?|1\d\d|2([0-4]\d|5[0-5]))\.){3}(\d\d?|1\d\d|2([0-4]\d|5[0-5]))\-((\d\d?|1\d\d|2([0-4]\d|5[0-5]))\.){3}(\d\d?|1\d\d|2([0-4]\d|5[0-5]))$/;

/**
 * url
 * @type {RegExp}
 */
export const URL_REG = /^(https?):\/\/(((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[!\$&'\(\)\*\+,;=]|:)*@)?(((\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5]))|((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)*(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.?)(:\d*)?)(\/((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[!\$&'\(\)\*\+,;=]|:|@)+(\/(([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[!\$&'\(\)\*\+,;=]|:|@)*)*)?)?(\?((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[\{\}!\$&'\(\)\*\+,;=]|:|@)|[\uE000-\uF8FF]|\/|\?)*)?(#((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[\{\}!\$&'\(\)\*\+,;=]|:|@)|\/|\?)*)?$/i;
/**
 * 宽泛的 URL 不限制协议头
 * @type {RegExp}
 */
export const NO_LIMIT_SCHEMA_URL_REG = /^([a-z]+):\/\/(((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[!\$&'\(\)\*\+,;=]|:)*@)?(((\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5]))|((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)*(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.?)(:\d*)?)(\/((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[!\$&'\(\)\*\+,;=]|:|@)+(\/(([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[!\$&'\(\)\*\+,;=]|:|@)*)*)?)?(\?((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[\{\}!\$&'\(\)\*\+,;=]|:|@)|[\uE000-\uF8FF]|\/|\?)*)?(#((([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(%[\da-f]{2})|[\{\}!\$&'\(\)\*\+,;=]|:|@)|\/|\?)*)?$/i;

/**
 * NUMBER_REG 数字
 * @type {RegExp}
 */
export const NUMBER_REG = /^[-]*[0-9]+([\.][0-9]+)?$/;

/**
 * 指定的数字正则
 * @param precision - 小数点前允许precision位
 * @param fixed -小数点后允许precision位
 */
export const getNumberReg = (precision: number, fixed: number) =>
  new RegExp(`^[-]?\\d{0,${precision}}([\.]\\d{0,${fixed}})?$`);

/**
 * 换行符
 * @type {RegExp}
 */
export const LINE_BREAK_REG = /\r\n|[\r\n]/;

/**
 * 邮政编码
 * @type {RegExp}
 */
export const POST_CODE_REG = /^[0-9]{6}$/;
