/* eslint-disable */
import _ from 'lodash';
import { Alert } from 'rsuite';
import Promise from 'promise';

import { loadJsFile } from '@/utils';
import loadCssFile from '@/utils/loadCssFile';
import { GET } from '@/utils/request/fetch';
import { initSessionStorage, removeStorage } from '@/utils/storage';
import { goLogIn, setUserProfile, getUserProfile, removeUserProfile } from '@/utils/location';
import { action, store } from '@/utils/redux/configureStore';
import { API_ENV, API_PROFILE } from '@/constants/APIs';
import { isIE } from '@/constants/browserConst';
import { setEnvToSession } from '@/utils/env';
import ACTION_TYPES from '@/constants/actionTypes';
import STATUS from '@/constants/responseStatus';
import { __PUBLIC__, QUICK_DEV } from '@/constants/config';

const LOAD_JS_FAILURE = 'LOAD_JS_FAILURE';
const LOAD_CSS_FAILURE = 'LOAD_CSS_FAILURE';

// 异步加载文件
const initFiles = (func: Function, files: string[], success: Function, fail: Function) => {
  let count = 0;
  files.forEach(file =>
    func(
      file,
      () => {
        count++;
        if (count === files.length) {
          success && success();
        }
      },
      fail
    )
  );
};

// 加载ENV
const loadEnv = () =>
  GET(API_ENV).then(rsp => {
    const { status } = rsp;
    if (status === STATUS.SUCCESS) {
      // 200成功
      const data = _.get(rsp, 'data.data');
      setEnvToSession(data); // 设置ENV
      store.dispatch(action(ACTION_TYPES.UPDATE_ENVIRONMENT_INFO, rsp));
      return Promise.resolve(data);
    } else if ([STATUS.NO_ACCESS, STATUS.NOT_FOUND].includes(status)) {
      // 403、404，还需进行下一步，来加载css文件供展示相应错误页面
      return Promise.resolve(null);
    } else {
      // 其他状态码，均作为抛出错误
      return Promise.reject(status);
    }
  });

// 加载Profile
const loadProfile = (env: object) => {
  const userProfileFromStorage = getUserProfile();
  if (!_.isEmpty(userProfileFromStorage) && QUICK_DEV) {
    return Promise.resolve([env, userProfileFromStorage]);
  }

  // @ts-ignore
  return GET(API_PROFILE).then(rsp => {
    const { status, data = {} } = rsp;

    if (status === STATUS.SUCCESS && !_.isNil(env)) {
      // env与profile均获取成功，才会存储用户信息
      initSessionStorage(_.get(data, 'id'));
      setUserProfile(data);
      store.dispatch(action(ACTION_TYPES.UPDATE_USER_INFO, rsp));
    } else {
      // 否则，清除当前用户信息
      removeStorage();
      removeUserProfile();
    }

    if (status === STATUS.SUCCESS) {
      // 200 成功
      return Promise.resolve([env, data]);
    } else if ([STATUS.NO_ACCESS, STATUS.NOT_FOUND].includes(status)) {
      // 403、404为可处理异常，仍需加载css，因为要显示错误页
      return Promise.resolve([env, status]);
    } else {
      // 其他状态码，均不用加载css，作为错误抛出
      return Promise.reject(status);
    }
  });
};

// 加载CSS主题文件
const loadCSS = ([env, profile]: any[]) =>
  new Promise((resolve, reject) => {
    if (process.env.NODE_ENV !== 'production') {
      // 非生产环境直接请求这个文件，还会返回index.html
      _.isObject(profile) ? resolve() : reject(profile);
      return;
    }
    const theme = _.get(profile, 'hacProfile.theme') || _.get(env, 'skinColor', 'default');
    initFiles(
      loadCssFile,
      [`${__PUBLIC__}/resources/css/theme-${theme}.css`],
      () => (_.isObject(profile) ? resolve() : reject(profile)),
      () => reject(LOAD_CSS_FAILURE)
    );
  });

// 为IE浏览器加载polyfill和es6-shim文件
const loadJS = () =>
  new Promise((resolve, reject) => {
    console.log('public', __PUBLIC__);
    if (!isIE) {
      resolve();
      return;
    }
    initFiles(
      loadJsFile,
      [
        `${__PUBLIC__}/resources/js/es6-shim/0.35.1/es6-shim.min.js`,
        `${__PUBLIC__}/resources/js/es6-shim/0.35.1/es6-sham.min.js`,
        `${__PUBLIC__}/resources/js/polyfill.min-features-Intl-locale.en.js`
      ],
      () => resolve(),
      () => reject(LOAD_JS_FAILURE)
    );
  });

const ready = () =>
  Promise.all([
    // JS的加载需异步执行
    Promise.resolve()
      .then(loadEnv)
      .then(loadProfile)
      .then(loadCSS)
      .catch(message => {
        // 对以上步骤抛出的异常进行处理，此处返回reject则不允许移除loading并渲染内部页面
        switch (message) {
          case STATUS.NOT_LOGGED_IN: // 401，跳转登录页
            goLogIn();
            return Promise.reject(STATUS.NOT_LOGGED_IN);
          case STATUS.NO_ACCESS: // 403，跳转403错误页
            return { errorUrl: '/error/403' };
          case STATUS.NOT_FOUND: // 404，跳转404错误页
            return { errorUrl: '/error/404' };
          case LOAD_CSS_FAILURE: // CSS文件加载失败，Alert提示
            Alert.warning('fail to load css files');
            return;
          default:
            // 其余错误暂以Alert形式显示
            _.isString(message) && Alert.error(message);
            return Promise.reject(message);
        }
      }),

    Promise.resolve()
      .then(loadJS)
      .catch(message => {
        Alert.warning('fail to load js files');
        return Promise.reject(message);
      })
  ]);

export default ready;
