import React, { useCallback } from 'react';
import _ from 'lodash';
import Cell, { BaseCellProps } from './FloatActionCell';

export default React.memo(({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const status = _.get(rowData, dataKey);
  const renderContent = useCallback(() => {
    return <span>{_.capitalize(status)}</span>;
  }, [status]);

  return (
    <Cell dataKey={dataKey} renderContent={renderContent} renderTitle={renderContent} {...props} />
  );
});
