import React, { useState, useEffect, useCallback } from 'react';
import _ from 'lodash';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import SelectPicker from '@/components/Picker/SelectPicker';
import Toggle from '@/components/Form/Toggle';
import HelpIcon from '@/components/HelpIcon';
import RadioGroup from '@/components/Form/RadioGroup';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { ParaModel } from '@/constants/form/models';
import { typeData } from './constants';
import { API_BE } from '@/constants/APIs';

const beParams = {
  status: 1,
  orderColumn: 'name',
  orderType: 'asc'
};

export default React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();
    const typeDataIntl = usePickerDataFromObj(typeData);

    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editAttribute' : 'i18n_newAttribute')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        className="para-drawer"
        onSubmit={onSubmit}
        onHide={onClose}
      >
        {drawer.isEdit && <span>{formatMessage('i18n_editAttributeTips')}</span>}
        <Form
          formData={formData}
          formErrors={formErrors}
          model={ParaModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 名称 */}
          <CustomField
            name="name"
            label={formatMessage('i18n_name')}
            info={formatMessage('i18n_helpTips')}
            message={formatMessage('i18n_helpTips')}
            required
          />

          <CustomField name="desc" label={formatMessage('i18n_description')} />

          <CustomField
            name="type"
            label={
              <span>
                {formatMessage('i18n_type')}
                <HelpIcon description={formatMessage('i18n_msgAttributeType')} />
              </span>
            }
            accepter={RadioGroup}
            data={typeDataIntl}
            inline
            itemWidth="45%"
            className="para-type-group"
          />

          <CustomField
            name="beId"
            label={formatMessage('i18n_primaryOwner')}
            accepter={SelectPicker}
            url={API_BE}
            params={beParams}
            searchable
            required
          />

          <CustomField
            name="isDefault"
            label={
              <>
                {formatMessage('i18n_needToBeCalculated')}
                <HelpIcon description={formatMessage('i18n_msgCalculated')} />
              </>
            }
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_yes')}
            unCheckedChildren={formatMessage('i18n_no')}
          />

          <CustomField
            name="isForVerify"
            label={formatMessage('i18n_isForVerify')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_yes')}
            unCheckedChildren={formatMessage('i18n_no')}
          />
        </Form>
      </Drawer>
    );
  }
);
