import React from 'react';
import { Dropdown } from 'rsuite';
import { DropdownProps } from 'rsuite/lib/Dropdown/index';

interface DropDownDataEntity {
  eventKey: string;
  label: string;
}

export interface CustomDropDownProps extends DropdownProps {
  data: Array<DropDownDataEntity>;
}

export default (props: CustomDropDownProps) => {
  const { data, ...rest } = props;
  return (
    <Dropdown {...rest}>
      {data.map(d => (
        <Dropdown.Item key={d.eventKey} eventKey={d.eventKey}>
          {d.label}
        </Dropdown.Item>
      ))}
    </Dropdown>
  );
};
