import React, { useCallback, useEffect, useState, useContext, useMemo, useRef } from 'react';
import { Nav, Icon, Tooltip, Whisper } from 'rsuite';
import { withRouter, useLocation, useHistory } from 'react-router-dom';
import { PlacementType } from 'rsuite/lib/Notification';
import _ from 'lodash';

import SvgIcon from '@/components/Icon/SvgIcon';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import SelectPicker from '@/components/Picker/SelectPicker';
import TreePicker from '@/components/Picker/TreePicker';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { API_ADVERTISER, API_CAMPAIGN_LIST } from '@/constants/APIs';
import { goFirstPage, getUserProfile } from '@/utils/location';
import { validateUrl } from '@/utils/auth';
import { findTree } from '@/utils/tree';
import { getStorage } from '@/utils/storage';

import { CampaignCtx } from './context';

const pickerProps = {
  cleanable: false,
  appearance: 'subtle' as 'subtle' | 'default',
  placement: 'rightStart' as PlacementType,
  block: true,
  className: 'sidebar-projects-current',
  menuClassName: 'side-menu'
};

interface CampaignDataEntity {
  id: number | string;
  name: string;
  children?: Array<CampaignDataEntity>;
}

const checkIsCampaign = (pathname: string) => /^\/campaigns/.test(pathname);
const checkIsCrossCampaign = (pathname: string) => /^\/cross-campaigns/.test(pathname);
const dataPath = 'data.records';
// 为什么需要encode一下，因为 loadingStatus是根据id的一个打平的数据，如果二级和三级id一样，那么会出现混乱
const encodeByLevel = (level: 0 | 1 | 2, originId: any) => `${originId}${'L'.repeat(level + 1)}`;
const decodeByLevel = (level: 0 | 1 | 2, transformedId: string) =>
  (transformedId || '').replace('L'.repeat(level + 1), '');

export const ProjectNav = withRouter(({ expand }: any) => {
  const { campaignDetail = {}, campaignDetailLoading } = useContext(CampaignCtx);
  const campaignInfo = useMemo(
    () => ({
      id: encodeByLevel(2, (campaignDetail || {}).id),
      name: (campaignDetail || {}).name
    }),
    [campaignDetail]
  );

  const userId =
    _.get(campaignDetail, 'userId') ||
    _.get(getStorage(), 'selectedView.id') ||
    _.get(getUserProfile(), 'id');
  const { pathname } = useLocation();
  const history = useHistory();
  const isCampaign = checkIsCampaign(pathname);
  const isCrossCampaign = checkIsCrossCampaign(pathname);
  const { formatMessage } = useFmtMsgFori18n();
  const [campaignData, setCampaignData] = useState<Array<CampaignDataEntity>>([]);
  const [loadingValues, setLoadingValues] = useState([]);
  const containerRef = useRef<HTMLDivElement>(null);

  const { onFetch: onFetchCampaign, loading: campaignLoading } = useFetch<CommonResponse>( // 不处理请求取消
    FETCH_METHOD.GET,
    API_ADVERTISER,
    { params: { orderColumn: 'name', status: 1 }, cancelPrevious: false }
  );

  const { onFetch: onFetchCampaignList } = useFetch<CommonResponse>( // 不处理请求取消
    FETCH_METHOD.GET,
    API_CAMPAIGN_LIST,
    { params: { report: 1, orderColumn: 'name' }, cancelPrevious: false }
  );

  // 增加路由判断 到站流量 归因模型 返回对应的转化归因活动报表页面
  const handleItemClicked = useCallback(() => {
    let ppp = '/campaign-account-overview';
    if (/^\/campaigns\/[0-9]+\/landing-traffic$/.test(pathname)) {
      ppp = '/landing-traffic';
    }
    if (/^\/campaigns\/[0-9]+\/attribution$/.test(pathname)) {
      ppp = '/attribution';
    }
    if (validateUrl(ppp) === true) {
      history.push(ppp);
    } else {
      goFirstPage(userId, history.replace);
    }
  }, [userId, history, pathname]);

  const renderTreeIcon = (node: any) => {
    if (loadingValues.includes(node.value)) {
      return <Icon icon="spinner" spin />;
    }
    return null;
  };

  const handleExpandCampaign = useCallback(
    (expandedValues, activeNode) => {
      const { value, expand: hasExpanded, children: nodeChildren = [] } = activeNode;
      // FIXME: 这里依赖了组件内部的实现判断value，等组件不再过滤数据了，把这里改掉
      const valueList = value.split('+');
      const level = valueList.length - 1;
      const lastId: string = _.last(valueList);
      if (level === 1 && !hasExpanded && !_.get(nodeChildren, 'length', 0)) {
        setLoadingValues(loadingVal => _.union(loadingVal, [value]));
        onFetchCampaignList({ params: { brandId: decodeByLevel(1, lastId) } })
          .then(resp => {
            const result = _.get(resp, dataPath, []);
            setCampaignData(prevCampaignData => {
              const newCampaignData = _.cloneDeep(prevCampaignData);
              const node = findTree(newCampaignData, (i: any) => String(i.id) === String(lastId));
              if (node) {
                (node as any).children = result.map((r: any) => ({
                  id: encodeByLevel(2, r.id),
                  name: r.name
                }));
              }

              return newCampaignData;
            });
          })
          .finally(() => {
            setLoadingValues(loadingVal => _.difference(loadingVal, [value]));
          });
      }
    },
    [onFetchCampaignList]
  );

  const handleSelect = useCallback(
    activeNode => {
      const { value } = activeNode;
      const valueList = value.split('+');
      const level = valueList.length - 1;
      if (isCampaign) {
        if (level === 2) {
          const id = decodeByLevel(2, _.last(valueList));
          history.push(`/campaigns/${id}/overview`);
        }
      } else if (isCrossCampaign) {
        const id = 'TODO';
        history.push(`/cross-campaigns/${id}`);
      }
    },
    [isCampaign, isCrossCampaign, history]
  );

  useEffect(() => {
    if (isCampaign) {
      onFetchCampaign().then(resp => {
        const result = _.get(resp, dataPath, []);
        const formattedResult: Array<CampaignDataEntity> = result.map((r: any) => ({
          id: encodeByLevel(0, r.id),
          name: r.name,
          children: r.children.map((rr: any) => ({
            id: encodeByLevel(1, rr.id),
            name: rr.name,
            children: [] as Array<CampaignDataEntity> // 手动构造一个空数组，便于异步请求
          }))
        }));

        setCampaignData(formattedResult);
      });
    }

    if (isCrossCampaign) {
      onFetchCampaign();
    }
  }, [isCrossCampaign, isCampaign]);

  return (
    <>
      {(isCampaign || isCrossCampaign) && (
        <Whisper
          trigger="hover"
          placement="right"
          speaker={<Tooltip>{formatMessage('i18n_back')}</Tooltip>}
        >
          <div className="back-icon" onClick={handleItemClicked}>
            <Icon icon="list" />
          </div>
        </Whisper>
      )}
      <div className="project-nav-wrap" ref={containerRef}>
        <Nav>
          {(isCampaign || isCrossCampaign) && (
            <Nav.Item
              onClick={handleItemClicked}
              icon={
                expand ? <Icon icon="angle-left" /> : <SvgIcon icon="list" width={16} height={16} />
              }
              className="sidebar-projects-nav"
            >
              <span className="rs-nav-item-text">{formatMessage('i18n_back')}</span>
            </Nav.Item>
          )}
          {isCrossCampaign && (
            <SelectPicker
              {...pickerProps}
              loading={false}
              placeholder={false ? formatMessage('i18n_loading') : ''}
              container={(): HTMLDivElement => containerRef.current}
            />
          )}
          {isCampaign && (
            <TreePicker
              {...pickerProps}
              data={campaignData}
              value={campaignInfo.id}
              loading={campaignLoading}
              onExpand={handleExpandCampaign}
              onSelect={handleSelect}
              renderTreeIcon={renderTreeIcon}
              placeholder={
                campaignDetailLoading ? formatMessage('i18n_loading') : campaignInfo.name
              }
              defaultExpandAll={false}
              container={(): HTMLDivElement => containerRef.current}
            />
          )}
        </Nav>
      </div>
    </>
  );
});
