import React, { memo, useCallback, useMemo, useState, useEffect } from 'react';
import { Button } from 'rsuite';

import * as APIs from '@/constants/APIs';
import Table, { Column, HeaderCell } from '@/components/Table';
import { FloatActionCell, VerifyCell } from '@/components/Table/Cells';
import SelectPicker from '@/components/Picker/SelectPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps } from '@/constants/config';

import { filterParamKeys, isForVerifyData, TAB_KEY, utilsFn } from './constants';

type TableProps = BasicReportProps & {
  tab: TAB_KEY;
  handleFormCreate: Function;
  handleFormEdit: Function;
};

export default memo(
  ({
    tab,
    filterParams,
    tableParams,
    setTableParams,
    handleSearchWord,
    handleChangeCustomParam,
    handleFormCreate,
    handleFormEdit
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const isForVerifyDataIntl = usePickerDataFromObj(isForVerifyData);
    const { tableData, onChange } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_PARA,
      postParams: {
        ...(tab === TAB_KEY.USER
          ? { hideDefaultAttr: 1, hideUserAttr: '' }
          : { hideUserAttr: 1, hideDefaultAttr: '' })
      }
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    // 需要在局部维护一个searchWord，是因为切换tab时需要重置search input，因此需要受控
    // 但不能直接使用filterParams中的参数，是因为handleSearchWord做了debounce处理，用户输入会延迟影响到filterParams，造成卡顿感受
    const [searchWord, setSearchWord] = useState('');
    const handleSearch = useCallback(
      word => {
        setSearchWord(word);
        handleSearchWord(word);
      },
      [handleSearchWord]
    );
    useEffect(() => {
      setSearchWord('');
    }, [tab]);

    const handleChangeIsForVerify = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.IS_FOR_VERIFY]: val
        });
      },
      [handleChangeCustomParam]
    );

    const renderToolbar = (): React.ReactElement[] => {
      return [
        tab === TAB_KEY.USER && (
          <Button appearance="primary" key="new" onClick={handleFormCreate as () => void}>
            {formatMessage('i18n_create')}
          </Button>
        ),
        <SelectPicker
          key="isForVerify"
          prepend={formatMessage('i18n_isForVerify')}
          data={isForVerifyDataIntl}
          value={utilsFn.getIsForVerify(filterParams)}
          onChange={handleChangeIsForVerify}
        />,
        <SearchInput
          key="search"
          value={searchWord}
          placeholder={formatMessage('i18n_name')}
          onChange={handleSearch}
        />
      ];
    };

    const renderActions = useCallback(
      rowData => {
        return (
          tab === TAB_KEY.USER && (
            <FloatActionCell.Actions.Edit
              onClick={() => {
                handleFormEdit({
                  id: rowData.id
                });
              }}
            />
          )
        );
      },
      [tab, handleFormEdit]
    );

    return (
      <Table
        {...tableDataMemo}
        renderToolbar={renderToolbar}
        offsetTopHeight={56}
        onChange={onChange}
      >
        <Column flexGrow={1} minWidth={200} fixed sortable>
          <HeaderCell>{formatMessage('i18n_name')}</HeaderCell>
          <FloatActionCell dataKey="name" renderActions={renderActions} />
        </Column>

        {/* BE */}
        {tab === TAB_KEY.USER && (
          <Column flexGrow={1} minWidth={200} sortable>
            <HeaderCell>BE</HeaderCell>
            <FloatActionCell dataKey="beName" />
          </Column>
        )}

        {/* 跟踪代码参数 */}
        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_codeParameter')}</HeaderCell>
          <FloatActionCell dataKey="paraTrackCodeKey" />
        </Column>

        {/* 是否用于识别用户 */}
        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_isForVerify')}</HeaderCell>
          <VerifyCell dataKey="isForVerify" />
        </Column>

        {/* 描述 */}
        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_description')}</HeaderCell>
          <FloatActionCell dataKey="desc" />
        </Column>
      </Table>
    );
  }
);
