import React, { useState, useCallback, useRef } from 'react';

interface DrawerDisplayProps {
  show: boolean;
  loading: boolean;
  submitting: boolean;
}

interface Props {
  mapFromResponse?: Function;
  mapErrors?: Function;
  submitSuccessCallback?: Function;
}

interface Result {
  drawer: DrawerDisplayProps;
  drawerRef: React.RefObject<any>;
  handleShow: Function; // 显示
  handleClose: Function; // 关闭
  setDrawer: Function;
}

export default function useDrawerForm({ submitSuccessCallback }: Props): Result {
  const [drawer, setDrawer] = useState<DrawerDisplayProps>({
    show: false,
    loading: false,
    submitting: false
  });
  const drawerRef = useRef(null);

  // 显示
  const handleShow = useCallback(() => {
    setDrawer({
      show: true,
      loading: false,
      submitting: false
    });
  }, []);

  // 关闭
  const handleClose = useCallback(() => {
    setDrawer({
      show: false,
      loading: false,
      submitting: false
    });
    submitSuccessCallback && submitSuccessCallback();
  }, [submitSuccessCallback]);

  return {
    drawer,
    drawerRef,
    handleShow,
    handleClose,
    setDrawer
  };
}
