import React from 'react';
import _ from 'lodash';
import CheckTreePicker, { CustomCheckTreePickerProps } from '@/components/Picker/CheckTreePicker';
import * as APIs from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface ReqParamProps {
  beId?: Array<number>;
  campaignId?: number | string;
  sub?: number;
}
interface AdvBrandProps<T = any> {
  chosenAdvBrand: ReadonlyArray<T>;
  reqParam?: ReqParamProps;
}

const pickerDefaultParam = {
  supportAll: true,
  style: { maxWidth: '300px' },
  menuStyle: { maxWidth: '400px' },
  height: 250,
  cleanable: true,
  searchable: true
};

export default ({
  chosenAdvBrand,
  reqParam,
  ...rest
}: AdvBrandProps & CustomCheckTreePickerProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const beId = _.get(reqParam, 'beId', '') ? _.get(reqParam, 'beId', '').toString() : null;
  return (
    <CheckTreePicker
      value={chosenAdvBrand}
      url={APIs.API_ADVERTISER}
      params={{
        status: 1,
        ...reqParam,
        orderColumn: 'name',
        orderType: 'asc',
        beId
      }}
      prepend={formatMessage('i18n_brandFilter')}
      {...pickerDefaultParam}
      {...rest}
    />
  );
};
