import React, { memo } from 'react';
import _ from 'lodash';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import Toggle from '@/components/Form/Toggle';
import { ConversionGroupModel } from '@/constants/form/models';
import SelectPicker from '@/components/Picker/SelectPicker';
import { API_BE } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

const beParams = {
  orderColumn: 'name',
  orderType: 'asc',
  status: 1
};

export default memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();
    const title =
      formatMessage(`i18n_${drawer.isEdit ? 'edit' : 'create'}`) +
      formatMessage('i18n_conversiongroup');
    return (
      <Drawer
        show={drawer.show}
        title={title}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        onSubmit={onSubmit}
        onHide={onClose}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={ConversionGroupModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} />
          {/* 状态 */}
          <CustomField
            name="status"
            label={formatMessage('i18n_status')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_enabled')}
            unCheckedChildren={formatMessage('i18n_disabled')}
          />
          {/* 所属BE */}
          <CustomField
            name="beId"
            label={formatMessage('i18n_primaryOwner')}
            accepter={SelectPicker}
            placeholder={formatMessage('i18n_select', { name: 'BE' })}
            url={API_BE}
            params={beParams}
            menuStyle={{ maxWidth: '400px' }}
            required
            searchable
            disabled={drawer.isEdit}
          />
        </Form>
      </Drawer>
    );
  }
);
