import React, { useCallback, useMemo, useContext } from 'react';
import { Icon } from 'rsuite';
import _ from 'lodash';

import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';

import { FilterNodeConfig, FILTER_OPERATION } from '../constants';
import KeyColumn from './RowColumn/KeyColumn';
import OperatorColumn, { getDefaultOpValue } from './RowColumn/OperatorColumn';
import ValueColumn from './RowColumn/ValueColumn';
import { PickerAsyncDataCtx, FilterExtCtx } from '../context';

export interface FilterRowValue {
  key?: string;
  operation?: string;
  value?: Array<any> | string;
}

export interface FilterRowProps {
  index: number;
  id: string;
  filterData?: Array<FilterNodeConfig>;
  filterValue?: FilterRowValue;
  onUpdate?: (id: string, newFilterVal: FilterRowValue & { id: string }) => void;
  onDelete: (id: string) => void;
}

const getFilterItemByFilterKey = (filterData: Array<FilterNodeConfig>, filterKey: string) => {
  return (_.find(filterData, { key: filterKey }) || {}) as FilterNodeConfig;
};

// 找 filterList 里面对应当前第一列key的项的operation
const getFilterItemField = (
  filterData: Array<FilterNodeConfig>,
  filterKey: string,
  targetField: keyof FilterNodeConfig
) => getFilterItemByFilterKey(filterData, filterKey)[targetField];

export const FilterRow = ({
  index,
  id,
  filterData = [],
  filterValue = {},
  onUpdate,
  onDelete
}: FilterRowProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const pickerDataResult = useContext(PickerAsyncDataCtx);
  const { disabled } = useContext(FilterExtCtx);
  const className = `filter-tools-wrapper ${index > 0 ? 'or' : ''}`;
  const title = !index ? formatMessage('i18n_filter') : formatMessage('i18n_with');

  // 第一个组件的候选项
  const allFilterKeyData = useMemo(() => filterData.map(f => ({ id: f.key, name: f.label })), [
    filterData
  ]);
  // 从当前的value中获取到当前行的值 { key, operation, value }
  const {
    key: keyOfCurrentRow,
    operation: operatorOfCurrentRow,
    value: valueOfCurrentRow
  } = filterValue;
  // 根据当前的key的选中的值或获取一些当前选择的filter的常量配置数据
  const {
    operation: itemOperator,
    type: itemType,
    label: itemLabel,
    asyncFnKey: itemAsyncKeys,
    data: itemConstData = [],
    labelKey: itemdataLabelKey,
    valueKey: itemDataValueKey,
    childrenKey: itemDataChildrenKey,
    i18nFormatKey: itemi18nKey,
    valueInnerToOuter: itemInnerToOuter,
    valueOuterToInner: itemOuterToInner,
    mapLabel: itemMapLabel
  } = getFilterItemByFilterKey(filterData, keyOfCurrentRow);

  const valueResultOfCurrentRow = _.isFunction(itemOuterToInner)
    ? itemOuterToInner(valueOfCurrentRow as Array<any>)
    : valueOfCurrentRow;
  const currentConstDataIntl = usePickerDataFromObj(itemConstData, {
    ...(itemi18nKey && { labelKey: itemi18nKey, skipNoi18nPrefix: true }),
    ...(!itemi18nKey && { notFormat: true })
  });
  // 只取第一个
  const firstAsyncKey = _.first(itemAsyncKeys);
  let valueColumnData = []; // 需要约定下 value的key，不过通过config 配置也是可以的
  let valueColumnLoading = false;

  if (firstAsyncKey) {
    // 针对第3列是请求的数据，一般第三列可以直接拿到吧，第四列的话如果有，需要自己调用context中回调函数的API获取
    const result = pickerDataResult[firstAsyncKey];
    valueColumnData = _.get(result, 'data');
    valueColumnLoading = _.get(result, 'loading');
  } else {
    valueColumnData = currentConstDataIntl;
    valueColumnLoading = false;
  }

  const handleDelClicked = useCallback(() => {
    onDelete && onDelete(id);
  }, [id, onDelete]);

  // 更新第一个项 key
  const handleUpdateKey = useCallback(
    (val: string) => {
      if (val === keyOfCurrentRow) {
        return;
      }

      const defaultOpVal = getDefaultOpValue(getFilterItemField(
        filterData,
        val,
        'operation'
      ) as FILTER_OPERATION);
      const defaultValueVal = getFilterItemField(filterData, val, 'defaultValue');
      onUpdate &&
        onUpdate(id, {
          id,
          key: val,
          operation: defaultOpVal,
          value: defaultValueVal as string | Array<any>
        });
    },
    [id, onUpdate, filterData, keyOfCurrentRow]
  );

  // 更新第二个项 operator
  const handleUpdateOperator = useCallback(
    (val: FILTER_OPERATION) => {
      if (val === operatorOfCurrentRow) {
        return;
      }

      const defaultValueVal = getFilterItemField(filterData, keyOfCurrentRow, 'defaultValue');
      onUpdate &&
        onUpdate(id, {
          id,
          key: keyOfCurrentRow,
          operation: val,
          value: defaultValueVal as string | Array<any>
        });
    },
    [id, onUpdate, keyOfCurrentRow, filterData, operatorOfCurrentRow]
  );

  // 更新第三个项 value
  const handleUpdateValue = useCallback(
    (val: any) => {
      if (val === valueOfCurrentRow) {
        return;
      }

      const result = _.isFunction(itemInnerToOuter) ? itemInnerToOuter(val) : val;
      onUpdate &&
        onUpdate(id, {
          id,
          key: keyOfCurrentRow,
          operation: operatorOfCurrentRow,
          value: result
        });
    },
    [id, onUpdate, keyOfCurrentRow, operatorOfCurrentRow, valueOfCurrentRow, itemInnerToOuter]
  );

  return (
    <div className={className}>
      <div className="filter-tools-title">{title}</div>
      <div className="filter-tools-select">
        {/** 选择filter的key */}
        <KeyColumn data={allFilterKeyData} value={keyOfCurrentRow} onChange={handleUpdateKey} />
        {keyOfCurrentRow && (
          <OperatorColumn
            key={keyOfCurrentRow}
            operatorKey={itemOperator}
            operatorValue={operatorOfCurrentRow}
            onChange={handleUpdateOperator}
          />
        )}
        {operatorOfCurrentRow && keyOfCurrentRow && (
          <div className="value-column">
            <ValueColumn
              key={`${operatorOfCurrentRow}-${keyOfCurrentRow}`} // 这个很重要，不然的话切换key可能会出现这个组件没有重新渲染，data一样的情况
              disabled={disabled}
              loading={valueColumnLoading}
              data={valueColumnData}
              filterLabel={itemLabel}
              filterAsyncKeys={itemAsyncKeys}
              filterKey={keyOfCurrentRow}
              labelKey={itemdataLabelKey}
              valueKey={itemDataValueKey}
              childrenKey={itemDataChildrenKey}
              type={itemType}
              value={valueResultOfCurrentRow}
              onChange={handleUpdateValue}
              mapLabel={itemMapLabel}
            />
          </div>
        )}
      </div>
      <div className="filter-tools-action">
        {/*eslint-disable */}
        <a className="btn filter-btn-close" onClick={disabled ? undefined : handleDelClicked}>
          <Icon icon="close" />
        </a>
        {/* eslint-enable */}
      </div>
    </div>
  );
};

FilterRow.displayName = 'FilterRow';
