import React, { memo, useCallback, useMemo } from 'react';
import { Button } from 'rsuite';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import BEPicker from '@/components/Picker/logic/BEPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { FloatActionCell, NameCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { useModal } from '@/components/hooks/useModal';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import template from '@/utils/template';
import { BasicReportProps } from '@/constants/config';
import { API_TEAM } from '@/constants/APIs';
import { filterParamKeys, utilsFn } from './constants';
import Modal from './Modal';

const mapStatusFn = (statusFromBackend: number) => {
  switch (statusFromBackend) {
    case 1:
      return 'active';
    case 0:
    default:
      return 'paused';
  }
};

type TableProps = BasicReportProps & {
  handleFormCreate: Function;
  handleFormEdit: Function;
};

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    refetchTableData,
    handleSearchWord,
    handleChangeCustomParam,
    handleFormCreate,
    handleFormEdit
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: API_TEAM
    });

    const modal = useModal(false, {}, template`${API_TEAM}/${'teamId'}`, refetchTableData);
    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleBEChange = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.BE]: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleExportClicked = useCallback(() => {
      onExport && onExport({}, template`${API_TEAM}.xlsx`);
    }, [onExport]);

    const renderToolbar = () => {
      return (
        <>
          <Button appearance="primary" onClick={handleFormCreate as () => void}>
            {formatMessage('i18n_create')}
          </Button>
          <BEPicker
            chosenBE={utilsFn.getChosenBE(filterParams)}
            beChangeHandler={handleBEChange}
            isReport={false}
          />
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
          <Button key="exportTable" className="pull-right" onClick={handleExportClicked}>
            {formatMessage('i18n_exportTable')}
          </Button>
          ,
        </>
      );
    };

    const renderActions = useCallback(
      rowData => {
        const { status } = rowData;

        const disableClickHandler = () => {
          modal.showModal({
            ...rowData,
            status: 0
          });
        };

        const enableClickHandler = () => {
          modal.showModal({
            ...rowData,
            status: 1
          });
        };

        return [
          <FloatActionCell.Actions.Edit
            key="edit"
            onClick={() => {
              handleFormEdit({
                id: rowData.id
              });
            }}
          />,
          status === 1 ? (
            <FloatActionCell.Actions.Disable key="disable" onClick={disableClickHandler} />
          ) : (
              <FloatActionCell.Actions.Enable key="enable" onClick={enableClickHandler} />
            )
        ];
      },
      [handleFormEdit, modal]
    );

    return (
      <>
        <Modal {...modal} />
        <Table {...tableDataMemo} onChange={onChange} renderToolbar={renderToolbar} wordWrap>
          {/* ID */}
          <Column width={100} fixed sortable>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>

          {/* 名称 */}
          <Column flexGrow={1} minWidth={200} fixed sortable>
            <HeaderCell>{formatMessage('i18n_name')}</HeaderCell>
            <NameCell
              dataKey="name"
              showStatus={true}
              statusMapperFn={mapStatusFn}
              renderActions={renderActions}
            />
          </Column>

          {/* 所属BE */}
          <Column flexGrow={1} minWidth={250} sortable>
            <HeaderCell>{formatMessage('i18n_belongBE')}</HeaderCell>
            <FloatActionCell dataKey="BE" />
          </Column>

          {/* 备注 */}
          <Column flexGrow={1} minWidth={250} sortable>
            <HeaderCell>{formatMessage('i18n_remark')}</HeaderCell>
            <FloatActionCell dataKey="note" />
          </Column>
        </Table>
      </>
    );
  }
);
