import React, { memo, useContext, useMemo, useCallback } from 'react';
import { Panel } from 'rsuite';
import _ from 'lodash';
import { API_CONVERSION_REPORT } from '@/constants/APIs';
import { useUserId } from '@/components/hooks/useCertainParams';
import { useMainPage } from '@/components/hooks/useMainPage';
import Toolbar, { pickerName } from '../Toolbar';
import {
  defaultFilter,
  hierarchy,
  reportKeys,
  FilterParam,
  commonParamFormatter,
  filterParamKeys
} from '../constants';
import { UserModuleDateCtx } from '../../context';
import SecondToolbar from './SecondToolbar';
import Chart from '@/components/Chart/ReportCustomChart';
import Table from './Table';
import { cellData } from './constants';

const customParamFormatter = (filterParam: FilterParam) => {
  const originIndicators = _.get(filterParam, filterParamKeys.INDICATORS);
  const originModel = _.get(filterParam, filterParamKeys.ATTRMODEL);
  return {
    ...filterParam,
    [filterParamKeys.INDICATORS]: !_.isEmpty(originIndicators) ? originIndicators.join(',') : '',
    [filterParamKeys.ATTRMODEL]: !_.isEmpty(originModel) ? originModel.join(',') : ''
  };
};

export default memo(() => {
  const {
    commonParam,
    fmtCommonParam,
    handleChangeCommonParam,
    handleDebouncedChangeCommonParam,
    customParamResult
  } = useMainPage({
    defaultCommonParam: {
      ..._.omit(defaultFilter, [
        filterParamKeys.SEG,
        filterParamKeys.INDICATORS,
        filterParamKeys.ATTRMODEL
      ])
    },
    commonParamFormatter,
    customConfig: [
      {
        isTable: true,
        defaultParam: {
          [filterParamKeys.SEG]: defaultFilter[filterParamKeys.SEG],
          [filterParamKeys.INDICATORS]: cellData.map((item: any) => item.id),
          [filterParamKeys.ATTRMODEL]: ['default']
        },
        paramFormatter: customParamFormatter
      }
    ]
  });
  const userId = useUserId();
  const [tableResult] = customParamResult;
  const { param, fmtParam, handleChangePagination, handleChangeCustomParam } = tableResult;
  const { dateRangeValTimeFormat, setDateRangeValByDateArray } = useContext(UserModuleDateCtx);

  const postParams = useMemo(
    () => ({
      id: userId,
      hierarchy,
      report: reportKeys.ATTRIBUTION
    }),
    [userId]
  );
  const handleSearchWord = useCallback(
    (word: string) => {
      handleDebouncedChangeCommonParam({
        sSearch: word
      });
    },
    [handleDebouncedChangeCommonParam]
  );
  return (
    <>
      <Panel className="page-toolbar">
        <Toolbar
          show={[pickerName.BE, pickerName.ADVBRAND, pickerName.ACTION]}
          dateParams={dateRangeValTimeFormat}
          filterParams={commonParam}
          handleChangeDate={setDateRangeValByDateArray}
          handleChangeCommonParam={handleChangeCommonParam}
        />
      </Panel>
      <Panel className="panel-table">
        <SecondToolbar
          filterParams={commonParam}
          handleChangeCommonParam={handleChangeCommonParam}
          handleSearchWord={handleSearchWord}
        />

        <Chart
          dateParams={dateRangeValTimeFormat}
          filterParams={fmtCommonParam}
          postParams={postParams}
          api={API_CONVERSION_REPORT}
          collapsible
          customType={2}
        />
        <Table
          dateParams={dateRangeValTimeFormat}
          filterParams={param}
          tableParams={fmtParam}
          postParams={postParams}
          setTableParams={handleChangePagination}
          handleSearchWord={handleSearchWord}
          handleChangeCustomParam={handleChangeCustomParam}
        />
      </Panel>
    </>
  );
});
