import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';

import * as APIs from '@/constants/APIs';

import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell, DateCell, TextCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps, secondaryData } from '@/constants/config';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { segData, filterParamKeys } from '@/views/common/conversionReport/constants';
import { FilterParam } from './constants';

const mockData: CustomTableDataProps<CellDataProps> = {
  data: [
    {
      secondary: '2020/03/09',
      value: 'aqy',
      placementId: 1,
      impressions: 40,
      clicks: 20,
      uimpressions: '--',
      uclicks: '--',
      clickRate: 50.0,
      placementName: 'test'
    }
  ],
  page: {
    current: 0,
    pagesize: 10,
    total: 6,
    size: 100
  },
  total: {
    impressions: 140,
    uimpressions: 70,
    clicks: 70,
    uclicks: '--'
  }
};
interface CellDataProps {
  secondary: string;
  value: string;
  placementId: number;
  impressions: string | number;
  clicks: string | number;
  uimpressions: string | number;
  uclicks: string | number;
  clickRate: number;
  placementName: string;
}

type TableProps = Partial<BasicReportProps<FilterParam>>;
export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    handleSearchWord,
    setTableParams
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();

    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: APIs.API_TAB_REPORT,
      postParams
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleExportClicked = useCallback(() => {
      onExport && onExport(postParams, APIs.API_XLSX);
    }, [onExport, postParams]);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_parameterValue')}
          onChange={handleSearchWord}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };
    // 渲染表列
    const renderCol = (): React.ReactElement[] => {
      // 判断是否显示平台列 高级过滤包含平台||细分为平台
      const platform = _.filter(filterParams.advanceFilter, { key: 'platform' })[0];
      const hasPlatform = !_.isEmpty(_.get(platform, 'value'));
      const showPlatform = filterParams.segmentation === 'platform' || hasPlatform;
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams.secondary)[0],
        'label'
      );

      return [
        <Column width={100} sortable fixed key="id">
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>,
        <Column width={200} sortable resizable fixed key="para">
          <HeaderCell>{formatMessage('i18n_customAttributesValue')}</HeaderCell>
          <TextCell dataKey="value" maxLine={1} />
        </Column>,
        <Column width={200} sortable resizable fixed key="placement">
          <HeaderCell>{formatMessage(`i18n_${filterParams.mDimension}`)}</HeaderCell>
          <TextCell dataKey="name" maxLine={1} />
        </Column>,
        filterParams.mDimension === 'placement' && (
          <Column width={150} sortable resizable fixed key="site">
            <HeaderCell>{formatMessage('i18n_site')}</HeaderCell>
            <TextCell dataKey="siteName" maxLine={1} />
          </Column>
        ),
        showPlatform && (
          <Column width={150} sortable resizable key="segmentation">
            <HeaderCell> {formatMessage('i18n_platform')}</HeaderCell>
            <Cell dataKey="segmentation" />
          </Column>
        ),
        filterParams.secondary && (
          <Column
            width={filterParams.secondary === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams.secondary === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        filterParams.secondary === 'hourly' && (
          <Column width={100} key="hourly" sortable resizable>
            <HeaderCell>{formatMessage('i18n_hour')}</HeaderCell>
            <Cell dataKey="timeslot" />
          </Column>
        ),
        <Column width={150} align="right" sortable key="impressions">
          <HeaderCell>{formatMessage('i18n_impressionsNum')}</HeaderCell>
          <NumberCell dataKey="impressions" />
        </Column>,
        <Column width={150} align="right" sortable key="uimpressions">
          <HeaderCell>{formatMessage('i18n_uniqueImpressionsNum')}</HeaderCell>
          <NumberCell dataKey="uimpressions" />
        </Column>,
        <Column width={150} align="right" sortable key="clicks">
          <HeaderCell>{formatMessage('i18n_clicksNum')}</HeaderCell>
          <NumberCell dataKey="clicks" />
        </Column>,
        <Column width={200} align="right" sortable key="uclicks">
          <HeaderCell>{formatMessage('i18n_uniqueClicksNum')}</HeaderCell>
          <NumberCell dataKey="uclicks" />
        </Column>,
        <Column width={150} align="right" sortable key="ctr">
          <HeaderCell>{formatMessage('i18n_ctr')}</HeaderCell>
          <Cell dataKey="clickRate" />
        </Column>
      ];
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
        {renderCol()}
      </Table>
    );
  }
);
