import React, { useState, useCallback, useMemo, useEffect } from 'react';
import { Panel, Breadcrumb, Steps, Alert } from 'rsuite';
import { useHistory, useParams } from 'react-router-dom';
import _ from 'lodash';

import { API_CROSS_CAMPAIGN } from '@/constants/APIs';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { useUserId } from '@/components/hooks/useCertainParams';
import { CrossCampaignModel } from '@/constants/form/models';

import Step2 from './step2';
import Step1 from './step1';
import { CreateFormCtx } from './context';
import { mapFromResponse, defaultFormData, mapToRequest } from './constants';

interface CUCommonProps {
  isEdit: boolean;
}

const showError = (errors: any, handlePrevStep: Function) => {
  const step1Fields = [
    'name',
    'desc',
    'targetPeople',
    'age',
    'measure',
    'tencentTotalReport',
    'calendar',
    'enableFreq',
    'algorithmAdjust',
    'crossLevel'
  ];
  if (!_.isEmpty(_.intersection(_.keys(errors), step1Fields))) {
    handlePrevStep && handlePrevStep();
  } else if (_.includes(_.keys(errors), 'error')) {
    Alert.error(errors.error);
  }
};

export default React.memo(({ isEdit }: CUCommonProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const history = useHistory();
  const userId = useUserId();
  const { id } = useParams();
  const [step, setStep] = useState(0);
  const [selectedAvailable, setSelectedAvailable] = useState([]);

  const handelNavToCrossCampaign = useCallback(() => {
    history.push('/cross-campaigns');
  }, [history, userId]);

  const submitErrorCallback = useCallback((err: any) => {
    showError(err, () => {
      setStep(0);
    });
  }, []);

  const submitSuccessCallback = useCallback(() => {
    handelNavToCrossCampaign();
  }, [handelNavToCrossCampaign]);

  const fetchEditCallback = useCallback((res: any) => {
    setSelectedAvailable(_.get(res, 'crossLevelItems', []).map((i: any) => i.id));
  }, []);

  const drawerFormResult = useDrawerForm({
    defaultFormData,
    api: API_CROSS_CAMPAIGN,
    mapFromResponse,
    mapToRequest,
    model: CrossCampaignModel,
    submitErrorCallback,
    submitSuccessCallback,
    fetchEditCallback
  });

  const handleCancel = useCallback(() => {
    history.push('/cross-campaigns');
  }, [history, userId]);

  const ctxValMemo = useMemo(
    () => ({
      drawerFormResult,
      isEdit,
      setStep,
      handleCancel,
      setSelectedAvailable,
      selectedAvailable
    }),
    [handleCancel, isEdit, drawerFormResult, setSelectedAvailable, selectedAvailable]
  );

  useEffect(() => {
    if (isEdit && id) {
      drawerFormResult.handleFormEdit({ id });
    } else {
      drawerFormResult.handleFormCreate();
    }
  }, [isEdit, id]);

  return (
    <div className="page-config-cross-form">
      <CreateFormCtx.Provider value={ctxValMemo}>
        <Breadcrumb>
          <Breadcrumb.Item onClick={handelNavToCrossCampaign}>
            {formatMessage('i18n_menuCrossCampaignManage')}
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            {formatMessage('i18n_create')}/{formatMessage('i18n_edit')}
          </Breadcrumb.Item>
        </Breadcrumb>
        <Panel className="panel-table">
          <Steps current={step}>
            <Steps.Item title={formatMessage('i18n_basicInfo')} />
            <Steps.Item title={formatMessage('i18n_selectCrossProject')} />
          </Steps>
          {step === 0 && <Step1 />}
          {step === 1 && <Step2 />}
        </Panel>
      </CreateFormCtx.Provider>
    </div>
  );
});
