import React, { useMemo } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';
import { useHistory } from 'react-router';

import Table, { Cell, Column, HeaderCell } from '@/components/Table';
import {
  DateTimeCell,
  FloatActionCell,
  TextCell,
  MultiLineCell,
  CampaignStatusCell,
  CpoStatusCell,
  DateCell
} from '@/components/Table/Cells';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SelectPicker from '@/components/Picker/SelectPicker';
import AdvBrandPicker from '@/components/Picker/logic/AdvBrandPicker';
import BEPicker from '@/components/Picker/logic/BEPicker';
import { getShortNameById, BasicReportProps } from '@/constants/config';
import { API_CAMPAIGN, API_CAMPAIGN_GET } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { useModal } from '@/components/hooks/useModal';
import { getUserProfile } from '@/utils/location';
import { useUserId } from '@/components/hooks/useCertainParams';
import { defaultCampaignIdKeyFromState } from '@/views/common/management/placement/constants';
import { statusData, FilterParam, checkEditable } from './constants';
import Modal from './Modal';
import template from '@/utils/template';

const { useCallback } = React;
const maxLine = 3;
type TableProps = BasicReportProps<any, FilterParam> & {
  onCreate: () => void;
  onEdit: Function;
  onCpoEdit: Function;
};

const Component = React.memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    handleSearchWord,
    handleChangeCustomParam,
    onCreate,
    onEdit,
    onCpoEdit,
    refetchTableData,
    ...props
  }: TableProps): React.ReactElement => {
    const { internal } = getUserProfile();
    const { formatMessage } = useFmtMsgFori18n();
    const history = useHistory();
    const userId = useUserId();
    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: API_CAMPAIGN
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
    const updateModal = useModal(false, {}, API_CAMPAIGN_GET, refetchTableData);

    // 改变filter
    const handleChangeStatus = useCallback(
      value => {
        handleChangeCustomParam({
          status: value
        });
      },
      [handleChangeCustomParam]
    );

    const handleBEChange = useCallback(
      val => {
        handleChangeCustomParam({
          beId: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleChangeAdvertiser = useCallback(
      value => {
        handleChangeCustomParam({
          advertiser: value
        });
      },
      [handleChangeCustomParam]
    );

    const handleExport = useCallback(() => {
      onExport({}, template`${API_CAMPAIGN}.xlsx`);
    }, [onExport]);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <Button key="create" appearance="primary" onClick={onCreate}>
          {formatMessage('i18n_create')}
        </Button>,
        <BEPicker
          key="be"
          chosenBE={filterParams.beId}
          beChangeHandler={handleBEChange}
          isReport={false}
        />,
        <AdvBrandPicker
          key="advBrand"
          className="cus-picker"
          supportAll
          chosenAdvBrand={filterParams.advertiser}
          onChange={handleChangeAdvertiser}
        />,
        <SelectPicker
          key="status"
          prepend={formatMessage('i18n_status')}
          labelKey="label"
          valueKey="value"
          data={statusData}
          value={filterParams.status}
          onChange={handleChangeStatus}
          style={{
            width: 200
          }}
        />,
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button key="exportTable" className="pull-right" onClick={handleExport}>
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };

    // 定义cell button
    const renderActions = useCallback(
      (rowData: Record<string, any>) => {
        const { name, status, id, cpoReportStatus } = rowData;
        const editAble = checkEditable(status);
        const handleManagementClicked = () => {
          history.push('/placement-management', { [defaultCampaignIdKeyFromState]: id });
        };

        const handleComplete = () => {
          updateModal.showModal({
            status: 4,
            name,
            id
          });
        };

        const handleCancel = () => {
          updateModal.showModal({
            status: 0,
            name,
            id
          });
        };
        const list = [
          <FloatActionCell.Actions.Manage onClick={handleManagementClicked} key="detail" />,
          <FloatActionCell.Actions.Edit
            key="edit"
            onClick={() => {
              onEdit({ id: rowData.id });
            }}
          />,
          <FloatActionCell.Actions.Complete
            key="complete"
            onClick={handleComplete}
            disabled={!editAble}
          />,
          <FloatActionCell.Actions.Cancel
            key="cancel"
            onClick={handleCancel}
            disabled={!editAble}
          />
        ];
        internal &&
          list.splice(
            2,
            0,
            <FloatActionCell.Actions.Cpo
              key="cpo"
              disabled={cpoReportStatus === 'calculating'}
              onClick={() => {
                onCpoEdit({ id: rowData.id });
              }}
            />
          );
        return list;
      },
      [onEdit, userId, history, updateModal]
    );

    return (
      <>
        <Modal {...updateModal} />
        <Table
          {...props}
          {...tableDataMemo}
          renderToolbar={renderToolbar}
          onChange={onChange}
          wordWrap
        >
          {/* ID */}
          <Column width={100} fixed sortable>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>
          {/* 推广计划名称 */}
          <Column width={200} resizable fixed sortable>
            <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
            <TextCell dataKey="name" renderActions={renderActions} maxLine={maxLine} />
          </Column>
          {/* 主要拥有者 */}
          <Column width={200} resizable sortable>
            <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
            <TextCell
              dataKey="primary"
              formatter={d =>
                `${getShortNameById(_.get(d, 'role', ''))}: ${_.get(d, 'beName', '')}`
              }
              maxLine={maxLine}
            />
          </Column>
          {/* 次要拥有者 */}
          <Column width={200} resizable>
            <HeaderCell>{formatMessage('i18n_secondaryOwner')}</HeaderCell>
            <MultiLineCell
              dataKey="secondaries"
              maxLine={maxLine}
              formatter={d =>
                _.isArray(d) &&
                d.reduce(
                  (acc, i) =>
                    _.isEmpty(i)
                      ? acc
                      : acc.concat(
                        `${getShortNameById(_.get(i, 'role', ''))}: ${_.get(i, 'beName', '')}`
                      ),
                  []
                )
              }
            />
          </Column>
          {/* 广告主 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_advertiser')}</HeaderCell>
            <TextCell dataKey="advertiser" maxLine={maxLine} />
          </Column>
          {/* 品牌 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_brand')}</HeaderCell>
            <TextCell dataKey="brand" maxLine={maxLine} />
          </Column>
          {/* 状态 */}
          <Column width={150} sortable>
            <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
            <CampaignStatusCell dataKey="status" />
          </Column>
          {/* 开始日期 */}
          <Column width={180} sortable>
            <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
            <DateCell dataKey="startDate" />
          </Column>
          {/* 结束日期 */}
          <Column width={180} sortable>
            <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
            <DateCell dataKey="endDate" />
          </Column>
          {/* 数据收集开始日期 */}
          <Column width={180} sortable>
            <HeaderCell>{formatMessage('i18n_dataCollectionStartDate')}</HeaderCell>
            <DateCell dataKey="collectStartDate" />
          </Column>
          {/* 数据处理结束日期 */}
          <Column width={180} sortable>
            <HeaderCell>{formatMessage('i18n_dataCollectionEndDate')}</HeaderCell>
            <DateCell dataKey="processingEndDate" />
          </Column>
          {/* 目标人群 */}
          <Column width={150} sortable>
            <HeaderCell>{formatMessage('i18n_targetPerson')}</HeaderCell>
            <FloatActionCell dataKey="target" />
          </Column>
          {/* 监测类型 */}
          <Column width={250}>
            <HeaderCell>{formatMessage('i18n_monitorType')}</HeaderCell>
            <FloatActionCell dataKey="monitorType" />
          </Column>
          {/* 频次 */}
          {/* <Column flexGrow={1} minWidth={100}>
          <HeaderCell>{formatMessage('i18n_frequency')}</HeaderCell>
          <FloatActionCell dataKey='frequency' />
        </Column> */}
          {/* 受众累计报 */}
          {/* <Column flexGrow={1} minWidth={250} sortable>
          <HeaderCell>{formatMessage('i18n_tencentTotalReport')}</HeaderCell>
          <FloatActionCell dataKey='tencent' />
        </Column> */}
          {/* 受众测量 */}
          {/* <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_audienceMeasurement')}</HeaderCell>
          <FloatActionCell dataKey='measure' />
        </Column> */}
          {/* UA频次 */}
          {/* <Column flexGrow={1} minWidth={100}>
          <HeaderCell>{formatMessage('i18n_UAFreq')}</HeaderCell>
          <FloatActionCell dataKey='UAfrequency' />
        </Column> */}
          {/* 站外流量监测 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_externalFlowMonitor')}</HeaderCell>
            <FloatActionCell dataKey="extTraffic" />
          </Column>
          {/* IVT供应商 */}
          {/* <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_ivtVendor')}</HeaderCell>
          <FloatActionCell dataKey='ivtVendor' />
        </Column> */}
          {/* 品牌标识 */}
          {/* <Column flexGrow={1} minWidth={200}>
          <HeaderCell>{formatMessage('i18n_brandRemark')}</HeaderCell>
          <TextCell dataKey='ivtVendorBrand' maxLine={maxLine}/>
        </Column> */}
          {/* IVT类型 */}
          {/* <Column flexGrow={1} minWidth={100} sortable>
          <HeaderCell>{formatMessage('i18n_ivtType')}</HeaderCell>
          <FloatActionCell dataKey='ivtType' />
        </Column> */}
          {/* 可见度监测 */}
          {/* <Column flexGrow={1} minWidth={150}>
          <HeaderCell>{formatMessage('i18n_viewabledMonitor')}</HeaderCell>
          <FloatActionCell dataKey='viewability' />
        </Column> */}
          {/* MTA供应商 */}
          {/* <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_mtaVendor')}</HeaderCell>
          <TextCell dataKey='MtaVendor' maxLine={maxLine}/>
        </Column> */}
          {/* 类别 */}
          {/* <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_category')}</HeaderCell>
          <TextCell dataKey='MtaCategory' maxLine={maxLine}/>
        </Column> */}
          {/* 创建者 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_creator')}</HeaderCell>
            <TextCell dataKey="creator" maxLine={maxLine} />
          </Column>
          {/* 创建时间 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_createTime')}</HeaderCell>
            <DateTimeCell dataKey="creationTime" />
          </Column>
          {/* 是否测试 */}
          <Column width={120}>
            <HeaderCell>{formatMessage('i18n_isTest')}</HeaderCell>
            <FloatActionCell dataKey="isTest" />
          </Column>
          {/* cpo状态 */}
          {internal && (
            <Column flexGrow={1} minWidth={150} sortable>
              <HeaderCell>{formatMessage('i18n_cpoReportStatus')}</HeaderCell>
              <CpoStatusCell dataKey="cpoReportStatus" />
            </Column>
          )}
        </Table>
      </>
    );
  }
);

Component.displayName = 'Table';
export default Component;
