import _ from 'lodash';

import { getStorage, updateStorageByKey } from '@/utils/storage';

export enum DateKeys {
  USER = 'user',
  CAMPAIGN = 'campaign',
  CROSS_CAMPAIGN = 'crossCampaign'
}

export const setDateStorageByKey = (key: DateKeys, dateRange: Array<string>) => {
  updateStorageByKey('date', {
    ..._.get(getStorage(), 'date'),
    [key]: {
      start: dateRange[0],
      end: dateRange[1]
    }
  });
};

export const getDateStorageByKey = (key: DateKeys) => {
  let storage = getStorage();
  let result = null;
  try {
    result = _.get(storage, `date.${key}`, null);
  } catch {
    result = null;
  }

  if (result && _.get(result, 'start', '') && _.get(result, 'end', '')) {
    return [_.get(result, 'start', ''), _.get(result, 'end', '')] as Array<string>;
  }

  return null;
};
