import React, { useContext, useCallback } from 'react';
import _ from 'lodash';

import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import MultiField from '@/components/Form/MultiField';
import CustomSelectPicker from '@/components/Picker/SelectPicker';
import Toggle from '@/components/Form/Toggle';
import CustomRadioGroup from '@/components/Form/RadioGroup';
import CustomCheckboxGroup from '@/components/Form/CheckboxGroup';
import BERoleGroupPicker from '@/components/Picker/logic/BERoleGroupPicker';
import RangePicker from '@/components/Picker/RangePicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { CrossCampaignModel } from '@/constants/form/models';
import { __HAA__ } from '@/constants/config';
import { CreateFormCtx } from '../context';
import {
  genderData,
  tencentData,
  calendarWeeklyData,
  levelData,
  ageEndList,
  ageStartList
} from '../constants';

export default React.memo(() => {
  const { formatMessage } = useFmtMsgFori18n();
  const { isEdit, drawerFormResult } = useContext(CreateFormCtx);
  const {
    formData,
    formErrors,
    formRef,
    setFormErrors,
    checkForm,
    handleFormChange
  } = drawerFormResult;
  const genderDataIntl = usePickerDataFromObj(genderData);
  const levelDataIntl = usePickerDataFromObj(levelData);

  const handleChangeMeasure = useCallback(
    val => {
      const prevVal = _.get(formData, 'measure', false);
      if (!prevVal && val) {
        handleFormChange(true, 'enableFreq');
        handleFormChange(true, 'algorithmAdjust');
      } else if (prevVal && !val) {
        handleFormChange(false, 'enableFreq');
        handleFormChange(false, 'algorithmAdjust');
      }
    },
    [formData, handleFormChange]
  );

  // FIXME: don't know why every time formdata Change, it will render twice
  return (
    <Form
      formData={formData}
      formErrors={formErrors}
      formRef={formRef}
      setFormErrors={setFormErrors}
      checkForm={checkForm}
      onChange={handleFormChange as (val: any, key?: string) => void}
      model={CrossCampaignModel}
      checkTrigger="blur"
    >
      <CustomField name="name" label={formatMessage('i18n_name')} required />

      <CustomField name="desc" label={formatMessage('i18n_description')} />
      {/** 主要拥有者 */}
      <MultiField
        accepter={BERoleGroupPicker}
        names={['primaryId', 'primaryRole']}
        label={formatMessage('i18n_primaryOwner')}
        beName="primaryId"
        roleName="primaryRole"
        rolePickerProps={{
          data: [{ name: 'Agency (G)', id: 1 }],
          style: { width: 200 }
        }}
        bePickerProps={{
          data: [{ name: 'Nielsen', id: 1 }],
          style: { width: 200 }
        }}
        disabled
      />

      {/** 目标人群 */}
      <CustomField
        name="targetPeople"
        label={formatMessage('i18n_targetPerson')}
        accepter={CustomSelectPicker}
        data={genderDataIntl}
      />
      {/* 年龄范围 */}
      <MultiField
        names={['targetStartAge', 'targetEndAge']}
        label={formatMessage('i18n_ageRange')}
        accepter={RangePicker}
        startData={ageStartList}
        endData={ageEndList}
        startName="targetStartAge"
        endName="targetEndAge"
        style={{
          width: '157px'
        }}
        className="age-range-picker"
      />
      {/** 受众测量 */}
      {!__HAA__ && (
        <CustomField
          name="measure"
          label={formatMessage('i18n_audienceMeasurement')}
          accepter={Toggle}
          checkedChildren={formatMessage('i18n_open')}
          unCheckedChildren={formatMessage('i18n_close')}
          disabled={isEdit}
          onChange={handleChangeMeasure}
        />
      )}
      {/** 受众累计报 */}
      {!__HAA__ && (
        <CustomField
          name="tencentTotalReport"
          label={formatMessage('i18n_tencentTotalReport')}
          className={formData.measure ? '' : 'checkbox-uncheck-when-disabled'}
          accepter={CustomRadioGroup}
          itemWidth={165}
          inline
          data={tencentData}
          disabled={!formData.measure || isEdit}
        />
      )}
      {/** 受众自然周报 */}
      {!__HAA__ && (
        <CustomField
          name="calendar"
          label={formatMessage('i18n_tencentCalendarWeekly')}
          inline
          accepter={CustomCheckboxGroup}
          data={calendarWeeklyData}
          disabled={!formData.measure || isEdit}
          className={
            formData.measure
              ? 'calenadar-weekly'
              : 'calenadar-weekly checkbox-uncheck-when-disabled'
          }
        />
      )}
      {/** UA频次 */}
      {!__HAA__ && (
        <CustomField
          name="enableFreq"
          label={formatMessage('i18n_UAFreq')}
          accepter={Toggle}
          checkedChildren={formatMessage('i18n_open')}
          unCheckedChildren={formatMessage('i18n_close')}
          disabled={!formData.measure || isEdit}
        />
      )}
      {/** 算法调整模块 */}
      {!__HAA__ && (
        <CustomField
          name="algorithmAdjust"
          label={formatMessage('i18n_algorithmAdjust')}
          accepter={Toggle}
          checkedChildren={formatMessage('i18n_open')}
          unCheckedChildren={formatMessage('i18n_close')}
          disabled={!formData.measure || isEdit}
        />
      )}
      {/** 选择交叉项目 */}
      <CustomField
        name="crossLevel"
        label={formatMessage('i18n_crossLevel')}
        accepter={CustomRadioGroup}
        disabled={isEdit}
        inline
        itemWidth={100}
        data={levelDataIntl}
      />
    </Form>
  );
});
