import React, { memo } from 'react';
import _ from 'lodash';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';

import { useFmtMsgFori18n } from '@/utils/formatMessage';

import { ObjectProps } from '../type';

interface Props {
  data: Array<ObjectProps>;
  legend: string[];
}

export default memo<Props>(function ({ data, legend }) {
  const { formatMessage } = useFmtMsgFori18n();

  const cols = [],
    pv: number[] = [];
  for (let i = 30; i > 0; i--) {
    cols.push(`-${i}${formatMessage('i18n_minute')}`);
  }
  !_.isNil(data) &&
    data.forEach((item: ObjectProps) => {
      pv.push(item.value);
    });

  const newData = [
    {
      name: legend[0],
      data: pv
    }
  ];

  const config = {
    type: CHART_KEY.BAR,
    data: {
      cols,
      data: newData
    },
    series: [
      {
        barWidth: 4
      }
    ]
  };

  return <Chart config={config} height={217} pure />;
});
