import React, { memo, useMemo, useCallback } from 'react';

import * as APIs from '@/constants/APIs';
import CheckPicker from '@/components/Picker/CheckPicker';
import Table, { Column, HeaderCell } from '@/components/Table';
import { DateTimeCell, FloatActionCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps } from '@/constants/config';

import { filterParamKeys, utilsFn, CONF_LOG_TYPE } from './constants';
import LogOpCell from './part/LogOpCell';
import DetailCell from './part/DetailCell';

export default memo(
  ({ filterParams, tableParams, setTableParams, handleChangeCustomParam }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const objectTypeVal = utilsFn.getDimension(filterParams);
    const objectTypeData = CONF_LOG_TYPE.map(i => ({
      name: formatMessage(`i18n_${i.name}`),
      id: i.id
    }));

    const { tableData, onChange } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_LOG
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
    const handleObjectTypeChange = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.DIMENSION]: val
        });
      },
      [handleChangeCustomParam]
    );

    const renderToolbar = () => {
      return (
        <CheckPicker
          prepend={formatMessage('i18n_objectType')}
          data={objectTypeData}
          value={objectTypeVal}
          onChange={handleObjectTypeChange}
          supportAll
        />
      );
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_time')}</HeaderCell>
          <DateTimeCell dataKey="date" />
        </Column>

        <Column flexGrow={1} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_user')}</HeaderCell>
          <FloatActionCell dataKey="user" />
        </Column>

        <Column flexGrow={2} minWidth={200} sortable>
          <HeaderCell>{formatMessage('i18n_object')}</HeaderCell>
          <FloatActionCell dataKey="t" />
        </Column>

        <Column flexGrow={1} minWidth={300} sortable>
          <HeaderCell>{formatMessage('i18n_operation')}</HeaderCell>
          <LogOpCell dataKey="op" />
        </Column>

        <Column flexGrow={1} minWidth={300} sortable>
          <HeaderCell>{formatMessage('i18n_detail')}</HeaderCell>
          <DetailCell dataKey="detail" />
        </Column>
      </Table>
    );
  }
);
