import * as React from 'react';
import BaseLoader from '@/components/BaseLoader';

/**
 * 给Picker添加loading状态的样式
 */

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

export interface UseLoadingProps {
  loading?: boolean; // 是否显示loading样式
  placeholder?: React.ReactNode;
  renderValue?: Function;
  renderMenu?: Function;
}

export interface OutProps {
  placeholder: React.ReactNode;
  renderValue: Function;
  renderMenu: Function;
}

const { useCallback } = React;

const useLoadingForPicker: PropMapper<UseLoadingProps, OutProps> = ({
  loading,
  placeholder,
  renderValue,
  renderMenu,
  ...props
}) => {
  const renderValueWithLoading = useCallback(() => <BaseLoader />, []);

  const renderMenuWithLoading = useCallback(() => <BaseLoader />, []);

  return {
    placeholder: loading ? <BaseLoader /> : placeholder,
    renderValue: loading ? renderValueWithLoading : renderValue,
    renderMenu: loading ? renderMenuWithLoading : renderMenu,
    ...props
  };
};

export default useLoadingForPicker;
