import { useRef, useEffect, useReducer } from 'react';

/**
 * 记录某个值的前一次渲染时的值
 */
export function usePrevious(value: any) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

/**
 * 当某个值改变时，触发回调
 */
export function usePropChange(cb: Function, value: any) {
  const prevValue = usePrevious(value);
  useEffect(() => {
    if (prevValue && prevValue !== value) {
      cb();
    }
  }, [cb, prevValue, value]);
}

/**
 * 对应 componentDidMount 的 Hook
 */
export const useDidMount = (cb: Function) => {
  useEffect(() => {
    cb();
  }, [cb]);
};

/**
 * 对应 componentWillUnmount 的 Hook
 */
export const useWillUnmount = (cb: Function) => {
  useEffect(() => {
    cb();
  }, [cb]);
};

export const useForceUpdate = () => {
  const [, forceUpdate] = useReducer(x => x + 1, 0);
  return forceUpdate;
};
