import prefix from '@/utils/prefix';

export const basicClassName = 'campaign-cpo';
export const prefixClassName = prefix(basicClassName);

export enum filterParamKeys {
  SEC = 'secondary', // 次级维度|时间维度
  ATTRMODEL = 'cpoAttributeModel', // 归因模型
  ACTION = 'cpoConversion', // 转化活动
  PARA = 'cpoAttributes' // 自定义属性
}
export interface FilterParam {
  [filterParamKeys.SEC]?: string | number;
  [filterParamKeys.ATTRMODEL]?: Array<string>;
  [filterParamKeys.ACTION]?: string | number;
  [filterParamKeys.PARA]?: Array<string>;
}

export const defaultFilter: FilterParam = {
  [filterParamKeys.SEC]: 'total',
  [filterParamKeys.ATTRMODEL]: ['imp_last'],
  [filterParamKeys.ACTION]: null,
  [filterParamKeys.PARA]: null
};
