import React from 'react';
import classnames from 'classnames';
import { Icon } from 'rsuite';

import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface BaseLoaderProps {
  className?: string;
}

export default React.memo(({ className, ...props }: BaseLoaderProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  return (
    <p className={classnames('picker-loader', className)} {...props}>
      {formatMessage('i18n_loading')} <Icon icon="spinner" pulse />
    </p>
  );
});
