import _ from 'lodash';

import getRandomNum from '@/utils/random';

import { AdvFilterInnerValue, AdvFilterValue } from '../constants';

export const removeIdForObj = (val: AdvFilterInnerValue) => {
  // 不改变原数组哦
  return _.map(_.cloneDeep(val), v => {
    delete v.id;
    return v;
  });
};

export const genNewFilterNode = () => [{ id: getRandomNum() }];

export const addIdIfNotExist = (val: AdvFilterValue): AdvFilterInnerValue =>
  val.map(v => ({
    ...v,
    ...(!v.id && { id: getRandomNum() })
  }));

export const newNodeIfEmpty = (val: AdvFilterInnerValue): AdvFilterInnerValue => {
  if (val.length) {
    return val;
  }

  return genNewFilterNode();
};

export const removeEmptyVal = (val: AdvFilterInnerValue) => {
  return val.filter(v => v.key && ((!_.isArray(v.value) && v.value) || !_.isEmpty(v.value)));
};
