import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';

import SearchInput from '@/components/Table/Tools/SearchInput';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { DateTimeCell, FloatActionCell, MultiLineCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import { getUserProfile } from '@/utils/location';
import useAdsConfigDataIntl from './useAdsConfigDataIntl';
import { PC, MOBILE, OTT } from './constants';

type TableProps = BasicReportProps & {
  handleFormCreate: Function;
  handleFormEdit: Function;
};

export default memo(
  ({
    tableParams,
    setTableParams,
    handleSearchWord,
    handleFormCreate,
    handleFormEdit
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const adsConfigDataIntl = useAdsConfigDataIntl();

    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_ADSERVER_VAR,
      postParams: {
        var: ''
      }
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport({
          var: '.xlsx'
        });
    }, [onExport]);

    const renderToolbar = () => {
      return (
        <>
          <Button appearance="primary" onClick={handleFormCreate as () => void}>
            {formatMessage('i18n_create')}
          </Button>
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
          <Button key="exportTable" className="pull-right" onClick={handleExportClicked}>
            {formatMessage('i18n_exportTable')}
          </Button>
          ,
        </>
      );
    };

    const renderActions = useCallback(
      rowData => {
        const { internal } = getUserProfile();
        if (!internal) {
          return;
        }
        return (
          <FloatActionCell.Actions.Edit
            onClick={() => {
              handleFormEdit({
                id: rowData.id
              });
            }}
          />
        );
      },
      [handleFormEdit]
    );

    const formatField = useCallback(
      (placeholder: any) => (data: any = {}) => {
        if (_.isObject(data)) {
          return [
            `${formatMessage('i18n_pc')}: ${_.get(data, PC, '')}`,
            `${formatMessage('i18n_mobile')}: ${_.get(data, MOBILE, '')}`,
            `OTT: ${_.get(data, OTT, '')}`
          ];
        }

        return placeholder;
      },
      [formatMessage]
    );

    return (
      <Table {...tableDataMemo} onChange={onChange} renderToolbar={renderToolbar} wordWrap>
        <Column width={100} fixed sortable>
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>
        <Column width={200} resizable fixed sortable>
          <HeaderCell>{formatMessage('i18n_name')}</HeaderCell>
          <FloatActionCell dataKey="name" renderActions={renderActions} />
        </Column>
        {(adsConfigDataIntl as any).map(
          ({ name, dataKey, label, tableHeader, emptyLabel }: any) => (
            <Column width={200} key={name}>
              <HeaderCell>{tableHeader || label}</HeaderCell>
              <MultiLineCell dataKey={dataKey || name} formatter={formatField(emptyLabel)} />
            </Column>
          )
        )}
        <Column width={200} sortable>
          <HeaderCell>{formatMessage('i18n_createTime')}</HeaderCell>
          <DateTimeCell dataKey="created" />
        </Column>
      </Table>
    );
  }
);
