import { useEffect, useCallback } from 'react';
import useFetch, { FETCH_METHOD, transformToChartData } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { BasicReportProps } from '@/constants/config';
import useUniKey from '@/components/hooks/useUniKey';

interface Ret {
  data: any;
  loading: boolean;
  handRefresh: Function;
}

export default function useChart(
  { dateParams, filterParams, api, postParams }: BasicReportProps,
  deps: any[] = []
): Ret {
  const { loading, onFetch: onUpdateChartData, response: chartResponse } = useFetch<CommonResponse>(
    FETCH_METHOD.GET,
    api,
    {
      cacheParams: true,
      params: {
        var: 'chart',
        ...postParams
      }
    }
  );

  const [updateKey, newUpdateKey] = useUniKey();

  /* eslint-disable */
  useEffect(() => {
    onUpdateChartData({
      params: {
        ...dateParams,
        ...filterParams
      }
    });
  }, [dateParams, filterParams, updateKey]);
  /* eslint-disable */

  const { data } = transformToChartData(chartResponse);

  // 刷新chart
  const handRefresh = useCallback(() => {
    newUpdateKey();
  }, [newUpdateKey]);

  return {
    loading,
    data,
    handRefresh
  };
}
