import { GET } from '@/utils/request/fetch';
import * as APIs from '@/constants/APIs';

export function onFetchSummary(params: object, cb: (res: object) => void) {
  return GET(APIs.API_BRAND_OVERVIEW, {
    params: {
      ...params,
      var: 'summary'
    }
  }).then(cb);
}

export function onFetchTable(params: object, cb: (res: object) => void) {
  return GET(APIs.API_BRAND_OVERVIEW, {
    params,
    var: 'json'
  }).then(cb);
}
