import React, { memo, useMemo } from 'react';
import _ from 'lodash';
import 'echarts/lib/component/title';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import MapChart from '@rsuite/charts/lib/charts/MapChart';
import VisualMap from '@rsuite/charts/lib/components/VisualMap';
import 'echarts/map/js/china';
import 'echarts/map/js/world';

import {
  nameMapEnForCountry,
  nameMapEnForProvince,
  nameMapZhForCountry,
  nameMapZhForProvince,
  provinceGeoEn,
  provinceGeoZh,
  countryGeoEn,
  countryGeoZh
} from '@hypers/geo-transform';

import { getCurrentLocale } from '@/utils/locale';
import { ChartProps } from './type';

function fmtRegionName(values: string) {
  if (!values) {
    return null;
  }
  if (values.indexOf('省') > -1) {
    values = _.replace(values, '省', '');
  }
  if (values.indexOf('市') > -1) {
    values = _.replace(values, '市', '');
  }
  return values;
}

export default memo(
  ({ config, option = {}, loading, ...props }: ChartProps & BaseObject<any>) => {
    const { formatMessage } = useFmtMsgFori18n();
    const {
      data: { data = [] },
      map = 'china'
    } = config;
    const locale = getCurrentLocale() || 'zh-CN';
    const isZh = locale === 'zh-CN';
    const isWorldMap = map === 'world';
    const isChinaMap = map === 'china';
    let nameMap: any = null,
      geoData: any = null;
    if (isZh) {
      if (isWorldMap) {
        nameMap = nameMapZhForCountry;
        geoData = countryGeoZh;
      } else if (isChinaMap) {
        nameMap = nameMapZhForProvince;
        geoData = provinceGeoZh;
      }
    } else {
      if (isWorldMap) {
        nameMap = nameMapEnForCountry;
        geoData = countryGeoEn;
      } else if (isChinaMap) {
        nameMap = nameMapEnForProvince;
        geoData = provinceGeoEn;
      }
    }
    const chartData = useMemo(() => {
      const list: any[] = [];
      if (data && !_.isEmpty(data)) {
        data.map((i: any) => {
          if (_.startsWith(i.name, 'geo')) {
            i.name = geoData[i.name];
          }
          return i;
        });
        data.forEach((i: any) => {
          list.push([fmtRegionName(i.name), i.value]);
        });
      }
      return list;
    }, [data, geoData]);
    return (
      <MapChart
        loading={loading}
        map={map}
        data={chartData}
        nameMap={nameMap}
        option={{
          ..._.omit(option, 'legend'),
          xAxis: {
            show: false
          }
        }}
        {...props}
      >
        <VisualMap text={[formatMessage('i18n_maxValue'), formatMessage('i18n_minValue')]} />
      </MapChart>
    );
  },
  (prevProps, nextProps) => _.isEqual(prevProps, nextProps)
);
