import React, { memo, useContext } from 'react';
import { Panel, Loader, Icon } from 'rsuite';
import _ from 'lodash';

import Bars from '@rsuite/charts/lib/series/Bars';
import Line from '@rsuite/charts/lib/series/Line';
import Funnel from '@rsuite/charts/lib/series/Funnel';
import Pie from '@rsuite/charts/lib/series/Pie';
import YAxis from '@rsuite/charts/lib/components/YAxis';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { RouteInfoCtx } from '@/components/Routes/RouteContext';

import {
  CHART_KEY,
  CHART_COLORS,
  CHART_DEF_OPT,
  formatAxisMax,
  formatAxisMaxToPer,
  formatAxisToNumber,
  formatAxisToPer,
  SPLIT_NUM
} from './config';
import MixChart from './MixChart';
import BarChart from './BarChart';
import PieChart from './PieChart';
import MapChart from './MapChart';
import OriginChart from './OriginChart';
import { ChartProps } from './type';

const CHART_TYPE: { [key: string]: any } = {
  [CHART_KEY.MIX]: MixChart,
  [CHART_KEY.BAR]: BarChart,
  [CHART_KEY.LINE]: MixChart,
  [CHART_KEY.STACK]: BarChart,
  [CHART_KEY.PIE]: PieChart,
  [CHART_KEY.MAP]: MapChart,
  [CHART_KEY.ORIGIN]: OriginChart
  // [CHART_KEY.FUNNEL]: FunnelChart
};

const SERIES_TYPE: { [key: string]: any } = {
  [CHART_KEY.LINE]: Line,
  [CHART_KEY.BAR]: Bars,
  [CHART_KEY.LINE]: Line,
  [CHART_KEY.STACK]: Line,
  [CHART_KEY.PIE]: Pie,
  // [CHART_KEY.MAP]: MapChart,
  [CHART_KEY.FUNNEL]: Funnel
};

// 设置serie
export const getSerie = (serie: any, chartType: string) => {
  const name = _.isString(serie) ? serie : serie.name;
  const type = _.get(serie, 'type', chartType);
  const props = { symbol: 'emptyCircle', symbolSize: 5, smooth: true };
  if (_.isObject(serie)) {
    _.merge(props, serie);
  }
  if (type === 'bar') {
    _.merge(props, {
      barMaxWidth: 20,
      barWidth: _.get(props, 'barWidth', 15),
      stack: serie.stack
    });
  }
  //	donut是否为环形图
  if (type === 'pie') {
    _.merge(props, {
      donut: true
    });
  }

  const Serie = SERIES_TYPE[type];
  return <Serie key={name} name={name} {...props} />;
};

// 渲染y坐标
export const formatYAxis = (yAxis: any, option?: any) => {
  const yAxisProps = _.get(option, 'yAxis', {});
  if (_.isEmpty(yAxis) || !yAxis) {
    return (
      <YAxis
        min={0}
        max={formatAxisMax}
        splitNumber={SPLIT_NUM}
        axisLabel={formatAxisToNumber}
        {...yAxisProps}
      />
    );
  }

  // todo:百分比指标暂不设置splitNumber，因为会导致分隔不均匀，让echart自动分隔，之后用interval实现
  if (!_.isArray(yAxis)) {
    return (
      <YAxis
        min={0}
        max={yAxis.type === 'per' ? formatAxisMaxToPer : formatAxisMax}
        name={yAxis.name}
        axisLabel={yAxis.type === 'per' ? formatAxisToPer : formatAxisToNumber}
        {...yAxisProps}
      />
    );
  }
  return (
    yAxis &&
    yAxis.map((axis: any, index: number) => {
      let key = axis.name ? axis.name : index;
      return (
        <YAxis
          key={key}
          min={0}
          max={formatAxisMax}
          name={axis.name}
          splitNumber={SPLIT_NUM}
          splitLine={axis.splitLine}
          axisLabel={axis.type === 'per' ? formatAxisToPer : formatAxisToNumber}
          {...yAxisProps}
        />
      );
    })
  );
};

export default memo(
  ({
    config,
    option = {},
    loading = false,
    collapsible = false,
    pure = false,
    title,
    height = 300
  }: ChartProps) => {
    const { title: titleFromRoute } = useContext(RouteInfoCtx);
    const { formatMessage } = useFmtMsgFori18n();
    const defaultProps = {
      message: (
        <div className="general-empty-block">
          <Icon icon="info" size="lg" />
          {formatMessage('i18n_dataEmpty')}
        </div>
      ),
      loading: <Loader center content={formatMessage('i18n_loading')} />
    };
    // 下载图片
    const CHART_TOOLBOX = {
      toolbox: {
        show: true,
        top: 0,
        left: 25,
        itemSize: 30,
        feature: {
          saveAsImage: {
            backgroundColor: '#fff',
            show: true,
            iconStyle: {
              borderWidth: '#000'
            },
            title: formatMessage('i18n_saveAsImage'),
            emphasis: {
              iconStyle: {
                textFill: '#575757',
                textAlign: 'left',
                textPosition: 'right'
              }
            },
            icon: 'image:///resources/images/down.png'
          }
        }
      }
    };
    const { type = 'line', data: chartData, hasTools = false } = config;
    // 是否显示工具栏
    let extendOption = hasTools
      ? {
        grid: {
          top: hasTools ? 80 : 0
        },
        ...CHART_TOOLBOX
      }
      : {};
    // chart颜色
    const items =
      _.get(chartData, 'records') || _.get(chartData, 'data') || _.get(chartData, 'items') || [];
    let color = [];
    switch (_.size(items) || _.size(option.series)) {
      case 1:
      case 2:
        color = CHART_COLORS.min;
        break;
      case 4:
        color = CHART_COLORS.mid;
        break;
      default:
        color = CHART_COLORS.max;
        break;
    }
    // 合并title到chart的option中，为了chart保存图片显示title
    const newTitle = title || formatMessage(titleFromRoute);
    option = _.merge(
      {},
      CHART_DEF_OPT,
      { color },
      {
        title: {
          show: false,
          text: newTitle
        }
      },
      option,
      extendOption
    );
    const Chart = CHART_TYPE[type];
    const dom = (
      <Chart
        config={config}
        option={option}
        loading={loading}
        height={height}
        locale={{
          emptyMessage: !loading && defaultProps.message,
          loading: defaultProps.loading
        }}
      />
    );
    if (pure) {
      return dom;
    }
    return (
      <>
        <Panel
          className="chart-wrapper"
          header={newTitle}
          collapsible={collapsible}
          defaultExpanded
        >
          {dom}
        </Panel>
      </>
    );
  }
);
