import _ from 'lodash';
import dayjs from 'dayjs';
import React, { useCallback } from 'react';
import { FULL_DATE_TIME } from '@/constants/dateFormat';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from './FloatActionCell';

export interface DateTimeCellProps<T> extends BaseCellProps<T> {
  format?: string;
}

const DateTimeCell = ({
  rowData,
  dataKey,
  format = FULL_DATE_TIME,
  ...props
}: DateTimeCellProps<any>) => {
  let data = _.get(rowData, dataKey) || null;
  const renderContent = useCallback(() => {
    return <span>{data ? dayjs(data).format(format) : DEFAULT_TABLE_CELL_CONTENT}</span>;
  }, [data, format]);

  const renderTitle = useCallback(() => {
    return <span>{data ? dayjs(data).format(format) : ''}</span>;
  }, [data, format]);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderTitle={renderTitle}
      renderContent={renderContent}
      {...props}
    />
  );
};

export default React.memo(DateTimeCell);
