import _ from 'lodash';
import React from 'react';
import {
  MapConfig,
  toPickerData,
  mapParams2PickerValue,
  mapPickerValue2Params,
  PickerDataConfig
} from '@/utils/pickerMapper';

/**
 * 对Picker的data进行格式化拼接，用于树结构
 * 除了对data进行格式化处理外，传进的value值，和onChange抛出的值也会格式化成树结构
 */

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

export interface UseFormattedDataProps {
  notFormat?: boolean; // 是否不需要格式化，默认需要
  notMapValue?: boolean; // 是否不需要格式化value，默认需要
  searchKeys?: string[]; // 用于搜索的键名，由于搜索也要改造data，所以对于树结构数据可以放在此步
  mapLabel?: PickerDataConfig['mapLabel']; // 用于格式化data
  formatConfig?: MapConfig; // 格式化value的配置
  data?: any[];
  value?: any;
  labelKey?: string;
  valueKey?: string;
  childrenKey?: string;
  onChange?: (val?: string[], e?: React.SyntheticEvent<HTMLElement>, allChosen?: boolean) => any; // 增加allChosen参数表示是否全选
}

export interface OutProps {
  data: any[];
  value: any;
  labelKey: string;
  valueKey: string;
  childrenKey: string;
  onChange: (val?: string[], e?: React.SyntheticEvent<HTMLElement>) => any;
  notFormat: boolean;
  searchKeys: string[];
  mapLabel: PickerDataConfig['mapLabel'];
}

const { useCallback, useEffect, useState } = React;

const useFormattedDataForPicker: PropMapper<UseFormattedDataProps, OutProps> = ({
  notFormat = false,
  notMapValue = false,
  searchKeys = [],
  mapLabel,
  formatConfig,
  data = [],
  value,
  labelKey = 'label',
  valueKey = 'value',
  childrenKey = 'children',
  onChange,
  ...props
}) => {
  const [formattedData, setFormattedData] = useState(data);
  const [formattedValue, setFormattedValue] = useState(value);

  useEffect(() => {
    setFormattedData(
      notFormat || _.isEmpty(data)
        ? data
        : toPickerData(data, {
            labelKey,
            valueKey,
            childrenKey,
            nameKeys: searchKeys.filter(i => i !== labelKey).concat(labelKey), // labelKey对应的值必须存在且在最后，以保证renderValue可使用到，参见useSearchForPicker
            mapLabel
          })
    );
  }, [notFormat, data, labelKey, valueKey, childrenKey, mapLabel]);

  useEffect(() => {
    setFormattedValue(
      notFormat || notMapValue || _.isEmpty(value)
        ? value
        : mapParams2PickerValue(value, formattedData, formatConfig)
    );
  }, [formattedData, formatConfig, notFormat, value]);

  const handleChange = useCallback(
    (val: string[], e: React.SyntheticEvent<HTMLElement>) => {
      const newValue =
        notFormat || notMapValue ? val : mapPickerValue2Params(val, formattedData, formatConfig);
      const allChosen =
        !_.isEmpty(val) &&
        _.isEmpty(_.difference(_.map(formattedData, notFormat ? valueKey : 'value'), val));
      _.isFunction(onChange) && onChange(newValue, e, allChosen);
    },
    [formattedData, formatConfig, notFormat, onChange]
  );

  return {
    data: formattedData,
    value: formattedValue,
    labelKey: notFormat ? labelKey : 'label', // 格式化后
    valueKey: notFormat ? valueKey : 'value',
    childrenKey: notFormat ? childrenKey : 'children',
    onChange: handleChange,
    searchKeys,
    notFormat, // useSearchForPicker 也会用到notFormat和mapLabel这两个参数，所以需抛出
    mapLabel,
    ...props
  };
};

export default useFormattedDataForPicker;
