import React, { useCallback } from 'react';
import _ from 'lodash';
import { toDate } from '@/utils/date/index';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from './FloatActionCell';

const dateReg = /\d{4}\/\d{2}\/\d{2}/;
export default ({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const dateStr = _.get(rowData, dataKey, DEFAULT_TABLE_CELL_CONTENT);
  const date = toDate(_.get(dateStr.match(dateReg), '0'));

  const renderContent = useCallback(() => {
    return <span>{dateStr.replace(dateReg, date)}</span>;
  }, [date, dateStr]);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderContent={renderContent}
      renderTitle={renderContent}
      {...props}
    />
  );
};
