import React, { useContext, useCallback, useState, useMemo } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';

import { API_CROSS_CAMPAIGN_GET } from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { DEFAULT_PAGE_SIZE, getShortNameById } from '@/constants/config';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import {
  FloatActionCell,
  MultiLineCell,
  DateCell,
  CampaignStatusCell
} from '@/components/Table/Cells';
import download from '@/utils/download';
import { getPathname, mergeViewId } from '@/utils/location';

import { CreateFormCtx } from '../context';
import UploadStatusCell from '../part/UploadStatusCell';
import RemoveCell from '../part/RemoveCell';
import { checkLevelIsCampaign, getAccBatchResult } from '../constants';

// 内部维护了一个 page
export default () => {
  const { formatMessage } = useFmtMsgFori18n();
  const { drawerFormResult, isEdit, setSelectedAvailable } = useContext(CreateFormCtx);
  const {
    formData: { crossLevel, batchResult },
    handleFormChange
  } = drawerFormResult;
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(DEFAULT_PAGE_SIZE);

  const tableDataMemo = useMemo(() => {
    return {
      data: _.slice(batchResult, (page - 1) * pageSize, pageSize * page),
      loading: false,
      page: {
        current: page,
        size: pageSize,
        total: batchResult.length
      }
    };
  }, [batchResult, page, pageSize]);

  const handleChangeTablePage = useCallback((p: number) => {
    setPage(p);
  }, []);

  const handleChangeTableLength = useCallback((pageSizeObj: any) => {
    const { pageSize: pSize } = pageSizeObj;
    setPageSize(pSize);
    setPage(1);
  }, []);

  const handleRemove = useCallback(
    (rowData: any) => {
      const { id } = rowData;
      setSelectedAvailable(prev => _.filter(prev, p => p !== id)); // 移除上面表格的
      handleFormChange(_.filter(batchResult, p => p.id !== id), 'batchResult'); // 移除这个表格的
    },
    [setSelectedAvailable, handleFormChange, batchResult]
  );

  const handleExport = useCallback(() => {
    download(
      API_CROSS_CAMPAIGN_GET,
      mergeViewId(
        mergeViewId({
          var: 'xlsx',
          id: Number(getPathname().split('/')[4])
        })
      )
    );
  }, []);

  const renderTableToolBar = useCallback(() => {
    const accLen = _.get(getAccBatchResult(batchResult), 'length', 0);
    return (
      <React.Fragment>
        <span className="selected-count">
          {formatMessage('i18n_correctTips')}: {accLen}
        </span>
        <span className="selected-count">
          {formatMessage('i18n_errorTips')}: {batchResult.length - accLen}
        </span>
        {isEdit && (
          <Button className="pull-right" onClick={handleExport} key="export">
            {formatMessage('i18n_exportTable')}
          </Button>
        )}
      </React.Fragment>
    );
  }, [formatMessage, batchResult, handleExport, isEdit]);

  const renderCampaignColumns = () => {
    return [
      <Column flexGrow={1} minWidth={200} fixed key="campaign-name">
        <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
        <FloatActionCell dataKey="name" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-advertiser">
        <HeaderCell>{formatMessage('i18n_advertiser')}</HeaderCell>
        <FloatActionCell dataKey="advertiser" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-brand">
        <HeaderCell>{formatMessage('i18n_brand')}</HeaderCell>
        <FloatActionCell dataKey="brand" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-primary">
        <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
        <MultiLineCell
          dataKey="primary"
          /* eslint-disable */
          formatter={(value, rowData) => {
            if (_.isObject(value)) {
              return [
                `${getShortNameById(_.get(value, 'role', ''))}: ${_.get(value, 'beName', '')}`
              ];
            } else if (_.isString(value) && _.isEmpty(_.get(rowData, 'errorInfo'))) {
              return [`${getShortNameById(_.get(rowData, 'role', ''))}: ${value}`];
            } else {
              return ['--'];
            }
          }}
          /* eslint-enable */
        />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-status">
        <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
        <CampaignStatusCell dataKey="status" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-startDate">
        <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
        <DateCell dataKey="startDate" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-endDate">
        <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
        <DateCell dataKey="endDate" />
      </Column>,

      <Column flexGrow={1} width={200} key="campaign-target">
        <HeaderCell>{formatMessage('i18n_targetPerson')}</HeaderCell>
        <FloatActionCell dataKey="target" />
      </Column>
    ];
  };

  const renderAdunitColumns = () => {
    return [
      <Column flexGrow={1} minWidth={200} fixed key="name">
        <HeaderCell>{formatMessage('i18n_placementName')}</HeaderCell>
        <FloatActionCell dataKey="name" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign_name">
        <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
        <FloatActionCell dataKey="campaignName" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="campaign-primary">
        <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
        <MultiLineCell
          dataKey="primary"
          /* eslint-disable */
          formatter={(value, rowData) => {
            if (_.isObject(value)) {
              return [
                `${getShortNameById(_.get(value, 'role', ''))}: ${_.get(value, 'beName', '')}`
              ];
            } else if (_.isString(value) && _.isEmpty(_.get(rowData, 'errorInfo'))) {
              return [`${getShortNameById(_.get(rowData, 'role', ''))}: ${value}`];
            } else {
              return ['--'];
            }
          }}
          /* eslint-enable */
        />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="adserver_id">
        <HeaderCell>{formatMessage('i18n_adserverAdunitId')}</HeaderCell>
        <FloatActionCell dataKey="adserverPid" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="site_id">
        <HeaderCell>{formatMessage('i18n_siteId')}</HeaderCell>
        <FloatActionCell dataKey="siteId" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="site_name">
        <HeaderCell>{formatMessage('i18n_siteName')}</HeaderCell>
        <FloatActionCell dataKey="siteName" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="status">
        <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
        <CampaignStatusCell dataKey="status" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="start_date">
        <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
        <DateCell dataKey="startDate" />
      </Column>,

      <Column flexGrow={1} minWidth={200} key="end_date">
        <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
        <DateCell dataKey="endDate" />
      </Column>
    ];
  };

  return (
    <Table
      {...tableDataMemo}
      onChangeLength={handleChangeTableLength}
      onChangePage={handleChangeTablePage}
      renderToolbar={renderTableToolBar}
      height={400}
    >
      <Column width={80} fixed>
        <HeaderCell>{formatMessage('i18n_delete')}</HeaderCell>
        <RemoveCell dataKey="errorInfo" onRemove={handleRemove} />
      </Column>
      <Column width={25} fixed>
        <HeaderCell />
        <UploadStatusCell dataKey="errorInfo" />
      </Column>
      {
        (
          <Column width={100} fixed>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>
        ) as any
      }
      {checkLevelIsCampaign(crossLevel) ? renderCampaignColumns() : renderAdunitColumns()}
    </Table>
  );
};
