import React, { memo, useMemo, useCallback } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';

import * as APIs from '@/constants/APIs';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import Table, { Column, HeaderCell } from '@/components/Table';
import { DateStringCell, FloatActionCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps } from '@/constants/config';

import {
  CROSS_DIM_TYPE,
  otherCells,
  utilsFn,
  filterParamKeys,
  secondaryData,
  defaultFilter
} from './constants';

export default memo(
  ({ filterParams, handleChangeCustomParam, tableParams, setTableParams }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const otherCellsIntl = usePickerDataFromObj(otherCells);
    const secondaryDataIntl = usePickerDataFromObj(secondaryData);
    const CrossDimTypeIntl = usePickerDataFromObj(CROSS_DIM_TYPE, { recursionChildKey: 'cells' });
    const { tableData, onExport, onChange } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_SUPPORT_TOOL_VAR,
      postParams: {
        var: 'json'
      }
    });

    const checkedCrossDim = utilsFn.getCrossDimensions(filterParams);
    const timeDimension = utilsFn.getTimeDimensions(filterParams);
    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
    const dims = CrossDimTypeIntl.filter(i => _.includes(checkedCrossDim, i.id));
    const excludes = _.union(..._.map(dims, 'excludes'));

    const crossDimCheckHandler = useCallback(
      (val: Array<any>) => {
        const targetVal = val.length ? val : defaultFilter.crossDimensions;
        handleChangeCustomParam({
          [filterParamKeys.CROSS_DIMENSION]: targetVal
        });
      },
      [handleChangeCustomParam]
    );

    const handleTimeDimensionChanged = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.TIME_DIMENSION]: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport({
          var: 'xlsx'
        });
    }, [onExport]);

    const renderToolbar = (): React.ReactElement[] => {
      return [
        <CheckPicker
          key="crossDimensions"
          data={CrossDimTypeIntl}
          value={checkedCrossDim}
          onChange={crossDimCheckHandler}
          disabledItemValues={defaultFilter.crossDimensions}
          prepend={formatMessage('i18n_crossDimensions')}
        />,
        <SelectPicker
          key="timeDimension"
          prepend={formatMessage('i18n_timerDimension')}
          data={secondaryDataIntl}
          value={timeDimension}
          onChange={handleTimeDimensionChanged}
        />,
        <Button key="exportTable" className="pull-right" onClick={handleExportClicked}>
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
        <Column width={_.get(tableParams, 'secondary') === 'weekly' ? 280 : 120} fixed sortable>
          <HeaderCell>{formatMessage('i18n_date')}</HeaderCell>
          <DateStringCell dataKey="secondary" />
        </Column>

        {dims.reduce(
          (arr, i) => {
            return [
              ...arr,
              ..._.map(i.cells, item => (
                <Column width={200} sortable key={item.dataKey}>
                  <HeaderCell>{item.name}</HeaderCell>
                  <FloatActionCell dataKey={item.dataKey} />
                </Column>
              ))
            ];
          },
          [] as any
        )}

        {otherCellsIntl
          .filter(cell => !_.includes(excludes, cell.dataKey))
          .map(cell => {
            const CellComponent = cell.CellType || FloatActionCell;
            return (
              <Column width={200} resizable sortable key={cell.dataKey}>
                <HeaderCell>{cell.name}</HeaderCell>
                <CellComponent dataKey={cell.dataKey} />
              </Column>
            );
          })}
      </Table>
    );
  }
);
