import _ from 'lodash';
import { getLastNDaysRangeBeforeCertainDate } from '@/utils/date';
import { TODAY } from '@/constants/config';
import { defaultFormData, dateRangeData, typeData, countryData, placementMetricsData } from './constants';

export const mapFromResponse = (rowData: Record<string, any>, campaigns: any[]) => {
  const data = _.get(rowData, 'data', {});
  return {
    ..._.pick(rowData, ['id', 'name', 'type', 'status']),
    ..._.omit(data, [
      'ids', 'segmentation', 'advanceFilter',
      'frequency', 'domain', 'hierarchy',
      'start', 'end'
    ]),
    campaigns,
    segmentations: data.segmentation || [],
    dateRange: data.timeRange === dateRangeData[5].id ?
      [data.start, data.end] :
      getLastNDaysRangeBeforeCertainDate(new Date(), 7),
    advancedFilter: data.advanceFilter || [],
    freqType: _.get(data, 'frequency.0', defaultFormData.freqType),
    freqVal: Number(_.get(data, 'frequency.1')),
    endTime: new Date(_.get(rowData, 'endTime')),
    receivers: (data.receivers || []).join(),
  };
};

export const mapToRequest = (data: Record<string, any> = {}, isEdit: boolean) => {
  const typeItem = typeData.find(i => i.id === data.type);
  const isCustomTime = data.timeRange === dateRangeData[5].id;

  return {
    ..._.omit(data, ['advancedFilter', 'campaigns', 'dateRange', 'freqType', 'freqVal']),
    ids: _.map(data.campaigns, 'id').join(),
    segmentations: _.intersection(data.segmentations, typeItem?.segmentations).join(),
    start: isCustomTime ? data.dateRange[0] : TODAY,
    end: isCustomTime ? data.dateRange[1] : TODAY,
    country: data.type === 2 && !_.includes(data.segmentations, 'city') || data.type === 1 ?
      data.country : countryData[0].id,
    advanceFilter: _.map(data.advancedFilter, i => _.omit(i, 'id')),
    metrics: _.intersection(data.metrics, [
      ...typeItem.metrics,
      ...(
        _.includes(data.segmentations, 'placement') ?
          _.map(placementMetricsData, 'id') : []
      )
    ]).join(),
    frequency: JSON.stringify([data.freqType, data.freqVal]),
    endTime: data.freqType === 'once' ? undefined : data.endTime,
  };
};