/**
 * 日期类型
 * @param name
 * @param isRequired
 * @return {*}
 */
export const getDateType = (name = formatMessage('i18n_date'), isRequired = true) => {
  const Type = DateType();
  if (!isRequired) {
    return Type;
  }
  return Type.isRequired(
    formatMessage('i18n_select', {
      name
    })
  );
};

// 日期范围
export const getDateRangeType = (name: string, isRequired = true) => {
  const Type = ArrayType();
  if (!isRequired) {
    return Type;
  }
  return Type.addRule(value => {
    return _.get(value, 'length', 0) === 2 && value.every(dateIsValid);
  }, formatMessage('i18n_dateRangeRequired'));
};
