import React, { useCallback } from 'react';
import _ from 'lodash';
import { Toggle } from 'rsuite';

export default React.memo(({ name, value, onChange, ...props }: any) => {
  const handleChange = useCallback(
    (val, e) => {
      onChange(val, e);
    },
    [onChange]
  );

  return <Toggle checked={Boolean(value)} onChange={handleChange} {...props} />;
});
