import React, { memo, useState, useCallback, useContext } from 'react';
import _ from 'lodash';
import CheckPicker from '@/components/Picker/CheckPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import { API_ADGROUP, API_BE, API_CAMPAIGN_LIST } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { filterParamKeys, SamplingReducerType } from '../constants';
import { SampleCtx } from '../context';

const beParams = {
  status: 1,
  orderColumn: 'name',
  orderType: 'asc'
};
const adunitParams = {
  orderColumn: 'name',
  orderType: 'asc'
};

export default memo(() => {
  const { formatMessage } = useFmtMsgFori18n();
  const [be, setBe] = useState(null);
  const [campaign, setCampaign] = useState(null);
  const { state, dispatch, handleChangeCustomParam } = useContext(SampleCtx);

  const handleClean = useCallback(() => {
    handleChangeCustomParam({
      [filterParamKeys.DIMENSION_ID]: null
    });
    dispatch({
      type: SamplingReducerType.SET_SAMPLE_DIMENSION_ID,
      sampleDimensionId: null
    });
    dispatch({
      type: SamplingReducerType.SET_POLLING_STATUS,
      pollingStatus: false
    });
  }, [handleChangeCustomParam, dispatch]);
  //change be
  const handleChangeBe = useCallback(
    value => {
      setBe(value);
      setCampaign(null);
      handleClean();
    },
    [handleClean]
  );
  //change campaign
  const handleChangeCampaign = useCallback(
    value => {
      setCampaign(value);
      handleClean();
    },
    [handleClean]
  );
  const handleSelect = useCallback(
    value => {
      handleChangeCustomParam({
        [filterParamKeys.DIMENSION_ID]: value
      });
      dispatch({
        type: SamplingReducerType.SET_SAMPLE_DIMENSION_ID,
        sampleDimensionId: value
      });
    },
    [handleChangeCustomParam, dispatch]
  );
  return (
    <>
      <CheckPicker
        prepend="BE"
        url={API_BE}
        params={beParams}
        style={{ maxWidth: '300px' }}
        menuStyle={{ maxWidth: '400px' }}
        value={be}
        onChange={handleChangeBe}
        placeholder={formatMessage('i18n_select', { name: '' })}
        appearance="subtle"
      />
      <CheckPicker
        url={API_CAMPAIGN_LIST}
        deps={[be]}
        value={campaign}
        onChange={handleChangeCampaign}
        params={{
          ...adunitParams,
          beId: be ? be.toString() : ''
        }}
        prepend={formatMessage('i18n_campaign')}
        style={{ maxWidth: '250px' }}
        menuStyle={{ maxWidth: '400px' }}
        placeholder={formatMessage('i18n_select', { name: '' })}
        appearance="subtle"
      />
      <SelectPicker
        url={API_ADGROUP}
        deps={[be, campaign]}
        value={state.sampleDimensionId}
        params={{
          ...adunitParams,
          campaignId: campaign ? campaign.toString() : ''
        }}
        onChange={handleSelect}
        searchable={true}
        prepend={formatMessage('i18n_placement')}
        style={{ maxWidth: '250px' }}
        menuStyle={{ maxWidth: '400px' }}
        placeholder={formatMessage('i18n_select', { name: '' })}
        appearance="subtle"
        groupBy="be"
        renderMenuItem={(label, item) => {
          const name = _.get(item, 'label') + '（' + _.get(item, 'id') + '）';
          return <span title={name}>{name}</span>;
        }}
      />
    </>
  );
});
