import React, { memo, useState, useCallback } from 'react';
import { Panel } from 'rsuite';
import { useMainPage } from '@/components/hooks/useMainPage';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import Navbar from '@/components/Navbar';
import { usePickerDataFromObj } from '@/utils/formatMessage';
import DrawerForm from './DrawerForm';
import Table from './Table';
import { defaultFilter, TAB_KEY, navData, defaultFormData } from './constants';
import { API_PARA } from '@/constants/APIs';
import { ParaModel } from '@/constants/form/models';

export default memo(() => {
  const navDataIntl = usePickerDataFromObj(navData, { labelKey: 'label' }) as any;
  const { customParamResult } = useMainPage({
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultFilter
      }
    ]
  });

  const [tableResult] = customParamResult;
  const {
    handleChangePagination,
    handleChangeCustomParam,
    param,
    fmtParam,
    handleSearchWord
  } = tableResult;

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  const {
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    handleFormChange,
    handleFormCreate,
    handleFormEdit,
    handleFormClose,
    handleFormSubmit,
    setFormErrors,
    checkForm
  } = useDrawerForm({
    defaultFormData,
    api: API_PARA,
    model: ParaModel,
    submitSuccessCallback: refetchTableData
  });

  const [tab, setTab] = useState<TAB_KEY>(TAB_KEY.SYSTEM);
  const navSelectHandler = useCallback(
    val => {
      setTab(val);
      handleChangeCustomParam({
        ...defaultFilter,
        sSearch: ''
      } as any);
    },
    [handleChangeCustomParam]
  );

  return (
    <>
      <Panel className="panel-table">
        <Navbar
          appearance="subtle"
          activeKey={tab}
          data={navDataIntl}
          onSelect={navSelectHandler}
        />
        <Table
          tab={tab}
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          handleSearchWord={handleSearchWord}
          handleChangeCustomParam={handleChangeCustomParam}
          handleFormCreate={handleFormCreate}
          handleFormEdit={handleFormEdit}
        />
        <DrawerForm
          drawer={drawer}
          formData={formData}
          originalFormData={originalFormData}
          formErrors={formErrors}
          formRef={formRef}
          onChange={handleFormChange}
          onSubmit={handleFormSubmit}
          onClose={handleFormClose}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        />
      </Panel>
    </>
  );
});
