import React, { useCallback } from 'react';
import _ from 'lodash';

import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface ExportStatusCellProps {
  refetchTableData?: Function;
  onFetch?: Function;
}

export default ({
  rowData,
  dataKey,
  refetchTableData,
  onFetch,
  ...props
}: BaseCellProps<any> & ExportStatusCellProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const handleRetry = useCallback(() => {
    onFetch({
      params: {
        id: rowData.id
      },
      data: {
        status: 'EMITED'
      }
    }).then(() => {
      refetchTableData && refetchTableData();
    });
  }, [onFetch, refetchTableData, rowData]);

  const renderContent = useCallback(() => {
    const status = _.get(rowData, dataKey);
    switch (status) {
      case 'EMITED':
        return <span>{formatMessage('i18n_emited')}</span>;
      case 'WAITEXC':
      case 'WAITING':
        return <span>{formatMessage('i18n_waitexc')}</span>;
      case 'COMPLETED':
        return <span>{formatMessage('i18n_completed')}</span>;
      case 'NO_DATA':
        return <span>{formatMessage('i18n_nodata')}</span>;
      case 'FAILED':
        return (
          <div>
            {formatMessage('i18n_exportStatusFailed')} &nbsp;({' '}
            <a onClick={handleRetry}>{formatMessage('i18n_recalculate')}</a>)
          </div>
        );
      default:
        return <span>{DEFAULT_TABLE_CELL_CONTENT}</span>;
    }
  }, [dataKey, rowData, formatMessage, handleRetry]);

  return <Cell dataKey={dataKey} renderContent={renderContent} {...props} />;
};
