const freqs = ['Total', 'Daily', 'Weekly', 'Monthly'];
export const frequencyData = _.map(new Array(40), (i, id) => ({
  id: id + 1,
  name: `${String((id % 10) + 1)}${id % 10 === 9 ? '+' : ''}${
    id / 10 < 1 ? '' : `/${freqs[Math.floor(id / 10)]}`
    }`,
  tag: freqs[Math.floor(id / 10)]
})).concat(
  {
    id: 51,
    name: 'NA',
    tag: 'Other'
  },
  {
    id: 52,
    name: 'NoCap',
    tag: 'Other'
  }
);

