export enum filterParamKeys {
  CAMPAIGN = 'campaignId',
  STATUS = 'status'
}

export interface CommonParam {
  campaignId: number[];
}

export interface CustomParam {
  status?: string | number;
}

export type FilterParam = CommonParam & CustomParam;

export const defaultCommonParam: CommonParam = {
  campaignId: []
};

export const defaultCustomParam: CustomParam = {
  status: 3
};

export const defaultFilter: FilterParam = {
  ...defaultCommonParam,
  ...defaultCustomParam
};

export enum uploadFilterParamKeys {
  TASK_ID = 'taskId'
}

export interface UploadFilterParam {
  taskId: string;
}

export const defaultUploadFilter: UploadFilterParam = {
  taskId: ''
};

export enum UploadReducerType {
  SET_UPLOADING_STATUS = 'SET_UPLOADING_STATUS',
  REQUEST_TABLE_DATA_START = 'REQUEST_TABLE_DATA_START',
  REQUEST_TABLE_DATA_ACC = 'REQUEST_TABLE_DATA_ACC',
  REQUEST_TABLE_DATA_EXP = 'REQUEST_TABLE_DATA_EXP',
  REQUEST_TABLE_DATA_INTERVAL_START = 'REQUEST_TABLE_DATA_INTERVAL_START',
  REQUEST_TABLE_DATA_INTERVAL_END = 'REQUEST_TABLE_DATA_INTERVAL_END'
}

export interface UploadReducerAction {
  type: UploadReducerType;
  isUploading?: boolean;
  data?: Array<any>;
}

export interface UploadReducerState {
  isUploading: boolean;
  data: Array<any>; // 表格的数据
  loading: boolean; // 表格loading的状态
  reqDelay: number | null; // 表格数据请求的interval
}

export const defaultCampaignIdKeyFromState = 'defaultCampaignId';
