import _ from 'lodash';
import { getStorage } from '@/utils/storage';
import { __DEBUG__, __PUBLIC__, __PUBLIC_API__ } from '@/constants/config';
import { getEnvFromSession } from '@/utils/env';
import { getValidMenus } from '@/utils/auth';
import { findTree } from '@/utils/tree';
import { userMenu } from '@/constants/MenuConfig';

// 跳转错误页面
export const goErrorPage = (code = 404, replace = window.location.replace) => {
  replace(`${window.location.origin}${__PUBLIC__}/error/${code}`);
};

// 获取用户信息
export function getUserProfile() {
  const profile = localStorage.getItem('profile') || '{}';
  try {
    return JSON.parse(decodeURIComponent(profile));
  } catch {
    return {};
  }
}

export function getUserIdFromProfile() {
  return _.get(getUserProfile(), 'id');
}

// 移除用户信息
export function removeUserProfile() {
  localStorage.removeItem('profile');
}

// 设置用户信息
export function setUserProfile(data: any) {
  localStorage.setItem('profile', encodeURIComponent(JSON.stringify(data)));
}

// 获取没有PUBLIC_PATH的url
export function getTrimmedUrl(url: string) {
  if (!_.isString(url)) {
    return '';
  }

  if (__PUBLIC__ === '') {
    return url;
  }

  return url.replace(new RegExp(`^${__PUBLIC__}`), '');
}

// 获取location.pathname，截取删除了PUBLIC_PATH
export function getPathname() {
  let { pathname } = window.location;
  if (pathname.startsWith(__PUBLIC__)) {
    pathname = pathname.slice(__PUBLIC__.length);
  }
  return pathname;
}

// 获取选择的视图的 id
export function getViewId(): number {
  const pathname = getTrimmedUrl(window.location.pathname);
  if (pathname === '/users') {
    return null;
  }
  if (/^\/users\/[0-9]+/.test(pathname)) {
    return Number(pathname.split('/')[2]);
  }

  return _.get(getStorage(), 'selectedView.id');
}

export function getCampaignId(): number {
  // TODO
  return 0;
}

// 合并视图 ID
export function mergeViewId(...params: any) {
  // const id = getViewId();
  const { id } = getUserProfile();
  if (!id) {
    return params[0];
  }
  // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
  // @ts-ignore
  return _.assign(...params, {
    userId: id
  });
}

// 获取有PUBLIC_PATH的url
export function getFullUrl(url: string) {
  const urlArr = url.split('/');
  if (__PUBLIC__ === '') {
    console.log('__PUBLIC__ empty');
  } else if (_.startsWith(urlArr[0], 'http')) {
    if (`/${urlArr[3]}` !== __PUBLIC__) {
      urlArr.splice(3, 0, __PUBLIC__.slice(1));
    }
  } else if (`/${urlArr[1]}` !== __PUBLIC__) {
    urlArr.splice(0, 1, __PUBLIC__);
  }
  return urlArr.join('/');
}

// 跳转到首页，取userMenu配置项里面的第一个有效的页面，会默认跟userInfo的validUrls做交集
export function getFirstPage(id?: any) {
  const { beId = '' } = getUserProfile();
  const userId = id || beId;
  const menus = getValidMenus({ userId }, userMenu);
  const home = findTree(menus, (i: any) => !!i.link);
  const target = _.get(home, 'link');
  return target;
}

export function goFirstPage(id?: any, replace = window.location.replace) {
  replace(getFirstPage(id));
}

// 替换location，并加上PUBLIC_PATH
export function replaceLocation(url: string) {
  let destUrl = url;

  if (_.startsWith(url, '/api/v1/')) {
    destUrl = `${__PUBLIC_API__}${url}`;
  } else if (_.startsWith(url, 'http')) {
    destUrl = url;
  } else {
    destUrl = getFullUrl(url);
  }

  window.location.replace(destUrl);
}

export function goLogIn() {
  const url = __DEBUG__ ? getEnvFromSession('localLoginUrl') : getEnvFromSession('loginUrl');
  // TODO: logout and remove user data
  replaceLocation(url);
}

// 判断是否在iframe中
export function isIframe() {
  return window.parent.location !== window.location;
}
