import React from 'react';
import { Popover, Whisper } from 'rsuite';
import SvgIcon from '@/components/Icon/SvgIcon';

interface Props {
  description: string;
}

export default ({ description }: Props) => {
  const speaker = (
    <Popover>
      <div
        style={{
          whiteSpace: 'pre-wrap',
          maxWidth: 370
        }}
      >
        {description}
      </div>
    </Popover>
  );
  return (
    <Whisper speaker={speaker} trigger="hover" placement="top">
      <span className="help-icon">
        <SvgIcon icon="help-o" width={12} height={12} />
      </span>
    </Whisper>
  );
};
