/* eslint-disable */
// 不然 不能ts-ignore
import _ from 'lodash';
import * as React from 'react';

interface InnerRefObj {
  innerRef?: any;
}

interface TableRefObj {
  tableRef?: any;
}

const innerRefKey = 'innerRef';
export const withRefByInnerRef = <T>(WrappedComponent: React.ElementType) => {
  return (props: T & InnerRefObj) => {
    return <WrappedComponent {..._.omit(props, innerRefKey)} ref={props[innerRefKey]} />;
  };
};

const tableRefKey = 'tableRef';
export const withRefByTableRef = <T>(WrappedComponent: React.ElementType) => {
  return (props: T & TableRefObj) => {
    return <WrappedComponent {..._.omit(props, tableRefKey)} ref={props[tableRefKey]} />;
  };
};
