import React from 'react';
import { Modal, Button } from 'rsuite';

import { useFmtMsgFori18n } from '@/utils/formatMessage';

export { UseModalResult } from '@/components/hooks/useModal';

interface Props {
  show?: boolean;
  loading?: boolean;
  confirm?: boolean;
  size?: 'lg' | 'md' | 'sm' | 'xs';
  title?: string | React.ElementType;
  submitText?: string | React.ElementType;
  cancelText?: string | React.ElementType;
  body?: string | React.ElementType;
  footer?: string | React.ElementType;
  children?: React.ReactElement;
  backdrop?: boolean;
  onHide?: () => void;
  onSubmit?: (value: boolean) => void;
}

export default React.memo(function Dialog({
  show,
  children,
  body,
  title,
  confirm,
  size,
  loading,
  onHide,
  onSubmit,
  footer,
  submitText,
  cancelText,
  backdrop,
  ...props
}: Props) {
  const { formatMessage } = useFmtMsgFori18n();
  function handleClose() {
    onHide && onHide();
  }

  function handleSubmit(value) {
    onSubmit && onSubmit(value);
  }

  return (
    <Modal
      {...props}
      backdrop={backdrop}
      size={confirm ? 'xs' : size || 'md'}
      show={show}
      onHide={handleClose}
    >
      {title && (
        <Modal.Header>
          <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
      )}
      <Modal.Body>{body || children}</Modal.Body>
      <Modal.Footer>
        {footer || (
          <>
            <Button
              key="submit"
              loading={loading}
              onClick={() => handleSubmit(true)}
              appearance="primary"
            >
              {submitText || formatMessage('i18n_confirm')}
            </Button>
            <Button
              key="cancel"
              loading={loading}
              onClick={() => handleSubmit(false)}
              appearance="subtle"
            >
              {cancelText || formatMessage('i18n_cancel')}
            </Button>
          </>
        )}
      </Modal.Footer>
    </Modal>
  );
});
