import React, { useContext } from 'react';

import { useFmtMsgFori18n } from '@/utils/formatMessage';
import SelectPicker from '@/components/Picker/SelectPicker';

import { FilterExtCtx } from '../../../context';

interface KeyColumnProps {
  value: string;
  data: Array<{ id: string; name: string }>;
  onChange: (val: string) => void;
}

export default ({ value, data, onChange }: KeyColumnProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const { keyColumnLoading, disabled } = useContext(FilterExtCtx);
  return (
    <SelectPicker
      cleanable
      data={data}
      value={value}
      onChange={onChange}
      loading={keyColumnLoading}
      placeholder={formatMessage('i18n_selDimension')}
      appearance="subtle"
      menuStyle={{ minWidth: 250 }}
      disabled={disabled}
    />
  );
};
