import { useState, useMemo, useCallback, useEffect, useRef } from 'react';
import _ from 'lodash';

import {
  TABLE,
  TABLE_PARAMS,
  DEFAULT_TABLE_PARAMS,
  TABLE_KEY,
  SEARCH_INPUT_DEBOUNCE_TIME
} from '@/constants/config';
import { getStorage, updateStorageByKey } from '@/utils/storage';

/**
 * 页面所有的Filter分为两类
 *  公共的Filter
 *  某个组件（表格、charts）私有的Filter，只会作用到这个组件上
 *
 * 传入必要的配置项
 *  默认的公共param，和他的格式化方法
 *  自定义组件的私有param，和他的格式化方法，以及这个自定义组件是否是表格（如果是表格的话内部会注入page/pagesize/orderType/orderColumn）
 *
 * 针对所有表格的选项，表格的page/pagesize/orderType/orderColumn
 *
 * 组件输出
 *  公用param的原始数据结构
 *  格式化后的param
 *  修改公用的param的函数
 *
 *  自定义组件的结果列表（跟传入数组的index对应），主要包含：
 *    自己作用域可以看到的所有的param的原始数据结构（包含公用的param）
 *    自己作用域可以看到的所有的param的格式化后的数据结构(common走common的format，自定义的走自定义的格式化然后合并，如果是表格，合并表格参数)
 *    单独修改自己作用域的param的函数
 *    单独修改页面分页参数的函数
 *    搜索的接口，改私有的param的sSearch字段，已经是debounced版本了
 */

interface MainPageInput<TComm, TParam> {
  defaultCommonParam?: TComm;
  commonParamFormatter?: (commonParam: TComm) => any;
  tableOptions?: TABLE; // 暂时没有支持 自定义config里面制定tableOptions
  customConfig?: Array<{
    // NOTE: 只会读取一次，不支持改变，组件内部实现不会监听这个变量的变化！
    isTable?: boolean; // 如果是table，返回的customParam对应的fmtParam，需要注入table独有的page/pagesize/orderType/orderColumn
    defaultParam?: TParam;
    // 这里为了让传入的paramFormatter更灵活，提供给回调函数的参数是 整个customParam原始数据结构(不包含common)
    paramFormatter?: (customParam?: TParam) => any;
  }>;
}

interface MainPageOutput<TComm, TParam> {
  commonParam: TComm; // 通用的param
  fmtCommonParam: any; // fmt之后的通用的param
  handleChangeCommonParam: (partialObj: Partial<TComm>) => void;
  handleDebouncedChangeCommonParam: (partialObj: Partial<TComm>) => void; // 同上，自带debounce
  /**
   * 想加这些接口，但是想想要不算了，这样的话 外面有一层含有表格分页的数据，里面还有一层，怪怪的。。
   * *** Keep It Simple 好了，我们的认定就是table只有表格才有，所以得自己指定一个customConfig，从哪里拿数据
   * 如果页面有全局搜索filter的时候，自己调用debounced方法去处理吧
   *
   * handleChangePagination: (partialTablePagination: TABLE | ((prevFmtParam: any) => any)) => void;
   * handleSearchWord: (word: string) => void;
   * fmtCommonParamWithPageInfo: any;
   */
  customParamResult: Array<{
    customParam: TParam; // 只包含customParam的原始数据结构
    param: TParam & TComm; // 合并了commonParam的param(原始的数据结构)
    fmtParam: any; // fmt之后的所有param，如果是table的，会插上参数，直接可以用这个请求了
    handleChangeCustomParam: (partialObj: Partial<TParam>) => void; // 只会改变当前custom的这个param，如果是table，把table的page置1
    handleDebouncedChangeCustomParam: (partialObj: Partial<TParam>) => void; // 同上，自带debounce
    // 只有在对应配置项是isTable的时候有效，只会改变fmtParam里面的分页信息
    handleChangePagination: (partialTablePagination: TABLE | ((prevFmtParam: any) => any)) => void;
    handleSearchWord: (word: string) => void; // 这里用于封装debounce版本的search，如果是表格，置表格页码为1
  }>;
}

const emptyFn = () => undefined as any;
export function useMainPage<TComm, TParam>({
  defaultCommonParam,
  commonParamFormatter,
  tableOptions,
  customConfig // DON'T DIRECTLY USE ME!!!
}: MainPageInput<TComm, TParam>): MainPageOutput<TComm, TParam> {
  const customConfigRef = useRef(customConfig).current;
  const session = useRef(getStorage()).current;

  // 通用的属性，这里比较好处理，保存useState保存原始数据结构，useMemo保存格式化后的数据结构
  const [commonParam, setCommonParam] = useState<TComm>(defaultCommonParam);
  const fmtCommonParam = useMemo(() => {
    if (_.isFunction(commonParamFormatter)) {
      return commonParamFormatter(commonParam);
    }

    return commonParam;
  }, [commonParam, commonParamFormatter]);

  const defaultCustomParamData: MainPageOutput<TComm, TParam>['customParamResult'] = useMemo(
    () =>
      _.map(customConfigRef, c => {
        const { isTable, defaultParam, paramFormatter } = c;
        // 原始的commonParam 和 原始的 param合并
        const mergedParam = _.assign({}, defaultCommonParam, defaultParam);
        // 格式化后的commonParam 与 格式化后的 customParam合并
        const fmtParam = _.assign(
          {},
          fmtCommonParam,
          _.isFunction(paramFormatter) ? paramFormatter(defaultParam) : defaultParam
        );

        return isTable
          ? {
              customParam: defaultParam,
              param: mergedParam,
              fmtParam: _.assign(
                {},
                fmtParam,
                DEFAULT_TABLE_PARAMS,
                _.pick(session, TABLE_PARAMS), // 优先从storage里面去
                tableOptions
              ),
              handleChangeCustomParam: emptyFn,
              handleDebouncedChangeCustomParam: emptyFn,
              handleChangePagination: emptyFn,
              handleSearchWord: emptyFn
            }
          : {
              customParam: defaultParam,
              param: mergedParam,
              fmtParam,
              handleChangeCustomParam: emptyFn,
              handleDebouncedChangeCustomParam: emptyFn,
              handleChangePagination: emptyFn,
              handleSearchWord: emptyFn
            };
      }),
    [defaultCommonParam, fmtCommonParam, session, tableOptions]
  );

  const [customParamResult, setCustomParamData] = useState(defaultCustomParamData);
  const handleChangeCustomParam = useCallback(
    (idx: number, partialObj: Partial<TParam>) => {
      const { isTable, paramFormatter } = customConfigRef[idx];
      setCustomParamData(prevCustomData =>
        _.map(prevCustomData, (c, i) => {
          if (i === idx) {
            const newParam = _.assign({}, c.param, partialObj);
            const newCustomParamOnly = _.assign({}, c.customParam, partialObj);
            return {
              ...c,
              customParam: newCustomParamOnly,
              param: newParam,
              fmtParam: _.assign(
                {},
                c.fmtParam,
                // 这里为了让传入的paramFormatter更灵活，提供给回调函数的参数是 不包含commonParam的所有customParam，这样支持增量更新
                _.isFunction(paramFormatter) ? paramFormatter(newCustomParamOnly) : partialObj,
                { ...(isTable && { [TABLE_KEY.PAGE]: 1 }) }
              )
            };
          }

          return c;
        })
      );
    },
    [customConfigRef]
  );

  const handleChangePagination = useCallback(
    (idx: number, partialTableParam: TABLE | ((prevFmtParam: any) => any)) => {
      const { isTable } = customConfigRef[idx];
      if (isTable) {
        // 只会在isTable的时候生效
        setCustomParamData(prevCustomData =>
          _.map(prevCustomData, (c, i) => {
            const ppp = _.isFunction(partialTableParam)
              ? partialTableParam(c.fmtParam)
              : partialTableParam;
            const validTableKeys = _.pick(ppp, TABLE_PARAMS);
            const prevPgSize = _.get(c.fmtParam, TABLE_KEY.PAGE_SIZE);
            const newPgSize = _.get(validTableKeys, TABLE_KEY.PAGE_SIZE);
            if (newPgSize !== prevPgSize) {
              updateStorageByKey(TABLE_KEY.PAGE_SIZE, newPgSize);
            }

            if (i === idx) {
              return {
                ...c,
                fmtParam: _.assign({}, c.fmtParam, validTableKeys)
              };
            }

            return c;
          })
        );
      }
    },
    [customConfigRef]
  );

  const handleChangeCommonParam = useCallback((partialObj: Partial<TComm>) => {
    setCommonParam(cc => _.assign({}, cc, partialObj));
  }, []);

  const handleDebouncedChangeCommonParam = useMemo(
    () =>
      _.debounce(
        (partialObj: Partial<TComm>) => handleChangeCommonParam(partialObj),
        SEARCH_INPUT_DEBOUNCE_TIME
      ),
    [handleChangeCommonParam]
  );

  useEffect(() => {
    setCustomParamData(prevCustomData =>
      _.map(prevCustomData, (c, idx) => {
        return {
          ...c,
          param: _.assign({}, c.param, commonParam),
          fmtParam: _.assign(
            {},
            c.fmtParam,
            _.isFunction(commonParamFormatter) ? commonParamFormatter(commonParam) : commonParam,
            { ...(customConfigRef[idx].isTable && { [TABLE_KEY.PAGE]: 1 }) }
          ),
          // 如果没有绑定函数的话（第一次），绑定对定的函数
          ...(c.handleChangeCustomParam === emptyFn && {
            handleChangeCustomParam: (partialObj: Partial<TParam>) =>
              handleChangeCustomParam(idx, partialObj)
          }),
          ...(c.handleDebouncedChangeCustomParam === emptyFn && {
            handleDebouncedChangeCustomParam: _.debounce(
              (partialObj: Partial<TParam>) => handleChangeCustomParam(idx, partialObj),
              SEARCH_INPUT_DEBOUNCE_TIME
            )
          }),
          ...(c.handleChangePagination === emptyFn && {
            handleChangePagination: (
              partialTableParam: Partial<TABLE> | ((prevFmtParam: any) => any)
            ) => handleChangePagination(idx, partialTableParam)
          }),
          ...(c.handleSearchWord === emptyFn && {
            // 调用的时候自动会把word传给debounced version of function
            handleSearchWord: _.debounce(
              w => handleChangeCustomParam(idx, { sSearch: w } as any),
              SEARCH_INPUT_DEBOUNCE_TIME
            )
          })
        };
      })
    );
  }, [
    commonParam,
    commonParamFormatter,
    handleChangeCustomParam,
    handleChangePagination,
    customConfigRef
  ]);

  return {
    commonParam,
    fmtCommonParam,
    handleChangeCommonParam,
    handleDebouncedChangeCommonParam,
    customParamResult
  };
}
