import React, { useCallback } from 'react';
import _ from 'lodash';
import { Tooltip, Whisper, Icon } from 'rsuite';
import Cell, { BaseCellProps } from './FloatActionCell';
import { formatMessage } from '@/utils';
import { TABLE_TOOLTIPS_DELAY_SHOW_TIME, AuditStatusData } from '@/constants/config';

interface StatusCellProps extends Partial<BaseCellProps<any>> {
  dataKey?: string;
}

const StatusCell = ({
  rowData,
  dataKey = 'status',
  ...props
}: StatusCellProps): React.ReactElement => {
  const renderContent = useCallback(() => {
    let statusFromBackend = _.get(rowData, dataKey);
    statusFromBackend = statusFromBackend === 'DELETED' ? 'PAUSED' : statusFromBackend;
    const msg = _.get(rowData, 'auditInfo');
    const speaker = (
      <Tooltip className="popover-not-title">
        <span className="word-break">{msg}</span>
      </Tooltip>
    );
    const html = msg ? (
      <Whisper placement="top" speaker={speaker} delayShow={TABLE_TOOLTIPS_DELAY_SHOW_TIME}>
        <Icon icon="question-circle2" />
      </Whisper>
    ) : null;
    const text = formatMessage(AuditStatusData.filter(i => i.value === statusFromBackend)[0].label);
    return (
      <>
        {text}
        <span style={{ marginLeft: 5 }}>{html}</span>
      </>
    );
  }, [rowData, dataKey]);

  return <Cell dataKey={dataKey} rowData={rowData} renderContent={renderContent} {...props} />;
};

export default React.memo(StatusCell);
