import _ from 'lodash';
import React, { useCallback } from 'react';
import { StatusIcon } from '@/components/Icon';
import { DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import Cell, { BaseCellProps } from './FloatActionCell';

export interface TextCellProps<T> extends BaseCellProps<T> {
  maxLine?: number;
  showStatus?: boolean;
  statusKey?: string;
  formatter?: (data?: any, rowData?: any) => any;
  statusMapperFn?: (statusFromBackend: string | number) => string;
}

const TextCell = ({
  rowData,
  dataKey,
  maxLine = 3,
  renderActions,
  formatter,
  showStatus = false,
  statusKey = 'status',
  statusMapperFn,
  ...props
}: TextCellProps<any>) => {
  const text = _.get(rowData, dataKey, DEFAULT_TABLE_CELL_CONTENT);
  const child = (
    <>
      {(_.isFunction(formatter) && formatter(text, rowData)) ||
        (_.isString(text) && text) ||
        DEFAULT_TABLE_CELL_CONTENT}
    </>
  );

  const renderContent = useCallback(() => {
    return (
      <div
        style={{
          overflow: 'hidden',
          display: '-webkit-box',
          WebkitBoxOrient: 'vertical',
          WebkitLineClamp: maxLine,
          whiteSpace: 'initial'
        }}
      >
        {child}
      </div>
    );
  }, [child, maxLine]);
  const renderTitle = useCallback(() => {
    return <>{child}</>;
  }, [child]);

  const renderEleBeforeContent = useCallback(() => {
    const statusFromBackend = _.get(rowData, statusKey, '');
    const targetStatus = (statusMapperFn && statusMapperFn(statusFromBackend)) || statusFromBackend;

    return <StatusIcon status={targetStatus} />;
  }, [rowData, statusKey, statusMapperFn]);

  return (
    <Cell
      className="name-cell"
      dataKey={dataKey}
      rowData={rowData}
      renderEleBeforeContent={renderEleBeforeContent}
      renderContent={renderContent}
      renderTitle={renderTitle}
      renderActions={renderActions}
      {...props}
    />
  );
};

export default React.memo(TextCell);
