import React, { useCallback } from 'react';
import _ from 'lodash';
import { Whisper, Tooltip } from 'rsuite';

import { DEFAULT_TABLE_CELL_CONTENT, TABLE_TOOLTIPS_DELAY_SHOW_TIME } from '@/constants/config';
import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';

export const PathCell = ({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const data = _.get(rowData, dataKey, []) || [];

  const renderContent = useCallback(() => {
    if (!data.length || _.isNil(data) || data === '--') {
      return <>{DEFAULT_TABLE_CELL_CONTENT}</>;
    }

    const originResult = data
      .filter((dd: any) => dd.item)
      .map((d: any, index: number) => {
        const { type, track, count, item } = d;
        const tooltip = <Tooltip>{track ? `${track}-${item}` : item}</Tooltip>;
        return (
          <div
            key={`${item}-${count}-${track}-${type}-${index}`}
            className={`pathcell-entity ${type}`}
          >
            <Whisper
              speaker={tooltip}
              delayShow={TABLE_TOOLTIPS_DELAY_SHOW_TIME}
              placement="autoVerticalStart"
            >
              <span>{item}</span>
            </Whisper>
          </div>
        );
      });

    let result = originResult;
    if (result.length > 6) {
      result = result
        .slice(0, 3)
        .concat(
          <div key="..." className="dotdotdot">
            <div>...</div>
          </div>
        )
        .concat(result.slice(-3));
    }

    return <div className="pathcell-container">{result}</div>;
  }, [data]);

  // 这个是整个cell的tooltip的render函数，这里用不到
  const renderTitle = useCallback(() => {
    return null;
  }, []);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderTitle={renderTitle}
      renderContent={renderContent}
      {...props}
    />
  );
};
