import * as React from 'react';
import { DrawerContainerContext } from '@/components/Drawer';

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

/**
 * 为picker添加drawer的container
 */

export interface UseContainerProps {
  container?: HTMLElement | (() => HTMLElement); // 可传入自定义container, 优先级最高
  withContainer?: boolean; // 设置开关，默认开启
}

interface OutProps {
  container: HTMLElement | (() => HTMLElement);
}

const { useContext } = React;

const useContainerForPicker: PropMapper<UseContainerProps, OutProps> = ({
  container,
  withContainer = true,
  ...props
}) => {
  const getContainer = useContext(DrawerContainerContext);

  return {
    container: container || (withContainer ? getContainer : null),
    ...props
  };
};

export default useContainerForPicker;
