/**
 * 可作为任意Cell的BaseComponent
 * 支持Action、以及超出N个之后变成Dropdown
 * 支持自定义渲染content
 */
/* eslint-disable */
import _ from 'lodash';
import React, { ReactElement } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { Table, Tooltip, Whisper, Icon, IconButton, Popover, Dropdown } from 'rsuite';
import { WhisperProps } from 'rsuite/lib/Whisper/index';
// @ts-ignore
import createChainedFunction from 'rsuite/lib/utils/createChainedFunction';

import { TABLE_TOOLTIPS_DELAY_SHOW_TIME, DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';
import * as Actions from './Actions';
import Action from './Action';

const { Cell } = Table;

export enum ACTION_TYPE {
  'ACTION',
  'DROPDOWN_ITEM'
}

export interface BaseCellProps<T> {
  rowData?: T;
  rowIndex?: number;
  dataKey: string;
  className?: string;
  titleKey?: true | string;
  renderTitle?: Function;
  maxActionNum?: number;
  delayShow?: number;
  // README: 这里如果不是传一个React.ReactChild的数组，是不会触发超过数量以dropdown形式展示的Feature的
  renderActions?: (
    rowData: T,
    rowIndex: number
  ) => React.ReactChild | React.ReactChildren | React.ReactChild[];
  renderContent?: (rowData: T) => ReactElement;
  renderEleBeforeContent?: (rowData?: T) => ReactElement;
  renderEleAfterContent?: (rowData?: T) => ReactElement;
  placementForContent?: WhisperProps['placement'];
  placementForDropDown?: WhisperProps['placement'];
}

interface State {
  moreActionBarOpen: boolean;
}

class FloatActionCell extends React.Component<BaseCellProps<BaseObject<any>>, State> {
  dropdown: any;

  static propTypes = {
    rowData: PropTypes.object,
    dataKey: PropTypes.string,
    maxActionNum: PropTypes.number,
    titleKey: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    delayShow: PropTypes.number,
    className: PropTypes.string,
    renderActions: PropTypes.func,
    renderContent: PropTypes.func,
    renderEleBeforeContent: PropTypes.func,
    renderEleAfterContent: PropTypes.func,
    children: PropTypes.any,
    renderTitle: PropTypes.func,
    placementForContent: PropTypes.string,
    placementForDropDown: PropTypes.string
  };

  static defaultProps = {
    maxActionNum: 3,
    delayShow: TABLE_TOOLTIPS_DELAY_SHOW_TIME,
    titleKey: true,
    placementForContent: 'autoVerticalStart',
    placementForDropDown: 'autoVerticalStart'
  };
  static Action: typeof Action;
  static Actions: typeof Actions;

  constructor(props: BaseCellProps<BaseObject<any>>) {
    super(props);
    this.state = {
      moreActionBarOpen: false
    };
  }

  bindDropdownRef = (ref: any) => {
    this.dropdown = ref;
  };

  hideDropdown = () => {
    this.dropdown && this.dropdown.hide();
  };

  handleDropdownToggleClick = () => {
    this.setState({
      moreActionBarOpen: true
    });
  };

  handleDropdownExited = () => {
    this.setState({
      moreActionBarOpen: false
    });
  };

  handleDropdownMenuItemClick: (onClick: () => void) => () => void = _.partial(
    createChainedFunction,
    this.hideDropdown,
    _
  );

  mapToDropDownItem = (action: ReactElement) =>
    React.cloneElement(action, {
      type: ACTION_TYPE.DROPDOWN_ITEM,
      onClick: this.handleDropdownMenuItemClick(_.get(action, 'props.onClick'))
    });

  render() {
    const {
      rowData,
      rowIndex,
      dataKey,
      className,
      titleKey,
      renderTitle,
      maxActionNum,
      delayShow,
      renderActions,
      renderContent,
      renderEleBeforeContent,
      renderEleAfterContent,
      placementForContent,
      placementForDropDown
    } = this.props;
    const props = _.omit(this.props, _.keys(FloatActionCell.propTypes));
    const data = _.get(rowData, dataKey);
    const title =
      (_.isFunction(renderTitle)
        ? renderTitle(rowData)
        : _.get(rowData, titleKey === true ? dataKey : titleKey)) || null;
    // @ts-ignore
    const children: ReactElement[] =
      (renderActions && renderActions(rowData, rowIndex)) || this.props.children || [];
    let content = renderContent ? (
      renderContent(rowData)
    ) : (
      <span>{data && data !== '' ? data : DEFAULT_TABLE_CELL_CONTENT}</span>
    );
    if (title && title !== DEFAULT_TABLE_CELL_CONTENT) {
      // 后端有时候会返回--，这时候应该有hoverTip
      const tooltip = <Tooltip>{title}</Tooltip>;
      content = (
        <Whisper placement={placementForContent} speaker={tooltip} delayShow={delayShow}>
          {content}
        </Whisper>
      );
    }
    const needDropDown = children.length > maxActionNum;
    // 固定数 =  最大 action 数 -1
    const fixedActionNum = maxActionNum - 1;
    const actions = needDropDown ? _.take(children, fixedActionNum) : children;
    const dropDownActions = needDropDown
      ? _.takeRight(children, children.length - fixedActionNum)
      : [];
    return (
      <Cell
        className={classnames(
          'float-action-cell',
          { 'more-action-bar-open': this.state.moreActionBarOpen },
          className
        )}
        {...props}
      >
        {renderEleBeforeContent && renderEleBeforeContent(rowData)}
        {content}
        {renderEleAfterContent && renderEleAfterContent(rowData)}
        <div className="float-action-cell-action-bar">
          {actions}
          {needDropDown && (
            <Whisper
              placement={placementForDropDown}
              trigger="click"
              triggerRef={this.bindDropdownRef}
              speaker={
                <Popover full>
                  <Dropdown.Menu>{dropDownActions.map(this.mapToDropDownItem)}</Dropdown.Menu>
                </Popover>
              }
              onExited={this.handleDropdownExited}
            >
              <IconButton
                onClick={this.handleDropdownToggleClick}
                appearance="link"
                circle
                icon={<Icon icon="more" />}
              />
            </Whisper>
          )}
        </div>
      </Cell>
    );
  }
}

FloatActionCell.Action = Action;
FloatActionCell.Actions = Actions;

export default FloatActionCell;
