import React, { memo, useCallback, useRef, useMemo } from 'react';
import _ from 'lodash';
import { Button, Alert } from 'rsuite';
import { RouteComponentProps } from 'react-router-dom';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SelectPicker from '@/components/Picker/SelectPicker';
import Table, { Column, Cell, HeaderCell, tableUtils } from '@/components/Table';
import { FloatActionCell, NameCell, TextCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { useModal } from '@/components/hooks/useModal';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import template from '@/utils/template';
import download from '@/utils/download';
import { BasicReportProps } from '@/constants/config';
import { API_CONVERSION, API_CONVERSION_CODE, API_CONVERSION_PUT } from '@/constants/APIs';
import Modal from './Modal';
import { defaultFilter, statusData, filterParamKeys, FilterParam } from './constants';

const mockData: CustomTableDataProps<any> = {
  data: [{ id: 14, name: '123123（来源：Web Analytics）', status: 1, src: 'HWA (项目ID：89437)' }],
  page: { current: 0, total: 8, size: 100 }
};

const maxLine = 1;
const mapStatusFn = (statusFromBackend: number) => {
  switch (statusFromBackend) {
    case 1:
      return 'active';
    case 0:
    default:
      return 'disabled';
  }
};

type TableProps = Partial<BasicReportProps<FilterParam> & RouteComponentProps<{}>> & {
  handleFormCreate: Function;
  handleFormEdit: Function;
  handleShowBatch: Function;
};

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    refetchTableData,
    handleSearchWord,
    handleChangeCommonParam,
    handleFormCreate,
    handleFormEdit,
    handleShowBatch
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();

    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      postParams: {
        subCount: 1
      },
      api: API_CONVERSION
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const tableRef = useRef(null);

    // 状态筛选
    const handleStatusChange = useCallback(
      (status: string) => {
        handleChangeCommonParam({ [filterParamKeys.STATUS]: status });
      },
      [handleChangeCommonParam]
    );

    // 导出表格
    const handleExportTable = useCallback(() => {
      onExport && onExport({}, template`${API_CONVERSION}.xlsx`);
    }, [onExport]);

    // 导出代码
    const handleExportCode = useCallback(() => {
      const conversionIds = tableUtils(tableRef).getCheckValues();
      if (_.isEmpty(conversionIds)) {
        Alert.error(formatMessage('i18n_selectOneRequired'));
        return;
      }
      download(API_CONVERSION_CODE, {
        conversionIds
      });
    }, [formatMessage]);

    const statusDataIntl = usePickerDataFromObj(statusData);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <Button key="create" appearance="primary" onClick={handleFormCreate as () => void}>
          {formatMessage('i18n_create')}
        </Button>,
        <Button key="import" appearance="default" onClick={handleShowBatch as () => void}>
          {formatMessage('i18n_bulkImportActivity')}
        </Button>,
        <SelectPicker
          key="status"
          prepend={formatMessage('i18n_status')}
          data={statusDataIntl}
          value={filterParams[filterParamKeys.STATUS]}
          onChange={handleStatusChange}
        />,
        // <Button
        //   key='logs'
        //   appearance='default'
        //   onClick={handleShowBatch as () => void}
        // >
        //   {formatMessage('i18n_import') + formatMessage('i18n_activityLogs')}
        // </Button>,
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportTable}
        >
          {formatMessage('i18n_exportTable')}
        </Button>,
        <Button key="export" className="pull-right" appearance="default" onClick={handleExportCode}>
          {formatMessage('i18n_exportCode')}
        </Button>
      ];
    };
    const modal = useModal(false, {}, API_CONVERSION_PUT, refetchTableData);
    const renderActions = useCallback(
      rowData => {
        const { status } = rowData;

        const disableClickHandler = () => {
          modal.showModal({
            ...rowData,
            status: 0
          });
        };

        const enableClickHandler = () => {
          modal.showModal({
            ...rowData,
            status: 1
          });
        };

        return [
          <FloatActionCell.Actions.Edit
            key="edit"
            onClick={() => {
              handleFormEdit({
                id: rowData.id
              });
            }}
          />,
          status === 1 ? (
            <FloatActionCell.Actions.Disable key="disable" onClick={disableClickHandler} />
          ) : (
              <FloatActionCell.Actions.Enable key="enable" onClick={enableClickHandler} />
            )
        ];
      },
      [handleFormEdit, modal]
    );

    return (
      <>
        <Modal {...modal} />
        <Table
          {...tableDataMemo}
          checkDataKey="id"
          innerRef={tableRef}
          renderToolbar={renderToolbar}
          onChange={onChange}
          wordWrap
        >
          <Column width={100} fixed sortable>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>
          {/* 活动组名称 */}
          <Column flexGrow={1} minWidth={150} fixed sortable>
            <HeaderCell>
              {formatMessage('i18n_activity')}
              {formatMessage('i18n_name')}
            </HeaderCell>
            <NameCell
              dataKey="name"
              showStatus={true}
              statusMapperFn={mapStatusFn}
              renderActions={renderActions}
              maxLine={maxLine}
            />
          </Column>
          {/* 来源 */}
          <Column flexGrow={1} minWidth={150}>
            <HeaderCell>{formatMessage('i18n_belongActivityGroup')}</HeaderCell>
            <TextCell dataKey="conversionGroup" maxLine={maxLine} />
          </Column>
          {/* 来源 */}
          <Column flexGrow={1} minWidth={300}>
            <HeaderCell>{formatMessage('i18n_source')}</HeaderCell>
            <TextCell dataKey="src" maxLine={maxLine} />
          </Column>
          {/* 类型 */}
          <Column flexGrow={1} minWidth={150}>
            <HeaderCell>{formatMessage('i18n_type')}</HeaderCell>
            <TextCell
              dataKey="isOffline"
              formatter={value =>
                value ? formatMessage('i18n_offlineActivity') : formatMessage('i18n_onlineActivity')
              }
              maxLine={maxLine}
            />
          </Column>
        </Table>
      </>
    );
  }
);
