import React, { memo, useCallback } from 'react';

import DatePicker from '@/components/Picker/DatePicker';
import BEPicker from '@/components/Picker/logic/BEPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { BasicReportProps } from '@/constants/config';

import { filterParamKeys, typeData, utilsFn } from './constants';
import { dateIsAfter, dateIsBefore, getToday, subDate } from '@/utils/date/index';

export default memo(({ filterParams, handleChangeCommonParam }: BasicReportProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const typeDataIntl = usePickerDataFromObj(typeData);
  const chosenBE = utilsFn.getChosenBE(filterParams);
  const classifyVal = utilsFn.getClassifyValue(filterParams);
  const isCross = utilsFn.getIsCross(filterParams);
  const dateVal = utilsFn.getDate(filterParams);

  const beChangeHandler = useCallback(
    (val: Array<any>) => {
      handleChangeCommonParam({
        [filterParamKeys.BEID]: val
      });
    },
    [handleChangeCommonParam]
  );

  const typeChangeHandler = useCallback(
    (val: string) => {
      handleChangeCommonParam({
        [filterParamKeys.CLASSIFY]: val
      });
    },
    [handleChangeCommonParam]
  );

  const dateChangeHandler = useCallback(
    (val: Date) => {
      handleChangeCommonParam({
        [filterParamKeys.DATE]: val
      });
    },
    [handleChangeCommonParam]
  );

  const disabledDate = useCallback((date: Date) => {
    const today = getToday();
    return dateIsAfter(date, today) || dateIsBefore(date, subDate(today, 6, 'month'));
  }, []);

  return (
    <>
      <DatePicker
        value={dateVal}
        cleanable={false}
        onChange={dateChangeHandler}
        disabledDate={disabledDate}
        appearance="subtle"
      />
      <SelectPicker
        prepend={formatMessage('i18n_type')}
        data={typeDataIntl}
        appearance="subtle"
        value={classifyVal}
        onChange={typeChangeHandler}
      />
      {!isCross && (
        <BEPicker chosenBE={chosenBE} beChangeHandler={beChangeHandler} appearance="subtle" />
      )}
    </>
  );
});
