/**
 * 内部实现功能点
 *  封装有不同的类型的filter，维护在一个常量里面，供调用者提供一些一些参数，可以拿到请求后端用的结果
 *    如果async的话 请求对应的API，预先定义好，有个绑定关系
 *    如果不是async的话
 *      分两种情况
 *        一种是选择的，一般会有data，也是常量
 *        还一种是input的
 *
 * 一些注意点
 *  release分支的有一个局部变量save\store，在应用之前的改动，需要保存状态，如果直接关闭需要恢复之前的状态
 */
import React, { useState, useMemo, useCallback } from 'react';
import _ from 'lodash';

import Drawer from '@/components/Drawer';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

import TagPanel from './TagPanel';
import { FilterPool } from './FilterPanel/FilterPool';
import { PickerAsyncDataCtx, PickerAsyncDataCtxProps, FilterExtCtx } from './context';
import { AdvFilterInnerValue, AdvFilterValue, FilterNodeConfig } from './constants';
import { newNodeIfEmpty, addIdIfNotExist, removeEmptyVal } from './utils';

/**
 * 组件两种情况
 *  一种是嵌在form内部
 *    没有缓存机制，每次触发改变都会调用onSubmit更新外面的数据value，读的也是外面的数据
 *  一种是以drawer的形式展开，需要一个trigger，会在确认之后展示tag的渲染
 *    有缓存机制，每次触发改变的是内部的state，只有在onSubmit之后才会改变外部的value
 *    每次重新打开表单，都会将外部的value同步到内部的state，即不提交直接关闭不生效
 *
 *  所以都需要提供value、onSubmit参数
 *    两种不同的使用场景，在组件内部实现的时候，会有不同的效果，主要通过传递不同的方法(同名但是方法内部实现区别)给子组件实现
 *
 *  本组件内部数据结构是一个数组，不通过索引index，而通过数组里面的每一个对象提供的id来区分组件避免重复渲染
 *    一开始的props的value，可能是从后端直接获取的没有id，组件会自动补全一个id
 *    onSubmit的参数，会带有id，建议在发送请求的时候删了这个id，在使用组件的过程中仍然维护改id，避免重复渲染
 */
export interface AdvFilterProps {
  inForm?: boolean; // 区分在form内还是在drawer内
  value: AdvFilterValue;
  onSubmit: (value: AdvFilterValue) => void; // 都有空，用于非受控的控制组件的值，相当于onConfirmedChange
  limit?: number; // 限制拓展的层级
  drawerShow?: boolean; // 只针对Drawer有用
  onClose?: Function; // 只针对Drawer有用
  submitLoading?: boolean; // 只针对Drawer有用
  filterData: Array<FilterNodeConfig>;
  asyncData: PickerAsyncDataCtxProps;
  keyColumnLoading?: boolean;
  disabled?: boolean;
}

export default React.memo(
  ({
    inForm = false,
    value,
    onSubmit,
    limit = 5,
    onClose,
    drawerShow = false,
    submitLoading = false,
    filterData,
    asyncData,
    keyColumnLoading = false,
    disabled = false,
    ...rest
  }: AdvFilterProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    // 处理空数组的补全 & 如果没有id的添加id
    const valueWithInnerSharp = useMemo(() => newNodeIfEmpty(addIdIfNotExist(value)), [value]);
    // 内部的保存的value的一个state
    const [filterVal, setFilterVal] = useState<AdvFilterInnerValue>(valueWithInnerSharp);

    const filterExtCtxVal = useMemo(() => ({ keyColumnLoading, disabled }), [
      keyColumnLoading,
      disabled
    ]);

    // 封装一个改变值的方法传给子组件，内部实现区分form与否决定是否缓存
    const handleSetFilterVal = useCallback(
      (val: AdvFilterInnerValue) => {
        if (inForm) {
          // 嵌在form里面的，直接触发onSubmit，不需要缓存
          if (_.isFunction(val)) {
            // 支持类似setData的功能，支持传一个函数
            // FIXME: 这样导致内嵌在Form内的组件，选了一个就被刷新了。
            onSubmit && onSubmit(val(valueWithInnerSharp));
          } else {
            onSubmit && onSubmit(val);
          }
        } else {
          // 先保存在内部的state里面
          setFilterVal(val);
        }
      },
      [inForm, onSubmit, valueWithInnerSharp]
    );

    // 给drawer用的，外部value => 内部state的同步，打开时同步外部的value到内部状态，关闭时抛弃更改
    const syncValueToInnerState = useCallback(
      (val?: AdvFilterInnerValue) => {
        // 同步控制在外部的value到本地的存储，filterVal
        setFilterVal(val || valueWithInnerSharp);
      },
      [valueWithInnerSharp]
    );

    // submit函数的封装，submit的时候同步一下，然后触发参数onSubmit
    const handleSubmit = useCallback(
      (val: AdvFilterInnerValue) => {
        syncValueToInnerState(val);
        onSubmit && onSubmit(val);
      },
      [onSubmit, syncValueToInnerState]
    );

    // drawer打开的时候同步下
    const handleDrawerShow = useCallback(() => {
      syncValueToInnerState();
    }, [syncValueToInnerState]);

    // drawer提交的时候，去除空值
    const handleDrawerSubmit = useCallback(() => {
      handleSubmit(removeEmptyVal(filterVal));
    }, [filterVal, handleSubmit]);

    // drawer关闭的时候同步下
    const handleDrawerClose = useCallback(() => {
      syncValueToInnerState();
      onClose && onClose();
    }, [onClose, syncValueToInnerState]);

    const FilterPoolWithData = () => (
      <FilterExtCtx.Provider value={filterExtCtxVal}>
        <PickerAsyncDataCtx.Provider value={asyncData}>
          <FilterPool
            // 如果嵌套在form里面的，直接传递memo之后的value，不然传递内部的state
            filterVal={inForm ? valueWithInnerSharp : filterVal}
            setFilterVal={handleSetFilterVal}
            filterData={filterData}
            asyncData={asyncData}
            onSubmit={handleSubmit}
            limitNum={limit}
            {...rest}
          />
        </PickerAsyncDataCtx.Provider>
      </FilterExtCtx.Provider>
    );

    // 如果不是在form里面的，一般会有drawer里面有设计稿组件和tag两种展现形式
    if (!inForm) {
      return (
        <>
          <Drawer
            show={drawerShow}
            onEnter={handleDrawerShow}
            onSubmit={handleDrawerSubmit}
            onHide={handleDrawerClose}
            submitLoading={submitLoading}
            title={formatMessage('i18n_advancedFilter')}
            btnMsg={formatMessage('i18n_apply')}
            backdrop="static"
            disabled={disabled}
          >
            {FilterPoolWithData()}
          </Drawer>
          {!drawerShow && (
            <TagPanel
              filterVal={filterVal}
              filterData={filterData}
              setFilterVal={setFilterVal}
              asyncData={asyncData}
              limitNum={limit}
              onSubmit={handleSubmit}
            />
          )}
        </>
      );
    }

    // 如果只在Form里面有，则只会有设计稿组件这样的展现形式
    return <>{FilterPoolWithData()}</>;
  }
);
