import React, { memo, useCallback, useContext, useEffect, useState } from 'react';
import { Col, Divider, Panel, Row } from 'rsuite';
import dayjs from 'dayjs';
import _ from 'lodash';

import DatePicker from '@/components/Picker/DatePicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import { useMainPage } from '@/components/hooks/useMainPage';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useFetchReportDate from '@/components/hooks/useFetchReportDate';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import { TODAY, YESTERDAY } from '@/constants/config';
import { YYYYMMDD } from '@/utils/date/format';
import { toDate } from '@/utils';
// part
import Hit from './part/Hit'; // 汇总
import FigurePie from './part/FigurePie'; // 受众画像 性别
import FigureBar from './part/FigureBar'; // 受众画像 年龄
import Line from './part/Line'; // 触达率
import Bar from './part/Bar'; // 独立频次分布
import Stack from './part/Stack'; // 广告计数|独立受众
import Table from './Table';
import { basicClassName, countryData, defaultTableParam } from './constants';
import { CampaignCtx } from '../context';
import { CampaignType } from '@/views/campaigns/common';
import { updateStatus } from '@/utils/session';

const tableParamFormatter = (f: any) => ({
  ...f,
  frequency: (_.get(f, 'frequency', []) || []).join(',')
});

export default memo(() => {
  const [ready, setReady] = useState<boolean>(false);
  const campaignId = useCampaignId();
  const { formatMessage } = useFmtMsgFori18n();
  const countryDataIntl = usePickerDataFromObj(countryData);
  const { type } = useContext(CampaignCtx);
  const campaignType = type === CampaignType.CAMPAIGN ? 'campaign' : 'crossCampaign';
  const { customParamResult } = useMainPage({
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultTableParam,
        paramFormatter: tableParamFormatter
      }
    ]
  });
  const [tableResult] = customParamResult;
  const { handleChangePagination, handleChangeCustomParam, param, fmtParam } = tableResult;

  const { reportDate, setReportDate } = useFetchReportDate({
    id: +campaignId,
    report: 'audiences'
  });
  const maxDate = dayjs(reportDate.maxDate).isBefore(dayjs(YESTERDAY))
    ? reportDate.maxDate
    : YESTERDAY;
  const minDate = dayjs(reportDate.start).isBefore(dayjs(TODAY)) ? reportDate.start : TODAY;
  const disabled = dayjs(TODAY).isSame(dayjs(minDate));

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  useEffect(() => {
    updateStatus({
      advanceFilter: [],
      objId: campaignId,
      hierarchy: campaignType,
      report: 'audiences'
    }).finally(() => {
      setReady(true);
    });
  }, []);

  return (
    <>
      <div className={basicClassName}>
        <div className="summary">
          {reportDate.end && (
            <Hit
              dateParams={reportDate}
              filterParams={defaultTableParam}
              id={campaignId}
              campaignType={campaignType}
            />
          )}
        </div>
        <Panel className="page-toolbar">
          <span style={{ paddingLeft: 12 }}>{reportDate.start}&nbsp;&nbsp;—</span>
          <DatePicker
            value={toDate(reportDate.end)}
            disabledDate={date => dayjs(date).isAfter(toDate(maxDate))}
            cleanable={false}
            appearance="subtle"
            disabled={disabled}
            onChange={value => {
              setReportDate({
                ...reportDate,
                end: YYYYMMDD(value)
              });
            }}
          />
          <SelectPicker
            prepend={formatMessage('i18n_china')}
            data={countryDataIntl}
            value={defaultTableParam.country}
            disabled
          />
        </Panel>
        <Panel className="panel-table">
          <Row>
            <Col xs={10}>
              <FigurePie
                dateParams={reportDate}
                filterParams={defaultTableParam}
                id={campaignId}
                campaignType={campaignType}
              />
            </Col>
            <Col xs={14}>
              <FigureBar
                dateParams={reportDate}
                filterParams={defaultTableParam}
                id={campaignId}
                campaignType={campaignType}
              />
            </Col>
          </Row>
          <Divider />
          <Stack
            dateParams={reportDate}
            filterParams={defaultTableParam}
            id={campaignId}
            type="impressions"
            title={formatMessage('i18n_impValue')}
            campaignType={campaignType}
          />
          <Divider />
          <Stack
            dateParams={reportDate}
            filterParams={defaultTableParam}
            id={campaignId}
            type="uimpressions"
            title={formatMessage('i18n_independentAudience')}
            campaignType={campaignType}
          />
          <Divider />
          <Bar
            dateParams={reportDate}
            filterParams={defaultTableParam}
            id={campaignId}
            campaignType={campaignType}
          />
          <Divider />
          <Line
            dateParams={reportDate}
            id={campaignId}
            campaignType={campaignType}
            filterParams={defaultTableParam}
          />
          <Divider />
          {reportDate.end && ready && (
            <Table
              dateParams={reportDate}
              filterParams={param}
              tableParams={fmtParam}
              postParams={{
                id: campaignId,
                hierarchy: campaignType,
                report: 'audiences'
              }}
              setTableParams={handleChangePagination}
              handleChangeCustomParam={handleChangeCustomParam}
              refetchTableData={refetchTableData}
              campaignId={campaignId}
              campaignType={campaignType}
            />
          )}
        </Panel>
      </div>
    </>
  );
});
