import React, { useCallback } from 'react';
import _ from 'lodash';
import { Popover, Tooltip, Whisper } from 'rsuite';
import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';

type Props = BaseCellProps<any> & {
  imgKey: string;
};

export default React.memo(({ rowData, dataKey, imgKey, ...props }: Props) => {
  const renderContent = useCallback(() => {
    const speaker = _.isEmpty(rowData[imgKey]) ? (
      <Tooltip>{rowData[dataKey]}</Tooltip>
    ) : (
      <Popover className="placement-preview-popover">
        <div className="preview-text">{rowData[dataKey]}</div>
        <img className="preview-img" src={rowData[imgKey]} alt={rowData[dataKey]} />
      </Popover>
    );
    return (
      <Whisper placement="autoVerticalStart" speaker={speaker}>
        <span>{rowData[dataKey]}</span>
      </Whisper>
    );
  }, [dataKey, imgKey, rowData]);

  const renderTitle = useCallback(() => null, []);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderContent={renderContent}
      renderTitle={renderTitle}
      {...props}
    />
  );
});
