import * as React from 'react';
import classnames from 'classnames';
import { CheckTreePicker } from 'rsuite';
import { CheckTreePickerProps } from 'rsuite/lib/CheckTreePicker';
import useFetchForPicker, {
  UseFetchProps
} from '@/components/Picker/PickerHooks/useFetchForPicker';
import useFormattedDataForPicker, {
  UseFormattedDataProps
} from '@/components/Picker/PickerHooks/useFormattedDataForPicker';
import useSearchForPicker, {
  UseSearchProps
} from '@/components/Picker/PickerHooks/useSearchForPicker';
import useAllForPicker, { UseAllProps } from '@/components/Picker/PickerHooks/useAllForPicker';
import useLoadingForPicker, {
  UseLoadingProps
} from '@/components/Picker/PickerHooks/useLoadingForPicker';
import usePrependForPicker, {
  UsePrependProps
} from '@/components/Picker/PickerHooks/usePrependForPicker';
import useFilterValueForPicker, {
  UseFilterValueProps
} from '@/components/Picker/PickerHooks/useFilterValueForPicker';
import useContainerForPicker, {
  UseContainerProps
} from '@/components/Picker/PickerHooks/useContainerForPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

import composer from './PickerHooks/composer';

export type CustomCheckTreePickerProps = Partial<CheckTreePickerProps> &
  UseFetchProps &
  UseFormattedDataProps &
  UseSearchProps &
  UseFilterValueProps &
  UseAllProps &
  UseLoadingProps &
  UsePrependProps &
  UseContainerProps;

const CustomCheckTreePicker = React.memo(
  React.forwardRef((props: CustomCheckTreePickerProps, ref?: React.RefObject<any>) => {
    const { formatMessage } = useFmtMsgFori18n();
    return (
      <CheckTreePicker
        {...composer<CustomCheckTreePickerProps, CheckTreePickerProps>(
          useFetchForPicker,
          useFormattedDataForPicker,
          useSearchForPicker,
          useFilterValueForPicker,
          useAllForPicker,
          useLoadingForPicker,
          usePrependForPicker,
          useContainerForPicker
        )({
          labelKey: 'name',
          valueKey: 'id',
          defaultExpandAll: true,
          isTree: true,
          isMulti: true,
          searchKeys: ['id', 'name'],
          placeholder: formatMessage('i18n_select', { name: '' }),
          placement: 'autoVerticalStart',
          ...props,
          className: classnames(props.className, 'cus-picker')
        })}
        ref={ref}
      />
    );
  })
);

CustomCheckTreePicker.displayName = 'CheckTreePicker';
export default CustomCheckTreePicker;
