import React, { useCallback, useContext, useMemo, useEffect } from 'react';
import _ from 'lodash';
import { Button, Checkbox } from 'rsuite';
import AdvBrandPicker from '@/components/Picker/logic/AdvBrandPicker';
import { CustomCheckTreePickerProps } from '@/components/Picker/CheckTreePicker';
import CheckPicker, { CustomCheckPickerProps } from '@/components/Picker/CheckPicker';
import { ErrorMessageWithAbsContainer } from '@/components/Form/ErrorMessage';
import { FormErrorContext } from '@/components/Form/FormContext';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { beRoleData, getShortNameById } from '@/constants/config';
import { API_MEDIA } from '@/constants/APIs';
import { advName, agName, pubName, defaultName, nameFlag } from './constants';

/**
 * 内部BE时：
 * 1、默认选择所有角色
 * 2、默认所有角色为全选
 * 3、全部禁用，不可更改
 */

/*
 * 校验时机：
 * 未提交前，任何改变不触发校验
 * 提交后，表单会进行整体校验
 * 之后仅在以有错误的情况下进行校验
 * * 修改列表触发该项校验
 * * 取消勾选清空该项错误
 * * 确认勾选清空defaultRole错误
 */

// 设置chosen而不是直接根据是否nil来判断是否勾选，是希望取消勾选后仍能在内部保存所选品牌/站点，
// 这样在重新勾选后能恢复之前的状态
type ItemType = {
  subList: any[];
  chosen: boolean;
};

interface Props {
  values?: Record<string, ItemType | number>;
  errors?: Record<string, any>;
  isInternal: boolean;
  disabled: boolean;
  onChange?: Function;
}

interface RenderItemProps {
  name: string; // 字段名
  label: string; // 显示标签
  pickerComponent?: React.ComponentType<any>; // Picker组件
  pickerProps?: CustomCheckTreePickerProps | CustomCheckPickerProps;
}

const siteParams = {
  orderColumn: 'name',
  orderType: 'asc',
  status: 1
};

export default React.memo(({ values, errors, isInternal, disabled, onChange }: Props) => {
  const { formatMessage } = useFmtMsgFori18n();
  const { formErrors, setFormErrors, checkForm } = useContext(FormErrorContext);

  const handleCheckRole = useCallback(
    (name: string) => (val: any, checked: boolean) => {
      const roleNames = [advName, agName, pubName];

      if (checked) {
        onChange(name)({
          subList: _.get(values, [name, 'subList'], []),
          chosen: true
        });

        // 如果是第一个勾选的角色，则将其设为缺省
        const isOnlyChosen = roleNames.every(i => !_.get(values, [i, 'chosen'], false));
        isOnlyChosen && onChange(defaultName)(nameFlag[name]);

        !_.isEmpty(formErrors[defaultName]) &&
          setFormErrors(prevErrors => ({ ...prevErrors, [defaultName]: undefined }));
      } else {
        // 如果取消勾选的是缺省角色，那么找到显示顺序第一的已勾选角色，设为缺省
        if (values[defaultName] === nameFlag[name]) {
          const firstChosenName = roleNames.find(
            i => i !== name && _.get(values, [i, 'chosen'], false)
          );

          onChange(defaultName)(_.isEmpty(firstChosenName) ? undefined : nameFlag[firstChosenName]);
        }

        onChange(name)({
          subList: _.get(values, [name, 'subList'], []),
          chosen: false
        });

        !_.isEmpty(formErrors[name]) &&
          setFormErrors(prevErrors => ({ ...prevErrors, [name]: undefined }));
      }
    },
    [onChange, values[advName], values[agName], values[pubName], values[defaultName], formErrors]
  );

  const handleChangePicker = useCallback(
    (name: string) => (val: any[]) => {
      const newValue = {
        subList: val,
        chosen: true
      };

      onChange(name)(newValue);
      !_.isEmpty(formErrors[name]) &&
        checkForm(name, newValue).then(msg => {
          setFormErrors(prevErrors => ({
            ...prevErrors,
            [name]: msg
          }));
        });
    },
    [onChange, formErrors]
  );

  const handleSetDefault = useCallback(
    (name: string) => () => {
      onChange(defaultName)(nameFlag[name]);
    },
    [onChange]
  );

  const renderItem = useCallback(
    ({ name, label, pickerComponent: Picker = AdvBrandPicker, pickerProps }: RenderItemProps) => {
      const isChosen = _.get(values, [name, 'chosen'], false);

      // 内部BE默认agency为默认角色
      const isDefault = isInternal ? name === agName : values[defaultName] === nameFlag[name];

      const pickerValue = _.get(values, [name, 'subList'], []);

      const roleName = _.get(beRoleData.find(r => r.id === nameFlag[name]), 'name', '');

      return (
        <div className="be-role-item">
          <Checkbox
            checked={isInternal || isChosen}
            onChange={handleCheckRole(name)}
            disabled={disabled || isInternal}
          >
            {`${_.capitalize(roleName)} (${getShortNameById(nameFlag[name])})`}
          </Checkbox>
          <Picker
            prepend={label}
            supportAll={isInternal}
            disabled={disabled || isInternal || !isChosen}
            value={isInternal || !isChosen ? [] : pickerValue}
            onChange={handleChangePicker(name)}
            menuStyle={{ maxWidth: '400px' }}
            {...pickerProps}
          />
          <Button
            appearance={isDefault ? 'ghost' : 'subtle'}
            onClick={handleSetDefault(name)}
            disabled={disabled || isInternal || !isChosen}
            className="set-default-btn"
          >
            {isDefault ? formatMessage('i18n_defaultRole') : formatMessage('i18n_setAsDefault')}
          </Button>
          <ErrorMessageWithAbsContainer error={_.get(errors, name)} />
        </div>
      );
    },
    [isInternal, disabled, handleCheckRole, handleChangePicker, handleSetDefault]
  );

  const AdvertiserRole = useMemo(
    () =>
      renderItem({
        name: advName,
        label: formatMessage('i18n_brand')
      }),
    [renderItem]
  );

  const AgencyRole = useMemo(
    () =>
      renderItem({
        name: agName,
        label: formatMessage('i18n_brand')
      }),
    [renderItem]
  );

  const PublisherRole = useMemo(
    () =>
      renderItem({
        name: pubName,
        label: formatMessage('i18n_site'),
        pickerComponent: CheckPicker,
        pickerProps: {
          url: API_MEDIA,
          params: siteParams
        }
      }),
    [renderItem]
  );

  return (
    <div className="be-role-setter">
      {AdvertiserRole}
      {AgencyRole}
      {PublisherRole}
      <ErrorMessageWithAbsContainer error={_.get(errors, defaultName)} />
    </div>
  );
});
