/* eslint-disable @typescript-eslint/camelcase */
import _ from 'lodash';
import { category, typeData } from '@/views/common/config/site/constants';

export const mapFromResponse = (rspData: Record<string, any>): Record<string, any> => {
  const { category: categoryVal, site_type, is_adnetwork, ...data } = rspData;
  return {
    ...data,
    categoryID: _.get(
      _.find(category, i => _.camelCase(i.name) === _.camelCase(categoryVal)),
      'id'
    ),
    type: _.get(_.find(typeData, i => i.name === site_type), 'id'),
    adnetwork: is_adnetwork
  };
};

export const mapToRequest = (formData: Record<string, any>): Record<string, any> => {
  return {
    ..._.mapValues(formData, val => (_.isNil(val) ? undefined : val))
  };
};
