export const ParaModel = Schema.Model({
  name: StringType()
    .rangeLength(
      1,
      200,
      formatMessage('i18n_stringRangeLength', {
        name: formatMessage('i18n_name'),
        minLength: 1,
        maxLength: 200
      })
    )
    .pattern(/^[a-zA-Z][a-zA-Z0-9_-]*$/, formatMessage('i18n_plsInputValidName'))
    .isRequired(formatMessage('i18n_plsEnter', { name: '' })),
  desc: getAllowStringType(formatMessage('i18n_description'), false),
  type: getEnumerateNumberType(formatMessage('i18n_type')),
  beId: getEnumerateNumberType(formatMessage('i18n_primaryOwner'), true)
});

export const AdserverModel = Schema.Model({
  name: getAllowStringType(formatMessage('i18n_name'), true),
  notes: getAllowStringType(formatMessage('i18n_remark'), false),
  ...adsConfigData.reduce((acc, { name, noPlatform }) => {
    return {
      ...acc,
      [name]: noPlatform
        ? StringType().addRule(
          value => _.isNil(value) || !_.isEmpty(_.trim(value)),
          formatMessage('i18n_required'),
          true
        )
        : ObjectType().addRule(
          value => _.isNil(value) || _.some(_.values(value), v => !_.isEmpty(_.trim(v))),
          formatMessage('i18n_required'),
          true
        )
    };
  }, {})
});

