export const CustomReportModel = Schema.Model({
  name: getAllowStringType(formatMessage('i18n_reportName'), true),
  desc: getAllowStringType(formatMessage('i18n_description'), false),
  campaigns: ArrayType().isRequired(formatMessage('i18n_selCampaignWarning')),
  secondary: StringType().addRule((value, data) => {
    const typeItem = typeData.find(i => i.id === data.type);
    return _.includes(typeItem.secondary, value);
  }, formatMessage('i18n_select', { name: '' })),
  metrics: ArrayType().isRequired(formatMessage('i18n_selOneMetric')),
  advancedFilter: ArrayType().addRule(
    value => {
      return (
        _.isEmpty(value) ||
        _.every(value, i => !_.isEmpty(i.key) && !_.isEmpty(i.operation) && !_.isEmpty(i.value))
      );
    },
    formatMessage('i18n_completeFilter'),
    true
  ),
  endTime: getDateType(formatMessage('i18n_finishDate'), false).addRule(
    (value, data) => {
      return data.freqType === 'once' || _.isDate(value);
    },
    formatMessage('i18n_finishDateRequired'),
    true
  ),
  receivers: getAllowStringType(formatMessage('i18n_getMail'), false).addRule(
    value => {
      if (!value) {
        return true;
      }
      const emails = value.split(',');
      return emails.every(email => EMAIL.test(email));
    },
    formatMessage('i18n_isEmail'),
    true
  )
});

export const CpoModel = Schema.Model({
  id: getEnumerateNumberType(''),
  dateLaunch: getDateRangeType('', true),
  dateOrder: getDateRangeType('', true).addRule(
    (value, data) => !dateIsAfter(data?.dateLaunch[1], data?.dateOrder[1]),
    formatMessage('i18n_cpoDateOrdered')
  ),
  costGroup: ArrayType()
    .isRequired(
      formatMessage('i18n_plsEnter', {
        name: ''
      })
    )
    .addRule(
      value => {
        if (!value) {
          return true;
        }
        return value.every((i: any) => GROUP_STRING.test(i.group));
      },
      formatMessage('i18n_formatError', {
        name: ''
      }),
      true
    )
    .addRule(
      value => {
        if (!value) {
          return true;
        }
        let num = _.uniq(value.map((i: any) => i.group));
        return num.length === value.map((i: any) => i.value).length;
      },
      formatMessage('i18n_nameCannotBeDuplicate'),
      true
    ),
  conversions: ArrayType()
    .minLength(1, formatMessage('i18n_selectOneRequired'))
    .isRequired(formatMessage('i18n_selectOneRequired')),
  cost: getEnumerateNumberType('', true).isRequired(
    formatMessage('i18n_plsEnter', {
      name: ''
    })
  ),
  auditEmail: getEmailType(formatMessage('i18n_email'), true, false).addRule(
    (value, data) => {
      return !data.isAudit || !_.isEmpty(value);
    },
    formatMessage('i18n_emailRequired'),
    true
  )
});

