import _ from 'lodash';
import React, { useRef, useCallback } from 'react';
import { Drawer as RSDrawer, Button, ButtonToolbar, Loader } from 'rsuite';
import classnames from 'classnames';
import prefix from '@/utils/prefix';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

const prefixClassName = prefix('basic-drawer');

const styles = {
  dataLoader: {
    zIndex: 10
  }
};

interface Props {
  submitLoading?: boolean;
  dataLoading?: boolean;
  show?: boolean;
  children?: React.ReactNode;
  body?: React.ElementType;
  title: React.ElementType | string;
  actions?: React.ElementType;
  size?: 'lg' | 'md' | 'sm' | 'xs';
  className?: string;
  dialogStyle?: BaseObject<string>;
  width?: string;
  onHide?: (event: React.MouseEvent) => void;
  onSubmit?: (event: React.MouseEvent) => void;
  onEnter?: () => void;
  backdrop?: boolean | 'static';
  btnMsg?: string;
  disabled?: boolean;
  full?: boolean;
}

export const DrawerContainerContext = React.createContext<() => HTMLDivElement>(null);

export default React.memo(function Drawer({
  show,
  children,
  dataLoading,
  body,
  title,
  size = 'md',
  className,
  dialogStyle,
  width,
  submitLoading,
  backdrop = 'static',
  actions,
  onHide,
  onSubmit,
  onEnter,
  btnMsg = '',
  disabled = false,
  ...props
}: Props) {
  const { formatMessage } = useFmtMsgFori18n();
  function handleClose(event: React.MouseEvent<HTMLInputElement>) {
    onHide && onHide(event);
  }

  function handleSubmit(event: React.MouseEvent) {
    onSubmit && onSubmit(event);
  }

  function handleEnter() {
    onEnter && onEnter();
  }

  const containerRef = useRef(null);

  const getContainer = useCallback(() => containerRef.current, [containerRef]);

  return (
    <RSDrawer
      {...props}
      size={size}
      show={show}
      className={classnames('basic-drawer', className, { 'loading-drawer': dataLoading })}
      onHide={handleClose}
      onEnter={handleEnter}
      dialogStyle={_.assign({ width }, dialogStyle)}
      backdrop={backdrop}
    >
      <div className={prefixClassName('header')} style={{ width }}>
        <button
          type="button"
          className={prefixClassName('close')}
          onClick={handleClose}
          aria-label="Close"
        >
          <span aria-hidden="true">×</span>
        </button>
        <h4 className={prefixClassName('title')}>{title}</h4>
        <ButtonToolbar className={prefixClassName('actions-bar')}>
          {_.isUndefined(actions) // 当 this.props.actions 为 null 时，则没有 Actions
            ? [
                <Button
                  key="submit"
                  loading={submitLoading}
                  disabled={disabled || dataLoading}
                  onClick={handleSubmit}
                  appearance="primary"
                >
                  {btnMsg || formatMessage('i18n_save')}
                </Button>
              ]
            : actions}
        </ButtonToolbar>
      </div>
      <div className={prefixClassName('body')} ref={containerRef}>
        {dataLoading && (
          <Loader
            style={styles.dataLoader}
            backdrop
            content={formatMessage('i18n_loading')}
            vertical
          />
        )}
        <DrawerContainerContext.Provider value={getContainer}>
          {body || children}
        </DrawerContainerContext.Provider>
      </div>
    </RSDrawer>
  );
});
