import React, { useState, useCallback, useEffect } from 'react';
import _ from 'lodash';
import PickerGroup from '@/components/Picker/PickerGroup';
import SelectPicker from '@/components/Picker/SelectPicker';
import { API_ADVERTISER } from '@/constants/APIs';
import { UseFetchProps } from '@/components/Picker/PickerHooks/useFetchForPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface Props {
  values?: Record<string, any>;
  errors?: Record<string, string>;
  disabled?: boolean;
  fetchOptions?: UseFetchProps;
  onChange?: Function;
}

const advParams = {
  status: 1,
  orderColumn: 'name',
  orderType: 'asc'
};
const advName = 'advertiserId';
const brandName = 'brandId';

export default React.memo(
  ({ values, errors, disabled = false, fetchOptions = {}, onChange }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();

    const [advBrandData, setAdvBrandData] = useState<Record<string, any>[]>([]);
    const [availableBrands, setAvailableBrands] = useState<Record<string, any>[]>([]);

    // 根据主picker的data以及value，确定副picker的data
    useEffect(() => {
      const advData = _.isArray(fetchOptions.data) ? fetchOptions.data : advBrandData;
      const advItem = _.find(advData, i => i.id === values[advName]);
      if (advItem) {
        setAvailableBrands(_.get(advItem, 'children', []));
      } else {
        setAvailableBrands([]);
      }
    }, [fetchOptions.data, advBrandData, values[advName]]);

    const handleFetchAdvSuccess = useCallback(
      data => {
        setAdvBrandData(data);
        _.isFunction(_.get(fetchOptions, 'onFetchSuccess')) && fetchOptions.onFetchSuccess(data);
      },
      [fetchOptions]
    );

    return (
      <PickerGroup values={values} errors={errors} onChange={onChange}>
        <SelectPicker
          name={advName}
          prepend={formatMessage('i18n_advertiser')}
          searchable
          disabled={disabled}
          url={API_ADVERTISER}
          params={advParams}
          onFetchSuccess={handleFetchAdvSuccess}
          shouldFilter
          {..._.omit(fetchOptions, 'onFetchSuccess')}
        />
        <SelectPicker
          name={brandName}
          prepend={formatMessage('i18n_brand')}
          data={availableBrands}
          searchable
          disabled={disabled || _.isNil(values[advName])}
        />
      </PickerGroup>
    );
  }
);
