import React, { memo, useReducer, useMemo, useCallback } from 'react';
import { Panel } from 'rsuite';

import { useMainPage } from '@/components/hooks/useMainPage';
import { TABLE } from '@/constants/config';

import Table, { extractDimensionId } from './Table';
import {
  defaultFilter,
  defaultReducerState,
  SamplingReducerType,
  SamplingReduerAction,
  SamplingState
} from './constants';
import { SampleCtx } from './context';

const reducer = (state: SamplingState, action: SamplingReduerAction) => {
  switch (action.type) {
    case SamplingReducerType.SET_POLLING_STATUS: {
      const { pollingStatus } = action;
      return {
        ...state,
        polling: pollingStatus
      };
    }

    case SamplingReducerType.SET_SAMPLE_DIMENSION_ID: {
      const { sampleDimensionId } = action;
      return {
        ...state,
        sampleDimensionId
      };
    }

    default:
      return state;
  }
};

const tableOptions: TABLE = {
  orderColumn: null,
  orderType: null,
  page: null,
  pageSize: null
};

const paramFormatter = (p: any) => ({
  ...p,
  dimensionId: extractDimensionId(p.dimensionId || '')
});

export default memo(() => {
  const { customParamResult } = useMainPage({
    tableOptions,
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultFilter,
        paramFormatter
      }
    ]
  });

  const [tableResult] = customParamResult;
  const { param, fmtParam, handleChangePagination, handleChangeCustomParam } = tableResult;

  const [state, dispatch] = useReducer(reducer, defaultReducerState);
  const uploadCtxMemoVal = useMemo(
    () => ({
      state,
      dispatch,
      handleChangeCustomParam
    }),
    [state, dispatch, handleChangeCustomParam]
  );

  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  return (
    <>
      <SampleCtx.Provider value={uploadCtxMemoVal}>
        <Table
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          handleChangeCustomParam={handleChangeCustomParam}
          refetchTableData={refetchTableData}
        />
      </SampleCtx.Provider>
    </>
  );
});
