import React, { useCallback, useState, useContext } from 'react';
import _ from 'lodash';
import { Form } from 'rsuite';
import { FileType } from 'rsuite/lib/Uploader';

import { API_ADUNIT_BATCHES } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import CustomField from '@/components/Form/CustomField';
import { SingleFileUploader } from '@/components/Form/SingleFileUploader';

import { UploadCtx } from './context';
import { UploadReducerType, uploadFilterParamKeys } from '../constants';

const fileKeyInForm = 'file';
export default () => {
  const { formatMessage } = useFmtMsgFori18n();
  const { dispatch, handleChangeCommonParam } = useContext(UploadCtx);
  const [formError, setFormError] = useState({
    [fileKeyInForm]: ''
  });
  const shouldUpload = useCallback(
    (file: FileType) => {
      const ext = _.last(file.name.split('.'));
      const result = _.lowerCase(ext) === 'xlsx';
      if (!result) {
        setFormError({
          [fileKeyInForm]: formatMessage('i18n_uploadFileTypeError')
        });
      }

      return Boolean(result);
    },
    [formatMessage]
  );

  const onSuccess = useCallback(
    res => {
      const taskId = _.get(res, 'taskId', '');
      handleChangeCommonParam({
        [uploadFilterParamKeys.TASK_ID]: taskId
      });
      dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_START });
      // 这里不取消loading的这个status，交给table的请求成功再取消，因为它会不断的请求，直到有数据返回
    },
    [dispatch, handleChangeCommonParam]
  );

  const onProgress = useCallback(() => {
    dispatch({ type: UploadReducerType.SET_UPLOADING_STATUS, isUploading: true });
  }, [dispatch]);

  const onRemove = useCallback(() => {
    dispatch({ type: UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_END });
  }, [dispatch]);

  const onError = useCallback(
    res => {
      const err = _.get(res, 'response.fields.error.0.message', '');
      if (err) {
        setFormError({
          [fileKeyInForm]: err
        });
      }

      dispatch({ type: UploadReducerType.SET_UPLOADING_STATUS, isUploading: false });
    },
    [dispatch]
  );

  const resetFormError = useCallback(() => {
    setFormError({
      [fileKeyInForm]: ''
    });
  }, []);

  const UploaderComp = useCallback(
    () => (
      <SingleFileUploader
        accept=".xlsx"
        autoUpload
        action={API_ADUNIT_BATCHES}
        shouldUpload={shouldUpload}
        onSuccess={onSuccess}
        onError={onError}
        onReupload={resetFormError}
        onProgress={onProgress}
        onRemove={onRemove}
        onChange={resetFormError}
      />
    ),
    [shouldUpload, onSuccess, onError, resetFormError, onProgress, onRemove]
  );

  return (
    <Form formError={formError}>
      <CustomField name="file" accepter={UploaderComp} />
    </Form>
  );
};
