import React, { useMemo, memo, useCallback, useRef, useState } from 'react';
import _ from 'lodash';
import { Nav, Panel, Uploader, Alert, Button } from 'rsuite';
import Drawer from '@/components/Drawer';
import { useMainPage } from '@/components/hooks/useMainPage';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import SelectPicker from '@/components/Picker/SelectPicker';
import { TextCell } from '@/components/Table/Cells';
import Table, { Column, Cell, HeaderCell, tableUtils } from '@/components/Table';
import {
  API_BE,
  API_CONVERSION_BATCH,
  API_CONVERSION_EXPORT,
  API_ADGROUP_CREATIVE_UPLOAD
} from '@/constants/APIs';
import { useFmtMsgFori18n, fmtHTMLMsgCompFori18n } from '@/utils/formatMessage';
import { getUserProfile } from '@/utils/location';

interface DrawerDisplayProps {
  show: boolean;
  loading: boolean;
  submitting: boolean;
}
interface DataProps {
  id: number;
  name: string;
  projectId: number | string;
  projectName: string;
  codeParam: string;
}

interface Props {
  drawer: DrawerDisplayProps;
  drawerRef: React.RefObject<any>;
  onShow: Function; // 显示
  onClose: Function; // 关闭
  setDrawer: Function;
}

export default memo(({ drawer, setDrawer, onClose }: Props) => {
  const { formatMessage } = useFmtMsgFori18n();
  const maxLine = 1;
  const src = '/resources/zh/import_activity_template.xlsx';
  const uploaderRef = useRef(null);
  const [fileName, setFileName] = useState<string>(null); // 上传文件名称
  const [info, setInfo] = useState<string>(null); // 错误信息
  const [fileList, setFileList] = useState([]); // 上传文件列表
  const [active, setActive] = useState<string>('local'); // tabs
  const tableRef = useRef(null);
  const { beId } = getUserProfile();
  const [value, setValue] = useState(beId);

  const handleChangeActive = useCallback((val: string) => {
    setActive(val);
  }, []);
  const handleSuccess = useCallback((rsp: any) => {
    setFileName(rsp.file_path);
  }, []);
  // 限制文件列表为1
  const handleChange = useCallback((val: any) => {
    setInfo(null);
    if (!_.isEmpty(val)) {
      setFileList([val[val.length - 1]]);
    } else {
      setFileList([]);
    }
  }, []);
  // 更改be
  const handleChangeBe = useCallback(val => {
    setValue(val);
  }, []);
  const handleRemove = useCallback(() => {
    setFileName(null);
  }, []);

  const { customParamResult } = useMainPage({
    defaultCommonParam: {},
    customConfig: [
      {
        isTable: true
      }
    ]
  });
  const [tableResult] = customParamResult;
  const { fmtParam, handleChangePagination, handleChangeCustomParam } = tableResult;
  const { tableData } = useTable({
    tableParams: fmtParam,
    setTableParams: handleChangePagination,
    api: API_CONVERSION_EXPORT,
    postParams: { type: 'HWA', beId: value }
  });
  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  const { onFetch: onCreateSubmit } = useFetch<CommonResponse>(
    FETCH_METHOD.POST,
    API_CONVERSION_BATCH,
    { disabledNotification: true }
  );

  const { onFetch: onHwaSubmit } = useFetch<CommonResponse>(
    FETCH_METHOD.POST,
    API_CONVERSION_EXPORT,
    { params: { type: 'HWA' }, disabledNotification: true }
  );

  const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
  const handleClose = useCallback(() => {
    setFileName(null);
    setInfo(null);
    setFileList([]);
    setActive('local');
    onClose();
  }, [onClose]);

  const handleSubmit = useCallback(() => {
    if (active === 'hwa') {
      const conversionIds = tableUtils(tableRef).getCheckValues();
      const list =
        tableDataMemo.data.filter((item: any) => conversionIds.indexOf(item.id) > -1) || [];
      if (_.isEmpty(list)) {
        Alert.error(formatMessage('i18n_select', { name: '' }) + formatMessage('i18n_projectId'));
      } else {
        onHwaSubmit({
          data: {
            data: list,
            src: 'HWA',
            beId: value
          }
        }).then(() => {
          Alert.success(formatMessage('i18n_importSuccess'));
          handleClose();
          refetchTableData();
        });
      }
    } else if (_.isEmpty(fileName)) {
      Alert.error(formatMessage('i18n_fileRequired'));
    } else {
      const params = { type: 'conversion', fileName, beId: value };
      onCreateSubmit({ data: params })
        .then(rsp => {
          // 取消提交加载状态
          setDrawer((prevState: any) => ({
            ...prevState,
            submitting: false
          }));
          // 批量错误显示为错误文件的下载链接
          if (_.get(rsp, 'data.info')) {
            return Promise.reject(_.get(rsp, 'data.info'));
          }
          const { errors } = rsp;
          const serverErrors = _.mapValues(errors, val => String(val));
          return _.isEmpty(serverErrors) ? Promise.resolve() : Promise.reject(serverErrors);
        })
        .then(rsp => {
          Alert.success(formatMessage('i18n_importSuccess'));
          handleClose();
        })
        .catch((err: any) => {
          if (_.isString(err)) {
            if (err.indexOf('/') > -1) {
              err = err.split('/');
              err = err[err.length - 1];
              err = `/api/v1/batchImportError?fileId=${err}`;
            }
            setInfo(err);
          } else {
            _.forIn(err, i => {
              Alert.error(i);
            });
          }
        });
    }
  }, [
    active,
    fileName,
    formatMessage,
    handleClose,
    onCreateSubmit,
    onHwaSubmit,
    setDrawer,
    tableDataMemo.data,
    value
  ]);

  return (
    <Drawer
      show={drawer.show}
      title={formatMessage('i18n_bulkImportActivity')}
      submitLoading={drawer.submitting}
      dataLoading={drawer.loading}
      onSubmit={handleSubmit}
      onHide={handleClose}
    >
      <Nav appearance="tabs" activeKey={active} onSelect={handleChangeActive}>
        <Nav.Item eventKey="local">{formatMessage('i18n_fromActivityTemplates')}</Nav.Item>
        <Nav.Item eventKey="hwa">{formatMessage('i18n_fromWebAnalytics')}</Nav.Item>
      </Nav>
      {active === 'local' && (
        <Panel>
          {fmtHTMLMsgCompFori18n('i18n_importTplDesc', {
            src,
            name: formatMessage('i18n_activityTpl')
          })}
          <div style={{ marginTop: 24 }}>
            <p>{formatMessage('i18n_belongBE')}</p>
            <SelectPicker
              searchable
              value={value}
              url={API_BE}
              params={{
                orderColumn: 'name',
                orderType: 'asc'
              }}
              style={{ width: '224px' }}
              onChange={handleChangeBe}
            />
          </div>
          <div style={{ margin: '24px 0 4px' }}>
            {formatMessage('i18n_batchUpload')}
            {formatMessage('i18n_activity')}
            <span style={{ color: 'red', position: 'relative', top: 3 }}> *</span>
          </div>

          <Uploader
            action={API_ADGROUP_CREATIVE_UPLOAD}
            accept=".xlsx"
            data={{
              type: 'conversion'
            }}
            fileList={fileList}
            onSuccess={handleSuccess}
            onRemove={handleRemove}
            onChange={handleChange}
            name="file"
            ref={uploaderRef}
          >
            <Button>{formatMessage('i18n_selFile')}</Button>
          </Uploader>

          {info && (
            <a
              style={{ marginTop: 24, color: '#f44336', display: 'block' }}
              href={info}
              target="blank"
            >
              {formatMessage('i18n_viewDetailsError')}
            </a>
          )}
        </Panel>
      )}
      {active === 'hwa' && (
        <>
          <SelectPicker
            searchable
            value={value}
            url={API_BE}
            prepend="BE"
            params={{
              orderColumn: 'name',
              orderType: 'asc'
            }}
            style={{ width: 224, margin: '24px 0 18px' }}
            onChange={handleChangeBe}
          />
          <Table
            {...tableDataMemo}
            checkDataKey="id"
            innerRef={tableRef}
            onChange={handleChangePagination}
          >
            <Column width={100} fixed sortable>
              <HeaderCell>{formatMessage('i18n_projectId')}</HeaderCell>
              <Cell dataKey="projectId" />
            </Column>
            {/* 项目名称 */}
            <Column flexGrow={1} minWidth={150} fixed sortable>
              <HeaderCell>{formatMessage('i18n_projectName')}</HeaderCell>
              <TextCell dataKey="projectName" maxLine={maxLine} />
            </Column>
            {/* 代码参数 */}
            <Column flexGrow={1} minWidth={150}>
              <HeaderCell>{formatMessage('i18n_codeParas')}</HeaderCell>
              <TextCell dataKey="codeParam" maxLine={maxLine} />
            </Column>
            {/* 事件名称 */}
            <Column flexGrow={1} minWidth={150}>
              <HeaderCell>{formatMessage('i18n_eventName')}</HeaderCell>
              <TextCell dataKey="name" maxLine={maxLine} />
            </Column>
          </Table>
        </>
      )}
    </Drawer>
  );
});
