import { GET } from '@/utils/request/fetch';
import * as APIs from '@/constants/APIs';

export function onFetchSummary(params: object, cb: (res: object) => void) {
  return GET(APIs.API_SUMMARY, {
    params: {
      ...params,
      hierarchy: 'campaign',
      report: 'campaign'
    }
  }).then(cb);
}

export function onFetchStatus(params: object, cb: (res: object) => void) {
  return GET(APIs.API_CAMPAIGN_STATUS, {
    params
  }).then(cb);
}

export function onFetchInfo(params: object, cb: (res: object) => void) {
  return GET(APIs.API_CAMPAIGN_GET, {
    params
  }).then(cb);
}

export function onFetchTop(params: object, cb: (res: object) => void) {
  return GET(APIs.API_DASHBOARD, {
    params: {
      ...params,
      hierarchy: 'campaign'
    }
  }).then(cb);
}

export function onFetchTable(params: object, cb: (res: object) => void) {
  return GET(APIs.API_BRAND_OVERVIEW, {
    params,
    var: 'json'
  }).then(cb);
}
