import React, { useCallback } from 'react';
import _ from 'lodash';

import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';

export default ({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const renderContent = useCallback(() => {
    const files = _.get(rowData, dataKey, []);
    return (
      <>
        {_.map(files, (fileEntity: any, index: number) => {
          const { filename, path } = fileEntity;
          return (
            <React.Fragment key={index}>
              <a href={path} target="_blank" rel="noopener noreferrer">
                {filename}
                {index < files.length - 1 ? ',' : null}
              </a>{' '}
            </React.Fragment>
          );
        })}
      </>
    );
  }, [rowData, dataKey]);

  return <Cell {...props} dataKey={dataKey} renderContent={renderContent} />;
};
