import React, { useCallback, useContext } from 'react';
import { Icon } from 'rsuite';

import { useFmtMsgFori18n } from '@/utils/formatMessage';

import { FilterRow } from './FilterRow';
import { FilterInnerProps } from '../constants';
import { FilterExtCtx } from '../context';
import { genNewFilterNode } from '../utils';

/**
 * 统一存储数据 存储结构如`mock.ts`所示
 *  提供增删查 把一行作为一个操作的对象，每个都是一行，每一行有一个id，
 *  改数据的方法作为参数传给下一级
 */
export const FilterPool = ({
  limitNum,
  filterData,
  filterVal,
  setFilterVal,
  onSubmit
}: FilterInnerProps) => {
  const { disabled } = useContext(FilterExtCtx);
  const { formatMessage } = useFmtMsgFori18n();
  const handleFilterAdd = useCallback(() => {
    const numOfNode = filterVal.length;
    if (numOfNode >= limitNum) {
      // 最多${limitNum}个
      return;
    }

    setFilterVal(v => v.concat(genNewFilterNode()));
  }, [filterVal, limitNum, setFilterVal]);

  const handleFilterDel = useCallback(
    (targetId: string) => {
      if (filterVal.length <= 1) {
        // 最少1个
        setFilterVal(genNewFilterNode());
      }

      setFilterVal(f => f.filter(v => v.id !== targetId));
    },
    [filterVal, setFilterVal]
  );

  const handleClearSelected = useCallback(() => {
    onSubmit && onSubmit([]);
  }, [onSubmit]);

  const handleFilterUpdate = useCallback(
    (id, newFilterItemVal) => {
      setFilterVal(prevFilter => prevFilter.map(f => (f.id === id ? newFilterItemVal : f)));
    },
    [setFilterVal]
  );

  return (
    <div className="page-filter">
      {filterVal.map((f, idx) => {
        return (
          <FilterRow
            key={f.id}
            index={idx}
            id={f.id}
            onDelete={handleFilterDel}
            onUpdate={handleFilterUpdate}
            filterData={filterData}
            filterValue={filterVal[idx]}
          />
        );
      })}
      <div className="page-filter-tips">
        {/*eslint-disable */}
        <span onClick={disabled ? undefined : handleFilterAdd}>
          <Icon icon="plus" />
        </span>
        {limitNum !== Infinity && formatMessage('i18n_filtersAdded', { 0: limitNum })}
        <a className="page-filter-clear" onClick={disabled ? undefined : handleClearSelected}>
          {formatMessage('i18n_clearSelected')}
        </a>
        {/* eslint-enable */}
      </div>
    </div>
  );
};

FilterPool.displayName = 'FilterPool';
