import React from 'react';
import _ from 'lodash';
import { Icon, Whisper, Popover } from 'rsuite';

import { Cell } from '@/components/Table';

export default ({ rowData, dataKey, ...rest }: any) => {
  const errorInfo = _.get(rowData, dataKey, []);
  const icon = _.isEmpty(errorInfo) ? (
    <Icon icon="ok-circle" style={{ color: '#34c3ff' }} /> // FixMe: 这里如果直接使用封装好的SuccessIcon和WarningIcon就无法Popover
  ) : (
    <Icon icon="exclamation-circle2" style={{ color: '#F44336' }} />
  );

  const result = _.isEmpty(errorInfo) ? (
    icon
  ) : (
    <Whisper
      placement="top"
      trigger="hover"
      speaker={
        <Popover>
          {errorInfo.map((error: any, idx: number) => (
            <div key={idx as any}>{error}</div>
          ))}
        </Popover>
      }
    >
      {icon}
    </Whisper>
  );

  return (
    <Cell className="update-status-cell" dataKey={dataKey} {...rest}>
      {result}
    </Cell>
  );
};
