import React, { useMemo, useCallback, useState } from 'react';
import _ from 'lodash';
import { Divider } from 'rsuite';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import MultiField from '@/components/Form/MultiField';
import SelectPicker from '@/components/Picker/SelectPicker';
import DatePicker from '@/components/Picker/DatePicker';
import RadioGroup from '@/components/Form/RadioGroup';
import CheckboxGroup from '@/components/Form/CheckboxGroup';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import { TOMORROW } from '@/constants/config';
import { CustomReportModel } from '@/constants/form/models';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import CampaignPicker from './part/CampaignPicker';
import FrequencyPicker from './part/FrequencyPicker';
import CustomAdvancedFilter from './part/CustomAdvancedFilter';
import {
  typeData,
  dateTypeData,
  otherData,
  dateRangeData,
  countryData,
  metricsData,
  placementMetricsData
} from './constants';

export default React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();
    const typeDataIntl = usePickerDataFromObj(typeData, {
      skipNoi18nPrefix: true
    });
    const dateTypeDataIntl = usePickerDataFromObj(dateTypeData);
    const otherDataIntl = usePickerDataFromObj(otherData);
    const dateRangeDataIntl = usePickerDataFromObj(dateRangeData);
    const countryDataIntl = usePickerDataFromObj(countryData);
    const metricsDataIntl = usePickerDataFromObj(metricsData);
    const placementMetricsDataIntl = usePickerDataFromObj(placementMetricsData);

    const typeItem = useMemo(() => typeData.find(i => i.id === formData.type), [formData.type]);
    const disableChange = drawer.isEdit && formData.status !== 1;

    const [campDrawerShow, setCampDrawerShow] = useState(false);
    const handleOpenCampDrawer = useCallback(() => {
      setCampDrawerShow(true);
    }, []);
    const handleCloseCampDrawer = useCallback(() => {
      setCampDrawerShow(false);
    }, []);

    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editCustomReport' : 'i18n_newCustomReport')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        className="custom-report-drawer"
        full={campDrawerShow}
        disabled={disableChange}
        onSubmit={onSubmit}
        onHide={onClose}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={CustomReportModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 基本信息 */}
          <div className="form-title">{formatMessage('i18n_basicInfo')}</div>

          {/* 报表名称 */}
          <CustomField name="name" label={formatMessage('i18n_reportName')} required />

          {/* 描述 */}
          <CustomField name="desc" label={formatMessage('i18n_description')} />

          {/* 类型 */}
          <CustomField
            name="type"
            label={formatMessage('i18n_type')}
            accepter={SelectPicker}
            data={typeDataIntl}
            disabled={drawer.isEdit}
          />

          <Divider />
          {/* 推广计划筛选 */}
          <div className="form-title">
            {formatMessage('i18n_campaignFilter')} <span style={{ color: 'red' }}>*</span>
          </div>

          <CustomField
            name="campaigns"
            accepter={CampaignPicker}
            disabled={disableChange}
            drawerShow={campDrawerShow}
            onOpenDrawer={handleOpenCampDrawer}
            onCloseDrawer={handleCloseCampDrawer}
          />

          <Divider />
          {/* 细分维度 */}
          <div className="form-title">{formatMessage('i18n_dimensions')}</div>

          {/* 时间 */}
          <CustomField
            name="secondary"
            label={formatMessage('i18n_time')}
            accepter={RadioGroup}
            data={dateTypeDataIntl.filter(i => _.includes(_.get(typeItem, 'secondary', []), i.id))}
            className="form-radio-group"
            itemWidth="calc(25% - 10px)"
          />

          {/* 其他维度 */}
          <CustomField
            name="segmentations"
            label={formatMessage('i18n_otherSeg')}
            accepter={CheckboxGroup}
            data={otherDataIntl.filter(i => _.includes(_.get(typeItem, 'segmentations', []), i.id))}
            className="form-checkbox-group"
            itemWidth="25%"
          />

          <Divider />
          {/* 过滤条件 */}
          <div className="form-title">{formatMessage('i18n_filters')}</div>

          {/* 时间范围 */}
          <CustomField
            name="timeRange"
            label={formatMessage('i18n_dateRange')}
            accepter={SelectPicker}
            data={dateRangeDataIntl}
          />

          {/* 自定义日期选择 */}
          {_.get(formData, 'timeRange') === dateRangeData[5].id && (
            <CustomField
              name="dateRange"
              accepter={DateRangePicker}
              cleanable={false}
              placement="autoVerticalStart"
            />
          )}

          {/* 国外 */}
          {((formData.type === 2 && !_.includes(formData.segmentations, 'city')) ||
            formData.type === 1) && (
            <CustomField
              name="country"
              label={formatMessage('i18n_abroad')}
              accepter={RadioGroup}
              data={countryDataIntl}
              className="form-radio-group"
              itemWidth="calc(25% - 10px)"
            />
          )}

          {_.size(formData.campaigns) > 0 && (
            <CustomField
              name="advancedFilter"
              accepter={CustomAdvancedFilter}
              type={formData.type}
              segmentations={formData.segmentations}
              campaigns={formData.campaigns}
              checkTrigger="none"
              className="advanced-filter"
            />
          )}

          <Divider />
          {/* 指标 */}
          <div className="form-title">
            {formatMessage('i18n_metrics')} <span style={{ color: 'red' }}>*</span>
          </div>

          <CustomField
            name="metrics"
            accepter={CheckboxGroup}
            data={[
              ...metricsDataIntl.filter(i => _.includes(_.get(typeItem, 'metrics', []), i.id)),
              ...(_.includes(formData.segmentations, 'placement') ? placementMetricsDataIntl : [])
            ]}
            className="form-checkbox-group"
            itemWidth="25%"
          />

          <Divider />
          {/* 定时设置 */}
          <div className="form-title">{formatMessage('i18n_schedule')}</div>

          {/* 频次 */}
          <MultiField
            names={['freqType', 'freqVal']}
            label={formatMessage('i18n_frequency')}
            disabled={_.get(formData, 'timeRange') === dateRangeData[5].id || drawer.isEdit}
            accepter={FrequencyPicker}
            className="frequency-picker-wrap"
          />

          {/* 结束 */}
          {_.get(formData, 'freqType') !== 'once' && (
            <CustomField
              name="endTime"
              label={formatMessage('i18n_end')}
              accepter={DatePicker}
              minDate={TOMORROW}
              required
              cleanable={false}
              placement="autoVerticalStart"
              ranges={[]}
            />
          )}

          {/* 接收邮箱 */}
          <CustomField
            name="receivers"
            placeholder={formatMessage('i18n_mailPlaceHolder')}
            label={formatMessage('i18n_getMail')}
          />
        </Form>
      </Drawer>
    );
  }
);
