import React, { useState, useCallback, useEffect } from 'react';
import _ from 'lodash';
import { IconButton, Icon, Whisper, Tooltip } from 'rsuite';
import { getShortNameById } from '@/constants/config';
import BERoleGroupPicker from './BERoleGroupPicker';
import { UseFetchProps } from '@/components/Picker/PickerHooks/useFetchForPicker';

type ItemType = { beId: number; roleId: number };

interface Props {
  value?: ItemType[];
  fetchOptions?: UseFetchProps;
  disabled?: boolean;
  addDisabled?: boolean;
  removeDisabled?: boolean;
  pickerHidden?: boolean;
  listHidden?: boolean;
  loading?: boolean;
  onChange?: (val: ItemType[]) => void;
}

const beName = 'be';
const roleName = 'role';

export default React.memo(
  ({
    value = [],
    fetchOptions = {},
    disabled = false,
    addDisabled = false,
    removeDisabled = false,
    pickerHidden = false,
    listHidden = false,
    loading = false,
    onChange,
    ...props
  }: Props) => {
    const [candidate, setCandidate] = useState({
      [beName]: null,
      [roleName]: null
    });

    const handleChangeCandidate = useCallback(
      key => (val: any) => {
        setCandidate(prevState => ({
          ...prevState,
          [key]: val
        }));
      },
      []
    );

    const handleAddItem = useCallback(() => {
      if (addDisabled || _.isNil(candidate[beName]) || _.isNil(candidate[roleName])) {
        return;
      }

      onChange([{ beId: candidate[beName], roleId: candidate[roleName] }, ...value]);

      setCandidate({
        [beName]: null,
        [roleName]: null
      });
    }, [addDisabled, candidate, onChange, value]);

    const handleRemoveItem = useCallback(
      (beId, roleId) => () => {
        if (removeDisabled) {
          return;
        }

        const newValue = value.filter(i => i.beId !== beId || i.roleId !== roleId);
        onChange(newValue);
      },
      [onChange, removeDisabled, value]
    );

    const [beData, setBEData] = useState([]);

    const onFetchBESuccess = useCallback(
      data => {
        setBEData(data);
        _.isFunction(_.get(fetchOptions, 'onFetchSuccess')) && fetchOptions.onFetchSuccess(data);
      },
      [fetchOptions]
    );

    const renderItem = useCallback(
      ({ beId, roleId }: any) => {
        const data = _.isArray(fetchOptions.data) ? fetchOptions.data : beData;
        const beItem = data.find(i => i.id === beId);
        const be = _.isNumber(beId) ? _.get(beItem, 'name', '') : beId;
        const role = getShortNameById(roleId);
        const itemName = `${role}: ${be}`;
        return beItem ? (
          <li key={beId + itemName}>
            <Whisper
              placement="autoVerticalStart"
              trigger="hover"
              speaker={<Tooltip>{itemName}</Tooltip>}
            >
              <div className="item-name">{itemName}</div>
            </Whisper>
            {removeDisabled || (
              <Icon icon="close" className="clickable" onClick={handleRemoveItem(beId, roleId)} />
            )}
          </li>
        ) : (
          false
        );
      },
      [fetchOptions.data, beData, handleRemoveItem, removeDisabled]
    );

    return (
      <div className="be-role-multi-picker">
        {pickerHidden || (
          <BERoleGroupPicker
            values={candidate}
            beName={beName}
            roleName={roleName}
            fetchOptions={{
              ...fetchOptions,
              onFetchSuccess: onFetchBESuccess
            }}
            disabled={disabled}
            cleanable
            bePickerProps={{
              disabledItemValues: _.map(value, 'beId'),
              loading
            }}
            onChange={handleChangeCandidate}
          />
        )}
        {pickerHidden || (
          <IconButton
            icon={<Icon icon="plus" />}
            onClick={handleAddItem}
            disabled={
              disabled || addDisabled || _.isNil(candidate[beName]) || _.isNil(candidate[roleName])
            }
          />
        )}
        {listHidden || <ul>{value.map(renderItem)}</ul>}
      </div>
    );
  }
);
