import React from 'react';

import {
  FilterInput,
  FilterSelect,
  FilterCommonProps,
  FilterCheck,
  FilterCascader
} from './ValueCandidate';
import {
  FILTER_TYPE,
  defaultChildrenKey,
  defaultLabelKey,
  defaultValueKey
} from '../../../constants';

interface ValueColumnProps extends Required<FilterCommonProps> {
  type: FILTER_TYPE;
}

export default ({
  type,
  labelKey = defaultLabelKey,
  valueKey = defaultValueKey,
  childrenKey = defaultChildrenKey,
  mapLabel = null,
  ...rest
}: ValueColumnProps) => {
  switch (type) {
    case FILTER_TYPE.INPUT: {
      return <FilterInput {...rest} />;
    }

    case FILTER_TYPE.SELECT: {
      return <FilterSelect labelKey={labelKey} valueKey={valueKey} mapLabel={mapLabel} {...rest} />;
    }

    case FILTER_TYPE.CHECK: {
      return <FilterCheck labelKey={labelKey} valueKey={valueKey} mapLabel={mapLabel} {...rest} />;
    }

    case FILTER_TYPE.CASCADER: {
      return (
        <FilterCascader
          labelKey={labelKey}
          valueKey={valueKey}
          childrenKey={childrenKey}
          mapLabel={mapLabel}
          {...rest}
        />
      );
    }

    default:
      return null;
  }
};
