import React, { useState, useCallback } from 'react';
import _ from 'lodash';
import { IconButton, Icon, Whisper, Tooltip } from 'rsuite';
import CpoGroupPicker from './CpoGroupPicker';
import { UseFetchProps } from '@/components/Picker/PickerHooks/useFetchForPicker';

type ItemType = { group: string; placement: number };

interface Props {
  value?: ItemType[];
  fetchOptions?: UseFetchProps;
  disabled?: boolean;
  addDisabled?: boolean;
  removeDisabled?: boolean;
  pickerHidden?: boolean;
  listHidden?: boolean;
  loading?: boolean;
  onChange?: (val: ItemType[]) => void;
  reqParams?: object;
}

const groupName = 'group';
const placementName = 'placement';

export default React.memo(
  ({
    value = [],
    fetchOptions = {},
    disabled = false,
    addDisabled = false,
    removeDisabled = false,
    pickerHidden = false,
    listHidden = false,
    loading = false,
    reqParams,
    onChange
  }: Props) => {
    const [candidate, setCandidate] = useState({
      [groupName]: null,
      [placementName]: null
    });

    const handleChangeCandidate = useCallback(
      key => (val: any) => {
        setCandidate(prevState => ({
          ...prevState,
          [key]: val
        }));
      },
      []
    );

    const handleAddItem = useCallback(() => {
      if (addDisabled || _.isNil(candidate[groupName]) || _.isNil(candidate[placementName])) {
        return;
      }

      onChange([{ group: candidate[groupName], placement: candidate[placementName] }, ...value]);

      setCandidate({
        [groupName]: '',
        [placementName]: null
      });
    }, [addDisabled, candidate, onChange, value]);

    const handleRemoveItem = useCallback(
      (group, placement) => () => {
        if (removeDisabled) {
          return;
        }

        const newValue = value.filter(i => i.group !== group || i.placement !== placement);
        onChange(newValue);
      },
      [onChange, removeDisabled, value]
    );

    const [placementData, setPlacementData] = useState([]);

    const onFetchBESuccess = useCallback(
      data => {
        setPlacementData(data);
      },
      [fetchOptions]
    );

    const renderItem = useCallback(
      ({ group, placement }: any) => {
        const names = placementData.filter(i => placement.indexOf(i.id) > -1).map(i => i.name);
        const itemName = `${group}: ${names}`;
        return (
          <li key={itemName}>
            <Whisper
              placement="autoVerticalStart"
              trigger="hover"
              speaker={<Tooltip>{itemName}</Tooltip>}
            >
              <div className="item-name">{itemName}</div>
            </Whisper>
            {removeDisabled || (
              <Icon
                icon="close"
                className="clickable"
                onClick={handleRemoveItem(group, placement)}
              />
            )}
          </li>
        );
      },
      [placementData, handleRemoveItem, removeDisabled]
    );
    return (
      <div className="be-role-multi-picker cpo-group-multi-picker">
        {pickerHidden || (
          <CpoGroupPicker
            values={candidate}
            groupName={groupName}
            placementName={placementName}
            fetchOptions={{
              ...fetchOptions,
              onFetchSuccess: onFetchBESuccess
            }}
            disabled={disabled}
            cleanable
            reqParams={reqParams}
            onChange={handleChangeCandidate}
          />
        )}
        {pickerHidden || (
          <IconButton
            icon={<Icon icon="plus" />}
            onClick={handleAddItem}
            disabled={
              disabled ||
              addDisabled ||
              _.isNil(candidate[groupName]) ||
              _.isNil(candidate[placementName]) ||
              _.isEmpty(candidate[placementName])
            }
          />
        )}
        {listHidden || <ul>{value.map(renderItem)}</ul>}
      </div>
    );
  }
);
