import * as React from 'react';
import _ from 'lodash';
import { InputGroup } from 'rsuite';
import SelectPicker from './SelectPicker';
import ErrorMessage from '@/components/Form/ErrorMessage';

/**
 * 组合两个SelectPicker，用于选择范围
 */

interface Props {
  values?: Record<string, any>;
  errors?: Record<string, string>;
  startData: any[];
  endData: any[];
  startName?: string;
  endName?: string;
  compareKey?: string; // data中用于比较大小的键名
  onChange?: Function;
  startPickerProps?: Record<string, any>;
  endPickerProps?: Record<string, any>;
}

const { useState, useEffect } = React;

const RangePicker = React.memo(
  ({
    values = {},
    errors,
    startData = [],
    endData = [],
    startName = 'start',
    endName = 'end',
    compareKey = 'compare',
    onChange,
    startPickerProps = {},
    endPickerProps = {},
    ...props
  }: Props) => {
    const [availableEndData, setAvailableEndData] = useState(endData);

    // 根据所选起始值，改变可选的终止值
    useEffect(() => {
      if (_.isNil(values[startName])) {
        return;
      }
      const valueKey = _.get(startPickerProps, 'valueKey', (props as any).valueKey) || 'id';
      const startItem = startData.find(i => i[valueKey] === values[startName]);
      setAvailableEndData(endData.filter(i => i[compareKey] > startItem[compareKey]));
    }, [values[startName]]);

    return (
      <div className="range-picker">
        <InputGroup>
          <SelectPicker
            value={values[startName]}
            data={startData}
            onChange={onChange(startName)}
            {...props}
            {...startPickerProps}
          />
          <InputGroup.Addon>~</InputGroup.Addon>
          <SelectPicker
            value={values[endName]}
            data={availableEndData}
            onChange={onChange(endName)}
            shouldFilter
            {...props}
            {...endPickerProps}
          />
        </InputGroup>
        <ErrorMessage
          error={_.values(errors)
            .filter(i => !_.isEmpty(i))
            .join('; ')}
        />
      </div>
    );
  }
);

RangePicker.displayName = 'RangePicker';
export default RangePicker;
