import React, { useCallback } from 'react';
import _ from 'lodash';
import { Tag } from 'rsuite';

import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

import { CONF_LOG_TYPE } from '../constants';

export default ({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const { formatMessage } = useFmtMsgFori18n();
  const logType = _.find(CONF_LOG_TYPE, _.matchesProperty('id', rowData.type));
  const tagName = _.get(logType, 'name');

  const TagEle = useCallback(() => {
    const i18nTag = tagName ? `i18n_${tagName}` : '';
    return (
      <span className="log-tag-wrap">
        <Tag className={rowData.type}>{formatMessage(i18nTag, {}, true)}</Tag>{' '}
      </span>
    );
  }, [rowData.type, tagName, formatMessage]);

  const renderContent = useCallback(() => {
    return <span>{rowData.op}</span>;
  }, [rowData.op]);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderEleBeforeContent={TagEle}
      renderContent={renderContent}
      renderTitle={renderContent}
      {...props}
    />
  );
};
