import React from 'react';
import _ from 'lodash';
import CheckTreePicker, { CustomCheckTreePickerProps } from '@/components/Picker/CheckTreePicker';
import { API_CONVERSIONGROUP } from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface ReqParamProps {
  beId?: Array<number>;
  campaignId?: number | string;
  sub?: number;
}
interface ActionProps<T = any> {
  chosenAction: ReadonlyArray<T>;
  reqParam?: ReqParamProps;
}

const pickerDefaultParam = {
  supportAll: true,
  style: { maxWidth: '300px' },
  menuStyle: { maxWidth: '400px' },
  height: 250,
  cleanable: true,
  searchable: true
};

export default ({ chosenAction, reqParam, ...rest }: ActionProps & CustomCheckTreePickerProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  const beId = reqParam.beId ? reqParam.beId.toString() : null;
  return (
    <CheckTreePicker
      value={chosenAction}
      url={API_CONVERSIONGROUP}
      params={{
        ...reqParam,
        sub: 1,
        beId
      }}
      childrenKey="sub"
      prepend={formatMessage('i18n_conversionActivities')}
      {...pickerDefaultParam}
      {...rest}
    />
  );
};
