import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import AttributePicker from '@/components/Picker/logic/AttributePicker';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import BEPicker from '@/components/Picker/logic/BEPicker';
import AdvBrandPicker from '@/components/Picker/logic/AdvBrandPicker';
import ActionPicker from '@/components/Picker/logic/ActionPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { API_HWA } from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';

import { filterParamKeys, FilterParam } from './constants';

// const picker = ['dateRangePicker', 'be', 'advBrand', 'action'];

export enum pickerName {
  DATE = 'dateRangePicker',
  BE = 'be',
  ADVBRAND = 'advBrand',
  ACTION = 'action',
  PROJECT = 'project',
  PARA = 'para'
}

type Props = BasicReportProps<FilterParam> & {
  show: Array<pickerName>;
  handleChangeDate: (newDateRange: Array<Date>) => void;
  handleChangeProjects?: Function;
};
export default memo(
  ({
    show,
    dateParams,
    filterParams,
    handleChangeDate,
    handleChangeCommonParam,
    handleChangeProjects
  }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();

    const handleSelectProject = useCallback(
      (value, item) => {
        if (_.includes(value, item.id) || _.isEmpty(value)) {
          handleChangeProjects(item, value);
        }
      },
      [handleChangeProjects]
    );

    const handleSelectPara = useCallback(
      (value, item) => {
        handleChangeCommonParam({
          [filterParamKeys.PARA]: `${value},${item.beId}`
        });
      },
      [handleChangeCommonParam]
    );

    const fmtBeId = useMemo(() => {
      return {
        beId: filterParams[filterParamKeys.BE] ? filterParams[filterParamKeys.BE].toString() : null
      };
    }, [filterParams]);

    const fmtPara = useMemo(() => {
      return _.toNumber(
        filterParams[filterParamKeys.PARA] && filterParams[filterParamKeys.PARA].indexOf(',') > -1
          ? filterParams[filterParamKeys.PARA].split(',')[0]
          : filterParams[filterParamKeys.PARA]
      );
    }, [filterParams]);
    return (
      <>
        <DateRangePicker
          value={[dateParams.start, dateParams.end]}
          maxDate={dateParams.maxDate}
          cleanable={false}
          appearance="subtle"
          onChange={handleChangeDate}
        />
        {_.includes(show, pickerName.BE) && (
          <BEPicker
            chosenBE={filterParams[filterParamKeys.BE]}
            beChangeHandler={(value: Array<any>) =>
              handleChangeCommonParam({
                [filterParamKeys.BE]: value,
                [filterParamKeys.ADVERTISER]: [],
                [filterParamKeys.ACTION]: [],
                [filterParamKeys.PROJECT]: [],
                [filterParamKeys.PARA]: ''
              })
            }
            appearance="subtle"
          />
        )}
        {_.includes(show, pickerName.ADVBRAND) && (
          <AdvBrandPicker
            key="advBrand"
            className="cus-picker"
            chosenAdvBrand={filterParams[filterParamKeys.ADVERTISER]}
            onChange={(value: Array<any>) =>
              handleChangeCommonParam({ [filterParamKeys.ADVERTISER]: value })
            }
            reqParam={fmtBeId}
            deps={[filterParams[filterParamKeys.BE]]}
            appearance="subtle"
          />
        )}
        {_.includes(show, pickerName.ACTION) && (
          <ActionPicker
            chosenAction={filterParams[filterParamKeys.ACTION]}
            onChange={(value: Array<any>) =>
              handleChangeCommonParam({ [filterParamKeys.ACTION]: value })
            }
            appearance="subtle"
            reqParam={fmtBeId}
            deps={[filterParams[filterParamKeys.BE]]}
          />
        )}
        {_.includes(show, pickerName.PROJECT) && (
          <CheckPicker
            onChange={(value: Array<string>) =>
              handleChangeCommonParam({ [filterParamKeys.PROJECT]: value })
            }
            onSelect={handleSelectProject}
            onClean={() => handleChangeProjects()}
            appearance="subtle"
            prepend={formatMessage('i18n_selectProjects')}
            placeholder={formatMessage('i18n_select', { name: '' })}
            url={API_HWA}
            value={filterParams[filterParamKeys.PROJECT]}
            dataPath={['data']}
            params={fmtBeId}
            deps={[filterParams[filterParamKeys.BE]]}
            renderMenuItem={(label, item) => {
              const name = _.get(item, 'label') + '（' + _.get(item, 'value') + '）';
              return <span title={name}>{name}</span>;
            }}
          />
        )}
        {_.includes(show, pickerName.PARA) && (
          <AttributePicker
            key="para"
            chosenValue={fmtPara}
            // onChange={(value: string) => handleChangeCommonParam({ [filterParamKeys.PARA]: value })}
            onSelect={handleSelectPara}
            onClean={() => handleChangeCommonParam({ [filterParamKeys.PARA]: null })}
            groupBy="beName"
            searchable
            reqParam={fmtBeId}
            deps={[filterParams[filterParamKeys.BE]]}
            renderMenuGroup={label => {
              return (
                <p className="para-menu-group" title={label as string}>
                  {label}
                </p>
              );
            }}
          />
        )}
      </>
    );
  }
);
