import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';

import * as APIs from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell, DateCell, TextCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { secondaryData, BasicReportProps } from '@/constants/config';

export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    handleSearchWord,
    setTableParams
  }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();

    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: APIs.API_TAB_REPORT,
      postParams
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleExportClicked = useCallback(() => {
      onExport && onExport(postParams, APIs.API_XLSX);
    }, [onExport, postParams]);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };

    // 渲染表列
    const renderCol = () => {
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams.secondary)[0],
        'label'
      );
      return [
        <Column width={130} sortable fixed resizable key="id">
          <HeaderCell>{`${formatMessage('i18n_placement')}ID`}</HeaderCell>
          <Cell dataKey="id" />
        </Column>,
        <Column width={180} sortable fixed resizable key="placement">
          <HeaderCell>{formatMessage('i18n_placement')}</HeaderCell>
          <TextCell dataKey="name" maxLine={1} />
        </Column>,
        <Column width={150} sortable fixed key="siteid">
          <HeaderCell>{`${formatMessage('i18n_site')}ID`}</HeaderCell>
          <Cell dataKey="siteId" />
        </Column>,
        <Column width={150} sortable resizable fixed key="site">
          <HeaderCell>{formatMessage('i18n_site')}</HeaderCell>
          <TextCell dataKey="siteName" maxLine={1} />
        </Column>,
        <Column width={150} sortable resizable key="segmentation">
          <HeaderCell>{formatMessage('i18n_platform')}</HeaderCell>
          <Cell dataKey="segmentationId" />
        </Column>,
        filterParams.secondary && (
          <Column
            width={filterParams.secondary === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams.secondary === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        <Column width={200} align="right" resizable sortable key="totalImpressions">
          <HeaderCell>{formatMessage('i18n_totalTrackedAds')}</HeaderCell>
          <NumberCell dataKey="totalImpressions" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="notSivt">
          <HeaderCell>={formatMessage('i18n_nonSivtImp')}</HeaderCell>
          <NumberCell dataKey="notSivt" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="impressions">
          <HeaderCell>{formatMessage('i18n_sivtImp')}</HeaderCell>
          <NumberCell dataKey="impressions" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="unusualFre">
          <HeaderCell>{formatMessage('i18n_unusualFrequency')}</HeaderCell>
          <NumberCell dataKey="unusualFre" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="unusualAct">
          <HeaderCell>{formatMessage('i18n_unusualAction')}</HeaderCell>
          <NumberCell dataKey="unusualAct" />
        </Column>,
        <Column width={250} align="right" resizable sortable key="unusualGeo">
          <HeaderCell>{formatMessage('i18n_unusualGeo')} </HeaderCell>
          <NumberCell dataKey="unusualGeo" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="unusualNet">
          <HeaderCell>{formatMessage('i18n_unusualNet')}</HeaderCell>
          <NumberCell dataKey="unusualNet" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="autoBrowse">
          <HeaderCell>{formatMessage('i18n_autoBrowse')}</HeaderCell>
          <NumberCell dataKey="autoBrowse" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="encourageClick">
          <HeaderCell>{formatMessage('i18n_encourageClick')}</HeaderCell>
          <NumberCell dataKey="encourageClick" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="activeOperate">
          <HeaderCell>{formatMessage('i18n_activeOperate')}</HeaderCell>
          <NumberCell dataKey="activeOperate" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="falsifyData">
          <HeaderCell>{formatMessage('i18n_falsifyData')}</HeaderCell>
          <NumberCell dataKey="falsifyData" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="errorDomain">
          <HeaderCell>{formatMessage('i18n_errorDomain')}</HeaderCell>
          <NumberCell dataKey="errorDomain" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="hideAd">
          <HeaderCell>{formatMessage('i18n_hideAd')}</HeaderCell>
          <NumberCell dataKey="hideAd" />
        </Column>,
        <Column width={200} align="right" resizable sortable key="viciouslyFlow">
          <HeaderCell>{formatMessage('i18n_viciouslyFlow')}</HeaderCell>
          <NumberCell dataKey="viciouslyFlow" />
        </Column>
      ];
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
        {renderCol()}
      </Table>
    );
  }
);
