import _ from 'lodash';

export const mapFromResponse = (rspData: Record<string, any>) => {
  return {
    ..._.omit(rspData, ['rtbasia'])
  };
};

export const mapToRequest = (formData: Record<string, any>, isEdit: boolean) => {
  let omitList = _.isNil(formData.auditInfo) ? ['auditInfo'] : [];
  omitList.push(isEdit ? 'advertiserId' : 'advertiser');
  return {
    ..._.omit(formData, omitList),
    notes: formData.notes ? formData.notes : ''
    // rtbasia: _.isArray(formData.rtbasia) ? formData.rtbasia : []
  };
};
