import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';
import { API_CONVERSION_REPORT } from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell, DateCell, TextCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps, secondaryData } from '@/constants/config';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';

import { cellData } from './constants';
import { FilterParam, segData, filterParamKeys } from '../constants';

const mockData: CustomTableDataProps<CellDataProps> = {
  data: [
    {
      value: '13637589456',
      id: 7,
      attributedPV: 35,
      unattributedPV: 24,
      attributedUV: 47,
      unattributedUV: 27,
      conversionPV: 69,
      conversionUV: 39,
      secondary: '',
      name: 'test-batchImport'
    },
    {
      value: '14',
      id: 7,
      attributedPV: 21,
      unattributedPV: 23,
      attributedUV: 12,
      unattributedUV: 3,
      conversionPV: 42,
      conversionUV: 11,
      secondary: '',
      name: 'test-batchImport'
    },
    {
      value: 'man',
      id: 8,
      attributedPV: 3,
      unattributedPV: 7,
      attributedUV: 8,
      unattributedUV: 24,
      conversionPV: 25,
      conversionUV: 12,
      secondary: '',
      name: 'test-batchImport2'
    }
  ],
  page: { current: 0, pagesize: 10, total: 3, size: 100 }
};
interface CellDataProps {
  value: string | number;
  id: number;
  attributedPV: string | number;
  unattributedPV: string | number;
  attributedUV: string | number;
  unattributedUV: string | number;
  conversionPV: string | number;
  conversionUV: string | number;
  secondary: string;
  name: string;
}

export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    setTableParams,
    handleChangeCustomParam
  }: BasicReportProps<FilterParam>) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: API_CONVERSION_REPORT,
      postParams: {
        ...postParams,
        var: 'json'
      }
    });
    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport(
          {
            ...postParams,
            var: 'xlsx'
          },
          API_CONVERSION_REPORT
        );
    }, [onExport, postParams]);

    const cellDataIntl = usePickerDataFromObj(cellData);
    const segDataIntl = usePickerDataFromObj(segData);
    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SelectPicker
          key="segmentation"
          prepend={formatMessage('i18n_segmentation')}
          data={segDataIntl}
          value={filterParams[filterParamKeys.SEG]}
          onChange={(value: string) => handleChangeCustomParam({ [filterParamKeys.SEG]: value })}
        />,
        <CheckPicker
          key="indicators"
          prepend={formatMessage('i18n_metrics')}
          placeholder={formatMessage('i18n_select', { name: '' })}
          data={cellDataIntl}
          value={filterParams[filterParamKeys.INDICATORS]}
          style={{ maxWidth: '224px' }}
          onChange={(value: any) =>
            handleChangeCustomParam({ [filterParamKeys.INDICATORS]: value })
          }
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };
    // 渲染已选指标列
    const renderSelectedCol = (): React.ReactElement[] => {
      return cellDataIntl.map((i: any) => {
        if (_.includes(filterParams[filterParamKeys.INDICATORS], i.id)) {
          return (
            <Column width={200} sortable resizable align="right" key={i.id}>
              <HeaderCell>{i.name}</HeaderCell>
              <NumberCell dataKey={i.id} />
            </Column>
          );
        }
        return null;
      });
    };
    // 渲染表列
    const renderCol = (): React.ReactElement[] => {
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams[filterParamKeys.SEC])[0],
        'label'
      );

      const list = [
        <Column width={100} sortable resizable fixed key="id">
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>,
        <Column width={150} sortable resizable fixed key="activity">
          <HeaderCell>{formatMessage('i18n_activity') + formatMessage('i18n_name')}</HeaderCell>
          <TextCell dataKey="name" maxLine={1} />
        </Column>,
        <Column width={150} sortable resizable fixed key="be">
          <HeaderCell>{formatMessage('i18n_belongBE')}</HeaderCell>
          <TextCell dataKey="be" maxLine={1} />
        </Column>,
        <Column width={150} sortable resizable fixed key="para">
          <HeaderCell>{formatMessage('i18n_customAttributes')}</HeaderCell>
          <TextCell dataKey="value" maxLine={1} />
        </Column>,
        filterParams[filterParamKeys.SEG] && (
          <Column width={150} sortable resizable key="segmentation">
            <HeaderCell>{formatMessage('i18n_platform')}</HeaderCell>
            <Cell dataKey="segmentation" />
          </Column>
        ),
        filterParams[filterParamKeys.SEC] && (
          <Column
            width={filterParams[filterParamKeys.SEC] === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams[filterParamKeys.SEC] === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        filterParams[filterParamKeys.SEC] === 'hourly' && (
          <Column width={100} key="hourly" sortable resizable>
            <HeaderCell>{formatMessage('i18n_hour')}</HeaderCell>
            <Cell dataKey="timeslot" />
          </Column>
        )
      ];
      return _.concat(list, renderSelectedCol());
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
        {renderCol()}
      </Table>
    );
  }
);
