import React, { useCallback, useState, useContext, useRef, useEffect } from 'react';
import { Form } from 'rsuite';
import { FileType } from 'rsuite/lib/Uploader';
import _ from 'lodash';

import { __PUBLIC__ } from '@/constants/config';
import { SingleFileUploader } from '@/components/Form/SingleFileUploader';
import { API_CROSS_CAMPAIGN_BATCHES } from '@/constants/APIs';
import CustomField from '@/components/Form/CustomField';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

import { CreateFormCtx } from '../context';
import { checkLevelIsCampaign } from '../constants';

const requestMapper = (f: any) => ({
  ...f,
  selectedId: (f.selectedId || []).toString()
});

const fileKeyInForm = 'file';
export default () => {
  const { formatMessage } = useFmtMsgFori18n();
  const [formError, setFormError] = useState({
    [fileKeyInForm]: ''
  });

  const { drawerFormResult, isEdit, selectedAvailable, setSelectedAvailable } = useContext(
    CreateFormCtx
  );
  const {
    formData: { crossLevel, crossCamId },
    handleFormChange
  } = drawerFormResult;

  const extDataRef = useRef({ uploaded: false }); // 为了维护个变量记录下是不是第一次提交，这里不存在state里面，而是直接用一个变量的形式直接更新
  const uploadDataRef = useRef({
    // 放在ref这样就可以不作为下面accepter组件的依赖了，这样这个ref变了也没有关系
    crossEntity: checkLevelIsCampaign(crossLevel) ? 'campaign' : 'placement',
    create: isEdit ? 0 : 1,
    selectedId: [],
    ...(isEdit && { crossCamId })
  });

  const shouldUpload = useCallback(
    (file: FileType) => {
      const ext = _.last(file.name.split('.'));
      const result = _.lowerCase(ext) === 'xlsx';
      if (!result) {
        setFormError({
          [fileKeyInForm]: formatMessage('i18n_uploadFileTypeError')
        });
      }

      return Boolean(result);
    },
    [formatMessage]
  );

  const onSuccess = useCallback(
    res => {
      const result = _.get(res, 'records', []);
      handleFormChange(result, 'batchResult');
      extDataRef.current.uploaded = true;

      // available表格是全集，在上传完之后 用后端的返回结果去覆盖下上面表格的筛选项
      // 新增的不会出现在selected里面，删除的会删掉
      setSelectedAvailable(_.map(result, r => r.id));
    },
    [handleFormChange, setSelectedAvailable]
  );

  const onProgress = useCallback(() => {
    //
  }, []);

  const onRemove = useCallback(() => {
    //
  }, []);

  const onError = useCallback(res => {
    const err = _.get(res, 'response.fields.error.0.message', '');
    if (err) {
      setFormError({
        [fileKeyInForm]: err
      });
    }
  }, []);

  const resetFormError = useCallback(() => {
    setFormError({
      [fileKeyInForm]: ''
    });
  }, []);

  const UploaderComp = useCallback(
    () => (
      <SingleFileUploader
        accept=".xlsx"
        autoUpload
        data={requestMapper(uploadDataRef.current)}
        action={API_CROSS_CAMPAIGN_BATCHES}
        shouldUpload={shouldUpload}
        onSuccess={onSuccess}
        onError={onError}
        onReupload={resetFormError}
        onProgress={onProgress}
        onRemove={onRemove}
        onChange={resetFormError}
      />
    ),
    [shouldUpload, onSuccess, onError, resetFormError, onProgress, onRemove]
  );

  useEffect(() => {
    // 没有upload过之前，都是使用 上面 availableTable的选择项，
    // 一旦变成true，就不在更新了，一直用的之前的
    if (extDataRef.current.uploaded === false) {
      uploadDataRef.current.selectedId = selectedAvailable;
    }
  }, [selectedAvailable]);

  return (
    <>
      <div className="cross-uploader">
        <div>
          <a href={`${__PUBLIC__}/resources/import_cross_template.xlsx`} download>
            {formatMessage('i18n_downExcel')}
          </a>
        </div>
        <Form formError={formError}>
          <CustomField name={fileKeyInForm} accepter={UploaderComp} />
        </Form>
      </div>
    </>
  );
};
