import * as React from 'react';
import { Icon, Tooltip, Whisper } from 'rsuite';
import { WhisperProps } from 'rsuite/lib/Whisper';

export const DescriptionIcon = <Icon className="form-filed-description-icon" icon="question2" />;

interface Props {
  message: React.ReactNode;
  children?: React.ReactNode;
}

const HelpInfo = React.memo(
  ({ placement = 'top', message, children = DescriptionIcon, ...props }: WhisperProps & Props) => (
    <Whisper placement={placement} speaker={<Tooltip>{message}</Tooltip>} {...props}>
      {children}
    </Whisper>
  )
);

HelpInfo.displayName = 'HelpInfo';
export default HelpInfo;
