import React, { useState, useCallback } from 'react';
import _ from 'lodash';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import SelectPicker from '@/components/Picker/SelectPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { API_ADVERTISER } from '@/constants/APIs';
import { BrandModel } from '@/constants/form/models';
import RTBAsiaPicker from './RTBAsiaPicker';

const advParams = {
  orderColumn: 'name',
  orderType: 'asc',
  status: 1,
  auditFlag: 1
};

export default React.memo(({
  drawer,
  formData,
  originalFormData,
  formErrors,
  formRef,
  onChange,
  onSubmit,
  onClose,
  setFormErrors,
  checkForm,
}: any) => {
  const { formatMessage } = useFmtMsgFori18n();

  // 新建提交时需要传广告主id，但编辑获取和提交都使用广告主名称
  // 处理方式是：在picker中使用名称，但是在新建时，需要在formData中保存一下id
  const [advData, setAdvData] = useState<any[]>([]);
  const handleFetchAdvSuccess = useCallback(data => {
    setAdvData(data);
  }, []);
  const handleChangeAdv = useCallback((val: string) => {
    if(drawer.isEdit){
      return;
    }
    const advertiserId = _.find(advData, i => i.name === val)?.id;
    _.isNumber(advertiserId) && onChange(advertiserId, 'advertiserId');
  }, [advData, drawer.isEdit, onChange]);

  return (
    <Drawer
      show={drawer.show}
      title={formatMessage(drawer.isEdit ? 'i18n_editBrand' : 'i18n_newBrand')}
      submitLoading={drawer.submitting}
      dataLoading={drawer.loading}
      className='brand-drawer'
      onSubmit={onSubmit}
      onHide={onClose}
    >
      <Form
        formData={formData}
        formErrors={formErrors}
        model={BrandModel}
        formRef={formRef}
        onChange={onChange}
        setFormErrors={setFormErrors}
        checkForm={checkForm}
      >
        {/* 名称 */}
        <CustomField
          name='name'
          label={formatMessage('i18n_name')}
          required
        />

        {/* 所属广告主 */}
        <CustomField
          name='advertiser'
          label={formatMessage('i18n_parentAdvertiser')}
          accepter={SelectPicker}
          valueKey='name'
          url={API_ADVERTISER}
          params={advParams}
          searchable
          required
          disabled={drawer.isEdit}
          onChange={handleChangeAdv}
          onFetchSuccess={handleFetchAdvSuccess}
        />

        {/* RTBAsia品牌名称 */}
        {/* <CustomField
          name='rtbasia'
          label={`RTBAsia ${formatMessage('i18n_brand')} ${formatMessage('i18n_name')}`}
          accepter={RTBAsiaPicker}
          checkTrigger='change'
          data={drawer.isEdit ?
            _.get(originalFormData, 'rtbasia', []).map((i: string) => ({name: i})) :
            []
          }
        /> *}

        {/* 备注 */}
        <CustomField
          name='notes'
          label={formatMessage('i18n_remark')}
          componentClass='textarea'
          rows={2}
        />
      </Form>
    </Drawer>
  );
});
