import React, { memo } from 'react';
import _ from 'lodash';

import { TIME } from '@/constants/config';
import * as APIs from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useChart from '@/components/hooks/useChart';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';
import { LoadingBlock, EmptyBlock } from '@/components/EmptyOrLoading';

import { prefixClassName } from '../constants';

const mockData: Array<DataDetail> = [
  {
    id: 2,
    clicks: 44,
    uclicks: '--',
    impressions: 4,
    uimpressions: '--',
    campaignId: 300171,
    secondary: '',
    clickRate: '1075.00%',
    name: 'DSK',
    value: 43
  },
  {
    id: 3,
    clicks: 20,
    uclicks: '--',
    impressions: 2,
    uimpressions: '--',
    campaignId: 300171,
    secondary: '',
    clickRate: '1075.00%',
    name: 'MKK',
    value: 43
  }
];

interface DataDetail {
  id: number | string;
  clicks: number | string;
  uclicks: number | string;
  impressions: number | string;
  uimpressions: number | string;
  campaignId: number;
  secondary: string;
  clickRate: string;
  name: string;
  value: number;
}

interface Props {
  dateParams?: TIME;
  id: number | string;
  tag?: string;
  mDimension?: string;
  labelKey?: string;
  valueKey?: string;
}

const pieOption = {
  type: 'pie',
  radius: ['50%', '80%'],
  label: {
    show: false
  }
};
const textStyle = {
  fontWeight: 'normal',
  fontSize: '16',
  textAlign: 'center'
};

export default memo<Props>(function ({ dateParams, id, tag, mDimension }) {
  const { data, loading } = useChart({
    dateParams,
    api: APIs.API_DASHBOARD,
    postParams: {
      id,
      hierarchy: 'campaign',
      tag,
      mDimension: tag === 'area' ? 'province' : mDimension
    }
  });
  const { formatMessage } = useFmtMsgFori18n();
  const newdata: Array<DataDetail> = _.get(data, 'records', []);
  let impData = [],
    clkData = [],
    serieData = [],
    titleData = [],
    legend = [],
    optionData = [];
  newdata.forEach(item => {
    legend.push(item.name);
    if (item.impressions !== null) {
      impData.push({
        name: item.name,
        value: item.impressions
      });
    }
    if (item.clicks !== null) {
      clkData.push({
        name: item.name,
        value: item.clicks
      });
    }
  });
  if (_.isEmpty(impData)) {
    serieData = [
      {
        center: ['50%', '50%'],
        ...pieOption,
        name: formatMessage('i18n_clicks'),
        id: 'clicks',
        data: clkData
      }
    ];
    titleData = [
      {
        text: formatMessage('i18n_clicks'),
        left: '49%',
        top: '47%',
        textAlign: 'center',
        textStyle
      }
    ];
  }
  if (_.isEmpty(clkData)) {
    serieData = [
      {
        center: ['50%', '50%'],
        ...pieOption,
        id: 'impressions',
        name: formatMessage('i18n_impressions'),
        data: impData
      }
    ];
    titleData = [
      {
        text: formatMessage('i18n_impressions'),
        left: '49%',
        top: '47%',
        textAlign: 'center',
        textStyle
      }
    ];
  }
  if (!_.isEmpty(clkData) && !_.isEmpty(impData)) {
    serieData = [
      {
        center: ['24%', '50%'],
        ...pieOption,
        name: formatMessage('i18n_clicks'),
        id: 'clicks',
        data: clkData
      },
      {
        center: ['76%', '50%'],
        ...pieOption,
        id: 'impressions',
        name: formatMessage('i18n_impressions'),
        data: impData
      }
    ];
    titleData = [
      {
        text: formatMessage('i18n_clicks'),
        left: '23%',
        top: '47%',
        textAlign: 'center',
        textStyle
      },
      {
        text: formatMessage('i18n_impressions'),
        left: '75%',
        top: '47%',
        textAlign: 'center',
        textStyle
      }
    ];
  }
  let option = {
    color: ['#34C3FF', '#1464AC', '#13BA9E', '#A873E6'],
    legend: {
      data: legend
    },
    title: titleData,
    series: serieData
  };

  const config = {
    type: CHART_KEY.PIE
  };

  if (loading) {
    return <LoadingBlock />;
  }

  return (
    <>
      <div className={prefixClassName('piechart')} style={{ height: 300, position: 'relative' }}>
        {_.isEmpty(data) ? (
          <EmptyBlock />
        ) : (
            <Chart config={config} option={option} loading={loading} pure />
          )}
      </div>
    </>
  );
});
