import React from 'react';
import SvgIcon from './SvgIcon';
import GoingIcon from './GoingIcon';

export default (status = 'ACTIVE' as any) => {
  let icon = null;
  switch (status) {
    case 'DRAFT':
    case 1:
      icon = (
        <SvgIcon
          icon="draft"
          className="campaign-status-icon"
          style={{ color: '#B58AE6' }}
          width={14}
          height={14}
        />
      );
      break;
    case 'READY':
    case 2:
      icon = (
        <SvgIcon
          icon="ready"
          className="campaign-status-icon"
          style={{ color: '#7ED321' }}
          width={14}
          height={14}
        />
      );
      break;
    case 'COMPLETE':
    case 4:
      icon = (
        <SvgIcon
          icon="check-circle"
          className="campaign-status-icon"
          style={{ color: '#7ED321' }}
          width={14}
          height={14}
        />
      );
      break;
    case 'UNREGISTER':
    case 5:
      icon = (
        <SvgIcon
          icon="remind"
          className="campaign-status-icon"
          style={{ color: '#FFB300' }}
          width={14}
          height={14}
        />
      );
      break;
    case 'ACTIVE':
    case 3:
      icon = <GoingIcon />;
      break;
    case 'CANCELLED':
    case 0:
      icon = (
        <SvgIcon
          icon="off-round"
          className="campaign-status-icon"
          style={{ color: '#A6A6A6' }}
          width={14}
          height={14}
        />
      );
      break;
    default:
      break;
  }
  return icon;
};
