import React, { memo, useReducer, useMemo } from 'react';
import { Panel, Breadcrumb, Content } from 'rsuite';
import { useMainPage } from '@/components/hooks/useMainPage';
import { useHistory } from 'react-router';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Table from './Table';
import Toolbar from './Toolbar';
import {
  defaultUploadFilter,
  UploadReducerAction,
  UploadReducerType,
  UploadReducerState
} from '../constants';
import { UploadCtx } from './context';

const reducer = (state: UploadReducerState, action: UploadReducerAction) => {
  switch (action.type) {
    case UploadReducerType.SET_UPLOADING_STATUS: {
      const { isUploading } = action;
      return {
        ...state,
        isUploading,
        data: []
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_START: {
      return {
        ...state,
        loading: true,
        isUploading: false
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_ACC: {
      const { data } = action;
      return {
        ...state,
        data,
        loading: false,
        isUploading: false
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_EXP: {
      return {
        ...state,
        loading: false,
        isUploading: false
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_START: {
      return {
        ...state,
        reqDelay: 3000
      };
    }

    case UploadReducerType.REQUEST_TABLE_DATA_INTERVAL_END: {
      return {
        ...state,
        reqDelay: null, // null表示不再执行了
        loading: false,
        isUploading: false
      };
    }

    default:
      return state;
  }
};

export default memo(() => {
  const { formatMessage } = useFmtMsgFori18n();
  const history = useHistory();
  const { commonParam, handleChangeCommonParam } = useMainPage({
    defaultCommonParam: defaultUploadFilter
  });

  const [state, dispatch] = useReducer(reducer, {
    isUploading: false,
    data: [],
    loading: false,
    reqDelay: null
  });
  const uploadCtxMemoVal = useMemo(
    () => ({
      state,
      dispatch,
      handleChangeCommonParam
    }),
    [state, dispatch, handleChangeCommonParam]
  );

  return (
    <>
      <Breadcrumb className="campaign-navigator">
        <Breadcrumb.Item
          onClick={() => {
            history.push('/placement-management');
          }}
        >
          {formatMessage('i18n_menuAdunitManage')}
        </Breadcrumb.Item>
        <Breadcrumb.Item
          onClick={() => {
            history.push('/placement-management/upload');
          }}
        >
          {formatMessage('i18n_batchUpload')}
        </Breadcrumb.Item>
      </Breadcrumb>
      <UploadCtx.Provider value={uploadCtxMemoVal}>
        <Panel className="upload-panel-no-overflow">
          <Toolbar />
        </Panel>
        <Panel className="upload-panel-table panel-table">
          <Table filterParams={commonParam} />
        </Panel>
      </UploadCtx.Provider>
    </>
  );
});
