import _ from 'lodash';
import classnames from 'classnames';
import { Table, Checkbox } from 'rsuite';
import React from 'react';
import { CheckboxProps } from 'rsuite/lib/Checkbox';

const { Cell } = Table;

export interface CheckBoxCellProps<T> {
  rowData?: T;
  dataKey: string;
  className?: string;
  getCheckBoxProps?: (rowData: T) => CheckboxProps;
  disabled?: ((data: any, rowData: T) => boolean) | boolean;
  checked?: ((data: any, rowData: T) => boolean) | boolean;
  onChange?: (value: any, checked: boolean, event: React.SyntheticEvent<HTMLInputElement>) => void;
}

const CheckBoxCell = ({
  rowData,
  dataKey,
  className,
  getCheckBoxProps,
  onChange,
  checked = false,
  disabled = false,
  ...props
}: CheckBoxCellProps<BaseObject<any>>) => {
  const data = _.get(rowData, dataKey, false);
  const checkedValue: boolean = _.isFunction(checked) ? checked(data, rowData) : checked;
  const disabledValue: boolean = _.isFunction(disabled) ? disabled(data, rowData) : disabled;
  return (
    <Cell style={{ padding: 0 }} {...props}>
      <div style={{ padding: '5px' }}>
        <Checkbox
          inline
          value={data}
          checked={checkedValue}
          disabled={disabledValue}
          onChange={onChange}
          {...(getCheckBoxProps && getCheckBoxProps(rowData)) || {}}
        />
      </div>
    </Cell>
  );
};

export default React.memo(CheckBoxCell);
