import React, { memo, useContext, useMemo } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';
import { useUserId } from '@/components/hooks/useCertainParams';
import { useMainPage } from '@/components/hooks/useMainPage';
import Toolbar, { pickerName } from '../Toolbar';
import {
  defaultFilter,
  hierarchy,
  reportKeys,
  commonParamFormatter,
  filterParamKeys
} from '../constants';
import { UserModuleDateCtx } from '../../context';

import Table from './Table';

export default memo(() => {
  const { commonParam, handleChangeCommonParam, customParamResult } = useMainPage({
    defaultCommonParam: {
      ...defaultFilter,
      [filterParamKeys.MDIMENSION]: 'campaign'
    },
    commonParamFormatter,
    customConfig: [
      {
        isTable: true
      }
    ]
  });
  const userId = useUserId();
  const [tableResult] = customParamResult;
  const { param, fmtParam, handleSearchWord, handleChangePagination } = tableResult;
  const { dateRangeValTimeFormat, setDateRangeValByDateArray } = useContext(UserModuleDateCtx);

  const postParams = useMemo(
    () => ({
      id: userId,
      hierarchy,
      report: reportKeys.PATH
    }),
    [userId]
  );
  return (
    <>
      <Panel className="page-toolbar">
        <Toolbar
          show={[pickerName.BE, pickerName.ADVBRAND, pickerName.ACTION]}
          dateParams={dateRangeValTimeFormat}
          filterParams={commonParam}
          handleChangeDate={setDateRangeValByDateArray}
          handleChangeCommonParam={handleChangeCommonParam}
        />
      </Panel>
      <Panel className="panel-table">
        <Table
          dateParams={dateRangeValTimeFormat}
          filterParams={param}
          tableParams={fmtParam}
          postParams={postParams}
          setTableParams={handleChangePagination}
          handleSearchWord={handleSearchWord}
          handleChangeCommonParam={handleChangeCommonParam}
        />
      </Panel>
    </>
  );
});
