import React, { useState, useCallback } from 'react';
import UserProfile, { FooterLanguageButton, FooterIconButton } from '@hypers/react-user-profile';
import _ from 'lodash';
import useIntl from '@/components/hooks/useIntl';
import { PROFILE } from '@/constants/config';
import { getUserProfile, removeUserProfile, getFullUrl } from '@/utils/location';
import { getCurrentLocale, setCurrentLocale } from '@/utils/locale';
import { getEnvFromSession } from '@/utils/env';
import { removeStorage } from '@/utils/storage';

type Locale = 'zh-CN' | 'en-US';

export default React.memo(() => {
  const [locale, setLocale] = useState<Locale>(getCurrentLocale());
  const userProfileResp = getUserProfile();

  const profile: PROFILE = {
    id: userProfileResp.id,
    name: _.get(userProfileResp, 'username', ''),
    email: _.get(userProfileResp, 'email', ''),
    avatar: _.get(userProfileResp, 'hacProfile.avatar', ''),
    avatarThumb: _.get(userProfileResp, 'hacProfile.avatarThumb', '')
  };

  // 修改语言
  const handleChangeLocale = useCallback(() => {
    const value = locale === 'zh-CN' ? 'en-US' : 'zh-CN';
    setLocale(value);
    setCurrentLocale(value);
    window.location.reload();
  }, [locale]);

  // 登出
  const handleLogout = () => {
    removeStorage();
    removeUserProfile();
    const { logoutUrl } = getEnvFromSession();
    window.location.href = getFullUrl(logoutUrl);
  };

  const { formatMessage } = useIntl();

  return (
    <UserProfile userInfo={profile}>
      <UserProfile.Footer>
        <FooterLanguageButton onClick={handleChangeLocale}>
          {locale === 'zh-CN' ? 'EN' : '简中'}
        </FooterLanguageButton>
        <FooterIconButton
          icon="cog"
          target="_blank"
          href={_.get(userProfileResp, 'hacProfile.profileUrl')}
        >
          {formatMessage({ id: 'setting' })}
        </FooterIconButton>
        <FooterIconButton icon="off" onClick={handleLogout}>
          {formatMessage({ id: 'logout' })}
        </FooterIconButton>
      </UserProfile.Footer>
    </UserProfile>
  );
});
