import React, { memo, useCallback } from 'react';
import { Form } from 'rsuite';
import _ from 'lodash';
import Dialog, { UseModalResult } from '@/components/AuditDialog';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import CustomField from '@/components/Form/CustomField';
import { ApplyModel } from '@/constants/form/models';
import { useModalForm } from '@/components/hooks/useModalForm';
import { extractErrMsgFromResp } from '@/components/hooks/useFetch';

const NOTE_KEY = 'auditInfo';
const ACTIVE = 'ACTIVE';
const FAILED = 'FAILED';
const DEFAULT_FORM_VAL = {
  [NOTE_KEY]: ''
};

export default memo((props: UseModalResult) => {
  const { data, resetModal, onFetch, refetchTableData, ...rest } = props;
  const { formatMessage } = useFmtMsgFori18n();
  const modalForm = useModalForm(DEFAULT_FORM_VAL, {});

  const onHide = useCallback(() => {
    resetModal && resetModal();
    modalForm.resetForm();
  }, [resetModal, modalForm]);

  const onSubmit = useCallback(
    value => {
      const auditInfo = _.get(modalForm.formVal, 'auditInfo');
      if (!value && !auditInfo) {
        modalForm.checkFormErr({
          auditInfo: formatMessage('i18n_plsEnter', {
            name: formatMessage('i18n_remark')
          })
        });
        return;
      }
      if (modalForm.formRef.current && modalForm.formRef.current.check()) {
        const reqParam = {
          id: data.id
        };
        let postData = {};
        if (value) {
          postData = {
            status: ACTIVE
          };
        } else {
          postData = {
            status: FAILED,
            ...modalForm.formVal
          };
        }
        onFetch({
          params: reqParam,
          data: postData
        })
          .then(rsp => {
            if (extractErrMsgFromResp(rsp)) {
              modalForm.checkFormErr(extractErrMsgFromResp(rsp));
            } else {
              refetchTableData && refetchTableData();
            }
            onHide();
          })
          .catch(e => {
            console.log(e);
          });
      }
    },
    [modalForm, onFetch, data, onHide, refetchTableData]
  );
  return (
    <Dialog
      {...rest}
      onSubmit={onSubmit}
      onHide={onHide}
      size="xs"
      backdrop={true}
      submitText={formatMessage('i18n_cpoPass')}
      cancelText={formatMessage('i18n_cpoRefuse')}
    >
      <>
        <div style={{ marginBottom: 18 }}>{formatMessage('i18n_applyDesc')}</div>
        <Form
          fluid
          model={ApplyModel}
          formValue={modalForm.formVal}
          onChange={modalForm.setFormVal}
          onCheck={modalForm.checkFormErr}
          formError={modalForm.formErr}
          ref={modalForm.formRef}
        >
          <CustomField
            name={NOTE_KEY}
            label={formatMessage('i18n_remark')}
            rows={2}
            required
            componentClass="textarea"
          />
        </Form>
      </>
    </Dialog>
  );
});
