import React, { useCallback } from 'react';
import _ from 'lodash';
import PickerGroup from '@/components/Picker/PickerGroup';
import SelectPicker from '@/components/Picker/SelectPicker';
import { usePickerDataFromObj } from '@/utils/formatMessage';
import { frequencyData, weekData, dateData } from '../constants';

const WEEK = 'weekly';
const MONTH = 'monthly';

const freqTypeName = 'freqType';
const freqValName = 'freqVal';

export default React.memo(({ values, errors, disabled, onChange }: any) => {
  const frequencyDataIntl = usePickerDataFromObj(frequencyData);
  const weekDataIntl = usePickerDataFromObj(weekData);

  const handleChange = useCallback(
    key => (val: string) => {
      // 选择每月或每周时，需重置具体频次；选择单次或每日时，需清空具体频次
      if (key === freqTypeName) {
        if (val === WEEK) {
          onChange(freqValName)(weekData[0].id);
        } else if (val === MONTH) {
          onChange(freqValName)(dateData[0].id);
        } else {
          _.isNumber(values[freqValName]) && onChange(freqValName)('');
        }
      }

      onChange(key)(val);
    },
    [onChange, values]
  );

  return (
    <PickerGroup values={values} errors={errors} onChange={handleChange}>
      <SelectPicker name={freqTypeName} data={frequencyDataIntl} disabled={disabled} />
      {_.includes([WEEK, MONTH], values[freqTypeName]) && (
        <SelectPicker
          name={freqValName}
          data={values[freqTypeName] === WEEK ? weekDataIntl : dateData}
          disabled={disabled}
        />
      )}
    </PickerGroup>
  );
});
