import React, { useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Schema } from 'rsuite';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import SelectPicker from '@/components/Picker/SelectPicker';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { checkDateRangeOverNMonth, getYesterday } from '@/utils/date';
import { getAllowStringType } from '@/constants/form/validateTypes';
import { getDivisionData, getLevelData, TAB_KEY, TableFilterParam, HierarchyIdKey, metricsData } from './constants';
import { DrawerCustomField } from './DrawerCustomField';

export default React.memo(
  ({
    param,
    drawer,
    formData = {},
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm,
    model,
    setDrawer
  }: any) => {
    const { isEdit } = drawer;
    const { formatMessage } = useFmtMsgFori18n();
    const { exportType } = param as TableFilterParam;
    const hierarchyDataIntl = usePickerDataFromObj(getLevelData(exportType));
    const maxDate = useMemo(() => {
      if (exportType === TAB_KEY.RAW_EXPORT) {
        return new Date();
      }

      return getYesterday();
    }, [exportType]);

    const handleChange = useCallback(
      (val, key) => {

        if (!_.isString(key) && _.isObject(val)) {
          // onChange 本身支持两种方式，我们只处理一种情况
          const currHierarchy = _.get(val, 'hierarchy');
          const prevHierarchy = formData.hierarchy;
          const currDivision = _.get(val, 'division');
          const divisionList = getDivisionData(exportType, currHierarchy);
          if (
            // 如果分包不在这个返回内了(数据受hierarchy的影响，那么就用第一个值)
            prevHierarchy !== currHierarchy &&
            !_.find(divisionList, { id: currDivision }) &&
            (exportType === TAB_KEY.RAW_EXPORT || exportType === TAB_KEY.DEVICE_ID)
          ) {
            const newDivision = _.get(divisionList, '0.id');
            onChange({
              ...val,
              division: newDivision
            });
            return;
          }
        }
        if (key === 'hierarchy') {
          const prevHierarchy = formData.hierarchy;
          //自定义导出 层级为转化活动 指标值默认为转化次数
          if (val !== prevHierarchy) {
            if (val === HierarchyIdKey.CONVERSION) {
              onChange({
                ...formData,
                hierarchy: val,
                metrics: metricsData.filter(i => i.id === 'conversion').map(i => i.id)
              });
              return;
            } else {
              onChange({
                ...formData,
                hierarchy: val,
                metrics: metricsData.filter(i => i.id !== 'conversion').map(i => i.id)
              });
              return;
            }
          }
        }

        onChange(val, key);
      },
      [onChange, formData, exportType]
    );

    // 重合度计算类型时需要根据formData动态指定model，其余情况与传入model保持一致
    const localModel = useMemo(
      () =>
        exportType === TAB_KEY.CALC_OVERLAP
          ? Schema.Model({
            name: getAllowStringType(formatMessage('i18n_name'), true),
            ..._.range(_.get(formData, 'data.length', 1)).reduce(
              (acc, i) => ({
                ...acc,
                [`ids-${i}`]: getAllowStringType(formatMessage('i18n_levelId'), true).addRule(
                  value => _.every(_.split(value, ','), i => !_.isNaN(Number(i))),
                  formatMessage('i18n_formatError', {
                    name: formatMessage('i18n_levelId')
                  })
                ),
                [`dateRange-${i}`]: Schema.Types.ArrayType().addRule(([d1, d2]) => !checkDateRangeOverNMonth(d1, d2, 12), formatMessage('taskMayFail'))
              }),
              {}
            )
          })
          : model,
      [exportType, formData.data?.length, model]
    );

    const handleSubmit = useCallback(() => {
      if (isEdit) {
        setDrawer((p: any) => ({
          ...p,
          isEdit: false
        }));
        onChange((prev: any) => ({
          ...prev,
          name: `${prev.name || ''}_副本`
        }));
      } else if (exportType === TAB_KEY.CALC_OVERLAP) {
        localModel
          .checkAsync({
            name: formData.name,
            ..._.reduce(
              formData.data,
              (acc, i, idx) => ({
                ...acc,
                [`ids-${idx}`]: i.id_set,
                [`dateRange-${idx}`]: i.dateRange
              }),
              {}
            )
          })
          .then((checkResults: Record<string, any>) =>
            _.toPairs(checkResults).reduce(
              (acc, [name, { hasError, errorMessage }]) =>
                hasError
                  ? {
                    ...acc,
                    [name]: errorMessage
                  }
                  : acc,
              {}
            )
          )
          .then((errors: Record<string, any>) => {
            if (_.isEmpty(_.keys(errors))) {
              onSubmit();
            } else {
              setFormErrors(errors);
            }
          });
      } else {
        onSubmit();
      }
    }, [formData, setDrawer, onChange, onSubmit]);

    return (
      <Drawer
        show={drawer.show}
        title={isEdit ? formatMessage('i18n_viewTask') : formatMessage('i18n_newTask')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        onSubmit={handleSubmit}
        onHide={onClose}
        btnMsg={isEdit ? formatMessage('i18n_cpAndNewTask') : ''}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={model}
          formRef={formRef}
          onChange={handleChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/** 任务名称 */}
          <CustomField
            name="name"
            label={formatMessage('i18n_taskName')}
            maxLength={200}
            required
            disabled={isEdit}
          />
          {exportType !== TAB_KEY.CALC_OVERLAP && (
            <>
              {/** 时间范围 */}
              <CustomField
                name="dateRange"
                label={formatMessage('i18n_dateRange')}
                accepter={DateRangePicker}
                cleanable={false}
                required
                maxDate={maxDate}
                disabled={isEdit}
              />
              {/** 导出层级 */}
              <CustomField
                name="hierarchy"
                label={formatMessage('i18n_exportLevel')}
                accepter={SelectPicker}
                data={hierarchyDataIntl}
                disabled={isEdit}
              />
              {/** 层级ID */}
              <CustomField
                name="ids"
                label={formatMessage('i18n_levelId')}
                message={formatMessage('i18n_idListTips')}
                required
                disabled={isEdit}
              />
            </>
          )}
          <DrawerCustomField
            type={exportType}
            hierarchyData={hierarchyDataIntl}
            maxDate={maxDate}
            hierarchy={formData.hierarchy}
            disabled={isEdit}
            localModel={localModel}
          />
        </Form>
      </Drawer>
    );
  }
);
