import React, { memo } from 'react';
import _ from 'lodash';
import SelectPicker from '@/components/Picker/SelectPicker';
import SamplePicker from './part/SampleGroupPicker';
import { filterParamKeys, sampleTypeData, defaultFilter } from './constants';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { BasicReportProps } from '@/constants/config';

interface Props extends Partial<BasicReportProps> {
  polling: boolean;
}

export default memo(({ filterParams, handleChangeCustomParam, polling }: Props) => {
  const { formatMessage } = useFmtMsgFori18n();
  const sampleTypeDataIntl = usePickerDataFromObj(sampleTypeData);
  return (
    <>
      <SelectPicker
        prepend={formatMessage('i18n_sampleType')}
        data={sampleTypeDataIntl}
        value={filterParams.dimension}
        onChange={(value: any) =>
          handleChangeCustomParam({
            [filterParamKeys.DIMENSION]: value
          })
        }
        disabled={polling}
        appearance="subtle"
      />
      {filterParams.dimension === defaultFilter.dimension && (
        <SamplePicker
          filterParams={filterParams}
          handleChangeCustomParam={handleChangeCustomParam}
        />
      )}
    </>
  );
});
