import React, { useCallback } from 'react';
import _ from 'lodash';
import { InputGroup, Input } from 'rsuite';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import MultiField from '@/components/Form/MultiField';
import Toggle from '@/components/Form/Toggle';
import CheckPicker from '@/components/Picker/CheckPicker';
import BERoleMultiPicker from '@/components/Picker/logic/BERoleMultiPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { BEModel } from '@/constants/form/models';
import { API_PRODUCT } from '@/constants/APIs';
import { __HAA__ } from '@/constants/config';
import BERoleSetter from './BERoleSetter';
import { advName, agName, pubName, defaultName } from './constants';

const InfoInput = ({ title, ...props }: Record<string, any>) => (
  <InputGroup className="info-input">
    <InputGroup.Addon>{title}</InputGroup.Addon>
    <Input {...props} />
  </InputGroup>
);

export default React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any) => {
    const { formatMessage } = useFmtMsgFori18n();

    const handleChangeInternal = useCallback(
      (val: boolean) => {
        onChange(!val && formData.isLockBeRole, 'isLockBeRole');
        onChange(!val && formData.isLockAdS, 'isLockAdS');

        // 设为内部时清除有关BE角色的错误
        const roleNames = [advName, agName, pubName, defaultName];
        val &&
          _.some(roleNames, i => !_.isEmpty(formErrors[i])) &&
          setFormErrors((prevErrors: Record<string, any>) => ({
            ...prevErrors,
            [advName]: undefined as any,
            [agName]: undefined as any,
            [pubName]: undefined as any,
            [defaultName]: undefined as any
          }));
      },
      [formData.isLockBeRole, formData.isLockAdS, formErrors]
    );

    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editBE' : 'i18n_newBE')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading}
        className="be-drawer"
        onSubmit={onSubmit}
        onHide={onClose}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={BEModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} required />

          {/* 状态 */}
          <CustomField
            name="status"
            label={formatMessage('i18n_status')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_enabled')}
            unCheckedChildren={formatMessage('i18n_disabled')}
          />

          {/* 联系人姓名 */}
          <CustomField name="contact" label={formatMessage('i18n_contactName')} />

          {/* 联系人邮件 */}
          <CustomField name="email" label={formatMessage('i18n_contactEmail')} />

          {/* 是否内部 */}
          <CustomField
            name="internal"
            label={formatMessage('i18n_isInternal')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_yes')}
            unCheckedChildren={formatMessage('i18n_no')}
            disabled={drawer.isEdit}
            onChange={handleChangeInternal}
          />

          {/* BE角色 */}
          <MultiField
            names={['advertiserRole', 'agencyRole', 'publisherRole', 'defaultRole']}
            label={formatMessage('i18n_beRole')}
            accepter={BERoleSetter}
            className="be-role-setter"
            checkTrigger="none"
            isInternal={formData.internal}
            disabled={drawer.isEdit}
            required
          />

          {/* 锁定缺省BE角色 */}
          <CustomField
            name="isLockBeRole"
            label={formatMessage('i18n_lockDefaultBERole')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_locked')}
            unCheckedChildren={formatMessage('i18n_unlocked')}
            disabled={formData.internal}
          />

          {/* 锁定缺省广告服务器 */}
          <CustomField
            name="isLockAdS"
            label={formatMessage('i18n_lockDefaultAdServer')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_locked')}
            unCheckedChildren={formatMessage('i18n_unlocked')}
            disabled={formData.internal}
          />

          {/* 产品列表 */}
          {!__HAA__ && (
            <CustomField
              name="products"
              label={formatMessage('i18n_productList')}
              accepter={CheckPicker}
              required
              disabled={formData.internal}
              supportAll={formData.internal}
              url={API_PRODUCT}
              value={formData.internal ? [] : undefined}
            />
          )}

          {/* 合作方 */}
          <CustomField
            name="collaborators"
            label={formatMessage('i18n_collaborator')}
            accepter={BERoleMultiPicker}
          />

          {/* 组织信息-地址 */}
          <CustomField
            name="address"
            label={formatMessage('i18n_organizationInfo')}
            accepter={InfoInput}
            title={formatMessage('i18n_address')}
            checkTrigger="blur"
          />

          {/* 组织信息-邮编 */}
          <CustomField
            name="postalCode"
            accepter={InfoInput}
            title={formatMessage('i18n_postalCode')}
            checkTrigger="blur"
          />

          {/* 组织信息-电话 */}
          <CustomField
            name="Tel"
            accepter={InfoInput}
            title={formatMessage('i18n_Tel')}
            checkTrigger="blur"
          />

          {/* 组织信息-官网 */}
          <CustomField
            name="website"
            accepter={InfoInput}
            title={formatMessage('i18n_website')}
            checkTrigger="blur"
          />

          {/* 备注 */}
          <CustomField name="note" label={formatMessage('i18n_remark')} />

          {/* 注释 */}
          {drawer.isEdit && (
            <CustomField
              name="annotation"
              label={formatMessage('i18n_annotation')}
              rows={2}
              componentClass="textarea"
            />
          )}
        </Form>
      </Drawer>
    );
  }
);
