import * as React from 'react';
import { ErrorMessage } from 'rsuite';
import { ErrorMessageProps } from 'rsuite/lib/ErrorMessage';
import _ from 'lodash';

export const ErrMsg = React.memo(
  ({ style, error, placement = 'bottomStart' }: ErrorMessageProps) => (
    <div className="rs-form-control-wrapper cus-error" style={style}>
      <ErrorMessage
        className="rs-form-control-message-wrapper"
        placement={placement}
        show={!_.isEmpty(error)}
      >
        {error}
      </ErrorMessage>
    </div>
  )
);

// 上面返回的就是一个reletive的组件，它会占地方
// 直接用在CustomField 组件里面需要在外面包一个空的绝对定位的div，不占地方给ErrorMsg做容器
export const ErrorMessageWithAbsContainer = (props: ErrorMessageProps) => {
  return (
    <div style={{ position: 'absolute' }}>
      <ErrMsg {...props} />
    </div>
  );
};

export default ErrorMessageWithAbsContainer;
