import React from 'react';

import { useFmtMsgFori18n } from '@/utils/formatMessage';
import SelectPicker from '@/components/Picker/SelectPicker';

import { FilterCommonProps } from './common';

export default ({
  data,
  loading,
  filterLabel,
  value,
  disabled,
  onChange,
  labelKey,
  valueKey,
  mapLabel
}: FilterCommonProps) => {
  const { formatMessage } = useFmtMsgFori18n();
  return (
    <SelectPicker
      appearance="subtle"
      data={data}
      loading={loading}
      searchable
      cleanable
      value={value}
      placeholder={`${formatMessage('i18n_justSelect')}${filterLabel}`}
      disabled={disabled}
      onSelect={onChange}
      labelKey={labelKey}
      valueKey={valueKey}
      mapLabel={mapLabel}
    />
  );
};
