import React, { memo } from 'react';
import _ from 'lodash';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { FilterParam, filterParamKeys } from '@/views/common/conversionReport/constants';
import { BasicReportProps } from '@/constants/config';

export default memo(
  ({ filterParams, handleChangeCommonParam, handleSearchWord }: BasicReportProps<FilterParam>) => {
    const { formatMessage } = useFmtMsgFori18n();
    return (
      <>
        <div className="table-wrap-table-toolbar">
          <SecondaryPicker
            key="secondary"
            data={[null, 'daily', 'weekly']}
            value={filterParams[filterParamKeys.SEC]}
            onChange={(value: string) => handleChangeCommonParam({ [filterParamKeys.SEC]: value })}
            cleanable={false}
          />
          <SearchInput
            key="search"
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
          ,
        </div>
      </>
    );
  }
);
