import { useState, useMemo, useCallback, useEffect } from 'react';
import _ from 'lodash';
import { useLocation } from 'react-router';

import {
  getLastNDaysRangeBeforeCertainDate,
  getToday,
  getYesterday,
  YYYYMMDD,
  getOverlapBetweenTwoRange,
  getEarlierDate
} from '@/utils/date';
import { getUserProfile } from '@/utils/location';
import { TIME } from '@/constants/config';
import useFetch, { FETCH_METHOD, UseFetchResult } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';

import { getDateStorageByKey, setDateStorageByKey, DateKeys } from './utils';
import { DateCtxProps } from './types';

const defaultDaysSpan = 6;
export const useModuleDate = (
  sessionStorageKey: DateKeys,
  requestUrl?: string | APIDictObject,
  requestParam?: BaseObject<any>
): DateCtxProps => {
  const location = useLocation();
  const { internal } = getUserProfile();
  // 最大的可选范围，不区分内外部用户 都是今天
  const maxDatePreset = YYYYMMDD(getToday());

  // 默认的范围都是昨天往前推7天
  const defaultDateRangeVal = useMemo(
    () =>
      internal
        ? getLastNDaysRangeBeforeCertainDate(getYesterday(), defaultDaysSpan)
        : getLastNDaysRangeBeforeCertainDate(getYesterday(), defaultDaysSpan),
    [internal]
  );

  let sessionResult = getDateStorageByKey(sessionStorageKey);
  if (sessionResult) {
    sessionResult = [
      getEarlierDate(sessionResult[0], maxDatePreset),
      getEarlierDate(sessionResult[1], maxDatePreset)
    ];
  }

  // 如果session中有数据，那就取session中的
  const [dateRangeVal, setDateRangeVal] = useState<Array<string>>(
    sessionResult || defaultDateRangeVal
  );
  const dateRangeValTimeFormat: TIME = useMemo(
    () => ({
      start: dateRangeVal[0],
      end: dateRangeVal[1],
      maxDate: maxDatePreset
    }),
    [dateRangeVal]
  );

  const setDateRangeValByStringArray = useCallback(
    (val: Array<string>) => {
      setDateRangeVal(val);

      // 存到storage里面
      setDateStorageByKey(sessionStorageKey, val);
    },
    [sessionStorageKey]
  );

  const setDateRangeValByDateArray = useCallback(
    val => {
      const stringArr = [YYYYMMDD(val[0]), YYYYMMDD(val[1])];
      setDateRangeValByStringArray(stringArr);
    },
    [setDateRangeValByStringArray]
  );

  const [validDateLoaded, setValidDateLoaded] = useState(false);
  const { onFetch: onFetchReportDate, response: reportDateResp } = useFetch<CommonResponse>(
    FETCH_METHOD.GET,
    requestUrl,
    { params: requestParam }
  );

  const reportDateFn = useCallback(
    (options?: UseFetchResult<any>['onFetch']) => {
      return onFetchReportDate(options as any).then((resp: any) => {
        const startDate = _.get(resp, 'data.start_date', '');
        const endDate = _.get(resp, 'data.end_date', '');
        const validStartDate = getEarlierDate(startDate, maxDatePreset);
        const validEndDate = getEarlierDate(endDate, maxDatePreset);
        setDateRangeVal(prevDateRange => {
          // 这样的写法 永远取得最新的，避免拿到旧的dateRangeVal
          const result = getOverlapBetweenTwoRange(
            validStartDate,
            validEndDate,
            getEarlierDate(prevDateRange[0], maxDatePreset),
            getEarlierDate(prevDateRange[1], maxDatePreset)
          );
          if (result) {
            // 与现在的（之前系统选中后保存在storage里面的时间段）有交集，取交集
            const dateRangeResult = [YYYYMMDD(result[0]), YYYYMMDD(result[1])];
            setDateStorageByKey(sessionStorageKey, dateRangeResult);
            return dateRangeResult;
          }

          // 日期没有交集，默认策略(今天/昨天往前推7天)，取交集
          const result2 = getOverlapBetweenTwoRange(
            validStartDate,
            validEndDate,
            getEarlierDate(defaultDateRangeVal[0], maxDatePreset),
            getEarlierDate(defaultDateRangeVal[1], maxDatePreset)
          );
          if (result2) {
            const dateRangeResult = [YYYYMMDD(result2[0]), YYYYMMDD(result2[1])];
            setDateStorageByKey(sessionStorageKey, dateRangeResult);
            return dateRangeResult;
          }

          // 还不行？用后端返回的（可选的结束时间）时间往前推7天，// 因为结束时间
          const t = getLastNDaysRangeBeforeCertainDate(validEndDate, 7);
          const result3 = getOverlapBetweenTwoRange(
            validStartDate,
            validEndDate,
            getEarlierDate(t[0], maxDatePreset),
            getEarlierDate(t[1], maxDatePreset)
          );

          if (result3) {
            const dateRangeResult = [YYYYMMDD(result3[0]), YYYYMMDD(result3[1])];
            setDateStorageByKey(sessionStorageKey, dateRangeResult);
            return dateRangeResult;
          }

          return [validStartDate, validEndDate];
        });
        setValidDateLoaded(true);
      });
    },
    [defaultDateRangeVal, sessionStorageKey]
  ); // FIXME: onFetch 返回的每次都是变得，所以这里加不得

  // date_date 只在campaign/audience用到
  const maxDateFromResp = useMemo(() => _.get(reportDateResp, 'data.end_date', ''), [
    reportDateResp
  ]);
  const maxDateFromRespBeforePreset = useMemo(
    () => getEarlierDate(maxDateFromResp, maxDatePreset),
    [maxDateFromResp, maxDatePreset]
  );
  const minDateFromResp = useMemo(() => _.get(reportDateResp, 'data.start_date', ''), [
    reportDateResp
  ]);

  useEffect(() => {
    // 这里监听pathname改变，如果改变了，重置成初始状态
    setValidDateLoaded(false);
  }, [location.pathname]);

  return {
    dateRangeVal, // 原始的数据类型，字符的array
    dateRangeValTimeFormat, // 传给报表页面，需要start、end这种形式，所以格式化一下
    setDateRangeValByStringArray,
    setDateRangeValByDateArray,
    reportDateFn,
    validDateLoaded, // 日期已经和接口请求的交叉计算完了，可以直接设置了
    reportDateResp, // 返回值传下去，这样的话可以取到最大的值等等
    maxDateFromResp, //接口返回的最大日期
    maxDateFromRespBeforePreset, //交集之后的最大日期
    maxDatePreset, //默认最大日期 今天
    minDateFromResp //交集之后的最小日期
  };
};
