import * as React from 'react';
import { CheckboxGroup, Checkbox } from 'rsuite';
import { CheckboxGroupProps } from 'rsuite/lib/CheckboxGroup';

// ToDo: 如何通过typescript限制data的具体类型
interface Props extends CheckboxGroupProps {
  labelKey?: string;
  valueKey?: string;
  data?: any[];
  disabled?: boolean | any[]; // 可为boolean，控制所有成员的禁用；也可为数组，指定相应value的成员禁用
  itemWidth?: number | string; // 指定成员宽度
}

const CustomCheckboxGroup = React.memo(
  ({
    value = [],
    itemWidth,
    data,
    labelKey = 'name',
    valueKey = 'id',
    disabled = false,
    children,
    ...props
  }: Props) => {
    return (
      <CheckboxGroup {...props} value={value} inline>
        {children ||
          data.map(i => (
            <Checkbox
              value={i[valueKey]}
              key={i[labelKey]}
              disabled={typeof disabled === 'boolean' ? disabled : disabled.includes(i[valueKey])}
              style={{
                width: itemWidth || '100%',
                display: itemWidth && 'inline-block'
              }}
            >
              {i[labelKey]}
            </Checkbox>
          ))}
      </CheckboxGroup>
    );
  }
);

CustomCheckboxGroup.displayName = 'CheckboxGroup';
export default CustomCheckboxGroup;
