import React, { memo, useCallback, useContext, useEffect, useMemo } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';
import { useLocation } from 'react-router-dom';

import Card from '@/components/Card';
import { Toolbar } from '@/components/Table';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import AdvBrandPicker from '@/components/Picker/logic/AdvBrandPicker';
import BEPicker from '@/components/Picker/logic/BEPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { useMainPage } from '@/components/hooks/useMainPage';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Chart from '@/components/Chart/ReportCustomChart';
import { useUserId } from '@/components/hooks/useCertainParams';
import * as APIs from '@/constants/APIs';

import {
  defaultFilter,
  filterParamKeys,
  statusData,
  FilterParam,
  defaultBrandKey,
  defaultAdvertiserKey
} from './constants';
import Table from './Table';
import { UserModuleDateCtx } from '../../context';

const filterParamFormatter = (filterParam: FilterParam) => {
  const originBE = _.get(filterParam, filterParamKeys.BE);
  const originAdvertiser = _.get(filterParam, filterParamKeys.ADVERTISER);
  return {
    ...filterParam,
    [filterParamKeys.BE]: originBE.toString(),
    [filterParamKeys.ADVERTISER]: _.get(originAdvertiser, 'length', 0)
      ? JSON.stringify(originAdvertiser)
      : ''
  };
};

export default memo(() => {
  const { state } = useLocation();
  const defaultBrand = _.get(state, defaultBrandKey);
  const defaultAdvertiser = _.get(state, defaultAdvertiserKey);
  const {
    commonParam,
    fmtCommonParam,
    handleChangeCommonParam,
    handleDebouncedChangeCommonParam,
    customParamResult
  } = useMainPage({
    defaultCommonParam: {
      ...defaultFilter,
      [filterParamKeys.ADVERTISER]: defaultAdvertiser
        ? [{ id: defaultAdvertiser, children: defaultBrand ? [defaultBrand] : [] }]
        : []
    },
    commonParamFormatter: filterParamFormatter,
    customConfig: [
      {
        isTable: true
      }
    ]
  });
  const userId = useUserId();
  const postParams = useMemo(
    () => ({
      id: userId,
      hierarchy: 'overview',
      reportName: 'campaigns'
    }),
    [userId]
  );
  const [tableResult] = customParamResult;
  const { fmtParam, handleChangePagination } = tableResult;
  const { formatMessage } = useFmtMsgFori18n();
  const {
    dateRangeVal,
    dateRangeValTimeFormat,
    setDateRangeValByDateArray,
    maxDatePreset
  } = useContext(UserModuleDateCtx);

  const handleBEChange = useCallback(
    (beVal: Array<any>) => {
      handleChangeCommonParam({
        [filterParamKeys.BE]: beVal
      });
    },
    [handleChangeCommonParam]
  );

  // 改变filter
  const handleChangeStatus = useCallback(
    value => {
      handleChangeCommonParam({
        status: value
      });
    },
    [handleChangeCommonParam]
  );

  const handleAdvBrandChange = useCallback(
    (advBrandVal: Array<any>) => {
      handleChangeCommonParam({
        [filterParamKeys.ADVERTISER]: advBrandVal
      });
    },
    [handleChangeCommonParam]
  );

  const handleSearchWord = useCallback(
    (word: string) => {
      handleDebouncedChangeCommonParam({
        sSearch: word
      });
    },
    [handleDebouncedChangeCommonParam]
  );
  //渲染完成之后清空history中的state
  useEffect(() => {
    history.replaceState({}, '');
  }, []);
  return (
    <>
      <Panel className="page-toolbar">
        <DateRangePicker
          value={dateRangeVal}
          maxDate={maxDatePreset}
          cleanable={false}
          appearance="subtle"
          onChange={setDateRangeValByDateArray}
        />
      </Panel>
      <Card>
        <Toolbar style={{ paddingTop: 20 }}>
          <BEPicker
            key="be"
            chosenBE={_.get(commonParam, filterParamKeys.BE, [])}
            beChangeHandler={handleBEChange}
          />
          <AdvBrandPicker
            key="advBrand"
            className="cus-picker"
            chosenAdvBrand={_.get(commonParam, filterParamKeys.ADVERTISER, [])}
            onChange={handleAdvBrandChange}
            reqParam={{ beId: commonParam[filterParamKeys.BE] }}
            deps={commonParam[filterParamKeys.BE]}
          />
          <SelectPicker
            key="status"
            prepend={formatMessage('i18n_status')}
            labelKey="label"
            valueKey="value"
            data={statusData}
            value={commonParam.status}
            onChange={handleChangeStatus}
            style={{
              width: 200
            }}
          />
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
        </Toolbar>
        <Chart
          dateParams={dateRangeValTimeFormat}
          filterParams={fmtCommonParam}
          api={APIs.NEW_API_REPORT}
          postParams={postParams}
          collapsible
          customType={1}
        />
        <Table
          dateParams={dateRangeValTimeFormat}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          postParams={postParams}
        />
      </Card>
    </>
  );
});
