import React from 'react';
import _ from 'lodash';
import PickerGroup from '@/components/Picker/PickerGroup';
import SelectPicker from '@/components/Picker/SelectPicker';
import CheckPicker from '@/components/Picker/CheckPicker';
import { API_BE, API_TEAM } from '@/constants/APIs';

interface Props {
  values?: Record<string, any>;
  errors?: Record<string, string>;
  onChange?: Function;
}

const beParam = {
  orderColumn: 'name',
  orderType: 'asc',
  status: 1
};

export const BEKey = 'beId';
export const TeamKey = 'teams';
const BETeamGroupPicker = React.memo(({ values, errors, onChange }: Props) => {
  return (
    <PickerGroup values={values} errors={errors} onChange={onChange}>
      <SelectPicker name={BEKey} prepend="BE" url={API_BE} params={beParam} searchable />
      <CheckPicker
        name={TeamKey}
        prepend="Team"
        url={API_TEAM}
        params={{ beId: values[BEKey] }}
        deps={[values[BEKey]]}
        disabled={!_.isNumber(values[BEKey])}
        shouldFilter
        searchable
        className="hac-team-picker"
      />
    </PickerGroup>
  );
});

export default BETeamGroupPicker;
