import React, { memo } from 'react';
import _ from 'lodash';
import { BasicReportProps } from '@/constants/config';
import * as APIs from '@/constants/APIs';
import useChart from '@/components/hooks/useChart';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';

export default memo<BasicReportProps>(function({ dateParams, filterParams, id }) {
  const { data, loading } = useChart({
    dateParams,
    filterParams,
    api: APIs.API_GLOBALS,
    postParams: {
      id
    }
  });

  const config = {
    type: CHART_KEY.LINE,
    data
  };

  return <Chart config={config} loading={loading} collapsible />;
});
