import React, { memo } from 'react';
import _ from 'lodash';

import { TIME } from '@/constants/config';
import * as APIs from '@/constants/APIs';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useChart from '@/components/hooks/useChart';
import Chart from '@/components/Chart';
import { CHART_KEY } from '@/components/Chart/config';
import { numberFormat } from '@/utils/format';

import { prefixClassName } from '../constants';

interface ChartDataDetail {
  max: number | string;
  name: string;
  data: object[];
}
const mockData: ChartDataDetail[] = [{ max: 0, name: 'impressions', data: [] }];

interface Props {
  dateParams?: TIME;
  id: number | string;
  tag?: string;
  mDimension?: string;
  labelKey?: string;
  valueKey?: string;
}

export default memo<Props>(function({ dateParams, id, tag, mDimension }) {
  const { data, loading } = useChart({
    dateParams,
    api: APIs.API_DASHBOARD,
    postParams: {
      id,
      hierarchy: 'campaign',
      tag,
      mDimension: 'province'
    }
  });
  const { formatMessage } = useFmtMsgFori18n();
  const newdata = _.get(data, 'data', mockData).filter(
    (i: ChartDataDetail) => i.name === mDimension
  )[0];
  // 只显示 Active(impressions|clicks) 数据
  const chartData = {
    ...newdata,
    name: formatMessage(`i18n_${newdata.name}`)
  };

  const config = {
    type: CHART_KEY.MAP,
    data: chartData
  };
  const option = {
    tooltip: {
      trigger: 'item',
      formatter: ({ data: toolData }) => {
        if (toolData) {
          return `${toolData.name}: ${numberFormat(toolData.value)}`;
        }
      }
    },
    visualMap: {
      max: chartData.max,
      show: true
    }
  };

  return (
    <>
      <div className={prefixClassName('piechart')} style={{ height: 300, position: 'relative' }}>
        <Chart config={config} loading={loading} option={option} pure />
      </div>
    </>
  );
});
