import React, { useCallback, useEffect, useMemo, useState } from 'react';
import HACNotificationContainer from '@hypers/react-hac-notification-container';
import _ from 'lodash';
import { Alert, Button, ButtonGroup, ButtonToolbar } from 'rsuite';

import SelectPicker from '@/components/Picker/SelectPicker';
import CustomField from '@/components/Form/CustomField';
import MultiField from '@/components/Form/MultiField';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import Form from '@/components/Form';
import Toggle from '@/components/Form/Toggle';
import fetch, { FETCH_METHOD } from '@/utils/request/fetch';
import getRandomNum from '@/utils/random';
import { getUserProfile } from '@/utils/location';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { API_PERMISSION, API_PERMISSION_ROLE, API_PROFILE } from '@/constants/APIs';
import { HacModel } from '@/constants/form/models';
import { __DEBUG__ } from '@/constants/config';
import BETeamGroupPicker, { TeamKey, BEKey } from './BETeamGroupPicker';
import { userRoleData, defaultFormVal } from './constants';
import RESPONSE_STATUS from '@/constants/responseStatus';

export default () => {
  const { formatMessage } = useFmtMsgFori18n();
  const userRoleDataIntl = usePickerDataFromObj(userRoleData);
  const {
    formData,
    formErrors,
    formRef,
    handleFormChange,
    setFormErrors,
    checkForm
  } = useDrawerForm({
    defaultFormData: defaultFormVal,
    api: API_PERMISSION, // useless
    model: HacModel as any
  });

  const [randomAuth] = useState(getRandomNum());
  const [randomUnAuth] = useState(getRandomNum());
  const [originUserInfo, setOriginUserInfo] = useState({});
  const initialRequest = useCallback(
    (email: string) => {
      fetch(API_PERMISSION, {
        params: {
          email
        }
      })
        .then((res: any) => {
          const data = _.get(res, 'data', {}) || {};
          setOriginUserInfo(data);
          handleFormChange((prev: any) => ({
            ...prev,
            ...data
          }));
        })
        .catch(() => {
          Alert.error('原始数据接口异常');
        });
    },
    [handleFormChange]
  );

  const triggerSubmit = useCallback(() => {
    console.log('确定提交');
    if (!formData.email) {
      Alert.error('请输入 email');
      return;
    }

    checkForm(formData)
      .then(errors => {
        if (!_.every(_.values(errors), _.isEmpty)) {
          setFormErrors(errors);
          return true;
        } else {
          return false;
        }
      })
      .then(hasError => {
        if (!hasError) {
          return fetch(API_PERMISSION, {
            method: FETCH_METHOD.POST,
            data: {
              ...formData,
              // apiOnly: formData.apiOnly ? 1 : 0,
              teams: _.join(formData.teams, ',')
              // 模拟后端报错的情况422
              // beId: 's',
              // role: '123',
              // teams: '123asd'
            },
            mutex: randomAuth
          });
        }
      })
      .then(rsp => {
        const status = _.get(rsp, 'status');
        if (status === RESPONSE_STATUS.SUCCESS) {
          HACNotificationContainer.notificationHACSubmitSuccess();
        } else if (status === RESPONSE_STATUS.FIELD_INVALID) {
          setFormErrors(_.get(rsp, 'errors', {}));
        }
      });
  }, [formData, randomAuth, setFormErrors, checkForm]);

  const triggerUnAuth = useCallback(() => {
    console.log('取消授权');
    fetch(API_PROFILE, {
      method: FETCH_METHOD.POST,
      data: {
        email: formData.email,
        status: 'PAUSED'
      },
      mutex: randomUnAuth
    }).then((res: any) => {
      HACNotificationContainer.notificationHACDeauthorizeSuccess();
    });
  }, [formData.email, randomUnAuth]);

  const triggerReset = useCallback(() => {
    console.log('放弃修改');
    handleFormChange(originUserInfo);
  }, [handleFormChange, originUserInfo]);

  const injectInfoToFormData = useCallback(
    (info: any) => {
      handleFormChange((prev: any) => ({
        ...prev,
        ...info
      }));
    },
    [handleFormChange]
  );

  const handleHacPageLoadComplete = useCallback(
    (userData: any) => {
      console.log('页面加载成功，用户信息', userData);
      const email = _.get(userData, 'email', '');
      const name = _.get(userData, 'name', '');
      const groupName = _.get(userData, 'group.name', '');
      initialRequest(email);
      injectInfoToFormData({
        email,
        name,
        groupName
      });
    },
    [initialRequest, injectInfoToFormData]
  );

  const handleSubmit = useCallback(() => {
    triggerSubmit();
  }, [triggerSubmit]);
  const handleCancel = useCallback(() => {
    triggerReset();
  }, [triggerReset]);
  const handleDeauthorize = useCallback(() => {
    triggerUnAuth();
  }, [triggerUnAuth]);

  const BEVal = _.get(formData, BEKey);
  const BEReadyDeps = useMemo(() => {
    return [BEVal];
  }, [BEVal]);

  useEffect(() => {
    if (__DEBUG__) {
      const { email } = getUserProfile();
      injectInfoToFormData({
        email,
        groupName: 'Aurora',
        name: 'aurora'
      });
      initialRequest(email);
    }
  }, []);

  return (
    <div className="aurora-hac">
      <HACNotificationContainer
        onHacPageLoadComplete={handleHacPageLoadComplete}
        onSubmit={handleSubmit}
        onCancel={handleCancel}
        onDeauthorize={handleDeauthorize}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          onChange={handleFormChange as any}
          formRef={formRef}
          checkForm={checkForm}
          models={HacModel}
          setFormErrors={setFormErrors}
        >
          {/* HAA暂时隐藏用户角色选项 */}
          {/* <CustomField
            name="role"
            label={formatMessage('i18n_userRole')}
            accepter={SelectPicker}
            data={userRoleDataIntl}
            required
          /> */}
          <MultiField
            names={[BEKey, TeamKey]}
            label={formatMessage('i18n_selBETeam')}
            accepter={BETeamGroupPicker}
            required
          />
          <CustomField
            name="permissionRole"
            labelKey="name"
            valueKey="name"
            accepter={SelectPicker}
            label={formatMessage('i18n_businessRole')}
            disabled={!_.isFinite(BEVal)}
            url={BEReadyDeps ? API_PERMISSION_ROLE : null}
            params={{ be: BEVal }}
            deps={BEReadyDeps}
            shouldFilter
            required
          />
          {/* HAA暂时隐藏API Only选项 */}
          {/* <CustomField
            accepter={Toggle}
            label={formatMessage('i18n_apiOnly')}
            name='apiOnly'
            checkedChildren={formatMessage('i18n_yes')}
            unCheckedChildren={formatMessage('i18n_no')}
          /> */}
        </Form>
      </HACNotificationContainer>
      {__DEBUG__ && (
        <ButtonToolbar>
          <ButtonGroup>
            <Button onClick={triggerUnAuth}>取消授权</Button>
            <Button onClick={triggerSubmit}>确认授权</Button>
            <Button onClick={triggerReset}>放弃修改</Button>
          </ButtonGroup>
        </ButtonToolbar>
      )}
    </div>
  );
};
