import React from 'react';
import { SelectPicker, InputGroup, Input, Icon } from 'rsuite';
import { InputProps } from 'rsuite/lib/Input';
import { SelectPickerProps } from 'rsuite/lib/SelectPicker';

const styles = {
  wrapper: {
    display: 'inline-block',
    verticalAlign: 'bottom'
  }
};

interface FilterItem {
  label: string;
  value: any;
}

interface SearchInputProps {
  filterProps?: SelectPickerProps;
  onChange: (word: string) => void;
  width?: number;
  wrapperClassName?: string;
}

export default React.memo(function SearchInput({
  filterProps,
  onChange,
  width = 200,
  wrapperClassName,
  ...props
}: SearchInputProps & InputProps) {
  return (
    <div style={styles.wrapper} className={wrapperClassName}>
      {filterProps && <SelectPicker appearance="subtle" {...filterProps} />}
      <InputGroup style={{ display: 'inline-block', width, verticalAlign: 'middle' }} inside>
        <Input onChange={onChange} {...props} maxLength={200} />
        <InputGroup.Addon>
          <Icon icon="search" />
        </InputGroup.Addon>
      </InputGroup>
    </div>
  );
});
