import _ from 'lodash';

export enum TAB_KEY {
  SYSTEM = 'SYSTEM',
  USER = 'USER'
}

export const isForVerifyData = [
  {
    name: 'i18n_both',
    id: ''
  },
  {
    name: 'i18n_yes',
    id: 1
  },
  {
    name: 'i18n_no',
    id: 0
  }
];

export const navData = [
  {
    eventKey: TAB_KEY.SYSTEM,
    label: 'i18n_defaultAttributes'
  },
  {
    eventKey: TAB_KEY.USER,
    label: 'i18n_customAttributes'
  }
];

export const typeData = [{ id: 0, name: 'i18n_enumerated' }, { id: 1, name: 'i18n_numeric' }];

export const defaultFormData = {
  type: 0,
  isDefault: 0,
  isForVerify: 0
};

export enum filterParamKeys {
  IS_FOR_VERIFY = 'isForVerify'
}

export interface FilterParam {
  isForVerify: string | number;
}

export const defaultFilter: FilterParam = {
  isForVerify: isForVerifyData[0].id
};

export const utilsFn = {
  checkIsUserTab(tabs: TAB_KEY) {
    return tabs === TAB_KEY.USER;
  },
  getIsForVerify(filterParam: FilterParam) {
    return _.get(filterParam, filterParamKeys.IS_FOR_VERIFY, defaultFilter.isForVerify);
  }
};
