import React, { memo, useCallback, useMemo } from 'react';
import _ from 'lodash';
import { Button } from 'rsuite';

import * as APIs from '@/constants/APIs';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { NumberCell, DateCell, TextCell } from '@/components/Table/Cells';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { secondaryData, BasicReportProps } from '@/constants/config';

export default memo(
  ({
    dateParams,
    filterParams,
    tableParams,
    postParams,
    handleSearchWord,
    setTableParams
  }: BasicReportProps) => {
    const { formatMessage } = useFmtMsgFori18n();

    const { tableData, onExport, onChange } = useTable({
      dateParams,
      tableParams,
      setTableParams,
      api: APIs.API_TAB_REPORT,
      postParams
    });

    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleExportClicked = useCallback(() => {
      onExport && onExport(postParams, APIs.API_XLSX);
    }, [onExport, postParams]);

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Button
          key="exportTable"
          className="pull-right"
          appearance="default"
          onClick={handleExportClicked}
        >
          {formatMessage('i18n_exportTable')}
        </Button>
      ];
    };

    // 渲染表列
    const renderCol = () => {
      const sec = _.get(
        _.filter(secondaryData, item => item.value === filterParams.secondary)[0],
        'label'
      );
      return [
        <Column width={130} sortable fixed resizable key="id">
          <HeaderCell>{`${formatMessage('i18n_placement')}ID`}</HeaderCell>
          <Cell dataKey="id" />
        </Column>,
        <Column width={180} sortable fixed resizable key="placement">
          <HeaderCell>{formatMessage('i18n_placement')}</HeaderCell>
          <TextCell dataKey="name" maxLine={1} />
        </Column>,
        <Column width={150} sortable fixed key="siteid">
          <HeaderCell>{`${formatMessage('i18n_site')}ID`}</HeaderCell>
          <Cell dataKey="siteId" />
        </Column>,
        <Column width={150} sortable resizable fixed key="site">
          <HeaderCell>{formatMessage('i18n_site')}</HeaderCell>
          <TextCell dataKey="siteName" maxLine={1} />
        </Column>,
        <Column width={150} sortable resizable key="segmentation">
          <HeaderCell>{formatMessage('i18n_platform')}</HeaderCell>
          <Cell dataKey="segmentationId" />
        </Column>,
        filterParams.secondary && (
          <Column
            width={filterParams.secondary === 'weekly' ? 250 : 100}
            align="right"
            key="secondary"
            sortable
            resizable
          >
            <HeaderCell>
              {filterParams.secondary === 'hourly'
                ? formatMessage('i18n_dataDay')
                : formatMessage(`${sec}`)}
            </HeaderCell>
            <DateCell dataKey="secondary" />
          </Column>
        ),
        // 总广告计数
        <Column width={150} align="right" resizable sortable key="impressions">
          <HeaderCell>{formatMessage('i18n_totalImp')}</HeaderCell>
          <NumberCell dataKey="impressions" />
        </Column>,
        // 可测量曝光
        <Column width={180} align="right" resizable sortable key="measurable">
          <HeaderCell>{formatMessage('i18n_measurableImp')}</HeaderCell>
          <NumberCell dataKey="measured" />
        </Column>,
        // 可见曝光
        <Column width={180} align="right" resizable sortable key="viewability">
          <HeaderCell>{formatMessage('i18n_viewability')}</HeaderCell>
          <NumberCell dataKey="viewable" />
        </Column>,
        // 不可见曝光
        <Column width={200} align="right" resizable sortable key="nonViewable">
          <HeaderCell>{formatMessage('i18n_nonviewableImp')}</HeaderCell>
          <NumberCell dataKey="nonViewable" />
        </Column>,
        // 不可测量广告计数
        <Column width={200} align="right" resizable sortable key="nonmeasurable">
          <HeaderCell>{formatMessage('i18n_nonmeasurableImp')}</HeaderCell>
          <NumberCell dataKey="undetermined" />
        </Column>,
        // 可测量率
        <Column width={150} align="right" resizable sortable key="measuredRate">
          <HeaderCell>{formatMessage('i18n_measuredRate')}</HeaderCell>
          <Cell dataKey="measuredRate" />
        </Column>,
        // 可见率
        <Column width={150} align="right" resizable sortable key="viewableRate">
          <HeaderCell>{formatMessage('i18n_viewableRate')}</HeaderCell>
          <Cell dataKey="viewableRate" />
        </Column>,
        // 可见曝光分布
        <Column width={200} align="right" resizable sortable key="viewableDistribution">
          <HeaderCell>{formatMessage('i18n_viewableDistribution')}</HeaderCell>
          <Cell dataKey="viewableDistribution" />
        </Column>,
        // 不可见曝光分布
        <Column width={200} align="right" resizable sortable key="nonViewableDistribution">
          <HeaderCell>{formatMessage('i18n_nonViewableDistribution')}</HeaderCell>
          <Cell dataKey="nonViewableDistribution" />
        </Column>,
        // 不可测量曝光分布
        <Column width={220} align="right" resizable sortable key="undeterminedDistribution">
          <HeaderCell>{formatMessage('i18n_undeterminedDistribution')}</HeaderCell>
          <Cell dataKey="undeterminedDistribution" />
        </Column>
      ];
    };

    return (
      <Table {...tableDataMemo} renderToolbar={renderToolbar} onChange={onChange}>
        {renderCol()}
      </Table>
    );
  }
);
