import _ from 'lodash';
import React, { useCallback } from 'react';
import Cell, { BaseCellProps } from './FloatActionCell';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

const VerifyCell = ({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const { formatMessage } = useFmtMsgFori18n();
  const text = _.get(rowData, dataKey) ? formatMessage('i18n_yes') : formatMessage('i18n_no');
  const renderContent = useCallback(() => {
    return <span>{text}</span>;
  }, [text]);
  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderContent={renderContent}
      renderTitle={renderContent}
      {...props}
    />
  );
};

export default React.memo(VerifyCell);
