export const defaultFormData = {};

export enum filterParamKeys {
  CAMPAIGN = 'campaignId',
  CROSS_DIMENSION = 'crossDimensions',
  DATE = 'date',
  TIME_DIMENSION = 'secondary'
}

export interface CommonParam {
  campaignId: Array<number>;
  date: Date;
}

export interface CustomParam {
  crossDimensions: Array<any>;
  secondary: string;
}

export type FilterParam = CommonParam & CustomParam;

export const defaultCommonParam: CommonParam = {
  campaignId: [],
  date: getYesterday()
};

export const defaultCustomParam: CustomParam = {
  crossDimensions: [CROSS_DIM_TYPE[0].id],
  secondary: secondaryData[0].id
};

export const defaultFilter: FilterParam = {
  ...defaultCustomParam,
  ...defaultCommonParam
};

export const utilsFn = {
  getCampainIdList(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.CAMPAIGN, defaultFilter.campaignId);
  },
  getCrossDimensions(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.CROSS_DIMENSION, defaultFilter.crossDimensions);
  },
  getTimeDimensions(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.TIME_DIMENSION, defaultFilter.secondary);
  },
  getDate(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.DATE, defaultFilter.date);
  }
};
