import React, { useState, useCallback, useEffect } from 'react';
import _ from 'lodash';
import PickerGroup from '../PickerGroup';
import SelectPicker from '../SelectPicker';
import { beRoleData } from '@/constants/config';
import { API_BE } from '@/constants/APIs';
import { UseFetchProps } from '@/components/Picker/PickerHooks/useFetchForPicker';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

interface Props {
  values?: Record<string, any>;
  errors?: Record<string, string>;
  beName?: string;
  roleName?: string;
  disabled?: boolean;
  cleanable?: boolean;
  fetchOptions?: UseFetchProps;
  onChange?: Function;
  bePickerProps?: Record<string, any>;
  rolePickerProps?: Record<string, any>;
}

const beParams = {
  status: 1,
  orderColumn: 'name',
  orderType: 'asc',
  external: 1
};

export default React.memo(
  ({
    values,
    errors,
    beName,
    roleName,
    disabled = false,
    cleanable = false,
    fetchOptions = {},
    bePickerProps,
    rolePickerProps,
    onChange
  }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();

    const [beData, setBEData] = useState<Record<string, any>[]>([]);
    const [availableRoles, setAvailableRoles] = useState<Record<string, any>[]>([]);

    // 根据主picker的data以及value，确定副picker的data
    useEffect(() => {
      const data = _.isArray(fetchOptions.data) ? fetchOptions.data : beData;
      const beItem = _.find(data, i => i.id === values[beName]);
      if (beItem) {
        const roleList = _.get(beItem, 'role', '');
        setAvailableRoles(
          roleList.split(',').map((i: any) => {
            const { id, name, shortName } = beRoleData.find(r => r.name === _.lowerCase(i)) || {};
            return {
              id,
              name: `${_.capitalize(name)} (${shortName})`
            };
          })
        );
      } else {
        setAvailableRoles([]);
      }
    }, [fetchOptions.data, beData, values[beName]]);

    const handleFetchBESuccess = useCallback(
      data => {
        setBEData(data);
        _.isFunction(_.get(fetchOptions, 'onFetchSuccess')) && fetchOptions.onFetchSuccess(data);
      },
      [fetchOptions]
    );

    return (
      <PickerGroup
        values={values}
        errors={errors}
        onChange={onChange} // onChange会通过PickerGroup分配给以下两个picker，但是be的onChange有额外行为
      >
        <SelectPicker
          name={beName}
          prepend="BE"
          searchable
          cleanable={cleanable}
          disabled={disabled}
          url={API_BE}
          params={beParams}
          onFetchSuccess={handleFetchBESuccess}
          {..._.omit(fetchOptions, 'onFetchSuccess')}
          {...bePickerProps}
        />
        <SelectPicker
          name={roleName}
          prepend={formatMessage('i18n_role')}
          data={availableRoles}
          disabled={disabled || _.isNil(values[beName])}
          {...rolePickerProps}
        />
      </PickerGroup>
    );
  }
);
