import React, { memo, useState, useEffect, useCallback } from 'react';
import _ from 'lodash';

import { TIME } from '@/constants/config';
import { numberFormat } from '@/utils/format';
import { EmptyBlock, LoadingBlock } from '@/components/EmptyOrLoading';

import { onFetchTop } from '../actions';
import { prefixClassName } from '../constants';

const mockData = [
  {
    id: 2678,
    clicks: 0,
    uclicks: '--',
    impressions: '--',
    uimpressions: '--',
    campaignId: 300478,
    secondary: '',
    clickRate: '50.00%',
    value: '--',
    name: '字节跳动 asd- ByteDance'
  },
  {
    id: 2679,
    clicks: 0,
    uclicks: '--',
    impressions: 0,
    uimpressions: '--',
    campaignId: 300478,
    secondary: '',
    clickRate: '50.00%',
    value: 0,
    name: '字节跳动 - ByteDance'
  }
];

interface Props {
  dateParams?: TIME;
  id: string | number;
  tag?: string;
  mDimension?: string;
  labelKey?: string;
  valueKey?: string;
}

interface CampaignSummary {
  id: number;
  clicks?: number | string;
  uclicks?: number | string;
  impressions?: number | string;
  uimpressions?: number | string;
  campaignId?: number;
  secondary?: string;
  clickRate?: number | string;
  value: number | string;
  name: string;
  [key: string]: any;
}

export default memo<Props>(function ({
  dateParams,
  id,
  tag,
  mDimension,
  labelKey = 'name',
  valueKey = 'value'
}) {
  const [data, setData] = useState<Array<CampaignSummary>>();
  const [loading, setLoading] = useState(false);
  const orderParams =
    tag === 'area'
      ? {}
      : {
        orderType: 'desc',
        orderColumn: valueKey !== 'value' ? valueKey : 'clickRate'
      };
  useEffect(() => {
    setLoading(true);
    onFetchTop(
      {
        start: dateParams.start,
        end: dateParams.end,
        id,
        tag,
        mDimension: tag === 'area' ? 'province' : mDimension,
        ...orderParams
      },
      res => {
        setData(_.get(res, 'data.records', mockData));
      }
    ).finally(() => {
      setLoading(false);
    });
  }, [dateParams.start, dateParams.end, dateParams, id, tag, mDimension]);

  // 根据valuekey 排序
  const showData = tag === 'ctr' ? data : _.orderBy(data, valueKey, 'desc');
  const max = _.get(showData, [0, valueKey]);
  //判断最大值是否为0|空|--|undefined
  const handelIsEmpty = useCallback(() => {
    if (max === 0 || max === '' || max === '--' || max === undefined) {
      return true;
    }
    return false;
  }, [showData, max]);

  if (loading) {
    return <LoadingBlock />;
  }
  return (
    <>
      <div className={prefixClassName('barchart')} style={{ height: 300, position: 'relative' }}>
        {_.isEmpty(data) || handelIsEmpty() ? (
          <EmptyBlock />
        ) : (
            showData.map(i => {
              const name = i[labelKey];
              const value = i[valueKey];
              const percent = _.isString(value) && value.indexOf('%') > 0 ? value : value / max;
              return (
                <div key={name + value}>
                  <div className="barchart-label">
                    <span>{name}</span>
                    <span>{numberFormat(value)}</span>
                  </div>
                  <div
                    className="barchart-bar"
                    style={{
                      width: numberFormat(percent, {
                        style: 'percent'
                      })
                    }}
                  />
                </div>
              );
            })
          )}
      </div>
    </>
  );
});
