import React, { memo, useCallback, useEffect } from 'react';
import { Form, Alert } from 'rsuite';
import _ from 'lodash';

import Dialog, { UseModalResult } from '@/components/Confirm';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import CustomField from '@/components/Form/CustomField';
import { AnnotationModel } from '@/constants/form/models';
import { useModalForm } from '@/components/hooks/useModalForm';
import { extractErrMsgFromResp } from '@/components/hooks/useFetch';
import { YYYYMMDD } from '@/utils/date';
import DatePicker from '@/components/Picker/DatePicker';

const NOTE_KEY = 'annotation';
const DEFAULT_FORM_VAL = {
  [NOTE_KEY]: ''
};

export default memo((props: UseModalResult) => {
  const { data, resetModal, onFetch, refetchTableData, ...rest } = props;
  const { status, date, ...restData } = data;
  const { formatMessage } = useFmtMsgFori18n();
  const modalForm = useModalForm(DEFAULT_FORM_VAL, {});

  const onHide = useCallback(() => {
    resetModal && resetModal();
    modalForm.resetForm();
  }, [resetModal, modalForm]);

  const onSubmit = useCallback(() => {
    if (modalForm.formRef.current && modalForm.formRef.current.check()) {
      const reqData = {
        ...modalForm.formVal,
        ...restData,
        date: YYYYMMDD(date),
        status
      };

      onFetch({
        data: reqData
      })
        .then(rsp => {
          if (extractErrMsgFromResp(rsp)) {
            const err: any = extractErrMsgFromResp(rsp);
            if (_.isArray(err.message)) {
              Alert.error(_.join(err.message, '\n'));
            }

            modalForm.checkFormErr(err);
          } else {
            refetchTableData && refetchTableData();
          }

          onHide();
        })
        .catch(e => {
          console.log(e);
        });
    }
  }, [modalForm, onFetch, restData, date, status, onHide, refetchTableData]);

  useEffect(() => {
    modalForm.setFormVal((prev: any) => ({
      ...prev,
      date
    }));
  }, [date, modalForm]);

  return (
    <Dialog
      {...rest}
      onSubmit={onSubmit}
      onHide={onHide}
      title={`${status === 0 ? 'Campaign release' : 'Campaign hold'}`}
    >
      <>
        <Form
          fluid
          model={AnnotationModel}
          formValue={modalForm.formVal}
          onChange={modalForm.setFormVal}
          onCheck={modalForm.checkFormErr}
          formError={modalForm.formErr}
          ref={modalForm.formRef}
        >
          <CustomField
            label={formatMessage('i18n_dataDate')}
            name="date"
            accepter={DatePicker}
            disabled
          />
          <CustomField
            name={NOTE_KEY}
            label={formatMessage('i18n_annotation')}
            rows={2}
            required
            componentClass="textarea"
          />
        </Form>
      </>
    </Dialog>
  );
});
