import React, { useContext, useEffect, useMemo, useState, useCallback } from 'react';
import _ from 'lodash';
import { Icon } from 'rsuite';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { AdvCustomFilter } from '@/components/AdvancedFilter';
import { useAsyncData } from '@/components/AdvancedFilter/FilterContainer/preset/useAsyncData';
import { allFilter } from '@/components/AdvancedFilter/FilterContainer/preset/config';
import { ASYNC_FN_KEY } from '@/components/AdvancedFilter/FilterContainer/preset/enum';
import { AdvFilterValue } from '@/components/AdvancedFilter/constants';
import { FormErrorContext } from '@/components/Form/FormContext';
import { typeData } from '../constants';

interface Props {
  name?: string;
  value?: any;
  type: number;
  segmentations: string[]; // 'site' | 'placement' | 'platform' | 'demo' | 'city'
  campaigns: any[];
  onChange?: Function;
}

// 需要使用campaignIds作参数的维度
const asyncKeysWithCampaignIds = [ASYNC_FN_KEY.SITE, ASYNC_FN_KEY.PLACEMENT];

export default React.memo(({ name, value, type, segmentations, campaigns, onChange }: Props) => {
  const { formatMessage } = useFmtMsgFori18n();

  const { formErrors, setFormErrors, checkForm } = useContext(FormErrorContext);
  const handleChange = useCallback(
    (val: AdvFilterValue | Function) => {
      onChange(val);

      // 因为val可能是一个function，这一项在CustomField中设置了不随change而校验，但提交时还是会校验
      // 这里设置成当原本有错误时，会随change而校验，以及时清除无效错误信息
      if (!_.isEmpty(formErrors[name])) {
        checkForm(
          name,
          _.isFunction(val) ? val({ [name]: value })[name] : val // 如果val为function，需要先用当前value计算出新的value
        ).then(errMsg => {
          setFormErrors(prevErrors => ({ ...prevErrors, [name]: errMsg }));
        });
      }
    },
    [checkForm, formErrors, name, onChange, setFormErrors, value]
  );

  // 提取campaign ids作为参数
  const campaignIds = useMemo(() => _.map(campaigns, 'id').join(','), [campaigns]);

  // 可选的维度会根据所选类型type进行变化，因此真正有效的维度需要根据typeData进行过滤
  // 另外，"城市"维度在与后端交互时使用city，在高级过滤中使用region，此处统一为region
  const formattedSegments = useMemo(
    () =>
      segmentations
        .filter(i => {
          const typeItem = typeData.find(j => j.id === type);
          return _.includes(_.get(typeItem, 'segmentations'), i);
        })
        .join()
        .replace(/city/, 'region')
        .split(','),
    [type, segmentations]
  );

  // 利用所选维度筛选出高级过滤所需的filterData
  const filterData = useMemo(
    () => allFilter(formatMessage).filter(f => _.includes(formattedSegments, f.key)),
    [formattedSegments]
  );

  // 此处asyncData传空，因为想单独控制各个data的加载时机
  const asyncData = useAsyncData([]);

  // 记录由于campaigns改变而需要重新加载的async keys
  const [keysToRefetch, setKeysToRefetch] = useState([]);

  // 当所选campaigns改变时
  useEffect(() => {
    if (_.isEmpty(campaignIds)) {
      // 选择为空，不处理
      return;
    }

    asyncKeysWithCampaignIds.forEach(key => {
      // 仅有需要使用campaignIds的维度需要处理
      if (formattedSegments.includes(key)) {
        // 当这个维度已选时需重新加载，但是原数据为空时不加载，否则会和下方useEffect产生重复请求
        !_.isEmpty(asyncData[key].data) &&
          asyncData[key].fetchFn({ params: { campaignId: campaignIds } }).then(() => {
            // 由于重新获取了可选数据，所以将已选数据清空
            handleChange((prevFormData: any) => ({
              ...prevFormData,
              [name]: _.map(prevFormData[name], i => ({
                ...i,
                value: i.key === key ? [] : i.value
              }))
            }));
          });
      } else {
        // 如果这个维度没有被选择，则需标记一下，使它在被选择时加载
        setKeysToRefetch(prevState => [...prevState, key]);
      }
    });
  }, [campaignIds]);

  // 当所选维度改变时
  useEffect(() => {
    // 不在可选维度中的项直接过滤掉
    handleChange((prevFormData: any) => {
      return {
        ...prevFormData,
        [name]: _.filter(prevFormData[name], i => formattedSegments.includes(i.key))
      };
    });

    if (_.isEmpty(formattedSegments)) {
      // 选择为空，不处理
      return;
    }

    _.toPairs(asyncData).forEach(([key, val]) => {
      // 在所选维度包含当前key的情况下，如果原来未获取过，或者被标记需重新请求的，需要重新加载
      if (formattedSegments.includes(key) && (_.isEmpty(val.data) || keysToRefetch.includes(key))) {
        if (asyncKeysWithCampaignIds.includes(key as ASYNC_FN_KEY)) {
          val.fetchFn({ params: { campaignId: campaignIds } });
          setKeysToRefetch(prevState => _.without(prevState, key));
        } else {
          val.fetchFn();
        }
      }
    });
  }, [formattedSegments]);

  return (
    <div className="form-filter">
      <div className="form-filter-title">
        <Icon icon="plus" />
        &nbsp;&nbsp;{formatMessage('i18n_filtersAdd')}{' '}
        <span>({formatMessage('i18n_optional')})</span>
      </div>
      <AdvCustomFilter
        inForm
        value={value}
        filterData={filterData}
        asyncData={asyncData}
        onSubmit={handleChange}
        keyColumnLoading={false}
        limit={5}
      />
    </div>
  );
});
