import React, { memo, useCallback } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';
import { useHistory } from 'react-router';

import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import {
  FloatActionCell,
  TextCell,
  MultiLineCell,
  CampaignStatusCell,
  DateCell,
  NumberCell
} from '@/components/Table/Cells';
import { getUserProfile } from '@/utils/location';
import { BasicReportProps } from '@/constants/config';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import * as APIs from '@/constants/APIs';
import useTable from '@/components/hooks/useTable';

const maxLine = 3;

export default memo(({ dateParams, tableParams, setTableParams, postParams }: BasicReportProps) => {
  const history = useHistory();
  const { tableData, onChange, onExport } = useTable({
    dateParams,
    tableParams,
    setTableParams,
    api: APIs.NEW_API_REPORT,
    postParams: {
      ...postParams,
      var: 'json'
    }
  });
  const { formatMessage } = useFmtMsgFori18n();

  const renderActions = (rowData: Record<string, any>) => {
    return (
      <>
        <FloatActionCell.Actions.Report
          onClick={() => {
            history.push(`/campaigns/${rowData.id}`);
          }}
        />
      </>
    );
  };

  const handleExport = useCallback(() => {
    onExport({
      ...postParams,
      var: 'xlsx'
    });
  }, [onExport]);

  // 定义table toolbar
  const renderToolbar = (): React.ReactElement[] => {
    return [
      <Button key="exportTable" className="pull-right" onClick={handleExport}>
        {formatMessage('i18n_exportTable')}
      </Button>
    ];
  };
  const { internal } = getUserProfile();
  return (
    <>
      <Table
        data={tableData.data}
        page={tableData.page}
        loading={tableData.loading}
        sortColumn={tableParams.orderColumn}
        sortType={tableParams.orderType}
        renderToolbar={renderToolbar}
        onChange={onChange}
        wordWrap
      >
        {/* ID */}
        <Column width={100} fixed sortable>
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>

        {/* 推广计划名称 */}
        <Column width={250} resizable fixed sortable>
          <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
          <TextCell dataKey="name" maxLine={maxLine} renderActions={renderActions} />
        </Column>

        {/* 主要拥有者 */}
        <Column width={200} resizable sortable>
          <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
          <MultiLineCell
            dataKey="primaryOwner"
            formatter={data => _.isString(data) && [data.split(':').join(': ')]}
            maxLine={maxLine}
          />
        </Column>

        {/* 次要拥有者 */}
        {internal && (
          <Column width={150} resizable>
            <HeaderCell>{formatMessage('i18n_secondaryOwner')}</HeaderCell>
            <MultiLineCell
              dataKey="secondaryOwner"
              formatter={data =>
                _.isArray(data) &&
                data.reduce(
                  (acc, i) => (_.isEmpty(i) ? acc : acc.concat(i.split(':').join(': '))),
                  []
                )
              }
              maxLine={maxLine}
            />
          </Column>
        )}

        {/* 广告主 */}
        <Column width={150} sortable>
          <HeaderCell>{formatMessage('i18n_advertiser')}</HeaderCell>
          <TextCell dataKey="advertiser" maxLine={maxLine} />
        </Column>
        {/* 品牌 */}
        <Column width={150} sortable>
          <HeaderCell>{formatMessage('i18n_brand')}</HeaderCell>
          <TextCell dataKey="brand" maxLine={maxLine} />
        </Column>
        {/* 状态 */}
        <Column width={120} sortable>
          <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
          <CampaignStatusCell dataKey="status" />
        </Column>
        {/* 开始日期 */}
        <Column width={100} sortable>
          <HeaderCell>{formatMessage('i18n_startTime')}</HeaderCell>
          <DateCell dataKey="startDate" />
        </Column>
        {/* 结束日期 */}
        <Column width={100} sortable>
          <HeaderCell>{formatMessage('i18n_endTime')}</HeaderCell>
          <DateCell dataKey="endDate" />
        </Column>
        {/* 目标人群 */}
        <Column width={150} sortable>
          <HeaderCell>{formatMessage('i18n_targetPerson')}</HeaderCell>
          <FloatActionCell dataKey="target" />
        </Column>

        {/* 曝光次数 */}
        <Column width={150} align="right" key="impressions" sortable>
          <HeaderCell>{formatMessage('i18n_impressions')}</HeaderCell>
          <NumberCell dataKey="impressions" />
        </Column>

        {/* 独立曝光次数 */}
        <Column width={150} align="right" key="uimpressions" sortable>
          <HeaderCell>{formatMessage('i18n_uimpressions')}</HeaderCell>
          <NumberCell dataKey="uimpressions" />
        </Column>

        {/* 点击次数 */}
        <Column width={150} align="right" key="clicks" sortable>
          <HeaderCell>{formatMessage('i18n_clicks')}</HeaderCell>
          <NumberCell dataKey="clicks" />
        </Column>

        {/* 独立点击次数 */}
        <Column width={150} align="right" key="uclicks" sortable>
          <HeaderCell>{formatMessage('i18n_uclicks')}</HeaderCell>
          <NumberCell dataKey="uclicks" />
        </Column>
      </Table>
    </>
  );
});
