import React, { memo, useCallback, useMemo } from 'react';
import { Button } from 'rsuite';

import * as APIs from '@/constants/APIs';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SelectPicker from '@/components/Picker/SelectPicker';
import Table, { Column, Cell, HeaderCell } from '@/components/Table';
import { DateTimeCell, AuditStatusCell, FloatActionCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { BasicReportProps, AuditStatusData } from '@/constants/config';
import template from '@/utils/template';
import { getUserProfile } from '@/utils/location';
import { useModal } from '@/components/hooks/useModal';
import Modal from '@/components/AuditModal';

type TableProps = BasicReportProps & {
  handleFormCreate: Function;
  handleFormEdit: Function;
};

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    handleSearchWord,
    handleFormCreate,
    handleFormEdit,
    handleChangeCustomParam,
    refetchTableData
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const { tableData, onExport, onChange } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_MEDIA_VAR,
      postParams: {
        var: ''
      }
    });
    const { internal } = getUserProfile();
    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);
    const auditStatusDataIntl = usePickerDataFromObj(AuditStatusData, { labelKey: 'label' });
    const handleExportClicked = useCallback(() => {
      onExport &&
        onExport({
          var: '.xlsx'
        });
    }, [onExport]);

    const renderToolbar = () => {
      return (
        <>
          <Button appearance="primary" onClick={handleFormCreate as () => void}>
            {internal
              ? formatMessage('i18n_create')
              : formatMessage('i18n_applyBtn') + formatMessage('i18n_site')}
          </Button>
          <SelectPicker
            key="status"
            prepend={formatMessage('i18n_status')}
            labelKey="label"
            valueKey="value"
            data={auditStatusDataIntl}
            value={filterParams.status}
            onChange={(value: string) => handleChangeCustomParam({ status: value })}
            style={{
              width: 200
            }}
          />
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
          <Button key="exportTable" className="pull-right" onClick={handleExportClicked}>
            {formatMessage('i18n_exportTable')}
          </Button>
        </>
      );
    };

    const renderActions = useCallback(
      rowData => {
        if (!internal) {
          return;
        }
        return (
          <FloatActionCell.Actions.Edit
            onClick={() => {
              handleFormEdit({
                id: rowData.id
              });
            }}
          />
        );
      },
      [handleFormEdit, internal]
    );
    const modal = useModal(false, {}, template`${APIs.API_MEDIA}/${'id'}`, refetchTableData);
    const renderReviewAction = useCallback(
      rowData => {
        const enableClickHandler = () => {
          modal.showModal(rowData);
        };
        if (_.toLower(rowData.status) === 'audit' && internal) {
          return [<FloatActionCell.Actions.Review key="Review" onClick={enableClickHandler} />];
        }
      },
      [handleFormEdit, modal]
    );

    return (
      <>
        <Modal {...modal} />
        <Table {...tableDataMemo} onChange={onChange} renderToolbar={renderToolbar}>
          <Column width={100} fixed sortable>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>
          <Column width={200} resizable fixed sortable>
            <HeaderCell>{formatMessage('i18n_name')}</HeaderCell>
            <FloatActionCell titleKey dataKey="name" renderActions={renderActions} />
          </Column>
          <Column width={200} fixed sortable>
            <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
            <AuditStatusCell dataKey="status" renderActions={renderReviewAction} />
          </Column>
          <Column width={200} sortable>
            <HeaderCell>URL</HeaderCell>
            <FloatActionCell dataKey="domain" />
          </Column>
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_defaultAdServer')}</HeaderCell>
            <FloatActionCell dataKey="defaultAds" />
          </Column>
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_industryType')}</HeaderCell>
            <FloatActionCell dataKey="category" />
          </Column>
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_type')}</HeaderCell>
            <FloatActionCell dataKey="site_type" />
          </Column>
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_isAdNet')}</HeaderCell>
            <FloatActionCell dataKey="adnetwork" />
          </Column>
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_createTime')}</HeaderCell>
            <DateTimeCell dataKey="created" />
          </Column>
        </Table>
      </>
    );
  }
);
