import _ from 'lodash';

import {
  CAMPAIGN_STATUS as CAMPAIGN_STATUS_CONST,
  CROWN as CROWN_CONST,
  DEVICE as DEVICE_CONST
} from '@/constants/config';

import { ASYNC_FN_KEY, FILTER_KEY } from './enum';
import {
  defaultChildrenKey,
  defaultLabelKey,
  FILTER_OPERATION,
  FILTER_TYPE,
  FilterNodeConfig,
  TagFormatterEntity,
  TagFormatterFn
} from '../../constants';

const mapLabelIdName = (node: any) => `${node.id}-${node.name}`;

// 人口属性
const demoInnerToOuter = (data: Array<any>) => {
  if (!_.isArray(data)) {
    return [];
  }

  let gender = [] as Array<any>;
  data.map(d => gender.push(_.lowerCase(_.split(d, '+')[0])));
  gender = _.sortedUniq(gender);
  const result = gender.map(i => ({
    gender: i,
    ages: []
  }));
  data.forEach(i => {
    result.forEach(r => {
      if (r.gender === _.lowerCase(_.split(i, '+')[0])) {
        r.ages.push(_.split(i, '+')[1]);
      }
    });
  });

  return result;
};

// 人口属性
const demoOuterToInner = (value: Array<any>) => {
  let data = [] as Array<any>;
  value.forEach(item => {
    let name = _.upperCase(item.gender);
    if (_.isArray(item.ages) && !_.isEmpty(item.ages)) {
      item.ages.forEach((one: any) => {
        data.push(`${name}+${one}`);
      });
    }
  });

  return data;
};

// 地区
const regionInnerToOuter = (data: Array<any>) => {
  return data.map(t => _.mapKeys(t, (v, k) => (k === 'id' ? 'province' : k)));
};

// 地区
const regionOuterToInner = (data: Array<any>) => {
  return data.map(t => _.mapKeys(t, (v, k) => (k === 'province' ? 'id' : k)));
};

const tagFormatterWithMapLabel: TagFormatterFn = (n, val, a) => {
  return val.map((v: any, idx: number) => {
    const { data } = a[n.asyncFnKey[0]];
    const dataLabel = n.mapLabel(_.find(data, { id: v }));
    return { label: `${n.label}：${dataLabel}`, removeKey: `${idx}` };
  });
};

const tagFormatterWithTree: TagFormatterFn = (n, v, a, f) => {
  const result = [] as Array<TagFormatterEntity>;
  const childKey = n.childrenKey || defaultChildrenKey;
  const labelKey = n.labelKey || defaultLabelKey;
  const { mapLabel, label, tags } = n;
  const resultLabel = tags ? f(`i18n_${tags}`, {}, true) : label;

  const { data } = a[n.asyncFnKey[0]];
  v.forEach((vv: any, vvIdx: number) => {
    const firstLevelItem: any = _.find(data, { id: vv.id }) || { [labelKey]: '' };
    if (!_.get(vv, 'children.length', 0)) {
      // 就是全选了一级的
      result.push({
        label: `${resultLabel}：${
          _.isFunction(mapLabel) ? mapLabel(firstLevelItem) : firstLevelItem[labelKey]
        }`,
        removeKey: `${vvIdx}`
      });
    } else {
      (vv.children || []).forEach((ss: any, ssIdx: number) => {
        const secondLevelItem: any = _.find(firstLevelItem[childKey], { id: ss });
        result.push({
          label: `${resultLabel}：${
            _.isFunction(mapLabel) ? mapLabel(secondLevelItem) : secondLevelItem[labelKey]
          }`,
          removeKey: `${vvIdx}*${ssIdx}`
        });
      });
    }
  });

  return result;
};

const tagFormatterWithCheck: TagFormatterFn = (n, v, a, f) => {
  const labelKey = n.labelKey || defaultLabelKey;
  return v.map((vv: any, vvIdx: number) => {
    const { data } = n;
    const label = f(_.find(data, { value: vv })[labelKey], {}, true);
    return { label: `${n.label}：${label}`, removeKey: `${vvIdx}` };
  });
};

// 组件的理念是 存的结构基本就是可以直接用于请求的结构，所以尽量采用配置项的形式，只要不是支持第四列，或者有bug，不用修改组件的代码，调整只需修改本配置文件。
