/* eslint-disable */
import * as React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import { Checkbox, DOMHelper, Table as RSTable, Icon, Loader } from 'rsuite';
import classnames from 'classnames';
import { TableProps as RSTableProps } from 'rsuite/lib/Table';
import prefix from '@/utils/prefix';
import { withRefByInnerRef } from '@/components/HOCs/withInnerRef';
import {
  DEFAULT_PAGE_SIZE,
  MIN_PAGE_HEIGHT,
  TABLE_LENGTH_MENU_ITEMS,
  TABLE_KEY
} from '@/constants/config';
import { isNotEmpty } from '@/utils';
import { FormattedMessage } from 'react-intl';
import { CheckBoxCell } from './Cells';
export { default as Toolbar } from './Toolbar';
export const { Column, HeaderCell, Cell, ColumnGroup } = RSTable;

const { on, getHeight } = DOMHelper;

const baseClassName = 'table-wrap';

const MIN_TABLE_HEIGHT = 360; // 表格最小高度

const FRAME_MARGIN_TOP = 40; // 整个页面上方外边距
const BREADCRUMB_HEIGHT = 36 + 20; // 面包屑自身高度36，包含面包屑到下方组件的间隙20
const EXTRA_TOOLBAR_HEIGHT = 56 + 30; // 额外工具栏自身高度56，包含工具栏到下方组件的间隙30
const TABLE_PANEL_PADDING_TOP = 20; // 表格panel中上方内边距
const TABLE_TOOLBAR_HEIGHT = 54; // 表格内工具栏高度

const PAGINATION_HEIGHT = 64; // 分页工具高度
const FOOTER_HEIGHT = 17 + 30; // 脚注高度30，包含脚注到上方组件的间隙17
const FRAME_MARGIN_BOTTOM = 30; // 整个页面下方外边距

const propTypes = {
  wrapperClassNames: PropTypes.string,
  data: PropTypes.array,
  page: PropTypes.shape({
    current: PropTypes.number,
    size: PropTypes.number,
    total: PropTypes.number
  }),
  loading: PropTypes.bool,
  sortColumn: PropTypes.string,
  sortType: PropTypes.string,
  onChange: PropTypes.func,
  onChangePage: PropTypes.func,
  onChangeLength: PropTypes.func,
  onSortColumn: PropTypes.func,
  checkDataKey: PropTypes.string,
  onCheck: PropTypes.func,
  withPagination: PropTypes.bool,
  disableCheckedValues: PropTypes.array,
  // 当触发 onChange 方法的时候是否清空 checkValues
  tableChangeClearCheckValues: PropTypes.bool,
  toolbar: PropTypes.any,
  height: PropTypes.number
};

type CheckValue = string | number;

export interface TableCommonProps<T> {
  wrapperClassNames?: string;
  className?: string;
  data: any[];
  loading?: boolean;
  sortColumn?: string;
  sortType?: 'desc' | 'asc';
  onChange?: (params: Record<string, any>) => void;
  onChangePage?: (page: number, params: Record<string, any>) => void;
  onChangeLength?: (pageSizeObj: { pageSize: number }, params: Record<string, any>) => void;
  onSortColumn?: (
    orderColumn: string,
    orderType: 'asc' | 'desc',
    params: Record<string, any>
  ) => void;
  checkDataKey?: string;
  onCheck?: (checkedValue: T[], changedValue: T[], checked: boolean, isCheckAll: boolean) => void;
  checkValues?: T[];
  wordWrap?: boolean;
  shouldUpdateScroll?: boolean;
  offsetTopHeight?: number; // 额外指定table上方需缩减的高度
  offsetBottomHeight?: number; // 额外指定table上方需缩减的高度
  height?: number; // 指定自定义高度
}

export interface TableInnerProps {
  children: React.ReactElement | React.ReactElement[];
}

export interface TableDefaultProps<T> {
  page: {
    current: number;
    size: number;
    total?: number;
  };
  disableCheckedValues?: T[];
  withPagination?: boolean;
  tableChangeClearCheckValues?: boolean;
  renderToolbar?: () => React.ReactNode;
  container?: () => HTMLElement | Window;
}

interface State<T> {
  params: {
    current: number;
    orderType?: 'desc' | 'asc';
    orderColumn?: string;
    size: number;
  };
  id: string;
  height: number;
  checkValues?: T[];
}

type TableProps = TableDefaultProps<CheckValue> & TableCommonProps<CheckValue> & TableInnerProps;

class Table extends React.PureComponent<TableProps, Partial<RSTableProps>, State<CheckValue>> {
  _onWindowResizeListener: any;

  static propTypes = propTypes;

  static defaultProps: TableDefaultProps<CheckValue> = {
    page: {
      current: 1,
      size: DEFAULT_PAGE_SIZE
    },
    disableCheckedValues: [],
    withPagination: true,
    tableChangeClearCheckValues: true
  };

  constructor(props: TableDefaultProps<CheckValue> & TableCommonProps<CheckValue>) {
    super(props as any);
    // 这里 由于 api 规范中 指定了 排序参数为 `orderColumn` 和  `orderType` ，所以 params 中使用`orderColumn`、`orderType`
    const { sortColumn: orderColumn, sortType: orderType } = props; // $FlowFixMe
    this.state = {
      id: `table${+new Date()}`,
      height: 0,
      params: _.extend(props.page || Table.defaultProps.page, { orderColumn, orderType }),
      checkValues: []
    };
  }

  componentDidMount() {
    this._onWindowResizeListener = on(window, 'resize', _.debounce(this.handleWindowResize, 50));
    this.handleWindowResize();
  }

  componentDidUpdate(
    prevProps: Readonly<
      TableDefaultProps<CheckValue> & TableCommonProps<CheckValue> & TableInnerProps
    >
  ): void {
    // offsetHeight改变需重新计算高度
    if (
      prevProps.offsetTopHeight !== this.props.offsetTopHeight ||
      prevProps.offsetBottomHeight !== this.props.offsetBottomHeight
    ) {
      this.handleWindowResize();
    }
  }

  componentWillUnmount() {
    if (this._onWindowResizeListener) {
      this._onWindowResizeListener.off();
    }
  }

  calculateTableHeight = (): number => {
    const {
      height,
      container,
      renderToolbar,
      withPagination,
      offsetTopHeight = 0,
      offsetBottomHeight = 0
    } = this.props;

    if( _.isNumber(height) ){
      return height;
    }

    const withBreadcrumb = document.getElementsByClassName('rs-breadcrumb').length > 0;
    const withExtraToolbar = document.getElementsByClassName('page-toolbar').length > 0;
    const withTableToolbar = _.isFunction(renderToolbar);
    const withFooter = document.getElementsByClassName('rs-footer').length > 0;

    // 上方不可用高度
    const unusableTopHeight =
      FRAME_MARGIN_TOP +
      (withBreadcrumb ? BREADCRUMB_HEIGHT : 0) +
      (withExtraToolbar ? EXTRA_TOOLBAR_HEIGHT : 0) +
      TABLE_PANEL_PADDING_TOP +
      (withTableToolbar ? TABLE_TOOLBAR_HEIGHT : 0) +
      offsetTopHeight -
      (withBreadcrumb ? 28 : 0) - // 如果有面包屑，页面上方外边距会缩短28px
      (!withBreadcrumb && withExtraToolbar ? 20 : 0); // 如果有额外工具栏，页面上方外边距会缩短20px

    // 下方不可用高度
    const unusableBottomHeight =
      (withPagination ? PAGINATION_HEIGHT : 0) +
      (withFooter ? FOOTER_HEIGHT : 0) +
      FRAME_MARGIN_BOTTOM +
      offsetBottomHeight;

    // 指定容器高度或者body高度
    const containerHeight = container ? getHeight(container()) : getHeight(window);

    const result =
      Math.max(containerHeight, MIN_PAGE_HEIGHT) - unusableTopHeight - unusableBottomHeight;

    return Math.max(result, MIN_TABLE_HEIGHT);
  };

  mergeParams = (source: Record<string, any>) => {
    const params = _.extend({}, this.state.params, source);
    this.setState({ params });
    return params;
  };

  getCheckAbleValues = () => {
    const { data, checkDataKey, disableCheckedValues } = this.props;
    return _.filter(
      _.map(data, obj => _.get(obj, checkDataKey)),
      value => !_.includes(disableCheckedValues, value)
    );
  };

  setCheckValues = (checkValues: CheckValue[]) => {
    this.setState({
      checkValues
    });
  };

  handleWindowResize = () => {
    this.setState({
      height: this.calculateTableHeight()
    });
  };

  // state.params 发生变化时 调用此方法
  handleChange = (params: any) => {
    // 当存在 checkbox 时 Table 发生改变则需要清除所有已选值
    this.props.tableChangeClearCheckValues &&
      !_.isEmpty(this.props.checkDataKey) &&
      this.handleCheckChange([], [], false);
    _.attempt(this.props.onChange, params);
  };

  handleChangePage = (page: number) => {
    const params = this.mergeParams({ page });
    this.handleChange(params);
    _.attempt(this.props.onChangePage, page, params);
  };

  handleSortColumn = (orderColumn: string, orderType: 'asc' | 'desc') => {
    // 当排序列改变时 不改变OrderType
    // orderType = orderColumn !== this.state.params ? .orderColumn ? 'desc' : orderType;
    orderType = orderColumn !== _.get(this.state.params, 'orderColumn') ? 'desc' : orderType;
    const params = this.mergeParams({ page: 1, orderColumn, orderType });
    this.handleChange(params);
    _.attempt(this.props.onSortColumn, orderColumn, orderType, params);
  };

  handleChangeLength = (pageSize: number) => {
    const params = this.mergeParams({ page: 1, [TABLE_KEY.PAGE_SIZE]: pageSize });
    this.handleChange(params);
    _.attempt(
      this.props.onChangeLength,
      {
        [TABLE_KEY.PAGE_SIZE]: pageSize
      },
      params
    );
  };

  handleCheckChange = (
    checkValues: CheckValue[],
    values: CheckValue[],
    checked: boolean,
    isCheckAll: boolean = false
  ) => {
    const { onCheck } = this.props;
    this.setCheckValues(checkValues);
    onCheck && onCheck(checkValues, values, checked, isCheckAll);
  };

  // @ts-ignore TS6133
  handleCheckCellCheckAllChange = (value: CheckValue[], checked: boolean) => {
    const checkAbleValues = this.getCheckAbleValues();
    const checkValues = checked ? checkAbleValues : [];
    this.handleCheckChange(checkValues, checkAbleValues, checked, true);
  };

  handleCheckCellChange = (value: CheckValue, checked: boolean) => {
    let checkValues = [...this.getCheckValues()];
    if (checked) {
      checkValues.push(value);
    } else {
      checkValues = _.without(checkValues, value);
    }
    this.handleCheckChange(checkValues, [value], checked);
  };

  getCheckValues = () => {
    const { checkValues } = this.props;
    if (_.isUndefined(checkValues)) {
      return this.state.checkValues;
    }
    return checkValues || [];
  };

  renderCheckColumn() {
    const { checkDataKey, disableCheckedValues = [], loading } = this.props;
    const checkValues = this.getCheckValues();
    const checkAbleData = this.getCheckAbleValues();
    const validCheckValues = _.intersection(checkValues, checkAbleData);
    const hasValue = validCheckValues.length > 0;
    const checkedAll = hasValue && validCheckValues.length === checkAbleData.length;
    const indeterminate = hasValue && validCheckValues.length !== checkAbleData.length;
    return (
      <Column key="checkColumn" width={80} fixed align="center">
        <HeaderCell style={{ padding: 0 }}>
          <div style={{ padding: '2px' }}>
            <Checkbox
              inline
              disabled={loading || checkAbleData.length === 0}
              checked={checkedAll}
              indeterminate={indeterminate}
              onChange={this.handleCheckCellCheckAllChange}
            />
          </div>
        </HeaderCell>
        <CheckBoxCell
          dataKey={checkDataKey}
          checked={value => checkValues.includes(value)}
          disabled={value => disableCheckedValues.includes(value)}
          onChange={this.handleCheckCellChange}
        />
      </Column>
    );
  }

  render() {
    const {
      wrapperClassNames,
      className,
      data,
      loading,
      page,
      sortColumn,
      sortType,
      onChange,
      onChangePage,
      onChangeLength,
      onSortColumn,
      renderToolbar,
      children,
      checkDataKey,
      onCheck,
      disableCheckedValues,
      checkValues,
      withPagination,
      tableChangeClearCheckValues,
      wordWrap = false,
      offsetTopHeight,
      offsetBottomHeight,
      ...props
    } = this.props;
    const checkAble = !_.isEmpty(checkDataKey);
    const activePage = page.size ? Math.floor(page.current / page.size) + 1 : 1;
    return (
      <div className={classnames(baseClassName, wrapperClassNames)}>
        {_.isFunction(renderToolbar) && (
          <div className={prefix(baseClassName, 'table-toolbar')}>{renderToolbar()}</div>
        )}
        <RSTable
          id={this.state.id}
          height={this.state.height}
          {...props}
          className={classnames(className, prefix(baseClassName, 'table'))}
          data={data || []}
          loading={loading}
          onSortColumn={this.handleSortColumn}
          sortColumn={sortColumn}
          sortType={sortType}
          wordWrap={wordWrap}
          locale={{
            emptyMessage: (
              <span>
                <Icon icon="info" size="lg" style={{ marginRight: 5, color: '#2196f3' }} />
                <FormattedMessage id="dataEmpty" />
              </span>
            ),
            loading: <FormattedMessage id="loading" />
          }}
        >
          {[
            checkAble ? this.renderCheckColumn() : null,
            ...React.Children.toArray(children)
          ].filter(isNotEmpty)}
        </RSTable>
        {withPagination && (
          <RSTable.Pagination
            lengthMenu={TABLE_LENGTH_MENU_ITEMS}
            activePage={activePage}
            displayLength={page.size || DEFAULT_PAGE_SIZE}
            total={page.total || 0}
            onChangePage={this.handleChangePage}
            onChangeLength={this.handleChangeLength}
          />
        )}
      </div>
    );
  }
}

/**
 * @param tableRef table的Ref
 * 返回table的一些常用的utils，这里多做了个一个中间层，避免组件内部的改动对调用者产生影响
 */
export const tableUtils = (tableRef: React.RefObject<React.ReactElement>) => ({
  // 获取checkbox选中的row的keys集合
  getCheckValues: _.get(tableRef, 'current.getCheckValues', () => [] as any) as () => Array<any>
});

export default withRefByInnerRef<TableProps>(Table);
