import React, { memo, useState, useEffect } from 'react';
import _ from 'lodash';
import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { CHART_KEY, CHART_TITLE, TOOL_PER, AXIS_PER } from '@/components/Chart/config';
import { percentToNumber } from '@/utils/format';
import Chart from '@/components/Chart';

const mockData: DataProps = {
  cols: ['2020-03-02', '2020-03-03'],
  data: [{ data: ['0.00%', '0.86%'], name: '触达率' }]
};

interface Props extends BasicReportProps {
  campaignType: 'campaign' | 'crossCampaign';
}

interface DataProps {
  cols: string[];
  data: Array<ChartDataProps>;
}

interface ChartDataProps {
  data: Array<number | string>;
  name: string;
}

export default memo<Props>(function({ dateParams, id, filterParams, campaignType }) {
  const { loading, onFetch } = useFetch<CommonResponse>(FETCH_METHOD.GET, APIs.API_CHART, {
    cacheParams: true,
    params: {
      id,
      type: 'hitRate',
      hierarchy: campaignType,
      report: 'audiences',
      end: dateParams.end,
      secondary: 'daily',
      ...filterParams
    }
  });
  const [data, setData] = useState<DataProps>(mockData);
  const { formatMessage } = useFmtMsgFori18n();

  useEffect(() => {
    dateParams.end &&
      onFetch().then(rsp => {
        !_.isEmpty(_.get(rsp, 'data.data')) && setData(_.get(rsp, 'data'));
      });
  }, [id, dateParams.end]);
  // 字符串数值转化成小数
  let newData: Array<ChartDataProps> = [];
  data.data.forEach((i: ChartDataProps) => {
    let sdata: number[] = [];
    i.data.forEach(o => {
      sdata.push(parseFloat(percentToNumber(o)));
    });
    newData.push({
      name: i.name,
      data: sdata
    });
  });
  const option = {
    color: ['#34C3FF'],
    tooltip: TOOL_PER
    // xAxis: {
    //   type: 'category',
    //   data: data.cols,
    //   axisTick: {
    //     alignWithLabel: true
    //   },
    //   axisLine: {
    //     show: false
    //   }
    // },
    // yAxis: [AXIS_PER({})],
  };
  const config = {
    type: CHART_KEY.LINE,
    data: {
      cols: data.cols,
      data: newData
    },
    yAxis: {
      type: 'per'
    },
    hasTools: true
  };

  return (
    <Chart
      title={formatMessage('i18n_hitRateTrendChart')}
      config={config}
      height={250}
      option={option}
      loading={loading}
    />
  );
});
