import _ from 'lodash';

export const hierarchy = 'conversion';

export enum reportKeys {
  ATTRIBUTE = 'conversionattribute', // 自定义属性报表
  FLOW = 'landingtraffic', //	到站流量报表
  SUSP = 'suspiciousconversions', // 可疑转化报表
  ACTION = 'conversionaction', // 转化活动报表
  ATTRIBUTION = 'attribution', // 归因模型
  PATH = 'conversionpath' // 转化路径
}

export enum filterParamKeys {
  BE = 'beId', // 报表所属BE，当不传ids, objId时必传，beId以逗号分隔
  MDIMENSION = 'mDimension', // 主要维度
  ADVERTISER = 'advertiser', // 广告主品牌
  ACTION = 'conversionSubs', // 选择的线上活动
  PARA = 'attributeId', // 选择的自定义属性
  SEC = 'secondary', // 次级维度|时间维度
  SEG = 'segmentation', // 细分维度
  INDICATORS = 'indicators', // 指标
  ATTRMODEL = 'attributeModel', // 归因模型
  ABNOMAL = 'abnormal', // 异常类型
  PROJECT = 'projects' // 项目选择
}

export interface FilterParam {
  [filterParamKeys.BE]?: Array<any>;
  [filterParamKeys.MDIMENSION]?: string | number;
  [filterParamKeys.ADVERTISER]?: Array<any>; // eg:[{"id": 1, "children": [1,2]}]
  [filterParamKeys.ACTION]?: Array<any>; // eg:[{"id": 1, "children": [1,2]}]
  [filterParamKeys.PARA]?: string | number;
  [filterParamKeys.SEC]?: string | number;
  [filterParamKeys.SEG]?: string | number;
  [filterParamKeys.INDICATORS]?: Array<string>;
  [filterParamKeys.ATTRMODEL]?: Array<string>;
  [filterParamKeys.ABNOMAL]?: string;
  [filterParamKeys.PROJECT]?: Array<string | number>;
  sSearch?: string;
}

export const defaultFilter: FilterParam = {
  [filterParamKeys.BE]: [],
  [filterParamKeys.ADVERTISER]: [],
  [filterParamKeys.ACTION]: [],
  [filterParamKeys.SEC]: null,
  [filterParamKeys.SEG]: null,
  [filterParamKeys.ABNOMAL]: null
};
