export const commonParamFormatter = (filterParam: FilterParam) => {
  const originBE = _.get(filterParam, filterParamKeys.BE);
  const originAdvertiser = _.get(filterParam, filterParamKeys.ADVERTISER);
  const originAction = _.get(filterParam, filterParamKeys.ACTION);
  const originModel = _.get(filterParam, filterParamKeys.ATTRMODEL);
  const originProject = _.get(filterParam, filterParamKeys.PROJECT);
  const originPara = _.get(filterParam, filterParamKeys.PARA);
  let rest = {};
  if (originPara && originPara.toString().indexOf(',') > -1) {
    rest = {
      [filterParamKeys.BE]: originPara.toString().split(',')[1],
      [filterParamKeys.PARA]: originPara.toString().split(',')[0]
    };
  }
  return {
    ...filterParam,
    [filterParamKeys.BE]: originBE.toString(),
    [filterParamKeys.ADVERTISER]: _.get(originAdvertiser, 'length', 0)
      ? JSON.stringify(originAdvertiser)
      : '',
    [filterParamKeys.ACTION]: _.get(originAction, 'length', 0) ? JSON.stringify(originAction) : '',
    [filterParamKeys.PROJECT]: !_.isEmpty(originProject) ? originProject.join(',') : '',
    ...rest
  };
};
