// @flow
import { useCallback, useEffect } from 'react';
import _ from 'lodash';

import { BasicReportProps, TABLE_KEY, TABLE, QUICK_DEV } from '@/constants/config';
import useFetch, { FETCH_METHOD, transformToTableData } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import download from '@/utils/download';

interface Ret {
  tableData: any;
  onChange: (params: Record<string, any>) => void;
  onExport: (params: Record<string, any>, api?: Url) => void;
}

export default function useTable(
  {
    dateParams,
    tableParams,
    setTableParams,
    api,
    postParams,
    requestChecker,
    onBeforeFetch,
    onFetchSuccess
  }: BasicReportProps,
  deps: any[] = []
): Ret {
  const { loading, onFetch: onUpdateTableData, response: tableResponse } = useFetch<CommonResponse>(
    FETCH_METHOD.GET,
    api,
    {
      cacheParams: true,
      params: {
        ...postParams
      }
    }
  );
  const tableData = transformToTableData(tableResponse);
  // 获取表格data
  useEffect(() => {
    const params = {
      ...dateParams,
      ...tableParams
    };

    // 决定是否请求，定义且返回false则不请求，其他情况都请求
    if (_.isFunction(requestChecker) && !requestChecker(params)) {
      return;
    }

    onBeforeFetch && onBeforeFetch();
    onUpdateTableData({ params }).then(onFetchSuccess);
    // 这里多写一个依赖， JSON后的postParam，这样本地变量(非tableParam)的改变 引起的postParam 的改变也会重新更新数据，users/xx/paras页面用到了
  }, [dateParams, tableParams, JSON.stringify(postParams)]);

  // 表格参数change
  const handleTableChange = useCallback(
    (nextParams: TABLE) => {
      setTableParams((prevParams: TABLE) => ({
        ...prevParams,
        [TABLE_KEY.PAGE]: 1,
        ...nextParams
      }));
    },
    [setTableParams]
  );

  // 导出表格 添加导出表格api参数 默认和获取table数据同一api
  const handleTableExport = useCallback(
    (param, xlsxApi: Url = api) => {
      download(xlsxApi, {
        ...dateParams,
        ...tableParams,
        ...param
      });
    },
    [api, dateParams, tableParams]
  );

  return {
    tableData: {
      ...tableData,
      loading,
      orderType: _.get(tableParams, 'orderType', ''),
      orderColumn: _.get(tableParams, 'orderColumn', '')
    },
    onChange: handleTableChange,
    onExport: handleTableExport
  };
}

interface TableDataFormatObj {
  data: Array<any>;
  loading: boolean;
  page: {
    current: number;
    size: number;
    total?: number;
  };
  sortColumn: string;
  sortType: 'desc' | 'asc';
}

const defaultData = QUICK_DEV ? [{}] : []; // dev:fast模式下，默认为1一个元素
export const extractTableData = (tableData: Ret['tableData']): TableDataFormatObj => {
  const tableLoading = _.get(tableData, 'loading', false);
  const tableRawData = _.get(tableData, 'data', defaultData);
  const tablePage = _.get(tableData, 'page', {});
  const sortColumn = _.get(tableData, 'orderColumn', '');
  const sortType = _.get(tableData, 'orderType', 'desc');

  return {
    data: tableRawData,
    loading: tableLoading,
    page:
      QUICK_DEV && !_.get(tablePage, 'total', 0)
        ? { current: 1, size: 100, total: 299 }
        : tablePage,
    sortColumn,
    sortType
  };
};
