import _ from 'lodash';
import { emptyAdsConfig } from './constants';

export const mapFromResponse = (rspData: Record<string, any>) => {
  const data = _.mapValues(
    rspData,
    (val, key) => (_.isEqual(val, emptyAdsConfig[key]) ? undefined : val) // 编辑获取时会获取到所有项的值，即使为空，需要将其转化为undefined才能正确显示
  );
  data.notes = data.notes || '';
  return data;
};

// 新建时，除名称和备注外，只传填写有值的项
// 编辑时，要把undefined的项转为空值传递
export const mapToRequest = (formData: Record<string, any>, isEdit: boolean) => {
  return isEdit
    ? _.mapValues(formData, (val, key) =>
        ['name', 'notes'].includes(key) || !_.isUndefined(val) ? val : emptyAdsConfig[key]
      )
    : _.mapValues(formData, (val, key) =>
        ['name', 'notes'].includes(key) || !_.isEqual(emptyAdsConfig[key], val) ? val : undefined
      );
};
