import React, { useState, useEffect, useCallback } from 'react';
import _ from 'lodash';

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

/**
 * 用于当picker原有的value已不存在于picker的data中时，自动过滤该value
 */

export interface UseFilterValueProps {
  shouldFilter?: boolean; // 开关，默认不开启
  isMulti?: boolean; // value是否是多选的
  isTree?: boolean;
  value?: any;
  valueKey?: string;
  data?: any[];
  onChange?: Function;
}

interface OutProps {
  value: any;
  valueKey: string;
  data: any[];
  onChange: Function;
}

const useFilterValueForPicker: PropMapper<UseFilterValueProps, OutProps> = ({
  shouldFilter = false,
  isMulti = false,
  isTree = false,
  value,
  valueKey = 'value',
  data = [],
  onChange,
  ...props
}) => {
  const [filteredValue, setFilteredValue] = useState(value);
  const [filterStarted, setFilterStarted] = useState(false);

  useEffect(() => {
    let newValue = value;

    if(shouldFilter && filterStarted){
      if(isMulti){ // 多项选择时，value为数组
        if(isTree){ // 树形结构，遍历较复杂且耗性能，不如直接置空
          newValue = [];
        }else{ // 非树形结构时，求交集即可
          newValue = _.intersection(value, _.map(data, valueKey));
        }
      }else{ // 单项选择时，直接根据data是否包含当前value取舍
        const item = _.find(data, i => i[valueKey] === value);
        newValue = item ? value : undefined;
      }
      _.isEqual(value, newValue) || onChange?.(newValue);
    }

    setFilteredValue(newValue);
    !_.isEmpty(data) && !filterStarted && setFilterStarted(true);
  }, [data, value]);

  return {
    value: filteredValue,
    valueKey,
    data,
    onChange,
    ...props
  };
};

export default useFilterValueForPicker;