import React, { memo, useState, useEffect } from 'react';
import _ from 'lodash';
import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import useChart from '@/components/hooks/useChart';
import Chart from '@/components/Chart';
import { CHART_KEY, formatAxisMax, SPLIT_NUM } from '@/components/Chart/config';
import { toShortNum, toThousands } from '@/utils/format';

interface Props {
  api?: Url;
  postParams?: object;
  random?: string; // 用于刷新charts，一开始为空，这样不会重复请求
}
interface DataProps {
  data?: [string | number];
  name?: string;
  index?: number;
}

const yAxisStyle = {
  axisLine: {
    show: false,
    lineStyle: {
      color: '#ccc'
    }
  },
  axisTick: {
    show: false
  },
  splitLine: {
    lineStyle: {
      color: 'rgba(240, 240, 240, 0.8)'
    }
  }
};
const xAxisStyle = {
  boundaryGap: true,
  axisLine: {
    lineStyle: {
      color: '#ccc'
    }
  },
  splitLine: {
    show: false,
    lineStyle: {
      color: '#E5E5EA'
    }
  }
};

// 报表通用chart 显示impressions clicks 数据
export default memo(
  ({
    dateParams,
    filterParams,
    api = APIs.API_CHART,
    postParams = {},
    random
  }: Props & BasicReportProps) => {
    const { data, loading, handRefresh } = useChart({
      dateParams,
      filterParams,
      api,
      postParams: {
        ...postParams,
        var: 'chart'
      }
    });
    const { formatMessage } = useFmtMsgFori18n();
    const [chartData, setChartData] = useState([]);
    useEffect(() => {
      if (_.isEmpty(data) || !data) {
        return;
      }
      let records: Array<DataProps> =
        _.get(data, 'records') || _.get(data, 'data') || _.get(data, 'items') || [];
      // 数据按照给定顺序排序
      const order = ['impressions', 'uimpressions', 'clicks', 'uclicks'];
      records.map((i: DataProps) => {
        i.index = order.indexOf(i.name);
        return i;
      });
      records = _.orderBy(records, 'index');
      const newData: any[] = [];
      if (!_.isEmpty(records)) {
        records.forEach((i: any) => {
          newData.push({
            name: i.name,
            data: i.data,
            yAxisIndex: _.lowerCase(i.stack).indexOf('clicks') > -1 ? 1 : 0,
            type: CHART_KEY.BAR,
            barMaxWidth: 20,
            stack: i.stack
          });
        });
      }
      setChartData(newData);
    }, [data]);
    // 一开始random为空，不请求，如果random非空在请求
    useEffect(() => {
      if (random) {
        handRefresh();
      }
    }, [handRefresh, random]);

    const cols: any[] = _.get(data, 'cols') || [];
    let newCols: any[] = [];
    cols.map(item =>
      newCols.push(
        _.truncate(item, {
          length: 15
        })
      )
    );
    let legendData = chartData.map(i => i.name);
    let formtYAxis = {
      min: 0,
      max: formatAxisMax,
      splitNumber: SPLIT_NUM,
      axisLabel: {
        formatter(val: any) {
          return toShortNum(val);
        }
      }
    };
    const option = {
      legend: {
        data: legendData
      },
      grid: {
        containLabel: true
      },
      tooltip: {
        trigger: 'axis',
        confine: true, //是否将 tooltip 框限制在图表的区域内
        extraCssText: 'white-space:pre-wrap', //额外附加到浮层的 css 样式 换行样式
        formatter: data => {
          let res = '';
          if (!_.isEmpty(data)) {
            res = cols[data[0].dataIndex] + '<br >';
            data.map((item: any) => {
              res += item.marker + item.seriesName + ': ' + toThousands(item.value) + '<br>';
            });
          }
          return res;
        }
      },
      xAxis: [
        {
          type: 'category',
          data: newCols,
          axisLabel: {
            interval: 0,
            rotate: 40,
            show: true
          },
          ...xAxisStyle
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: formatMessage('impressions'),
          ...formtYAxis,
          ...yAxisStyle
        },
        {
          type: 'value',
          name: formatMessage('clicks'),
          ...formtYAxis,
          ...yAxisStyle
        }
      ],
      series: chartData
    };

    return (
      <>
        <Chart
          config={{ type: CHART_KEY.ORIGIN, hasTools: true }}
          option={option}
          loading={loading}
          collapsible
        />
      </>
    );
  }
);
