import React from 'react';
import _ from 'lodash';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import PickerGroup from '../PickerGroup';
import CheckTreePicker from '@/components/Picker/CheckTreePicker';
import CheckPicker from '../CheckPicker';
import { API_CAMPAIGN, API_ADVERTISER } from '@/constants/APIs';
import { UseFetchProps } from '@/components/Picker/PickerHooks/useFetchForPicker';

interface Props {
  values?: Record<string, any>;
  errors?: Record<string, string>;
  disabled?: boolean;
  cleanable?: boolean;
  fetchOptions?: UseFetchProps;
  onChange?: Function;
}

const advName = 'advertisers';
const campaignName = 'campaigns';

const pickerDefaultParam = {
  supportAll: true,
  style: { maxWidth: '300px' },
  menuStyle: { maxWidth: '400px' },
  height: 250,
  cleanable: true,
  searchable: true
};

export default React.memo(
  ({ values, errors, disabled = false, cleanable = false, onChange }: Props) => {
    const { formatMessage } = useFmtMsgFori18n();
    return (
      <div className="adv-brand-campaign-picker ">
        <PickerGroup values={values} errors={errors} onChange={onChange}>
          <CheckTreePicker
            name={advName}
            url={API_ADVERTISER}
            params={{
              orderColumn: 'name',
              orderType: 'asc',
              status: 1
            }}
            prepend={formatMessage('i18n_brandFilter')}
            {...pickerDefaultParam}
          />
          <CheckPicker
            name={campaignName}
            url={API_CAMPAIGN}
            deps={[_.isEmpty(values[advName]) ? undefined : values[advName]]}
            params={{
              advertiser:
                !_.isNil(values[advName]) && values[advName].length
                  ? JSON.stringify(values[advName])
                  : ''
            }}
            searchable
            cleanable={cleanable}
            disabled={disabled || _.isNil(values[advName]) || _.isEmpty(values[advName])}
            appearance="subtle"
            prepend={formatMessage('i18n_campaign')}
            style={{ maxWidth: '300px' }}
            menuStyle={{ maxWidth: '400px' }}
            placeholder={formatMessage('i18n_select', { name: '' })}
            shouldFilter
          />
        </PickerGroup>
      </div>
    );
  }
);
