import * as React from 'react';
import { RadioGroup, Radio } from 'rsuite';
import { RadioGroupProps } from 'rsuite/lib/RadioGroup';

interface Props extends RadioGroupProps {
  labelKey?: string;
  valueKey?: string;
  data?: any[];
  disabled?: boolean | any[]; // 可为boolean，控制所有成员的禁用；也可为数组，指定相应value的成员禁用
  itemWidth?: number | string; // 指定成员宽度
  useDefaultStyle?: boolean; // 默认的他不香吗？
}

const CustomRadioGroup = React.memo(
  ({
    itemWidth,
    data,
    labelKey = 'name',
    valueKey = 'id',
    disabled = false,
    children,
    useDefaultStyle = false,
    ...props
  }: Props) => {
    return (
      <RadioGroup {...props} inline>
        {children ||
          data.map(i => (
            <Radio
              value={i[valueKey]}
              key={i[labelKey]}
              disabled={typeof disabled === 'boolean' ? disabled : disabled.includes(i[valueKey])}
              style={{
                ...(!useDefaultStyle && {
                  width: itemWidth || '100%',
                  display: itemWidth && 'inline-block'
                })
              }}
            >
              {i[labelKey]}
            </Radio>
          ))}
      </RadioGroup>
    );
  }
);

CustomRadioGroup.displayName = 'RadioGroup';
export default CustomRadioGroup;
