import React, { useState, useCallback, useEffect } from 'react';
import _ from 'lodash';
import TreePicker from '@/components/Picker/TreePicker';
import { API_REGION } from '@/constants/APIs';
import { defaultFormData } from '../constants';

/*
 * RegionPicker内部需要使用string型value，比如
 * 选择市为省id+市id，'123+456'；单独选择省，则只有省id '123'
 * 但提供给外部，以及从外部接受的value只是单一的number型id
 *
 * 另外，需要把region请求里的其他地区过滤掉
 * */

interface Props {
  value?: number;
  disabled?: boolean;
  onChange?: Function;
}

export default React.memo(({ value, disabled, onChange, ...props }: Props) => {
  const [regionData, setRegionData] = useState([]);
  const [regionValue, setRegionValue] = useState();

  useEffect(() => {
    // regionData的结构形如[{id, name, children: [{id, name, provinceId, provinceName}]}]
    if (_.isNil(value) || value === defaultFormData.targetGeo) {
      setRegionValue(undefined);
      return;
    }

    let provinceId = null;
    let cityId = null;
    for (let i = 0; i < regionData.length; i++) {
      const province = regionData[i];
      if (province.id === value) {
        provinceId = province.id;
        break;
      }
      const cities = _.get(province, 'children', []);
      for (let j = 0; j < cities.length; j++) {
        const city = cities[j];
        if (city.id === value) {
          provinceId = province.id;
          cityId = city.id;
          break;
        }
      }
      if (_.isNumber(cityId)) {
        break;
      }
    }

    if (_.isNumber(provinceId)) {
      setRegionValue(_.isNumber(cityId) ? `${provinceId}+${cityId}` : String(provinceId));
    } else {
      setRegionValue(undefined);
    }
  }, [value, regionData]);

  const handleChange = useCallback(
    val => {
      const geoVal = _.last((val || '').split('+'));
      onChange(_.isEmpty(geoVal) ? defaultFormData.targetGeo : Number(geoVal));
    },
    [onChange]
  );

  const handleFormatData = useCallback(data => {
    // 根据其他地区的id和所在省id相同，过滤掉其他地区
    const newData = _.map(data, ({ id: provinceId, children, ...other }) => ({
      ...other,
      id: provinceId,
      children: _.filter(children, ({ id: cityId }) => cityId !== provinceId)
    }));
    setRegionData(newData);
    return newData;
  }, []);

  return (
    <TreePicker
      value={regionValue}
      url={API_REGION}
      dataRespFormatter={handleFormatData}
      onChange={handleChange}
      disabled={disabled}
      defaultExpandAll
      {...props}
    />
  );
});
