import _ from 'lodash';

export enum filterParamKeys {
  BE = 'beId'
}

export interface FilterParam {
  beId: Array<number>;
}

export const defaultFilter: FilterParam = {
  beId: []
};

export const utilsFn = {
  getChosenBE(filterParam: FilterParam) {
    return _.get(filterParam, filterParamKeys.BE, []);
  }
};
