export const emptyAdsConfig: Record<string, any> = {}; // 用于提供一份空数据模版，供提交

export const getAdsConfigOptions = (emptyLabel?: string) => [
  {
    id: 0,
    name: emptyLabel || 'i18n_none'
  },
  {
    id: 1,
    name: 'i18n_macroparameter'
  }
];

export interface AdsConfigItem {
  name?: string; // 表单或表格中的字段名
  dataKey?: string; // 表格中的字段名，未设置就取name

  // 以下四项均可能包含国际化内容
  label?: string; // 表单或表格中展示的名称
  labelSuffix?: string; // 名称需添加的后缀
  tableHeader?: string; // 表格中展示的名称，未设置就取label+labelSuffix
  emptyLabel?: string; // 未填该项时，显示的名称，未设置就取i18n_none

  noPlatform?: boolean; // 不需要为不同平台分别设置参数
  defaultVal?: string; // 该项的默认参数
  defaultPC?: string; // PC平台默认参数，未设置就取defaultValue
  defaultMobile?: string; // 移动平台默认参数，未设置就取defaultValue
  defaultOTT?: string; // OTT平台默认参数，未设置就取defaultValue
}

