import React, { memo, useCallback, useContext } from 'react';
import _ from 'lodash';
import { Panel, Nav } from 'rsuite';

import Card from '@/components/Card';
import { Toolbar } from '@/components/Table';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import { useMainPage } from '@/components/hooks/useMainPage';
import SelectPicker from '@/components/Picker/SelectPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';

import Chart from './Chart';
import Table from './Table';
import { UserModuleDateCtx } from './context';

const defaultCommonParam = {
  mdimension: 'be',
  secondary: '',
  sSearch: ''
};

const dateData = [
  {
    id: '',
    name: 'i18n_none'
  },
  {
    id: 'daily',
    name: 'i18n_daily'
  }
];

export default memo(() => {
  const dateDataIntl = usePickerDataFromObj(dateData);
  const {
    commonParam,
    fmtCommonParam,
    handleChangeCommonParam,
    handleDebouncedChangeCommonParam,
    customParamResult
  } = useMainPage({
    defaultCommonParam,
    customConfig: [
      {
        isTable: true
      }
    ]
  });

  const [tableResult] = customParamResult;
  const { handleChangePagination, fmtParam } = tableResult;

  const handleSearchWord = useCallback(
    (word: string) => {
      handleDebouncedChangeCommonParam({
        sSearch: word
      });
    },
    [handleDebouncedChangeCommonParam]
  );

  const handelChangeSecondary = useCallback(
    val => {
      handleChangeCommonParam({
        secondary: val
      });
    },
    [handleChangeCommonParam]
  );

  const { formatMessage } = useFmtMsgFori18n();
  const {
    dateRangeVal,
    dateRangeValTimeFormat,
    setDateRangeValByDateArray,
    maxDatePreset
  } = useContext(UserModuleDateCtx);

  return (
    <>
      <Panel className="page-toolbar">
        <DateRangePicker
          value={dateRangeVal}
          maxDate={maxDatePreset}
          cleanable={false}
          appearance="subtle"
          onChange={setDateRangeValByDateArray}
        />
      </Panel>
      <Card>
        <Nav appearance="subtle" activeKey="trend">
          <Nav.Item eventKey="trend">{formatMessage('i18n_trend')}</Nav.Item>
        </Nav>
        <Toolbar style={{ marginTop: 18 }}>
          <SelectPicker
            prepend={formatMessage('i18n_timerDimension')}
            onChange={handelChangeSecondary}
            value={_.get(commonParam, 'secondary')}
            data={dateDataIntl}
          />
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
        </Toolbar>

        <Chart dateParams={dateRangeValTimeFormat} filterParams={fmtCommonParam} />
        <Table
          dateParams={dateRangeValTimeFormat}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
        />
      </Card>
    </>
  );
});
