import { DOMHelper } from 'rsuite';

export const {
  hasClass,
  addClass,
  removeClass,
  toggleClass,
  addStyle,
  removeStyle,
  getStyle,
  on,
  off,
  scrollLeft,
  scrollTop,
  getHeight,
  getWidth,
  getOffset,
  getOffsetParent,
  getPosition,
  contains
} = DOMHelper;

export function removeElement(el: HTMLElement | null) {
  el && el.parentNode.removeChild(el);
}
