import React, { useCallback } from 'react';
import { Icon } from 'rsuite';
import _ from 'lodash';
import classnames from 'classnames';

import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';

export default ({
  rowData,
  dataKey,
  onRemove,
  ...rest
}: BaseCellProps<any> & { onRemove?: Function }) => {
  const disabledRemove = !_.isEmpty(_.get(rowData, dataKey));
  const renderContent = useCallback(() => {
    return (
      <Icon
        icon="close"
        onClick={() => disabledRemove || onRemove(rowData)}
        className={classnames({
          'remove-icon': !disabledRemove,
          'disabled-remove-icon': disabledRemove
        })}
      />
    );
  }, [disabledRemove, onRemove, rowData]);

  const renderTitle = useCallback(() => {
    return null;
  }, []);

  return (
    <Cell dataKey={dataKey} renderContent={renderContent} renderTitle={renderTitle} {...rest} />
  );
};
