import _ from 'lodash';
import React from 'react';
import { Table, Tooltip, Whisper } from 'rsuite';
import { Link } from 'react-router-dom';
import { TABLE_TOOLTIPS_DELAY_SHOW_TIME, DEFAULT_TABLE_CELL_CONTENT } from '@/constants/config';

const { Cell } = Table;

export interface LinkCellCellProps<T> {
  rowData?: T;
  dataKey: string;
  url?: string;
  getUrl?: (rowData: T) => string;
  delayShow?: number;
}

const styles = {
  link: { width: 'initial', maxWidth: '100%' }
};

function LinkCell({
  rowData,
  dataKey,
  url,
  getUrl,
  delayShow = TABLE_TOOLTIPS_DELAY_SHOW_TIME,
  ...props
}: LinkCellCellProps<BaseObject<any>>) {
  const text = _.get(rowData, dataKey, '--');

  const speaker = (
    <Tooltip className="popover-not-title">
      <span className="word-break">{text}</span>
    </Tooltip>
  );

  const targetUrl = url || getUrl(rowData);
  return (
    <Whisper placement="top" speaker={speaker} delayShow={delayShow}>
      <Cell {...props}>
        {targetUrl ? (
          <Link to={targetUrl} className="text-ellipsis hover-active" style={styles.link}>
            <span>{text}</span>
          </Link>
        ) : (
          DEFAULT_TABLE_CELL_CONTENT
        )}
      </Cell>
    </Whisper>
  );
}

export default React.memo(LinkCell);
