import React, { memo, useCallback } from 'react';
import { Button } from 'rsuite';
import _ from 'lodash';
import { useHistory } from 'react-router';

import { useFmtMsgFori18n } from '@/utils/formatMessage';
import Table, { Column, Cell, HeaderCell, Toolbar } from '@/components/Table';
import { DateTimeCell, NumberCell, FloatActionCell } from '@/components/Table/Cells';
import * as APIs from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import useTable from '@/components/hooks/useTable';
import { FULL_DATE } from '@/constants/dateFormat';

const SummaryTable = memo(({ dateParams, tableParams, setTableParams, id }: BasicReportProps) => {
  const history = useHistory();
  const { tableData, onChange, onExport } = useTable({
    dateParams,
    tableParams,
    setTableParams,
    api: APIs.API_GLOBALS,
    postParams: {
      var: 'json',
      id
    }
  });

  const { formatMessage } = useFmtMsgFori18n();
  const handleExportClicked = useCallback(() => {
    onExport &&
      onExport({
        var: 'xlsx'
      });
  }, [onExport]);

  const renderActions = (rowData: Record<string, any>) => {
    return (
      <>
        <FloatActionCell.Actions.Report
          onClick={() => {
            history.push({
              pathname: '/campaign-account-overview',
              state: {
                beId: rowData.id
              }
            });
          }}
        />
        <FloatActionCell.Actions.Manage
          onClick={() => {
            history.push({
              pathname: '/campaign-management',
              state: {
                beId: rowData.id
              }
            });
          }}
        />
      </>
    );
  };
  const renderCols = () => {
    const secondary = _.get(tableParams, 'secondary');
    let cols = [
      <Column width={100} sortable fixed key="id">
        <HeaderCell>ID</HeaderCell>
        <Cell dataKey="id" />
      </Column>,
      <Column width={200} sortable resizable fixed key="name">
        <HeaderCell>BE</HeaderCell>
        <FloatActionCell dataKey="name" renderActions={renderActions} />
      </Column>,
      secondary && (
        <Column width={150} align="right" sortable key="secondary">
          <HeaderCell>{formatMessage(`i18n_${secondary}`)}</HeaderCell>
          <DateTimeCell format={FULL_DATE} dataKey="secondary" />
        </Column>
      ),
      <Column flexGrow={1} minWidth={150} align="right" sortable key="impressions">
        <HeaderCell>{formatMessage('i18n_impressions')}</HeaderCell>
        <NumberCell dataKey="impressions" />
      </Column>,
      <Column flexGrow={1} minWidth={150} align="right" sortable key="uimpressions">
        <HeaderCell>{formatMessage('i18n_uimpressions')}</HeaderCell>
        <NumberCell dataKey="uimpressions" />
      </Column>,
      <Column flexGrow={1} minWidth={150} align="right" sortable key="clicks">
        <HeaderCell>{formatMessage('i18n_clicks')}</HeaderCell>
        <NumberCell dataKey="clicks" />
      </Column>,
      <Column flexGrow={1} minWidth={150} align="right" sortable key="uclicks">
        <HeaderCell>{formatMessage('i18n_uclicks')}</HeaderCell>
        <NumberCell dataKey="uclicks" />
      </Column>,
      <Column flexGrow={1} minWidth={150} align="right" sortable key="total">
        <HeaderCell>{formatMessage('i18n_total')}</HeaderCell>
        <NumberCell dataKey="total" />
      </Column>
    ];
    return cols;
  };
  return (
    <>
      <Toolbar>
        <Button className="pull-right" appearance="default" onClick={handleExportClicked}>
          {formatMessage('i18n_exportTable')}
        </Button>
      </Toolbar>
      <Table
        data={tableData.data}
        page={tableData.page}
        loading={tableData.loading}
        sortColumn={tableParams.orderColumn}
        sortType={tableParams.orderType}
        onChange={onChange}
        wordWrap
      >
        {renderCols()}
      </Table>
    </>
  );
});

export default SummaryTable;
