import React from 'react';
import { Schema } from 'schema-typed/types/Schema';

export interface FormDataContextProps {
  formData?: Record<string, any>;
  onChange?: (value: any, key?: string) => void;
}

export interface FormErrorContextProps {
  formErrors?: Record<string, any>;
  setFormErrors?:
    | ((errors: Record<string, any>) => void)
    | ((callback: (prevErrors: Record<string, any>) => Record<string, any>) => void);
  checkForm?: (
    fieldNameOrData?: string | Record<string, any>,
    value?: any,
    data?: Record<string, any>
  ) => Promise<any>;
  model?: Schema;
}

export const FormDataContext = React.createContext<FormDataContextProps>({});
export const FormErrorContext = React.createContext<FormErrorContextProps>({});
