import { GET } from '@/utils/request/fetch';
import * as APIs from '@/constants/APIs';
import { CampaignType } from './types.d';

export function onFetchCampaign(params: object) {
  return GET(APIs.API_CAMPAIGN_GET, {
    params
  });
}

export function onFetchCrossCampaign(params: object) {
  return GET(APIs.API_CROSSCAMPAIGN_GET, {
    params: {
      ...params,
      var: 'json'
    }
  });
}

export function onFetchAnyCampaignDetail(type: CampaignType, params: object) {
  if (type === CampaignType.CROSS_CAMPAIGN) {
    return onFetchCrossCampaign(params);
  }

  return onFetchCampaign(params);
}

export function onFetchCampaignRoute(params: object) {
  return GET(APIs.API_CAMPAIGN_INFO, {
    params
  });
}

export function onFetchReportDate(params: object, cb: (res: object) => void) {
  return GET(APIs.API_REPORT_DATE, {
    params
  }).then(cb);
}
