import React, { memo } from 'react';
import { Panel } from 'rsuite';

import { useMainPage } from '@/components/hooks/useMainPage';
import { YYYYMMDD } from '@/utils/date';

import Table from './Table';
import Toolbar from './Toolbar';
import {
  FilterParam,
  filterParamKeys,
  defaultCustomParam,
  defaultCommonParam,
  CommonParam,
  CustomParam
} from './constants';

const commonParamFormatter = (filterParam: CommonParam) => {
  const originDateParam = filterParam[filterParamKeys.DATE];
  const originCampaignParam = filterParam[filterParamKeys.CAMPAIGN] || [];
  return {
    ...filterParam,
    [filterParamKeys.DATE]: null as any,
    start: YYYYMMDD(originDateParam),
    end: YYYYMMDD(originDateParam),
    [filterParamKeys.CAMPAIGN]: originCampaignParam.toString()
  };
};

const customParamFormatter = (filterParam: FilterParam) => {
  const originCrossDimension = filterParam[filterParamKeys.CROSS_DIMENSION] || [];
  return {
    ...filterParam,
    [filterParamKeys.CROSS_DIMENSION]: originCrossDimension.toString()
  };
};

export default memo(() => {
  const { commonParam, handleChangeCommonParam, customParamResult } = useMainPage({
    defaultCommonParam,
    commonParamFormatter,
    customConfig: [
      {
        isTable: true,
        defaultParam: defaultCustomParam,
        paramFormatter: customParamFormatter
      }
    ]
  });

  const [tableResult] = customParamResult;
  const { param, fmtParam, handleChangePagination, handleChangeCustomParam } = tableResult;

  return (
    <>
      <Panel className="page-toolbar">
        <Toolbar filterParams={commonParam} handleChangeCommonParam={handleChangeCommonParam} />
      </Panel>
      <Panel className="panel-table">
        <Table
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          handleChangeCustomParam={handleChangeCustomParam}
        />
      </Panel>
    </>
  );
});
