import React, { memo, useCallback } from 'react';

import Dialog, { UseModalResult } from '@/components/Confirm';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

export default memo((props: UseModalResult) => {
  const { formatMessage } = useFmtMsgFori18n();
  const { data, resetModal, onFetch, refetchTableData, ...rest } = props;
  const { id } = data;

  const onHide = useCallback(() => {
    resetModal && resetModal();
  }, [resetModal]);

  const onSubmit = useCallback(() => {
    onFetch({
      params: {
        id
      },
      data: {
        status: 'DELETED'
      }
    })
      .then(() => {
        refetchTableData && refetchTableData();
      })
      .finally(() => {
        resetModal && resetModal();
      });
  }, [resetModal, onFetch, id, refetchTableData]);

  return (
    <Dialog {...rest} onSubmit={onSubmit} onHide={onHide} title={formatMessage('i18n_deleteTask')}>
      <>
        <div style={{ marginBottom: 18 }}>{formatMessage('i18n_deleteTaskConfirm')}</div>
      </>
    </Dialog>
  );
});
