import React from 'react';

import { generateComponent } from '@/components/Layouts/NormalLayout';
import { crossCampaignMenu } from '@/constants/MenuConfig';

import { CommonCampaignRoutes, CampaignType } from '../common';

const pathToStringUrlParam = { campaignId: ':campaignId' };
const crossCampaignRoute = generateComponent(crossCampaignMenu, pathToStringUrlParam); // react-router的路由要全部加载不然switch就重定向走了

export default () => {
  return (
    <CommonCampaignRoutes type={CampaignType.CROSS_CAMPAIGN} routesConfig={crossCampaignRoute} />
  );
};
