import * as React from 'react';
import classnames from 'classnames';
import { compose, setDisplayName } from 'recompose';

interface Props {
  className?: any;
  inline?: boolean;
  gap?: 'vertical' | 'horizontal';
  children?: React.ReactNode;
}

const defaultPrefix = (className: string) => ['card-wrap', className].join('-');

export default compose(setDisplayName('Card'))(
  React.memo(function Card({ className, inline, gap, ...props }: Props) {
    return (
      <div
        className={classnames(
          'card-wrap',
          { inline },
          gap && defaultPrefix(`${gap}-gap`),
          className
        )}
        {...props}
      />
    );
  })
);
