import React, { useEffect, useState, useMemo, useContext } from 'react';
import _ from 'lodash';
import { Switch, Redirect, useHistory } from 'react-router-dom';
import { Alert } from 'rsuite';

import BasicRoute from '@/components/Routes/BasicRoute';
import NormalLayout from '@/components/Layouts/NormalLayout';
import { campaignMenu, crossCampaignMenu } from '@/constants/MenuConfig';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import { getValidMenus, useAuthPath } from '@/utils/auth';
import { goFirstPage } from '@/utils/location';

import Base from './base';
import { ProjectNav } from './ProjectNav';
import { onFetchAnyCampaignDetail, onFetchCampaignRoute } from './actions';
import { CampaignCtx, CampaignCtxProps } from './context';
import { CampaignType } from './types.d';

interface CampaignLayoutProps {
  children: React.ReactChildren | React.ReactChild;
  type: CampaignType;
}

const CampaignLayout = React.memo(({ children, type }: CampaignLayoutProps) => {
  const campaignId = useCampaignId();
  const { replace } = useHistory();
  const [campaignDetail, setCampaignDetail] = useState({});
  const [campaignValidUrls, setCampaignValidUrls] = useState([]);
  const [campaignDetailLoading, setCampaignDetailLoading] = useState(true);
  const [campaignValidUrlsLoading, setCampaignValidUrlsLoading] = useState(true);
  const ctxVal: CampaignCtxProps = useMemo(
    () => ({
      // 这个应该放在这里，因为这个campaignDetail里面的campaign的信息侧边栏也用到了
      type,
      campaignDetail,
      campaignDetailLoading,
      campaignValidUrls,
      campaignValidUrlsLoading
    }),
    [type, campaignDetail, campaignDetailLoading, campaignValidUrls, campaignValidUrlsLoading]
  );

  const menu = useMemo(() => {
    const menuConfigToCompare = type === CampaignType.CAMPAIGN ? campaignMenu : crossCampaignMenu;
    // 默认是空的，因为在这个campaignInfo的接口没有返回的时候 还不知道那些路由是可以显示的
    return getValidMenus({}, menuConfigToCompare, campaignValidUrls, true);
  }, [campaignValidUrls, type]);

  useEffect(() => {
    setCampaignDetailLoading(true);
    onFetchAnyCampaignDetail(type, { campaignId })
      .then(campaignDetailRes => {
        const status = _.get(campaignDetailRes, 'status', 0);
        if (status === 3) {
          // 请求成功
          setCampaignDetail(_.get(campaignDetailRes, 'data'));
          setCampaignDetailLoading(false);
          setCampaignValidUrlsLoading(true);
          onFetchCampaignRoute({
            ...(type === CampaignType.CAMPAIGN ? { campaignId } : { crossCampaignId: campaignId })
          }).then(routeRes => {
            const routeStatus = _.get(routeRes, 'status', 0);
            if (routeStatus === 3) {
              // 请求成功
              const urls = _.get(routeRes, 'data.campaignValidUrl', []);
              setCampaignValidUrls(urls);
              setCampaignValidUrlsLoading(false);
            } else {
              goFirstPage('', replace);
            }
          });
        } else if (status === 8) {
          Alert.error(_.get(campaignDetailRes, 'errors.id', ''));
          setTimeout(() => {
            goFirstPage('', replace);
          }, 2000);
        } else {
          goFirstPage('', replace);
        }
      })
      .catch(() => {
        setCampaignDetailLoading(false);
      });
  }, [campaignId, replace, type]);

  return (
    <CampaignCtx.Provider value={ctxVal}>
      <NormalLayout menuConfig={menu} renderProjectNav={ProjectNav}>
        {children}
      </NormalLayout>
    </CampaignCtx.Provider>
  );
});

// 针对campaign鉴权
const CampaignAuthHOC = React.memo((props: any) => {
  const { campaignValidUrls, campaignValidUrlsLoading, campaignDetailLoading } = useContext(
    CampaignCtx
  );
  const result = useAuthPath(!campaignValidUrlsLoading, campaignValidUrls); // campaign下面的和campaign的过滤
  if (campaignValidUrlsLoading || campaignDetailLoading || !result) {
    return null;
  }

  return props.children;
});

interface IndexProps {
  type: CampaignType;
  routesConfig: Array<any>;
}

const Routes = React.memo(({ type, routesConfig }: IndexProps) => {
  const redirectUrl =
    type === CampaignType.CAMPAIGN
      ? '/campaigns/:campaignId/overview'
      : '/cross-campaigns/:campaignId/audiences-profile';
  const campaignId = useCampaignId();

  return (
    // 这里需要加个key因为希望做到的是，campaignId不同，完全重新渲染
    <CampaignLayout key={campaignId} type={type}>
      <CampaignAuthHOC>
        <BasicRoute component={Base}>
          <Switch>
            {routesConfig}
            <Redirect to={redirectUrl} /> {/** 主要是给 /campaigns/22/ || /campaigns/22 用的 */}
          </Switch>
        </BasicRoute>
      </CampaignAuthHOC>
    </CampaignLayout>
  );
});

export default Routes;
