import React, { useCallback, useContext } from 'react';
import _ from 'lodash';
import { FormGroup, ControlLabel, HelpBlock } from 'rsuite';
import HelpInfo from '@/components/HelpInfo';
import { FormDataContext, FormErrorContext } from '@/components/Form/FormContext';

export default React.memo(
  ({
    className,
    label,
    required,
    help,
    names,
    message,
    accepter: Component,
    checkTrigger,
    onChange,
    ...props
  }: any) => {
    const { formData, onChange: onFormChange } = useContext(FormDataContext);
    const { checkForm, formErrors, setFormErrors } = useContext(FormErrorContext);

    const handleChange = useCallback(
      (key: string) => (val: any) => {
        _.isFunction(onFormChange) && onFormChange(val, key);
        checkTrigger !== 'none' &&
          _.isFunction(checkForm) &&
          checkForm(key, val).then(err => {
            setFormErrors(prevErrors => ({
              ...prevErrors,
              [key]: err
            }));
          });
        _.isFunction(onChange) && onChange(val, key);
      },
      [checkForm, onChange, onFormChange, setFormErrors, checkTrigger]
    );

    return (
      <FormGroup className={className}>
        {label && (
          <ControlLabel>
            {label}
            {required && <span style={{ color: 'red' }}> *</span>}
            {help && <HelpInfo message={help} />}
          </ControlLabel>
        )}
        <Component
          values={_.pick(formData, names)}
          errors={_.pick(formErrors, names)}
          onChange={handleChange}
          {...props}
        />
        {message && <HelpBlock>{message}</HelpBlock>}
      </FormGroup>
    );
  }
);
