import React, { memo, useCallback, useRef, useState, useEffect } from 'react';
import _ from 'lodash';
import { Alert, Button, Divider } from 'rsuite';
import { Link, RouteComponentProps } from 'react-router-dom';

import * as APIs from '@/constants/APIs';
import SearchInput from '@/components/Table/Tools/SearchInput';
import SelectPicker from '@/components/Picker/SelectPicker';
import DropDown from '@/components/Picker/DropDown';
import Table, { Column, Cell, HeaderCell, tableUtils } from '@/components/Table';
import { FloatActionCell, CampaignStatusCell, DateCell, TextCell } from '@/components/Table/Cells';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { getShortNameById, BasicReportProps } from '@/constants/config';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import { useModal } from '@/components/hooks/useModal';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { getUserIdFromProfile } from '@/utils/location';
import Modal from './Modal';
import ImgPreviewCell from './part/ImgPreviewCell';
import {
  primaryData,
  encodeTypes,
  SELECTED,
  ALL,
  NEW,
  filterParamKeys,
  defaultFilter,
  dropDownData
} from './constants';

const tableDataChecker = (param: any) => _.get(param, filterParamKeys.CAMPAIGN, []).length;

type TableProps = Partial<BasicReportProps & RouteComponentProps<{}>> & {
  handleFormCreate: Function;
  handleFormEdit: Function;
};
export default memo(
  ({
    location,
    filterParams,
    tableParams,
    setTableParams,
    refetchTableData,
    handleSearchWord,
    handleChangeCustomParam,
    handleFormCreate,
    handleFormEdit
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const dropDownDataIntl = usePickerDataFromObj(dropDownData, { labelKey: 'label' });
    const encodeTypesIntl = usePickerDataFromObj(encodeTypes, { skipNoi18nPrefix: true });
    const userId = getUserIdFromProfile();

    const [localTableData, setLocalTableData] = useState(extractTableData({}));

    const handleFetchSuccess = useCallback((rsp: CommonResponse) => {
      setLocalTableData(prevState => ({
        ...prevState,
        data: _.get(rsp, 'data.records')
      }));
    }, []);

    const { tableData, onChange, onExport } = useTable({
      tableParams,
      setTableParams,
      api: APIs.API_ADGROUP,
      requestChecker: tableDataChecker,
      onFetchSuccess: handleFetchSuccess
    });

    const cancelModal = useModal(false, {}, APIs.API_ADGROUP_PUT, refetchTableData);

    useEffect(() => {
      setLocalTableData(prevState => ({
        ...extractTableData(tableData),
        data: tableDataChecker(filterParams) ? _.get(prevState, 'data', []) : []
      }));
    }, [tableData.loading, filterParams.campaignId]);

    const tableRef = useRef(null);
    const [encode, setEncode] = useState('');

    const renderActions = useCallback(
      rowData => {
        const editAble = !_.includes(['CANCELLED', 'COMPLETE'], rowData.status);

        const cancelClickHandler = () => {
          cancelModal.showModal({
            ...rowData,
            status: 0
          });
        };

        return [
          <FloatActionCell.Actions.Edit
            key="edit"
            onClick={() => {
              handleFormEdit({
                id: rowData.id
              });
            }}
          />,
          <FloatActionCell.Actions.Cancel
            key="cancel"
            onClick={cancelClickHandler}
            disabled={!editAble}
          />
        ];
      },
      [cancelModal, handleFormEdit]
    );

    const handleStatusChanged = useCallback(
      (status: string) => {
        handleChangeCustomParam({
          [filterParamKeys.STATUS]: status
        });
      },
      [handleChangeCustomParam]
    );

    const handleEncodeChanged = useCallback((val: string) => {
      setEncode(val);
    }, []);

    const handleExportClicked = useCallback(
      params => {
        // 选择导出所选时，必须要有已选值
        if (_.isString(params.ids) && !params.ids) {
          Alert.warning(formatMessage('i18n_exportWarning'));
          return;
        }
        onExport &&
          onExport(
            {
              ...params,
              userId,
              page: undefined,
              pageSize: undefined
            },
            APIs.API_PLACEMENT_EXPORT
          );
      },
      [onExport, userId]
    );

    const handleDropDownSelect = useCallback(
      key => {
        const { data } = localTableData;
        const checkedValues = tableUtils(tableRef).getCheckValues();
        let params = { encode } as any;
        switch (key) {
          case ALL:
            break;
          case SELECTED:
            params.ids = _.intersection(checkedValues, data.map(i => i.id)).join(',');
            break;
          case NEW:
            params.status = 'DRAFT';
            break;
          default:
            break;
        }

        handleExportClicked(params);
      },
      [encode, localTableData, handleExportClicked]
    );

    // 定义table toolbar
    const renderToolbar = (): React.ReactElement[] => {
      return [
        <Button key="create" appearance="primary" onClick={handleFormCreate as () => void}>
          {formatMessage('i18n_create')}
        </Button>,
        <SelectPicker
          key="secondary"
          prepend={formatMessage('i18n_status')}
          data={primaryData}
          value={_.get(filterParams, filterParamKeys.STATUS, defaultFilter.status)}
          onChange={handleStatusChanged}
        />,
        <SearchInput
          key="search"
          placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
          onChange={handleSearchWord}
        />,
        <Divider vertical key="divider" />,
        <SelectPicker
          key="encode"
          prepend={formatMessage('i18n_encode')}
          data={encodeTypesIntl}
          value={encode}
          onChange={handleEncodeChanged}
        />,
        <DropDown
          data={dropDownDataIntl as any}
          title={formatMessage('i18n_exportCode')}
          appearance="default"
          key="exportCode"
          onSelect={handleDropDownSelect}
        />,
        <Button
          key="keyword"
          className="pull-right"
          componentClass={Link}
          to={`${location.pathname}/keyword`}
        >
          {formatMessage('i18n_keywordCode')}
        </Button>,
        <Button
          key="exportTable"
          className="pull-right"
          componentClass={Link}
          to={`${location.pathname}/upload`}
        >
          {formatMessage('i18n_batchUpload')}
        </Button>
      ];
    };

    return (
      <>
        <Modal {...cancelModal} />
        <Table
          {...localTableData}
          checkDataKey="id"
          innerRef={tableRef}
          renderToolbar={renderToolbar}
          onChange={onChange}
        >
          <Column width={100} fixed sortable>
            <HeaderCell>ID</HeaderCell>
            <Cell dataKey="id" />
          </Column>
          {/* 广告点位名称 */}
          <Column width={200} resizable fixed sortable>
            <HeaderCell>{formatMessage('i18n_placementName')}</HeaderCell>
            <ImgPreviewCell dataKey="name" imgKey="thumbnailUrl" renderActions={renderActions} />
          </Column>
          {/* 推广计划名称 */}
          <Column width={200} resizable sortable>
            <HeaderCell>{formatMessage('i18n_campaignName')}</HeaderCell>
            <FloatActionCell dataKey="campaignName" />
          </Column>
          {/* 主要拥有者 */}
          <Column width={200} resizable sortable>
            <HeaderCell>{formatMessage('i18n_primaryOwner')}</HeaderCell>
            <TextCell
              dataKey="primary"
              formatter={dataa =>
                `${getShortNameById(_.get(dataa, 'role', ''))}: ${_.get(dataa, 'beName', '')}`
              }
              maxLine={1}
            />
          </Column>
          {/* AdServer 广告点位 ID */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_adserverAdunitId')}</HeaderCell>
            <FloatActionCell dataKey="adserverPid" />
          </Column>
          {/* 站点ID */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_siteId')}</HeaderCell>
            <FloatActionCell dataKey="siteId" />
          </Column>
          {/* 站点名称 */}
          <Column width={200} resizable sortable>
            <HeaderCell>{formatMessage('i18n_siteName')}</HeaderCell>
            <FloatActionCell dataKey="siteName" />
          </Column>
          {/* 状态 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_status')}</HeaderCell>
            <CampaignStatusCell dataKey="status" />
          </Column>
          {/* 素材名 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_material')}</HeaderCell>
            <FloatActionCell dataKey="creative" />
          </Column>
          {/* 设备 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_device')}</HeaderCell>
            <FloatActionCell dataKey="targetDevice" />
          </Column>
          {/* 广告位置 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_adPosition')}</HeaderCell>
            <FloatActionCell dataKey="position" />
          </Column>
          {/* 素材大小 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_materialSize')}</HeaderCell>
            <FloatActionCell dataKey="program" />
          </Column>
          {/* 买法 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_buyMethod')}</HeaderCell>
            <FloatActionCell dataKey="buyMethod" />
          </Column>
          {/* 频次控制 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_frequencyCap')}</HeaderCell>
            <FloatActionCell dataKey="frequencyCap" />
          </Column>
          {/* 定投区域 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_targetedGeo')}</HeaderCell>
            <FloatActionCell dataKey="targetGeo" />
          </Column>
          {/* 备注 */}
          <Column width={200}>
            <HeaderCell>{formatMessage('i18n_remark')}</HeaderCell>
            <FloatActionCell dataKey="remark" />
          </Column>
          {/* 开始日期 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_beginDate')}</HeaderCell>
            <DateCell dataKey="startDate" />
          </Column>
          {/* 结束日期 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_finishDate')}</HeaderCell>
            <DateCell dataKey="endDate" />
          </Column>
          {/* 创建者 */}
          <Column width={200} sortable>
            <HeaderCell>{formatMessage('i18n_creator')}</HeaderCell>
            <FloatActionCell dataKey="creatorName" />
          </Column>
        </Table>
      </>
    );
  }
);
