import React, { memo, useCallback, useEffect, useState } from 'react';
import { Panel, Divider, Alert, Form, Icon, Button, Tag } from 'rsuite';
import Icons from '@/assets/icons';
import _ from 'lodash';
import { beRoleData } from '@/constants/config';
import { API_CPO_AUDIT } from '@/constants/APIs';
import fetch, { FETCH_METHOD } from '@/utils/request/fetch';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import RESPONSE_STATUS from '@/constants/responseStatus';
import CustomLabel from './CustomLabel';

const mockData = {
  campaign: {
    id: 4,
    name: 'T-cmpC-状态3-控制文件测试请勿修改',
    primary: {
      beId: 64,
      role: 2,
      beName: 'Hypers02'
    },
    secondaries: [],
    status: 3,
    s2sPlatformId: '',
    adunitRelate: true,
    advertiserId: 1,
    advertiserName: 'test',
    brandId: 1,
    brandName: 'sd',
    agentId: null,
    measure: false,
    tencent: '',
    notes: 'test',
    targetStartAge: 2,
    targetEndAge: '+',
    target: 2,
    extTraffic: true,
    ivtVendor: null,
    monitorType: 3,
    start_date: '2020-03-12',
    end_date: '2021-12-16',
    unique_field: {
      city_ue: 'd2e6f922bccd11e992e81c39479ab3cf',
      mapping_demo: '2c2816329c9511e9a9e4000ec6dd14aa',
      device_type_index: '2c2bbe689c9511e9a9e4000ec6dd14aa',
      platform_type_index: '2c2eee449c9511e9a9e4000ec6dd14aa',
      sharing_factor: '2c3292b09c9511e9a9e4000ec6dd14aa',
      non_coverage_factor: '2c35fda69c9511e9a9e4000ec6dd14aa',
      exponential_param: '2c3a88309c9511e9a9e4000ec6dd14aa',
      preprocess_python_so: '2c3e84ee9c9511e9a9e4000ec6dd14aa',
      frequency_model_param: '2c4580789c9511e9a9e4000ec6dd14aa',
      group_demo: '2c49c8229c9511e9a9e4000ec6dd14aa',
      frequency_python_so: '2c4dd99e9c9511e9a9e4000ec6dd14aa',
      cross_cmp_python_so: '2c51552e9c9511e9a9e4000ec6dd14aa'
    },
    enableFreq: false,
    ivtVendorBrand: '',
    ivtType: '',
    viewability: false,
    mtaCategory: null,
    mtaVendor: null,
    isTest: false,
    frequency: false
  },
  cpoReport: {
    campaignId: 4,
    launchStartDate: '2020-09-09',
    launchEndDate: '2020-08-09',
    orderStartDate: '2020-09-09',
    orderEndDate: '2020-09-09',
    costGroup: {
      group1: [1, 2, 3],
      group2: [2, 3, 4]
    },
    conversions: '1,2,3',
    cost: 12,
    isAudit: true,
    status: 'ready',
    auditEmail: '12@qq.com',
    creator: 'admin_test111',
    created: '2020-05-15 20:05:11'
  }
};

export default memo(props => {
  const campaignId = _.get(props, 'match.params.campaignId');
  const { formatMessage } = useFmtMsgFori18n();
  const [data, setData] = useState<any>({});
  //获取campaign cpo详情
  const fetchData = useCallback(() => {
    fetch(API_CPO_AUDIT, {
      params: {
        campaignId
      }
    })
      .then((res: any) => {
        const data = _.get(res, 'data');
        setData(data);
      })
      .catch(error => {
        console.log(error);
        Alert.error('原始数据接口异常');
      });
  }, []);

  useEffect(() => {
    fetchData();
  }, []);

  const camData = _.get(data, 'campaign');
  const cpoData = _.get(data, 'cpoReport');

  const handlePass = useCallback(value => {
    fetch(API_CPO_AUDIT, {
      method: FETCH_METHOD.POST,
      data: {
        status: value,
        campaignId
      }
    })
      .then((rsp: any) => {
        const status = _.get(rsp, 'status');
        if (status === RESPONSE_STATUS.SUCCESS) {
          Alert.success(formatMessage('i18n_operationSucceeds'));
        }
      })
      .catch(() => {
        Alert.error('原始数据接口异常');
      });
  }, []);
  const renderHeader = useCallback(() => {
    return (
      <div style={{ color: '#272c36', fontSize: 16, position: 'relative' }}>
        {_.get(camData, 'name')}
        <div style={{ position: 'absolute', right: 0, top: 0 }}>
          <Button size="sm" onClick={() => handlePass('pass')} style={{ marginRight: 10 }}>
            <Icon icon={Icons.reviewPass} /> {formatMessage('i18n_cpoPass')}
          </Button>
          <Button size="sm" onClick={() => handlePass('refuse')}>
            <Icon icon={Icons.reviewRefuse} /> {formatMessage('i18n_cpoRefuse')}
          </Button>
        </div>
      </div>
    );
  }, [camData]);

  const boolToString = useCallback(value => {
    if (!value) {
      return formatMessage('i18n_close');
    }
    return formatMessage('i18n_open');
  }, []);

  //根据roleId获取名称
  const roleName = useCallback(id => {
    if (!id) {
      return null;
    }
    return beRoleData.filter(i => i.id === id)[0].name;
  }, []);
  //渲染次级拥有者
  const renderSec = useCallback(() => {
    const secondaries = _.get(camData, 'secondaries', []);
    return secondaries.map((i: any) => {
      return (
        <Tag key={i.beId}>
          BE{formatMessage('i18n_name')}：{i.beName} - {roleName(i.role)}
        </Tag>
      );
    });
  }, [camData]);
  //渲染投放组
  const renderGroup = useCallback(() => {
    const newGroup: any[] = [];
    const costGroup = _.get(cpoData, 'costGroup');
    !_.isEmpty(costGroup) &&
      Object.keys(costGroup).forEach(i => {
        newGroup.push({
          group: i,
          placement: costGroup[i]
        });
      });
    return newGroup.map(i => {
      return (
        <Tag key={i.group}>
          Group： {i.group}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          {formatMessage('i18n_placement')}：{i.placement}
        </Tag>
      );
    });
  }, [cpoData]);
  //渲染转化活动
  const renderConversion = useCallback(() => {
    const conversions = _.get(cpoData, 'conversions', '').split(',');
    return conversions.map((i: any) => {
      return <Tag key={i}>{i}</Tag>;
    });
  }, [cpoData]);
  return (
    <Panel bordered>
      {renderHeader()}
      <Divider />
      <Form>
        <CustomLabel label={formatMessage('i18n_description')} value={_.get(camData, 'notes')} />
        <CustomLabel
          label={formatMessage('i18n_primaryOwner')}
          value={
            'BE：' +
            _.get(camData, 'primary.beName') +
            ' ' +
            formatMessage('i18n_role') +
            '：' +
            roleName(_.get(camData, 'primary.role'))
          }
        />
        <CustomLabel
          label={formatMessage('i18n_brandFilter')}
          value={
            formatMessage('i18n_advertiser') +
            '：' +
            _.get(camData, 'advertiserName') +
            ' ' +
            formatMessage('i18n_brand') +
            '：' +
            _.get(camData, 'brandName')
          }
        />
        <CustomLabel label={formatMessage('i18n_secondaryOwner')} value={renderSec()} />
        <CustomLabel label={formatMessage('i18n_targetPerson')} value={_.get(camData, 'target')} />
        <CustomLabel
          label={formatMessage('i18n_ageRange')}
          value={_.get(camData, 'targetStartAge') + '-' + _.get(camData, 'targetEndAge')}
        />
        <CustomLabel
          label={formatMessage('i18n_monitorType')}
          value={_.get(camData, 'monitorType')}
        />
        <CustomLabel
          label={formatMessage('i18n_externalFlowMonitor')}
          value={boolToString(_.get(camData, 'extTraffic'))}
        />

        <CustomLabel
          label={formatMessage('i18n_test')}
          value={boolToString(_.get(camData, 'isTest'))}
        />
      </Form>
      <Divider />
      <div style={{ color: '#272c36', fontSize: 16, marginBottom: 18 }}>
        {formatMessage('i18n_cpoFormTitle')}
      </div>
      <Form>
        <CustomLabel
          label={formatMessage('i18n_cpoLaunchDate')}
          value={_.get(cpoData, 'launchStartDate') + ' ~ ' + _.get(cpoData, 'launchEndDate')}
        />
        <CustomLabel
          label={formatMessage('i18n_cpoOrderDate')}
          value={_.get(cpoData, 'orderStartDate') + ' ~ ' + _.get(cpoData, 'orderEndDate')}
        />
        <CustomLabel label={formatMessage('i18n_cpoGroup')} value={renderGroup()} />
        <CustomLabel
          label={formatMessage('i18n_conversionActivities')}
          value={renderConversion()}
        />
        <CustomLabel label={formatMessage('i18n_cpoCost')} value={_.get(cpoData, 'cost')} />
        <CustomLabel label={formatMessage('i18n_creator')} value={_.get(cpoData, 'creator')} />
        <CustomLabel label={formatMessage('i18n_createTime')} value={_.get(cpoData, 'created')} />
      </Form>
    </Panel>
  );
});
