import _ from 'lodash';
import React from 'react';
import { useFmtMsgFori18n } from '@/utils/formatMessage';

/**
 * 在Picker 输入框中添加前缀标签
 * 需转换placeholder和renderValue，组合时放在最后
 */

type Props = Record<string, any>;
type PropMapper<T = Props, K = Props> = (oldProps: T) => K;

export interface UsePrependProps {
  prepend?: React.ReactNode; // 前缀标签，未设置就不显示
  data?: any;
  value?: any;
  labelKey?: string;
  valueKey?: string;
  placeholder?: React.ReactNode;
  renderValue?: Function;
}

export interface OutProps {
  placeholder: React.ReactNode;
  renderValue: Function;
  labelKey: string;
  valueKey: string;
}

const { useCallback, useEffect } = React;

const usePrependForPicker: PropMapper<UsePrependProps, OutProps> = ({
  prepend,
  data,
  value,
  labelKey,
  valueKey,
  placeholder,
  renderValue,
  ...props
}) => {
  const { formatMessage } = useFmtMsgFori18n();

  const renderValueWithPrepend = useCallback(
    (values: any, items: any, elements: any) => {
      const prependElement = prepend ? ( // 前缀
        <span className="picker-prepend-filled">{`${prepend}:`}&nbsp;</span>
      ) : (
        ''
      );
      let content: React.ReactElement; // 内容

      if (_.isFunction(renderValue)) {
        // 若已定义renderValue，则直接调用
        content = renderValue(values, items, elements);
      } else if (_.isArray(items)) {
        // 若是层级数据，需计数
        const labels = items.map(i => _.get(i, labelKey, '')).filter(i => i !== '');
        const count = labels.length;
        content = (
          <>
            <span className="rs-picker-value-list">{labels.join(', ')}</span>
            <span className="rs-picker-value-count" title={count.toString()}>
              {count > 99 ? '99+' : count}
            </span>
          </>
        );
      } else {
        // 若是单级数据，直接取labelKey
        content = _.get(items, labelKey, '');
      }

      return (
        <>
          {prependElement}
          {content}
        </>
      );
    },
    [prepend, labelKey, renderValue]
  );

  return {
    labelKey,
    valueKey,
    placeholder: (
      <>
        {prepend && (
          <span className="picker-prepend-empty">
            {prepend}
            {placeholder ? ': ' : ''}
          </span>
        )}
        {placeholder || (prepend ? '' : formatMessage('i18n_select', { name: '' }))}
      </>
    ),
    renderValue: renderValueWithPrepend,
    data,
    value,
    ...props
  };
};

export default usePrependForPicker;
