import { useState, useCallback } from 'react';

import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';

/**
 * 这里保存data 和 Modal的一些值，同时封装了Modal常用的fetch loading 等等status
 */
export interface UseModalResult {
  show: boolean;
  data: any;
  showModal: (nextData?: any) => void;
  resetModal: () => void;
  loading: boolean;
  onFetch: (options?: any) => Promise<any>;
  refetchTableData: Function;
}

export const useModal = (
  defaultShow = false,
  defaultData = {},
  api: Url,
  refetchTableData?: Function,
  method = FETCH_METHOD.PUT
): UseModalResult => {
  const { loading, onFetch } = useFetch<CommonResponse>(method, api, {
    cacheParams: false,
    params: {}
  });

  const [modal, setModal] = useState({
    show: defaultShow,
    data: defaultData
  });

  const showModal = useCallback(
    (nextData = defaultData) => {
      setModal(prevData => ({
        ...prevData,
        show: true,
        ...(nextData && { data: nextData })
      }));
    },
    [defaultData]
  );

  const resetModal = useCallback(() => {
    setModal({
      show: false,
      data: defaultData
    });
  }, [defaultData]);

  return {
    ...modal,
    showModal,
    resetModal,
    loading,
    onFetch,
    refetchTableData
  };
};
