import React, { useCallback } from 'react';
import _ from 'lodash';
import Cell, { BaseCellProps } from '@/components/Table/Cells/FloatActionCell';

export default ({ rowData, dataKey, ...props }: BaseCellProps<any>) => {
  const data = _.get(rowData, dataKey) || null;
  const list = _.split(data, ';');

  const renderContent = useCallback(() => {
    return <span>{data}</span>;
  }, [data]);

  const renderTitle = useCallback(() => {
    return (
      <span>
        {_.map(list, (text, index) => {
          const notLast = index < list.length - 1;
          return (
            <React.Fragment key={index}>
              <span>
                {_.trim(text)}
                {notLast ? ';' : null}
              </span>
              {notLast ? <br /> : null}
            </React.Fragment>
          );
        })}
      </span>
    );
  }, [list]);

  return (
    <Cell
      dataKey={dataKey}
      rowData={rowData}
      renderContent={renderContent}
      renderTitle={renderTitle}
      {...props}
    />
  );
};
