import React, { memo, useCallback } from 'react';
import { Panel } from 'rsuite';
import _ from 'lodash';

import { API_SITE } from '@/constants/APIs';
import TreePicker from '@/components/Picker/TreePicker';
import { useMainPage } from '@/components/hooks/useMainPage';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { TODAY } from '@/constants/config';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import Chart from '@/components/Chart/ReportCustomChart';

// part
import DailyImp from './part/DailyImp';
import DailyClk from './part/DailyClk';
import Hourly from './part/Hourly';
import Card from './part/Card';
import HorizontalBar from './part/HorizontalBar';
import Map from './part/Map';
import { basicClassName, prefixClassName } from './constants';

const defaultCommonParam = {
  selectObj: {
    key: null as any,
    value: null as any
  }
};

export default memo(() => {
  const campaignId = useCampaignId();
  const { formatMessage } = useFmtMsgFori18n();

  const { commonParam, fmtCommonParam, handleChangeCommonParam } = useMainPage({
    defaultCommonParam
  });

  // 暂时根据node的refKey判断节点深度 示例 0-0-1 是site节点
  const handleChange = useCallback(
    (activeNode, value) => {
      const reg = /\d+-\d+-\d+/;
      const key = reg.test(activeNode.refKey) ? 'placementId' : 'siteId';
      handleChangeCommonParam({
        selectObj: {
          key,
          value
        }
      });
    },
    [handleChangeCommonParam]
  );

  const handleClean = useCallback(() => {
    handleChangeCommonParam({
      selectObj: {
        key: 'siteId',
        value: null
      }
    });
  }, [handleChangeCommonParam]);

  const obj: {
    siteId: any;
    placementId: any;
  } =
    commonParam.selectObj.key === 'siteId'
      ? { siteId: commonParam.selectObj.value, placementId: null }
      : { siteId: null, placementId: commonParam.selectObj.value };
  return (
    <>
      <div className={basicClassName}>
        <Panel className="page-toolbar">
          <div className="today-label">{TODAY}</div>
          <TreePicker
            prepend={formatMessage('i18n_menuSiteAdunit')}
            url={API_SITE}
            style={{ maxWidth: '300px' }}
            menuStyle={{ maxWidth: '400px', minWidth: '224px' }}
            params={{ campaignId, sub: 1, status: 1 }}
            defaultExpandAll
            supportAll
            appearance="subtle"
            childrenKey="sub"
            onSelect={handleChange}
            onClean={handleClean}
            notFormat
            value={_.get(commonParam, 'selectObj.value', '')}
          />
        </Panel>
        <DailyImp id={campaignId} filterParams={fmtCommonParam} />
        <DailyClk id={campaignId} filterParams={fmtCommonParam} />
        <Hourly id={campaignId} filterParams={fmtCommonParam} />
        <div className={prefixClassName('card')}>
          <Card
            title={`${formatMessage('i18n_areaTop10')} (${formatMessage('i18n_lastThirtyMins')})`}
            className="cardmap"
          >
            <Map id={campaignId} tag="geo" filterParams={fmtCommonParam} />
          </Card>
          <Card
            title={`${formatMessage('i18n_siteTop5')} (${formatMessage('i18n_lastThirtyMins')})`}
          >
            <HorizontalBar
              id={campaignId}
              tag="site"
              labelKey="name"
              valueKey="sum"
              filterParams={fmtCommonParam}
            />
          </Card>

          <Card
            title={`${formatMessage('i18n_placementTop5')} (${formatMessage(
              'i18n_lastThirtyMins'
            )})`}
          >
            <HorizontalBar
              id={campaignId}
              tag="placement"
              labelKey="name"
              valueKey="sum"
              filterParams={fmtCommonParam}
            />
          </Card>
        </div>
        <Chart
          dateParams={{
            start: TODAY,
            end: TODAY
          }}
          postParams={{
            id: campaignId,
            dqa: 'realtime',
            hierarchy: 'campaign',
            report: 'campaign',
            secondary: 'hourly',
            ...obj
          }}
          interval
          title={formatMessage('i18n_todayHourTrend')}
          customType={1}
          pure
        />
      </div>
    </>
  );
});
