// 百分比tooltips
export const TOOL_PER = {
  trigger: 'axis',
  formatter(params: any) {
    let data = _.isArray(params) ? params[0] : params;
    let val;
    if (data.value === 0) {
      val = '0.00%';
    } else {
      val = data.value ? toPercent(data.value) : '';
    }
    return val;
  }
};

// 百分比坐标
export const AXIS_PER = (data: any) => {
  const max = toAxisPer(data.max) || 1;
  const splitNumber = data.splitNumber || SPLIT_NUM;
  return {
    type: 'value',
    axisLine: {
      show: false
    },
    axisTick: {
      show: false
    },
    max,
    min: 0,
    splitNumber: data.splitNumber || null,
    interval: max / splitNumber,
    axisLabel: {
      formatter(val: any) {
        return toPercent(val);
      }
    }
  };
};

