// 视觉映射组件
export const VISUAL_MAP = (option: object) => {
  return _.merge(
    {
      type: 'piecewise',
      show: true,
      splitNumber: SPLIT_NUM,
      min: 0,
      max: 10,
      text: [getDict('i18n_maxValue'), getDict('i18n_minValue')],
      inRange: {
        color: ['#C6D9E8', '#4E85B7']
      },
      orient: 'horizontal',
      inverse: true,
      left: 0,
      bottom: 0,
      itemGap: 5
    },
    option
  );
};

// 数值坐标
export const AXIS_NUM = (data: any) => {
  let maxOption: object = {
    min: 0,
    max: 4,
    interval: 1
  };
  if (!data) {
    maxOption = {};
  }
  if (data && (data.seriesData !== undefined || data.max !== undefined)) {
    let max, maxData;
    if (data.max !== undefined) {
      max = data.max;
    }
    if (data.seriesData !== undefined) {
      maxData = data.seriesData.map(item => _.max(item.data));
      max = _.max(maxData);
    }
    const base = Number(1 + Array(max.toString().length).join('0'));

    const per = base * SPLIT_NUM;
    let trueMax = max;
    if (max >= base && max <= per) {
      trueMax = per;
    }
    if (max > per) {
      let i = 0;
      while (per * i < max) {
        i++;
        trueMax = per * i;
      }
    }
    maxOption = {
      max: trueMax <= 4 ? 4 : trueMax,
      interval: trueMax <= 4 ? 1 : trueMax / 4
    };
  }

  return {
    nameLocation: 'end',
    nameTextStyle: {
      fontWeight: 400,
      fontSize: 14,
      color: '#272C36'
    },
    axisLine: {
      show: false
    },
    axisTick: {
      show: false
    },
    splitNumber: SPLIT_NUM,
    nameGap: 30,
    axisLabel: {
      formatter(val: any) {
        return toShortNum(val);
      }
    },
    ...data,
    ...maxOption
  };
};

