// 数值坐标
export const formatAxisToNumber = (text: any) => toShortNum(text);
// 百分比坐标
export const formatAxisToPer = (text: any) => toPercent(text);

// 坐标最大值格式化
export const formatAxisMax = (val: any) => {
  let { max } = val;

  const base = Number(1 + Array(max.toString().length).join('0'));
  const per = base * SPLIT_NUM;
  let trueMax = max;
  if (max >= base && max <= per) {
    trueMax = per;
  }
  if (max > per) {
    let i = 0;
    while (per * i < max) {
      i++;
      trueMax = per * i;
    }
  }
  max = trueMax <= 4 ? 4 : trueMax;
  return max;
};

export const formatAxisMaxToPer = (val: any) => {
  let { max } = val;
  return toAxisPer(max);
};
