export enum filterParamKeys {
  STATUS = 'status',
  BEID = 'beId',
  CLASSIFY = 'classify',
  TIME_DIMENSION = 'secondary',
  DATE = 'date'
}

export interface CommonParam {
  date: Date;
  beId: Array<number>;
  classify: string;
}

export interface CustomParam {
  status: Array<number>;
  secondary: string;
}

export type FilterParam = CommonParam & CustomParam;

export const defaultCommonParam: CommonParam = {
  date: getYesterday(),
  classify: typeData[0].id,
  beId: []
  // date: new Date('2020-04-07'),
  // classify: typeData[0].id,
  // beId: [31,94,48,54,80,111,119,34,132,87,129,124,75,125,289,291,39,133,50,69,3,20,49,33,37,63,60,61,17,18,19,25,26,27,28,29,30,72,74,67,32,38,46,112,86,84,110,76,123,64,118,95,85,115,51,58,62,55,56],
};

export const defaultCustomParam: CustomParam = {
  status: [1],
  secondary: secondaryData[0].id
};

export const defaultFilter: FilterParam = {
  ...defaultCommonParam,
  ...defaultCustomParam
};

export const utilsFn = {
  getChosenBE(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.BEID, defaultFilter.beId);
  },
  getClassifyValue(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.CLASSIFY, defaultFilter.classify);
  },
  getTimeDimensionValue(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.TIME_DIMENSION, defaultFilter.secondary);
  },
  getIsCross(filterParams: FilterParam) {
    return this.getClassifyValue(filterParams) !== typeData[0].id;
  },
  getIsDaily(filterParams: FilterParam) {
    return this.getTimeDimensionValue(filterParams) === secondaryData[1].id;
  },
  getDate(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.DATE, defaultFilter.date);
  },
  getStatus(filterParams: FilterParam) {
    return _.get(filterParams, filterParamKeys.STATUS, defaultFilter.status);
  }
};
