import _ from 'lodash';

export const toThousands = (num: any, digits = 0) =>
  (num === null || num === undefined || num === '--' || Number.isNaN(num)
    ? '--'
    : (+num).toFixed(digits)
  ).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,');
export const toPercent = (num: any, digits = 2, forcePercentMark = false) =>
  !forcePercentMark && (num === null || num === undefined || Number.isNaN(num))
    ? '--'
    : `${toThousands(num * 100, digits)}%`;

export const percentToNumber = (value: string | number) =>
  value === null || value === undefined || value === '--'
    ? '0'
    : (parseFloat(value as string) / 100).toFixed(4);

export const checkNumber = (value: any) => !_.isNaN(parseFloat(value));

export function toShortNum(num: any) {
  if (!checkNumber(num)) {
    return '--';
  }
  num = Number.parseFloat(num);
  if (num >= 0 && num < 1000) {
    return num.toFixed(0);
  }
  if (num >= 1000 && num < 1000000) {
    return `${(num / 1000).toFixed(0)}K`;
  }
  if (num >= 1000000 && num < 1000000000) {
    return `${(num / 1000000).toFixed(0)}M`;
  }
  if (num >= 1000000000 && num < 1000000000000) {
    return `${(num / 1000000000).toFixed(0)}B`;
  }
  return num;
}

export function toAxisPer(num: number) {
  if (Number.isNaN(num)) {
    return num;
  }
  if (num < 0.01) {
    return 0.01;
  }
  num = Math.ceil(num * 10) / 10;
  return num;
}

type PickerData =
  | string
  | {
      id: number;
      name: string;
      children?: PickerData[];
      sub?: PickerData[];
    };

interface PickerParamItem {
  id: number;
  children: number[];
}

/**
 * 转换请求获取到的picker data的格式
 * 对于children，用加号拼合父子id构成其value
 * {id: 1, name: 'A', children: [{id: 11, name: 'AA'}]}  =>
 * {value: '1', label: 'A', children: [{value: '1+11', label: 'AA'}]}
 * @param data 原始picker data数组，元素可能是字符串，可能是对象
 * @param valuePrefix value前缀，用于递归
 * @param showId 是否在label中添加id前缀
 * */
export function toPickerData(data: PickerData[] = [], valuePrefix = '', showId = false) {
  if (!_.isArray(data)) return [];
  return data.map(item => {
    if (typeof item === 'string') return { value: item, label: item };

    const newItem: any = {
      label: showId ? `${item.id}-${item.name}` : item.name,
      value: valuePrefix === '' ? `${item.id}` : `${valuePrefix}+${item.id}`
    };
    let children = item.children || item.sub;
    if (children && _.isArray(children) && _.size(children) > 0)
      newItem.children = toPickerData(children, newItem.value, showId);
    return newItem;
  });
}

/**
 * 把picker value中加号拼合的形式，转换成请求参数的形式
 * ['1', '2+34', '2+35'] =>
 * [{id: 1, children: []}, {id: 2, children: [34, 35]}]
 * */
export function fmtPickerData(value: string[]): PickerParamItem[] {
  const result: PickerParamItem[] = [];
  value.forEach(item => {
    const [parentId, childId] = item.split('+').map(_.parseInt);
    if (_.isUndefined(childId)) result.push({ id: parentId, children: [] });
    else {
      const existed = result.find(i => i.id === parentId);
      if (existed) {
        existed.children.push(childId);
      } else {
        result.push({ id: parentId, children: [childId] });
      }
    }
  });
  return result;
}

/**
 * 上述过程的逆过程，把请求参数的形式转化为picker value的形式
 * [{id: 1, children: []}, {id: 2, children: [34, 35]}] =>
 * ['1', '2+34', '2+35']
 * */
export function toPickerValue(value: PickerParamItem[] = []): string[] {
  const result: string[] = [];
  value.forEach(item => {
    const { id, children } = item;
    if (children.length > 0)
      children.forEach(child => {
        result.push(`${id}+${child}`);
      });
    else result.push(id.toString());
  });
  return result;
}

/**
 * 数字格式化
 * @param {Number} number
 * @param {Object} options
 * @param {String} options.style "decimal" | "currency" | "percent"
 * @param {Boolean} options.useGrouping - 是否使用分组分隔符
 * @param {Number} options.minimumIntegerDigits - 要使用的最小整数位数。默认值为 1
 * @param {Number} options.minimumFractionDigits - 要使用的最小小数位数。默认值为 0
 * @param {Number} options.maximumFractionDigits - 要使用的最大小数位数。默认值是 minimumFractionDigits 和 3 中的较大值
 * @param {Number} options.minimumSignificantDigits - 要使用的最小有效位数。默认值为1
 * @param {Number} options.maximumSignificantDigits - 要使用的最大有效位数。可能的值为1到21; 默认值为21
 */
export function numberFormat(number: any, options?: object) {
  if (!checkNumber(number)) {
    return '--';
  }
  if (_.isString(number) && number.indexOf('%') > 0) {
    if (!options) {
      return number;
    }
    return new Intl.NumberFormat(undefined, {
      style: 'percent',
      ...options
    }).format(parseFloat(number) / 100);
  }
  return new Intl.NumberFormat(undefined, {
    ...options
  }).format(parseFloat(number));
}
