import _ from 'lodash';

import { flattenNodes, mapTree, filterTree } from '@/utils/tree';
import { getUserProfile } from '@/utils/location';
import { __DEBUG__ } from '@/constants/config';
import { validUrlToCommonUrl, feUrlToCommonUrl } from './utils';

/**
 * 将第二个参数传进去的路由树，和第三个参数的打平的validUrl相交，只返回交集部分的树
 * @param strUrlParams 如果第四个参数preserveLink是false(默认值)，这个参数决定link的toStringURL的参数值
 * @param urlConfigTree 用于比较的树1
 * @param validUrls 用于比较的打平的一个url列表，["/users/${'userId'}/crosscampaignreport"]
 * @param preserveLink 是否保留链接，goHomePage用到的不保留，把APIObj => string直接跳转 || 路由生成和侧边栏需要保留
 */
export const getValidMenus = (
  strUrlParams: any,
  urlConfigTree: any,
  validUrls = _.get(getUserProfile(), 'validUrls'),
  preserveLink = false
) => {
  const formattedValidUrls = _.map(validUrls, url => validUrlToCommonUrl(url));
  let menus = mapTree(urlConfigTree, ({ link, key, text, ...item }: any) => {
    let formattedLink;
    if (preserveLink) {
      // 这个参数主要是给侧边栏使用的，在侧边栏里面，侧边栏在渲染的时候才会读取当前的urlParam里面的id然后写进去
      formattedLink = link;
    } else {
      formattedLink =
        link && _.isFunction(link.toStringURL) ? link.toStringURL(strUrlParams) : link;
    }

    return {
      ...item,
      key,
      link: formattedLink,
      text: `i18n_${key}` || text,
      route: feUrlToCommonUrl(link) // 用于跟validateURL对比的路径
    };
  });

  const isAllow = (i: any) => {
    if (__DEBUG__) {
      // 如果是dev模式，跳过鉴权，显示全部的路由
      return true;
    }

    if (i.route && _.includes(formattedValidUrls, i.route)) {
      // 路由 在合法范围内
      return true;
    }

    const childRoutes = _.filter(flattenNodes(i.children, 'route'), _.isString);
    if (_.intersection(formattedValidUrls, childRoutes).length > 0) {
      // 子项中有路由在合法范围内的
      return true;
    }

    return false;
  };

  menus = filterTree(menus, isAllow);
  return menus || [];
};
