/**
 * 对dayjs 一些方法的封装
 */
import dayjs from 'dayjs';

import { YYYYMMDD } from './format';

export * from './format';

/**
 * 快捷的获取时间
 */
export const getToday = (): Date => {
  return dayjs(YYYYMMDD(new Date())).toDate();
};

export const getYesterday = (): Date => {
  return dayjs()
    .subtract(1, 'day')
    .toDate();
};

export const getLastNDaysRangeBeforeCertainDate = (
  date: dayjs.ConfigType,
  nDays: number
): Array<string> => {
  return [
    YYYYMMDD(
      dayjs(date)
        .subtract(nDays, 'd')
        .toDate()
    ),
    YYYYMMDD(dayjs(date).toDate())
  ];
};

export const getNYearBeforeCertainDate = (datestr: string, nYears: number): string => {
  return YYYYMMDD(
    dayjs(datestr)
      .subtract(nYears, 'y')
      .toDate()
  );
};

export const initDate = (date: dayjs.ConfigType) => {
  return dayjs(date).toDate();
};

export const initDateRange = (dateRange: Array<dayjs.ConfigType>) => {
  return dateRange.map(d => dayjs(d).toDate());
};

/**
 * 操作的封装
 */
export const addDate = (date: Date, value: number, unit: dayjs.OpUnitType): dayjs.ConfigType => {
  return dayjs(date)
    .add(value, unit)
    .toDate();
};

export const subDate = (date: Date, value: number, unit: dayjs.OpUnitType): dayjs.ConfigType => {
  return dayjs(date)
    .subtract(value, unit)
    .toDate();
};

/**
 * 查询的封装
 */
export const dateIsValid = (dateObj: dayjs.ConfigType) => {
  return dayjs(dateObj).isValid();
};

export const dateIsBefore = (dateObj: dayjs.ConfigType, dateToCompObj: dayjs.ConfigType) => {
  return dayjs(dateObj).isBefore(dayjs(dateToCompObj));
};

export const dateIsAfter = (dateObj: dayjs.ConfigType, dateToCompObj: dayjs.ConfigType) => {
  return dayjs(dateObj).isAfter(dayjs(dateToCompObj));
};

export const dateIsSame = (dateObj: dayjs.ConfigType, dateToCompObj: dayjs.ConfigType) => {
  return dayjs(dateObj).isSame(dayjs(dateToCompObj));
};

const isBeforeOrSame = (date1: dayjs.ConfigType, date2: dayjs.ConfigType) => {
  return dayjs(date1).isSame(dayjs(date2)) || dayjs(date1).isBefore(dayjs(date2));
};

const isAfterOrSame = (date1: dayjs.ConfigType, date2: dayjs.ConfigType) => {
  return dayjs(date1).isSame(dayjs(date2)) || dayjs(date1).isAfter(dayjs(date2));
};

export const getOverlapBetweenTwoRange = (
  initialRangeStartDate: dayjs.ConfigType,
  initialRangeEndDate: dayjs.ConfigType,
  comparedRangeStartDate: dayjs.ConfigType,
  comparedRangeEndDate: dayjs.ConfigType
): false | Array<Date> => {
  if (
    // 没有交集的情况
    dayjs(initialRangeEndDate).isBefore(dayjs(comparedRangeStartDate)) || // 1结束比2开始早
    dayjs(initialRangeStartDate).isAfter(dayjs(comparedRangeEndDate)) // 1开始比2结束晚 == 2结束比1开始早
  ) {
    return false;
  }

  if (
    // 1包含2
    isBeforeOrSame(initialRangeStartDate, comparedRangeStartDate) &&
    isAfterOrSame(initialRangeEndDate, comparedRangeEndDate)
  ) {
    return [dayjs(comparedRangeStartDate).toDate(), dayjs(comparedRangeEndDate).toDate()];
  }

  if (
    // 2包含1
    isBeforeOrSame(comparedRangeStartDate, initialRangeStartDate) &&
    isAfterOrSame(comparedRangeEndDate, initialRangeEndDate)
  ) {
    return [dayjs(initialRangeStartDate).toDate(), dayjs(initialRangeEndDate).toDate()];
  }

  // 有交集
  if (
    isAfterOrSame(initialRangeEndDate, comparedRangeStartDate) &&
    isBeforeOrSame(initialRangeEndDate, comparedRangeEndDate)
  ) {
    return [dayjs(comparedRangeStartDate).toDate(), dayjs(initialRangeEndDate).toDate()];
  }

  if (
    isAfterOrSame(comparedRangeEndDate, initialRangeStartDate) &&
    isBeforeOrSame(comparedRangeEndDate, initialRangeEndDate)
  ) {
    return [dayjs(initialRangeStartDate).toDate(), dayjs(comparedRangeEndDate).toDate()];
  }

  console.error('date overlap algorithm error');
  return false;
};

/**
 * 默认禁用的封装
 */
export const setDisabledDate = (maxDate: Date, minDate: Date, date: Date) => {
  if (!maxDate && !minDate) {
    return false;
  }

  // 如果小于最小时间 则禁用
  if (minDate && dateIsBefore(date, minDate)) {
    return true;
  }

  // 如果大于最大时间 || 今天 则禁用
  return maxDate && dateIsAfter(date, maxDate);
};

export const getEarlierDate = (m1: dayjs.ConfigType, m2: dayjs.ConfigType): string => {
  if (!dayjs(m1).isValid() || !dayjs(m2).isValid()) {
    if (dayjs(m1).isValid()) {
      return YYYYMMDD(dayjs(m1).toDate());
    }

    if (dayjs(m2).isValid()) {
      return YYYYMMDD(dayjs(m2).toDate());
    }

    return '';
  }

  if (dayjs(m1).isBefore(dayjs(m2))) {
    return YYYYMMDD(dayjs(m1).toDate());
  }

  return YYYYMMDD(dayjs(m2).toDate());
};

export const checkDateRangeOverNMonth = (
  date1: dayjs.ConfigType,
  date2: dayjs.ConfigType,
  monthNum: number
) => {
  if (dayjs(date1).isBefore(dayjs(date2))) {
    // d1 before d2
    return dayjs(date1).isBefore(dayjs(date2).subtract(monthNum, 'M'));
  }

  // d2 before or same as d1
  return dayjs(date2).isBefore(dayjs(date1).subtract(monthNum, 'M'));
};
