import React, { memo, useCallback, useState, useContext, useEffect, useMemo } from 'react';
import _ from 'lodash';
import { Panel } from 'rsuite';

import { useMainPage } from '@/components/hooks/useMainPage';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { useCampaignId } from '@/components/hooks/useCertainParams';
import { AdvFilterUncontrolled, AdvFilterTrigger } from '@/components/AdvancedFilter';
import { updateStatus } from '@/utils/session';
import Card from '@/components/Card';
import { defaultFilter, segData, givtData, countryData } from '../constants';
import { basicClassName, primaryData, filters } from './constants';
import Chart from './Chart';
// import Chart from '@/components/Chart/OriginChart';
import Table from './Table';
import { CampaignCtx, DateCtx } from '../context';

const defaultCommonParam = {
  ...defaultFilter,
  mDimension: 'site', // 主要维度,
  country: 'include'
};

export default memo(() => {
  const { formatMessage } = useFmtMsgFori18n();
  const primaryDataIntl = usePickerDataFromObj(primaryData);
  const segDataIntl = usePickerDataFromObj(segData);
  const givtDataIntl = usePickerDataFromObj(givtData);
  const countryDataIntl = usePickerDataFromObj(countryData);

  const campaignId = useCampaignId();
  const postParams = useMemo(
    () => ({
      id: campaignId,
      hierarchy: 'campaign',
      report: 'site'
    }),
    [campaignId]
  );

  const {
    dateRangeVal,
    dateRangeValTimeFormat,
    reportDateFn,
    maxDateFromRespBeforePreset,
    validDateLoaded,
    setDateRangeValByDateArray,
    minDateFromResp
  } = useContext(DateCtx);
  const { campaignValidUrls } = useContext(CampaignCtx);

  const { commonParam, fmtCommonParam, handleChangeCommonParam, customParamResult } = useMainPage({
    defaultCommonParam,
    customConfig: [
      {
        isTable: true
      }
    ]
  });
  const [tableResult] = customParamResult;
  const { handleChangePagination, param, fmtParam, handleSearchWord } = tableResult;

  const refetchAllData = useCallback(() => {
    handleChangeCommonParam({});
  }, [handleChangeCommonParam]);

  // 时间维度为小时清空类型选值
  const onFilter = useCallback(
    (key, value) => {
      if (value === 'hourly') {
        handleChangeCommonParam({ givt: null, [key]: value });
      } else {
        handleChangeCommonParam({
          [key]: value
        });
      }
    },
    [handleChangeCommonParam]
  );

  const [filterShow, setFilterShow] = useState(false);
  const [submitLoading, setSubmitLoading] = useState(false);
  const handleCloseFilter = useCallback(() => {
    setFilterShow(false);
  }, []);

  const handleShowFilter = useCallback(() => {
    setFilterShow(true);
  }, []);

  const handleSubmitFilter = useCallback(
    val => {
      setSubmitLoading(true);
      updateStatus({
        objId: campaignId,
        hierarchy: 'campaign',
        report: 'site',
        advanceFilter: val
      }).finally(() => {
        refetchAllData();
        setFilterShow(false);
        setSubmitLoading(false);
      });
    },
    [refetchAllData, campaignId]
  );

  const handleChangeMDimension = useCallback(
    value => {
      handleChangeCommonParam({
        mDimension: value
      });
    },
    [handleChangeCommonParam]
  );

  const handleChangeSegmentation = useCallback(
    value => {
      handleChangeCommonParam({
        segmentation: value
      });
    },
    [handleChangeCommonParam]
  );

  const handleChangeGivt = useCallback(
    value => {
      handleChangeCommonParam({
        givt: value
      });
    },
    [handleChangeCommonParam]
  );

  const handelChangeCountry = useCallback(
    value => {
      handleChangeCommonParam({
        country: value
      });
    },
    [handleChangeCommonParam]
  );

  useEffect(() => {
    reportDateFn({
      params: {
        secondary: 'daily',
        report: 'site'
      }
    });
    updateStatus({
      objId: campaignId,
      hierarchy: 'campaign',
      report: 'site',
      advanceFilter: []
    });
  }, [reportDateFn]);

  if (!validDateLoaded) {
    return null;
  }

  return (
    <>
      <div className={basicClassName}>
        <Panel className="page-toolbar">
          <DateRangePicker
            value={dateRangeVal}
            maxDate={maxDateFromRespBeforePreset}
            minDate={minDateFromResp}
            cleanable={false}
            appearance="subtle"
            onChange={setDateRangeValByDateArray}
          />
          <SelectPicker
            prepend={formatMessage('i18n_primary')}
            data={primaryDataIntl}
            value={commonParam.mDimension}
            onChange={handleChangeMDimension}
            appearance="subtle"
          />
          <SecondaryPicker
            onChange={(value: string) => onFilter('secondary', value)}
            value={commonParam.secondary}
            data={['daily', 'weekly', 'hourly', 'total']}
            cleanable={false}
            appearance="subtle"
          />
          <SelectPicker
            prepend={formatMessage('i18n_segmentation')}
            data={segDataIntl}
            value={commonParam.segmentation}
            onChange={handleChangeSegmentation}
            appearance="subtle"
          />
          {campaignValidUrls.includes('/campaigns/${\'campaignId\'}/givt') && (
            <SelectPicker
              prepend={formatMessage('i18n_type')}
              data={givtDataIntl}
              value={commonParam.givt}
              disabled={commonParam.secondary !== 'daily'}
              onChange={handleChangeGivt}
              appearance="subtle"
            />
          )}
          <SelectPicker
            prepend={formatMessage('i18n_abroad')}
            data={countryDataIntl}
            value={commonParam.country}
            onChange={handelChangeCountry}
            appearance="subtle"
          />
          <AdvFilterTrigger onClick={handleShowFilter} />
        </Panel>
        <AdvFilterUncontrolled
          filters={filters}
          onSubmit={handleSubmitFilter}
          onClose={handleCloseFilter}
          campaignId={campaignId}
          drawerShow={filterShow}
          submitLoading={submitLoading}
        />
        <Card>
          <Chart
            dateParams={dateRangeValTimeFormat}
            filterParams={fmtCommonParam}
            postParams={postParams}
          />
          <Panel className="panel-table">
            {validDateLoaded && (
              <Table
                dateParams={dateRangeValTimeFormat}
                filterParams={param}
                tableParams={fmtParam}
                postParams={postParams}
                setTableParams={handleChangePagination}
                handleSearchWord={handleSearchWord}
              />
            )}
          </Panel>
        </Card>
      </div>
    </>
  );
});
