// @flow
import React, { memo, useContext } from 'react';
import { DateRangePicker } from 'rsuite';
import dayjs from 'dayjs';
import { DateRangePickerProps, RangeType } from 'rsuite/lib/DateRangePicker';

import { toDate } from '@/utils';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { DrawerContainerContext } from '@/components/Drawer';
// eslint-disable-next-line @typescript-eslint/no-var-requires
const weekOfYear = require('dayjs/plugin/weekOfYear');

dayjs.extend(weekOfYear);

export const {
  allowedMaxDays,
  allowedDays,
  allowedRange,
  after,
  afterToday,
  before,
  beforeToday,
  combine
} = DateRangePicker;

// 允许选择今天之前的多少天
export const allowedMaxDaysOfBeforeToday = (limitRangeDay: number) =>
  combine(allowedMaxDays(limitRangeDay), afterToday());

// 允许选择今天之后的多少天
export const allowedMaxDaysOfAfterToday = (limitRangeDay: number) =>
  combine(allowedMaxDays(limitRangeDay), beforeToday());

interface Props extends Omit<DateRangePickerProps, 'value'> {
  value?: dayjs.ConfigType[];
  maxDate?: dayjs.ConfigType;
  minDate?: dayjs.ConfigType;
  cleanable?: boolean;
}

// ToDo: 时间范围对range的影响；单独设置minDate未起效

export default memo<Props>(({ maxDate, minDate, value, ...props }) => {
  const { formatMessage } = useFmtMsgFori18n();
  const getContainer = useContext(DrawerContainerContext);

  // 时间快捷方式
  const ranges = [
    {
      label: formatMessage('i18n_today'),
      value: [dayjs(), dayjs()]
    },
    {
      label: formatMessage('i18n_yesterday'),
      value: [dayjs().subtract(1, 'd'), dayjs().subtract(1, 'd')]
    },
    {
      label: formatMessage('i18n_thisWeek'),
      value:
        (dayjs(maxDate) as any).week() === (dayjs() as any).week()
          ? [
              dayjs()
                .startOf('w')
                .add(1, 'd'),
              dayjs(maxDate)
            ]
          : [
              dayjs()
                .startOf('w')
                .add(1, 'd'),
              dayjs()
            ]
    },
    {
      label: formatMessage('i18n_thisMonth'),
      value:
        dayjs(maxDate).month() === dayjs().month()
          ? [dayjs().startOf('M'), dayjs(maxDate)]
          : [dayjs().startOf('M'), dayjs()]
    },
    {
      label: formatMessage('i18n_lastSevenDays', { number: 7 }),
      value: [dayjs().subtract(7, 'd'), dayjs().subtract(1, 'd')]
    },
    {
      label: formatMessage('i18n_lastThirtyDays', { number: 30 }),
      value: [dayjs().subtract(30, 'd'), dayjs().subtract(1, 'd')]
    }
  ];
  const newRanges = ranges.map(({ label, value: rValue }) => ({
    label,
    value: [rValue[0].toDate(), rValue[1].toDate()]
  }));

  // 默认最小时间为最大时间-365天
  const defMin = '0001-01-01';
  const disabledDate = maxDate
    ? allowedRange(toDate(minDate || defMin), toDate(maxDate))
    : props.disabledDate;
  const newValue = value ? value : [dayjs(), dayjs()];
  return (
    <>
      <DateRangePicker
        ranges={newRanges as RangeType[]}
        value={[toDate(newValue[0]), toDate(newValue[1])]}
        disabledDate={disabledDate}
        isoWeek
        container={getContainer}
        {...props}
      />
    </>
  );
});
