import React, { memo, useCallback, useContext, useEffect, useMemo } from 'react';
import _ from 'lodash';
import { Panel, Divider } from 'rsuite';
import { useLocation } from 'react-router-dom';
import Card from '@/components/Card';
import { API_BRAND_OVERVIEW } from '@/constants/APIs';
import { Toolbar } from '@/components/Table';
import DateRangePicker from '@/components/Picker/DateRangePicker';
import BEPicker from '@/components/Picker/logic/BEPicker';
import SecondaryPicker from '@/components/Picker/logic/SecondaryPicker';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import { useUserId } from '@/components/hooks/useCertainParams';
import Chart from '@/components/Chart/ReportCustomChart';
import { useMainPage } from '@/components/hooks/useMainPage';
import { filterParamKeys } from './constants';
import Summary from './Summary';
import Table from './Table';
import { UserModuleDateCtx } from '../../context';

const defaultCommonParam = {
  [filterParamKeys.BE]: [] as Array<any>,
  secondary: null as '',
  sSearch: '',
  mDimension: 'be', // 主要维度
  repType: 'accountlistoverall',
  hierarchy: 'user', // 层级
  report: 'account' // 报表类型
};

const commonParamFormatter = (param: any) => {
  return {
    ...param,
    [filterParamKeys.BE]: param[filterParamKeys.BE].join()
  };
};

export default memo(() => {
  const { state } = useLocation();
  const defaultBe = _.get(state, 'beId') ? [_.toNumber(_.get(state, 'beId'))] : [];

  const {
    customParamResult,
    fmtCommonParam,
    handleChangeCommonParam,
    handleDebouncedChangeCommonParam,
    commonParam
  } = useMainPage({
    defaultCommonParam: {
      ...defaultCommonParam,
      [filterParamKeys.BE]: defaultBe
    },
    commonParamFormatter,
    customConfig: [
      {
        isTable: true
      }
    ]
  });

  const [tableResult] = customParamResult;
  const { param, fmtParam, handleChangePagination, handleChangeCustomParam } = tableResult;

  const { formatMessage } = useFmtMsgFori18n();
  const userId = useUserId();
  const {
    dateRangeVal,
    dateRangeValTimeFormat,
    setDateRangeValByDateArray,
    maxDatePreset
  } = useContext(UserModuleDateCtx);

  const handleBEChange = useCallback(
    (beVal: Array<any>) => {
      handleChangeCommonParam({
        [filterParamKeys.BE]: beVal
      });
    },
    [handleChangeCommonParam]
  );

  const handleChangeSecondary = useCallback(
    val => {
      handleChangeCommonParam({
        secondary: val
      });
    },
    [handleChangeCommonParam]
  );

  const handleSearchWord = useCallback(
    (word: string) => {
      handleDebouncedChangeCommonParam({
        sSearch: word
      });
    },
    [handleDebouncedChangeCommonParam]
  );
  const postParams = useMemo(
    () => ({
      id: userId
    }),
    [userId]
  );

  //渲染完成之后清空history中的state
  useEffect(() => {
    history.replaceState({}, '');
  }, []);

  return (
    <>
      <Panel className="page-toolbar">
        <DateRangePicker
          value={dateRangeVal}
          maxDate={maxDatePreset}
          cleanable={false}
          appearance="subtle"
          onChange={setDateRangeValByDateArray}
        />
      </Panel>
      <Card>
        <Summary dateParams={dateRangeValTimeFormat} id={userId} />
        <Divider />
        <Toolbar>
          <BEPicker
            key="be"
            chosenBE={commonParam[filterParamKeys.BE]}
            beChangeHandler={handleBEChange}
          />
          <SecondaryPicker
            onChange={handleChangeSecondary}
            value={_.get(commonParam, 'secondary')}
            data={[null, 'daily']}
            cleanable={false}
          />
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
        </Toolbar>
        {/* <Chart dateParams={dateRangeValTimeFormat} filterParams={fmtCommonParam} id={userId} /> */}

        <Chart
          dateParams={dateRangeValTimeFormat}
          filterParams={fmtCommonParam}
          postParams={postParams}
          api={API_BRAND_OVERVIEW}
          collapsible
          customType={1}
        />
        <Table
          dateParams={dateRangeValTimeFormat}
          filterParams={param}
          tableParams={fmtParam}
          postParams={postParams}
          setTableParams={handleChangePagination}
          handleChangeCustomParam={handleChangeCustomParam}
        />
      </Card>
    </>
  );
});
