export const getDivisionData = (exportType: TAB_KEY, hierarchy: HierarchyIdKey) => {
  switch (exportType) {
    case TAB_KEY.RAW_EXPORT: {
      if (hierarchy === HierarchyIdKey.BE || hierarchy === HierarchyIdKey.CAMPAIGN) {
        return [divisionData[0], divisionData[1], divisionData[2]];
      }

      if (hierarchy === HierarchyIdKey.PLACEMENT) {
        return [divisionData[0], divisionData[2]];
      }
      return [divisionData[0]];
    }
    case TAB_KEY.DEVICE_ID: {
      if (hierarchy === HierarchyIdKey.BE || hierarchy === HierarchyIdKey.CAMPAIGN) {
        return [divisionData[0], divisionData[1], divisionData[2], divisionData[3]];
      }

      if (hierarchy === HierarchyIdKey.PLACEMENT) {
        return [divisionData[0], divisionData[2], divisionData[3]];
      }

      return [divisionData[0]];
    }
    default:
      return [];
  }
};

