export const defaultFormData = {
  dateRange: [getYesterday(), getYesterday()] as Array<any>,
  hierarchy: hierarchyData[0].id
};

export const defaultFormCalcData = {
  dateRange: [getYesterday(), getYesterday()] as Array<any>,
  dimension: hierarchyData[1].id
};

export const getDefaultFormData = (exportType: TAB_KEY) => {
  switch (exportType) {
    case TAB_KEY.RAW_EXPORT:
      return {
        ...defaultFormData,
        ...{
          division: divisionData[0].id,
          limit: limits[0].id,
          fields: fieldsData.slice()
        }
      };
    case TAB_KEY.DEVICE_ID:
      return {
        ...defaultFormData,
        ...{
          division: divisionData[0].id,
          limit: limits[0].id,
          frequencyChecked: true,
          frequency: [1, 20],
          devicePrefix: deviceTypeData[0]
        }
      };
    case TAB_KEY.CALC_OVERLAP:
      return {
        ...{
          exportType: pkTypeData[0].id,
          data: [
            {
              ...defaultFormCalcData
            }
          ]
        }
      };
    case TAB_KEY.CUSTOM_REPORT:
      return {
        ...defaultFormData,
        ...{
          dimension: [],
          metrics: metricsData.filter(i => i.id !== 'conversion').map(m => m.id),
          secondary: 'total'
        }
      };
    default:
      return defaultFormData;
  }
};
