import React, { useState, useEffect, useCallback, useRef } from 'react';
import _ from 'lodash';
import { Uploader } from 'rsuite';
import { API_ADGROUP_CREATIVE_UPLOAD } from '@/constants/APIs';

interface Props {
  value?: string;
  isEdit?: boolean;
  url?: string;
  onChange?: Function;
  setUploadError: Function;
  setUploading: Function;
}

export default React.memo(
  ({ value, isEdit, url, onChange, setUploadError, setUploading, ...props }: Props) => {
    const uploaderRef = useRef(null);
    const [fileList, setFileList] = useState([]);

    // 编辑时，需要设置fileList，使其下载相应文件
    useEffect(() => {
      if (!isEdit) {
        return;
      }
      !_.isEmpty(url) &&
        setFileList([
          {
            url,
            name: value
          }
        ]);
    }, [url]);

    // 每次只能上传一张图片，需要取消正在进行的上传
    const handleChange = useCallback(
      list => {
        _.isEmpty(list) && onChange('');

        for (let i = 0; i < list.length - 1; i++) {
          uploaderRef.current.handleRemoveFile &&
            uploaderRef.current.handleRemoveFile(list[i].fileKey);
        }

        const newList = list.length > 1 ? list.slice(-1) : list;
        setFileList(newList);
      },
      [onChange]
    );

    const handleUpload = useCallback(() => {
      setUploadError('');
      setUploading(true);
    }, []);

    const handleSuccess = useCallback(rsp => {
      setUploading(false);
      onChange(_.get(rsp, 'file_path'));
    }, []);

    const handleError = useCallback(rsp => {
      const err = _.get(rsp, 'response.fields.error.0.message', '');
      setUploadError(err);
      setUploading(false);
    }, []);

    return (
      <Uploader
        accept="image/*"
        action={API_ADGROUP_CREATIVE_UPLOAD}
        listType="picture-text"
        fileList={fileList}
        onChange={handleChange}
        onUpload={handleUpload}
        onSuccess={handleSuccess}
        onError={handleError}
        {...props}
        name="file"
        ref={uploaderRef}
      />
    );
  }
);
