import React, { useCallback, useEffect, useState } from 'react';
import _ from 'lodash';
import classnames from 'classnames';
import Drawer from '@/components/Drawer';
import Form from '@/components/Form';
import CustomField from '@/components/Form/CustomField';
import MultiField from '@/components/Form/MultiField';
import CheckboxGroup from '@/components/Form/CheckboxGroup';
import Toggle from '@/components/Form/Toggle';
import SelectPicker from '@/components/Picker/SelectPicker';
import RangePicker from '@/components/Picker/RangePicker';
import { CampaignModel } from '@/constants/form/models';
import BERoleGroupPicker from '@/components/Picker/logic/BERoleGroupPicker';
import BERoleMultiPicker from '@/components/Picker/logic/BERoleMultiPicker';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { CommonResponse } from '@/utils/request/parsingResponse';
import { API_BE, API_BE_GET } from '@/constants/APIs';
import { beRoleData } from '@/constants/config';
import { getUserProfile } from '@/utils/location';
import AdvBrandGroupPicker from './part/AdvBrandGroupPicker';
import IvtVendorPicker from './part/IvtVendorPicker';
import IvtTypePicker from './part/IvtTypePicker';
import MtaVendorPicker from './part/MtaVendorPicker';
import {
  genderData,
  mainBEBaseParams,
  advBrandBaseParams,
  secBEBaseParams,
  chosenSecBEBaseParams,
  monitorTypeData,
  monitorVal,
  tencentData,
  ageStartList,
  ageEndList,
  checkEditable
} from './constants';

/**
 * 此表单请求逻辑较复杂
 * 主要对获取可选主拥、可选广告主品牌、可选次拥、已选次拥四个请求有控制
 */

const DrawerForm = React.memo(
  ({
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    onChange,
    onSubmit,
    onClose,
    setFormErrors,
    checkForm
  }: any): React.ReactElement => {
    const { formatMessage } = useFmtMsgFori18n();
    const genderDataIntl = usePickerDataFromObj(genderData);
    const monitorTypeDataIntl = usePickerDataFromObj(monitorTypeData);

    const { internal, beId } = getUserProfile();

    /* 以下定义四个请求的行为 */

    // 1. 获取可选主拥，可依赖picker的fetch功能，通过deps、params、onFetchSuccess来控制
    const [mainBEFlag, setMainBEFlag] = useState();
    const [mainBEParams, setMainBEParams] = useState(mainBEBaseParams);
    const [onMainBESuccess, setOnMainBESuccess] = useState();
    const handleFetchMainBE = useCallback(
      (callback?: Function, params?: Record<string, any>) => {
        const { isEdit } = drawer;
        const { id: campaignId, brandId } = formData;
        setMainBEFlag(f => !f);
        setMainBEParams(
          _.extend(mainBEBaseParams, {
            featureFlag: isEdit ? 1 : 0,
            brandId: isEdit ? _.get(params, 'brandId', brandId) : undefined,
            campaignId: isEdit ? campaignId : undefined
          })
        );
        _.isFunction(callback) && setOnMainBESuccess(callback);
      },
      [drawer.isEdit, formData.id, formData.brandId]
    );

    // 2. 获取可选广告主品牌，可依赖picker的fetch功能，通过deps、params、onFetchSuccess来控制
    const [advBrandFlag, setAdvBrandFlag] = useState();
    const [advBrandParams, setAdvBrandParams] = useState(advBrandBaseParams);
    const [onAdvBrandSuccess, setOnAdvBrandSuccess] = useState();
    const handleFetchAdvBrand = useCallback(
      (callback?: Function, params?: Record<string, any>) => {
        const { primaryBE, primaryRole } = formData;
        setAdvBrandFlag(f => !f);
        setAdvBrandParams(
          _.extend(advBrandBaseParams, {
            beRole: JSON.stringify([
              {
                beId: _.get(params, 'beId', primaryBE),
                role: _.get(params, 'role', primaryRole)
              }
            ])
          })
        );
        _.isFunction(callback) && setOnAdvBrandSuccess(callback);
      },
      [formData.primaryBE, formData.primaryRole]
    );

    // 3. 获取可选次拥，由于需要剔除已选主拥，并且在外部时情况特殊，不能依赖picker的fetch功能，需单独处理
    const [secBEData, setSecBEData] = useState([]);
    const { onFetch: onFetchSecBE, loading: secBELoading, response: secBERsp } = useFetch<
      CommonResponse
    >(FETCH_METHOD.GET, API_BE, { params: secBEBaseParams });
    const handleFetchSecBE = useCallback(
      (callback?: Function, params?: Record<string, any>) => {
        const { isEdit } = drawer;
        const { id: campaignId, brandId, primaryBE, primaryRole } = formData;
        onFetchSecBE({
          params: {
            featureFlag: isEdit ? 1 : 0,
            brandId: _.get(params, 'brandId', brandId) || undefined,
            campaignId: isEdit ? campaignId : undefined,
            primaryId: JSON.stringify([
              {
                beId: _.get(params, 'beId', primaryBE),
                role: _.get(params, 'role', primaryRole)
              }
            ])
          }
        })
          .then(rsp => {
            return _.get(rsp, 'data.records', []);
          })
          .then(callback);
      },
      [drawer.isEdit, formData.id, formData.brandId, formData.primaryBE, formData.primaryRole]
    );
    // 在可选次拥和已选次拥中剔除已选主拥
    useEffect(() => {
      const data = _.get(secBERsp, 'data.records', []);
      setSecBEData(data.filter(i => i.id !== formData.primaryBE));
      _.isArray(formData.secondaries) &&
        onChange(formData.secondaries.filter(i => i.beId !== formData.primaryBE), 'secondaries');
    }, [secBERsp, formData.primaryBE]);

    // 4. 获取已选次拥，由于需要剔除已选主拥，并且在外部时情况特殊，不能依赖picker的fetch功能，需单独处理
    const { onFetch: onFetchChosenSecBE } = useFetch<CommonResponse>(FETCH_METHOD.GET, API_BE, {});
    const [chosenSecBE, setChosenSecBE] = useState([]);
    const handleFetchChosenSecBE = useCallback(
      (callback?: Function, params?: Record<string, any>) => {
        const { isEdit } = drawer;
        const { id: campaignId, brandId, primaryBE, primaryRole } = formData;
        onFetchChosenSecBE({
          params: _.extend(chosenSecBEBaseParams, {
            featureFlag: isEdit ? 1 : 0,
            brandId: _.get(params, 'brandId', brandId) || undefined,
            campaignId: isEdit ? campaignId : undefined,
            primaryId: JSON.stringify([
              {
                beId: _.get(params, 'beId', primaryBE),
                role: _.get(params, 'role', primaryRole)
              }
            ])
          })
        })
          .then(rsp => {
            const data = _.get(rsp, 'data.records', []).map(i => ({
              id: i.id,
              name: i.name,
              role: i.role,
              beId: i.id,
              roleId: _.get(beRoleData.find(j => j.name === _.lowerCase(i.role)), 'id')
            }));
            setChosenSecBE(data);
            onChange(data, 'secondaries'); // 获取到的已选次拥要反映在formData上
            return data;
          })
          .then(callback);
      },
      [drawer.isEdit, formData.id, formData.brandId, formData.primaryBE, formData.primaryRole]
    );

    /* 以下重写部分表单方法，主要用于进行联动清空操作 */

    // 修改主拥回调
    const handleChangeMainBE = useCallback(
      (val, key) => {
        if (formData[key] === val) {
          return;
        }

        // 若改变主拥BE，清空已选主拥角色
        if (key === 'primaryBE') {
          _.isNumber(formData.primaryRole) && onChange(null, 'primaryRole');
        }
      },
      [formData.primaryBE, formData.primaryRole]
    );

    // 修改广品回调
    const handleChangeAdvBrand = useCallback(
      (val, key) => {
        if (formData[key] === val) {
          return;
        }

        // 若改变广告主，清空已选品牌
        if (key === 'advertiserId') {
          _.isNumber(formData.brandId) && onChange(null, 'brandId'); // 清空不能为undefined，否则无效
        }
      },
      [formData.advertiserId, formData.brandId]
    );

    /* 以下通过一系列useEffect，并根据不同情景条件进行各组件间的联动行为 */

    // 外部时，要先获取当前用户所属BE详情
    const [externalBEData, setExternalBEData] = useState<Record<string, any>>([]);
    const { onFetch: onFetchBEDetail, loading: beDetailLoading } = useFetch<CommonResponse>(
      FETCH_METHOD.GET,
      API_BE_GET,
      { params: { beId } }
    );
    useEffect(() => {
      !internal &&
        onFetchBEDetail().then(rsp => {
          setExternalBEData([
            {
              id: _.get(rsp, 'data.id'),
              name: _.get(rsp, 'data.name'),
              role: _.keys(_.get(rsp, 'data.role', {})).join(','),
              isLockBeRole: _.get(rsp, 'data.isLockBeRole'),
              defaultRoleId: _.get(
                _.values(_.get(rsp, 'data.role', {})).find(i => i.defaultRole),
                'beRoleFlag'
              )
            }
          ]);
        });
    }, []);

    // 外部新建时，获取到所属BE详情且打开表单后需预填主拥数据
    const [preset, setPreset] = useState(false);
    useEffect(() => {
      if (!preset && drawer.show && !internal && !drawer.isEdit) {
        const belongBE = _.get(externalBEData, '0', {});
        if (_.isNumber(belongBE.id)) {
          const presetData: any = {};
          presetData.primaryBE = beId;
          onChange(beId, 'primaryBE');

          belongBE.isLockBeRole &&
            _.isNumber(belongBE.defaultRoleId) &&
            onChange(belongBE.defaultRoleId, 'primaryRole');
          setPreset(true);
        }
      }
    }, [drawer.show, externalBEData]);

    // 打开表单时
    useEffect(() => {
      if ((!drawer.isEdit && drawer.show) || (drawer.isEdit && drawer.show && !drawer.loading)) {
        // 内部新建打开表单需获取可选主拥
        // 此处编辑不调用是因为，此方法会在编辑填充广品后通过useEffect调用
        internal && !drawer.isEdit && handleFetchMainBE();
      }
    }, [drawer.isEdit, drawer.show, drawer.loading]);

    // 已选主拥发生改变且不为空时
    useEffect(() => {
      // 获取可选广品
      _.isNumber(formData.primaryBE) && _.isNumber(formData.primaryRole) && handleFetchAdvBrand();
    }, [formData.primaryBE, formData.primaryRole]);

    // 已选广品发生改变且不为空时
    useEffect(() => {
      // 防止过滤掉广告主，但品牌还存在的情况
      if (!_.isNumber(formData.advertiserId) && _.isNumber(formData.brandId)) {
        onChange(null, 'brandId');
      }

      if (_.isNumber(formData.advertiserId) && _.isNumber(formData.brandId)) {
        const { isEdit } = drawer;

        // 内部，加载可选次拥
        internal &&
          handleFetchSecBE(data => {
            // 对已选次拥进行过滤
            onChange(
              _.filter(formData.secondaries, source => {
                const item = _.find(data, other => other.id === source.beId);
                if (!item) {
                  return false;
                }
                const legalRoles = _.map(_.get(item, 'role', '').split(','), _.lowerCase);
                return !!_.find(
                  beRoleData,
                  i => i.id === source.roleId && legalRoles.includes(i.name)
                );
              }),
              'secondaries'
            );
          });
        !internal && handleFetchChosenSecBE(); // 外部，加载已选次拥
        internal && isEdit && handleFetchMainBE(); // 内部编辑，加载可选主拥
      }
    }, [formData.advertiserId, formData.brandId]);

    /* 其他 */

    const handleCloseForm = useCallback(() => {
      // 关闭表单时，需要重置这些flag，否则重复打开表单会触发额外请求
      setMainBEFlag(undefined);
      setAdvBrandFlag(undefined);
      onClose();
    }, []);

    // 可以把{val1: true, val2: false}的形式转化为[val1]
    const getDisabled = useCallback(
      (obj: any): any =>
        _.toPairs(obj).reduce((acc, i) => acc.concat(i[1] ? Number(i[0]) : []), []),
      []
    );

    // const [checkedImp, setCheckedImp] = useState(_.get(formData, 'monitorType', []).includes(monitorVal.imp));
    // const [checkedClk, setCheckedClk] = useState(_.get(formData, 'monitorType', []).includes(monitorVal.clk));
    // const [checkedMeasure, setCheckedMeasure] = useState(_.get(formData, 'monitorType', []).includes(monitorVal.measure));
    //
    // const handleChangeMonitorType = useCallback(val => {
    //   const prevCheckedMeasure = _.isEmpty(_.difference(
    //     [monitorVal.imp, monitorVal.measure],
    //     _.get(formData, 'monitorType', [])
    //   ));
    //   const newCheckedMeasure = _.isEmpty(_.difference([monitorVal.imp, monitorVal.measure], val));
    //
    //   setCheckedImp(val.includes(monitorVal.imp));
    //   setCheckedClk(val.includes(monitorVal.clk));
    //   setCheckedMeasure(newCheckedMeasure);
    //
    //   // 选择'受众测量'，'受众累计报'默认选择'日累计'，否则不选
    //   // 选择'受众测量'，'UA频次'默认开启，否则关闭
    //   if(!prevCheckedMeasure && newCheckedMeasure){
    //     onChange(1, 'enableFreq');
    //     onChange(_.union(formData.tencent, [0]), 'tencent');
    //   }else if(prevCheckedMeasure && !newCheckedMeasure){
    //     onChange(0, 'enableFreq');
    //     onChange(_.difference(formData.tencent, [0]), 'tencent');
    //   }
    // }, [formData.monitorType, formData.tencent]);

    return (
      <Drawer
        show={drawer.show}
        title={formatMessage(drawer.isEdit ? 'i18n_editCampaign' : 'i18n_newCampaign')}
        submitLoading={drawer.submitting}
        dataLoading={drawer.loading || beDetailLoading}
        className="campaign-drawer"
        onSubmit={onSubmit}
        disabled={!checkEditable(formData.status)}
        onHide={handleCloseForm}
      >
        <Form
          formData={formData}
          formErrors={formErrors}
          model={CampaignModel}
          formRef={formRef}
          onChange={onChange}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        >
          {/* 名称 */}
          <CustomField name="name" label={formatMessage('i18n_name')} required />

          {/* 描述 */}
          <CustomField name="notes" label={formatMessage('i18n_description')} />

          {/* 主要拥有者 */}
          <MultiField
            names={['primaryBE', 'primaryRole']}
            label={formatMessage('i18n_primaryOwner')}
            accepter={BERoleGroupPicker}
            beName="primaryBE"
            roleName="primaryRole"
            fetchOptions={{
              data: internal || externalBEData,
              params: mainBEParams,
              deps: [mainBEFlag],
              onFetchSuccess: onMainBESuccess
            }}
            onChange={handleChangeMainBE}
            bePickerProps={{
              // 外部，或内部编辑未填品牌时，禁用主拥BE
              disabled: !internal || (drawer.isEdit && !_.isNumber(formData.brandId))
            }}
            rolePickerProps={{
              disabled:
                (internal && !drawer.isEdit && !_.isNumber(formData.primaryBE)) || // 内部新建未填主拥BE时，禁用主拥角色
                (internal && drawer.isEdit && !_.isNumber(formData.brandId)) || // 内部编辑未填品牌时，禁用主拥角色
                (!internal && (drawer.isEdit || _.get(externalBEData, 'isLockBeRole'))) // 外部编辑，或外部新建锁定角色时，禁用主拥角色
            }}
            required
          />

          {/* 广告主品牌 */}
          <MultiField
            names={['advertiserId', 'brandId']}
            label={formatMessage('i18n_brandFilter')}
            accepter={AdvBrandGroupPicker}
            fetchOptions={{
              params: advBrandParams,
              deps: [advBrandFlag],
              onFetchSuccess: onAdvBrandSuccess
            }}
            onChange={handleChangeAdvBrand}
            disabled={!_.isNumber(formData.primaryBE) || !_.isNumber(formData.primaryRole)}
            required
          />

          {/* 次要拥有者 */}
          <CustomField
            name="secondaries"
            label={formatMessage('i18n_secondaryOwner')}
            accepter={BERoleMultiPicker}
            fetchOptions={{
              data: internal ? secBEData : chosenSecBE // 外部时，不会获取可选次拥，所以用已选次拥填充
            }}
            loading={secBELoading}
            pickerHidden={!internal} // 外部时，不提供添加操作
            disabled={
              !_.isNumber(formData.advertiserId) ||
              !_.isNumber(formData.brandId) || // 未填广告主或品牌时，禁止添加次拥
              _.get(formData, 'secondaries.length', 0) >= 10 // 超过限额时，禁止添加次拥}
            }
            removeDisabled={!internal} // 外部时，禁止删除已选
            message={
              <span>
                {formatMessage('i18n_allowMaxNum', { maxNum: 10 })} (
                {_.get(formData, 'secondaries.length', 0)}/10)
              </span>
            }
          />

          {/* 目标人群 */}
          <CustomField
            name="target"
            label={formatMessage('i18n_targetPerson')}
            accepter={SelectPicker}
            data={genderDataIntl}
          />

          {/* 年龄范围 */}
          <MultiField
            names={['ageStart', 'ageEnd']}
            label={formatMessage('i18n_ageRange')}
            accepter={RangePicker}
            startData={ageStartList}
            endData={ageEndList}
            startName="ageStart"
            endName="ageEnd"
            style={{
              width: '157px'
            }}
          />

          {/* 监测类型 */}
          <CustomField
            name="monitorType"
            label={formatMessage('i18n_monitorType')}
            accepter={CheckboxGroup}
            className={classnames({
              'checkbox-uncheck-when-disabled': !drawer.isEdit,
              'monitor-type-group': true
            })}
            required
            itemWidth="45%"
            // onChange={handleChangeMonitorType}
            data={monitorTypeDataIntl}
          // disabled={drawer.isEdit || getDisabled({
          //   [monitorVal.uimp]: !_.get(formData, 'monitorType', []).includes(monitorVal.imp),
          //   [monitorVal.uclk]: !_.get(formData, 'monitorType', []).includes(monitorVal.clk),
          //   // [monitorVal.measure]: !checkedImp
          // })}
          />

          {/* 受众累计报 */}
          {/* <CustomField
          name='tencent'
          label={formatMessage('i18n_tencentTotalReport')}
          accepter={CheckboxGroup}
          itemWidth='45%'
          className={classnames({
            'checkbox-uncheck-when-disabled': !checkedMeasure,
            'tencent-group': true
          })}
          data={tencentData}
          disabled={getDisabled({
            0: true,
            1: !checkedMeasure
          })}
        /> */}

          {/* UV频次 */}
          {/* <CustomField
          name='frequency'
          label={formatMessage('i18n_UVFreq')}
          accepter={Toggle}
          checkedChildren={formatMessage('i18n_open')}
          unCheckedChildren={formatMessage('i18n_close')}
          disabled={drawer.isEdit}
        /> */}

          {/* UA频次 */}
          {/* <CustomField
          name='enableFreq'
          label={formatMessage('i18n_UAFreq')}
          accepter={Toggle}
          checkedChildren={formatMessage('i18n_open')}
          unCheckedChildren={formatMessage('i18n_close')}
          disabled={!checkedMeasure}
        /> */}

          {/* 站外流量监测 */}
          <CustomField
            name="extTraffic"
            label={formatMessage('i18n_externalFlowMonitor')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_open')}
            unCheckedChildren={formatMessage('i18n_close')}
          />

          {/* IVT供应商、品牌标识 */}
          {/* <MultiField
          names={['ivtVendor', 'ivtVendorBrand']}
          label={formatMessage('i18n_ivtVendor')}
          accepter={IvtVendorPicker}
          brandId={formData.brandId}
          disabled={drawer.isEdit && !_.isEmpty(originalFormData.ivtVendor)} // 若编辑时已经填写则禁用
        /> */}

          {/* IVT类型、可见度监测 */}
          {/* <MultiField
          names={['ivtType', 'viewability']}
          label={formatMessage('i18n_ivtType')}
          accepter={IvtTypePicker}
          disabled={!_.isNumber(formData.ivtVendor)}
        /> */}

          {/* MTA供应商、MTA行业 */}
          {/* <MultiField
          names={['vendorId', 'categoryId']}
          label={formatMessage('i18n_mtaVendor')}
          accepter={MtaVendorPicker}
          disabled={drawer.isEdit}
        /> */}

          {/* 测试 */}
          <CustomField
            name="isTest"
            label={formatMessage('i18n_test')}
            accepter={Toggle}
            checkedChildren={formatMessage('i18n_yes')}
            unCheckedChildren={formatMessage('i18n_no')}
          />

          {/* 注释 */}
          {drawer.isEdit && (
            <CustomField
              name="note"
              label={formatMessage('i18n_annotation')}
              rows={2}
              componentClass="textarea"
            />
          )}
        </Form>
      </Drawer>
    );
  }
);

DrawerForm.displayName = 'DrawerForm';
export default DrawerForm;
