import React, { useCallback, useContext } from 'react';
import { Button, ButtonToolbar } from 'rsuite';

import { useFmtMsgFori18n } from '@/utils/formatMessage';

import Form from './Form';
import { CreateFormCtx } from '../context';

export default React.memo(() => {
  const { formatMessage } = useFmtMsgFori18n();
  const { setStep, drawerFormResult, handleCancel } = useContext(CreateFormCtx);
  const { formRef } = drawerFormResult;
  const handleNext = useCallback(() => {
    if (!formRef.current.check()) {
      return;
    }

    setStep(1);
  }, [formRef, setStep]);

  return (
    <div className="step-1">
      <Form />
      <ButtonToolbar>
        <Button appearance="primary" onClick={handleNext}>
          {formatMessage('i18n_nextStep')}
        </Button>
        <Button onClick={handleCancel}>{formatMessage('i18n_cancel')}</Button>
      </ButtonToolbar>
    </div>
  );
});
