import React, { memo, useCallback, useMemo } from 'react';
import { Button } from 'rsuite';
import Table, { Cell, Column, HeaderCell } from '@/components/Table';
import SearchInput from '@/components/Table/Tools/SearchInput';
import { DateCell, DateTimeCell, FloatActionCell, NameCell } from '@/components/Table/Cells';
import BEPicker from '@/components/Picker/logic/BEPicker';
import SelectPicker from '@/components/Picker/SelectPicker';
import useTable, { extractTableData } from '@/components/hooks/useTable';
import useFetch, { FETCH_METHOD } from '@/components/hooks/useFetch';
import { useFmtMsgFori18n, usePickerDataFromObj } from '@/utils/formatMessage';
import { getFullUrl } from '@/utils/location';
import { CommonResponse } from '@/utils/request/parsingResponse';
import template from '@/utils/template';
import { API_CUSTOM_REPORT } from '@/constants/APIs';
import { BasicReportProps } from '@/constants/config';
import RESPONSE_STATUS from '@/constants/responseStatus';
import { FilterParam, filterParamKeys, primaryData, utilsFn } from './constants';

type TableProps = BasicReportProps<FilterParam> & {
  handleFormCreate: Function;
  handleFormEdit: Function;
};

const mapStatusFn = (statusFromBackend: number) => {
  let info = 'active';
  switch (statusFromBackend) {
    case 1:
      info = 'active'; // green
      break;
    case 2:
      info = 'paused'; // gray
      break;
    case 3:
      info = 'ended'; // orange
      break;
    default:
      break;
  }

  return info;
};
const beFetchParam = {
  status: 1,
  orderColumn: 'name',
  orderType: 'asc'
};

export default memo(
  ({
    filterParams,
    tableParams,
    setTableParams,
    handleSearchWord,
    handleChangeCustomParam,
    handleFormCreate,
    handleFormEdit,
    refetchTableData
  }: TableProps) => {
    const { formatMessage } = useFmtMsgFori18n();
    const primaryDataIntl = usePickerDataFromObj(primaryData);
    const { tableData, onChange } = useTable({
      tableParams,
      setTableParams,
      api: API_CUSTOM_REPORT
    });

    const statusVal = utilsFn.getStatus(filterParams);
    const tableDataMemo = useMemo(() => extractTableData(tableData), [tableData]);

    const handleBEChange = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.BE]: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleStatusChanged = useCallback(
      val => {
        handleChangeCustomParam({
          [filterParamKeys.STATUS]: val
        });
      },
      [handleChangeCustomParam]
    );

    const handleDownload = useCallback(filename => {
      window.location.href = getFullUrl((filename.startsWith('http') ? '' : 'http://') + filename);
    }, []);

    const { onFetch: onCancelReport } = useFetch<CommonResponse>( // 不处理请求取消
      FETCH_METHOD.GET,
      template`${API_CUSTOM_REPORT}/${'id'}`,
      {
        params: {
          status: 2
        }
      }
    );

    const handleCancel = useCallback(
      id => {
        onCancelReport({ params: { id } }).then(rsp => {
          if (rsp.status === RESPONSE_STATUS.SUCCESS) {
            refetchTableData();
          }
        });
      },
      [refetchTableData]
    );

    const renderToolbar = () => {
      return (
        <>
          <Button appearance="primary" onClick={handleFormCreate as () => void}>
            {formatMessage('i18n_create')}
          </Button>
          <BEPicker
            params={beFetchParam}
            chosenBE={utilsFn.getChosenBE(filterParams)}
            beChangeHandler={handleBEChange}
          />
          <SelectPicker
            prepend={formatMessage('i18n_status')}
            data={primaryDataIntl}
            value={statusVal}
            onChange={handleStatusChanged}
          />
          <SearchInput
            placeholder={formatMessage('i18n_conversionPathSearchPlaceholder')}
            onChange={handleSearchWord}
          />
        </>
      );
    };

    const renderActions = useCallback(
      rowData => {
        const { id, status, filename } = rowData;
        return [
          <FloatActionCell.Actions.Download
            key="download"
            disabled={!filename}
            title={formatMessage('i18n_reportDownLatest')}
            onClick={() => {
              handleDownload(filename);
            }}
          />,
          <FloatActionCell.Actions.Edit
            key="edit"
            onClick={() => {
              handleFormEdit(rowData);
            }}
          />,
          status !== 2 && (
            <FloatActionCell.Actions.Cancel
              key="cancel"
              onClick={() => {
                handleCancel(id);
              }}
            />
          )
        ];
      },
      [handleFormEdit, handleDownload, handleCancel]
    );

    return (
      <Table {...tableDataMemo} onChange={onChange} renderToolbar={renderToolbar} wordWrap>
        {/* ID */}
        <Column width={100} sortable fixed key="id">
          <HeaderCell>ID</HeaderCell>
          <Cell dataKey="id" />
        </Column>

        {/* 报表名称 */}
        <Column width={200} resizable fixed sortable>
          <HeaderCell>{formatMessage('i18n_reportName')}</HeaderCell>
          <NameCell
            dataKey="name"
            showStatus={true}
            statusMapperFn={mapStatusFn}
            renderActions={renderActions}
          />
        </Column>

        {/* 频次 */}
        <Column width={80} sortable>
          <HeaderCell>{formatMessage('i18n_frequency')}</HeaderCell>
          <Cell dataKey="frequency" />
        </Column>

        {/* 执行时间(最近一次) */}
        <Column width={200} align="right" sortable>
          <HeaderCell>{formatMessage('i18n_updatedLatest')}</HeaderCell>
          <DateTimeCell dataKey="missionBegin" />
        </Column>

        {/* 结束时间(最近一次) */}
        <Column width={200} align="right" sortable>
          <HeaderCell>{formatMessage('i18n_endTimeLatest')}</HeaderCell>
          <DateTimeCell dataKey="missionEnd" />
        </Column>

        {/* 结束日期 */}
        <Column width={100} align="right" sortable>
          <HeaderCell>{formatMessage('i18n_endDate')}</HeaderCell>
          <DateCell dataKey="endTime" />
        </Column>

        {/* 所属BE */}
        <Column width={200} sortable>
          <HeaderCell>{formatMessage('i18n_belongBE')}</HeaderCell>
          <FloatActionCell dataKey="beName" />
        </Column>

        {/* 创建者 */}
        <Column width={200} sortable>
          <HeaderCell>{formatMessage('i18n_creator')}</HeaderCell>
          <FloatActionCell dataKey="user" />
        </Column>

        {/* 创建日期 */}
        <Column width={200} sortable>
          <HeaderCell>{formatMessage('i18n_createdDate')}</HeaderCell>
          <DateTimeCell dataKey="created" />
        </Column>
      </Table>
    );
  }
);
