import React, { memo, useCallback } from 'react';
import { Panel } from 'rsuite';
import { RouteComponentProps } from 'react-router-dom';
import _ from 'lodash';
import { useMainPage } from '@/components/hooks/useMainPage';
import { defaultFilter, filterParamKeys, FilterParam, defaultFormData } from './constants';
import useDrawerForm from '@/components/hooks/useDrawerForm';
import { useUserId } from '@/components/hooks/useCertainParams';
import { API_CONVERSION } from '@/constants/APIs';
import { ConversionModel } from '@/constants/form/models';
import { mapFromResponse, mapToRequest } from './dataMapper';
import Toolbar from './Toolbar';
import Table from './Table';
import DrawerForm from './DrawerForm';
import useImport from './useImport';
import DrawerImport from './DrawerImport';

const filterParamFormatter = (filterParam: FilterParam) => {
  const originBE = _.get(filterParam, filterParamKeys.BE);
  const originConversionGroup = _.get(filterParam, filterParamKeys.GROUP, []);
  return {
    ...filterParam,
    [filterParamKeys.BE]: originBE.toString(),
    [filterParamKeys.GROUP]: _.isEmpty(originConversionGroup) ? '' : originConversionGroup.join(',')
  };
};

type RootCompProps = RouteComponentProps<{}>;

export default memo(() => {
  const { commonParam, handleChangeCommonParam, customParamResult } = useMainPage({
    defaultCommonParam: defaultFilter,
    commonParamFormatter: filterParamFormatter,
    customConfig: [
      {
        isTable: true
      }
    ]
  });

  const userId = useUserId();
  const [tableResult] = customParamResult;
  const {
    param,
    fmtParam,
    handleSearchWord,
    handleChangePagination,
    handleChangeCustomParam
  } = tableResult;
  const refetchTableData = useCallback(() => {
    handleChangeCustomParam({});
  }, [handleChangeCustomParam]);

  // 批量导入
  const {
    drawer: ImportDrawer,
    drawerRef,
    handleShow: ImportShow,
    handleClose: ImportClose,
    setDrawer
  } = useImport({
    submitSuccessCallback: refetchTableData
  });

  const {
    drawer,
    formData,
    originalFormData,
    formErrors,
    formRef,
    handleFormChange,
    handleFormCreate,
    handleFormEdit,
    handleFormClose,
    handleFormSubmit,
    setFormErrors,
    checkForm
  } = useDrawerForm({
    api: API_CONVERSION,
    defaultFormData,
    model: ConversionModel,
    mapFromResponse,
    mapToRequest,
    submitSuccessCallback: refetchTableData
  });
  return (
    <>
      <Panel className="page-toolbar">
        <Toolbar
          filterParams={commonParam}
          handleChangeCommonParam={handleChangeCommonParam}
          userId={userId}
        />
      </Panel>
      <Panel className="panel-table">
        <Table
          filterParams={param}
          tableParams={fmtParam}
          setTableParams={handleChangePagination}
          refetchTableData={refetchTableData}
          handleSearchWord={handleSearchWord}
          handleChangeCommonParam={handleChangeCommonParam}
          handleFormCreate={handleFormCreate}
          handleFormEdit={handleFormEdit}
          handleShowBatch={ImportShow}
        />
        <DrawerForm
          drawer={drawer}
          formData={formData}
          originalFormData={originalFormData}
          formErrors={formErrors}
          formRef={formRef}
          onChange={handleFormChange}
          onSubmit={handleFormSubmit}
          onClose={handleFormClose}
          setFormErrors={setFormErrors}
          checkForm={checkForm}
        />
        <DrawerImport
          drawer={ImportDrawer}
          drawerRef={drawerRef}
          onShow={ImportShow}
          onClose={ImportClose}
          setDrawer={setDrawer}
        />
      </Panel>
    </>
  );
});
