import React, { useCallback, useState, useEffect } from 'react';
import _ from 'lodash';
import { Button, Whisper, Tooltip, Icon } from 'rsuite';
import { useFmtMsgFori18n } from '@/utils/formatMessage';
import CampaignDrawer from './CampaignDrawer';

export default React.memo(
  ({ value, disabled, drawerShow, onChange, onOpenDrawer, onCloseDrawer }: any) => {
    const { formatMessage } = useFmtMsgFori18n();

    const [checkedItems, setCheckedItems] = useState(value);

    useEffect(() => {
      setCheckedItems(value);
    }, [value]);

    const handleRemove = useCallback(
      id => () => {
        onChange(_.filter(value, i => i.id !== id));
      },
      [onChange, value]
    );

    const handleCloseDrawer = useCallback(() => {
      setCheckedItems(value);
      onCloseDrawer();
    }, [onCloseDrawer, value]);

    const handleSubmit = useCallback(() => {
      onChange(checkedItems);
      onCloseDrawer();
    }, [checkedItems, onChange]);

    return (
      <>
        {disabled || (
          <Button onClick={onOpenDrawer}>
            {formatMessage('i18n_select', { name: '' })}
            {formatMessage('i18n_campaign')}
          </Button>
        )}
        <ul className="campaign-list">
          {_.map(value, i => (
            <li key={i.id}>
              <Whisper
                placement="autoVerticalStart"
                trigger="hover"
                speaker={<Tooltip>{`${i.id} - ${i.name}`}</Tooltip>}
              >
                <div className="item-name">{`${i.id} - ${i.name}`}</div>
              </Whisper>
              {!disabled && (
                <Icon icon="close" className="clickable" onClick={handleRemove(i.id)} />
              )}
            </li>
          ))}
        </ul>
        <CampaignDrawer
          show={drawerShow}
          checkedItems={checkedItems}
          setCheckedItems={setCheckedItems}
          onSubmit={handleSubmit}
          onClose={handleCloseDrawer}
        />
      </>
    );
  }
);
