// 分页
export interface TABLE {
  [TABLE_KEY.PAGE]?: number;
  [TABLE_KEY.ORDER_TYPE]?: 'desc' | 'asc';
  [TABLE_KEY.ORDER_COLUMN]?: string;
  [TABLE_KEY.PAGE_SIZE]?: number;
}
// 时间
export interface TIME {
  start?: string;
  end?: string;
  minDate?: string;
  maxDate?: string;
}

// 筛选条件(需要保存)
export interface SESSION extends TABLE, TIME {
  selectedUser?: SelectedUser;
  selectedAccount?: any;
  selectedCampaign?: SelectedCampaign;
}

type DateStorage = { start: string; end: string };
export interface SessionData {
  id: string;
  pageSize?: number;
  date?: {
    user: DateStorage;
    campagign: DateStorage;
    crossCampaign: DateStorage;
  };
}

// 当前用户
export interface SelectedUser {
  id?: null | number;
  name?: null | string;
}

// 当前推广计划
export interface SelectedCampaign {
  id?: null | number;
  name?: null | string;
  start?: string;
  end?: string;
  maxDate?: string;
  minDate?: string;
}

export interface FILTER {
  sSearch?: null | string;
  [key: string]: any;
}

// post参数，可传入T来扩充自定义参数
export type PARAMS<T = {}> = SESSION & {
  sSearch?: null | string;
} & { [P in keyof T]: T[P] };

export interface PROFILE {
  id?: number;
  name?: string;
  email?: string;
  role?: string;
  avatar?: string;
  avatarThumb?: string;
}

// 这里也得斟酌下 然后其他参数放在 BasicReportParam
export interface BasicReportProps<TComm = any, TCustom = any> {
  dateParams?: TIME;
  filterParams?: TComm & TCustom;
  fmtFilterParams?: any;
  tableParams?: any;
  setTableParams?: Function;
  handleChangeCommonParam?: (partialObj: Partial<TComm>) => void;
  handleChangePagination?: (partialPagination: TABLE | ((prevFmtParam: any) => void)) => void;
  handleChangeCustomParam?: (partialObj: Partial<TCustom>) => void;
  handleSearchWord?: (word: string) => void;
  refetchTableData?: Function;
  postParams?: object;
  api?: Url;
  id?: number | string;
  updateKey?: number;
  requestChecker?: (param: any) => boolean; // 决定是否请求，定义且返回false则不请求，其他情况都请求
  onBeforeFetch?: () => void;
  onFetchSuccess?: (rsp: CommonResponse) => any;
}

