// 性别
export const GENDER = [
  {
    value: 'BOTH',
    label: 'i18n_both'
  },
  {
    value: 'FEMALE',
    label: 'i18n_female'
  },
  {
    value: 'MALE',
    label: 'i18n_male'
  }
];

export const CROWN = () => {
  let data = _.cloneDeep(GENDER);
  let newData: Array<{ label: string; value: string; role: string }> = [];
  data.forEach(item => {
    AGE_LIST.forEach(one => {
      newData.push({
        ...one,
        value: `${item.value}+${one.value}`,
        label: one.label,
        role: item.label
      });
    });
  });

  return newData;
};

