import * as React from 'react';
import { addLocaleData, IntlProvider } from 'react-intl';
import zh from 'react-intl/locale-data/zh';
import en from 'react-intl/locale-data/en';
import locales from '@/locales/index';
import { InjectIntlContext } from '@/components/hooks/useIntl';

// 添加语言库
addLocaleData([...zh, ...en]);

interface Props {
  language?: string;
  children(language: string): React.ReactNode;
}

export default React.memo(({ language = 'zh-CN', children }: Props) => (
  <IntlProvider locale={language} messages={locales[language]}>
    <InjectIntlContext>{children(language)}</InjectIntlContext>
  </IntlProvider>
));
